import { CCFloat, Component, Label, Vec2, Vec3, _decorator } from 'cc'
import { EDITOR_NOT_IN_PREVIEW } from 'cc/env'
const { ccclass, property, executeInEditMode, requireComponent } = _decorator

@ccclass('DynamicLabel')
@requireComponent(Label)
@executeInEditMode(true)
export class DynamicLabel extends Component {
    @property({ type: CCFloat, min: 1, max: 200 })
    public get size() {
        return this._size
    }

    public set size(value: number) {
        this._size = value
        this.label.fontSize = this.size * this.quality
    }

    @property({ type: CCFloat, min: 0.1, max: 4 })
    public get quality() {
        return this._quality
    }

    public set quality(value: number) {
        this._quality = value

        this.node.scale = new Vec3(1 / this.quality, 1 / this.quality, 1)
        this.label.fontSize = this.size * this.quality
        this.label.outlineWidth = this._outlineWidth * this.quality
        this.label.shadowOffset = this._shadowOffset.multiplyScalar(this.quality)
    }

    private _quality: number = 1
    private _size: number = 1
    private _outlineWidth: number = 0
    private _shadowOffset: Vec2 = new Vec2(0, 0)

    private label!: Label

    protected __preload(): void {
        this.label = this.getComponent(Label)!
    }

    protected start(): void {
        this._quality = 1 / this.node.scale.x
        this._size = this.label.fontSize / this.quality
        this._outlineWidth = this.label.outlineWidth
        this._shadowOffset = this.label.shadowOffset

        this.quality = window.devicePixelRatio
        if (EDITOR_NOT_IN_PREVIEW) return

        console.info(` ${this.node.name}<Label> quality:`, this.quality)
    }
}
