import { tween } from 'cc'
import AnimUtils from '../../../utils/AnimUtils'
import LayoutUtils from '../../../utils/LayoutUtils'
import type Board from '../board'

const withCenteredBoard =
    // biome-ignore lint/suspicious/noExplicitAny: <explanation>
        <F extends (...args: any[]) => any, O = ReturnType<F> extends Promise<infer O> ? O : never>(
            board: Board,
            fn: F,
        ) =>
        async (...args: Parameters<F>): Promise<O> => {
            const { left: oLeft, right: oRight } = board.widget
            const target = (oLeft + oRight) / 2
            await AnimUtils.runTweenAsync(
                tween(board.widget).to(0.2, { left: target, right: target }, { easing: 'cubicOut' }),
            )
            const out = await fn(...args)

            const { top, bottom, left, right } = board.getWidgetMargins(LayoutUtils.getCurrentLayout())

            await AnimUtils.runTweenAsync(
                tween(board.widget).to(
                    0.2,
                    {
                        top,
                        bottom,
                        left,
                        right,
                    },
                    { easing: 'cubicOut' },
                ),
            )
            return out
        }

export default withCenteredBoard
