type ValueOf<T> = T[keyof T]
export type TCodeSoundValue = ValueOf<typeof CodeSoundKeys>
type TCodeSoundKeys = { [key in TCodeSoundValue]?: number }

const values = O.vals(CodeSoundKeys) as TCodeSoundValue[]
const SoundKeys = values.reduce((result: TCodeSoundKeys, key, index) => {
    result[key] = index
    return result
}, {})

ccenum(SoundKeys)

export default SoundKeys
export const SoundKeysMapping = values
