import { Director, director, instantiate, Label, Node, NodeEventType, type Prefab, type Quat, type Vec3 } from 'cc'

interface NodeMetadata {
    node: Node
    layer: string
    originalParent: Node
    originalSiblingIndex: number
    originalLocalTransform: TransformData
    eventHandler: () => void
    parentTransformHandler: () => void
    keepInLayer: boolean
    needsPositionUpdate: boolean
}

interface TransformData {
    position: Vec3
    rotation: Quat
    scale: Vec3
}

class SortNodeUtilsCore {
    protected static inst$: SortNodeUtilsCore

    protected layerPrefab$!: Prefab
    protected layerNodes: Map<string, Node> = new Map()
    protected nodeMetadata: Map<string, NodeMetadata> = new Map()
    protected movedNodes: Set<string> = new Set()

    public static get inst() {
        if (!this.inst$) {
            this.inst$ = new this()
        }

        return this.inst$
    }

    constructor() {
        this.listenEvents$()
    }

    protected listenEvents$ = () => {
        director.on(Director.EVENT_AFTER_UPDATE, this.onAfterUpdate$)
        director.on(Director.EVENT_AFTER_RENDER, this.onAfterRender$)
    }

    public static setLayerPrefab(layerPrefab: Prefab) {
        this.inst.layerPrefab$ = layerPrefab
    }

    public static createLayer(container: Node, layer: string, depth: number) {
        const { inst } = this

        if (!container?.isValid) {
            console.warn(`[SortNode] createLayer: Invalid container for layer "${layer}"`)
            return
        }

        if (inst.layerNodes.has(layer)) {
            console.warn(`[SortNode] createLayer: Layer "${layer}" already exists`)
            return
        }

        let layerNode: Node

        if (inst.layerPrefab$) {
            layerNode = instantiate(inst.layerPrefab$)
            layerNode.name = `Layer:${layer}`

            const label = layerNode.getComponentInChildren(Label)
            if (label) {
                label.string = layer
            }
        } else {
            layerNode = new Node(`Layer:${layer}`)
        }

        container.insertChild(layerNode, depth)
        inst.layerNodes.set(layer, layerNode)

        console.debug(`[SortNode] createLayer: "${layer}" in ${container.name} at depth ${depth}`)
    }

    public static setNodeToLayer(node: Node, layer: string) {
        const { inst } = this

        if (!node?.isValid) {
            console.warn(`[SortNode] setNodeToLayer: Invalid node for layer "${layer}"`)
            return
        }

        const layerNode = inst.layerNodes.get(layer)
        if (!layerNode) {
            console.error(`[SortNode] setNodeToLayer: Layer "${layer}" not found`)
            return
        }

        if (inst.nodeMetadata.has(node.uuid)) return

        const originalParent = node.parent
        if (!originalParent) {
            console.warn('[SortNode] setNodeToLayer: Node has no parent')
            return
        }

        // Setup cleanup on node destruction
        const onDestroyed$ = () => {
            this.removeNodeFromLayer(node)
        }
        node.once(NodeEventType.NODE_DESTROYED, onDestroyed$)

        // Listen to parent transform changes to update node position
        const onParentTransformChanged$ = () => {
            const metadata = inst.nodeMetadata.get(node.uuid)
            if (!metadata) return

            metadata.needsPositionUpdate = true
        }

        originalParent.on(NodeEventType.TRANSFORM_CHANGED, onParentTransformChanged$)

        // Store all node metadata in one place
        inst.nodeMetadata.set(node.uuid, {
            node,
            layer,
            originalParent,
            originalSiblingIndex: node.getSiblingIndex(),
            originalLocalTransform: {
                position: node.getPosition().clone(),
                rotation: node.getRotation().clone(),
                scale: node.getScale().clone(),
            },
            eventHandler: onDestroyed$,
            parentTransformHandler: onParentTransformChanged$,
            keepInLayer: false,
            needsPositionUpdate: false,
        })

        // const metadata = inst.nodeMetadata.get(node.uuid)
        // console.debug('[SortNode] setNodeToLayer:', node.name, metadata)
    }

    public static removeNodeFromLayer(node: Node) {
        const { inst } = this

        const metadata = inst.nodeMetadata.get(node.uuid)
        if (!metadata) return

        // Cleanup event handlers
        node.off(NodeEventType.NODE_DESTROYED, metadata.eventHandler)
        metadata.originalParent.off(NodeEventType.TRANSFORM_CHANGED, metadata.parentTransformHandler)

        // * Remove keepInLayer first
        metadata.keepInLayer = false

        // Restore node before cleanup
        inst.restoreNode$(metadata)
        inst.revertNodeSiblingIndex$(metadata)

        // Clean up all references
        inst.movedNodes.delete(node.uuid)
        inst.nodeMetadata.delete(node.uuid)

        // console.debug('[SortNode] removeNodeFromLayer:', node.name, metadata)
    }

    public static keepNodeInLayer(node: Node) {
        const { inst } = this

        if (!node?.isValid) {
            console.warn('[SortNode] keepNodeInLayer: Invalid node')
            return
        }

        const metadata = inst.nodeMetadata.get(node.uuid)
        if (!metadata) {
            console.warn('[SortNode] keepNodeInLayer: Node not found in layer')
            return
        }

        metadata.keepInLayer = true
    }

    public static setActiveLayer(layer: string, active: boolean) {
        const { inst } = this

        if (!inst.layerNodes.has(layer)) {
            console.warn(`[SortNode] setActiveLayer: Layer "${layer}" not found`)
            return
        }

        const layerNode = inst.layerNodes.get(layer)
        if (layerNode) {
            layerNode.active = active
        }

        console.debug(`[SortNode] setActiveLayer: Layer "${layer}" ${active ? 'enabled' : 'disabled'}`)
    }

    // === Private Lifecycle Methods ===

    protected onAfterUpdate$ = () => {
        this.sortNodes$()
    }

    protected onAfterRender$ = () => {
        this.restoreNodes$()
    }

    // === Private Core Methods ===

    protected sortNodes$ = () => {
        if (this.layerNodes.size === 0) return
        if (this.nodeMetadata.size === 0) return

        this.nodeMetadata.forEach((metadata, uuid) => {
            const { node, layer, needsPositionUpdate } = metadata

            // Validate node
            if (!node?.isValid || !node.activeInHierarchy) return

            const layerNode = this.layerNodes.get(layer)
            if (!layerNode?.isValid) return

            // If parent transform changed, restore to parent for 1 frame to update position
            if (needsPositionUpdate) {
                this.revertNodeTransform$(metadata)
                metadata.needsPositionUpdate = false
                this.movedNodes.add(uuid)
                return
            }

            // Already in layer, skip
            if (node.parent === layerNode) return

            // Move node to layer and track it
            node.setParent(layerNode, true)
            this.movedNodes.add(uuid)
        })
    }

    protected revertNodeTransform$ = (metadata: NodeMetadata) => {
        const { node, originalParent, originalLocalTransform } = metadata
        const { position, rotation, scale } = originalLocalTransform

        node.setParent(originalParent, true)
        node.setPosition(position)
        node.setRotation(rotation)
        node.setScale(scale)
    }

    protected restoreNodes$ = () => {
        if (this.movedNodes.size === 0) return

        this.movedNodes.forEach((uuid) => {
            const metadata = this.nodeMetadata.get(uuid)
            if (metadata) {
                // If node is at original parent (from position update), it will be moved back to layer in next cycle
                // Otherwise, restore it normally
                if (metadata.node.parent !== metadata.originalParent) {
                    this.restoreNode$(metadata)
                }
            }
        })

        // Clear moved nodes after restoration
        this.movedNodes.clear()
    }

    protected restoreNode$ = (metadata: NodeMetadata) => {
        const { node, originalParent, keepInLayer } = metadata

        // Keep node in layer if requested
        if (keepInLayer) return

        // Already restored, skip
        if (node.parent === originalParent) return

        node.setParent(originalParent, true)
    }

    protected revertNodeSiblingIndex$ = (metadata: NodeMetadata) => {
        const { node, originalSiblingIndex } = metadata

        if (!node?.isValid || !node.activeInHierarchy) return

        const parent = node.parent
        if (!parent?.isValid) return

        const currentIndex = node.getSiblingIndex()
        const siblingLength = parent.children.length

        // Already at correct index or original index is out of bounds
        if (currentIndex === originalSiblingIndex) return
        if (originalSiblingIndex >= siblingLength) return

        node.setSiblingIndex(originalSiblingIndex)
    }
}

class SortNodeUtils extends SortNodeUtilsCore {
    // * Custom logic here
}

export default SortNodeUtils
