import { _decorator, Animation, Component } from 'cc'
import { Localize } from '../../components/text/Localize'
import GameEvents from '../../constants/GameEvents'
import { CodeSoundKeys } from '../../constants/SoundKeys'
import Board from '../../gameplay/game-objects/board'
import LevelManager from '../../gameplay/manager/LevelManager'
import { ParticleSystem } from '../../particles/ParticleSystem'
import AudioUtils from '../../utils/AudioUtils'
import ComponentUtils from '../../utils/ComponentUtils'
import LayoutUtils from '../../utils/LayoutUtils'
import SceneUtils from '../../utils/SceneUtils'
import ScreenUtils from '../../utils/ScreenUtils'
import { PlayerScore } from './components/player-score/PlayerScore'

const { ccclass, disallowMultiple, property } = _decorator
const { match, player, monitorError } = codex

const {
    Utils: { Object: O },
} = Ludex

@ccclass('ChallengeResultScene')
@disallowMultiple
export class ChallengeResultScene extends Component {
    @property(Localize)
    readonly drawLocalizer!: Localize

    @property(Localize)
    readonly winLocalizer!: Localize

    @property(Localize)
    readonly loseLocalizer!: Localize

    @property(Localize)
    readonly waitingLocalizer!: Localize

    @property({ type: PlayerScore, group: 'Score' })
    readonly playerScore!: PlayerScore

    @property({ type: PlayerScore, group: 'Score' })
    readonly opponentScore!: PlayerScore

    @property({ group: 'Confetti Effect', type: ParticleSystem })
    private readonly leftConfetti!: ParticleSystem

    @property({ group: 'Confetti Effect', type: ParticleSystem })
    private readonly rightConfetti!: ParticleSystem

    @property(LevelManager)
    readonly levelManager!: LevelManager

    @property(Animation)
    readonly headerAnimation!: Animation

    public __preload() {
        this.validateRequiredComponents()
        this.listenEvents()
    }

    private validateRequiredComponents() {
        ComponentUtils.lazyValidateProperties(this)
    }

    private listenEvents() {
        this.node.scene.on(GameEvents.RESULT_SCENE_HOME, this.goToDashboardScene)
        this.node.scene.on(GameEvents.RESULT_SCENE_REPLAY, this.handleTryAgainClicked, this)
    }

    public start() {
        this.scheduleOnce(this.preloadScenes, 3)

        try {
            const state = match.getMatchState()
            const { playerId, opponentId } = state.customData

            if (!playerId || !opponentId) {
                throw new Error(
                    `ResultScene: playerId is ${playerId} and opponentId is ${opponentId}. Expected to not be null.`,
                )
            }

            const { photo: playerPhoto } = player.getPlayer()

            const { score: playerScore = 0, finished: isPlayerFinish = false } = state.profiles[playerId] ?? {}

            const {
                photo: opponentPhoto = '',
                score: opponentScore = 0,
                finished: isOpponentFinish = false,
            } = state.profiles[opponentId] ?? {}

            const isMatchFinished = isPlayerFinish && isOpponentFinish
            const isPlayerWon = isMatchFinished && playerScore > opponentScore
            const isOpponentWon = isMatchFinished && opponentScore > playerScore

            // Update player info
            this.playerScore.loadPhoto(playerPhoto, playerId)
            this.opponentScore.loadPhoto(opponentPhoto, opponentId)

            this.playerScore.setPlayerScore(playerScore)
            this.opponentScore.setPlayerScore(opponentScore)

            this.playerScore.showCrown(isPlayerWon)
            this.opponentScore.showCrown(isOpponentWon)

            if (!isPlayerFinish || !isOpponentFinish) {
                if (!isPlayerFinish) this.playerScore.playWaitingAnimation()
                if (!isOpponentFinish) this.opponentScore.playWaitingAnimation()
                this.playStatusAnimation('Waiting')
            } else if (playerScore === opponentScore) {
                this.playStatusAnimation('Draw')
            } else if (isPlayerWon) {
                this.playStatusAnimation('Win')
            } else if (isOpponentWon) {
                this.playStatusAnimation('Lose')
            }
        } catch (error) {
            console.warn('ResultScene: Error on load', error)
            this.goToDashboardScene()
        }
    }

    private preloadScenes(): void {
        SceneUtils.preloadDashboardScene()
    }

    public async handleTryAgainClicked() {
        let { level = 1 } = player.getGameData() ?? {}
        //? Avoid playing level 0 (Newbie Guide)
        level = Math.max(1, level)

        const levelData = await this.levelManager.loadLevelData(level)

        try {
            if (!levelData) {
                throw new Error('Level data is not found')
            }

            const { opponentId } = match.getMatchState().customData

            if (!opponentId) {
                throw new Error('Opponent id is not found')
            }

            ScreenUtils.showLoadingScreen()

            const fen = Board.generateFen(levelData)

            const {
                mode: gravity = '',
                rules = { addRandomTilesPerMove: 0, maxSpaceOfPair: 0 },
                time = 0,
                level,
            } = levelData

            await match.challenge.friend.processAsync({
                playerId: player.getPlayerId(),
                opponentId,
                extraData: {
                    challengeData: {
                        level,
                        fen,
                        gravity,
                        rules,
                        time,
                    },
                },
            })

            ScreenUtils.closeLoadingScreen()
            this.goToGameScene()
        } catch (error) {
            if (O.hasOwn(error, 'code') && error.code === 'USER_INPUT') {
                // ? User cancelled. Safely ignore
                ScreenUtils.closeLoadingScreen()
                return
            }

            console.warn('ReplayChallengeFriend', error)

            ScreenUtils.closeLoadingScreen()

            const duration = 2
            ScreenUtils.showNotificationScreenWithLocalizedKey('error.challengeFail', duration)

            monitorError?.sendException(
                // biome-ignore lint/style/useTemplate: <explanation>
                error instanceof Error ? error : new Error('ReplayChallengeFriend' + JSON.stringify(error)),
            )

            this.scheduleOnce(this.goToDashboardScene, duration)
        }
    }

    public handleHomeClicked() {
        this.goToDashboardScene()
    }

    private goToDashboardScene = () => {
        this.playerScore.cleanupOverlay()
        this.opponentScore.cleanupOverlay()

        SceneUtils.switchToDashboardScene()
    }

    private goToGameScene = () => {
        this.playerScore.cleanupOverlay()
        this.opponentScore.cleanupOverlay()

        SceneUtils.switchToGameScene()
    }

    private playStatusAnimation(status: 'Draw' | 'Win' | 'Lose' | 'Waiting'): void {
        if (status === 'Win' || status === 'Waiting') {
            this.triggerConfettiAnimation()
        }

        const localizer = this.getSpriteLocalizer(status)
        localizer.node.active = true

        localizer.waitUntilLocalized().then(() => {
            switch (status) {
                case 'Draw':
                    this.playDrawAnimation()
                    break
                case 'Win':
                    this.playWinAnimation()
                    break
                case 'Lose':
                    this.playLoseAnimation()
                    break
                case 'Waiting':
                    this.playWaitingAnimation()
                    break
            }
        })
    }

    private getSpriteLocalizer(status: 'Draw' | 'Win' | 'Lose' | 'Waiting') {
        switch (status) {
            case 'Draw':
                return this.drawLocalizer
            case 'Win':
                return this.winLocalizer
            case 'Lose':
                return this.loseLocalizer
            case 'Waiting':
                return this.waitingLocalizer
        }
    }

    @ComponentUtils.onlyRunInLifeCycle
    private playDrawAnimation(): void {
        this.headerAnimation.play('RibbonDraw')
        AudioUtils.playSound(CodeSoundKeys.LEVEL_FAIL)
    }

    @ComponentUtils.onlyRunInLifeCycle
    private playWinAnimation(): void {
        this.headerAnimation.play('RibbonWin')
        AudioUtils.playSound(CodeSoundKeys.LEVEL_COMPLETE)
    }

    @ComponentUtils.onlyRunInLifeCycle
    private playLoseAnimation(): void {
        this.headerAnimation.play('RibbonLose')
        AudioUtils.playSound(CodeSoundKeys.LEVEL_FAIL)
    }

    @ComponentUtils.onlyRunInLifeCycle
    private playWaitingAnimation(): void {
        this.headerAnimation.play('RibbonWaiting')
        AudioUtils.playSound(CodeSoundKeys.LEVEL_COMPLETE)
    }

    @ComponentUtils.onlyRunInLifeCycle
    private triggerConfettiAnimation(): void {
        const isLandscape = LayoutUtils.isLandscape()

        const burstCount = isLandscape ? 50 : 30
        const startSpeedMin = isLandscape ? 5000 : 2000
        const startSpeedMax = isLandscape ? 15000 : 10000
        const leftAngle = isLandscape ? 40 : 60
        const rightAngle = isLandscape ? 110 : 100
        const arc = isLandscape ? 30 : 20

        this.leftConfetti.bursts[0]!.count = this.rightConfetti.bursts[0]!.count = burstCount
        this.leftConfetti.startSpeed.constantMin = this.rightConfetti.startSpeed.constantMin = startSpeedMin
        this.leftConfetti.startSpeed.constantMax = this.rightConfetti.startSpeed.constantMax = startSpeedMax
        this.leftConfetti.shapeModule.angle = leftAngle
        this.rightConfetti.shapeModule.angle = rightAngle
        this.leftConfetti.shapeModule.arc = this.rightConfetti.shapeModule.arc = arc

        this.scheduleOnce(() => {
            this.leftConfetti.play()
            this.rightConfetti.play()
        }, 0.2)
    }
}
