import LevelGravity from '../../../../constants/LevelGravity'

/**
 * Parse a string into level modes by extracting the words and matching them to the LevelMode enum. Using string's search()
 * @param text
 */
export default function parseLevelGravity(text: string): LevelGravity[] {
    // ex 'fall-down-put-columns-middle' gets converted into ['fall-down', 'put-columns-middle']
    const words = text.split('-')
    const levelModes: LevelGravity[] = []

    let i = 0
    while (i < words.length) {
        let levelGravity = null
        const word = words[i]

        switch (word) {
            case 'normal':
                i += 1
                break
            case 'fall':
                levelGravity = LevelGravity.FallDown
                i += 2
                break
            case 'fly':
                levelGravity = LevelGravity.FlyUp
                i += 2
                break
            case 'push':
                if (words[i + 1] === 'left') {
                    levelGravity = LevelGravity.PushLeft
                    i += 2
                } else {
                    levelGravity = LevelGravity.PushRight
                    i += 2
                }
                break
            case 'put':
                if (words[i + 1] === 'columns') {
                    levelGravity = LevelGravity.PutColumnMiddle
                    i += 3
                } else {
                    levelGravity = LevelGravity.PutRowMiddle
                    i += 3
                }
                break
        }

        if (levelGravity) {
            levelModes.push(levelGravity)
        }
    }

    return levelModes
}
