import { Component, Label, Node, Vec3, _decorator, tween } from 'cc'
import GameplayEvents from 'db://assets/gameplay/constants/GameEvents'
import AnimUtils from '../../../../../utils/AnimUtils'
import type { StatusConfig } from '../GameSceneLayout'

const { ccclass, disallowMultiple, property } = _decorator
@ccclass('Clock')
@disallowMultiple
export class Clock extends Component {
    @property(Node)
    public clockIcon!: Node

    @property(Label)
    private timeLabel!: Label

    public __preload(): void {
        this.node.scene.on(GameplayEvents.TIME_UPDATED, this.updateTimeLabel, this)
        this.node.scene.on(GameplayEvents.RESCUE_START, this.handleRescueStart, this)
        this.node.scene.on(GameplayEvents.RESCUE_FINISHED, this.handleRescueFinished, this)
    }

    public updateStatus(config: StatusConfig): void {
        if (config.time !== undefined) this.updateTimeLabel(config.time)
    }

    private updateTimeLabel(time: number) {
        const timeText = this.convertSecondsToMmSs(time)

        this.timeLabel.string = timeText
    }

    private convertSecondsToMmSs(seconds: number): string {
        const minutes: number = Math.floor(seconds / 60)
        const remainingSeconds: number = Math.floor(seconds % 60)

        const formattedMinutes: string = this.padZero(minutes)
        const formattedSeconds: string = this.padZero(remainingSeconds)

        return `${formattedMinutes}:${formattedSeconds}`
    }

    private padZero(number: number): string {
        return number < 10 ? `0${number}` : `${number}`
    }

    private async handleRescueStart(): Promise<void> {
        const scaleUpTween = tween(this.node).to(0.5, { scale: new Vec3(1.1, 1.1, 1) })
        const clockIconTween = tween(this.clockIcon)
            .to(0.15, { angle: -15 }, { easing: 'cubicOut' })
            .to(0.15, { angle: 0 }, { easing: 'cubicIn' })
            .to(0.15, { angle: 15 }, { easing: 'cubicOut' })
            .to(0.15, { angle: 0 }, { easing: 'cubicIn' })
        const clockIconTweenLoop = tween(this.clockIcon).repeat(5, clockIconTween)

        AnimUtils.runTweenAsync(clockIconTweenLoop)
        AnimUtils.runTweenAsync(scaleUpTween)
    }

    private async handleRescueFinished(): Promise<void> {
        const scaleDownTween = tween(this.node).to(0.5, { scale: new Vec3(1, 1, 1) })
        AnimUtils.runTweenAsync(scaleDownTween)
    }
}
