import { _decorator, Component, type EventTouch, Input, Label, RichText, UIOpacity } from 'cc'
import AdsUtils from '../../utils/AdsUtils'
import ComponentUtils from '../../utils/ComponentUtils'

const { ccclass, property, requireComponent } = _decorator

const {
    Events,
    Plugins: {
        Ads: { Types: AdType },
    },
} = Ludex

const { event, ads } = codex

const LOG_CONFIG = {
    LOADING: { color: '#00FFFF', prefix: 'LOADING' },
    LOADED: { color: '#00FF00', prefix: 'LOADED' },
    LOAD_FAILED: { color: '#FF00FF', prefix: 'LOAD FAIL' },
    SHOWING: { color: '#FFFF00', prefix: 'SHOWING...' },
    SHOW: { color: '#00FF00', prefix: 'SHOW' },
    SHOW_FAILED: { color: '#FF0000', prefix: 'SHOW FAIL' },
    CLOSED: { color: '#AAAAAA', prefix: 'CLOSED' },
} as const

const MAX_LOG_LINES = 20

@ccclass('DebugAdsPlugin')
@requireComponent(UIOpacity)
export class DebugAdsPlugin extends Component {
    @ComponentUtils.required()
    @property(Label)
    public statusLabel!: Label

    @ComponentUtils.required()
    @property(Label)
    public timersLabel!: Label

    @ComponentUtils.required()
    @property(RichText)
    public logLabel!: RichText

    @ComponentUtils.required()
    @property(UIOpacity)
    public opacity!: UIOpacity

    private logLines$: string[] = []
    private touchMs$ = 0

    protected onLoad(): void {
        this.logInitializedAds$()

        this.setupEventListeners$('on')

        this.schedule(() => {
            this.updateStatus$()
            this.updateTimers$()
        }, 0.5)
    }

    protected onDestroy(): void {
        this.setupEventListeners$('off')
        this.unscheduleAllCallbacks()
    }

    private setupEventListeners$ = (state: 'on' | 'off'): void => {
        const handlers = [
            [Events.AD_LOADING, this.onAdLoading$],
            [Events.AD_LOADED, this.onAdLoaded$],
            [Events.AD_LOAD_FAILED, this.onAdLoadFailed$],
            [Events.AD_SHOWING, this.onAdShowing$],
            [Events.AD_SHOW, this.onAdShow$],
            [Events.AD_SHOW_FAILED, this.onAdShowFailed$],
            [Events.AD_CLOSED, this.onAdClosed$],
        ] as const

        handlers.forEach(([evt, func]) => {
            event[state](evt, func)
        })

        this.node[state](Input.EventType.TOUCH_END, this.handleTouchEnd$)
        this.node[state](Input.EventType.TOUCH_START, this.handleTouchStart$)
    }

    private logInitializedAds$ = () => {
        if (!ads) {
            this.addLog$('Ads plugin not available', '#FF0000')
            return
        }

        // @ts-expect-error - getAdsInstances is a method of the Ads plugin
        const adsInstances = ads.getAdsInstances()

        if (!adsInstances || Object.keys(adsInstances).length === 0) {
            this.addLog$('No ad instances initialized', '#FFFF00')
            return
        }

        this.addLog$(`Initialized ${Object.keys(adsInstances).length} ad instances:`, '#00FF00')
        Object.entries(adsInstances).forEach(([_, ad]: [string, any]) => {
            const { deviceOS, placementId } = ad
            this.addLog$(`${placementId} on ${deviceOS}`, '#00FFFF')
        })
    }

    private handleTouchStart$ = (event: EventTouch) => {
        event.preventSwallow = this.opacity.opacity === 0
        if (!event.touch || !this.opacity) return

        const isDoubleTap = Date.now() - this.touchMs$ < 300

        if (isDoubleTap) {
            if (this.opacity.opacity === 255) {
                this.opacity.opacity = 0
            } else {
                this.opacity.opacity = 255
            }
        }

        this.touchMs$ = Date.now()
    }

    private handleTouchEnd$ = (event: EventTouch) => {
        event.preventSwallow = this.opacity.opacity === 0
    }

    private onAdLoading$ = (payload: unknown) => this.logEvent$(LOG_CONFIG.LOADING, payload)
    private onAdLoaded$ = (payload: unknown) => this.logEvent$(LOG_CONFIG.LOADED, payload)
    private onAdLoadFailed$ = (payload: unknown) => this.logEvent$(LOG_CONFIG.LOAD_FAILED, payload)

    private onAdShowing$ = (payload: unknown) => this.logEvent$(LOG_CONFIG.SHOWING, payload)
    private onAdShow$ = (payload: unknown) => this.logEvent$(LOG_CONFIG.SHOW, payload)
    private onAdShowFailed$ = (payload: unknown) => this.logEvent$(LOG_CONFIG.SHOW_FAILED, payload)

    private onAdClosed$ = (payload: unknown) => this.logEvent$(LOG_CONFIG.CLOSED, payload)

    private logEvent$ = (config: { color: string; prefix: string }, payload: any) => {
        const { type, placementId } = payload
        this.addLog$(`${config.prefix}: ${type} (${placementId})`, config.color)
    }

    private addLog$ = (message: string, colorHex: string) => {
        const time = new Date().toLocaleTimeString().split(' ')[0]
        const line = `<color=${colorHex}>[${time}] ${message}</color>`

        this.logLines$.push(line)
        if (this.logLines$.length > MAX_LOG_LINES) {
            this.logLines$.shift()
        }

        if (this.logLabel) {
            this.logLabel.string = this.logLines$.join('\n')
        }
    }

    private updateStatus$ = () => {
        const enabled = ads.enabled
        // @ts-expect-error - getDeviceOS is a method of the Ads plugin
        const deviceOS = ads.getDeviceOS()
        // @ts-expect-error - getDegradationTracking is a method of the Ads plugin
        const degradation = ads.getDegradationTracking()
        const safeAreaBottom = ads.getSafeAreaBottom(1)
        const bannerHeight = AdsUtils.getBannerAdHeight()

        let degradationInfo = 'N/A'
        let lastSuccess = 'N/A'
        if (degradation) {
            const lastSuccessTime = degradation.lastSuccessTime
            lastSuccess = lastSuccessTime > 0 ? new Date(lastSuccessTime).toLocaleTimeString() : 'Never'
            degradationInfo = `${degradation.failedAttempts}`
        }

        const disabledTypes = Object.values(AdType).filter((type) => !AdsUtils.isAdEnabled(type))

        this.statusLabel.string = [
            `Enabled: ${enabled}`,
            `DeviceOS: ${deviceOS}`,
            `Last Success: ${lastSuccess}`,
            `Banner Height: ${bannerHeight}`,
            `SafeAreaBottom: ${safeAreaBottom}`,
            `Degradation Attempts: ${degradationInfo}`,
            `Disabled: ${disabledTypes.length ? disabledTypes.join(', ') : 'None'}`,
        ].join('\n')
    }

    private updateTimers$ = () => {
        // @ts-expect-error - getLastCallAdShownTime is a method of the Ads plugin
        const lastShown = ads.getLastCallAdShownTime()
        if (!lastShown) return

        const now = Date.now() / 1000

        const timersText = Object.values(AdType)
            .map((type) => {
                const lastTime = lastShown[type] || 0
                const diff = lastTime > 0 ? (now - lastTime).toFixed(1) : 'Never'
                return `${type}: ${diff}s`
            })
            .join('\n')

        this.timersLabel.string = `Time Since Last Show\n${timersText}`
    }
}
