import { Animation, Component, UIOpacity, Widget, _decorator, game, tween } from 'cc'
import { Localize } from '../../../../../components/text/Localize'
import GameEvents from '../../../../../constants/GameEvents'
import GameWorld from '../../../../../constants/GameWorld'
import GameplayEvents from '../../../../../gameplay/constants/GameEvents'
import AnimUtils from '../../../../../utils/AnimUtils'
import ComponentUtils from '../../../../../utils/ComponentUtils'
import LayoutUtils from '../../../../../utils/LayoutUtils'

const {
    Utils: { Signal: S },
} = Ludex
const { ccclass, property } = _decorator
const { Layout } = GameWorld
const ComboSpriteLocalizeKeys = ['good', 'great', 'excellent'] as const satisfies Ludex.Plugins.Language.TextureKey[]

@ccclass('ComboPopup')
export class ComboPopup extends Component {
    @ComponentUtils.required()
    @property(Localize)
    private readonly spriteLocalizer!: Localize

    @ComponentUtils.required()
    @property(Animation)
    private readonly animation!: Animation

    @ComponentUtils.required()
    @property(UIOpacity)
    private readonly textUiOpacity!: UIOpacity

    private widget!: Widget

    private readonly comboController = new S.SignalController()

    protected __preload(): void {
        this.widget = this.getComponent(Widget)!

        ComponentUtils.validateRequiredProperties(this)
    }

    protected onLoad(): void {
        // @ts-expect-error - This event is custom event
        game.on(GameEvents.ORIENTATION_CHANGED, this.handleOrientationChanged, this)
        this.handleOrientationChanged(LayoutUtils.getCurrentLayout())
    }

    protected onDestroy(): void {
        game.off(GameEvents.ORIENTATION_CHANGED, this.handleOrientationChanged, this)
        this.comboController.abort()
    }

    protected start() {
        this.node.scene.on(GameplayEvents.COMBO_UPDATED, this.handleOnComboUpdated, this)
    }

    public async handleOnComboUpdated(combo: number) {
        if (combo < 2) return

        for (let i = ComboSpriteLocalizeKeys.length - 1; i >= 0; i--) {
            const key = ComboSpriteLocalizeKeys[i] as (typeof ComboSpriteLocalizeKeys)[number]

            if (combo >= i + 2) {
                const signal = this.comboController.renew()
                this.spriteLocalizer.key = key

                this.textUiOpacity.opacity = 0

                await this.spriteLocalizer.waitUntilLocalized()
                if (signal.aborted) return

                AnimUtils.runTweenAsync(tween(this.textUiOpacity).to(0.25, { opacity: 255 }), signal)
                this.animation.play()

                return
            }
        }
    }

    private handleOrientationChanged(layout: string): void {
        if (layout === Layout.Portrait) {
            this.widget.left = 20
            this.widget.right = 20
        } else if (layout === Layout.Landscape) {
            this.widget.left = 700
            this.widget.right = 120
        }
    }
}
