import { Component, UITransform, Widget, _decorator, game } from 'cc'
import DashboardEvents from '../../../../../constants/DashboardEvents'
import GameEvents from '../../../../../constants/GameEvents'
import { ScreenKeysEnum } from '../../../../../constants/ScreenKeys'
import { StarProgressBar } from '../../../../../screens/star-chest-screen/StarProgressBar'
import LayoutUtils from '../../../../../utils/LayoutUtils'
import ScreenUtils from '../../../../../utils/ScreenUtils'
import { DailyRewardsWidget } from '../event-buttons/buttons/DailyRewardsWidget'
import { EventButton } from '../event-buttons/event-button/EventButton'
const { ccclass, property } = _decorator

const { player } = codex

@ccclass('DashboardHeader')
export class DashboardHeader extends Component {
    @property({ type: StarProgressBar, displayName: 'Progress bar', group: 'Star Chest' })
    private starProgressBar!: StarProgressBar

    @property({ type: EventButton, displayName: 'Button Component', group: 'Star Chest' })
    private chestButton!: EventButton

    @property(DailyRewardsWidget)
    readonly dailyRewardsWidget!: DailyRewardsWidget

    @property(UITransform)
    private uiTransform!: UITransform

    @property(Widget)
    private containerWidget!: Widget

    @property(UITransform)
    private containerUITransform!: UITransform

    public __preload(): void {
        game.on(DashboardEvents.STARS_UPDATED, this.handleStarsUpdateEvent, this)
        game.on(GameEvents.ORIENTATION_CHANGED, this.handleOrientationChanged, this)
        game.on(GameEvents.VIEW_RESIZED, this.handleOrientationChanged, this)
    }

    protected start(): void {
        this.handleOrientationChanged()
    }

    public onDestroy(): void {
        game.off(DashboardEvents.STARS_UPDATED, this.handleStarsUpdateEvent, this)
        game.off(GameEvents.ORIENTATION_CHANGED, this.handleOrientationChanged, this)
        game.off(GameEvents.VIEW_RESIZED, this.handleOrientationChanged, this)
    }

    public updateDailyRewardsButton() {
        this.dailyRewardsWidget.refresh()
    }

    public updateStarProgress(stars: number): void {
        this.handleStarsUpdateEvent(stars)
    }

    private updateStarChestButtonAnimation(stars: number): void {
        this.chestButton.isAvailable = stars >= 20
    }

    private handleStarsUpdateEvent(stars?: number): void {
        const s = stars ?? player.getGameData()?.stars
        if (s === undefined) return

        this.starProgressBar.setRange(s, 20)
        this.updateStarChestButtonAnimation(s)
    }

    private handleOrientationChanged(): void {
        if (LayoutUtils.isPortrait()) {
            this.containerWidget.isAlignLeft = this.containerWidget.isAlignRight = true
        } else if (LayoutUtils.isLandscape()) {
            this.containerWidget.isAlignLeft = this.containerWidget.isAlignRight = false
            this.containerUITransform.width = Math.min(this.uiTransform.width, 1366)
        }

        this.containerWidget.getComponentsInChildren(Widget).forEach((widget, _) => {
            widget.updateAlignment()
        })
    }

    //? Callback for click event of settings button
    public handleClickedSettingsButton(): void {
        ScreenUtils.openScreen(ScreenKeysEnum.SETTINGS_SCREEN)
    }
}
