import type { EditBox } from 'cc'
import { Button, Component, Node, _decorator, game } from 'cc'
import GameEvents from '../../constants/GameEvents'
import { AvailableLocales } from '../../constants/Locales'
import { ScreenKeysEnum } from '../../constants/ScreenKeys'
import ScreenUtils from '../../utils/ScreenUtils'
import type { Gameplay } from '../Gameplay'
import GameplayEvents from '../constants/GameEvents'
import ItemKeys from '../constants/ItemKeys'
import { TileTypes } from '../constants/TileType'

const {
    Dtos: { Player },
    Utils: { Function: F },
} = Ludex

const { ccclass, property } = _decorator

const { player, language } = codex

@ccclass('DevToolsContainer')
export class DevToolsContainer extends Component {
    @property(Node)
    public devToolsToggle!: Node

    public isActive = true
    public game: Gameplay | null = null

    private bundleName: string = 'lowest'

    private currentLocaleIndex = 0

    public injectGameplay(game: Gameplay) {
        this.game = game
    }

    protected start(): void {
        this.initCurrentLocale()
    }

    private initCurrentLocale() {
        const currentLocale = language.getCurrentLocale()
        this.currentLocaleIndex = AvailableLocales.indexOf(currentLocale)
    }

    private hideAllButtons() {
        const buttons = this.getComponentsInChildren(Button)

        for (const button of buttons) {
            const { parent } = button.node
            if (!parent) continue

            if (parent.name === this.devToolsToggle.name) continue
            parent.active = false
        }
    }

    private showAllButtons() {
        const buttons = this.getComponentsInChildren(Button)

        for (const button of buttons) {
            const { parent } = button.node
            if (!parent) continue

            if (parent.name === this.devToolsToggle.name) continue
            parent.active = true
        }
    }

    public handleClickDevToolsButton() {
        if (this.isActive) {
            this.hideAllButtons()
            this.isActive = false
        } else {
            this.showAllButtons()
            this.isActive = true
        }
    }

    // ? Callback for ResetDataButton
    public handleResetData() {
        player.setPlayerData(Player.Data.getDefaults() as object)

        ScreenUtils.openScreen(ScreenKeysEnum.NOTIFICATION_SCREEN, {
            duration: 3,
            message: 'Items and stars will only be reset if you refresh the game page now.',
        })

        this.node.scene.emit(GameplayEvents.START_OVER, 0)
    }

    public async handleNextLevel() {
        const level = (this.game?.getGameData()?.level ?? 1) + 1
        this.game?.setGameData({ level: level - 1 })
        player.setGameData({ level })
        this.node.scene.emit(GameplayEvents.NEXT_LEVEL)
    }

    public async handlePrevLevel() {
        const level = Math.max((this.game?.getGameData()?.level ?? 2) - 1, 1)
        this.game?.setGameData({ level: level - 1 })
        player.setGameData({ level })
        this.node.scene.emit(GameplayEvents.NEXT_LEVEL)
    }

    // ? Callback for SelectLevelButton
    public async handleEnterSelectLevel(editBox: EditBox) {
        const level = Number(editBox.string)
        this.game?.setGameData({ level: level - 1 })
        player.setGameData({ level })
        this.node.scene.emit(GameplayEvents.NEXT_LEVEL)
    }

    public handleClickBuildVersionButton() {
        const buildVersion = codex.getBuildVersion()
        ScreenUtils.showNotificationScreen(`Build version: ${buildVersion}`)
    }

    // ? Callback for button
    public handleClickWinLevelButton() {
        this.node.scene.emit(GameplayEvents.GAME_FINISH)
    }

    // ? Callback for button
    public handleClickTimeOutButton() {
        this.game?.clockManager.setTime(0.1)
    }

    // ? Callback for button
    public handleClickExplodeButton() {
        const game = this.game
        if (!game) return

        const bomb = game.getGameData()?.levelData?.specials.bomb
        if (!bomb || bomb <= 0) return

        const bombTiles = game.board.getBombTiles()

        if (bombTiles.length === 0) {
            console.info('No bomb tiles found')
            return
        }

        const bombTile = bombTiles[0]
        if (!bombTile || bombTile.special?.type !== TileTypes.BOMB) {
            throw new Error('Invalid bomb tile')
        }

        bombTile.special.detonateImmediately()
    }

    // ? Callback for button
    public handleFillItem() {
        this.node.scene.emit(GameplayEvents.ADD_ITEM, {
            item: ItemKeys.HINT,
            quantity: 2,
        } as AddItemPayload)

        this.node.scene.emit(GameplayEvents.ADD_ITEM, {
            item: ItemKeys.STORM,
            quantity: 2,
        } as AddItemPayload)

        this.node.scene.emit(GameplayEvents.ADD_ITEM, {
            item: ItemKeys.THUNDER,
            quantity: 2,
        } as AddItemPayload)

        this.node.scene.emit(GameplayEvents.ADD_ITEM, {
            item: ItemKeys.PLIERS,
            quantity: 2,
        } as AddItemPayload)
    }

    public handleAutoPlay() {
        this.node.scene.emit(GameplayEvents.AUTO_PLAY)
    }

    public debouncedStarsNotification = F.debounce(() => {
        const gameData = player.getGameData()
        if (!gameData) return

        ScreenUtils.showNotificationScreen(`Stars added. Current stars: ${gameData.stars}`)
    }, 700)

    public handleAddStars() {
        const gameData = player.getGameData()
        if (!gameData) return
        player.setGameData({ stars: gameData.stars + 10 })
        this.debouncedStarsNotification()
    }

    public handleSwitchQualityAssets() {
        switch (this.bundleName) {
            case 'lowest':
                this.bundleName = 'highest'
                break
            case 'highest':
                this.bundleName = 'lowest'
                break
        }

        game.emit(GameEvents.REQUEST_UPGRADE_ASSET, { quality: this.bundleName })
    }

    public async handleSwitchLocale() {
        this.currentLocaleIndex = (this.currentLocaleIndex + 1) % AvailableLocales.length
        const locale = AvailableLocales[this.currentLocaleIndex]

        ScreenUtils.showLoadingNotificationScreen()

        try {
            if (!locale) throw new Error('Locale is undefined')
            await this.preloadLanguageAsync(locale)

            game.emit(GameEvents.CHOOSE_LANGUAGE, { locale, saveToPlayerData: true } as ChooseLanguagePayload)
            ScreenUtils.showNotificationScreen(`Language changed to ${locale}`)
        } catch (error) {
            console.error(error)
            ScreenUtils.showNotificationScreen('Failed to change language')
        }
    }

    private preloadLanguageAsync(locale: Ludex.Plugins.Language.Locale) {
        return new Promise((resolve, reject) => {
            game.emit(GameEvents.PRELOAD_LANGUAGE, {
                locale,
                onLoad: resolve,
                onError: reject,
            } as PreloadLanguagePayload)
        })
    }
}
