import { LeaderboardColor } from 'db://assets/gameplay/constants/Leaderboard'
import AnimUtils from 'db://assets/utils/AnimUtils'
import ComponentUtils from 'db://assets/utils/ComponentUtils'
import type AnimAbortSignal from 'db://assets/utils/common/AnimAbortSignal'
import {
    _decorator,
    Node as CCNode,
    Color,
    Component,
    Label,
    Sprite,
    SpriteFrame,
    tween,
    UIOpacity,
    UITransform,
    Vec3,
} from 'cc'
import CacheUtils from '../../../../../../../utils/CacheUtils'

const { ccclass, property } = _decorator

export interface LeaderConfig {
    playerId: string
    rank: number
    name: string
    photo: string
    score: number
    isHighlight: boolean
}

const {
    Utils: { Number: N, Signal: S },
} = Ludex

@ccclass('Leader')
export class Leader extends Component {
    @property(Label)
    private nameLabel!: Label
    @property(UIOpacity)
    private nameOpacity!: UIOpacity

    @property({ type: CCNode, group: 'Container' })
    private container!: CCNode

    @property({ type: Sprite, group: 'Background' })
    private background!: Sprite
    @property({ type: SpriteFrame, group: 'Background' })
    private highlightBackground!: SpriteFrame

    @property({ type: Sprite, group: 'Rank' })
    private rankSprite!: Sprite
    @property({ type: Label, group: 'Rank' })
    private rankLabel!: Label
    @property({ type: SpriteFrame, group: 'Rank' })
    private goldRank!: SpriteFrame
    @property({ type: SpriteFrame, group: 'Rank' })
    private silverRank!: SpriteFrame
    @property({ type: SpriteFrame, group: 'Rank' })
    private bronzeRank!: SpriteFrame
    @property({ type: SpriteFrame, group: 'Rank' })
    private highlightRank!: SpriteFrame

    @property({ type: Sprite, group: 'Avatar' })
    private avatar!: Sprite
    @property({ type: UIOpacity, group: 'Avatar' })
    private avatarOpacity!: UIOpacity

    @property({ type: Sprite, group: 'Score' })
    private scoreBackground!: Sprite
    @property({ type: Label, group: 'Score' })
    private scoreLabel!: Label
    @property({ type: SpriteFrame, group: 'Score' })
    private highlightScoreBackground!: SpriteFrame
    @property({ type: UIOpacity, group: 'Score' })
    private scoreOpacity!: UIOpacity

    private defaultBackground!: SpriteFrame
    private defaultRank!: SpriteFrame
    private defaultAvatar!: SpriteFrame
    private defaultScoreBackground!: SpriteFrame

    @property({ type: UIOpacity })
    private uiOpacity!: UIOpacity

    @property({ type: UITransform })
    public uiTransform!: UITransform

    private readonly signalController = new S.SignalController()

    public onLoad() {
        this.defaultBackground = this.background.spriteFrame!
        this.defaultRank = this.rankSprite.spriteFrame!

        this.defaultScoreBackground = this.scoreBackground.spriteFrame!
        this.defaultAvatar = this.avatar.spriteFrame!

        this.validateRequiredComponents()
    }

    private validateRequiredComponents(): void {
        ComponentUtils.lazyValidateProperties(this)
    }

    @ComponentUtils.onlyRunInLifeCycle
    public updateInfo(payload: LeaderConfig, maxRank?: number): void {
        this.updateBackground(payload)
        this.updateRank(payload, maxRank)
        this.updatePhoto(payload)
        this.updateUsername(payload)
        this.updateScore(payload)
    }

    private updateBackground(payload: LeaderConfig): void {
        this.background.spriteFrame = payload.isHighlight ? this.highlightBackground : this.defaultBackground
    }

    public updateRank(payload: Pick<LeaderConfig, 'rank' | 'isHighlight'>, maxRank?: number): void {
        const rank = payload.rank

        this.rankLabel.string = maxRank && rank > maxRank ? `${maxRank}+` : rank.toString()
        this.rankSprite.spriteFrame = this.defaultRank

        let color = new Color(LeaderboardColor.DEFAULT_LABEL_OUTLINE)

        if (payload.isHighlight) {
            this.rankSprite.spriteFrame = this.highlightRank
            color = new Color(LeaderboardColor.HIGHLIGHT_LABEL_OUTLINE)
        }

        switch (rank) {
            case 1:
                this.rankSprite.spriteFrame = this.goldRank
                color = new Color(LeaderboardColor.GOLD_RANK_OUTLINE)

                break
            case 2:
                this.rankSprite.spriteFrame = this.silverRank
                color = new Color(LeaderboardColor.SILVER_RANK_OUTLINE)

                break
            case 3:
                this.rankSprite.spriteFrame = this.bronzeRank
                color = new Color(LeaderboardColor.BRONZE_RANK_OUTLINE)

                break
        }

        this.rankLabel.shadowColor = color
        this.rankLabel.outlineColor = color
    }

    @ComponentUtils.onlyRunInLifeCycleAsync
    public async updatePhoto(payload: Pick<LeaderConfig, 'photo' | 'playerId'>): Promise<void> {
        const signal = this.signalController.renew()

        if (!this.avatar) return
        const { playerId, photo } = payload

        if (CacheUtils.isAvatarCached(payload.playerId, 70)) {
            const spriteFrame = await CacheUtils.getPlayerAvatar(photo, playerId, 70, signal)
            if (!spriteFrame || signal.aborted) return

            this.setPhoto(spriteFrame)
            return
        }

        this.avatar.spriteFrame = this.defaultAvatar

        //? Wait for a short time to prevent load image too fast
        await AnimUtils.waitTweenAsync(0.1, signal)
        if (signal.aborted) return

        const spriteFrame = await CacheUtils.getPlayerAvatar(photo, playerId, 70, signal)
        if (!spriteFrame || signal.aborted) return

        this.setPhoto(spriteFrame)
    }

    @ComponentUtils.onlyRunInLifeCycle
    private setPhoto(spriteFrame: SpriteFrame): void {
        if (!this.node.active) return

        spriteFrame.packable = true
        this.avatar.spriteFrame = spriteFrame
    }

    private updateUsername(payload: LeaderConfig): void {
        this.nameLabel.string = payload.name

        this.nameLabel.color = new Color(
            payload.isHighlight ? LeaderboardColor.HIGHLIGHT_LABEL : LeaderboardColor.DEFAULT_LABEL,
        )
    }

    public updateScore(payload: Pick<LeaderConfig, 'isHighlight' | 'score'>): void {
        const score = Math.floor(payload.score)
        this.scoreLabel.string = N.getShortNumber(score, 7)

        const label = this.scoreLabel.getComponent(Label)!

        label.outlineColor = new Color(
            payload.isHighlight ? LeaderboardColor.HIGHLIGHT_LABEL_OUTLINE : LeaderboardColor.DEFAULT_LABEL_OUTLINE,
        )

        label.shadowColor = new Color(
            payload.isHighlight ? LeaderboardColor.HIGHLIGHT_LABEL_OUTLINE : LeaderboardColor.DEFAULT_LABEL_OUTLINE,
        )

        this.scoreBackground.spriteFrame = payload.isHighlight
            ? this.highlightScoreBackground
            : this.defaultScoreBackground
    }

    public async runShowUpAnimation(signal: AnimAbortSignal, delay: number, duration = 0.3): Promise<void> {
        this.setVisible(false)

        const position = this.container.position.clone()
        this.container.position = new Vec3(position.x, position.y - 100, position.z)

        const floatTween = tween(this.container).delay(delay)
        floatTween.to(duration, { position }, { easing: 'backOut' })

        const opacityTween = tween(this.uiOpacity).delay(delay)
        opacityTween.to(
            duration,
            { opacity: 255 },
            {
                easing: 'cubicOut',
                onUpdate: () => {
                    this.nameOpacity.opacity = this.uiOpacity.opacity
                    this.scoreOpacity.opacity = this.uiOpacity.opacity
                    this.avatarOpacity.opacity = this.uiOpacity.opacity
                },
            },
        )

        const cb = () => {
            if (!this.enabledInHierarchy) return

            this.container.position = position
            this.uiOpacity.opacity = 255
            this.avatar.node.active = true
        }

        signal.addAbortListener(cb)
        await Promise.all([AnimUtils.runTweenAsync(floatTween, signal), AnimUtils.runTweenAsync(opacityTween, signal)])
        signal.removeAbortListener(cb)
    }

    public async runWorldPositionAnimation(
        worldPosition: Vec3,
        duration: number,
        signal: AnimAbortSignal,
    ): Promise<void> {
        const positionTween = tween(this.node).to(duration, { worldPosition }, { easing: 'quadOut' })

        await AnimUtils.runTweenAsync(positionTween, signal)
    }

    public async runScaleAnimation(scale: number, duration: number, signal: AnimAbortSignal): Promise<void> {
        const scaleTween = tween(this.node).to(
            duration,
            { scale: new Vec3(scale, scale, scale) },
            { easing: 'backOut' },
        )

        await AnimUtils.runTweenAsync(scaleTween, signal)
    }

    public async runUpdateDataAnimation(
        oldRank: number,
        newRank: number,
        oldScore: number,
        newScore: number,
        duration: number,
        signal: AnimAbortSignal,
    ) {
        await AnimUtils.runOnUpdateAsync(
            this.node,
            duration,
            (ratio) => {
                const rank = Math.floor(oldRank + (newRank - oldRank) * ratio)
                const score = Math.floor(oldScore + (newScore - oldScore) * ratio)

                this.updateRank({ rank, isHighlight: true })
                this.updateScore({ score, isHighlight: true })
            },
            signal,
        )
    }

    public setVisible(visible: boolean): void {
        const opacity = visible ? 255 : 0

        this.uiOpacity.opacity = opacity
        this.nameOpacity.opacity = opacity
        this.scoreOpacity.opacity = opacity
        this.avatarOpacity.opacity = opacity
    }
}
