import AnimUtils from 'db://assets/utils/AnimUtils'
import type AnimAbortSignal from 'db://assets/utils/common/AnimAbortSignal'
import { _decorator, Node as CCNode, Component, tween, UIOpacity, Vec3 } from 'cc'

const { ccclass, disallowMultiple, property } = _decorator

@ccclass('LeaderSkeleton')
@disallowMultiple
export class LeaderSkeleton extends Component {
    private uiOpacity!: UIOpacity

    @property(CCNode)
    private container!: CCNode

    public __preload(): void {
        this.uiOpacity = this.getComponent(UIOpacity)!
        this.validateRequiredComponents()
    }

    public async hide(signal: AnimAbortSignal): Promise<void> {
        await AnimUtils.runTweenAsync(
            tween(this.uiOpacity)
                .to(0.2, {
                    opacity: 0,
                })
                .call(() => {
                    this.node.active = false
                }),
            signal,
        )
    }

    public async show(delay: number, signal: AnimAbortSignal): Promise<void> {
        this.node.active = true

        this.uiOpacity.opacity = 0

        const position = this.container.position.clone()
        this.container.position = new Vec3(position.x, position.y - 100, position.z)

        const float = tween(this.container).delay(delay)
        float.to(0.2, { position }, { easing: 'cubicOut' })

        const opacity = tween(this.uiOpacity).delay(delay)
        opacity.to(0.2, { opacity: 255 }, { easing: 'cubicOut' })

        const cb = () => {
            this.container.position = position
            this.uiOpacity.opacity = 255
        }

        signal.addAbortListener(cb)
        await Promise.all([AnimUtils.runTweenAsync(float, signal), AnimUtils.runTweenAsync(opacity, signal)])
        signal.removeAbortListener(cb)
    }

    private validateRequiredComponents(): void {
        if (!this.uiOpacity) {
            throw new Error('UIOpacity component is not attached to the same node')
        }

        if (!this.container) {
            throw new Error('Container is not attached to the same node')
        }
    }
}
