import { Label, _decorator } from 'cc'
import { PanelAnimationController } from '../../../../../components/panel-animator/PanelAnimationController'
import GameplayEvents from '../../../../../gameplay/constants/GameEvents'
import ComponentUtils from '../../../../../utils/ComponentUtils'
import ThemeUtils from '../../../../../utils/ThemeUtils'
import { DashboardPage } from '../DashboardPage'
import { HomeContent } from './HomeContent'
const { ccclass, property } = _decorator

const { player, storage, monitorError } = codex
const {
    Utils: { Number: N },
} = Ludex

@ccclass('HomePage')
export class HomePage extends DashboardPage {
    private homeContent!: HomeContent

    @ComponentUtils.required()
    @property({ type: Label, group: 'Placeholders' })
    private coinLabel!: Label

    @ComponentUtils.required()
    private panelAnimationController!: PanelAnimationController

    public onLoad(): void {
        this.panelAnimationController = this.getComponent(PanelAnimationController) as PanelAnimationController
        super.onLoad()
    }

    public onEnable(): void {
        this.node.scene.on(GameplayEvents.COIN_CHANGED, this.updatePlaceholderCoins, this)
    }

    public onDisable(): void {
        this.node.scene.off(GameplayEvents.COIN_CHANGED, this.updatePlaceholderCoins, this)
    }

    private async updatePlaceholderCoins(value: CoinDataPayload): Promise<void> {
        this.coinLabel.string = N.getShortNumber(value.coins, 3)
    }

    @ComponentUtils.onlyRunInLifeCycleAsync
    public override async mount(): Promise<void> {
        await super.mount()
        this.onMount()
    }

    @ComponentUtils.onlyRunInLifeCycle
    private onMount() {
        if (!this.homeContent) {
            this.homeContent = this.container.getComponentInChildren(HomeContent)!
            if (!this.homeContent) throw new Error('HomePage: homeContent not found')
        }

        const { coins = 0 } = player.getGameData() ?? {}

        this.updatePlaceholderCoins({ coins })

        this.homeContent.updateStatus()

        this.initUnlockProgressData()
    }

    private initUnlockProgressData() {
        const shouldPlayUnlockAnimation = this.homeContent.shouldPlayUnlockAnimation()
        const currentProgress = { ...ThemeUtils.getCurrentMapProgress() }

        const dashboardStorage = storage.getStorage('dashboard')

        if (dashboardStorage === null || dashboardStorage === undefined) {
            monitorError?.sendException(new Error('DashboardScene: dashboardStorage not found'))
            if (!shouldPlayUnlockAnimation) {
                this.homeContent.setUnlockProgress(currentProgress)
            }

            return
        }

        const lastProgress = { ...dashboardStorage.lastMapPayload }

        if (shouldPlayUnlockAnimation) {
            this.homeContent.setUnlockProgress(lastProgress)
        } else {
            this.homeContent.setUnlockProgress(currentProgress)
        }
    }

    @ComponentUtils.onlyRunInLifeCycleAsync
    public override async transitionIn(): Promise<void> {
        this.homeContent.transitionIn()
        await super.transitionIn()
        await this.panelAnimationController.showUI()
    }

    @ComponentUtils.onlyRunInLifeCycleAsync
    public override async transitionOut(): Promise<void> {
        this.homeContent.transitionOut()
        await super.transitionOut()
        await this.panelAnimationController.hideUI()
    }

    public hasLoadedPage(): boolean {
        return true
    }
}
