import { CCString, Component, Label, _decorator } from 'cc'
const { ccclass, property } = _decorator

@ccclass('EventBadge')
export class EventBadge extends Component {
    @property(Label)
    private label!: Label

    @property(CCString)
    public get text(): string {
        return this.label.string
    }

    public set text(value: string) {
        this.label.string = value
    }

    public onLoad(): void {
        this.validateRequiredComponents()
    }

    private validateRequiredComponents(): void {
        if (!this.label) {
            throw new Error('EventBadge: Missing required component Label')
        }
    }
}
