const {
    Plugins: { Analytics: { Events: AnalyticsEvents } = {} },
} = Ludex

const { analytics } = codex

const withItemAnalytics =
    <F extends (...args: any[]) => any>(item: ItemKey, fn: F) =>
    (...args: Parameters<F>): ReturnType<F> => {
        if (AnalyticsEvents) {
            analytics.event(AnalyticsEvents.USE_ITEM, {
                item_name: item,
            })
        }

        return fn(...args)
    }

export default withItemAnalytics
