import { _decorator, Component, Node, tween, UIOpacity, Vec3 } from 'cc'
import { Localize } from '../../../../../../components/text/Localize'
import AnimUtils from '../../../../../../utils/AnimUtils'
import ComponentUtils from '../../../../../../utils/ComponentUtils'
import MatchUtils from '../../../../../../utils/MatchUtils'
import type { StatusConfig } from '../../GameSceneLayout'
import { LeaderRail } from './LeaderRail'
import { TournamentProgressBar } from './TournamentProgressBar'

const { ccclass, property } = _decorator

const {
    Utils: { Array: A, Signal: S, Valid: V },
    Match: { Modes },
} = Ludex

type Leader = {
    rank: number
    score: number
}

type Signal = Ludex.Utils.Signal.Signal

@ccclass('TournamentRail')
export class TournamentRail extends Component {
    @property(TournamentProgressBar)
    private progressBar!: TournamentProgressBar

    @property(LeaderRail)
    private playerLeader!: LeaderRail

    @property(Node)
    private flag!: Node

    @property(Node)
    private sword!: Node

    @property(Localize)
    private currentRankLocalizer!: Localize

    @property(Localize)
    private nextRankLocalizer!: Localize

    @property(Node)
    private devTools!: Node

    @property(UIOpacity)
    private readonly challengerJoinedUiOpacity!: UIOpacity
    private challengerJoinedY: number = 0

    private leaderData: Leader[] = []

    private uiOpacity!: UIOpacity

    private nextLeaderIndex: number = 0

    private score: number = 0

    private minScore: number = 0

    private maxScore: number = 0

    private signal = new S.SignalController()

    protected __preload() {
        this.uiOpacity = this.getComponent(UIOpacity)!
        this.playerLeader.updatePhoto()
        this.devTools.active = V.isDebugger()
        this.challengerJoinedY = this.challengerJoinedUiOpacity.node.position.y
    }

    @ComponentUtils.onlyRunInLifeCycleAsync
    public async updateStatus(config: StatusConfig): Promise<void> {
        if (MatchUtils.getMatchMode() !== Modes.TOURNAMENT) {
            this.uiOpacity.opacity = 0
            this.playerLeader.cleanupOverlay()
            return
        }

        this.uiOpacity.opacity = 255
        this.playerLeader.updatePhoto()

        const { leaderData, score } = config
        if (!leaderData || score === undefined) return

        const signal = this.signal.renew()

        this.score = score
        this.nextLeaderIndex = this.getScoreReachedLeaderIndex(this.score, leaderData)
        this.syncRail(leaderData, signal, true)
    }

    private async syncRail(leaderData: Leader[], signal: Signal, immediately: boolean = false): Promise<void> {
        const minLeaderData = leaderData[this.nextLeaderIndex - 1]
        const maxLeaderData = leaderData[this.nextLeaderIndex]

        const minRank = minLeaderData ? minLeaderData.rank : maxLeaderData ? maxLeaderData.rank + 1 : 1
        this.setRankLabel(minRank, this.currentRankLocalizer)

        const reachMaxLeader = !maxLeaderData

        this.nextRankLocalizer.node.active = !reachMaxLeader
        this.runActiveAnimation(this.sword, !reachMaxLeader, signal, immediately)
        this.playerLeader.setCrown(reachMaxLeader)
        this.playerLeader.updateScore(this.score, signal, immediately)

        const duration = immediately ? 0 : undefined

        if (!reachMaxLeader) {
            this.playerLeader.stopSparkParticle()

            this.setRankLabel(maxLeaderData.rank, this.nextRankLocalizer)
            this.minScore = minLeaderData ? minLeaderData.score : 0
            this.maxScore = maxLeaderData.score

            let progress = (this.score - this.minScore) / (this.maxScore - this.minScore)
            progress = 0.8 * progress + 0.1

            if (progress < this.progressBar.getProgress()) this.showScoreUpdatedNotice(signal)
            await this.progressBar.updateProgress(progress, signal, duration)
        } else {
            this.playerLeader.playSparkParticle()
            await this.progressBar.updateProgress(1, signal, duration)
        }
    }

    public async handleScoreUpdated(config: StatusConfig): Promise<void> {
        const { score, leaderData } = config
        if (score === undefined || !leaderData) return

        this.leaderData = leaderData

        const signal = this.signal.renew()

        this.score = score

        const reachedIndex = this.getScoreReachedLeaderIndex(score, leaderData)
        if (reachedIndex > this.nextLeaderIndex) {
            this.nextLeaderIndex = reachedIndex

            let duration = (1 - this.progressBar.getProgress()) / 1.5
            duration = Math.min(duration, 0.5)

            await this.progressBar.updateProgress(1, signal, duration, 'cubicIn')
            this.runBubbleAnimation(this.sword, signal)
            await this.progressBar.updateProgress(0, signal, 0)
            this.runBubbleAnimation(this.flag, signal)
        } else if (reachedIndex < this.nextLeaderIndex) {
            this.nextLeaderIndex = reachedIndex

            let duration = this.progressBar.getProgress() / 1.5
            duration = Math.min(duration, 0.5)

            await this.progressBar.updateProgress(0, signal, duration, 'cubicIn')
            this.runBubbleAnimation(this.flag, signal)
            await this.progressBar.updateProgress(1, signal, 0)
            this.runBubbleAnimation(this.sword, signal)
        }

        this.syncRail(leaderData, signal)
    }

    private async runBubbleAnimation(node: Node, signal: Signal): Promise<void> {
        node.scale = Vec3.ONE

        const scaleTween = tween(node)
            .to(0.1, { scale: new Vec3(1.2, 1.2, 1.2) })
            .to(0.2, { scale: Vec3.ONE }, { easing: 'backOut' })

        await AnimUtils.runTweenAsync(scaleTween, signal)
        node.scale = Vec3.ONE
    }

    private async runActiveAnimation(
        node: Node,
        active: boolean,
        signal: Signal,
        immediately: boolean = false,
    ): Promise<void> {
        const startScale = active ? Vec3.ZERO : Vec3.ONE
        const targetScale = active ? Vec3.ONE : Vec3.ZERO

        if (immediately) {
            node.scale = targetScale
            return
        }

        if (node.scale.equals(targetScale)) return

        const easing = active ? 'backOut' : 'quadOut'

        node.scale = startScale

        const scaleTween = tween(node)
            .to(0.1, { scale: new Vec3(1.2, 1.2, 1.2) })
            .to(0.2, { scale: targetScale }, { easing })

        await AnimUtils.runTweenAsync(scaleTween, signal)

        node.scale = targetScale
    }

    private getScoreReachedLeaderIndex(score: number, leaderData: Leader[]): number {
        const index = A.searchIndex(leaderData, (leader) => leader.score >= score)
        if (index === -1) return leaderData.length
        return index
    }

    private setRankLabel(rank: number, label: Localize): void {
        if (rank === 1) {
            label.key = 'status.first'
            return
        }

        if (rank === 2) {
            label.key = 'status.second'
            return
        }

        if (rank === 3) {
            label.key = 'status.third'
            return
        }

        label.key = 'status.nth'
        label.setParams([rank.toString()])
    }

    //? DEV TOOLS
    public handleDecreaseRank(): void {
        for (const leader of this.leaderData) {
            leader.score += this.getMedianScore() * 2
        }
        this.handleScoreUpdated({ score: this.score, leaderData: this.leaderData })
    }

    public handleIncreaseRank(): void {
        for (const leader of this.leaderData) {
            leader.score -= this.getMedianScore() * 2
        }
        this.handleScoreUpdated({ score: this.score, leaderData: this.leaderData })
    }

    private getMedianScore(): number {
        return (this.leaderData[this.leaderData.length - 1]!.score - this.leaderData[0]!.score) / this.leaderData.length
    }

    private async showScoreUpdatedNotice(signal: Signal) {
        this.challengerJoinedUiOpacity.node.active = true
        this.challengerJoinedUiOpacity.opacity = 0

        const reset = () => {
            this.challengerJoinedUiOpacity.node.active = false
            this.challengerJoinedUiOpacity.opacity = 0
        }

        signal.once(reset)

        const startPosition = new Vec3(
            this.challengerJoinedUiOpacity.node.position.x,
            this.challengerJoinedY - 10,
            this.challengerJoinedUiOpacity.node.position.z,
        )

        const midPosition = new Vec3(
            this.challengerJoinedUiOpacity.node.position.x,
            this.challengerJoinedY,
            this.challengerJoinedUiOpacity.node.position.z,
        )

        const endPosition = new Vec3(
            this.challengerJoinedUiOpacity.node.position.x,
            this.challengerJoinedY + 10,
            this.challengerJoinedUiOpacity.node.position.z,
        )

        this.challengerJoinedUiOpacity.node.setPosition(startPosition)

        const fadeIn = AnimUtils.runTweenAsync(tween(this.challengerJoinedUiOpacity).to(0.5, { opacity: 255 }), signal)

        const moveIn = AnimUtils.runTweenAsync(
            tween(this.challengerJoinedUiOpacity.node).to(
                0.5,
                {
                    position: midPosition,
                },
                {
                    easing: 'cubicOut',
                },
            ),
            signal,
        )

        const wait = AnimUtils.waitTweenAsync(1, signal)

        await Promise.all([fadeIn, moveIn, wait])

        const moveOut = AnimUtils.runTweenAsync(
            tween(this.challengerJoinedUiOpacity.node).to(
                0.5,
                {
                    position: endPosition,
                },
                {
                    easing: 'cubicIn',
                },
            ),
            signal,
        )

        const fadeOut = AnimUtils.runTweenAsync(tween(this.challengerJoinedUiOpacity).to(0.5, { opacity: 0 }), signal)

        await Promise.all([moveOut, fadeOut])

        signal.off(reset)
    }
}
