import {
    _decorator,
    CCInteger,
    Component,
    Enum,
    easing,
    game,
    Sprite,
    type SpriteFrame,
    tween,
    UIOpacity,
    UITransform,
    Widget,
} from 'cc'
import DashboardEvents from '../../constants/DashboardEvents'
import DashboardScreens from '../../constants/DashboardScreens'
import GameEvents from '../../constants/GameEvents'
import GameWorld from '../../constants/GameWorld'
import AnimUtils from '../../utils/AnimUtils'
import ComponentUtils from '../../utils/ComponentUtils'
import AnimAbortSignal from '../../utils/common/AnimAbortSignal'
import ResourceUtils from '../../utils/ResourceUtils'

const { ccclass, property } = _decorator

const { monitorError } = codex
const {
    Utils: { Number: N },
} = Ludex

const { Layout } = GameWorld

@ccclass('MultiBackground')
export class MultiBackground extends Component {
    @ComponentUtils.required()
    @property({ type: UIOpacity })
    private readonly fallbackBackground!: UIOpacity

    @ComponentUtils.required()
    @property({ type: Sprite })
    private readonly background1!: Sprite

    @ComponentUtils.required()
    @property({ type: Sprite })
    private readonly background2!: Sprite

    @ComponentUtils.required()
    @property({ type: UIOpacity })
    private readonly background1UiOpacity!: UIOpacity

    @ComponentUtils.required()
    @property({ type: UIOpacity })
    private readonly background2UiOpacity!: UIOpacity

    @ComponentUtils.required()
    @property({ type: UITransform })
    private readonly screenUiTransform!: UITransform

    @ComponentUtils.required()
    @property({ type: UITransform })
    private readonly background1UiTransform!: UITransform

    @ComponentUtils.required()
    @property({ type: UITransform })
    private readonly background2UiTransform!: UITransform

    @property(CCInteger)
    public backgroundNudgeAmount = 50

    @ComponentUtils.required()
    @property(Widget)
    public nudgeableWidget!: Widget

    @property({ type: Enum(DashboardScreens) })
    public pageOrder: DashboardScreens[] = []

    @property({ type: Enum(DashboardScreens) })
    public homePage: DashboardScreens = DashboardScreens.home

    private currentPage: DashboardScreens = DashboardScreens.home
    private currentThemeId?: ThemeKeys

    private currentBackground!: Sprite
    private currentBackgroundUiOpacity!: UIOpacity
    private nextBackground!: Sprite
    private nextBackgroundUiOpacity!: UIOpacity

    private tweenSignal: AnimAbortSignal | undefined

    protected __preload(): void {
        this.currentBackground = this.background1
        this.nextBackground = this.background2

        this.currentBackgroundUiOpacity = this.background1UiOpacity
        this.nextBackgroundUiOpacity = this.background2UiOpacity
    }

    public onLoad(): void {
        ComponentUtils.validateRequiredProperties(this, MultiBackground)
        this.listenToNavigationEvents()
    }

    public onDestroy() {
        this.unlistenToNavigationEvents()
    }

    private listenToNavigationEvents() {
        this.node.scene.on(DashboardEvents.REQUEST_NAVIGATION_ANIMATION, this.tryNudgeBackground)
        // @ts-expect-error - This event is custom event
        game.on(GameEvents.ORIENTATION_CHANGED, this.handleOrientationChanged, this)
    }

    private unlistenToNavigationEvents() {
        this.node.scene.off(DashboardEvents.REQUEST_NAVIGATION_ANIMATION, this.tryNudgeBackground)
        game.off(GameEvents.ORIENTATION_CHANGED, this.handleOrientationChanged, this)
    }

    private readonly tryNudgeBackground = ({ to }: { from: DashboardScreens | null; to: DashboardScreens }) => {
        this.currentPage = to
        const direction = this.getDirection(this.homePage, to)

        const directionSign = N.sign(direction)
        const directionAmount = Math.abs(direction)

        const nudgeAmount = this.backgroundNudgeAmount * directionAmount

        const backgroundWidth = Math.min(this.background1UiTransform.width, this.background2UiTransform.width)

        const spareDistance = Math.max(0, (backgroundWidth - this.screenUiTransform.width) / 2)
        const nudgeableAmount = Math.min(spareDistance, nudgeAmount) * directionSign

        tween(this.nudgeableWidget)
            .to(0.5, { horizontalCenter: nudgeableAmount }, { easing: easing.cubicInOut })
            .start()
    }

    private getDirection(from: DashboardScreens, to: DashboardScreens): number {
        const fromIndex = this.pageOrder.indexOf(from)
        const toIndex = this.pageOrder.indexOf(to)

        if (fromIndex === -1 || toIndex === -1) {
            monitorError?.sendException(new Error('MultiBackground.getDirection: fromIndex or toIndex is -1'))

            return 0
        }

        return fromIndex - toIndex
    }

    @ComponentUtils.onlyRunInLifeCycleAsync
    public async setCurrentTheme(themeId: ThemeKeys, signal?: AnimAbortSignal) {
        if (this.currentThemeId === themeId) return
        this.currentThemeId = themeId

        this.tweenSignal?.abort()
        const ourSignal = new AnimAbortSignal()
        signal?.chain(ourSignal)
        this.tweenSignal = ourSignal

        const sprite = await ResourceUtils.loadThemeAsync(themeId)

        if (!sprite) {
            monitorError?.sendException(
                new Error(`MultiBackground.setCurrentTheme: Failed to load theme ${themeId}'s sprite.`),
            )

            return
        }

        if (ourSignal.aborted) return
        await this.updateBackground(sprite, ourSignal)
    }

    @ComponentUtils.onlyRunInLifeCycleAsync
    private async updateBackground(sprite: SpriteFrame, signal?: AnimAbortSignal) {
        // ? Do not pack the sprite to avoid fusing the dynamic texture
        // ! This is causing an error where the sprite is not reloaded when its dynamic texture is updated
        sprite.packable = false

        const currBackground = this.currentBackground
        const nextBackground = this.nextBackground

        const currBackgroundUiOpacity = this.currentBackgroundUiOpacity
        const nextBackgroundUiOpacity = this.nextBackgroundUiOpacity

        const fallbackBackground = this.fallbackBackground

        nextBackground.spriteFrame = sprite
        nextBackground.node.setSiblingIndex(1)
        nextBackgroundUiOpacity.opacity = 0
        fallbackBackground.opacity = 255

        await AnimUtils.runTweenAsync(
            tween(nextBackgroundUiOpacity).to(0.25, {
                opacity: 255,
            }),
            signal,
        )

        currBackgroundUiOpacity.opacity = 0
        fallbackBackground.opacity = 0

        this.currentBackground = nextBackground
        this.nextBackground = currBackground

        this.currentBackgroundUiOpacity =
            nextBackground === this.background1 ? this.background1UiOpacity : this.background2UiOpacity
        this.nextBackgroundUiOpacity =
            currBackground === this.background1 ? this.background1UiOpacity : this.background2UiOpacity
    }

    private handleOrientationChanged(layout: string): void {
        if (layout === Layout.Portrait) {
            const direction = this.getDirection(this.homePage, this.currentPage)

            const directionSign = N.sign(direction)
            const directionAmount = Math.abs(direction)

            const nudgeAmount = this.backgroundNudgeAmount * directionAmount

            const backgroundWidth = Math.min(this.background1UiTransform.width, this.background2UiTransform.width)

            const spareDistance = Math.max(0, (backgroundWidth - this.screenUiTransform.width) / 2)
            const nudgeableAmount = Math.min(spareDistance, nudgeAmount) * directionSign

            this.nudgeableWidget.horizontalCenter = nudgeableAmount
        } else if (layout === Layout.Landscape) {
            this.nudgeableWidget.horizontalCenter = 0
        }
    }
}
