import { Button, Component, Label, Prefab, Sprite, SpriteFrame, _decorator, find } from 'cc'
import { Sound } from '../../components/audio/Sound'
import { Localize } from '../../components/text/Localize'
import CoinData from '../../constants/CoinData'
import GameplayEvents from '../../gameplay/constants/GameEvents'
import ItemKeys, { type ItemKey } from '../../gameplay/constants/ItemKeys'
import AdsUtils from '../../utils/AdsUtils'
import ComponentUtils from '../../utils/ComponentUtils'
import { BaseScreen } from '../BaseScreen'
const { ccclass, property } = _decorator

const { event, player, analytics, language } = codex

const {
    Events,
    Utils: { Array: A },
    Plugins: {
        Ads: { Types: AdsTypes } = {},
        Analytics: { Events: AnalyticsEvents } = {},
    },
} = Ludex

const ValidItemKeys = Object.values(ItemKeys)

@ccclass('GetItemScreen')
export class GetItemScreen extends Component {
    private screen!: BaseScreen

    private data!: Required<GetItemScreenData>

    @property({ type: Prefab, group: 'Item' })
    public itemFxPrefab!: Prefab

    @property({ type: Sprite, group: 'Item' })
    public itemSprite!: Sprite

    @property({ type: SpriteFrame, group: 'Item' })
    public hintSpriteFrame!: SpriteFrame

    @property({ type: SpriteFrame, group: 'Item' })
    public stormSpriteFrame!: SpriteFrame

    @property({ type: SpriteFrame, group: 'Item' })
    public thunderSpriteFrame!: SpriteFrame

    @property({ type: SpriteFrame, group: 'Item' })
    public pliersSpriteFrame!: SpriteFrame

    @property({ type: Label, group: 'Title' })
    public titleLabel!: Label

    @property({ type: Localize, group: 'Title' })
    public titleLocalizer!: Localize

    @property({ type: Label, group: 'Description' })
    public descriptionLabel!: Label

    @property({ type: Localize, group: 'Description' })
    public descriptionLocalizer!: Localize

    @property(Label)
    public coinLabel!: Label

    @property({ type: Button, group: 'Buttons' })
    public closeButtonComponent!: Button

    @property({ type: Button, group: 'Buttons' })
    public buyItemButtonComponent!: Button

    @property({ type: Button, group: 'Buttons' })
    public getNowButtonComponent!: Button

    public __preload() {
        this.enabled = false
        this.screen = this.getComponent(BaseScreen)!
        this.validateRequiredComponents()
        this.listenScreenEvents()

        if (AdsTypes) {
            AdsUtils.preloadAdsByType(AdsTypes.REWARDED_VIDEO)
        }
    }

    public onDestroy(): void {
        this.unListenScreenEvents()
    }

    private validateRequiredComponents() {
        ComponentUtils.lazyValidateProperties(this)
    }

    private listenScreenEvents() {
        // ? Here's a demo of all the events a screen has
        event.on(Events.OPENING_SCREEN, this.handleOpeningScreen)
        event.on(Events.OPENED_SCREEN, this.handleOpenedScreen)
    }

    private unListenScreenEvents() {
        event.off(Events.OPENING_SCREEN, this.handleOpeningScreen)
        event.off(Events.OPENED_SCREEN, this.handleOpenedScreen)
    }

    private handleOpeningScreen = (payload: SwitchScreenPayload) => {
        if (!this.screen) {
            throw new Error('GetItemScreen: screen not found')
        }

        if (!this.validateEventPayload(payload)) return
        if (!this.validateEventPayloadData(payload.screenData)) return

        this.setInteractableButtons(true)

        this.data = payload.screenData
        this.updateStatus(payload.screenData)
    }

    private handleOpenedScreen = (payload: SwitchScreenPayload) => {
        if (!this.validateEventPayload(payload)) return
        this.open()
    }

    private validateEventPayload(payload: SwitchScreenPayload) {
        const { screenName } = payload ?? {}
        if (screenName !== this.node.name) return false

        return true
    }

    private validateEventPayloadData(data?: ScreenData): data is Required<GetItemScreenData> {
        if (!data) return false

        if (!('item' in data) || !A.has(ValidItemKeys, data.item)) return false

        if (!('quantity' in data) || typeof data.quantity !== 'number') return false

        if (
            'localizedMessageKey' in data &&
            data.localizedMessageKey !== undefined &&
            !language.hasTextKey(data.localizedMessageKey)
        )
            return false

        return true
    }

    public open() {
        this.node.scene.emit(GameplayEvents.GAME_PAUSE)
    }

    public close() {
        this.setInteractableButtons(false)

        event.emit(Events.CLOSE_SCREEN, { screenName: this.node.name })
        this.node.scene.emit(GameplayEvents.GAME_RESUME)
    }

    private updateStatus(data: Required<GetItemScreenData>): void {
        this.updateItemIcon(data.item)
        this.updateTitle(data.item)

        if (data.localizedMessageKey) {
            this.updateDescription(data.localizedMessageKey)
        }

        this.updateButtons(data.item)
    }

    private updateItemIcon(itemType: ItemKey): void {
        switch (itemType) {
            case ItemKeys.HINT:
                this.itemSprite.spriteFrame = this.hintSpriteFrame
                break
            case ItemKeys.STORM:
                this.itemSprite.spriteFrame = this.stormSpriteFrame
                break
            case ItemKeys.THUNDER:
                this.itemSprite.spriteFrame = this.thunderSpriteFrame
                break
            case ItemKeys.PLIERS:
                this.itemSprite.spriteFrame = this.pliersSpriteFrame
                break
        }
    }

    private getItemLocalizedTitleKey(itemType: ItemKey): Ludex.Plugins.Language.TextKey {
        switch (itemType) {
            case ItemKeys.HINT:
                return 'item.hint'
            case ItemKeys.STORM:
                return 'item.storm'
            case ItemKeys.THUNDER:
                return 'item.thunder'
            case ItemKeys.PLIERS:
                return 'item.pliers'
        }
    }

    private updateTitle(itemType: ItemKey): void {
        this.titleLocalizer.key = this.getItemLocalizedTitleKey(itemType)
    }

    private updateDescription(key: Ludex.Plugins.Language.TextKey): void {
        this.descriptionLocalizer.key = key
    }

    private updateButtons(itemType: ItemKey): void {
        const canBuyItem = this.canBuyItem(itemType)

        this.buyItemButtonComponent.node.active = canBuyItem
        this.getNowButtonComponent.node.active = !canBuyItem

        if (canBuyItem) {
            this.coinLabel.string = `${CoinData.ITEMS[itemType] * 3}`
        }
    }

    private canBuyItem(itemType: ItemKey): boolean {
        const gameData = player.getGameData()
        if (!gameData) return false

        const { coins } = gameData

        return coins >= CoinData.ITEMS[itemType] * 3
    }

    public async handleAddItem(): Promise<void> {
        if (!this.data.item) return

        const quantity = 3

        if (AnalyticsEvents) {
            analytics.event(AnalyticsEvents.EARN_ITEM, {
                item_name: this.data.item,
                value: quantity,
            })
        }

        this.node.scene.emit(GameplayEvents.ADD_ITEM, {
            animate: {
                startWorldPosition: this.itemSprite.node.worldPosition.clone(),
            },
            item: this.data.item,
            quantity,
        } as AddItemPayload)
    }

    // ? Callback for click event of GetNowButton
    public async handleGetNowButtonClicked(): Promise<void> {
        this.stopAllSounds()

        const success = await AdsUtils.showRewardVideoAdAsync()
        if (!success) return

        this.handleAddItem()
        this.close()
    }

    public async handleBuyItemButtonClicked(): Promise<void> {
        this.node.scene.emit(GameplayEvents.REMOVE_COINS, {
            coins: CoinData.ITEMS[this.data.item] * 3,
            collectionAnimation: {
                animateTextOnly: true,
            },
        } as CoinDataPayload)

        this.handleAddItem()
        this.close()
    }

    private stopAllSounds(): void {
        find('Sound')?.getComponent(Sound)?.stopAll()
    }

    private setInteractableButtons(interactable: boolean) {
        this.closeButtonComponent.interactable = interactable
        this.buyItemButtonComponent.interactable = interactable
        this.getNowButtonComponent.interactable = interactable
        this.screen.backgroundMaskButton.interactable = interactable
    }
}
