import { Component, _decorator, math, tween } from 'cc'
import GameplayEvents from 'db://assets/gameplay/constants/GameEvents'
import AnimUtils from '../../utils/AnimUtils'

const { ccclass, disallowMultiple } = _decorator

@ccclass('ClockManager')
@disallowMultiple
export class ClockManager extends Component {
    private time = 0
    private remaining = 0
    private running = false

    private halfTime = false

    protected onLoad(): void {
        this.node.scene.on(GameplayEvents.RESCUE_START, this.refillTime, this)
    }

    public init() {
        this.running = false
        this.halfTime = false
    }

    public setTime(time: number) {
        this.time = time
        this.remaining = time

        this.node.scene.emit(GameplayEvents.TIME_UPDATED, this.remaining)
    }

    public getRemainingTime(): number {
        return this.remaining
    }

    public startCountdown() {
        this.running = true
        this.remaining = this.time
    }

    public pause() {
        this.running = false
    }

    public resume() {
        this.running = true
    }

    private endCountDown() {
        this.running = false
        this.remaining = 0
        this.node.scene.emit(GameplayEvents.TIME_OUT)
    }

    public update(deltaTime: number) {
        if (!this.running) return

        if (this.remaining <= 0) {
            this.endCountDown()
            return
        }

        this.remaining -= deltaTime

        if (!this.halfTime && this.remaining <= this.time / 2) {
            this.halfTime = true
            this.node.scene.emit(GameplayEvents.HALF_TIME)
        }

        if (this.remaining >= 0) {
            this.node.scene.emit(GameplayEvents.TIME_UPDATED, this.remaining)
        }
    }

    private async refillTime(): Promise<void> {
        const addedTime = 120
        const counterTween = tween(this.node).to(
            3,
            {},
            {
                onUpdate: (_, ratio) => {
                    if (!ratio) return
                    const current = math.lerp(0, addedTime, ratio)

                    this.setTime(current)
                },
            },
        )

        await AnimUtils.runTweenAsync(counterTween)
        this.setTime(addedTime)
        this.node.scene.emit(GameplayEvents.RESCUE_FINISHED)
    }
}
