import { Component, _decorator } from 'cc'
import type { Gameplay } from '../Gameplay'
import type BaseState from '../states/BaseState'
import FailState from '../states/FailState'
import FinishState from '../states/FinishState'
import InitializeState from '../states/InitializeState'
import PlayingState from '../states/PlayingState'
import ReadyState from '../states/ReadyState'
const { ccclass } = _decorator

// ? "isX" methods only used for specific state checking, don't use it for other purpose. ex: check when switch state

@ccclass('StateManager')
export class StateManager extends Component {
    private state!: BaseState
    private gameplay!: Gameplay

    public getGameplay(): Gameplay {
        return this.gameplay
    }

    public init(gameplay: Gameplay): void {
        this.gameplay = gameplay
        this.state = new InitializeState(this)
    }

    public change(state: BaseState): void {
        this.state = state
    }

    public initialize(): void {
        this.state.initialize()
    }

    /**
     * @public
     * @internal Demo state checking
     */
    public isInitialize(): boolean {
        return this.state instanceof InitializeState
    }

    public ready(): void {
        this.state.ready()
    }

    /**
     * @public
     * @internal Demo state checking
     */
    public isReady(): boolean {
        return this.state instanceof ReadyState
    }

    public playing(): void {
        this.state.playing()
    }

    public isPlaying(): boolean {
        return this.state instanceof PlayingState
    }

    public fail(failKey?: FailKey): void {
        this.state.fail(failKey)
    }

    public isFail(): boolean {
        return this.state instanceof FailState
    }

    public finish(): void {
        this.state.finish()
    }

    /**
     * @public
     * @internal Demo state checking
     */
    public isFinished(): boolean {
        return this.state instanceof FinishState
    }

    public update(deltaTime: number) {
        if (!this.state) return
        this.state.update(deltaTime)
    }

    public createInitializeState(...args: ConstructorParameters<typeof InitializeState>): InitializeState {
        return new InitializeState(...args)
    }

    public createReadyState(...args: ConstructorParameters<typeof ReadyState>): ReadyState {
        return new ReadyState(...args)
    }

    public createPlayingState(...args: ConstructorParameters<typeof PlayingState>): PlayingState {
        return new PlayingState(...args)
    }

    public createFailState(...args: ConstructorParameters<typeof FailState>): FailState {
        return new FailState(...args)
    }

    public createFinishState(...args: ConstructorParameters<typeof FinishState>): FinishState {
        return new FinishState(...args)
    }
}
