import SortNodeUtils from 'db://assets/utils/SortNodeUtils'
import { _decorator, CCBoolean, Component, Label, math, type Node, type Tween, tween, UITransform } from 'cc'
import AnimUtils from '../../../../../../utils/AnimUtils'
import ComponentUtils from '../../../../../../utils/ComponentUtils'

const { ccclass, property, disallowMultiple } = _decorator

@ccclass('ScoreBlock')
@disallowMultiple
export class ScoreBlock extends Component {
    @property(Label)
    protected scoreText!: Label

    @property(CCBoolean)
    protected isResizeWidth: boolean = true

    @property({
        visible: function (this: ScoreBlock) {
            return this.isResizeWidth
        },
    })
    protected maxScoreWidth: number = 0

    protected panelWidthDiff: number = 0

    protected score!: number

    protected scoreUITransform!: UITransform

    protected uiTransform!: UITransform

    protected uiTween!: Tween<Node>

    public onLoad(): void {
        this.uiTransform = this.node.getComponent(UITransform)!
        this.scoreUITransform = this.scoreText.node.getComponent(UITransform)!
        this.panelWidthDiff = this.uiTransform.width - this.maxScoreWidth

        this.validateRequiredComponents()
    }

    private validateRequiredComponents(): void {
        ComponentUtils.lazyValidateProperties(this)
    }

    public async updateScore(score: number): Promise<void> {
        const addScore = score - this.score

        const addScoreTween = tween(this.node).to(
            0.1,
            {},
            {
                onUpdate: (_, ratio) => {
                    if (!ratio) return
                    const value = Math.floor(addScore * ratio) + this.score
                    this.scoreText.string = value.toString()
                    this.resizeWidth()
                },
            },
        )

        await AnimUtils.runTweenAsync(addScoreTween)
        this.setScore(score)
        await this.resizeWidth()
    }

    @ComponentUtils.onlyRunInLifeCycle
    public setScore(score?: number): void {
        const correctScore = score ?? 0
        this.score = correctScore
        this.scoreText.string = correctScore.toString()
    }

    protected async resizeWidth(): Promise<void> {
        if (!this.isResizeWidth) return

        const currentWidth = this.uiTransform.width
        const nextWidth =
            (this.scoreUITransform.width > this.maxScoreWidth ? this.scoreUITransform.width : this.maxScoreWidth) +
            this.panelWidthDiff

        if (nextWidth <= currentWidth && this.score !== 0) return

        if (this.uiTween) this.uiTween.stop()
        this.uiTween = tween(this.node).to(
            0.05,
            {},
            {
                easing: 'sineOut',
                onUpdate: (_target, ratio) => {
                    if (!ratio) return
                    this.uiTransform.width = math.lerp(currentWidth, nextWidth, ratio)
                },
            },
        )
        await AnimUtils.runTweenAsync(this.uiTween)
        this.uiTransform.width = nextWidth

        SortNodeUtils.setNodeToLayer(this.scoreText.node, 'GameUnpackLabel')
    }
}
