import { CCInteger, Node as CCNode, Component, type Prefab, UIOpacity, Widget, _decorator, instantiate } from 'cc'
import AnalyticsUtils from '../../../../utils/AnalyticsUtils'
import AnimUtils from '../../../../utils/AnimUtils'
import ComponentUtils from '../../../../utils/ComponentUtils'
const { ccclass, property, disallowMultiple, requireComponent } = _decorator

@ccclass('DashboardPage')
@disallowMultiple
@requireComponent(Widget)
@requireComponent(UIOpacity)
export class DashboardPage extends Component {
    @ComponentUtils.required()
    @property({
        type: CCNode,
        displayName: "Page's container",
    })
    protected container!: CCNode

    @ComponentUtils.required()
    protected containerUIOpacity!: UIOpacity

    @ComponentUtils.required()
    @property({ type: CCNode, displayName: 'Placeholder' })
    protected placeholder!: CCNode

    @ComponentUtils.required()
    protected placeholderUIOpacity!: UIOpacity

    @property({
        displayName: "Don't create prefab",
        tooltip: "Tell the component to not create the prefab even if it hasn't create yet",
    })
    public hasCreated: boolean = false

    protected pagePrefab: Prefab | null = null

    @property({
        displayName: 'Darken background',
    })
    protected darkenBackground: boolean = false

    @property({
        type: CCInteger,
        min: 0,
        max: 255,
        displayName: 'Background opacity',
        visible: function (this: DashboardPage) {
            return this.darkenBackground
        },
    })
    protected backgroundOpacity = 150

    @property({
        displayName: 'Use navbar shadow',
    })
    protected useNavbarShadow: boolean = false

    @property({
        type: CCInteger,
        min: 0,
        max: 255,
        displayName: 'Navbar shadow opacity',
        visible: function (this: DashboardPage) {
            return this.useNavbarShadow
        },
    })
    protected navbarShadowOpacity = 150

    @ComponentUtils.required()
    protected uiOpacity!: UIOpacity

    @ComponentUtils.required()
    protected widgetComponent!: Widget

    public onLoad(): void {
        this.uiOpacity = this.getComponent(UIOpacity) as UIOpacity
        this.widgetComponent = this.getComponent(Widget) as Widget
        this.containerUIOpacity = this.container.getComponent(UIOpacity) as UIOpacity
        this.placeholderUIOpacity = this.placeholder.getComponent(UIOpacity) as UIOpacity

        ComponentUtils.validateRequiredProperties(this)
    }

    public start(): void {
        this.container.active = true
        this.placeholder.active = true
    }

    public getWidget(): Widget {
        return this.widgetComponent
    }

    /**
     * Show the page (placeholder and container untouched)
     */
    public show(): void {
        this.uiOpacity.opacity = 255
    }

    /**
     * Hide the page (placeholder and container untouched)
     */
    public hide(): void {
        this.uiOpacity.opacity = 0
    }

    /**
     * This method is called when this page is the target of a navigation action.
     */
    @ComponentUtils.onlyRunInLifeCycleAsync
    public async mount(): Promise<void> {
        if (!this.hasCreated) {
            await this.create()
        }

        this.mountInLifeCycle()
    }

    @ComponentUtils.onlyRunInLifeCycle
    private mountInLifeCycle() {
        this.placeholderUIOpacity.opacity = 0
        this.containerUIOpacity.opacity = 255

        AnalyticsUtils.trackCurrentPage(this.node.name, true)
    }

    /**
     * This method is called when this page is not on the screen anymore.
     */
    public unmount(): void {
        if (this.placeholderUIOpacity.opacity === 255 && this.containerUIOpacity.opacity === 0) return

        this.placeholderUIOpacity.opacity = 255
        this.containerUIOpacity.opacity = 0
    }

    /**
     * This method is called when navigation to this page finishes.
     */
    public transitionIn(): Promise<void> {
        return Promise.resolve()
    }

    /**
     * This method is called when navigation to another page starts.
     */
    public transitionOut(): Promise<void> {
        return Promise.resolve()
    }

    public hasLoadedPage(): boolean {
        return true
    }

    public getContainer(): CCNode {
        return this.container
    }

    public getDarkenBackground(): number {
        return this.darkenBackground ? this.backgroundOpacity : 0
    }

    public getNavbarShadow(): number {
        return this.useNavbarShadow ? this.navbarShadowOpacity : 0
    }

    @ComponentUtils.onlyRunInLifeCycleAsync
    public async create(): Promise<void> {
        if (!this.pagePrefab || this.hasCreated) return

        const page = instantiate(this.pagePrefab)

        this.container.addChild(page)

        // ? Wait at least 0s for next frame
        // ? when Cocos finished instantiating the prefab
        // ? before playing animations to avoid stuttering
        await AnimUtils.waitTweenAsync(0)

        this.hasCreated = true
    }
}
