import { Component, UIOpacity, _decorator } from 'cc'
import ComponentUtils from '../../../utils/ComponentUtils'
const { ccclass, requireComponent } = _decorator

@ccclass('ViewNode')
@requireComponent(UIOpacity)
export class ViewNode extends Component {
    public order: number = 0

    private uiOpacity!: UIOpacity

    public __preload(): void {
        this.uiOpacity = this.getComponent(UIOpacity)!

        this.validateRequiredComponents()
    }

    private validateRequiredComponents(): void {
        ComponentUtils.lazyValidateProperties(this)
    }

    public setVisible(visible: boolean) {
        this.uiOpacity.opacity = visible ? 255 : 0
    }

    public isVisible(): boolean {
        return this.uiOpacity.opacity > 0
    }
}
