import SoundKeys, { CodeSoundKeys, SoundKeysMapping } from 'db://assets/constants/SoundKeys'
import AudioUtils from 'db://assets/utils/AudioUtils'
import ComponentUtils from 'db://assets/utils/ComponentUtils'
import { Animation, AnimationClip, Button, CCBoolean, Component, type EventTouch, Node, _decorator } from 'cc'
const { ccclass, property, requireComponent } = _decorator

const { analytics } = codex
const {
    Utils: { Array: A, Valid: V },
    Plugins: {
        Analytics: { Events: AnalyticsEvents } = {},
    },
} = Ludex

@ccclass('SwitchButton')
@requireComponent([Button, Animation])
export class SwitchButton extends Component {
    @property(CCBoolean)
    public enable = true

    @property(Node)
    private nodeOn!: Node

    @property(Node)
    private nodeOff!: Node

    @property(CCBoolean)
    public useSound = true

    @property({
        visible(this: SwitchButton) {
            return this.useSound
        },
        type: SoundKeys,
    })
    private soundKey = SoundKeys[CodeSoundKeys.BUTTON_CLICK]

    @property(CCBoolean)
    private useDesktopHover = true

    @property({
        visible(this: SwitchButton) {
            return this.useDesktopHover
        },
        type: AnimationClip,
    })
    private mouseEnterClip!: AnimationClip

    @property({
        visible(this: SwitchButton) {
            return this.useDesktopHover
        },
        type: AnimationClip,
    })
    private mouseLeaveClip!: AnimationClip

    private buttonComponent!: Button
    private animationComponent!: Animation

    private isMouseLeaved = false

    public __preload() {
        this.buttonComponent = this.getComponent(Button)!
        this.animationComponent = this.getComponent(Animation)!

        this.validateRequiredComponents()
    }

    public onLoad() {
        const clips = this.animationComponent.clips
            .map((clip) => clip?.name)
            .filter((clip): clip is string => clip !== null && clip !== undefined && typeof clip === 'string')

        if (this.useDesktopHover) {
            if (!A.has(clips, this.mouseEnterClip.name)) {
                this.animationComponent.addClip(this.mouseEnterClip)
            }

            if (!A.has(clips, this.mouseLeaveClip.name)) {
                this.animationComponent.addClip(this.mouseLeaveClip)
            }
        }
    }

    private validateRequiredComponents() {
        ComponentUtils.lazyValidateProperties(this)
    }

    public onEnable() {
        //? Support for window
        this.node.on(Node.EventType.MOUSE_ENTER, this.onMouseEnter)
        this.node.on(Node.EventType.MOUSE_LEAVE, this.onMouseLeave)
    }

    public onDisable() {
        //? Support for window
        this.node.off(Node.EventType.MOUSE_ENTER, this.onMouseEnter)
        this.node.off(Node.EventType.MOUSE_LEAVE, this.onMouseLeave)
    }

    private onMouseEnter = (_event: EventTouch) => {
        if (!this.useDesktopHover) return
        if (!this.buttonComponent.interactable) return

        this.isMouseLeaved = false

        this.animationComponent.play(this.mouseEnterClip.name)
    }

    private onMouseLeave = (_event: EventTouch) => {
        if (!this.useDesktopHover) return
        if (!this.buttonComponent.interactable) return
        if (this.isMouseLeaved) return

        this.isMouseLeaved = true

        this.animationComponent.play(this.mouseLeaveClip.name)
    }

    public switch(): boolean {
        if (!this.node.active) return false

        // TODO: handle event from component button
        if (AnalyticsEvents) {
            analytics.event(AnalyticsEvents.BUTTON_CLICK, {
                button_name: this.node.name,
                enable: !this.enable,
            })
        }

        if (this.useSound && V.isString(this.soundKey)) {
            AudioUtils.playSound(SoundKeysMapping[this.soundKey]!)
        }

        return this.updateStatus(!this.enable)
    }

    public setEnabled(enable: boolean): void {
        if (!this.node.active) return

        this.updateStatus(enable)
    }

    private updateStatus(enable: boolean): boolean {
        if (this.enable === enable) {
            return this.enable
        }

        this.onMouseLeave(null!)

        this.enable = enable

        this.nodeOn.active = enable
        this.nodeOff.active = !enable

        return this.enable
    }
}
