import { _decorator, Component, game, UITransform, Widget } from 'cc'
import GameEvents from '../../../../constants/GameEvents'
import GameWorld from '../../../../constants/GameWorld'
import LayoutUtils from '../../../../utils/LayoutUtils'

const { ccclass } = _decorator
const { Layout } = GameWorld

@ccclass('PageContent')
export class PageContent extends Component {
    protected widget!: Widget

    protected uiTransform!: UITransform

    protected __preload(): void {
        this.widget = this.getComponent(Widget)!
        this.uiTransform = this.getComponent(UITransform)!
    }

    protected onLoad(): void {
        // @ts-expect-error - This event is custom event
        game.on(GameEvents.ORIENTATION_CHANGED, this.handleOrientationChanged, this)
        this.handleOrientationChanged(LayoutUtils.getCurrentLayout())
    }

    protected onDestroy(): void {
        game.off(GameEvents.ORIENTATION_CHANGED, this.handleOrientationChanged, this)
    }

    private handleOrientationChanged(layout: string): void {
        if (layout === Layout.Portrait) {
            this.widget.isAlignLeft = true
            this.widget.isAlignRight = true
        } else if (layout === Layout.Landscape) {
            this.widget.isAlignLeft = false
            this.widget.isAlignRight = false
            this.uiTransform.width = 1000
        }
    }
}
