import {
    _decorator,
    Animation,
    assert,
    Component,
    clamp,
    find,
    type IVec2Like,
    randomRangeInt,
    tween,
    UITransform,
    Vec3,
} from 'cc'
import AnimUtils from '../../../utils/AnimUtils'
import ComponentUtils from '../../../utils/ComponentUtils'
import RayManager from './RayManager'

const { ccclass, property } = _decorator

const VEC3_0_300 = new Vec3(0, 300)

@ccclass('Cloud')
export default class Cloud extends Component {
    @ComponentUtils.required()
    @property(Animation)
    private readonly animation!: Animation

    @property({ type: [RayManager] })
    private rays: RayManager[] = []

    private cachedCanvasUiTransform: UITransform | null = null
    private get canvas(): UITransform {
        const transform = this.cachedCanvasUiTransform ?? find('Canvas')?.getComponent(UITransform)
        assert(transform !== null && transform !== undefined, 'Cloud: canvasUiTransform is null')
        return transform
    }

    protected onLoad(): void {
        ComponentUtils.validateRequiredProperties(this, Cloud)
    }

    public getLightningTween(position: Readonly<IVec2Like>) {
        return tween(this.node)
            .call(() => {
                AnimUtils.runAnimationAsync(this.animation)
            })
            .delay(0.2)
            .parallel(...this.rays.map((ray) => ray.createRayAnimation(position)))
    }

    public getMoveToScreenTween(worldPosition: IVec2Like, direction: 'left' | 'right') {
        const { width: screenWidth } = this.canvas
        const worldX = direction === 'left' ? -300 : screenWidth + 300

        const moveTween = tween(this.node)
            .set({ worldPosition: new Vec3(worldX, worldPosition.y) })
            .to(0.6, { worldPosition: new Vec3(worldPosition.x, worldPosition.y, 0) }, { easing: 'quadOut' })

        const scale = clamp(
            worldPosition.y < this.canvas.height / 2 ? 1.2 : 1.7 - worldPosition.y / this.canvas.height,
            0.7,
            2,
        )

        const scaleTween = tween(this.node)
            .set({ scale: new Vec3(scale * 1.1, scale * 0.9, 1) })
            .to(0.6, { scale: new Vec3(scale, scale, 1) }, { easing: 'quadIn' })

        return tween(this.node).parallel(moveTween, scaleTween)
    }

    public getMoveOutScreenTween(direction: 'left' | 'right') {
        const { width: screenWidth, height: screenHeight } = this.canvas
        const x = direction === 'left' ? -screenWidth / 2 - 300 : screenWidth / 2 + 300
        const targetPosition = new Vec3(x, this.node.position.y)
        const hiddenPosition = new Vec3(0, -screenHeight / 2 - 500)

        const moveTween = tween(this.node)
            .call(() => {
                targetPosition.set(targetPosition.x, this.node.position.y)
            })
            .to(0.6, { position: () => targetPosition }, { easing: 'quadIn' })
            .set({ position: hiddenPosition })
        return moveTween
    }

    public getMoveToScreenCenterTween() {
        const { width: screenWidth, height: screenHeight } = this.canvas

        const { x: initialX, y: initialY } = this.getRandomInitialPosition(screenWidth, screenHeight)

        const moveTween = tween(this.node)
            .set({ position: new Vec3(initialX, initialY, 0) })
            .to(0.8, { position: VEC3_0_300 }, { easing: 'quadOut' })

        return moveTween
    }

    private getRandomInitialPosition(screenWidth: number, screenHeight: number) {
        const direction = randomRangeInt(0, 4) as 0 | 1 | 2 | 3

        switch (direction) {
            case 0: // Top
                return {
                    x: Math.random() * screenWidth - screenWidth / 2,
                    y: screenHeight / 2 + 300,
                }
            case 1: // Bottom
                return {
                    x: Math.random() * screenWidth - screenWidth / 2,
                    y: -screenHeight / 2 - 300,
                }
            case 2: // Left
                return {
                    x: -screenWidth / 2 - 300,
                    y: Math.random() * screenHeight - screenHeight / 2,
                }
            case 3: // Right
                return {
                    x: screenWidth / 2 + 300,
                    y: Math.random() * screenHeight - screenHeight / 2,
                }
        }
    }
}
