import ComponentUtils from 'db://assets/utils/ComponentUtils'
import { _decorator, Animation, AnimationClip, Component, ParticleSystem2D, Vec3 } from 'cc'
import AnimUtils from '../../../../../utils/AnimUtils'
import type AnimAbortSignal from '../../../../../utils/common/AnimAbortSignal'

const { ccclass, property } = _decorator

@ccclass('Coin')
export class Coin extends Component {
    @property(ParticleSystem2D)
    public emittingParticles!: ParticleSystem2D

    @property(Animation)
    public animation!: Animation

    @property(AnimationClip)
    private spin!: AnimationClip

    @property(AnimationClip)
    private rest!: AnimationClip

    protected __preload(): void {
        ComponentUtils.lazyValidateProperties(this)
    }

    @ComponentUtils.onlyRunInLifeCycle
    public resetDisabled() {
        this.stopParticles()
        this.stopAnimation()
        this.hide()
        this.node.active = false
        this.node.setPosition(0, 0, this.node.position.z)
    }

    @ComponentUtils.onlyRunInLifeCycle
    public hide() {
        this.node.setScale(Vec3.ZERO)
    }

    @ComponentUtils.onlyRunInLifeCycle
    public stopParticles() {
        this.emittingParticles.stopSystem()
    }

    @ComponentUtils.onlyRunInLifeCycle
    public playTrailParticles() {
        this.emittingParticles.resetSystem()
    }

    @ComponentUtils.onlyRunInLifeCycleAsync
    public async playSpinAnimationAfterDelay(seconds = 0, signal?: AnimAbortSignal): Promise<void> {
        if (seconds > 0) await AnimUtils.waitTweenAsync(seconds)
        await this.playSpinAnimation(signal)
    }

    @ComponentUtils.onlyRunInLifeCycleAsync
    private playSpinAnimation(signal?: AnimAbortSignal) {
        return AnimUtils.runAnimationAsync(this.animation, this.spin.name, signal)
    }

    @ComponentUtils.onlyRunInLifeCycle
    public stopAnimation(): void {
        this.animation.play(this.rest.name)
    }
}
