import { _decorator, tween } from 'cc'
import { PanelAnimationController } from '../../../../../components/panel-animator/PanelAnimationController'
import AnimUtils from '../../../../../utils/AnimUtils'
import ComponentUtils from '../../../../../utils/ComponentUtils'
import ThemeUtils from '../../../../../utils/ThemeUtils'
import { DashboardPage } from '../DashboardPage'
import { CollectionsContent } from './CollectionsContent'
const { ccclass } = _decorator

@ccclass('CollectionsPage')
export class CollectionsPage extends DashboardPage {
    private themeContent!: CollectionsContent

    private payload: CollectionsPayload[] | null = null

    @ComponentUtils.required()
    private panelAnimationController!: PanelAnimationController

    public onLoad(): void {
        this.panelAnimationController = this.getComponent(PanelAnimationController) as PanelAnimationController
        super.onLoad()
    }

    public async start(): Promise<void> {
        super.start()

        // this.pagePrefab = GeneralUtils.getAsset(ResourcePaths.COLLECTIONS_PAGE, Prefab)
    }

    public setPayload(payload: CollectionsPayload[]): void {
        this.payload = payload
    }

    @ComponentUtils.onlyRunInLifeCycleAsync
    public override async mount(): Promise<void> {
        // if (!this.pagePrefab) {
        //     const data = await GeneralUtils.loadPrefabAsync(ResourcePaths.COLLECTIONS_PAGE)
        //     if (data && this.node) {
        //         this.pagePrefab = data
        //     }
        // }

        await super.mount()
        this.onMount()
    }

    @ComponentUtils.onlyRunInLifeCycle
    private onMount() {
        this.containerUIOpacity.opacity = 0

        if (!this.themeContent) {
            this.themeContent = this.container.getComponentInChildren(CollectionsContent)!

            if (!this.themeContent) {
                throw new Error('No theme content found')
            }
        }

        const collectionPayloads = ThemeUtils.getCollectionUnlockProgress()
        this.themeContent.updateUnlockedProgress(collectionPayloads)

        if (this.payload) this.themeContent.init(this.payload)
        
    }

    @ComponentUtils.onlyRunInLifeCycleAsync
    public override async transitionIn(): Promise<void> {
        await super.transitionIn()
        this.themeContent.restoreScrollPosition()

        this.themeContent.listenEvents()

        const fadeIn = AnimUtils.runTweenAsync(tween(this.containerUIOpacity).to(0.2, { opacity: 255 }))

        const uiAnim = this.panelAnimationController.showUI()

        await Promise.all([fadeIn, uiAnim])
    }

    @ComponentUtils.onlyRunInLifeCycleAsync
    public override async transitionOut(): Promise<void> {
        await super.transitionOut()
        this.themeContent.preserveScrollPosition()
        this.themeContent.unlistenUIEvents()

        await this.panelAnimationController.hideUI()

        this.themeContent.unlistenEvents()
    }
}
