import { _decorator, Component, game, instantiate, type Node, Widget } from 'cc'
import GameEvents from '../../../../constants/GameEvents'
import GameWorld from '../../../../constants/GameWorld'
import ResourcePaths from '../../../../gameplay/constants/ResourcePaths'
import ComponentUtils from '../../../../utils/ComponentUtils'
import LayoutUtils from '../../../../utils/LayoutUtils'
import ResourceUtils from '../../../../utils/ResourceUtils'

const { ccclass, property } = _decorator
const { Layout } = GameWorld

@ccclass('PageHeader')
export class PageHeader extends Component {
    @ComponentUtils.required()
    @property(Widget)
    private readonly widget!: Widget

    private leaves!: Node

    protected __preload(): void {
        ComponentUtils.validateRequiredProperties(this, PageHeader)
        if (LayoutUtils.isLandscape()) this.loadLeaves()
    }

    protected onLoad(): void {
        // @ts-expect-error - This event is custom event
        game.on(GameEvents.ORIENTATION_CHANGED, this.handleOrientationChanged, this)
        this.handleOrientationChanged(LayoutUtils.getCurrentLayout())
    }

    protected onDestroy(): void {
        game.off(GameEvents.ORIENTATION_CHANGED, this.handleOrientationChanged, this)
    }

    private async handleOrientationChanged(layout: string): Promise<void> {
        if (layout === Layout.Portrait) {
            if (this.leaves) this.leaves.active = false
            this.widget.left = -80
            this.widget.right = -80
        } else if (layout === Layout.Landscape) {
            this.widget.left = 0
            this.widget.right = 0
            await this.loadLeaves()
            if (this.leaves) this.leaves.active = true
        }
    }

    @ComponentUtils.onlyRunInLifeCycleAsync
    private async loadLeaves(): Promise<void> {
        if (this.leaves) return

        const leavesPrefab = await ResourceUtils.loadPrefabAsync(ResourcePaths.PAGE_LEAVES_PREFAB)
        if (this.leaves || !leavesPrefab || !this.enabledInHierarchy) return

        this.leaves = instantiate(leavesPrefab)
        this.node.addChild(this.leaves)
    }
}
