import { CCString, Component, _decorator } from 'cc'
import { EDITOR_NOT_IN_PREVIEW } from 'cc/env'
import { AvailableLocales } from '../../constants/Locales'
import ResourceUtils from '../../utils/ResourceUtils'
const { ccclass, property } = _decorator

const {
    Utils: { Array: A, Valid: V },
} = Ludex

const { language } = codex

@ccclass('LocalizeTextureKey')
export class LocalizeTextureKey {
    @property({ visible: false })
    private _key: string = ''

    public get key(): string {
        return this._key
    }

    @property(CCString)
    public set key(value: string) {
        const isValid = language.hasTextureKey(value)

        if (!isValid) {
            console.warn(`Key ${value} not found in locales`)
            return
        }

        if (EDITOR_NOT_IN_PREVIEW) {
            console.info(`Key ${value} is valid`)
        }

        this._key = value
    }
}

@ccclass('SupportedLocaleKey')
export class SupportedLocaleKey {
    @property({ visible: false })
    private _key: string = ''

    public get key(): string {
        return this._key
    }

    @property(CCString)
    public set key(value: string) {
        const isValid = A.has(AvailableLocales, value)

        if (!isValid) {
            console.warn(`Key ${value} not found. Supported locales are ${AvailableLocales.join(', ')}`)
            return
        }

        if (EDITOR_NOT_IN_PREVIEW) {
            console.info(`Key ${value} is valid`)
        }

        this._key = value
    }
}

@ccclass('LocalizePreloader')
export class LocalizePreloader extends Component {
    @property([LocalizeTextureKey])
    readonly keys: LocalizeTextureKey[] = []

    @property({ type: SupportedLocaleKey, tooltip: 'Preload only for these locales' })
    readonly locales: SupportedLocaleKey[] = []

    public __preload(): void {
        this.preloadTextures()
    }

    protected preloadTextures() {
        const currentLocale = language.getCurrentLocale()

        const locales = this.locales.map((locale) => locale.key)

        if (!A.has(locales, currentLocale)) {
            if (V.isDebugger()) {
                console.info(
                    'Skipping preloading of textures because current locale is not in the list of preloading locales',
                )
            }

            return
        }

        const paths: string[] = []

        for (const keyComp of this.keys) {
            if (language.hasTextureKey(keyComp.key) === false) continue
            const textureKey = language.getTexture(keyComp.key)
            const path = `${textureKey}/spriteFrame`
            paths.push(path)
        }

        ResourceUtils.loadSpriteFramesAsync(paths)
    }
}
