import { CCFloat, Component, ParticleSystem2D, Sprite, type Tween, UIOpacity, Vec3, _decorator, tween } from 'cc'
import AnimUtils from '../../../utils/AnimUtils'
import ComponentUtils from '../../../utils/ComponentUtils'
import type AnimAbortSignal from '../../../utils/common/AnimAbortSignal'

const { ccclass, property } = _decorator

@ccclass('StarChest')
export class StarChest extends Component {
    private shakingTween: { ogPos: Vec3; tw: Tween<unknown> } | undefined

    @property(Sprite)
    private sprite!: Sprite

    @property(UIOpacity)
    private glowOpacity!: UIOpacity

    @property(ParticleSystem2D)
    private explosionParticles!: ParticleSystem2D

    @property(UIOpacity)
    private circle!: UIOpacity

    @property(UIOpacity)
    private uiOpacity!: UIOpacity

    @property({
        type: CCFloat,
        readonly: true,
        displayName: 'Starting Offset X',
        group: 'Reward Animations',
    })
    private get startingOffsetX(): number {
        return this.node.position.x
    }

    private ogCenterX = 0

    @property({
        type: CCFloat,
        readonly: true,
        displayName: 'Starting Offset Y',
        group: 'Reward Animations',
    })
    private get startingOffsetY(): number {
        return this.node.position.y
    }

    private ogCenterY = 0

    @property({
        type: CCFloat,
        displayName: 'Centered Offset X',
        group: 'Reward Animations',
    })
    private endingOffsetX = 0

    @property({
        type: CCFloat,
        displayName: 'Centered Offset Y',
        group: 'Reward Animations',
    })
    private endingOffsetY = 0

    @property({
        type: CCFloat,
        min: 0,
        displayName: 'Starting Scale',
        group: 'Reward Animations',
    })
    private startingScale = 0.5

    @property({
        type: CCFloat,
        min: 0,
        displayName: 'Ending Scale',
        group: 'Reward Animations',
    })
    private endingScale = 1

    public __preload(): void {
        this.validateRequiredComponents()

        this.ogCenterX = this.startingOffsetX
        this.ogCenterY = this.startingOffsetY
    }

    private validateRequiredComponents(): void {
        ComponentUtils.lazyValidateProperties(this)
    }

    public async moveToCenter(signal: AnimAbortSignal): Promise<void> {
        const targetPosition = new Vec3(this.endingOffsetX, this.endingOffsetY, this.node.position.z)

        await AnimUtils.runTweenAsync(
            tween(this.node).to(
                0.7,
                {
                    position: targetPosition,
                },
                {
                    easing: 'quintInOut',
                },
            ),
            signal,
        )

        this.node.position = targetPosition
    }

    public respawnAtStartingState(): void {
        this.node.position = new Vec3(this.ogCenterX, this.ogCenterY, this.node.position.z)
        this.glowOpacity.opacity = 0

        this.sprite.node.setScale(this.startingScale, this.startingScale, this.startingScale)
        this.glowOpacity.node.setScale(this.startingScale, this.startingScale, this.startingScale)

        this.uiOpacity.opacity = 255
    }

    public async scale(progress: number, signal: AnimAbortSignal): Promise<void> {
        const currentScale = this.startingScale + (this.endingScale - this.startingScale) * progress
        const targetScale = new Vec3(currentScale, currentScale, currentScale)

        await Promise.all([
            AnimUtils.runTweenAsync(
                tween(this.sprite.node).to(0.2, { scale: targetScale }, { easing: 'backOut' }),
                signal,
            ),
            AnimUtils.runTweenAsync(
                tween(this.glowOpacity.node).to(0.2, { scale: targetScale }, { easing: 'backOut' }),
                signal,
            ),
        ])

        this.sprite.node.setScale(targetScale)
        this.glowOpacity.node.setScale(targetScale)
    }

    public startShaking(): void {
        const ogPosition = this.node.position.clone()

        this.shakingTween = {
            ogPos: ogPosition,
            tw: tween(this.node)
                .to(
                    0.1,
                    {},
                    {
                        onUpdate: (_target, ratio) => {
                            if (ratio) {
                                // 0 -> x - 5
                                // 0.25 -> x
                                // 0.5 -> x + 5
                                // 0.75 -> x
                                // 1 -> x - 5
                                const x = ogPosition.x + (ratio - 0.5) * (5 * 2)
                                this.node.position = new Vec3(x, ogPosition.y, ogPosition.z)
                            }
                        },
                    },
                )
                .repeatForever()
                .start(),
        }
    }

    public stopShaking(): void {
        if (this.shakingTween) {
            this.shakingTween.tw.stop()
            this.node.position = this.shakingTween.ogPos
            this.shakingTween = undefined
        }
    }

    public async glow(signal: AnimAbortSignal): Promise<void> {
        const targetScale = this.sprite.node.scale.clone().multiplyScalar(1.1)
        await Promise.all([
            AnimUtils.runTweenAsync(
                tween(this.sprite.node).to(0.2, { scale: targetScale }, { easing: 'cubicInOut' }),
                signal,
            ),
            AnimUtils.runTweenAsync(tween(this.glowOpacity.node).to(0.2, { scale: targetScale }), signal),
            AnimUtils.runTweenAsync(tween(this.glowOpacity).to(0.5, { opacity: 255 }), signal),
        ])
    }

    public async explode(signal: AnimAbortSignal): Promise<void> {
        const targetScale = new Vec3(8, 8, 8)

        const animateCircle = async () => {
            this.circle.opacity = 255
            this.circle.node.setScale(Vec3.ZERO)

            await Promise.all([
                AnimUtils.runTweenAsync(tween(this.circle).to(0.3, { opacity: 0 }), signal),
                AnimUtils.runTweenAsync(
                    tween(this.circle.node).to(0.3, { scale: targetScale }, { easing: 'cubicOut' }),
                    signal,
                ),
            ])

            this.circle.opacity = 0
            this.circle.node.scale = targetScale
        }

        const animateSprites = async () => {
            await Promise.all([
                AnimUtils.runTweenAsync(
                    tween(this.glowOpacity.node).to(0.3, { scale: Vec3.ZERO }, { easing: 'cubicOut' }),
                    signal,
                ),
                AnimUtils.runTweenAsync(
                    tween(this.sprite.node).to(0.3, { scale: Vec3.ZERO }, { easing: 'cubicOut' }),
                    signal,
                ),
            ])

            this.sprite.node.setScale(Vec3.ZERO)
            this.glowOpacity.node.setScale(Vec3.ZERO)
        }

        await Promise.all([this.explodeParticles(signal), animateCircle(), animateSprites()])
    }

    private async explodeParticles(signal: AnimAbortSignal): Promise<void> {
        if (signal.aborted) return

        this.explosionParticles.node.active = true
        this.explosionParticles.resetSystem()

        await AnimUtils.waitTweenAsync(1)

        this.explosionParticles.node.active = false
    }
}
