import { CCFloat, Component, Sprite, UIOpacity, Vec3, _decorator, tween } from 'cc'
import AnimUtils from '../../../utils/AnimUtils'
import ComponentUtils from '../../../utils/ComponentUtils'
const { ccclass, property, disallowMultiple } = _decorator

@ccclass('ShiningStar')
@disallowMultiple
export class ShiningStar extends Component {
    @property(Sprite)
    private shine!: Sprite

    private shineOpacity!: UIOpacity
    private uiOpacity!: UIOpacity

    @property({ serializable: true, visible: false })
    private twScaleUpDuration: number = 0.5
    @property({ type: CCFloat, displayName: 'Scale up duration', min: 0.001 })
    public get scaleUpDuration(): number {
        return this.twScaleUpDuration
    }
    public set scaleUpDuration(value: number) {
        this.twScaleUpDuration = value
    }

    @property({ serializable: true, visible: false })
    private twBounceAndFallDuration: number = 0.5
    @property({ type: CCFloat, displayName: 'Bounce and Fall duration', min: 0.001 })
    public get bounceAndFallDuration(): number {
        return this.twBounceAndFallDuration
    }
    public set bounceAndFallDuration(value: number) {
        this.twBounceAndFallDuration = value
    }

    @property({ serializable: true, visible: false })
    private twStartScale: number = 0.5
    @property({ type: CCFloat, displayName: 'Start scale' })
    public get startScale(): number {
        return this.twStartScale
    }
    public set startScale(value: number) {
        this.twStartScale = value
    }

    @property({ serializable: true, visible: false })
    private twEndScale: number = 1
    @property({ type: CCFloat, displayName: 'End scale' })
    public get endScale(): number {
        return this.twEndScale
    }
    public set endScale(value: number) {
        this.twEndScale = value
    }

    private isBeingAnimated = false
    public get isAvailable(): boolean {
        return !this.isBeingAnimated
    }

    public __preload(): void {
        this.shineOpacity = this.shine.node.getComponent(UIOpacity)!
        this.uiOpacity = this.node.getComponent(UIOpacity)!
        this.validateRequiredComponents()
    }

    public onLoad(): void {
        this.uiOpacity.opacity = 0
    }

    public async playAnimation(): Promise<void> {
        if (!this.isAvailable) {
            throw new Error('ShiningStar: This star is being animated.')
        }

        this.isBeingAnimated = true

        await this.animateAppear()
        await this.animateDisappear()

        this.isBeingAnimated = false
    }

    private async animateAppear(): Promise<void> {
        this.shineOpacity.opacity = 255
        await Promise.all([this.animateFadeIn(), this.animateScaleUp()])
    }

    private async animateDisappear(): Promise<void> {
        await Promise.all([this.animateBounceAndFall(), this.animateScaleDown()])
        this.node.setScale(0, 0)
        this.uiOpacity.opacity = 0
    }

    private async animateBounceAndFall(): Promise<void> {
        const startX = this.node.position.x
        const startY = this.node.position.y

        const randAngleRange = [60, 120]
        const randAngle =
            (Math.random() * (randAngleRange[1] - randAngleRange[0]) + randAngleRange[0]) * (Math.PI / 180)
        const distance = 200
        const randX = startX + distance * Math.cos(randAngle)
        const randY = startY + distance * Math.sin(randAngle)

        const endY = startY - 500

        const bounce = AnimUtils.runTweenAsync(
            tween(this.node)
                .to(
                    this.twBounceAndFallDuration * 0.2,
                    {
                        position: new Vec3(randX, randY, this.node.position.z),
                    },
                    {
                        easing: 'sineOut',
                    },
                )
                .to(
                    this.twBounceAndFallDuration * 0.4,
                    {
                        position: new Vec3(randX, endY, this.node.position.z),
                    },
                    {
                        easing: 'cubicIn',
                    },
                ),
        )

        const randRotation = [0, 360]
        const rotation = AnimUtils.runTweenAsync(
            tween(this.node).to(
                this.twBounceAndFallDuration,
                {
                    eulerAngles: new Vec3(
                        0,
                        0,
                        Math.random() * (randRotation[1] - randRotation[0]) * (randX > startX ? -1 : 1),
                    ),
                },
                {
                    easing: 'sineOut',
                },
            ),
        )

        await Promise.all([bounce, rotation])

        this.node.eulerAngles = new Vec3(0, 0, 0)
    }

    private async animateFadeIn(): Promise<void> {
        //? Ensure that the duration is not 0
        const duration = Math.max(this.scaleUpDuration / 4, 0.01)
        await AnimUtils.runTweenAsync(tween(this.uiOpacity).to(duration, { opacity: 255 }))
    }

    private async animateScaleUp(): Promise<void> {
        this.node.setScale(this.startScale, this.startScale)

        const targetScale = new Vec3(this.endScale, this.endScale, 1)
        await AnimUtils.runTweenAsync(
            tween(this.node).to(this.scaleUpDuration, { scale: targetScale }, { easing: 'cubicOut' }),
        )
    }

    private async animateScaleDown(): Promise<void> {
        const targetScale = new Vec3(0, 0, 1)
        await AnimUtils.runTweenAsync(
            tween(this.node)
                .delay(this.bounceAndFallDuration * 0.1)
                .to(this.bounceAndFallDuration * 0.5, { scale: targetScale }, { easing: 'cubicIn' }),
        )
    }

    private validateRequiredComponents(): void {
        ComponentUtils.lazyValidateProperties(this)
    }
}
