import { Component, ProgressBar, Vec3, _decorator, tween, type TweenEasing } from 'cc'
import AnimUtils from '../../../../../../utils/AnimUtils'
import { LeaderRail } from './LeaderRail'
const { ccclass, property } = _decorator

const TOTAL_LENGTH = 474

type Signal = Ludex.Utils.Signal.Signal

@ccclass('TournamentProgressBar')
export class TournamentProgressBar extends Component {
    @property(LeaderRail)
    private playerLeader!: LeaderRail

    private progressBar!: ProgressBar

    protected __preload(): void {
        this.progressBar = this.getComponent(ProgressBar)!
    }

    public async updateProgress(
        progress: number,
        signal: Signal,
        duration: number = 0.5,
        easing: TweenEasing = 'cubicOut',
    ): Promise<void> {
        const progressTween = tween(this.progressBar).to(duration, { progress }, { easing, onUpdate: this.onUpdate })

        await AnimUtils.runTweenAsync(progressTween, signal)
    }

    public getProgress(): number {
        return this.progressBar.progress
    }

    private onUpdate = () => {
        if (!this.playerLeader || !this.playerLeader.node) return

        this.playerLeader.node.position = new Vec3(this.progressBar.progress * TOTAL_LENGTH - TOTAL_LENGTH / 2, 0, 0)
    }
}
