import { Component, UIOpacity, _decorator, tween } from 'cc'
import AnimUtils from '../../utils/AnimUtils'
import ComponentUtils from '../../utils/ComponentUtils'
import AnimAbortSignal from '../../utils/common/AnimAbortSignal'

const { ccclass, requireComponent, property } = _decorator

@ccclass('OpacityToggler')
@requireComponent([UIOpacity])
export class OpacityToggler extends Component {
    @property
    private activeValue = 255

    @property
    private inActiveValue = 128

    private opacityComponent!: UIOpacity
    private signal: AnimAbortSignal = new AnimAbortSignal()

    public __preload() {
        this.opacityComponent = this.getComponent(UIOpacity)!

        this.validateRequiredComponents()
    }

    private validateRequiredComponents() {
        ComponentUtils.lazyValidateProperties(this)
    }

    public changeState(active = false) {
        this.signal.abort()
        this.opacityComponent.opacity = active ? this.activeValue : this.inActiveValue
    }

    public async changeStateFade(active = false, duration = 0.2) {
        this.signal.abort()
        this.signal = new AnimAbortSignal()

        const target = active ? this.activeValue : this.inActiveValue

        await AnimUtils.runTweenAsync(tween(this.opacityComponent).to(duration, { opacity: target }), this.signal)
    }
}
