import { ResolutionPolicy, view } from 'cc'
import GameWorld from '../constants/GameWorld'
import CocosUtils from './CocosUtils'
import LayoutUtils from './LayoutUtils'

const { player, analytics } = codex

const {
    Plugins: { Analytics: { Events } = {} },
} = Ludex

type OverlayView = FBInstant.OverlayView

class OverlayUtilsCore {
    protected static inst$: OverlayUtilsCore

    public static get inst() {
        if (!this.inst$) {
            this.inst$ = new this()
        }
        return this.inst$
    }

    public static async shareBestScoreWithOverlay(): Promise<void> {
        try {
            const { data } = player.getPlayer()
            const { score: playerScore } = data || {}

            this.isOverlayViewAPIAvailable(GameSDK)

            await GameSDK.extra.shareAsync({
                text: 'Great! I have a best score!',
                intent: 'SHARE',
                imageOverlayPath: 'wideframes/share-async.xml',
                pathToCSS: 'wideframes/share-async-styles.css',
                initialData: { score: playerScore ?? 0 },
                shareDestination: ['NEWSFEED', 'GROUP', 'COPY_LINK', 'MESSENGER'],
                switchContext: false,
            })

            if (Events) {
                analytics.event(Events.SHARE, {
                    share_content_type: 'BestScore',
                    success: true,
                })
            }
        } catch (error) {
            if (Events) {
                analytics.event(Events.SHARE, {
                    share_content_type: 'BestScore',
                    success: false,
                })
            }

            console.error('Overlay sharing failed:', error)
        }
    }

    public static async inviteFriendsWithOverlay(): Promise<void> {
        try {
            const { data } = player.getPlayer()
            const { score: playerScore } = data || {}

            this.isOverlayViewAPIAvailable(GameSDK)

            await GameSDK.extra.inviteAsync({
                initialData: { score: playerScore ?? 0 },
                text: {
                    default: 'Great! I have a best score!',
                    localizations: {
                        vi_VN: 'Tht tuyt! Mnh va ph k lc ny!',
                    },
                },
                imageOverlayPath: 'wideframes/invite-async.xml',
                pathToCSS: 'wideframes/invite-async-styles.css',
            })

            if (Events) {
                analytics.event(Events.INVITE, {
                    invite_content_type: 'BestScore',
                    success: true,
                })
            }
        } catch (error) {
            if (Events) {
                analytics.event(Events.INVITE, {
                    invite_content_type: 'BestScore',
                    success: false,
                })
            }

            console.error('Overlay inviting failed:', error)
        }
    }

    public static isOverlayViewAPIAvailable(
        gameSDK: GameSDKAdapter,
    ): asserts gameSDK is FBInstantNEZPAdapter | FBInstantRestrictedAdapter {
        if (!gameSDK.isGameSDK('FacebookInstant')) {
            throw new Error('GameSDK is not FacebookInstant')
        }

        if (!gameSDK.extra.isNEZP() || gameSDK.extra.isRestricted()) {
            throw new Error('GameSDK is not NEZP or Restricted')
        }

        if (!('overlayViews' in gameSDK)) {
            throw new Error('GameSDK does not have overlayViews')
        }
    }

    public static async createOverlayViewAsync(
        pathToXML: string,
        domElement: HTMLElement,
        iframeStyle: string,
        pathToCSS: string,
        initialData: Record<string, unknown>,
    ): Promise<OverlayView> {
        this.isOverlayViewAPIAvailable(GameSDK)

        const overlay = await GameSDK.overlayViews.createOverlayViewAsync(
            pathToXML,
            domElement,
            iframeStyle,
            pathToCSS,
            initialData,
        )

        return overlay
    }

    public static getGameScaleFactor(): number {
        const { Portrait, Landscape } = GameWorld.DesignSize

        const screenWidth = CocosUtils.getScreenWidthCss()
        const screenHeight = CocosUtils.getScreenHeightCss()

        const isPrimaryLayoutLandscape = LayoutUtils.isLandscape()
        const designWidth = isPrimaryLayoutLandscape ? Landscape.WIDTH : Portrait.WIDTH
        const designHeight = isPrimaryLayoutLandscape ? Landscape.HEIGHT : Portrait.HEIGHT

        const scaleX = screenWidth / designWidth
        const scaleY = screenHeight / designHeight

        const viewResolutionPolicy = this.getViewResolutionPolicy()

        switch (viewResolutionPolicy.getContentStrategy()) {
            case ResolutionPolicy.ContentStrategy.FIXED_WIDTH:
                return scaleX
            case ResolutionPolicy.ContentStrategy.FIXED_HEIGHT:
                return scaleY
            default:
                return Math.max(scaleX, scaleY)
        }
    }

    protected static getViewResolutionPolicy(): ResolutionPolicy {
        const resolutionPolicy = view.getResolutionPolicy()

        return resolutionPolicy
    }
}

class OverlayUtils extends OverlayUtilsCore {
    // * Custom logic here
}

export default OverlayUtils
