import { _decorator, CCBoolean, pseudoRandom, Vec3 } from 'cc'
import { isCurveTwoValues } from '../ParticleFunction'
import type { Particle } from '../ParticleSimulator'
import CurveRange from '../particle-editor/CurveRange'
import { ModuleRandSeed } from './ModuleRandSeed'

const { ccclass, property } = _decorator

@ccclass('VelocityOvertimeModule')
export class VelocityOvertimeModule {
    @property({ type: CCBoolean })
    private enable!: boolean

    @property({ type: CurveRange })
    private x: CurveRange = new CurveRange()

    @property({ type: CurveRange })
    private y: CurveRange = new CurveRange()

    private _temp_v3 = new Vec3()

    constructor() {
        this.x.constant = 0
        this.y.constant = 0
    }

    public update(particle: Particle, dt: number): void {
        if (!this.enable) return

        const normalizedTime = 1 - particle.remainingLifetime / particle.startLifetime

        const randX = isCurveTwoValues(this.x) ? pseudoRandom(particle.randomSeed + ModuleRandSeed.VELOCITY_X) : 0
        const randY = isCurveTwoValues(this.y) ? pseudoRandom(particle.randomSeed + ModuleRandSeed.VELOCITY_Y) : 0

        const vel = Vec3.set(
            this._temp_v3,
            this.x.evaluate(normalizedTime, randX),
            this.y.evaluate(normalizedTime, randY),
            0,
        )

        particle.velocity.add(vel.multiplyScalar(dt))
    }
}
