import { Component, Node, type Tween, UIOpacity, Vec3, _decorator, tween } from 'cc'

const { ccclass, property } = _decorator

@ccclass('GuideHand')
class GuideHand extends Component {
    @property(Node)
    private hand!: Node

    @property(Node)
    private circleHighlight!: Node

    @property(UIOpacity)
    private circleHighlightOpacity!: UIOpacity

    private handTween!: Tween<Node>

    public start(): void {
        this.circleHighlight.setScale(0, 0)
        this.circleHighlightOpacity.opacity = 255
    }

    public playHandAnimation(startPosition: Vec3, endPosition: Vec3): void {
        if (this.handTween) {
            this.handTween.stop()
        }

        const moveTween = tween(this.hand)
            .to(0.8, { worldPosition: endPosition }, { easing: 'quartOut' })
            .to(0.2, { scale: new Vec3(0.9, 0.9, 0.9), angle: 15 })
            .call(() => {
                this.playHighlightAnimation()
            })
            .to(0.2, { scale: Vec3.ONE, angle: 0 })
            .to(0.8, { worldPosition: startPosition }, { easing: 'quartOut' })
            .to(0.2, { scale: new Vec3(0.9, 0.9, 0.9), angle: 15 })
            .call(() => {
                this.playHighlightAnimation()
            })
            .to(0.2, { scale: Vec3.ONE, angle: 0 })

        this.handTween = tween(this.hand).repeatForever(moveTween).start()
    }

    private playHighlightAnimation(): void {
        this.circleHighlight.setScale(0, 0)
        this.circleHighlightOpacity.opacity = 255
        this.circleHighlight.position = this.hand.position.clone()

        tween(this.circleHighlight)
            .to(0.6, { scale: new Vec3(1.5, 1.5) }, { easing: 'quadOut' })
            .start()

        tween(this.circleHighlightOpacity).to(0.6, { opacity: 0 }, { easing: 'quadIn' }).start()
    }
}

export default GuideHand
