import TileSpecialTypes from '../../constants/TileSpecialTypes'

const defaultEncoding: TileEncoding = {
    [TileSpecialTypes.bomb]: 'b',
    [TileSpecialTypes.star]: 's',
    [TileSpecialTypes.none]: 't',
}

type Grid<T> = {
    get(x: number, y: number): T
    width: number
    height: number
}

type Tile<I> = {
    isHidden(): boolean
    id: I | null
    special: { type: TileSpecialType } | undefined
}

export default function getGridFen<I, T extends Tile<I>>(
    grid: Readonly<Grid<T>>,
    encoding: TileEncoding = defaultEncoding,
): LevelFen {
    const { width, height } = grid

    const code = [] as string[]
    const tileTypes: Map<I, string> = new Map()

    // ? reversed to make the plain text more readable
    // but requires another reverse when parsing
    for (let y = height - 1; y >= 0; y--) {
        const row = [] as Array<string>
        for (let x = 0; x < width; x++) {
            const tile = grid.get(x, y)
            const identity = tile.isHidden() ? null : tile.id

            if (identity === null) {
                row.push('.')
                continue
            }

            const specialType = tile.special?.type ?? TileSpecialTypes.none
            const encodedType = encoding[specialType]

            if (!tileTypes.has(identity)) {
                const size = tileTypes.size.toString()
                tileTypes.set(identity, size)
            }

            const encodedTile = `${encodedType}${tileTypes.get(identity)}`
            row.push(encodedTile)
        }

        code.push(row.join(''))
    }

    // ? currently using the same variant for all orientations
    return {
        code: code.join(':'),
        idSize: tileTypes.size,
        encoding,
        rows: height,
        cols: width,
    }
}
