import type MissingPropertyError from './MissingPropertyError'

export default class MissingPropertiesError extends Error {
    readonly className: string
    readonly errors: MissingPropertyError[]

    constructor(className: string, errors: MissingPropertyError[]) {
        super(
            `${className} is missing these required properties: [${errors
                .map((error) => error.propertyName)
                .join(', ')}]`,
        )

        this.className = className
        this.errors = errors
    }
}
