import ComponentUtils from 'db://assets/utils/ComponentUtils'
import OverlayUtils from 'db://assets/utils/OverlayUtils'
import { _decorator, Component, Label, Node, Sprite, type Tween, tween, UIOpacity, UITransform } from 'cc'
import CacheUtils from '../../../../utils/CacheUtils'
import PlayerAvatarOverlay from '../../../common/PlayerAvatarOverlay'

const { ccclass, property } = _decorator
const DEFAULT_PLAYER_ID = '10'

@ccclass('PlayerScore')
export class PlayerScore extends Component {
    @property(Node)
    private crown!: Node

    @property(Label)
    private playerScore!: Label

    @property(UIOpacity)
    private playerScoreUiOpacity!: UIOpacity

    @property(UIOpacity)
    private waitingUiOpacity!: UIOpacity

    @property(Sprite)
    private avatar!: Sprite

    @property(Node)
    private defaultAvatarNode!: Node

    private avatarOverlay: PlayerAvatarOverlay | null = null

    private isOverlayMode: boolean = false

    private waitingAnimation!: Tween<UIOpacity>

    public onLoad(): void {
        this.createAvatarOverlay()
    }

    private createAvatarOverlay(): void {
        try {
            OverlayUtils.isOverlayViewAPIAvailable(GameSDK)

            this.isOverlayMode = true
            this.avatarOverlay = new PlayerAvatarOverlay(this.node.scene)
        } catch {
            return
        }
    }

    public showCrown(visible: boolean): void {
        this.crown.active = visible
    }

    public setPlayerScore(score: number): void {
        this.playerScore.string = score.toString()
    }

    public playWaitingAnimation(): void {
        this.waitingUiOpacity.node.active = true
        this.playerScoreUiOpacity.node.active = false

        this.waitingUiOpacity.opacity = 130
        this.playerScoreUiOpacity.opacity = 0

        const waitingOpacityTween = tween(this.waitingUiOpacity)
            .to(1, { opacity: 130 }, { easing: 'sineInOut' })
            .to(1, { opacity: 255 }, { easing: 'sineInOut' })
            .delay(0.25)

        this.waitingAnimation = tween(this.waitingUiOpacity).repeatForever(waitingOpacityTween).start()
    }

    public stopWaitingAnimation(): void {
        if (this.waitingAnimation) {
            this.waitingAnimation.stop()
        }

        this.waitingUiOpacity.opacity = 0
        this.playerScoreUiOpacity.opacity = 255

        this.waitingUiOpacity.node.active = false
        this.playerScoreUiOpacity.node.active = true
    }

    @ComponentUtils.onlyRunInLifeCycleAsync
    public async loadPhoto(photo: string, playerId: string): Promise<void> {
        try {
            const isDefaultPlayerId = playerId === DEFAULT_PLAYER_ID
            if (this.isOverlayMode && !isDefaultPlayerId) {
                const result = await this.showAvatarOverlay(playerId)
                if (result) return

                this.isOverlayMode = false
            }

            const spriteFrame = await CacheUtils.getPlayerAvatar(photo, playerId, 128)
            if (!spriteFrame) throw new Error('Failed to create sprite frame')
            if (!this.node.activeInHierarchy) return

            spriteFrame.packable = true
            this.avatar.spriteFrame = spriteFrame
        } catch (error) {
            console.error(error)
            return
        }
    }

    private async showAvatarOverlay(playerId: string): Promise<boolean> {
        if (!this.avatarOverlay) return false

        const { worldX, worldY } = this.getDefaultAvatarPosition()
        const result = await this.avatarOverlay.updateOverlay({
            playerId,
            worldX,
            worldY,
            avatarWidth: this.getDefaultAvatarWidth(),
        })

        return result ?? false
    }

    public cleanupOverlay(): void {
        this.avatarOverlay?.hideOverlay()
    }

    private getDefaultAvatarPosition(): { worldX: number; worldY: number } {
        const { worldPositionX, worldPositionY } = this.defaultAvatarNode
        return {
            worldX: worldPositionX,
            worldY: worldPositionY,
        }
    }

    private getDefaultAvatarWidth(): number {
        const uiTransform = this.defaultAvatarNode.getComponent(UITransform)!
        const { width } = uiTransform
        return width
    }
}
