import { Color, Component, Graphics, _decorator, game } from 'cc'
import { EDITOR_NOT_IN_PREVIEW } from 'cc/env'
import GameEvents from '../../constants/GameEvents'
import GameWorld from '../../constants/GameWorld'
import ComponentUtils from '../../utils/ComponentUtils'
const { ccclass, property, requireComponent, disallowMultiple, executeInEditMode } = _decorator

const { Portrait, Landscape } = GameWorld.DesignSize

@ccclass('DebugResolution')
@disallowMultiple
@requireComponent([Graphics])
@executeInEditMode(true)
export class DebugResolution extends Component {
    public static _drawPortrait = true
    public static _drawLandscape = true

    @property
    public set drawPortrait(value: boolean) {
        if (value === DebugResolution._drawPortrait) return

        if (value) {
            this.drawPortraitResolution()
        } else {
            this.graphics.clear()
        }

        DebugResolution._drawPortrait = value

        if (DebugResolution._drawLandscape) {
            this.drawLandscapeResolution()
        }
    }

    public get drawPortrait(): boolean {
        return DebugResolution._drawPortrait
    }

    @property
    public set drawLandscape(value: boolean) {
        if (value === DebugResolution._drawLandscape) return

        if (value) {
            this.drawLandscapeResolution()
        } else {
            this.graphics.clear()
        }

        DebugResolution._drawLandscape = value

        if (DebugResolution._drawPortrait) {
            this.drawPortraitResolution()
        }
    }

    public get drawLandscape(): boolean {
        return DebugResolution._drawLandscape
    }

    private graphics!: Graphics

    public __preload(): void {
        this.graphics = this.getComponent(Graphics)!

        this.validateRequiredComponents()

        this.listenGameEvents()
    }

    private validateRequiredComponents() {
        ComponentUtils.lazyValidateProperties(this)
    }

    private listenGameEvents() {
        game.on(GameEvents.LAYOUT_CHANGED, this.handleLayoutChange)
    }

    private handleLayoutChange = () => {
        this.graphics?.clear()
        this.start()
    }

    public start(): void {
        if (this.drawPortrait) {
            this.drawPortraitResolution()
        }

        if (this.drawLandscape) {
            this.drawLandscapeResolution()
        }
    }

    private drawPortraitResolution() {
        const { graphics } = this
        if (!graphics) return

        const width = Portrait.WIDTH
        const height = Portrait.HEIGHT
        const x = -width / 2
        const y = -height / 2

        graphics.strokeColor = Color.GREEN

        graphics.rect(x, y, width, height)
        graphics.stroke()
    }

    private drawLandscapeResolution() {
        const { graphics } = this
        if (!graphics) return

        const width = Landscape.WIDTH
        const height = Landscape.HEIGHT
        const x = -width / 2
        let y = -height / 2

        if (EDITOR_NOT_IN_PREVIEW) {
            // ? Make on top of layout in editor
            y = -height + Portrait.HEIGHT / 2
        }

        graphics.strokeColor = Color.CYAN

        graphics.rect(x, y, width, height)
        graphics.stroke()
    }
}
