import { _decorator, Component } from 'cc'
import { MultiBackground } from '../../components/background/MultiBackground'
import { OpacityToggler } from '../../components/common/OpacityToggler'
import DashboardEvents from '../../constants/DashboardEvents'
import DashboardScreens from '../../constants/DashboardScreens'
import SceneKeys from '../../constants/SceneKeys'
import { ScreenKeysEnum } from '../../constants/ScreenKeys'
import { CodeSoundKeys } from '../../constants/SoundKeys'
import { CustomModeLevel } from '../../gameplay/constants/CustomGameData'
import DashboardEventButtonTypes from '../../gameplay/constants/DashboardEventButtonTypes'
import GameModes from '../../gameplay/constants/GameMode'
import GuideKeys from '../../gameplay/constants/GuideKeys'
import MapData from '../../gameplay/constants/MapData'
import ResourcePaths from '../../gameplay/constants/ResourcePaths'
import { CoinManager } from '../../gameplay/manager/CoinManager'
import AnimUtils from '../../utils/AnimUtils'
import AudioUtils from '../../utils/AudioUtils'
import ComponentUtils from '../../utils/ComponentUtils'
import AnimAbortSignal from '../../utils/common/AnimAbortSignal'
import DailyRewardsUtils from '../../utils/DailyRewardsUtils'
import GeneralUtils from '../../utils/GeneralUtils'
import MatchUtils from '../../utils/MatchUtils'
import ResourceUtils from '../../utils/ResourceUtils'
import SceneUtils from '../../utils/SceneUtils'
import ScreenUtils from '../../utils/ScreenUtils'
import SortNodeUtils from '../../utils/SortNodeUtils'
import ThemeUtils from '../../utils/ThemeUtils'
import { AnimSkipOverlay } from '../common/anim-skip-overlay/AnimSkipOverlay'
import { CollectionsContent } from './components/pages/collections/CollectionsContent'
import { HomeContent } from './components/pages/home/HomeContent'
import { PageManager } from './components/pages/PageManager'
import { NavManager } from './components/ui/footer/nav-button/NavManager'

const { ccclass, disallowMultiple, property } = _decorator

const { storage, player, monitorError } = codex

const {
    Match: { Modes },
    Utils: { Array: A },
} = Ludex

// SceneComponent
// Required components: SceneManager

@ccclass('DashboardScene')
@disallowMultiple
export class DashboardScene extends Component {
    @ComponentUtils.required()
    @property(CoinManager)
    private readonly coinManager!: CoinManager

    @ComponentUtils.required()
    @property(NavManager)
    private readonly navManager!: NavManager

    @ComponentUtils.required()
    @property(MultiBackground)
    private readonly bgComp!: MultiBackground

    @ComponentUtils.required()
    @property(PageManager)
    private readonly pageManager!: PageManager

    @ComponentUtils.required()
    @property(AnimSkipOverlay)
    private readonly pageAnimSkipOverlay!: AnimSkipOverlay

    @ComponentUtils.required()
    @property(OpacityToggler)
    private readonly transitionOpacityToggler!: OpacityToggler

    private firstNavigation = true
    private isFirstNavToCollections = true
    private isFirstNavToTournament = true
    private navigationSignal: AnimAbortSignal | null = null

    public onLoad() {
        ComponentUtils.validateRequiredProperties(this)

        this.listenNavigationEvents()
        this.listenDashboardEvents()

        SortNodeUtils.createLayer(this.node, 'Particles', 6)

        this.coinManager.init()
        ThemeUtils.init()

        this.setPlayerLevel()

        this.updateBackgroundImageToCurrentTheme()

        this.pageManager.updateCollectionsContentPayload(ThemeUtils.getCollectionUnlockProgress())
    }

    private listenNavigationEvents() {
        this.node.scene.on(DashboardEvents.REQUEST_NAVIGATION, this.onNavigate, this)
    }

    private listenDashboardEvents() {
        this.node.scene.on(DashboardEvents.PLAY_BUTTON_PRESS, this.handleDashboardPlayButtonPress, this)
        this.node.scene.on(DashboardEvents.CUSTOM_PLAY_BUTTON_PRESS, this.handleCustomPlayButtonPress, this)
        this.node.scene.on(DashboardEvents.DASHBOARD_EVENTS_BUTTON_PRESS, this.handleEventButtonPress, this)
    }

    private setPlayerLevel() {
        let { level = 1 } = player.getGameData() ?? {}
        level = level === 0 ? 1 : level

        player.setGameData({ level })
    }

    public start() {
        //? do not sort
        if (player.getPlayerSetting('music')) {
            this.updateMusic()
        }

        this.pageManager.init()
        this.navManager.selectDefaultNavBtn()

        this.scheduleOnce(this.preloadScene, 5)
        this.scheduleOnce(this.preloadSkins, 3)
        this.scheduleOnce(this.preloadScreens, 10)
    }

    @ComponentUtils.onlyRunInLifeCycle
    private preloadScene(): void {
        if (SceneUtils.isSceneLoaded(SceneKeys.GAME_SCENE)) return

        SceneUtils.preloadGameScene()
    }

    @ComponentUtils.onlyRunInLifeCycle
    private preloadScreens(): void {
        ScreenUtils.preloadScreen(ScreenKeysEnum.LOADING_SCREEN)
        ScreenUtils.preloadScreen(ScreenKeysEnum.NOTIFICATION_SCREEN)

        ScreenUtils.preloadScreen(ScreenKeysEnum.SETTINGS_SCREEN)
        ScreenUtils.preloadScreen(ScreenKeysEnum.GET_COIN_SCREEN)
        ScreenUtils.preloadScreen(ScreenKeysEnum.STAR_CHEST_SCREEN)
        ScreenUtils.preloadScreen(ScreenKeysEnum.DAILY_REWARDS_SCREEN)

        const gameData = player.getGameData()
        const currentLevel = gameData?.level ?? 1

        if (currentLevel >= 13) {
            ScreenUtils.preloadScreen(ScreenKeysEnum.CUSTOM_GAME_SCREEN)
        }
    }

    @ComponentUtils.onlyRunInLifeCycle
    private preloadSkins(): void {
        const unlockedSkins = ThemeUtils.getUnlockedSkins()

        for (const skin of unlockedSkins) {
            const skinPath = `${ResourcePaths.TILE_SKIN_FOLDER}/${skin}`
            ResourceUtils.loadSpriteFramesDirAsync(skinPath)
        }
    }

    @ComponentUtils.onlyRunInLifeCycle
    private attemptCallSubscribeBot(): void {
        if (!GameSDK.isGameSDK('FacebookInstant')) return

        GameSDK.extra.checkAndSubscribeBotAsync().catch((error) => {
            console.warn('subscribeBotAsync', error)
        })
    }

    @ComponentUtils.onlyRunInLifeCycle
    private updateBackgroundImageToCurrentTheme(): void {
        const dashboardStorage = storage.getStorage('dashboard')

        if (dashboardStorage === null || dashboardStorage === undefined) {
            monitorError?.sendException(new Error('DashboardScene: dashboardStorage not found'))
            return
        }

        const animateToCurrentLevel = dashboardStorage.animateToCurrentLevel ?? false
        const lastProgress = { ...dashboardStorage.lastMapPayload }
        const currProgress = ThemeUtils.getCurrentMapProgress()

        const isSameProgress =
            lastProgress.currentTheme === currProgress.currentTheme && lastProgress.nextTheme === currProgress.nextTheme

        const leftTheme = currProgress.currentTheme

        if (!animateToCurrentLevel || isSameProgress || this.isDevToolTimeTravel(lastProgress.id, currProgress.id)) {
            this.bgComp.setCurrentTheme(leftTheme)
        } else {
            this.bgComp.setCurrentTheme(lastProgress.currentTheme)
        }
    }

    private isDevToolTimeTravel(lastProgressId: number, currProgressId: number): boolean {
        const lastMapIndex = A.searchIndex(MapData.mapOrder, (map) => map.id === lastProgressId)
        const currMapIndex = A.searchIndex(MapData.mapOrder, (map) => map.id === currProgressId)

        return lastMapIndex > currMapIndex
    }

    @ComponentUtils.onlyRunInLifeCycle
    private handleDashboardPlayButtonPress(): void {
        this.handlePlayButtonPress(Modes.SINGLE)
    }

    @ComponentUtils.onlyRunInLifeCycle
    private handleCustomPlayButtonPress(): void {
        this.handlePlayButtonPress(GameModes.CUSTOM)
    }

    @ComponentUtils.onlyRunInLifeCycleAsync
    private async handlePlayButtonPress(gameMode: string): Promise<void> {
        try {
            GeneralUtils.setBlockInput(true)
            const currMapPayload = ThemeUtils.getCurrentMapProgress()
            const { level = 1 } = player.getGameData() ?? {}

            await MatchUtils.setMatchCustomDataAsync({ level })
            await MatchUtils.startMatchAsync(gameMode)

            this.updateCurrentMapInStorage(currMapPayload)

            storage.setStorageData('collection', 'lastMapPack', currMapPayload.id)
            storage.setStorageData('collection', 'animateMapPackUnlock', false)

            await this.switchToGameScene(level)
        } catch (error) {
            GeneralUtils.setBlockInput(false)
            console.error(error)
        }
    }

    private async switchToGameScene(level: number): Promise<void> {
        if (SceneUtils.isSceneLoaded(SceneKeys.GAME_SCENE)) {
            await this.transitionOpacityToggler.changeStateFade(true, 0.25)
        }

        SceneUtils.switchToGameScene({ level })
    }

    private updateCurrentMapInStorage(currMap: CurrentMapProgressPayload) {
        storage.setStorageData('dashboard', 'animateToCurrentLevel', false)
        storage.setStorageData('dashboard', 'lastMapPayload', currMap)
    }

    private async updateMusic(): Promise<void> {
        if (AudioUtils.getCurrentMusic() !== CodeSoundKeys.BG_MUSIC) {
            await AudioUtils.setVolumeMusic(0, true)
            await AnimUtils.waitTweenAsync(1)
        }

        AudioUtils.playMusic(CodeSoundKeys.BG_MUSIC)
        // ? Channel 'global_music' has been created in 'LoadScene' as a persistent node for playing background music across all scenes.
        AudioUtils.turnUpMusic()
    }

    @ComponentUtils.onlyRunInLifeCycleAsync
    private async onNavigate(payload: NavigatePayload): Promise<void> {
        if (this.firstNavigation) {
            this.firstNavigation = false
            this.pageManager.unmountAndHideAll()
        }

        switch (payload.to) {
            case DashboardScreens.store:
                // ? Handle store navigation here
                break

            case DashboardScreens.collections:
                await this.triggerPageNavigation(payload)
                await this.handleLogicAfterNavigateToCollections()
                this.cleanupAfterPageNavigation()
                break

            case DashboardScreens.tournaments:
                ScreenUtils.preloadScreen(ScreenKeysEnum.TOURNAMENT_DETAIL_SCREEN)
                await this.triggerPageNavigation(payload)
                this.cleanupAfterPageNavigation()
                break

            case DashboardScreens.leaderboard:
                await this.triggerPageNavigation(payload)
                this.cleanupAfterPageNavigation()
                break

            case DashboardScreens.home:
                await this.handleNavigateToHome(payload)
                break

            default:
                throw new Error('DashboardScene: Unhandled page name')
        }
    }

    @ComponentUtils.onlyRunInLifeCycleAsync
    private async handleLogicAfterNavigateToCollections() {
        const content = this.pageManager
            .getPage(DashboardScreens.collections)
            ?.getContainer()
            .getComponentInChildren(CollectionsContent)

        if (content === undefined || content === null) {
            throw new Error('CollectionsContent not found')
        }

        const signal = new AnimAbortSignal()

        const requireAnimation = content.shouldAnimateThemeBlockUnlock()

        if (requireAnimation) {
            await this.openSkipOverlay(signal)
        }

        await this.tryPlayScrollToCollectionAnim(content, signal)
        await content.playThemeTabUnlockAnim(signal)

        if (requireAnimation) {
            await this.closeSkipOverlay()
        }

        signal.abort()
    }

    private async closeSkipOverlay() {
        await this.pageAnimSkipOverlay.close()
    }

    private async openSkipOverlay(signal: AnimAbortSignal) {
        await this.pageAnimSkipOverlay.open({
            onSkip: () => signal.abort(),
            opacity: 200,
        })
    }

    @ComponentUtils.onlyRunInLifeCycleAsync
    private async tryPlayScrollToCollectionAnim(content: CollectionsContent, signal: AnimAbortSignal) {
        const currentlyUnlockedIndex = ThemeUtils.getCurrentUnlockingThemeIndex()

        if (this.isFirstNavToCollections && currentlyUnlockedIndex !== null) {
            await content.scrollToCurrentUnlockingTheme(currentlyUnlockedIndex, signal)
            this.isFirstNavToCollections = false
        }
    }

    @ComponentUtils.onlyRunInLifeCycle
    private cleanupAfterPageNavigation() {
        this.navManager.setIgnoreInput(false)
    }

    @ComponentUtils.onlyRunInLifeCycleAsync
    private async triggerPageNavigation(payload: NavigatePayload) {
        this.navigationSignal?.abort()
        this.navigationSignal = new AnimAbortSignal()

        await this.pageManager.navigate(payload, this.navigationSignal)
    }

    @ComponentUtils.onlyRunInLifeCycleAsync
    private async handleNavigateToHome(payload: NavigatePayload) {
        this.navigationSignal?.abort()
        this.navigationSignal = new AnimAbortSignal()

        await this.pageManager.navigate(payload, this.navigationSignal, async () => {
            this.initializeHomepageCoinManager()
        })

        await this.handleLogicAfterNavigateToHome()
    }

    private async handleLogicAfterNavigateToHome() {
        if (!DailyRewardsUtils.hasShownPopup() && !this.shouldPlayLevelUpAnimation()) {
            if (await DailyRewardsUtils.hasClaimableRewards()) {
                await DailyRewardsUtils.openPopupScreenAndWaitForClose()
            }
        }

        this.attemptCallSubscribeBot()
        await this.waitUntilCoinManagerFinishesAnyAnimation()
        await this.waitUntilPageAnimSkipOverlayCloses()
        await this.tryNavigateToTournament()
        await this.tryPlayLevelUpAnimation()
        await this.tryShowCustomModeGuide()
    }

    @ComponentUtils.onlyRunInLifeCycleAsync
    private waitUntilCoinManagerFinishesAnyAnimation() {
        return this.coinManager.untilFinishesCollectAnim()
    }

    @ComponentUtils.onlyRunInLifeCycleAsync
    private waitUntilPageAnimSkipOverlayCloses() {
        return this.pageAnimSkipOverlay.untilClosed()
    }

    @ComponentUtils.onlyRunInLifeCycleAsync
    private async tryNavigateToTournament(): Promise<void> {
        const { animateTournamentLeader } = storage.getStorage('tournament')
        if (animateTournamentLeader) {
            return await this.navManager.selectByType(DashboardScreens.tournaments)
        }

        if (!this.isFirstNavToTournament) return
        this.isFirstNavToTournament = false

        try {
            if (!('getTournamentAsync' in GameSDK)) {
                throw new Error('Unavailable feature: tournament')
            }

            const tournament = await GameSDK.getTournamentAsync()

            if (tournament.getEndTime() < Date.now() / 1000) {
                return await this.navManager.selectByType(DashboardScreens.tournaments)
            }
        } catch (error) {
            // safe to ignore
        }
    }

    private shouldPlayLevelUpAnimation() {
        if (!this.enabledInHierarchy) return false

        const homepage = this.pageManager
            .getPage(DashboardScreens.home)
            ?.getContainer()
            .getComponentInChildren(HomeContent)

        if (homepage === undefined || homepage === null) {
            return false
        }

        const shouldPlayUnlockAnimation = homepage.shouldPlayUnlockAnimation()
        const shouldPlayPackUnlockAnimation = this.shouldPlayPackUnlockAnimation()

        return shouldPlayUnlockAnimation || shouldPlayPackUnlockAnimation
    }

    @ComponentUtils.onlyRunInLifeCycleAsync
    private async tryPlayLevelUpAnimation() {
        const homepage = this.pageManager
            .getPage(DashboardScreens.home)
            ?.getContainer()
            .getComponentInChildren(HomeContent)

        if (homepage === undefined || homepage === null) {
            throw new Error('DashboardScene: Homepage not found')
        }

        const signal = new AnimAbortSignal()
        if (this.navigationSignal) signal.chain(this.navigationSignal)

        const shouldPlayUnlockAnimation = homepage.shouldPlayUnlockAnimation()
        const shouldPlayPackUnlockAnimation = this.shouldPlayPackUnlockAnimation()

        if (!shouldPlayUnlockAnimation) {
            return
        }

        if (shouldPlayPackUnlockAnimation) {
            await this.pageAnimSkipOverlay.open({
                onSkip: () => signal.abort(),
                opacity: 150,
            })
        }

        await homepage.playUnlockProgressAnimation(signal)

        this.updateBackgroundImageToCurrentTheme()

        if (shouldPlayPackUnlockAnimation) {
            await this.pageAnimSkipOverlay.close()
            await signal.callAsync(() => this.navManager.selectByType(DashboardScreens.collections))
        }
    }

    private shouldPlayPackUnlockAnimation(): boolean {
        const dashboardStorage = storage.getStorage('dashboard')

        if (dashboardStorage === null || dashboardStorage === undefined) {
            throw new Error('DashboardScene: dashboardStorage not found')
        }

        const lastProgress = dashboardStorage.lastMapPayload
        const currProgress = ThemeUtils.getCurrentMapProgress()

        const lastMapIndex = A.searchIndex(MapData.mapOrder, (map) => map.id === lastProgress.id)
        const currMapIndex = A.searchIndex(MapData.mapOrder, (map) => map.id === currProgress.id)
        const isSameMap = lastMapIndex === currMapIndex

        return !isSameMap
    }

    @ComponentUtils.onlyRunInLifeCycle
    private initializeHomepageCoinManager() {
        const homepage = this.pageManager
            .getPage(DashboardScreens.home)
            ?.getContainer()
            .getComponentInChildren(HomeContent)

        if (homepage === undefined || homepage === null) {
            throw new Error('DashboardScene: Homepage not found')
        }

        this.coinManager.setTargetContainer(homepage.getCoinContainer())
        this.coinManager.emitCoinAmount()
    }

    @ComponentUtils.onlyRunInLifeCycleAsync
    private async tryShowCustomModeGuide() {
        const { level = 1, guideItemShowed = [] } = player.getGameData() ?? {}
        if (level !== CustomModeLevel || A.has(guideItemShowed, GuideKeys.CUSTOM_MODE)) return

        guideItemShowed.push(GuideKeys.CUSTOM_MODE)
        player.setGameData({ guideItemShowed })

        const homepage = this.pageManager
            .getPage(DashboardScreens.home)
            ?.getContainer()
            .getComponentInChildren(HomeContent)

        if (homepage === undefined || homepage === null) {
            throw new Error('DashboardScene: Homepage not found')
        }

        await homepage.showCustomGuide()
    }

    @ComponentUtils.onlyRunInLifeCycle
    private handleEventButtonPress(payload: DashboardEventButtonPayload) {
        switch (payload.type) {
            case DashboardEventButtonTypes.STAR_CHEST:
                ScreenUtils.openScreen(ScreenKeysEnum.STAR_CHEST_SCREEN)
                break
            case DashboardEventButtonTypes.DAILY_REWARDS:
                DailyRewardsUtils.openPopupScreen()
                break
            case DashboardEventButtonTypes.BALLOON:
                ScreenUtils.showNotificationScreenWithLocalizedKey('error.soon')
                break
            default:
                throw new Error('DashboardScene: Unhandled event button type')
        }
    }

    public handleTestLayoutButtonClicked() {
        // SceneUtils.switchScene(SceneKeys.TEST_LAYOUT_SCENE)
    }
}
