import { UITransform } from 'cc'
import CocosUtils from '../../utils/CocosUtils'
import type { Gameplay } from '../Gameplay'
import GameplayEvents from '../constants/GameEvents'
import GuideKeys from '../constants/GuideKeys'
import type GuidelinesManager from '../manager/GuidelinesManager'
import NewbieSteps from './NewbieSteps'

//? The ratio scale in Portrait mode
const GUIDE_TEXT_BOX_HEIGHT = 223

class Guide01Newbie {
    private gameplay: Gameplay
    private manager: GuidelinesManager

    private currentStep!: number

    constructor(manager: GuidelinesManager) {
        this.manager = manager
        this.gameplay = manager.gameplay
    }

    public setup(): void {
        this.currentStep = 0

        this.manager.node.scene.on(GameplayEvents.GRID_MATCH_SUCCESS, this.showNextStep, this)
        this.manager.node.scene.on(GameplayEvents.GRID_MATCH_FAIL, this.showNextStep, this)
        this.manager.node.scene.on(GameplayEvents.GRID_RESIZE, this.handleViewResized, this)

        this.manager.guideUi.guideSkipButton.on(UITransform.EventType.SIZE_CHANGED, this.alignSkipButton, this)

        this.showStep(this.currentStep)

        this.manager.node.scene.emit(GameplayEvents.HIDE_UI)
    }

    private showNextStep(): void {
        if (this.currentStep + 1 < NewbieSteps.length) {
            this.currentStep += 1
            this.showStep(this.currentStep)
        } else {
            this.close()
        }
    }

    private async showStep(step: number): Promise<void> {
        const newbieStep = NewbieSteps[step]
        if (!newbieStep) return

        const { board, effectManager } = this.gameplay

        const startTileCoords = newbieStep.startTile
        const endTileCoords = newbieStep.endTile

        const startTile = board.getTileAt(startTileCoords.x, startTileCoords.y)
        const endTile = board.getTileAt(endTileCoords.x, endTileCoords.y)

        if (!startTile || !endTile) return

        const startPosition = startTile.node.worldPosition
        const endPosition = endTile.node.worldPosition

        effectManager.resetMasks()

        effectManager.setMaskProperties(0, startPosition, startTile.node.worldScale)
        effectManager.setMaskProperties(1, endPosition, endTile.node.worldScale)
        effectManager.setBackgroundMaskActive(true)

        const {
            guideUi: { guideHand, guideTextBox, guideSkipButton },
        } = this.manager

        guideHand.node.active = true
        guideSkipButton.active = true

        guideHand.playHandAnimation(startPosition, endPosition)

        this.alignGuideTextBox()
        this.alignSkipButton()

        await guideTextBox.hide()
        guideTextBox.setLocalizedText(newbieStep.localeTextKey)
        await guideTextBox.show()

        board.setIgnoreInput(true)
        startTile.setIgnoreInput(false)
        endTile.setIgnoreInput(false)

        this.gameplay.comboManager.setEmitEvent(false)
    }

    private handleViewResized(): void {
        const newbieStep = NewbieSteps[this.currentStep]
        if (!newbieStep) return

        const { board, effectManager } = this.gameplay

        const startTileCoords = newbieStep.startTile
        const endTileCoords = newbieStep.endTile

        const startTile = board.getTileAt(startTileCoords.x, startTileCoords.y)
        const endTile = board.getTileAt(endTileCoords.x, endTileCoords.y)

        if (!startTile || !endTile) return

        const startPosition = startTile.node.worldPosition
        const endPosition = endTile.node.worldPosition

        effectManager.setMaskProperties(0, startPosition, startTile.node.worldScale)
        effectManager.setMaskProperties(1, endPosition, endTile.node.worldScale)

        this.alignGuideTextBox()
        this.alignSkipButton()
    }

    private alignGuideTextBox(): void {
        const { board } = this.gameplay
        const {
            guideUi: { guideTextBox },
        } = this.manager

        const boardBoundingBox = board.getBoundingBoxWorld()

        const boardWidth = boardBoundingBox.width
        const bottomBoard = boardBoundingBox.yMin
        const guideBoxNewXPosition = boardBoundingBox.center.x
        const guideBoxNewYPosition = guideTextBox.widget.bottom + (bottomBoard - guideTextBox.widget.bottom) / 2

        guideTextBox.node.setWorldPosition(guideBoxNewXPosition, guideBoxNewYPosition, 0)

        const guideBoxHeight = Math.max(
            Math.min(GUIDE_TEXT_BOX_HEIGHT, (bottomBoard - guideTextBox.widget.bottom) * 0.85),
            10, // Minimum height
        )

        guideTextBox.uiTransform.setContentSize(boardWidth, guideBoxHeight)
    }

    private alignSkipButton = () => {
        const { board } = this.gameplay
        const {
            guideUi: { guideSkipButton },
        } = this.manager
        if (!guideSkipButton) return

        const skipContainer = guideSkipButton.parent
        if (!skipContainer) return

        const boardBoundingBox = board.getBoundingBoxWorld()

        const topBoard = boardBoundingBox.yMax
        const rightBoard = boardBoundingBox.xMax

        const topScreen = CocosUtils.cssSizeToWorldSize(CocosUtils.getScreenHeightCss())

        const guideSkipButtonXPosition = rightBoard - skipContainer.getComponent(UITransform)!.width / 2
        const guideSkipButtonYPosition = Math.min(topBoard + 100, topBoard + (topScreen - topBoard) / 2)

        //? Change position of the container instead of the button because ExtendedButton can't change its position
        skipContainer.setWorldPosition(guideSkipButtonXPosition, guideSkipButtonYPosition, 0)
    }

    public async close(): Promise<void> {
        const { board, effectManager } = this.gameplay

        this.manager.node.scene.off(GameplayEvents.GRID_MATCH_SUCCESS, this.showNextStep, this)
        this.manager.node.scene.off(GameplayEvents.GRID_MATCH_FAIL, this.showNextStep, this)
        this.manager.node.scene.off(GameplayEvents.GRID_RESIZE, this.handleViewResized, this)

        this.manager.guideUi.guideSkipButton.off(UITransform.EventType.SIZE_CHANGED, this.alignSkipButton, this)

        board.setIgnoreInput(false)

        await Promise.all([this.manager.guideUi.guideTextBox.hide(), effectManager.setBackgroundMaskActive(false)])

        this.manager.guideUi.guideHand.node.active = false

        this.manager.completeGuide(GuideKeys.NEWBIE)
    }
}

export default Guide01Newbie
