import { _decorator, Component, find, game, Mask } from 'cc'
import GameEvents from '../../constants/GameEvents'
import GameWorld from '../../constants/GameWorld'
import SceneKeys from '../../constants/SceneKeys'
import { ScreenKeysEnum, ScreenNames } from '../../constants/ScreenKeys'
import { CodeSoundKeys } from '../../constants/SoundKeys'
import GameplayEvents from '../../gameplay/constants/GameEvents'
import TotalLevel from '../../gameplay/constants/TotalLevel'
import { CoinManager } from '../../gameplay/manager/CoinManager'
import AnimUtils from '../../utils/AnimUtils'
import AudioUtils from '../../utils/AudioUtils'
import ComponentUtils from '../../utils/ComponentUtils'
import MatchUtils from '../../utils/MatchUtils'
import SceneUtils from '../../utils/SceneUtils'
import ScreenUtils from '../../utils/ScreenUtils'
import SortNodeUtils from '../../utils/SortNodeUtils'

const { ccclass, property, disallowMultiple } = _decorator

const { player, event } = codex
const { Layout } = GameWorld
const {
    Match: { Modes },
    Events,
} = Ludex

@ccclass('GameScene')
@disallowMultiple
export class GameScene extends Component {
    @ComponentUtils.required()
    @property(CoinManager)
    private readonly coinManager!: CoinManager

    public __preload() {
        ComponentUtils.validateRequiredProperties(this)

        SortNodeUtils.createLayer(this.node, 'Particles', 6)
        SortNodeUtils.createLayer(this.node, 'GameUnpackLabel', 5)

        this.listenGameEvents()
    }

    protected onLoad(): void {
        this.coinManager.init()
    }

    private listenGameEvents() {
        // @ts-expect-error - This event is custom event
        game.on(GameEvents.ORIENTATION_CHANGED, this.handleGameMaskWhenLayoutChanged)

        this.node.scene.on(GameplayEvents.GAME_EXIT, this.handleGameExit)
        this.node.scene.on(GameplayEvents.SHOW_CHALLENGE_RESULTS, this.handleShowResults)
        this.node.scene.on(GameplayEvents.UPDATE_MUSIC, this.updateMusic)
    }

    private readonly handleGameExit = () => {
        SceneUtils.switchToDashboardScene()
    }

    private readonly handleShowResults = () => {
        SceneUtils.switchScene(SceneKeys.CHALLENGE_RESULT_SCENE)
    }

    private readonly handleGameMaskWhenLayoutChanged = (layout: string) => {
        const gameObjectsNode = find('Canvas/Game/GameObjects')
        if (!gameObjectsNode) return

        const maskComponent = gameObjectsNode.getComponent(Mask)
        if (!maskComponent) return

        maskComponent.enabled = layout === Layout.Landscape
    }

    public start() {
        if (player.getPlayerSetting('music')) {
            this.updateMusic()
        }

        this.scheduleOnce(this.preloadScenes, 20)
        this.scheduleOnce(this.preloadScreens, 10)
    }

    @ComponentUtils.onlyRunInLifeCycle
    private preloadScenes(): void {
        SceneUtils.preloadDashboardScene()
    }

    @ComponentUtils.onlyRunInLifeCycle
    private preloadScreens(): void {
        const gameData = player.getGameData()
        const currentLevel = gameData?.level ?? 1

        // ? Preload hard level screen for every 5th level
        if (currentLevel % 5 === 0) {
            ScreenUtils.preloadScreen(ScreenKeysEnum.HARD_LEVEL_SCREEN)
        }

        if (currentLevel >= TotalLevel) {
            ScreenUtils.preloadScreen(ScreenKeysEnum.CONGRATULATION_SCREEN)
            ScreenUtils.preloadScreen(ScreenKeysEnum.START_OVER_SCREEN)
        }

        ScreenUtils.preloadScreen(ScreenKeysEnum.PAUSE_SCREEN)
        ScreenUtils.preloadScreen(ScreenKeysEnum.LOADING_SCREEN)
        ScreenUtils.preloadScreen(ScreenKeysEnum.GET_ITEM_SCREEN)
        ScreenUtils.preloadScreen(ScreenKeysEnum.NOTIFICATION_SCREEN)

        this.node.scene.once(GameplayEvents.GAME_FINISH, () => {
            ScreenUtils.preloadScreen(ScreenKeysEnum.LEVEL_COMPLETE_SCREEN)
            ScreenUtils.preloadScreen(ScreenKeysEnum.GET_REWARDS_SCREEN)
        })

        event.once(Events.OPENED_SCREEN, (payload: SwitchScreenPayload) => {
            const { screenName } = payload
            if (screenName === ScreenNames[ScreenKeysEnum.PAUSE_SCREEN]) {
                ScreenUtils.preloadScreen(ScreenKeysEnum.QUIT_LEVEL_SCREEN)
                ScreenUtils.preloadScreen(ScreenKeysEnum.START_OVER_SCREEN)
                ScreenUtils.preloadScreen(ScreenKeysEnum.CUSTOM_GAME_SCREEN)
            }
        })
    }

    private readonly updateMusic = async () => {
        const mode = MatchUtils.getMatchMode()
        const isTournament = mode === Modes.TOURNAMENT
        const sound = isTournament ? CodeSoundKeys.TOURNAMENT_MUSIC : CodeSoundKeys.BG_MUSIC

        if (AudioUtils.getCurrentMusic() !== sound) {
            await AudioUtils.setVolumeMusic(0, true)
            await AnimUtils.waitTweenAsync(1)
        }

        //? Restart music in tournament mode
        AudioUtils.playMusic(sound, isTournament)

        AudioUtils.setVolumeMusic(isTournament ? 0.4 : 0.3, true)
    }
}
