import { _decorator, Component, instantiate, Layout, Node, Prefab, UIOpacity, UITransform } from 'cc'
import ComponentUtils from '../../../../../utils/ComponentUtils'
import type AnimAbortSignal from '../../../../../utils/common/AnimAbortSignal'
import type { TournamentData } from '../../../../../utils/TournamentUtils'
import { TournamentBlock } from './TournamentBlock'

const { ccclass, property } = _decorator
const { player } = codex

@ccclass('TournamentsBoard')
export class TournamentsBoard extends Component {
    @property(Prefab)
    private tournamentBlockPrefab!: Prefab

    private tournamentBlockArray: TournamentBlock[] = []

    @property(Layout)
    public layout!: Layout

    @property(Node)
    public createTournamentButton!: Node

    @property(UITransform)
    private viewport!: UITransform

    private tournamentBlockContentHeight!: number

    public uiOpacity!: UIOpacity

    protected onLoad() {
        this.uiOpacity = this.getComponent(UIOpacity)!

        this.validateRequiredComponents()
    }

    public start() {
        for (let index = 0; index < 6; index++) {
            this.addTournamentBlock()
        }

        const tournamentBlock = this.tournamentBlockArray[0]

        if (!tournamentBlock) return

        this.tournamentBlockContentHeight = tournamentBlock.uiTransform.contentSize.height + this.layout.spacingY
    }

    private validateRequiredComponents(): void {
        ComponentUtils.lazyValidateProperties(this)
    }

    public updateData(data: TournamentData[]) {
        let hasPlayerTournament = false

        data.forEach((tournament, index) => {
            let tournamentBlock = this.tournamentBlockArray[index]

            if (tournament.payload.playerId === player.getPlayerId()) {
                hasPlayerTournament = true
            }

            if (!tournamentBlock) {
                tournamentBlock = this.addTournamentBlock()
            }
            tournamentBlock.node.active = true

            tournamentBlock.updateInfo(tournament)
        })

        if (hasPlayerTournament) return

        this.setCreateTournamentButtonActive(true)
    }

    public addTournamentBlock() {
        const tournamentBlockNode = instantiate(this.tournamentBlockPrefab)

        this.layout.node.addChild(tournamentBlockNode)
        tournamentBlockNode.active = false

        const tournamentBlock = tournamentBlockNode.getComponent(TournamentBlock)!
        this.tournamentBlockArray.push(tournamentBlock)

        return tournamentBlock
    }

    public removeTournamentBlocks(): void {
        const tournamentBlocks = this.getActiveTournamentBlocks()

        for (const block of tournamentBlocks) {
            block.node.active = false
        }

        this.setCreateTournamentButtonActive(false)
    }

    private getActiveTournamentBlocks(): TournamentBlock[] {
        return this.tournamentBlockArray.filter((tournamentBlock) => {
            return tournamentBlock.node.parent === this.layout.node
        })
    }

    private setCreateTournamentButtonActive(active: boolean): void {
        if (!GameSDK.isGameSDK('FacebookInstant')) return

        if (active) {
            this.layout.node.addChild(this.createTournamentButton)
        } else {
            this.layout.node.removeChild(this.createTournamentButton)
        }

        this.createTournamentButton.active = active
    }

    public async showTournamentBlocks(contentHeight: number, signal: AnimAbortSignal): Promise<void> {
        const tournaments = this.getActiveTournamentBlocks()

        if (tournaments.length < 1) return

        const minTournamentsShow = Math.ceil(contentHeight / this.tournamentBlockContentHeight)
        const numberOfTournamentPlayShow = Math.min(tournaments.length, minTournamentsShow)
        const duration = 0.3

        await Promise.all(
            tournaments.map(async (tournament, index) => {
                if (index < numberOfTournamentPlayShow) {
                    await tournament.runShowPopUpAnimation(index * 0.1, duration, signal)
                    return
                }

                tournament.uiOpacity.opacity = 255
            }),
        )
    }

    public updateVisibility(): void {
        const { yMax: top, yMin: bot } = this.viewport.getBoundingBoxToWorld()

        const blocks = this.getActiveTournamentBlocks()

        for (const tournamentBlock of blocks) {
            const uiTransform = tournamentBlock.uiTransform

            if (uiTransform === undefined) continue

            const { yMax: topBlock, yMin: botBlock } = uiTransform.getBoundingBoxToWorld()

            const isOffScreen = topBlock < bot || botBlock > top

            tournamentBlock.uiOpacity.opacity = isOffScreen ? 0 : 255
        }
    }
}
