import { _decorator, Component, Label, Node, Sprite, SpriteFrame, tween, UIOpacity, UITransform, Vec3 } from 'cc'
import { Localize } from '../../../../../components/text/Localize'
import TournamentEvents from '../../../../../constants/Tournament'
import ThemeKeys from '../../../../../gameplay/constants/ThemeKeys'
import { ThemePreviewResourcesMapping } from '../../../../../gameplay/constants/ThemeResourcesPath'
import { TopLeader } from '../../../../../screens/tournament-detail-screen/components/TopLeader'
import AnimUtils from '../../../../../utils/AnimUtils'
import ComponentUtils from '../../../../../utils/ComponentUtils'
import type AnimAbortSignal from '../../../../../utils/common/AnimAbortSignal'
import LeaderboardUtils from '../../../../../utils/LeaderboardUtils'
import ResourceUtils from '../../../../../utils/ResourceUtils'
import ScreenUtils from '../../../../../utils/ScreenUtils'
import TournamentUtils, { type TournamentData } from '../../../../../utils/TournamentUtils'
import type { ITournamentLeadersPayload } from './TournamentContent'

const { ccclass, property } = _decorator

const { leaderboard, player, language } = codex

const {
    Utils: { Time: T, Object: O, Array: A, Number: N },
} = Ludex

@ccclass('TournamentBlock')
export class TournamentBlock extends Component {
    @ComponentUtils.required()
    @property(Node)
    private readonly container!: Node

    @ComponentUtils.required()
    @property(Label)
    private readonly title!: Label

    @ComponentUtils.required()
    @property({ type: Localize, group: 'Localize' })
    private readonly titleLocalizer!: Localize

    @ComponentUtils.required()
    @property({ type: Localize, group: 'Localize' })
    private readonly timeLocalizer!: Localize

    @ComponentUtils.required()
    @property({ type: Localize, group: 'Localize' })
    private readonly playerCountLocalizer!: Localize

    @ComponentUtils.required()
    @property({ type: Localize, group: 'Localize' })
    private readonly playButtonLocalize!: Localize

    @ComponentUtils.required()
    @property(UITransform)
    public uiTransform!: UITransform

    @ComponentUtils.required()
    @property(UIOpacity)
    public uiOpacity!: UIOpacity

    @ComponentUtils.required()
    @property({ type: Sprite, group: 'Banner' })
    private readonly banner!: Sprite

    @ComponentUtils.required()
    @property({ type: Node, group: 'Banner' })
    private readonly bannerMask!: Node

    @ComponentUtils.required()
    @property({ type: Node, group: 'Banner' })
    private readonly bannerLoadingRing!: Node

    @ComponentUtils.required()
    @property(TopLeader)
    private readonly topLeader!: TopLeader

    private payload!: TournamentData

    private leadersPayload!: ITournamentLeadersPayload
    private hasPlayedAnimation = false

    protected __preload(): void {
        ComponentUtils.validateRequiredProperties(this, TournamentBlock)
    }

    public async updateInfo(tournament: TournamentData): Promise<void> {
        this.payload = tournament

        const {
            endTime,
            title,
            payload: { playerName },
            tournamentType,
        } = tournament
        const { leaderboardId, themeId = ThemeKeys.AUTUMN } = tournament.payload

        if (!leaderboardId) return
        this.topLeader.setVisible(false)
        this.topLeader.node.active = false

        this.setPlayerCountWaiting()

        if (tournamentType === 'PAGE_HOSTED') {
            this.updateTitleTournamentTitle(title)
        } else {
            this.updateTitleWithPlayerName(playerName ?? title)
        }

        this.updateEndTime(endTime)
        this.updateTournamentBanner(themeId)

        const leadersPayload: ITournamentLeadersPayload = {
            leaders: [],
            amountPlayer: 0,
        }

        await this.fetchAndUpdateLeadersPayload(leaderboardId, leadersPayload, title)
    }

    @ComponentUtils.onlyRunInLifeCycleAsync
    private async fetchAndUpdateLeadersPayload(
        leaderboardId: string,
        leadersPayload: ITournamentLeadersPayload,
        title: string,
    ) {
        let payload = leadersPayload
        try {
            const result = await this.getLeadersPayload(leaderboardId)
            if (result) {
                payload = result
            }
        } catch (error) {
            console.error(`Fetch lb for tournament ${title}`, error)
        }

        this.updateLeadersPayload(payload)
    }

    @ComponentUtils.onlyRunInLifeCycle
    private updateLeadersPayload(leadersPayload: ITournamentLeadersPayload) {
        this.leadersPayload = leadersPayload
        const { player, amountPlayer, leaders } = this.leadersPayload

        this.updatePlayerCount(amountPlayer)
        this.updatePlayButtonLabel(player !== undefined)
        this.updateTopLeader(leaders ?? [])
    }

    private updateTitleWithPlayerName(name?: string): void {
        if (name === undefined) return
        this.titleLocalizer.enabled = true
        this.titleLocalizer.setParams([name])
    }

    private updateTitleTournamentTitle(title: string): void {
        this.titleLocalizer.enabled = false
        this.title.string = title
    }

    private updateEndTime(endTime: number): void {
        try {
            const timeString = T.getRelativeTime(endTime, language.getCurrentLocale())
            this.timeLocalizer.key = 'game.endsIn'
            this.timeLocalizer.setParams([timeString])
        } catch (error) {
            this.timeLocalizer.key = 'game.endsSoon'
        }
    }

    private updatePlayerCount(count: number): void {
        const shortPlayerNumber = N.getShortNumber(count, 3)

        if (count === 1) {
            this.playerCountLocalizer.key = 'game.player'
        } else {
            this.playerCountLocalizer.key = 'game.nPlayers'
            this.playerCountLocalizer.setParams([shortPlayerNumber])
        }
    }

    private setPlayerCountWaiting(): void {
        this.playerCountLocalizer.key = 'status.loading'
    }

    private async updateTopLeader(leaders: Ludex.Plugins.Leaderboards.Leader[]): Promise<void> {
        if (leaders.length === 0) return

        let topLeader = leaders[0]
        if (!topLeader) return

        //? In production, leaders are already sorted
        if (Ludex.Configs.Mockup.Leaderboards.Enabled) {
            topLeader = A.search(leaders, (leader) => leader.rank === 1) ?? topLeader
        }

        this.topLeader.node.active = true
        this.topLeader.updateInfo(topLeader)
        this.topLeader.runShowUpAnimation(0, 0.2)
    }

    private async updateTournamentBanner(themeId: ThemeKeys): Promise<void> {
        let sprite = ResourceUtils.getAsset(`${ThemePreviewResourcesMapping[themeId]}/spriteFrame`, SpriteFrame)

        if (!sprite) {
            this.bannerMask.active = true
            this.bannerLoadingRing.active = true
            sprite = await ResourceUtils.loadThemePreviewAsync(themeId)
        }

        if (!sprite) {
            //? Fallback to summer theme
            sprite = await ResourceUtils.loadThemePreviewAsync(ThemeKeys.SUMMER)
        }

        if (!this.banner || !sprite) return

        this.banner.spriteFrame = sprite
        this.bannerMask.active = false
        this.bannerLoadingRing.active = false
    }

    private updatePlayButtonLabel(isPlayerInTournament: boolean): void {
        this.playButtonLocalize.key = isPlayerInTournament ? 'game.continue' : 'game.join'
    }

    private async getLeadersPayload(leaderboardId: string): Promise<ITournamentLeadersPayload | null> {
        const leaderboardName = leaderboard.getLeaderboardName(leaderboardId)
        if (!leaderboardName) return null

        const leaderboardData = await LeaderboardUtils.getAllLeaderboardAsync(leaderboardId)
        if (!leaderboardData) return null

        const leaders = O.vals(leaderboardData.leaders)
        leaders.sort((a, b) => a.rank - b.rank)

        const playerId = player.getPlayerId()
        const playerData = await leaderboard.getLeadersByPlayerIdsAsync(leaderboardName, [playerId])

        const leadersPayload: ITournamentLeadersPayload = {
            leaders,
            amountPlayer: leaderboardData.amountPlayer ?? 0,
        }

        if (playerData.length > 0) {
            leadersPayload.player = playerData[0]
        }

        return leadersPayload
    }

    public async runShowPopUpAnimation(delay: number, duration: number, signal: AnimAbortSignal): Promise<void> {
        this.node.scale = new Vec3(0.8, 0.8, 0.8)
        this.uiOpacity.opacity = 0

        const position = this.container.position.clone()
        this.container.position = new Vec3(position.x, position.y - 100, position.z)

        const tween1 = tween(this.node).delay(delay)
        tween1.to(duration, { scale: Vec3.ONE }, { easing: 'elasticOut' })

        const tween2 = tween(this.container).delay(delay)
        tween2.to(duration, { position }, { easing: 'backOut' })

        const tween3 = tween(this.uiOpacity).delay(delay)
        tween3.to(duration, { opacity: 255 }, { easing: 'cubicOut' })

        const cb = () => {
            this.node.setScale(Vec3.ONE)
            this.container.setPosition(position)
            this.uiOpacity.opacity = 255
        }

        signal.addAbortListener(cb)

        await Promise.all([
            AnimUtils.runTweenAsync(tween1, signal),
            AnimUtils.runTweenAsync(tween2, signal),
            AnimUtils.runTweenAsync(tween3, signal),
        ])

        signal.removeAbortListener(cb)
    }

    public handleOnTournamentClicked(): void {
        const tournamentId = this.payload.id || ''
        const isTournamentExpired = TournamentUtils.validateTournamentExpired(tournamentId)

        if (isTournamentExpired) {
            this.node.scene.emit(TournamentEvents.RELOAD_TOURNAMENT)
            ScreenUtils.showNotificationScreenWithLocalizedKey('error.tourEnded', 2)
            return
        }

        try {
            const tournamentData = JSON.stringify(this.payload)
            const tournamentContextType = this.leadersPayload.player ? 'tournament_end' : 'tournament_start'
            ScreenUtils.showTournamentDetailScreen(tournamentData, tournamentContextType, this.hasPlayedAnimation)
            this.hasPlayedAnimation = true
        } catch (error) {
            console.warn('handleOnTournamentClicked', error)
        }
    }
}
