import { _decorator, Component, Node, Sprite } from 'cc'
import { ParticleSystem } from '../../particles/ParticleSystem'
import ComponentUtils from '../../utils/ComponentUtils'

const { ccclass, property } = _decorator

@ccclass('ItemCollectionFX')
export class ItemCollectionFX extends Component {
    @ComponentUtils.required()
    @property(Sprite)
    readonly sprite!: Sprite

    @ComponentUtils.required()
    @property(Node)
    readonly standardParticlesContainer!: Node

    @ComponentUtils.required()
    @property(Node)
    readonly quickParticlesContainer!: Node

    private standardParticles: ParticleSystem[] = []
    private quickParticles: ParticleSystem[] = []

    protected __preload(): void {
        this.standardParticles = this.standardParticlesContainer.getComponentsInChildren(ParticleSystem)
        this.quickParticles = this.quickParticlesContainer.getComponentsInChildren(ParticleSystem)
        ComponentUtils.validateRequiredProperties(this, ItemCollectionFX)
    }

    protected onDisable(): void {
        this.standardParticlesContainer.active = false
        this.quickParticlesContainer.active = false
    }

    public playExplosionParticles(useQuickVersion = false): void {
        if (useQuickVersion) {
            this.quickParticlesContainer.active = true
            this.playParticles(this.quickParticles)
        } else {
            this.standardParticlesContainer.active = true
            this.playParticles(this.standardParticles)
        }
    }

    private playParticles(particles: ParticleSystem[]): void {
        for (const particle of particles) {
            particle.play()
        }
    }
}
