import { CCFloat, Vec2, _decorator } from 'cc'
const { ccclass, property } = _decorator

export default class SecondOrderDynamics {
    // previous input
    private xp: Vec2

    // state variables
    private y: Vec2
    private yd: Vec2

    // dynamics constants
    private k1: number
    private k2: number
    private k3: number

    /**
     * Creates a new second order dynamics object
     * @param xi initial state
     * @param config dynamics configuration
     */
    constructor(xi: Vec2, config?: SODConfig) {
        const nConfig = config ?? {
            response: 1,
            dampen: 1,
            eager: 1,
        }

        // compute constants
        this.k1 = SecondOrderDynamics.calcK1(nConfig)
        this.k2 = SecondOrderDynamics.calcK2(nConfig)
        this.k3 = SecondOrderDynamics.calcK3(nConfig)

        // initialize state variables
        this.xp = xi.clone()
        this.y = xi.clone()
        this.yd = new Vec2(0, 0)
    }

    /**
     * Updates the dynamics object
     * @param deltaTime Delta time
     * @param target Target value
     * @param change Target's rate of change. Leave null to estimate from previous value
     * @returns The calculated value
     */
    public update(deltaTime: number, target: Vec2, change: Vec2 | null = null): Vec2 {
        let c = change
        if (c === null) {
            // estimate velocity
            c = new Vec2((target.x - this.xp.x) / deltaTime, (target.y - this.xp.y) / deltaTime)

            this.xp.x = target.x
            this.xp.y = target.y
        }

        // clamp k2 to guarantee stability without jitter
        const k2Stable = Math.max(this.k2, (deltaTime * deltaTime) / 2 + (deltaTime * this.k1) / 2, deltaTime * this.k1)

        // integrate position by velocity
        this.y.x = this.y.x + deltaTime * this.yd.x
        this.y.y = this.y.y + deltaTime * this.yd.y

        // integrate velocity by acceleration
        this.yd.x = this.yd.x + (deltaTime * (target.x + this.k3 * c.x - this.y.x - this.k1 * this.yd.x)) / k2Stable

        this.yd.y = this.yd.y + (deltaTime * (target.y + this.k3 * c.y - this.y.y - this.k1 * this.yd.y)) / k2Stable

        return this.y
    }

    public setDynamics(config: SODConfig): void {
        // compute constants
        this.k1 = SecondOrderDynamics.calcK1(config)
        this.k2 = SecondOrderDynamics.calcK2(config)
        this.k3 = SecondOrderDynamics.calcK3(config)
    }

    public reset(xi: Vec2) {
        this.xp.set(xi.x, xi.y)
        this.y.set(xi.x, xi.y)
        this.yd.set(0, 0)
    }

    public get currentState(): Vec2 {
        return this.y
    }

    private static calcK1(config: SODConfig): number {
        return config.dampen / (Math.PI * config.response)
    }

    private static calcK2(config: SODConfig): number {
        return 1 / (2 * Math.PI * config.response * (2 * Math.PI * config.response))
    }

    private static calcK3(config: SODConfig): number {
        return (config.eager * config.dampen) / (2 * Math.PI * config.response)
    }
}

@ccclass('SODConfig')
export class SODConfig {
    @property({
        type: CCFloat,
        displayName: 'Responsiveness',
        tooltip: 'Higher values will result shorter response times.',
    })
    public response: number = 200

    @property({
        type: CCFloat,
        displayName: 'Dampening',
        tooltip: 'Higher values will result in less overshoot and oscillation.',
    })
    public dampen: number = 20

    @property({
        type: CCFloat,
        displayName: 'Eagerness',
        tooltip:
            'Higher values will result in more overshoot and oscillation, negative values will result in anticipation.',
    })
    public eager: number = 0
}
