import { Component, Node, UIOpacity, UITransform, Vec3, _decorator, tween } from 'cc'
import { Localize } from '../../../../../../../../components/text/Localize'
import AnimUtils from '../../../../../../../../utils/AnimUtils'
import ComponentUtils from '../../../../../../../../utils/ComponentUtils'
import type AnimAbortSignal from '../../../../../../../../utils/common/AnimAbortSignal'

const { ccclass, property } = _decorator

const { Time: T } = Ludex.Utils
const { language } = codex

@ccclass('LeaderboardTimer')
export class LeaderboardTimer extends Component {
    @property(UITransform)
    private background!: UITransform

    @property(Node)
    private calendar!: Node

    @property(Localize)
    private timerLocalizer!: Localize

    private timerOpacity!: UIOpacity

    private backgroundDefaultWidth!: number

    private calendarDefaultPosition!: Vec3

    private alreadyShown: boolean = false

    public onLoad(): void {
        this.timerOpacity = this.timerLocalizer.node.getComponent(UIOpacity) as UIOpacity

        this.validateRequiredComponents()

        this.backgroundDefaultWidth = this.background.width
        this.calendarDefaultPosition = this.calendar.position.clone()
    }

    private validateRequiredComponents(): void {
        ComponentUtils.lazyValidateProperties(this)
    }

    public async show(signal: AnimAbortSignal): Promise<void> {
        if (this.alreadyShown || signal.aborted) return
        this.alreadyShown = true

        const calendarTween = tween(this.calendar)
            .to(0.3, { scale: Vec3.ONE }, { easing: 'backOut' })
            .to(0.3, { position: this.calendarDefaultPosition.clone() }, { easing: 'quadOut' })

        const backgroundTween = tween(this.background)
            .delay(0.3)
            .to(0.3, { width: this.backgroundDefaultWidth }, { easing: 'quadOut' })

        const labelTween = tween(this.timerOpacity).to(0.3, { opacity: 255 })

        await Promise.all([
            AnimUtils.runTweenAsync(calendarTween, signal),
            AnimUtils.runTweenAsync(backgroundTween, signal),
        ])

        this.calendar.scale = Vec3.ONE
        this.calendar.position = this.calendarDefaultPosition.clone()
        this.background.width = this.backgroundDefaultWidth

        await AnimUtils.runTweenAsync(labelTween, signal)
        this.timerOpacity.opacity = 255
    }

    public reset(): void {
        if (this.alreadyShown) return

        this.calendar.position = this.background.node.position.clone()
        this.calendar.scale = Vec3.ZERO

        this.background.width = 0

        this.timerOpacity.opacity = 0
    }

    public setTimeLabel(resettable?: string): void {
        if (!resettable) {
            this.timerLocalizer.key = 'status.loading'
            return
        }

        const now = new Date()
        let time = 0

        if (resettable === 'manually') {
            time = 0
        } else if (resettable === 'daily') {
            time = Date.UTC(now.getUTCFullYear(), now.getUTCMonth(), now.getUTCDate() + 1)
        } else if (resettable === 'weekly') {
            const day = now.getUTCDay()
            const add = day === 0 ? 1 : 8 - day
            time = Date.UTC(now.getUTCFullYear(), now.getUTCMonth(), now.getUTCDate() + add)
        } else if (resettable === 'monthly') {
            time = Date.UTC(now.getUTCFullYear(), now.getUTCMonth() + 1)
        } else {
            time = Date.UTC(now.getUTCFullYear(), now.getUTCMonth(), now.getUTCDate() + 1)
        }

        try {
            const timeString = T.getRelativeTime(time / 1000, language.getCurrentLocale())
            this.timerLocalizer.key = 'game.endsIn'
            this.timerLocalizer.setParams([timeString])
        } catch (error) {
            this.timerLocalizer.key = 'game.endsSoon'
        }
    }
}
