import { _decorator, Animation, Component, Node, tween, UIOpacity, Vec3 } from 'cc'
import { Localize } from '../../components/text/Localize'
import CoinData from '../../constants/CoinData'
import { CustomModeLevel } from '../../gameplay/constants/CustomGameData'
import FailKeys from '../../gameplay/constants/FailKeys'
import GameplayEvents from '../../gameplay/constants/GameEvents'
import AdsUtils from '../../utils/AdsUtils'
import AnimUtils from '../../utils/AnimUtils'
import AudioUtils from '../../utils/AudioUtils'
import ComponentUtils from '../../utils/ComponentUtils'
import { BaseScreen } from '../BaseScreen'

const { ccclass, property, disallowMultiple, requireComponent } = _decorator

const {
    Events,
    Plugins: { Ads: { Types: AdsTypes } = {} },
} = Ludex
const { event, player } = codex

@ccclass('RescuesScreen')
@disallowMultiple
@requireComponent(BaseScreen)
export class RescuesScreen extends Component {
    private screen!: BaseScreen

    @property(UIOpacity)
    public adOpacity!: UIOpacity

    @property([Node])
    private animNodes: Node[] = []

    @property(Localize)
    private titleLocalizer!: Localize

    @property(Localize)
    private descriptionLocalizer!: Localize

    @property(Node)
    private timer!: Node

    @property(Node)
    private bomb!: Node

    @property(Animation)
    private bombAnim!: Animation

    @property(Node)
    private replayButton!: Node

    @property(Node)
    private buyTimeButton!: Node

    private payload!: SwitchScreenPayload

    public __preload() {
        this.enabled = false
        this.screen = this.getComponent(BaseScreen)!
        this.validateRequiredComponents()
        this.listenScreenEvents()

        if (AdsTypes) {
            AdsUtils.preloadAdsByType(AdsTypes.REWARDED_VIDEO)
        }
    }

    public onDestroy(): void {
        this.unListenScreenEvents()
    }

    private validateRequiredComponents() {
        ComponentUtils.lazyValidateProperties(this)
    }

    private listenScreenEvents() {
        // ? Here's a demo of all the events a screen has
        event.on(Events.OPENING_SCREEN, this.handleOpeningScreen)
        event.on(Events.OPENED_SCREEN, this.handleOpenedScreen)
    }

    private unListenScreenEvents() {
        event.off(Events.OPENING_SCREEN, this.handleOpeningScreen)
        event.off(Events.OPENED_SCREEN, this.handleOpenedScreen)
    }

    private handleOpeningScreen = (payload: SwitchScreenPayload) => {
        if (!this.screen) {
            throw new Error('RescuesScreen: screen not found')
        }

        if (!this.validateEventPayload(payload)) return
    }

    private handleOpenedScreen = (payload: SwitchScreenPayload) => {
        if (!this.validateEventPayload(payload)) return
        this.open()
    }

    private validateEventPayload(payload: SwitchScreenPayload) {
        const { screenName } = payload
        if (screenName !== this.node.name) return false

        this.payload = payload

        return true
    }

    public async open() {
        AudioUtils.turnOffMusic()
        this.resetState()
        this.runOpenAnimation()
        this.node.scene.emit(GameplayEvents.HIDE_UI)
    }

    public close(screenData?: ScreenData): void {
        AudioUtils.turnUpMusic()
        event.emit(Events.CLOSE_SCREEN, {
            screenName: this.node.name,
            screenData: screenData,
        })
    }

    private resetState(): void {
        const isBomb = this.payload.screenData?.failKey === FailKeys.BOMB

        this.titleLocalizer.key = isBomb ? 'game.boom' : 'game.timesUp'
        this.descriptionLocalizer.key = isBomb ? 'game.bombDesc' : 'game.timesUpDesc'

        this.timer.active = !isBomb
        this.bomb.active = isBomb

        if (isBomb) this.bombAnim.play()

        const gameData = player.getGameData()
        if (!gameData) return

        const canContinue = gameData.coins >= CoinData.CONTINUE_GAME

        this.replayButton.active = !canContinue
        this.buyTimeButton.active = canContinue

        this.adOpacity.opacity = 0
    }

    private async runOpenAnimation(): Promise<void> {
        if (!this.screen.container) {
            console.warn('TimeUpScreen: screen not found')
            return
        }

        await Promise.all(this.animNodes.map((target, index) => this.runAppearTween(target, index * 0.1)))

        await this.runShowAdContainer()
    }

    private async runAppearTween(target: Node, delay: number): Promise<void> {
        await AnimUtils.runTweenAsync(
            tween(target)
                .set({ scale: new Vec3(0, 0, 0) })
                .delay(delay)
                .to(
                    1,
                    { scale: new Vec3(1, 1, 1) },
                    {
                        easing: (t: number) => {
                            return AnimUtils.elasticOut(t, 0.1, 0.45)
                        },
                    },
                ),
        )
    }

    private async runShowAdContainer(): Promise<void> {
        const t = tween(this.adOpacity)
        t.delay(1)
        t.to(0.5, { opacity: 255 })

        await AnimUtils.runTweenAsync(t)
    }

    public async handleClickReplay(): Promise<void> {
        const { level } = this.screen.data
        if (GameSDK.isGameSDK('Yandex')) {
            await AdsUtils.showInterstitialAdAsync({
                forLevel: level,
            })
        }

        this.close()
    }

    public handleClickBuyTime(): void {
        this.node.scene.emit(GameplayEvents.REMOVE_COINS, {
            coins: CoinData.CONTINUE_GAME,
            collectionAnimation: {
                animateTextOnly: true,
            },
        } as CoinDataPayload)

        this.close({ rescue: true })
    }

    public async handleClickAd(): Promise<void> {
        const success = await AdsUtils.showRewardVideoAdAsync()
        if (!success) return

        this.close({ rescue: true })
    }

    public handleClickClose(): void {
        const level = player.getGameData()?.level ?? 1
        const customMode = level >= CustomModeLevel
        this.close({ customMode })
    }
}
