import { Button, Component, Node, UITransform, Vec3, Widget, _decorator, game, tween } from 'cc'
import AutoLayout from '../../../../../../components/auto-layout/AutoLayout'
import DashboardEvents from '../../../../../../constants/DashboardEvents'
import DashboardScreens from '../../../../../../constants/DashboardScreens'
import GameEvents from '../../../../../../constants/GameEvents'
import GameWorld from '../../../../../../constants/GameWorld'
import { ScreenKeysEnum } from '../../../../../../constants/ScreenKeys'
import AnimUtils from '../../../../../../utils/AnimUtils'
import LayoutUtils from '../../../../../../utils/LayoutUtils'
import ScreenUtils from '../../../../../../utils/ScreenUtils'
import type { NavButton } from './NavButton'

const { ccclass, property } = _decorator
const {
    Utils: { Array: A, Signal: S },
} = Ludex

const { Layout } = GameWorld

@ccclass('NavManager')
export class NavManager extends Component {
    @property(Node)
    private selectorNode!: Node

    @property(Node)
    public selectedButtonNode!: Node

    @property(AutoLayout)
    private autoLayout!: AutoLayout

    @property({ type: Widget, group: 'Widgets' })
    private navWidget!: Widget

    @property({ type: Widget, group: 'Widgets' })
    private backgroundWidget!: Widget

    @property({ type: Widget, group: 'Widgets' })
    private layoutWidget!: Widget

    @property(UITransform)
    private uiTransform!: UITransform

    private selectedButton!: NavButton

    private navButtons: NavButton[] = []

    private selectorAnimationSignal = new S.SignalController()

    public get currentButton(): NavButton {
        return this.selectedButton
    }

    private isNavigating = false

    public onLoad(): void {
        this.navButtons = this.node.getComponentsInChildren('NavButton') as NavButton[]
        this.selectedButton = this.selectedButtonNode?.getComponentInChildren('NavButton')! as NavButton

        // @ts-expect-error - This event is custom event
        game.on(GameEvents.ORIENTATION_CHANGED, this.handleOrientationChanged, this)
        game.on(GameEvents.VIEW_RESIZED, this.handleViewResized, this)

        this.handleOrientationChanged(LayoutUtils.getCurrentLayout())
    }

    protected onDestroy(): void {
        game.off(GameEvents.ORIENTATION_CHANGED, this.handleOrientationChanged, this)
        game.off(GameEvents.VIEW_RESIZED, this.handleViewResized, this)
    }

    public async select(button: NavButton): Promise<void> {
        if (this.isNavigating) {
            return
        }

        if (!A.has(this.navButtons, button)) {
            throw new Error('NavManager: Button not found')
        }

        if (button === this.selectedButton) {
            return
        }

        if (button.isWIP) {
            this.handleWIPButton()
            return
        }

        this.isNavigating = true

        this.node.scene.emit(DashboardEvents.REQUEST_NAVIGATION, {
            from: this.selectedButton.buttonType,
            to: button.buttonType,
        } as NavigatePayload)

        return new Promise((resolve) => {
            this.node.scene.once(DashboardEvents.REQUEST_NAVIGATION_ANIMATION, () => {
                const deselectAnim = this.selectedButton.deselect()

                this.autoLayout.selectedIndex = this.navButtons.indexOf(button)

                this.selectedButton = button

                const selectedWorldX = this.autoLayout.getSelectedWorldX()
                const offsetDistance = button.getSelectedWorldDistanceX()
                const targetX = selectedWorldX - offsetDistance

                const selectorAnim = this.playSelectorAnimation(targetX)

                const selectAnim = button.select()

                Promise.all([deselectAnim, selectAnim, selectorAnim]).then(() => {
                    this.isNavigating = false
                    resolve()
                })
            })
        })
    }

    public async selectUsingSceneTransition(button: NavButton): Promise<void> {
        if (this.isNavigating) {
            return
        }

        if (A.has([DashboardScreens.store], button.buttonType)) {
            this.handleWIPButton()
            return
        }

        await this.select(button)
    }

    public async selectByType(screenType: DashboardScreens): Promise<void> {
        const button = A.search(this.navButtons, (button) => button.buttonType === screenType)

        if (!button) {
            throw new Error('NavManager: Button not found')
        }

        await this.selectUsingSceneTransition(button)
    }

    private handleWIPButton(): void {
        ScreenUtils.openScreen(ScreenKeysEnum.STAR_CHEST_SCREEN)
    }

    public async selectDefaultNavBtn(): Promise<void> {
        if (!this.currentButton || !A.has(this.navButtons, this.currentButton)) {
            throw new Error('NavManager: Button not found')
        }

        const from = null
        const to = this.currentButton.buttonType

        this.node.scene.emit(DashboardEvents.REQUEST_NAVIGATION, {
            from,
            to,
        } as NavigatePayload)

        return new Promise((resolve) => {
            this.node.scene.once(DashboardEvents.REQUEST_NAVIGATION_ANIMATION, () => {
                this.autoLayout.selectedIndex = this.navButtons.indexOf(this.currentButton)

                const animations = this.navButtons.map((button) => {
                    if (button === this.currentButton) {
                        return button.select(false)
                    }

                    return button.deselect(false, true)
                })

                const selectedWorldX = this.autoLayout.getSelectedWorldX()
                const offsetDistance = this.currentButton.getSelectedWorldDistanceX()

                const targetX = selectedWorldX - offsetDistance

                this.selectorNode.worldPosition = new Vec3(targetX, this.selectorNode.worldPosition.y, 0)
                Promise.all(animations).then(() => {
                    resolve()
                })
            })
        })
    }

    public async playSelectorAnimation(targetWorldX: number): Promise<void> {
        const signal = this.selectorAnimationSignal.renew()

        const originalScale = new Vec3(1, 1, 1)
        const originalPosition = this.selectorNode.worldPosition.clone()

        const squashX = 1.2
        const squashY = 0.8
        const squashedScale = new Vec3(squashX * originalScale.x, squashY * originalScale.y, 1)

        const selectorTween = tween(this.selectorNode).to(
            0.3,
            {
                worldPosition: new Vec3(targetWorldX, originalPosition.y, 0),
            },
            {
                easing: 'quintInOut',
                onUpdate: (_target, ratio) => {
                    if (ratio === undefined) return

                    const nextScale = this.calculateCurrentSquashScale(originalScale, squashedScale, ratio)

                    this.selectorNode.scale = nextScale
                },
            },
        )

        await AnimUtils.runTweenAsync(selectorTween, signal)

        this.selectorNode.scale = originalScale
    }

    public setIgnoreInput(ignore = true): void {
        for (const button of this.navButtons) {
            const b = button.getComponentInChildren(Button)
            if (b) {
                b.interactable = !ignore
            }
        }
    }

    private calculateCurrentSquashScale(original: Vec3, squashed: Vec3, ratio: number) {
        const calcRatio = this.splitIntoSquashRatio(ratio)
        return original.clone().lerp(squashed, calcRatio)
    }

    private splitIntoSquashRatio(ratio: number): number {
        if (ratio < 0.5) {
            return ratio * 2
        }

        return 1 - (ratio - 0.5) * 2
    }

    private handleOrientationChanged(layout: string): void {
        if (layout === Layout.Portrait) {
            this.navWidget.isAlignLeft = true
            this.navWidget.isAlignRight = true
            this.backgroundWidget.left = -50
            this.backgroundWidget.right = -50
            this.layoutWidget.left = 0
            this.layoutWidget.right = 0
        } else if (layout === Layout.Landscape) {
            this.navWidget.isAlignLeft = false
            this.navWidget.isAlignRight = false
            this.backgroundWidget.left = 0
            this.backgroundWidget.right = 0
            this.layoutWidget.left = 25
            this.layoutWidget.right = 25
            this.uiTransform.width = 1000
        }
    }

    private handleViewResized(): void {
        if (!this.selectedButton) return
        this.selectorAnimationSignal.abort()
        this.selectorNode.worldPositionX = this.selectedButton.node.worldPosition.x
        this.selectorNode.y = -164.5
    }
}
