import NewbieSteps from '../../../../guides/NewbieSteps'
import { getBestPath } from './getBestPath'

type Tile = {
    isHidden(): boolean
    isSimilarTo(other: Tile): boolean
}

type Grid = {
    get: (x: number, y: number) => Tile
    isInBounds: (x: number, y: number) => boolean
    width: number
    height: number
}

export function getErrorPath(
    grid: Grid,
    startTile: [number, number],
    endTile: [number, number],
    isNewbieLevel?: boolean,
): Path {
    if (isNewbieLevel) {
        const newbieStartTile = NewbieSteps[4].startTile
        const newbieEndTile = NewbieSteps[4].endTile

        if (
            startTile[0] === newbieStartTile.x &&
            startTile[1] === newbieStartTile.y &&
            endTile[0] === newbieEndTile.x &&
            endTile[1] === newbieEndTile.y
        ) {
            //? Special case for the newbie guide
            return [
                {
                    a: newbieStartTile,
                    b: newbieEndTile,
                },
            ] as Path
        }

        if (
            startTile[0] === newbieEndTile.x &&
            startTile[1] === newbieEndTile.y &&
            endTile[0] === newbieStartTile.x &&
            endTile[1] === newbieStartTile.y
        ) {
            //? Special case for the newbie guide
            return [
                {
                    a: newbieStartTile,
                    b: newbieEndTile,
                },
            ] as Path
        }
    }

    return getBestPath(grid, startTile, endTile)
}
