import { Component, _decorator, game } from 'cc'
import { OpacityToggler } from '../../components/common/OpacityToggler'
import DevTools from '../../constants/DevTools'
import GameEvents from '../../constants/GameEvents'
import GameWorld from '../../constants/GameWorld'
import { ScreenKeysEnum } from '../../constants/ScreenKeys'
import { InterstitialAdIndicator } from '../../scenes/game-scene/components/interstitial-ad-indicator/InterstitialAdIndicator'
import type { GameSceneLayout, StatusConfig } from '../../scenes/game-scene/components/ui/GameSceneLayout'
import { ComboContainer } from '../../scenes/game-scene/components/ui/combo/ComboContainer'
import { ComboProgressBar } from '../../scenes/game-scene/components/ui/combo/ComboProgressBar'
import { LandscapeLayout } from '../../scenes/game-scene/components/ui/landscape/LandscapeLayout'
import UIMasks from '../../scenes/game-scene/components/ui/mask/UIMasks'
import { PortraitLayout } from '../../scenes/game-scene/components/ui/portrait/PortraitLayout'
import LayoutUtils from '../../utils/LayoutUtils'
import MatchUtils from '../../utils/MatchUtils'
import ScreenUtils from '../../utils/ScreenUtils'
import type { Gameplay } from '../Gameplay'
import GameplayEvents from '../constants/GameEvents'
import { DevToolsContainer } from '../dev-tools/DevTools'
import type { SpecialItem } from '../game-objects/item/SpecialItem'

const { ccclass, property } = _decorator
const {
    Utils: { Valid: V },
    Match: { Modes },
} = Ludex
const { Layout } = GameWorld

@ccclass('UIManager')
export class UIManager extends Component {
    @property(InterstitialAdIndicator)
    readonly interstitialAdIndicator!: InterstitialAdIndicator

    @property(PortraitLayout)
    public portraitLayout!: PortraitLayout

    @property(LandscapeLayout)
    public landscapeLayout!: LandscapeLayout

    @property(DevToolsContainer)
    public devTools: DevToolsContainer | null = null

    @property(ComboProgressBar)
    public comboProgressBar!: ComboProgressBar

    @property(ComboContainer)
    public comboContainer!: ComboContainer

    @property(UIMasks)
    public uiMasks!: UIMasks

    private game!: Gameplay

    private uiShown: boolean = false

    public config: StatusConfig = {}

    public currentLayout!: GameSceneLayout

    private isResetDataListener: boolean = false

    public onLoad(): void {
        this.node.scene.on(DevTools.Events.UNLOCK_DEV_TOOLS, this.showDevTools)
        this.node.scene.on(GameplayEvents.RESCUE_START, this.showRescueAnimation, this)
        this.node.scene.on(GameplayEvents.RESCUE_FINISHED, this.hideRescueAnimation, this)
        this.node.scene.on(GameplayEvents.SCORE_UPDATED, this.handleScoreUpdated, this)

        // @ts-expect-error - This event is custom event
        game.on(GameEvents.ORIENTATION_CHANGED, this.onOrientationChanged, this)
        this.onOrientationChanged(LayoutUtils.getCurrentLayout())
    }

    protected onDestroy(): void {
        this.node.scene.off(DevTools.Events.UNLOCK_DEV_TOOLS, this.showDevTools)
        this.node.scene.off(GameplayEvents.RESCUE_START, this.showRescueAnimation, this)
        this.node.scene.off(GameplayEvents.RESCUE_FINISHED, this.hideRescueAnimation, this)
        this.node.scene.off(GameplayEvents.SCORE_UPDATED, this.handleScoreUpdated, this)

        game.off(GameEvents.ORIENTATION_CHANGED, this.onOrientationChanged, this)
    }

    public init(gameplay: Gameplay): void {
        this.currentLayout = this.portraitLayout
        this.game = gameplay

        this.initDevTools(gameplay)
        this.comboContainer.init(gameplay.board)
    }

    private initDevTools(gameplay: Gameplay): void {
        // ? For debugging
        if (!this.isResetDataListener) {
            window.addEventListener('reset-game-data', () => {
                this.devTools?.handleResetData()
            })
            this.isResetDataListener = true
        }

        if (!this.devTools) return

        this.devTools.node.active = V.isDebugger()
        this.devTools.injectGameplay(gameplay)
    }

    private showDevTools = () => {
        if (!this.devTools) return
        this.devTools.node.active = true
    }

    public setLevelText(level: number): void {
        this.config.level = level
        this.currentLayout.setLevelText(level)
    }

    public startCountdown(): void {
        this.comboProgressBar.resume()
    }

    public pauseTime(): void {
        this.comboProgressBar.pause()
    }

    public resumeTime(): void {
        this.comboProgressBar.resume()
    }

    public setComboUI(active: boolean): void {
        this.comboContainer.getComponent(OpacityToggler)!.changeStateFade(active, 0.1)
    }

    public disableLayout(): void {
        this.currentLayout.disableLayout()
    }

    public enableLayout(): void {
        this.currentLayout.enableLayout()
    }

    public updateStatus(): void {
        this.currentLayout.updateStatus({
            coins: this.game.coinManager.getCoins(),
            score: this.game.scoreManager.getCurrentScore(),
            leaderData: this.game.tournamentManager?.leaderData,
            items: this.game.itemManager.itemConfigs,
        })
        this.comboContainer.updateStatus()
    }

    public async showRescueAnimation(): Promise<void> {
        await this.uiMasks.show()
    }

    public async hideRescueAnimation(): Promise<void> {
        await this.uiMasks.hide()
    }

    public async playExitAnimUI(duration: number = 0.2): Promise<void> {
        this.uiShown = false
        await this.currentLayout.playExitAnimUI(duration)
    }

    public async playEntranceAnimUI(duration: number = 0.17): Promise<void> {
        this.uiShown = true
        await this.currentLayout.playEntranceAnimUI(duration)
    }

    public highlightCoinContainer(): void {
        this.currentLayout.highlightCoinContainer()
    }

    public unHighlightCoinContainer(): void {
        this.currentLayout.unHighlightCoinContainer()
    }

    // ? Callback for click event of PauseButton
    public handleClickedPauseGame() {
        ScreenUtils.openScreen(ScreenKeysEnum.PAUSE_SCREEN)
        this.node.scene.emit(GameplayEvents.GAME_PAUSE)
    }

    // ? Callback for click event of HomeButton
    public handleReturnHome() {
        ScreenUtils.openScreen(ScreenKeysEnum.QUIT_LEVEL_SCREEN)
        this.node.scene.emit(GameplayEvents.GAME_PAUSE)
    }

    public get items(): SpecialItem[] {
        return this.currentLayout.items
    }

    public setInteractableItems(interactable = true): void {
        for (const item of this.items) {
            if (!interactable) {
                item.button.interactable = false
                continue
            }

            if (item.itemType === undefined) {
                console.warn('Item type is not defined')
                continue
            }

            item.button.interactable = this.game.itemManager.canUseItem(item.itemType)
        }
    }

    private async onOrientationChanged(layout: string): Promise<void> {
        if (layout === Layout.Portrait) {
            this.landscapeLayout.node.active = false
            this.portraitLayout.node.active = true
            this.currentLayout = this.portraitLayout
        } else if (layout === Layout.Landscape) {
            this.portraitLayout.node.active = false
            this.landscapeLayout.node.active = true
            this.currentLayout = this.landscapeLayout
        }

        if (!this.currentLayout) return

        this.currentLayout.updateStatus({
            coins: this.game.coinManager.getCoins(),
            level: this.config.level,
            score: this.game.scoreManager.getCurrentScore(),
            time: this.game.clockManager.getRemainingTime(),
            leaderData: this.game.tournamentManager?.leaderData,
            items: this.game.itemManager.itemConfigs,
        })
        await this.currentLayout.waitForInitAsync()

        if (this.uiShown) {
            this.playEntranceAnimUI(0)
        } else {
            this.playExitAnimUI(0)
        }
    }

    private handleScoreUpdated(score: number): void {
        this.config.score = score

        this.currentLayout.handleScoreUpdated(score)

        if (MatchUtils.getMatchMode() !== Modes.TOURNAMENT) return

        this.currentLayout.tournamentRail.handleScoreUpdated({
            score,
            leaderData: this.game.tournamentManager?.leaderData,
        })
    }
}
