import type Grid from '../../grid'

type Tile = {
    getCoord(): Readonly<Vec2>
    setCoord(x: number, y: number): void
}

export default function createRotatedGrid<T extends Tile>(grid: Grid<T>, revert = false): Grid<T> {
    const rotatedGrid = grid.toRotated(revert)

    rotatedGrid.each((tile, x, y) => {
        tile.setCoord(x, y)
    })

    return rotatedGrid
}
