import { ScreenKeysEnum } from '../../constants/ScreenKeys'
import { CodeSoundKeys } from '../../constants/SoundKeys'
import AdsUtils from '../../utils/AdsUtils'
import AnimUtils from '../../utils/AnimUtils'
import AudioUtils from '../../utils/AudioUtils'
import MatchUtils from '../../utils/MatchUtils'
import SceneUtils from '../../utils/SceneUtils'
import ScreenUtils from '../../utils/ScreenUtils'
import TournamentUtils from '../../utils/TournamentUtils'
import GameplayEvents from '../constants/GameEvents'
import GameModes from '../constants/GameMode'
import ItemKeys from '../constants/ItemKeys'
import type { StateManager } from '../manager/StateManager'
import BaseState from './BaseState'

const { analytics, player, leaderboard } = codex
const {
    Match: { Modes },
    Utils: { Time: T },
    Plugins: { Ads: { Types: AdTypes } = {} },
} = Ludex

const NotifyPlayerBeforeShow = Ludex.Configs.Ads.NotifyPlayerBeforeShow
// const SECONDS_BETWEEN_SHOW_TOURNAMENT = 300

class FinishState extends BaseState {
    constructor(manager: StateManager) {
        super(manager)

        this.handleFinishStateFlow()
    }

    private async handleFinishStateFlow() {
        if (!this.game) return

        this.processCrazyGamesEvent()
        this.attemptCallCreateShortcut()

        this.game.uiManager.pauseTime()
        const mode = MatchUtils.getMatchMode()

        this.updatePlayerProgress()
        this.updateMatchCustomData()

        if (mode === Modes.CHALLENGE_FRIEND) {
            this.handleFinishChallenge()
        } else if (mode === Modes.TOURNAMENT) {
            this.handleFinishTournament()
        } else {
            this.handleFinishSingle()
        }

        this.logLevelCompleteAnalytics()
    }

    private processCrazyGamesEvent(): void {
        if ('gameplayStop' in GameSDK.extra) {
            GameSDK.extra.gameplayStop()
        }
    }

    private attemptCallCreateShortcut(): void {
        const { level = 0 } = this.data

        if (level < 2) return

        if (AdTypes && AdsUtils.isReadyAdShowable(AdTypes.INTERSTITIAL)) return

        GameSDK.extra.checkAndCreateShortcutAsync().catch((error: Error) => {
            console.warn('createShortcutAsync', error)
        })
    }

    private updatePlayerProgress(): void {
        const { level } = this.data
        const gameData = player.getGameData()
        if (!this.game || !level || !gameData) return

        const mode = MatchUtils.getMatchMode()

        const currentLevel = gameData.level
        const nextLevel = level + 1
        const correctLevel = Math.max(currentLevel, nextLevel)

        if (mode === Modes.SINGLE || mode === GameModes.CUSTOM) {
            // ? Update level progress in single mode or custom mode
            player.setGameData({ level: correctLevel })
        } else if (mode === Modes.TOURNAMENT) {
            // ? Update tournament progress in tournament mode
            const { tournamentId } = MatchUtils.getMatchCustomData() ?? {}
            if (!tournamentId) return

            const score = this.game.scoreManager.getCurrentScore()
            TournamentUtils.saveTournamentProgress(level, score, tournamentId)
        }
    }

    private updateMatchCustomData(): void {
        if (!this.game) return

        const { level = 1 } = this.data
        const score = this.game.scoreManager.getCurrentScore()

        MatchUtils.updateMatchStateAfterLevelFinish(level, score)
    }

    private logLevelCompleteAnalytics(): void {
        if (!this.game) return

        const { level = 0 } = this.data
        const score = this.game.scoreManager.getCurrentScore()
        const mode = MatchUtils.getMatchMode() || undefined

        analytics?.levelComplete(level, score, mode)
    }

    private async handleFinishChallenge(): Promise<void> {
        if (!this.game) return

        try {
            ScreenUtils.showLoadingScreen()

            const { challengeData } = MatchUtils.getMatchCustomData() ?? {}
            await MatchUtils.finishMatchAsync({ extraData: { challengeData } })

            this.game.node.scene.emit(GameplayEvents.SHOW_CHALLENGE_RESULTS)
        } catch (error) {
            console.warn('finish challenge error', error)
        } finally {
            ScreenUtils.closeLoadingScreen()
        }
    }

    private async handleFinishTournament(): Promise<void> {
        this.handlePostTournamentScore()

        if (!this.game) return
        const { level = 1 } = this.data

        AudioUtils.playSound(CodeSoundKeys.LEVEL_COMPLETE, { volume: 0.5 })

        this.game.effectManager.playConfettiEffect()

        await T.sleepAsync(500)

        await AdsUtils.showInterstitialAdAsync({
            forLevel: level,
        })

        this.game.node.scene.emit(GameplayEvents.NEXT_LEVEL)
    }

    private async handlePostTournamentScore(): Promise<void> {
        if (!this.game) return

        const score = this.game?.scoreManager.getCurrentScore()
        if (!score) return

        try {
            await GameSDK.tournament.postScoreAsync(score)
        } catch (error) {
            console.warn('GameSDK post score async', error)
        }

        const customData = MatchUtils.getMatchCustomData()
        if (!customData) return

        const { leaderboardId, playerId } = customData
        if (!leaderboardId || !playerId) return

        await leaderboard.setLeaderboardScoreAsync(leaderboardId, playerId, score)
    }

    private async handleFinishSingle(): Promise<void> {
        AudioUtils.turnUpMusic()

        const { level = 0 } = this.data
        const isHardLevel = level > 0 && level % 5 === 0
        const customModeUnlocked = level === 12

        if (customModeUnlocked) {
            await this.handleUnlockCustomMode()
            return
        }

        if (isHardLevel) {
            await this.handleFinishHardLevel()
        } else {
            await this.handleFinishNormalLevel()
        }

        this.handleNextLevelSingle()
    }

    private async handleFinishNormalLevel(): Promise<void> {
        if (!this.game) return

        const { level = 0 } = this.data
        const { coins = 0 } = player.getGameData() ?? {}
        const { score = 0 } = MatchUtils.getMatchCustomData() ?? {}

        if (NotifyPlayerBeforeShow) {
            await this.game.uiManager.interstitialAdIndicator.flash()
        }

        const isYandex = GameSDK.isGameSDK('Yandex')

        if (!isYandex) {
            //? Avoid showing ads after finishing the level in Yandex
            await this.showAdsNormalLevel(0.8)
        }

        AudioUtils.playSound(CodeSoundKeys.LEVEL_COMPLETE, { volume: 0.5 })
        await ScreenUtils.openScreenAndWaitForClose(ScreenKeysEnum.LEVEL_COMPLETE_SCREEN, {
            level,
            coins,
            score,
            overrideAnimation: {
                openScreen: 'LevelCompleteOpenScreen',
            },
            lookingForRewards: false,
        })

        if (isYandex) {
            //? Showing ads after user closes the level complete screen in Yandex
            await this.showAdsNormalLevel()
        }
    }

    private async showAdsNormalLevel(delay: number = 0): Promise<void> {
        if (!this.game) return

        const { level = 0 } = this.data
        const { custom } = player.getGameData() ?? {}

        if (MatchUtils.getMatchMode() === GameModes.CUSTOM && custom?.ads) {
            this.game.node.scene.emit(GameplayEvents.HIDE_UI)

            await AnimUtils.waitTweenAsync(delay)
            await AdsUtils.showRewardedInterstitialAdAsync({ showError: false })
        } else if (AdTypes && AdsUtils.isReadyAdShowable(AdTypes.INTERSTITIAL)) {
            this.game.node.scene.emit(GameplayEvents.HIDE_UI)

            await AnimUtils.waitTweenAsync(delay)
            await AdsUtils.showInterstitialAdAsync({ forLevel: level })
        }
    }

    private async handleFinishHardLevel(): Promise<void> {
        const { level = 0 } = this.data
        const coins = player.getGameData()?.coins ?? 0
        const score = MatchUtils.getMatchCustomData()?.score ?? 0

        AudioUtils.playSound(CodeSoundKeys.LEVEL_COMPLETE, { volume: 0.5 })
        const { screenData: levelCompleteScreenData } = await ScreenUtils.openScreenAndWaitForClose(
            ScreenKeysEnum.LEVEL_COMPLETE_SCREEN,
            {
                level,
                coins,
                score,
                overrideAnimation: {
                    openScreen: 'LevelCompleteOpenScreen',
                },
                lookingForRewards: true,
            },
        )
        const { canShowRewardsAd = false } = levelCompleteScreenData ?? {}

        if (!canShowRewardsAd) {
            await AdsUtils.showInterstitialAdAsync({
                forLevel: level,
            })
            return
        }

        const { screenData } = await ScreenUtils.openScreenAndWaitForClose(ScreenKeysEnum.GET_REWARDS_SCREEN)
        const { getRewardsSuccess = false, rewardsAdShown = false } = screenData ?? {}
        if (getRewardsSuccess) {
            this.getRewardItems()
        }

        await ScreenUtils.openScreenAndWaitForClose(ScreenKeysEnum.LEVEL_COMPLETE_SCREEN, {
            level,
            coins,
            score,
            getRewardsSuccess,
            lookingForRewards: false,
        })

        if (getRewardsSuccess || rewardsAdShown) return

        await AdsUtils.showInterstitialAdAsync({
            forLevel: level,
        })
    }

    private async handleUnlockCustomMode(): Promise<void> {
        if (!this.game) return

        const { level = 0 } = this.data
        const { coins = 0 } = player.getGameData() ?? {}
        const { score = 0 } = MatchUtils.getMatchCustomData() ?? {}

        AudioUtils.playSound(CodeSoundKeys.LEVEL_COMPLETE, { volume: 0.5 })
        await ScreenUtils.openScreenAndWaitForClose(ScreenKeysEnum.LEVEL_COMPLETE_SCREEN, {
            level,
            coins,
            score,
            overrideAnimation: {
                openScreen: 'LevelCompleteOpenScreen',
            },
            lookingForRewards: false,
        })

        SceneUtils.switchToDashboardScene()
    }

    private handleNextLevelSingle(): void {
        const level = this.data.level
        const gameData = player.getGameData()

        if (!this.game || !level || !gameData) return

        this.game.node.scene.emit(GameplayEvents.NEXT_LEVEL)
    }

    private getRewardItems(): void {
        if (!this.game) return

        this.game.node.scene.emit(GameplayEvents.ADD_ITEM, {
            item: ItemKeys.STORM,
            quantity: 1,
        } as AddItemPayload)

        this.game.node.scene.emit(GameplayEvents.ADD_ITEM, {
            item: ItemKeys.THUNDER,
            quantity: 1,
        } as AddItemPayload)
    }

    public initialize(): void {
        const state = this.manager.createInitializeState(this.manager)
        this.manager.change(state)
    }

    public ready(): void {
        // Don't allow ready in finish state
    }

    public playing(): void {
        // Don't allow playing in finish state
    }

    public fail(): void {
        // Don't allow fail in finish state
    }

    public finish(): void {
        // This is finish state, do nothing
    }

    public update(): void {
        //
    }
}

export default FinishState
