import { _decorator, Component, game, native } from 'cc'
import { ANDROID } from 'cc/env'
import Ads from '../../constants/Ads'
import SceneKeys from '../../constants/SceneKeys'
import MapData from '../../gameplay/constants/MapData'
import TotalLevel from '../../gameplay/constants/TotalLevel'
import AdsUtils from '../../utils/AdsUtils'
import AudioUtils from '../../utils/AudioUtils'
import ResourceUtils from '../../utils/ResourceUtils'
import ScreenUtils from '../../utils/ScreenUtils'
import ThemeUtils from '../../utils/ThemeUtils'
import WasmUtils from '../../utils/WasmUtils'

const { ccclass, disallowMultiple } = _decorator

const {
    Events,
    Utils: { Valid: V },
    Configs: {
        Core: { StartSDKAfterLoadGame },
    },
    Plugins: { Ads: { Types: AdsTypes } = {} },
} = Ludex

const { ads, player, event, context } = codex

@ccclass('BootScene')
@disallowMultiple
export class BootScene extends Component {
    public __preload() {
        this.setupAndroidPlatformAds()
        this.processCrazyGamesLoadingStart()

        // ? This flow use to preload assets before game start
        this.preloadAssets()

        this.listenEvents()
    }

    private async preloadAssets() {
        try {
            game.pause()

            await Promise.all([
                this.loadWasmModules(),
                this.loadBundles(),
                this.loadCurrentPlayerTheme(),
                this.loadGuideSkin(),
            ])
        } catch (error) {
            console.warn('processPreloadAssets', error)
        } finally {
            // ? If ads is showing, don't resume game
            if (!ads?.isAdFullSizeShowing()) {
                game.resume()
            }
        }
    }

    public onLoad() {
        this.handlePauseAudio()

        this.overrideGameSDK()

        this.setupLoadingFullScreen()
    }

    public async start() {
        await GameSDK.extra.waitSDKInitiatedAsync()

        this.processCrazyGamesLoadingStop()
        this.disableBannerAdsOnStartingLevels()
    }

    private listenEvents() {
        // ! This flow is custom for this game, only here, don't add to CorecosSystem
        event.catchUp(Events.GAME_READY, this.lazyLoadWasmModules)
        event.catchUp(Events.GAME_SDK_STARTED, this.processWhenGameSdkStarted)
    }

    private overrideGameSDK() {
        // ? This is example to override some methods
        if (!('FBInstant' in window)) return

        if ('shareAsync' in FBInstant) {
            const originalShareAsync = FBInstant.shareAsync

            FBInstant.shareAsync = async (payload: FBInstant.SharePayload) => {
                const customPayload: FBInstant.SharePayload = { ...payload }

                // ? This is example to override data
                // customPayload.data.playerId = null

                return originalShareAsync(customPayload)
            }
        }
    }

    private readonly setupLoadingFullScreen = () => {
        if (StartSDKAfterLoadGame) {
            // ? Hide loading full screen
            ScreenUtils.addShowedScene(SceneKeys.BOOT_SCENE)

            if (player.isFirstSession()) {
                ScreenUtils.addShowedScene(SceneKeys.GAME_SCENE)
            } else {
                ScreenUtils.addShowedScene(SceneKeys.DASHBOARD_SCENE)
            }
        } else {
            ScreenUtils.showLoadingFullScreen(SceneKeys.BOOT_SCENE)
        }
    }

    private processWhenGameSdkStarted = (): void => {
        this.setupGameData()
        this.sendMsGamesDailyNotification()
    }

    private setupGameData() {
        const gameData = player.getGameData()
        if (!gameData) return

        const { totalLevel } = gameData

        // * We need to correct the totalLevel to make sure it's keep max level of player
        const correctTotalLevel = Math.max(totalLevel, TotalLevel)

        const correctGameData = {
            ...gameData,
            totalLevel: correctTotalLevel,
        }

        player.setGameData(correctGameData)
    }

    private disableBannerAdsOnStartingLevels() {
        if (!AdsTypes) return

        const gameData = player.getGameData()
        if (!gameData) return

        const { level } = gameData
        const showBannerAds = level !== undefined && level > Ads.NUM_LEVEL_WITHOUT_BANNER_ADS
        AdsUtils.setAdEnable(AdsTypes.BANNER, showBannerAds)
    }

    private async loadCurrentPlayerTheme() {
        const mapInfo = MapData.mapOrder
        let currentMap: (typeof mapInfo)[number] = mapInfo[0]

        if (!V.isObject(currentMap)) return

        const gameData = player.getGameData()
        let playerLevel = gameData?.level

        if (!V.isNumber(playerLevel)) {
            return ResourceUtils.loadThemeAsync(currentMap.theme)
        }

        for (const element of mapInfo) {
            playerLevel -= element.levelCount
            if (playerLevel <= 0) {
                currentMap = element
                break
            }
        }

        return ResourceUtils.loadThemeAsync(currentMap.theme)
    }

    private async loadGuideSkin() {
        if (!player.isFirstSession()) return

        return ThemeUtils.loadLevelSpriteFrames(0)
    }

    private processCrazyGamesLoadingStart(): void {
        if ('gameLoadingStart' in GameSDK.extra) {
            GameSDK.extra.gameLoadingStart()
        }
    }

    private processCrazyGamesLoadingStop(): void {
        if ('gameLoadingStop' in GameSDK.extra) {
            GameSDK.extra.gameLoadingStop()
        }
    }

    private setupAndroidPlatformAds() {
        if (!ANDROID) return

        const extra = GameSDK.extra
        if ('setStaticNativeAdMethod' in extra) {
            extra.setStaticNativeAdMethod(native.reflection.callStaticMethod)
        }
    }

    private sendMsGamesDailyNotification() {
        const extra = GameSDK.extra

        if ('sendDailyNotificationAsync' in extra) {
            extra.sendDailyNotificationAsync().catch((error) => {
                console.warn('sendMsGamesDailyNotification', error)
            })
        }
    }

    private loadBundles = async () => {
        return Promise.all([ResourceUtils.loadBundle('loadable')])
    }

    // ? Lazy-load wasm modules
    // * Comment some modules if it's not needed for your game
    private loadWasmModules = async () => {
        // * Instruction optimize WASM modules loading:
        // ? Setting in Cocos Creator -> Project Settings -> Feature Cropping -> 2D: Spine Animation / 2D: 2D Physics System.
        // ? Spine: WasmUtils.loadModuleAsync('spine')
        // ? Physics Box2D: WasmUtils.loadModuleAsync('box2d')
        // ? Physics PhysX: WasmUtils.loadModuleAsync('physx')
        // ? Physics Bullet: WasmUtils.loadModuleAsync('bullet')
        //
        // ? Case 1 (Default): Don't configure "Load Manual" in Project Settings
        // * Safe to use, wasm modules will be loaded when game start, but it's not optimize for loading time`
        //
        // ? Case 2: If game "only" use Spine/Physics in Prefabs, and not immediately use in Main Scene
        // * Need to configure "Load Manual" in  Project Settings, and use code below
        // return Promise.all([WasmUtils.loadModuleAsync('spine'), WasmUtils.loadModuleAsync('box2d')])
        //
        // ? Case 3: Like Case 2, but only load wasm modules when player is first session (new player) or context is not SOLO (switch to GameScene)
        // * Need to configure "Load Manual" in  Project Settings, and use code below
        if (context.getContextType() !== 'SOLO' || player.isFirstSession()) {
            return Promise.all([WasmUtils.loadModuleAsync('spine')])
        }
        //
        // ? Case 4: If game use immediately Spine/Physics in Main Scene
        // * Need to disable "Load Manual" in Project Settings! This case is not supported for lazy-load wasm modules :D
    }

    private lazyLoadWasmModules = async () => {
        // ? Uncomment below some wasm modules if your game need
        await Promise.all([
            WasmUtils.loadModuleAsync('spine'),
            // WasmUtils.loadModuleAsync('box2d'),
            // WasmUtils.loadModuleAsync('physx'),
            // WasmUtils.loadModuleAsync('bullet'),
        ])
    }

    private handlePauseAudio = () => {
        event.on(Events.VISIBILITY_HIDDEN, this.handleMuteAudio)
        event.on(Events.VISIBILITY_VISIBLE, this.handleUnMuteAudio)
    }

    private readonly handleMuteAudio = () => {
        AudioUtils.muteAudio(true)
    }

    private readonly handleUnMuteAudio = () => {
        // ? If ads is showing, don't unmute audio
        if (ads?.isAdFullSizeShowing()) return

        AudioUtils.muteAudio(false)
    }
}
