import { Component, Sprite, UITransform, _decorator } from 'cc'
const { ccclass, property } = _decorator

@ccclass('GameMask')
export class GameMask extends Component {
    @property(UITransform)
    public uiTransform!: UITransform

    @property(Sprite)
    public sprite!: Sprite

    get texture() {
        return this.sprite.spriteFrame!.texture
    }
}
