import { CCString, Component, _decorator, director } from 'cc'
import { EDITOR_NOT_IN_PREVIEW } from 'cc/env'
import AudioChannel from './AudioChannel'
const { ccclass, property } = _decorator

const { audio, player } = codex

@ccclass('Music')
export class Music extends Component {
    @property(CCString)
    public channelId = 'global_music'

    private channel: AudioChannel | null = null

    public isMute: boolean = false

    private volume: number = 1

    public __preload() {
        if (!audio) return

        this.makePersistent()

        // * Only call new a class (not component) in _preload method
        this.channel = new AudioChannel(this.node, this.channelId)
        audio.addChannel(this.channel)
    }

    private makePersistent() {
        if (EDITOR_NOT_IN_PREVIEW) return
        if (director.isPersistRootNode(this.node)) return

        director.addPersistRootNode(this.node)
    }

    public playing(key: Ludex.Plugins.Audio.SoundKeys): boolean {
        return this.channel?.playing(key) ?? false
    }

    public play(key: Ludex.Plugins.Audio.SoundKeys, options?: Ludex.Plugins.Audio.PlayConfig): void {
        if (!this.isMusicEnable()) return

        const correctOptions = options ?? {}

        if (!correctOptions.loop) {
            correctOptions.loop = true
        }

        audio.play(key, correctOptions, this.channelId)
    }

    private isMusicEnable(): boolean {
        return !!player.getPlayerSetting('music')
    }

    public setVolume(volume: number) {
        this.volume = volume
        if (this.isMute) return
        audio.setVolume(volume, this.channelId)
    }

    public getVolume(): number {
        return audio.getVolume(this.channelId)
    }

    public pause(key: Ludex.Plugins.Audio.SoundKeys) {
        audio.pause(key, this.channelId)
    }

    public resume(key: Ludex.Plugins.Audio.SoundKeys) {
        if (!this.isMusicEnable()) return

        audio.resume(key, this.channelId)
    }

    public stop(key: Ludex.Plugins.Audio.SoundKeys) {
        audio.stop(key, this.channelId)
    }

    public mute(isMute: boolean) {
        this.isMute = isMute

        if (isMute) {
            audio.mute(this.channelId)
        } else {
            this.setVolume(this.volume)
            audio.unmute(this.channelId)
        }
    }
}
