import ComponentUtils from 'db://assets/utils/ComponentUtils'
import AnimAbortSignal from 'db://assets/utils/common/AnimAbortSignal'
import { _decorator, Component, Label, Mask, Node, Sprite, type SpriteFrame, tween, UIOpacity, Vec3 } from 'cc'
import AnimUtils from '../../../utils/AnimUtils'
import CacheUtils from '../../../utils/CacheUtils'

const { ccclass, property } = _decorator

export interface LeaderConfig {
    playerId: string
    name: string
    photo: string
    score: number
}

const {
    Utils: { Number: N },
} = Ludex

@ccclass('TopLeader')
export class TopLeader extends Component {
    @property(Label)
    private nameLabel: Label | null = null

    @property(Node)
    private defaultAvatar!: Node

    @property({ type: Sprite })
    private avatar!: Sprite

    @property({ type: Mask })
    private mask!: Mask

    @property({ type: UIOpacity })
    private avatarOpacity!: UIOpacity

    @property({ type: Label })
    private scoreLabel: Label | null = null

    @property({ type: UIOpacity })
    private uiOpacity!: UIOpacity

    private signal: AnimAbortSignal = new AnimAbortSignal()

    public onLoad() {
        this.validateRequiredComponents()
    }

    private validateRequiredComponents(): void {
        ComponentUtils.lazyValidateProperties(this)
    }

    @ComponentUtils.onlyRunInLifeCycle
    public updateInfo(payload: LeaderConfig): void {
        this.updateUsername(payload)
        this.updatePhoto(payload)
        this.updateScore(payload)
    }

    private updateUsername(payload: LeaderConfig): void {
        if (!this.nameLabel) return
        this.nameLabel.string = payload.name
    }

    @ComponentUtils.onlyRunInLifeCycleAsync
    public async updatePhoto(payload: Pick<LeaderConfig, 'photo' | 'playerId'>): Promise<void> {
        this.signal.abort()
        const signal = new AnimAbortSignal()
        this.signal = signal

        this.mask.enabled = false
        this.defaultAvatar.active = !CacheUtils.isAvatarCached(payload.playerId, 128)
        this.avatar.spriteFrame = null

        const { playerId, photo } = payload

        const spriteFrame = await CacheUtils.getPlayerAvatar(photo, playerId, 128, signal)
        if (!spriteFrame || signal.aborted) return

        this.setPhoto(spriteFrame)
    }

    @ComponentUtils.onlyRunInLifeCycleAsync
    private async setPhoto(spriteFrame: SpriteFrame): Promise<void> {
        if (!this.node.active) return

        spriteFrame.packable = true
        this.avatar.spriteFrame = spriteFrame

        const avatarTween = tween(this.avatarOpacity).to(0.2, { opacity: 255 })

        await AnimUtils.runTweenAsync(avatarTween, this.signal)

        if (!this.enabledInHierarchy) return

        this.mask.enabled = true
        this.defaultAvatar.active = false
    }

    public updateScore(payload: LeaderConfig): void {
        if (!this.scoreLabel) return

        const score = Math.floor(payload.score)
        this.scoreLabel.string = N.getShortNumber(score, 7)
    }

    public async runShowUpAnimation(delay: number, duration = 0.3, signal?: AnimAbortSignal): Promise<void> {
        this.node.scale = Vec3.ZERO

        const scaleTween = tween(this.node)
            .delay(delay)
            .to(duration, { scale: new Vec3(1, 1, 1) }, { easing: 'backOut' })

        const opacityTween = tween(this.uiOpacity).delay(delay).to(duration, { opacity: 255 })

        await Promise.all([AnimUtils.runTweenAsync(scaleTween, signal), AnimUtils.runTweenAsync(opacityTween, signal)])
    }

    public setVisible(visible: boolean): void {
        const opacity = visible ? 255 : 0

        this.uiOpacity.opacity = opacity
        this.avatarOpacity.opacity = opacity
    }
}
