import SortNodeUtils from 'db://assets/utils/SortNodeUtils'
import { _decorator, Component, Label, Node, tween, UIOpacity, Vec3 } from 'cc'
import GameplayEvents from '../../../../../gameplay/constants/GameEvents'
import ComponentUtils from '../../../../../utils/ComponentUtils'

const { ccclass, property } = _decorator

@ccclass('ComboLabel')
export class ComboLabel extends Component {
    @property(Label)
    private comboNumber!: Label

    @property(Node)
    private comboContainer!: Node

    private opacity!: UIOpacity

    protected onLoad(): void {
        this.opacity = this.getComponent<UIOpacity>(UIOpacity)!

        this.validateRequiredComponents()
    }

    private validateRequiredComponents(): void {
        ComponentUtils.lazyValidateProperties(this)
    }

    public start(): void {
        this.node.scene.on(GameplayEvents.COMBO_UPDATED, this.handleOnComboUpdated, this)
    }

    public handleOnComboUpdated(combo: number): void {
        if (combo > 1) {
            this.opacity.opacity = 255
            this.comboNumber.string = `${combo}`

            SortNodeUtils.setNodeToLayer(this.comboNumber.node, 'GameUnpackLabel')

            tween(this.comboContainer)
                .to(0.1, { scale: new Vec3(1.3, 1.3, 1.3) }, { easing: 'sineIn' })
                .to(0.1, { scale: Vec3.ONE }, { easing: 'sineOut' })
                .start()
        } else {
            SortNodeUtils.removeNodeFromLayer(this.comboNumber.node)
            this.opacity.opacity = 0
        }
    }
}
