import { Component, UIOpacity, _decorator, tween } from 'cc'
import AnimUtils from '../../../../../utils/AnimUtils'
import ComponentUtils from '../../../../../utils/ComponentUtils'
const { ccclass } = _decorator

@ccclass('LeaderboardIsEmpty')
export class LeaderboardIsEmpty extends Component {
    private uiOpacity!: UIOpacity

    public onLoad(): void {
        this.uiOpacity = this.getComponent(UIOpacity)!

        this.validateRequiredComponents()
    }

    private validateRequiredComponents(): void {
        ComponentUtils.lazyValidateProperties(this)
    }

    public async show(): Promise<void> {
        this.uiOpacity.opacity = 255

        const position = this.node.position.clone()

        const shakeTween = tween(this.node)

        shakeTween.to(0.05, { position: position.clone().add3f(5, 0, 0) })
        shakeTween.to(0.05, { position: position.clone().add3f(-5, 0, 0) })

        const returnTween = tween(this.node).repeat(2, shakeTween)

        returnTween.to(0.05, { position })

        await AnimUtils.runTweenAsync(returnTween)
    }

    public hide(): void {
        this.uiOpacity.opacity = 0
    }
}
