import { Component, Vec3, Widget, _decorator } from 'cc'
import GameplayEvents from '../../../../../gameplay/constants/GameEvents'
import type Board from '../../../../../gameplay/game-objects/board'
import CocosUtils from '../../../../../utils/CocosUtils'
import LayoutUtils from '../../../../../utils/LayoutUtils'
import MatchUtils from '../../../../../utils/MatchUtils'
const { ccclass } = _decorator

const { Modes } = Ludex.Match

const PORTRAIT_WIDGET_RIGHT = 22.5
const LANDSCAPE_WIDGET_RIGHT = 142.5

@ccclass('ComboContainer')
export class ComboContainer extends Component {
    private board!: Board
    private widget!: Widget

    protected __preload(): void {
        this.widget = this.node.getComponent(Widget) as Widget

        this.node.scene.on(GameplayEvents.GRID_RESIZE, this.handleViewResized, this)
    }

    public init(board: Board): void {
        this.board = board
    }

    public updateWidget(): void {
        if (LayoutUtils.isPortrait()) {
            this.node.scale = new Vec3(1, 1, 1)
            this.widget.right = PORTRAIT_WIDGET_RIGHT
            this.widget.top = MatchUtils.getMatchMode() === Modes.TOURNAMENT ? 180 : 160
            return
        }

        if (!this.board) return

        this.node.scale = new Vec3(1.5, 1.5, 1.5)

        const boardBoundingBox = this.board.getBoundingBoxWorld()
        const topBoard = boardBoundingBox.yMax
        const topScreen = CocosUtils.cssSizeToWorldSize(CocosUtils.getScreenHeightCss())

        this.widget.right = LANDSCAPE_WIDGET_RIGHT
        this.widget.top = (topScreen - topBoard) / 2
    }

    public updateStatus(): void {
        this.updateWidget()
    }

    private handleViewResized(): void {
        this.updateWidget()
    }
}
