import { Component, Vec3, _decorator } from 'cc'
import ComponentUtils from '../../../utils/ComponentUtils'
import { ShiningStar } from './ShiningStar'
const { ccclass, property, disallowMultiple } = _decorator

const { Array: A } = Ludex.Utils

@ccclass('StarParticles')
@disallowMultiple
export class StarParticles extends Component {
    @property({ type: [ShiningStar], displayName: 'Prebuilt Shining stars' })
    private shiningStars: ShiningStar[] = []

    public __preload(): void {
        this.validateRequiredComponents()
    }

    public async fireShiningStarAt(targetPosition: [number, number]): Promise<void> {
        const star = this.getFirstAvailableShiningStar()

        if (star === undefined) {
            throw new Error(
                'No available shining stars. Please add more prebuilt shining stars or calls this function later.',
            )
        }

        const [targetX, targetY] = targetPosition

        const position = new Vec3(targetX, targetY, this.node.worldPosition.z)
        star.node.worldPosition = position

        await star.playAnimation()
    }

    private getFirstAvailableShiningStar(): ShiningStar | undefined {
        return A.search(this.shiningStars, (star) => star.isAvailable)
    }

    private validateRequiredComponents(): void {
        ComponentUtils.lazyValidateProperties(this)
    }
}
