import { _decorator, Component } from 'cc'
import GameModes from '../../../../../gameplay/constants/GameMode'
import MatchUtils from '../../../../../utils/MatchUtils'
import type { StatusConfig } from '../GameSceneLayout'
import { ChallengeScoreBlock } from './score-block/ChallengeScoreBlock'
import { ScoreBlock } from './score-block/ScoreBlock'

const { ccclass, property } = _decorator

const {
    Match: { Modes },
} = Ludex
const { match } = codex

@ccclass('ScoreContainer')
export class ScoreContainer extends Component {
    @property(ScoreBlock)
    private scoreBlock!: ScoreBlock

    @property(ChallengeScoreBlock)
    private playerScoreBlock!: ChallengeScoreBlock

    @property(ChallengeScoreBlock)
    private opponentScoreBlock!: ChallengeScoreBlock

    private scoreBlockShown: boolean = true
    private challengeScoreBlockShown: boolean = false

    public updateStatus(config: StatusConfig): void {
        const { mode } = match.getMatchState()

        switch (mode) {
            case Modes.CHALLENGE_FRIEND:
                this.showChallengeScoreBlock()
                this.hideScoreBlock()
                if (config.score !== undefined) this.playerScoreBlock.setScore(config.score)
                break

            case Modes.SINGLE:
            case GameModes.CUSTOM:
                this.hideChallengeScoreBlock()
                this.showScoreBlock()
                if (config.score !== undefined) this.scoreBlock.setScore(config.score)
                break

            default:
                this.hideScoreBlock()
                this.hideChallengeScoreBlock()
        }
    }

    private showChallengeScoreBlock(): void {
        if (this.challengeScoreBlockShown) return
        this.challengeScoreBlockShown = true

        const state = match.getMatchState()
        const { playerId, opponentId } = MatchUtils.getMatchCustomData() ?? {}

        if (!playerId || !opponentId) return

        this.playerScoreBlock.node.active = true
        this.opponentScoreBlock.node.active = true

        const player = state.profiles[playerId]
        const opponent = state.profiles[opponentId]

        if (!player || !opponent) return

        this.playerScoreBlock.updateInfo(player.photo, playerId, player.score)
        this.opponentScoreBlock.updateInfo(opponent.photo, opponentId, opponent.score)
    }

    private hideChallengeScoreBlock(): void {
        if (!this.challengeScoreBlockShown) return
        this.challengeScoreBlockShown = false

        this.playerScoreBlock.node.active = false
        this.opponentScoreBlock.node.active = false

        this.playerScoreBlock.cleanupOverlay()
        this.opponentScoreBlock.cleanupOverlay()
    }

    private showScoreBlock(): void {
        if (this.scoreBlockShown) return
        this.scoreBlockShown = true

        const { score } = MatchUtils.getMatchCustomData() ?? {}

        this.scoreBlock.node.active = true
        this.scoreBlock.setScore(score)
    }

    private hideScoreBlock(): void {
        if (!this.scoreBlockShown) return
        this.scoreBlockShown = false

        this.scoreBlock.node.active = false
    }

    public updateScore(score: number): void {
        if (this.challengeScoreBlockShown) {
            this.playerScoreBlock.updateScore(score)
        } else if (this.scoreBlockShown) {
            this.scoreBlock.updateScore(score)
        }
    }
}
