import { _decorator, CCString, Component, ccenum, Enum, type Font, game, Label, Sprite, type SpriteFrame } from 'cc'
import { EDITOR_NOT_IN_PREVIEW } from 'cc/env'
import GameEvents from '../../constants/GameEvents'
import {
    AvailableLocales,
    DefaultLanguageData,
    DefaultLocale,
    EditorLocaleLoader,
    LocaleFontPaths,
    type LocalesWithoutDefault,
} from '../../constants/Locales'
import ComponentUtils from '../../utils/ComponentUtils'
import ResourceUtils from '../../utils/ResourceUtils'

const { ccclass, property, executeInEditMode, disallowMultiple } = _decorator

const {
    Utils: { Signal: S, Function: F, Array: A },
    Events,
} = Ludex

const { language, event } = codex

type Signal = InstanceType<typeof S.Signal>
type Locale = Ludex.Plugins.Language.Locale
type TextKey = Ludex.Plugins.Language.TextKey
type TextureKey = Ludex.Plugins.Language.TextureKey

export enum LocalizeType {
    Text = 0,
    Texture = 1,
}

ccenum(LocalizeType)

let EDITOR_READY = false

async function initForEditor() {
    language.add(DefaultLocale, DefaultLanguageData)

    const keys = Object.keys(EditorLocaleLoader) as LocalesWithoutDefault[]
    const data = await F.allSettled(
        keys.map(async (key) => {
            const json = await EditorLocaleLoader[key]()

            return {
                key,
                json,
            }
        }),
    )

    for (const item of data) {
        if (item.status === 'rejected') {
            console.error(item.reason)
            continue
        }

        const { key, json } = item.value
        language.add(key, json)
    }

    EDITOR_READY = true
}

function isValidLocale(value: string): value is Locale {
    return A.has(AvailableLocales, value)
}

const LOCALIZE_PARAMS_UPDATED_EVENT = 'localize-params-updated'

type LocalizeParamsUpdatedPayload = {
    text: string
    param: LocalizeParams
}

@ccclass('LocalizeParams')
export class LocalizeParams {
    @property({ visible: false })
    private _text: string = ''

    public get text(): string {
        return this._text
    }

    @property(CCString)
    public set text(value: string) {
        this._text = value
        game.emit(LOCALIZE_PARAMS_UPDATED_EVENT, { text: value, param: this } as LocalizeParamsUpdatedPayload)
    }
}

@ccclass('Localize')
@disallowMultiple
@executeInEditMode(true)
export class Localize extends Component {
    private localized = false

    @property({ visible: false })
    private _defaultLocale: Locale = 'en'

    public get defaultLocale(): Locale {
        return this._defaultLocale
    }

    @property({
        type: CCString,
        visible: function (this: Localize) {
            return this.l10nType === LocalizeType.Texture
        },
    })
    public set defaultLocale(value: string) {
        if (!isValidLocale(value)) {
            console.warn(`Key ${value} not found. Supported locales are ${AvailableLocales.join(', ')}`)
            return
        }

        if (EDITOR_NOT_IN_PREVIEW) {
            console.info(`Key ${value} is valid`)
        }

        this._defaultLocale = value
    }

    @property({ visible: false })
    private _l10nType: LocalizeType = LocalizeType.Text

    @property({ type: Enum(LocalizeType) })
    public get l10nType(): LocalizeType {
        return this._l10nType
    }

    public set l10nType(value: LocalizeType) {
        this._l10nType = value

        if (value === LocalizeType.Texture) {
            this.label = undefined
            this.params = []
        }

        if (value === LocalizeType.Text) {
            this.sprite = undefined
            this.packable = true
        }
    }

    @property({
        type: Label,
        visible: function (this: Localize) {
            return this.l10nType === LocalizeType.Text
        },
    })
    public label?: Label

    @property({
        visible: function (this: Localize) {
            return this.l10nType === LocalizeType.Text
        },
    })
    public updateFont = true

    @property({
        type: [LocalizeParams],
        visible: function (this: Localize) {
            return this.l10nType === LocalizeType.Text
        },
    })
    public params: LocalizeParams[] = []

    @property({
        type: Sprite,
        visible: function (this: Localize) {
            return this.l10nType === LocalizeType.Texture
        },
    })
    public sprite?: Sprite

    @property({
        visible: function (this: Localize) {
            return this.l10nType === LocalizeType.Texture
        },
    })
    public packable = true

    @property({ visible: false })
    private _key?: TextKey | TextureKey

    @property({ type: CCString })
    public set key(value: TextKey | TextureKey) {
        try {
            const isValid = this.isValidKey(value)

            if (!isValid) {
                console.warn(`Key ${value} not found in locales`)
                return
            }

            if (EDITOR_NOT_IN_PREVIEW) {
                console.info(`Key ${value} is valid`)
            }

            this._key = value
            this.updateLocalization()
        } catch (error) {
            console.warn(error)
        }
    }

    public get key(): TextKey | TextureKey | undefined {
        return this._key
    }

    private readonly updateSignalController = new S.SignalController()

    public setParams(params: (string | undefined)[]) {
        while (this.params.length < params.length) {
            this.params.push(new LocalizeParams())
        }

        for (let i = 0, len = this.params.length; i < len; i++) {
            const p = this.params[i] as (typeof this.params)[number]
            p.text = params[i] ?? p.text
        }
    }

    public waitUntilLocalized(): Promise<void> {
        if (this.localized) return Promise.resolve()
        return new Promise((resolve) => this.node.once(GameEvents.LOCALIZE_APPLIED, resolve))
    }

    public __preload() {
        if (EDITOR_NOT_IN_PREVIEW && !EDITOR_READY) {
            initForEditor()
        }
    }

    public isValidKey(key: string) {
        if (this.isTextKey(key)) {
            return true
        }

        if (this.isTextureKey(key)) {
            return true
        }

        return false
    }

    private isTextKey(key: string): key is TextKey {
        try {
            return this.l10nType === LocalizeType.Text && language.hasTextKey(key)
        } catch (error) {
            console.warn(error)
            return false
        }
    }

    private isTextureKey(key: string): key is TextureKey {
        try {
            return this.l10nType === LocalizeType.Texture && language.hasTextureKey(key)
        } catch (error) {
            console.warn(error)
            return false
        }
    }

    protected onEnable(): void {
        this.listenEvent()
    }

    protected onDisable(): void {
        this.unlistenEvent()
        this.updateSignalController.abort()
    }

    protected start(): void {
        this.initLocalization()
    }

    private listenEvent() {
        event.on(Events.LANGUAGE_CHANGED, this.updateLocalization)
        // @ts-expect-error - custom event with payload
        game.on(LOCALIZE_PARAMS_UPDATED_EVENT, this.onLocalizeParamsUpdated)
    }

    private unlistenEvent() {
        event.off(Events.LANGUAGE_CHANGED, this.updateLocalization)
        game.off(LOCALIZE_PARAMS_UPDATED_EVENT, this.onLocalizeParamsUpdated)
    }

    private readonly onLocalizeParamsUpdated = ({ param }: LocalizeParamsUpdatedPayload) => {
        if (A.has(this.params, param)) {
            this.updateLocalization()
        }
    }

    public readonly updateLocalization = () => this.updateLocalizationComp()

    private initLocalization() {
        return this.updateLocalizationComp(true)
    }

    private async updateLocalizationComp(skipTextureIfSameLocale = false) {
        if (EDITOR_NOT_IN_PREVIEW && !EDITOR_READY) {
            console.warn('Editor importing locales...')
            return
        }

        if (!this.enabledInHierarchy) return
        if (this.key === undefined) return

        const signal = this.updateSignalController.renew()

        try {
            this.localized = false

            if (this.isTextKey(this.key)) {
                await this.updateTextLocalization(this.key, signal)
            } else if (this.isTextureKey(this.key)) {
                const currentLocale = language.getCurrentLocale()
                const skippable =
                    !EDITOR_NOT_IN_PREVIEW && skipTextureIfSameLocale && currentLocale === this.defaultLocale
                if (!skippable) await this.updateTextureLocalization(this.key, signal)
            } else {
                throw new Error(`Key ${this.key} not found in locales`)
            }

            if (signal.aborted) return

            this.localized = true
            game.emit(GameEvents.LOCALIZE_APPLIED, { localize: this, key: this.key } as LocalizeAppliedPayload)
            this.node.emit(GameEvents.LOCALIZE_APPLIED, { localize: this, key: this.key } as LocalizeAppliedPayload)
        } catch (error) {
            console.warn(error)
        }
    }

    @ComponentUtils.onlyRunInLifeCycleAsync
    private async updateTextLocalization(key: TextKey, signal: Signal) {
        const locale = language.getCurrentLocale()

        const text = language.getText(
            key,
            this.params.map((p) => p.text),
            locale,
        )

        const label = this.label

        if (label === undefined) {
            throw new Error(`Text localize component <${this.node.name}> has no label`)
        }

        signal.wrap(() => this.setText(label, text))?.()

        if (this.updateFont) {
            await signal.wrap(() => this.setTextFont(label, locale, signal))?.()
        }
    }

    @ComponentUtils.onlyRunInLifeCycleAsync
    private async setText(label: Label, text: string) {
        label.string = text
    }

    @ComponentUtils.onlyRunInLifeCycleAsync
    private async setTextFont(label: Label, locale: Locale, signal: Signal) {
        if (!EDITOR_NOT_IN_PREVIEW) {
            await this.setTextFontRuntime(label, locale, signal)
        } else {
            await this.setTextFontEditor(label, locale, signal)
        }
    }

    @ComponentUtils.onlyRunInLifeCycleAsync
    private async setTextFontRuntime(label: Label, locale: Locale, signal: Signal) {
        const font = await ResourceUtils.loadFontAsync(locale)

        if (signal.aborted) return

        if (font !== null) {
            this.setLabelFontInLifeCycle(label, font)
        }
    }

    @ComponentUtils.onlyRunInLifeCycle
    private setLabelFontInLifeCycle(label: Label, font: Font) {
        label.font = font
    }

    @ComponentUtils.onlyRunInLifeCycleAsync
    private async setTextFontEditor(label: Label, locale: Locale, signal: Signal) {
        const uuid = await this.requestFontUuid(locale)
        if (uuid === null) return

        const nodeInfo = await signal.wrap(() => this.requestNodeInfo(label.node.uuid))?.()
        if (nodeInfo === undefined) return

        const propertyIndex = A.searchIndex(nodeInfo.__comps__, (c: { type: string }) => c.type === 'cc.Label')
        const path = `__comps__.${propertyIndex}.font`

        await signal.wrap(() => this.requestUpdateLabelFont(label.node.uuid, path, uuid))?.()
    }

    private async requestFontUuid(locale: Locale) {
        return await Editor.Message.request('asset-db', 'query-uuid', `db://assets/loadable/${LocaleFontPaths[locale]}`)
    }

    private requestNodeInfo(uuid: string) {
        return Editor.Message.request('scene', 'query-node', uuid)
    }

    @ComponentUtils.onlyRunInLifeCycleAsync
    private async requestUpdateLabelFont(nodeUuid: string, compPath: string, fontUuid: string) {
        await Editor.Message.request('scene', 'set-property', {
            uuid: nodeUuid,
            path: compPath,
            dump: {
                type: 'cc.Font',
                value: {
                    uuid: fontUuid,
                },
            },
        })
    }

    @ComponentUtils.onlyRunInLifeCycleAsync
    private async updateTextureLocalization(key: TextureKey, signal: Signal) {
        if (this.sprite === undefined) {
            throw new Error(`Texture localize component <${this.node.name}> has no sprite`)
        }

        const path = language.getTexture(key)

        if (!EDITOR_NOT_IN_PREVIEW) {
            await this.setSpriteRuntime(this.sprite, path, signal)
        } else {
            await this.setSpriteEditor(this.sprite, path, signal)
        }
    }

    @ComponentUtils.onlyRunInLifeCycleAsync
    private async setSpriteRuntime(sprite: Sprite, path: string, signal: Signal) {
        const spriteFrame = await ResourceUtils.loadSpriteFrameAsync(`${path}/spriteFrame`, this.packable)

        if (signal.aborted) return

        if (spriteFrame === null) {
            console.warn(`Sprite not found by path: ${path}`)
            return
        }

        this.setSprite(sprite, spriteFrame)
    }

    @ComponentUtils.onlyRunInLifeCycle
    private setSprite(sprite: Sprite, spriteFrame: SpriteFrame) {
        sprite.spriteFrame = spriteFrame
    }

    @ComponentUtils.onlyRunInLifeCycleAsync
    private async setSpriteEditor(sprite: Sprite, spritePath: string, signal: Signal) {
        const frameInfo = await this.requestSpriteFrameInfo(spritePath)

        if (frameInfo.length === 0) {
            console.error(`Sprite not found by path: ${spritePath}`)
            return
        }

        if (frameInfo.length > 1) {
            const infoDump = frameInfo.slice(0, 5).map((i: { path: string }) => i.path)
            const dump = `${infoDump.join(',')}${infoDump.length < frameInfo.length ? '...' : ''}`
            console.warn(`Ambiguous sprite's path: ${spritePath}. Found ${dump}`)
            return
        }

        const nodeInfo = await signal.wrap(() => this.requestNodeInfo(sprite.node.uuid))?.()
        if (nodeInfo === undefined) return

        const uuid = (frameInfo[0] as (typeof frameInfo)[number]).uuid

        const propertyIndex = A.searchIndex(nodeInfo.__comps__, (c: { type: string }) => c.type === 'cc.Sprite')
        const path = `__comps__.${propertyIndex}.spriteFrame`

        await signal.wrap(() => this.requestUpdateSpriteSpriteFrame(sprite.node.uuid, path, uuid))?.()
    }

    private requestSpriteFrameInfo(spritePath: string) {
        return Editor.Message.request('asset-db', 'query-assets', {
            pattern: `db://assets/loadable/${spritePath}*`,
            ccType: 'cc.SpriteFrame',
        })
    }

    private async requestUpdateSpriteSpriteFrame(nodeUuid: string, compPath: string, frameUuid: string) {
        await Editor.Message.request('scene', 'set-property', {
            uuid: nodeUuid,
            path: compPath,
            dump: {
                type: 'cc.SpriteFrame',
                value: {
                    uuid: frameUuid,
                },
            },
        })
    }
}
