import { Animation, AnimationClip, Button, Component, Label, Node, UIOpacity, Vec3, _decorator, lerp, tween } from 'cc'
import { Localize } from '../../components/text/Localize'
import CoinData from '../../constants/CoinData'
import { CustomModeLevel } from '../../gameplay/constants/CustomGameData'
import GameplayEvents from '../../gameplay/constants/GameEvents'
import { ParticleSystem } from '../../particles/ParticleSystem'
import { CoinContainer } from '../../scenes/common/coin-container/CoinContainer'
import AdsUtils from '../../utils/AdsUtils'
import AnimUtils from '../../utils/AnimUtils'
import ComponentUtils from '../../utils/ComponentUtils'
import AnimAbortSignal from '../../utils/common/AnimAbortSignal'
import LayoutUtils from '../../utils/LayoutUtils'
import { BaseScreen } from '../BaseScreen'
import { GetRewardsEffect } from './GetRewardsEffect'

const { ccclass, property, requireComponent } = _decorator

const {
    Events,
    Utils: { Valid: V, Signal: S },
    Plugins: { Ads: { Types: AdTypes } = {} },
} = Ludex
const { event, player } = codex

@ccclass('LevelCompleteScreen')
@requireComponent(BaseScreen)
export class LevelCompleteScreen extends Component {
    private screen!: BaseScreen

    private level: number = 1
    private coins: number = 40
    private currentCoins: number = 0
    private score: number = 0
    private playAnimation: boolean = false
    private getRewardsSuccess: boolean = false
    private lookingForRewards: boolean = false

    private playAnimationSignal: AnimAbortSignal = new AnimAbortSignal()

    @property(Label)
    private coinText!: Label

    @property(Label)
    private scoreText!: Label

    @property(UIOpacity)
    private lookingForRewardsText!: UIOpacity

    @property(GetRewardsEffect)
    private getRewardsEffect!: GetRewardsEffect

    @property({ type: Button, group: 'Play Button' })
    private playButton!: Button

    @property({ type: Node, group: 'Play Button' })
    private playBackground!: Node

    @property({ type: UIOpacity, group: 'Play Button' })
    private shineBackground!: UIOpacity

    @property({ type: Node, group: 'Play Button' })
    private hardLevelBackground!: Node

    @property({ type: Node, group: 'Play Button' })
    private buttonsContainer!: Node

    @property({ type: Button, group: 'Home Button' })
    private homeButton!: Button

    @property({ type: Animation, group: 'Coin Container' })
    private coinAnimation!: Animation

    @property({ type: CoinContainer, group: 'Coin Container' })
    private coinContainer!: CoinContainer

    @property({ type: Animation, group: 'Coin Container' })
    private coinContainerAnimation!: Animation

    @property({ type: AnimationClip, group: 'Coin Container' })
    private coinContainerEnterClip!: AnimationClip

    @property({ type: AnimationClip, group: 'Coin Container' })
    private coinContainerExitClip!: AnimationClip

    @property({ type: ParticleSystem, group: 'Particles' })
    private leftConfetti!: ParticleSystem

    @property({ type: ParticleSystem, group: 'Particles' })
    private rightConfetti!: ParticleSystem

    @property({ type: ParticleSystem, group: 'Particles' })
    private bokehParticles!: ParticleSystem

    @property(UIOpacity)
    private welldoneUiOpacity!: UIOpacity

    @property(Localize)
    private welldoneLocalize!: Localize

    private localizeVisibilityController = new S.SignalController()

    private coinContainerShown: boolean = false

    public __preload() {
        this.enabled = false
        this.screen = this.getComponent(BaseScreen)!
        this.validateRequiredComponents()
        this.listenScreenEvents()
    }

    public onDestroy(): void {
        this.unListenScreenEvents()
        this.localizeVisibilityController.abort()
    }

    private validateRequiredComponents() {
        ComponentUtils.lazyValidateProperties(this)
    }

    private listenScreenEvents() {
        // ? Here's a demo of all the events a screen has
        event.on(Events.OPENING_SCREEN, this.handleOpeningScreen)
        event.on(Events.OPENED_SCREEN, this.handleOpenedScreen)
    }

    private unListenScreenEvents() {
        event.off(Events.OPENING_SCREEN, this.handleOpeningScreen)
        event.off(Events.OPENED_SCREEN, this.handleOpenedScreen)
    }

    private handleOpeningScreen = (payload: SwitchScreenPayload) => {
        if (!this.screen) {
            throw new Error('StarChestScreen: screen not found')
        }

        if (!this.validateEventPayload(payload)) return

        this.setInteractableButtons(true)
        this.hideTextUntilLocalized()
    }

    private async hideTextUntilLocalized() {
        const signal = this.localizeVisibilityController.renew()
        this.welldoneUiOpacity.opacity = 0
        await this.welldoneLocalize.waitUntilLocalized()
        if (signal.aborted) return
        await AnimUtils.runTweenAsync(tween(this.welldoneUiOpacity).to(0.5, { opacity: 255 }), signal)
    }

    private handleOpenedScreen = (payload: SwitchScreenPayload) => {
        if (!this.validateEventPayload(payload)) return

        this.open()
    }

    private validateEventPayload(payload: SwitchScreenPayload) {
        const { screenName, screenData } = payload ?? {}
        if (screenName !== this.node.name) return false

        if (!V.isObject(screenData)) return false

        if (V.isNumber(screenData.level)) {
            this.level = screenData.level
            this.coins = this.level < 5 ? CoinData.LEVEL_BELOW_5 : CoinData.LEVEL_ABOVE_5
        }

        if (V.isNumber(screenData.coins)) {
            this.currentCoins = screenData.coins
        }

        if (V.isNumber(screenData.score)) {
            this.score = screenData.score
        }

        this.playAnimation = false
        if (V.isObject(screenData.overrideAnimation)) {
            this.playAnimation = screenData.overrideAnimation.openScreen === 'LevelCompleteOpenScreen'
        }

        this.lookingForRewards = false
        if (V.isBoolean(screenData.lookingForRewards)) {
            this.lookingForRewards = screenData.lookingForRewards
        }

        this.getRewardsSuccess = false
        if (V.isBoolean(screenData.getRewardsSuccess)) {
            this.getRewardsSuccess = screenData.getRewardsSuccess
        }

        return true
    }

    public open() {
        this.updateStatus()
        this.node.scene.emit(GameplayEvents.HIDE_UI)
        this.tryPlayAnimation()
    }

    public async close(canShowRewardsAd: boolean = false) {
        this.localizeVisibilityController.abort()

        this.stopParticles()

        if (this.coinContainerShown) {
            this.coinContainerShown = false
            await AnimUtils.runAnimationAsync(this.coinContainerAnimation, this.coinContainerExitClip.name)
        }

        event.emit(Events.CLOSE_SCREEN, {
            screenName: this.node.name,
            screenData: {
                canShowRewardsAd,
            },
        } as SwitchScreenPayload)
        this.node.scene.emit(GameplayEvents.ABORT_COIN_ANIMATION)
    }

    private updateStatus(): void {
        this.updateCoinContainer()
        this.updateButtonsContainer()
        this.lookingForRewardsText.opacity = 0
    }

    private updateCoinContainer(): void {
        this.coinContainer.updateCoins({
            coins: this.currentCoins,
        })
    }

    private updateButtonsContainer(): void {
        const gameData = player.getGameData()
        if (!gameData) return

        const nextLevel = this.level + 1 > gameData.totalLevel ? this.level : this.level + 1

        const isHardLevel = nextLevel % 5 === 0
        const customModeUnlocked = nextLevel === CustomModeLevel

        this.playButton.node.active = false
        this.homeButton.node.active = false

        if (customModeUnlocked) {
            this.homeButton.node.active = true
            return
        }

        this.playButton.node.active = true
        this.playBackground.active = !isHardLevel
        this.hardLevelBackground.active = isHardLevel
    }

    private tryPlayAnimation(): void {
        if (!this.playAnimation) {
            this.animateVisibleButtonContainer(true, false)

            if (!this.getRewardsSuccess) return
            this.getRewardsEffect.playAnimation(
                ['storm', 'thunder'],
                this.triggerCollectItemPlayButtonAnimation.bind(this),
            )

            return
        }

        this.screen.setScreenOpacity(255)
        this.coinText.string = 'x0'
        this.scoreText.string = '0'
        this.animateVisibleButtonContainer(false, false)

        // Play particle
        this.playParticles()

        // run this.animateCoin() after open animation
        const anim = this.screen.getComponent(Animation)
        if (anim) {
            anim.once(Animation.EventType.FINISHED, this.handleParticleEnd)
        } else {
            this.handleParticleEnd()
        }
    }

    private handleParticleEnd = async () => {
        if (this.level) {
            // ? Wait confetti to finish
            await AnimUtils.waitTweenAsync(0.15)
        }

        await Promise.all([this.animateCoin(), this.animateScore()])

        if (this.lookingForRewards) {
            await this.handleLookingForRewards()
        } else {
            await this.animateVisibleButtonContainer(true)
        }
    }

    private async handleLookingForRewards(): Promise<void> {
        await this.animateLookingForRewards()

        if (AdTypes && AdsUtils.isReadyAdShowable(AdTypes.REWARDED_INTERSTITIAL)) {
            this.close(true)
        } else {
            this.animateVisibleButtonContainer(true)
        }
    }

    private async animateCoin(): Promise<void> {
        if (!this.screen.container) {
            console.warn('LevelCompleteScreen: screen not found')
            return
        }

        this.coinAnimation.play()

        await AnimUtils.runOnUpdateAsync(this.node, 0.8, (ratio) => {
            const quantity = lerp(0, this.coins, ratio)
            const intQuantity = Math.floor(quantity)
            this.coinText.string = `x${intQuantity}`
        })

        await this.animateCoinCollection()

        this.coinText.string = `x${this.coins}`
    }

    private async animateScore(): Promise<void> {
        AnimUtils.runOnUpdateAsync(this.scoreText.node, 0.8, (ratio) => {
            const scoreNum = lerp(0, this.score, ratio)
            this.scoreText.string = `${Math.floor(scoreNum)}`
        })
        this.scoreText.node.setScale(1, 1, 1)
        this.scoreText.string = `${this.score}`
    }

    private async animateCoinCollection(): Promise<void> {
        this.coinContainerShown = true
        await AnimUtils.runAnimationAsync(this.coinContainerAnimation, this.coinContainerEnterClip.name)

        this.node.scene.emit(GameplayEvents.ADD_COINS, {
            coins: this.coins,
            collectionAnimation: {
                floatDuration: 0.5,
                flyDuration: 0.5,
                animateTextOnly: false,
                target: this.coinContainer,
            },
        } as CoinDataPayload)
    }

    private async animateLookingForRewards(): Promise<void> {
        const flashTween = tween(this.lookingForRewardsText)
        flashTween.to(0.25, { opacity: 255 }, { easing: 'linear' }).delay(0.25)
        flashTween.to(0.25, { opacity: 100 }, { easing: 'linear' }).delay(0.125)

        await AnimUtils.runTweenAsync(
            tween(this.lookingForRewardsText).repeat(2, flashTween).to(0.25, { opacity: 0 }, { easing: 'linear' }),
        )

        this.lookingForRewardsText.opacity = 0
    }

    private async animateVisibleButtonContainer(visible: boolean, animate: boolean = true): Promise<void> {
        const endScale = visible ? 1 : 0

        if (animate) {
            this.playAnimationSignal.abort()
            const signal = new AnimAbortSignal()
            this.playAnimationSignal = signal

            const startScale = visible ? 0 : 1

            this.buttonsContainer.setScale(startScale, startScale, this.buttonsContainer.scale.z)

            const scaleTween = tween(this.buttonsContainer).to(
                0.5,
                { scale: new Vec3(endScale, endScale, 1) },
                { easing: 'backOut' },
            )

            if (!visible) this.setInteractableButtons(false)
            await AnimUtils.runTweenAsync(scaleTween, signal)
            if (visible) this.setInteractableButtons(true)
        } else {
            this.setInteractableButtons(visible)
            this.buttonsContainer.setScale(endScale, endScale, this.buttonsContainer.scale.z)
        }
    }

    private async triggerCollectItemPlayButtonAnimation(): Promise<void> {
        this.playAnimationSignal.abort()
        const signal = new AnimAbortSignal()
        this.playAnimationSignal = signal

        const scaleTween = tween(this.buttonsContainer)
            .to(0.08, { scale: new Vec3(0.95, 1.05, 1) })
            .to(0.08, { scale: new Vec3(1.05, 0.95, 1) })
            .to(0.08, { scale: new Vec3(1, 1, 1) })

        const shineTween = tween(this.shineBackground)
            .to(0.08, { opacity: 50 }, { easing: 'quadOut' })
            .to(0.08, { opacity: 0 }, { easing: 'quadIn' })

        await Promise.all([AnimUtils.runTweenAsync(scaleTween, signal), AnimUtils.runTweenAsync(shineTween, signal)])
    }

    private playParticles(): void {
        const isLandscape = LayoutUtils.isLandscape()

        const burstCount = isLandscape ? 50 : 30
        const startSpeedMin = isLandscape ? 5000 : 2000
        const startSpeedMax = isLandscape ? 15000 : 10000
        const leftAngle = isLandscape ? 40 : 60
        const rightAngle = isLandscape ? 110 : 100
        const arc = isLandscape ? 30 : 20

        this.leftConfetti.bursts[0]!.count = this.rightConfetti.bursts[0]!.count = burstCount
        this.leftConfetti.startSpeed.constantMin = this.rightConfetti.startSpeed.constantMin = startSpeedMin
        this.leftConfetti.startSpeed.constantMax = this.rightConfetti.startSpeed.constantMax = startSpeedMax
        this.leftConfetti.shapeModule.angle = leftAngle
        this.rightConfetti.shapeModule.angle = rightAngle
        this.leftConfetti.shapeModule.arc = this.rightConfetti.shapeModule.arc = arc

        this.leftConfetti.play()
        this.rightConfetti.play()
        this.bokehParticles.play()
    }

    private stopParticles(): void {
        this.leftConfetti.stop()
        this.rightConfetti.stop()
        this.bokehParticles.stop()
    }

    private setInteractableButtons(interactable: boolean): void {
        this.playButton.interactable = interactable
        this.homeButton.interactable = interactable
    }

    // ? Callback for click event of PlayButton
    public handleClickedPlay() {
        this.setInteractableButtons(false)
        this.close()
    }

    // ? Callback for click event of HomeButton
    public handleClickedHome() {
        this.setInteractableButtons(false)
        this.close()
    }
}
