import CommonError from './CommonError'

export default class PlayerHasTournamentError extends CommonError {
    public playerId: string | number
    public tournamentId: string | number

    public constructor(playerId: string | number, tournamentId: string | number) {
        super('PLAYER_HAS_TOURNAMENT', 'Player has a tournament')
        this.playerId = playerId
        this.tournamentId = tournamentId
    }
}
