import ComponentUtils from 'db://assets/utils/ComponentUtils'
import {
    _decorator,
    Component,
    easing,
    type IVec2Like,
    instantiate,
    lerp,
    type Node,
    Prefab,
    pseudoRandomRange,
    randomRangeInt,
    tween,
    UIOpacity,
    UITransform,
    Vec2,
} from 'cc'
import Ray from './Ray'
import RayManagerProperties from './RayManagerProperties'

const { ccclass, property } = _decorator

@ccclass('RayManager')
export default class RayManager extends Component {
    @ComponentUtils.required()
    @property({ type: Prefab })
    public readonly rayPrefab!: Prefab

    @ComponentUtils.required()
    @property({ type: UIOpacity })
    public readonly uiOpacity!: UIOpacity

    @ComponentUtils.required()
    @property({ type: UITransform })
    public readonly uiTransform!: UITransform

    @property({ type: RayManagerProperties })
    public readonly properties: RayManagerProperties = new RayManagerProperties()

    private rayPool!: RayPool

    protected __preload(): void {
        ComponentUtils.validateRequiredProperties(this, RayManager)
        this.rayPool = createRayPool(this.rayPrefab, this.node)
        this.rayPool = prepareRays(this.rayPool, this.properties.segments)
    }

    protected onEnable(): void {
        this.rayPool = prepareRays(this.rayPool, this.properties.segments)
    }

    protected onDestroy(): void {
        this.rayPool = destroyAllRays(this.rayPool)
    }

    public createRayAnimation(target?: IVec2Like, seed?: number) {
        const [rays, pool] = getRays(this.rayPool, this.properties.segments)
        this.rayPool = pool

        const firstSeed = getSeed(seed)

        return createRayAnimation(this, rays, target, firstSeed).call(() => {
            this.rayPool = releaseRays(rays, this.rayPool)
        })
    }
}

type RayPool = {
    prefab: Prefab
    container: Node
    activeRays: Ray[]
    inactiveRays: Ray[]
}

const createRayPool = (prefab: Prefab, container: Node): RayPool => ({
    prefab,
    container,
    activeRays: [],
    inactiveRays: [],
})

const getRay = (pool: RayPool): [Ray, RayPool] => retrieveRay(pool) ?? createRay(pool)

const getRays = (pool: RayPool, count: number): [Ray[], RayPool] => {
    const rays: Ray[] = []
    let currentPool = pool

    for (let i = 0; i < count; i++) {
        const [ray, pool] = getRay(currentPool)
        rays.push(ray)
        currentPool = pool
    }

    return [rays, currentPool]
}

const prepareRays = (pool: RayPool, count: number): RayPool => {
    if (pool.inactiveRays.length >= count) {
        return pool
    }

    const raysToCreate = count - pool.inactiveRays.length
    let currentPool = pool

    for (let i = 0; i < raysToCreate; i++) {
        const [ray, pool] = createRay(currentPool)
        currentPool = releaseRay(ray, pool)
    }

    return currentPool
}

const releaseRay = (ray: Ray, pool: RayPool): RayPool => {
    ray.node.active = false

    const inactiveRays = [...pool.inactiveRays, ray]
    const activeRays = pool.activeRays.filter((r) => r !== ray)

    return { ...pool, activeRays, inactiveRays }
}

const releaseRays = (rays: Ray[], pool: RayPool): RayPool => {
    for (const ray of rays) {
        ray.node.active = false
    }

    const inactiveRays = [...pool.inactiveRays, ...rays]
    const activeRays = pool.activeRays.filter((r) => !rays.includes(r))

    return { ...pool, activeRays, inactiveRays }
}

const destroyAllRays = (pool: RayPool): RayPool => {
    pool.container.destroyAllChildren()

    const activeRays: Ray[] = []
    const inactiveRays: Ray[] = []

    return { ...pool, activeRays, inactiveRays }
}

const retrieveRay = (pool: RayPool): [Ray, RayPool] | undefined => {
    if (pool.inactiveRays.length === 0) return undefined

    const [first, ...rest] = pool.inactiveRays
    const ray = first as (typeof pool.inactiveRays)[number]

    const activeRays = [...pool.activeRays, ray]
    const inactiveRays = rest

    ray.node.active = true

    return [ray, { ...pool, activeRays, inactiveRays }]
}

const createRay = (pool: RayPool): [Ray, RayPool] => {
    const ray = instantiate(pool.prefab)
    pool.container.addChild(ray)

    const rayComponent = ray.getComponent(Ray)

    if (!rayComponent) {
        throw new Error('Ray component not found')
    }

    const activeRays = [...pool.activeRays, rayComponent]

    rayComponent.node.active = true

    return [
        rayComponent,
        {
            ...pool,
            activeRays,
        },
    ]
}

const alignRays = (rayManager: RayManager, rays: Ray[], target?: IVec2Like, seed = 0) => {
    const getSimpleRayAnchorsCached = getSimpleRayAnchors(
        rayManager.properties.segments,
        rayManager.uiTransform.height * rayManager.node.worldScale.y,
        rayManager.uiTransform.width * rayManager.node.worldScale.x,
        rayManager.properties.jaggedness,
    )

    const simpleAnchors = {
        start: { x: 0, y: 0 },
        end: { x: 0, y: rayManager.uiTransform.height * rayManager.node.worldScale.y },
    } as const

    const transformedAnchors = {
        start: rayManager.node.worldPosition,
        end: target ?? {
            x: 0,
            y: rayManager.node.worldPosition.y + rayManager.uiTransform.height * rayManager.node.worldScale.y,
        },
    } as const

    const transformFromSimpleToWorld = transformPoint(simpleAnchors.start, simpleAnchors.end)(
        transformedAnchors.start,
        transformedAnchors.end,
    )

    const alignRayCached = alignRay()

    const alignRayToTransformed = (ray: Ray, start: IVec2Like, end: IVec2Like) =>
        alignRayCached(ray, transformFromSimpleToWorld(start), transformFromSimpleToWorld(end))

    let lastEndAnchor: IVec2Like = { x: 0, y: 0 }
    let currentSeed = seed

    for (let i = 0; i < rayManager.properties.segments; i++) {
        const { start, end, seed } = getSimpleRayAnchorsCached(lastEndAnchor, i, currentSeed)
        const thickness = getRayThickness(rayManager, i / (rayManager.properties.segments - 1))
        const ray = rays[i] as (typeof rays)[number]

        const alignedRay = alignRayToTransformed(ray, start, end)
        alignedRay.uiTransform.setContentSize(alignedRay.uiTransform.width, thickness)

        lastEndAnchor = end
        currentSeed = seed
    }

    return rays
}

const alignRay = () => {
    const tempDirection: IVec2Like = { x: 0, y: 0 }

    return (ray: Ray, startAnchor: IVec2Like, endAnchor: IVec2Like): Ray => {
        const midX = (startAnchor.x + endAnchor.x) / 2
        const midY = (startAnchor.y + endAnchor.y) / 2

        Vec2.subtract(tempDirection, endAnchor, startAnchor)

        const distance = Vec2.len(tempDirection)
        const width = distance / ray.node.worldScale.x + ray.widthOffset

        Vec2.normalize(tempDirection, tempDirection)
        const rotation = (Math.atan2(tempDirection.y, tempDirection.x) * 180) / Math.PI

        ray.node.setWorldPosition(midX, midY, 0)
        ray.node.setRotationFromEuler(0, 0, rotation)
        ray.uiTransform.setContentSize(width, ray.uiTransform.height)

        return ray
    }
}

const getRayThickness = (rayManager: RayManager, ratio: number) =>
    lerp(rayManager.properties.thicknessStart, rayManager.properties.thicknessEnd, ratio)

const createRayAnimation = (rayManager: RayManager, rays: Ray[], target?: IVec2Like, firstSeed?: number) => {
    const secondSeed = getSeed(firstSeed)
    const thirdSeed = getSeed(secondSeed)

    return tween(rayManager.uiOpacity)
        .call(() => alignRays(rayManager, rays, target, firstSeed))
        .call(() => {
            for (const ray of rays) {
                ray.uiOpacity.opacity = 255
            }
        })
        .set({ opacity: 255 })
        .delay(0.05)
        .set({ opacity: 0 })
        .delay(0.05)
        .call(() => alignRays(rayManager, rays, target, secondSeed))
        .set({ opacity: 255 })
        .delay(0.05)
        .set({ opacity: 0 })
        .delay(0.05)
        .call(() => alignRays(rayManager, rays, target, thirdSeed))
        .set({ opacity: 255 })
        .parallel(
            tween(rayManager.uiOpacity)
                .delay(0.2)
                .set({ opacity: 0 })
                .call(() => {
                    for (const ray of rays) {
                        ray.uiOpacity.opacity = 0
                    }
                }),
            ...rays.map((ray, index, array) =>
                tween(ray.node).update(0.2, (_, ratio) => {
                    ray.uiTransform.setContentSize(
                        ray.uiTransform.width,
                        lerp(
                            getRayThickness(rayManager, index / (array.length - 1)),
                            rayManager.properties.thicknessEnd,
                            easing.cubicIn(ratio),
                        ),
                    )
                }),
            ),
        )
}

const X_RANDOMNESS_INITIAL_SCALE = 1
const X_RANDOMNESS_FINAL_SCALE = 0

const getSimpleRayAnchors =
    (segmentCount: number, containerHeight: number, horizontalDeviation: number, jaggedness: number) =>
    (lastEndAnchor: IVec2Like, i: number, seed: number): { start: IVec2Like; end: IVec2Like; seed: number } => {
        if (i === 0) {
            return {
                start: { x: 0, y: 0 },
                end: { x: 0, y: 0 },
                seed,
            }
        }

        if (i === segmentCount - 1) {
            return {
                start: { x: lastEndAnchor.x, y: lastEndAnchor.y },
                end: { x: 0, y: containerHeight },
                seed,
            }
        }

        const firstSeed = getSeed(seed)
        const secondSeed = getSeed(firstSeed)

        const startX = lastEndAnchor.x
        const startY = lastEndAnchor.y

        const lastRatio = (i - 1) / (segmentCount - 1)
        const ratio = i / (segmentCount - 1)

        const minX = Math.max(-horizontalDeviation, lerp(startX, startX - horizontalDeviation, jaggedness))
        const maxX = Math.min(horizontalDeviation, lerp(startX, startX + horizontalDeviation, jaggedness))

        const endX =
            pseudoRandomRange(secondSeed, minX, maxX) *
            lerp(X_RANDOMNESS_INITIAL_SCALE, X_RANDOMNESS_FINAL_SCALE, ratio)

        const minY = lerp(0, containerHeight, lastRatio)
        const maxY = lerp(0, containerHeight, ratio)

        const endY = pseudoRandomRange(firstSeed, minY, maxY)

        return { start: { x: startX, y: startY }, end: { x: endX, y: endY }, seed: secondSeed }
    }

const getSeed = (seed?: number) => pseudoRandomRange(seed ?? randomRangeInt(0, 1e6), 0, 1e6)

const transformPoint = (fromStart: IVec2Like, fromEnd: IVec2Like) => {
    const fromDirection: IVec2Like = {
        x: fromEnd.x - fromStart.x,
        y: fromEnd.y - fromStart.y,
    }

    return (toStart: IVec2Like, toEnd: IVec2Like) => {
        const toDirection: IVec2Like = { x: toEnd.x - toStart.x, y: toEnd.y - toStart.y }

        const fromAngle = Math.atan2(fromDirection.y, fromDirection.x)
        const toAngle = Math.atan2(toDirection.y, toDirection.x)

        const angle = toAngle - fromAngle

        const cosR = Math.cos(angle)
        const sinR = Math.sin(angle)

        const scale = Vec2.len(toDirection) / Vec2.len(fromDirection)

        const transVec: IVec2Like = {
            x: toStart.x - fromStart.x,
            y: toStart.y - fromStart.y,
        }

        const temp: IVec2Like = { x: 0, y: 0 }

        return (point: IVec2Like): IVec2Like => {
            temp.x = point.x * cosR - point.y * sinR
            temp.y = point.x * sinR + point.y * cosR

            temp.x *= scale
            temp.y *= scale

            temp.x += transVec.x
            temp.y += transVec.y

            return { x: temp.x, y: temp.y }
        }
    }
}
