import ItemKeys from '../../constants/ItemKeys'
import type EffectManager from '../../manager/EffectManager'
import type Board from '../board'
import type { Tile } from '../board'
import withBlockedInput from './withBlockedInput'
import withCenteredBoard from './withCenteredBoard'
import withEffectAnimationEvents from './withEffectAnimationEvents'
import withItemAnalytics from './withItemAnalytics'
import withLoweredVolume from './withLoweredVolume'

const useThunderItemLogic = async (board: Board, effectManager: EffectManager) => {
    const uncleared = board.getUncleared()
    if (uncleared.length < 2 * 2) return
    const pairs = board.getPairs(2) as [[Tile, Tile], [Tile, Tile]]
    const useThunder = withEffectAnimationEvents(board, withCenteredBoard(board, playThunderAnimationEffect))
    board.clearSelectedTile()
    await useThunder(effectManager, pairs, board)
}

const useThunderItem = withItemAnalytics(ItemKeys.THUNDER, withLoweredVolume(withBlockedInput(useThunderItemLogic)))

export default useThunderItem
function playThunderAnimationEffect(effectManager: EffectManager, pairs: [[Tile, Tile], [Tile, Tile]], board: Board) {
    return effectManager.playThunderAnimation(pairs, board)
}
