import { CCInteger, CCString, Component, Label, Vec3, _decorator, tween } from 'cc'
import { TapperedProgressBar } from '../../components/tappered-progress-bar/TapperedProgressBar'
import AnimUtils from '../../utils/AnimUtils'
import ComponentUtils from '../../utils/ComponentUtils'
import type AnimAbortSignal from '../../utils/common/AnimAbortSignal'

const { ccclass, property } = _decorator

@ccclass('StarProgressBar')
export class StarProgressBar extends Component {
    @property(TapperedProgressBar)
    private progressBar!: TapperedProgressBar

    @property(Label)
    private progressLabel!: Label

    @property(CCInteger)
    private current!: number

    @property(CCInteger)
    private total!: number

    @property(CCString)
    private separator!: string

    public setRange(current: number, total: number): void {
        this.total = total

        this.current = current

        this.updateProgress()
    }

    public onLoad(): void {
        this.validateRequiredComponents()
    }

    @ComponentUtils.onlyRunInLifeCycle
    private updateProgress() {
        this.progressBar.progress = Math.min(this.current, this.total) / this.total
        this.updateProgressLabel()
    }

    private updateProgressLabel() {
        this.progressLabel.string = `${this.current}${this.separator}${this.total}`
    }

    public async animateTo(
        current: number,
        signal?: AnimAbortSignal,
        onProgress?: (current: number) => void,
    ): Promise<void> {
        const progress = Math.min(current, this.total) / this.total

        const start = this.current

        let i = 1

        const progressTween = tween(this.progressBar).to(
            3,
            { progress },
            {
                easing: 'quadOut',
                onUpdate: (_target, ratio) => {
                    if (!ratio) return
                    this.current = start + Math.floor((current - start) * ratio)
                    onProgress?.(this.current)

                    if (this.current === start - i) {
                        i++
                        tween(this.progressLabel.node)
                            .to(0.05, { scale: new Vec3(1.2, 1.2, 1.2) })
                            .to(0.05, { scale: Vec3.ONE })
                            .start()
                    }

                    this.updateProgressLabel()
                },
            },
        )

        const cb = () => {
            this.current = current
            this.updateProgress()
        }

        signal?.addAbortListener(cb)
        await AnimUtils.runTweenAsync(progressTween, signal)
        signal?.removeAbortListener(cb)
    }

    private validateRequiredComponents(): void {
        if (!this.progressBar) {
            throw new Error('JourneyProgressBar: Missing required component TapperedProgressBar')
        }

        if (!this.progressLabel) {
            throw new Error('JourneyProgressBar: Missing required component Label')
        }
    }
}
