import { _decorator, ProgressBar, Sprite, UIOpacity } from 'cc'
const { ccclass, property, executeInEditMode, requireComponent } = _decorator

@ccclass('TapperedProgressBar')
@executeInEditMode
@requireComponent(UIOpacity)
export class TapperedProgressBar extends ProgressBar {
    @property({
        type: UIOpacity,
        displayName: 'Fill opacity',
    })
    private uiOpacity: UIOpacity | null = null

    @property({
        type: Sprite,
        displayName: 'Left Cap',
        tooltip: 'If assigned, will be displayed when progress is larger than 0%',
    })
    public leftCap: Sprite | null = null

    @property({
        type: Sprite,
        displayName: 'Right Cap',
        tooltip: 'If assigned, will be displayed when progress is at 100%',
    })
    public rightCap: Sprite | null = null

    /**
     * @en
     * The current progress of the bar sprite. The valid value is between 0-1.
     *
     * @zh
     *  0-1 
     */
    @property({
        range: [0, 1, 0.01],
        slide: true,
        tooltip: 'i18n:progress.progress',
        override: true,
    })
    public override get progress(): number {
        return super.progress
    }

    public override set progress(value: number) {
        super.progress = value
        this.updateAppearance()
    }

    private updateAppearance(): void {
        if (this.uiOpacity === null) return

        if (this.progress <= 0) {
            this.uiOpacity.opacity = 0
            return
        }

        if (this.progress > 0.1) {
            this.uiOpacity.opacity = 255
            return
        }

        // maps the progress from [0 -> 0.1] to [0 -> 255]
        const opacity = Math.floor((this.progress / 0.05) * 255)
        this.uiOpacity.opacity = opacity
    }

    public start(): void {
        this.updateAppearance()
    }
}
