import GeneralUtils from '../../../utils/GeneralUtils'

const withBlockedInput =
    // biome-ignore lint/suspicious/noExplicitAny: Needed to use function inference
        <F extends (...args: any[]) => any, O = ReturnType<F> extends Promise<infer O> ? O : never>(fn: F) =>
        async (...args: Parameters<F>): Promise<O> => {
            GeneralUtils.setBlockInput(true)
            const out = await fn(...args)
            GeneralUtils.setBlockInput(false)
            return out
        }

export default withBlockedInput
