import { type AudioClip, AudioSource, type Node } from 'cc'
import ResourceUtils from '../../utils/ResourceUtils'
import AudioPlayer from './AudioPlayer'

type SoundKeys = Ludex.Plugins.Audio.SoundKeys
type PlayConfig = Ludex.Plugins.Audio.PlayConfig

type TrackingAudioClip = {
    audioPlayer: AudioPlayer
    key: string
}
type MultipleTrackingDict = {
    [key: string]: {
        currentPostFix: number
        list: TrackingAudioClip[]
    }
}

const {
    Utils: { Array: A, Decorator },
} = Ludex

let BaseChannelManager = Ludex.Plugins.Audio?.BaseChannelManager

if (!BaseChannelManager) {
    BaseChannelManager = class BaseChannelManager {
        constructor(channelId: string) {
            console.warn('BaseChannelManager is not defined', channelId)
        }
    } as unknown as typeof Ludex.Plugins.Audio.BaseChannelManager
}

class AudioChannel extends BaseChannelManager {
    private node: Node

    private allowMultipleTracking: MultipleTrackingDict = {}

    constructor(node: Node, channelId: string) {
        super(channelId)
        this.node = node
    }

    public playing(key: SoundKeys): boolean {
        const audioPlayer = this.getAudioClip(key)?.audioPlayer

        if (audioPlayer && audioPlayer instanceof AudioPlayer) {
            return audioPlayer.getAudioSource().state === AudioSource.AudioState.PLAYING
        }

        return false
    }

    @Decorator.tryCatch()
    protected async onLoadAudio(key: SoundKeys, _config?: PlayConfig): Promise<AudioPlayer | null> {
        const clip = await ResourceUtils.loadAudioAsync(key)
        if (!clip) return null

        if (!this.node.isValid) return null
        return this.handleLoadedAudio(key, clip)
    }

    private handleLoadedAudio = (key: SoundKeys, clip: AudioClip): AudioPlayer => {
        const audioSource = this.node.addComponent(AudioSource)
        audioSource.clip = clip
        audioSource.playOnAwake = false

        return new AudioPlayer(key, audioSource)
    }

    protected playWithRealConfig(audioPlayer: AudioPlayer, config?: PlayConfig): void {
        if (!this.isCanPlay(config)) return

        if (config) {
            const { volume = 1, allowMultiple = false } = config
            config.volume = this.getVolume() * volume

            if (allowMultiple) {
                const clip = audioPlayer.getAudioSource().clip
                if (!clip) return

                const newAudioPlayer = this.requestDuplicateAudioPlayer(audioPlayer.getKey(), clip, config)
                newAudioPlayer.play(config)
                return
            }
        }

        audioPlayer.play(config)
    }

    private isCanPlay(config?: PlayConfig): boolean {
        if (config) {
            const { checkList = [] } = config

            const isPlayingExist = checkList.some((key) => {
                const audioClip = this.getAudioClip(key)

                if (audioClip) {
                    const audioPlayer = audioClip.audioPlayer
                    if (audioPlayer && audioPlayer instanceof AudioPlayer) {
                        return audioPlayer.getAudioSource().state === AudioSource.AudioState.PLAYING
                    }
                }

                return false
            })

            if (isPlayingExist) {
                return false
            }
        }

        return true
    }

    private requestDuplicateAudioPlayer(key: SoundKeys, clip: AudioClip, config?: PlayConfig): AudioPlayer {
        const existingAudioPlayer = this.getDuplicateAudioFromPool(key)
        if (existingAudioPlayer) {
            if (config) {
                const { key: dupKey } = existingAudioPlayer
                const { volume = 1 } = config
                this.setAudioClip(dupKey, { volume })
            }
            return existingAudioPlayer.audioPlayer
        }

        const allowMultipleTracking = this.allowMultipleTracking[key]
        if (!allowMultipleTracking) {
            throw new Error('Allow multiple tracking is not found')
        }

        const dupKey = `${key}_${allowMultipleTracking.currentPostFix}` as SoundKeys
        const newAudioPlayer = this.handleLoadedAudio(dupKey, clip)
        allowMultipleTracking.list.push({
            audioPlayer: newAudioPlayer,
            key: dupKey,
        })

        allowMultipleTracking.currentPostFix += 1

        this.setAudioClip(dupKey, {
            audioPlayer: newAudioPlayer,
            volume: 1,
        })

        return newAudioPlayer
    }

    private getDuplicateAudioFromPool(key: SoundKeys): TrackingAudioClip | null {
        if (this.allowMultipleTracking[key]) {
            const { list } = this.allowMultipleTracking[key]
            const audioClip = A.search(list, (audioClip) => {
                return audioClip.audioPlayer.getAudioSource().state !== AudioSource.AudioState.PLAYING
            })
            if (audioClip) {
                return audioClip
            }
        } else {
            this.allowMultipleTracking[key] = {
                currentPostFix: 1,
                list: [],
            }
        }

        return null
    }
}

export default AudioChannel
