import { pseudoRandomRangeInt } from 'cc'
import TileSpecialTypes from '../../constants/TileSpecialTypes'
import { getRandomTiles } from '../get'

type Tile = {
    setSpecialType(type: TileSpecialType, bombDuration?: number): void
}

type Grid<T> = {
    asArray(): ReadonlyArray<T>
}

// ? Min inclusive, max exclusive
const STARS_COUNT_RANGE = [1, 2] as const

export default function assignSpecialTiles<T extends Tile>(grid: Grid<T>, levelData: ILevelData, seed = Date.now()) {
    const { specials } = levelData
    const bombCount = specials.bomb
    const starsCount = pseudoRandomRangeInt(seed, ...STARS_COUNT_RANGE)

    const bombTiles = getRandomTiles(grid, bombCount, seed)

    const tilesWithoutBombs = grid.asArray().filter((tile) => !bombTiles.has(tile))
    const starsTiles = getRandomTiles(tilesWithoutBombs, starsCount, seed)

    for (const tile of bombTiles) {
        const bombDuration = (levelData.time * 2) / 3
        tile.setSpecialType(TileSpecialTypes.bomb, bombDuration)
    }

    for (const tile of starsTiles) {
        tile.setSpecialType(TileSpecialTypes.star)
    }
}
