import type { Vec2 } from 'cc'
import { _decorator, math } from 'cc'
import { ParticleSystem2DExtra } from '../ParticleSystem2DExtra'

const { ccclass } = _decorator

interface ExampleParticle extends Particle2DExtra {
    maxSize: number
    cycleDuration: number
    life: number
}

@ccclass('ExampleParticleSystem2DExtra')
export class ExampleParticleSystem2DExtra extends ParticleSystem2DExtra {
    public initParticle(particle: ExampleParticle): void {
        particle.maxSize = math.randomRange(0.25, 1) * particle.size

        particle.size = 0
        particle.deltaSize = 0
        particle.cycleDuration = math.randomRange(0.4, 0.6)
        particle.life = particle.timeToLive

        this.setInitPosition(particle.pos)
    }

    public updateParticle(particle: ExampleParticle, _dt: number): void {
        this.updateScale(particle)
    }

    private setInitPosition(out: Vec2) {
        const rectW = 700
        const rectH = 700
        const x = out.x
        const y = out.y

        out.x = x + math.randomRange(-rectW / 2, rectW / 2)
        out.y = y + math.randomRange(-rectH / 2, rectH / 2)

        return out
    }

    private updateScale(particle: ExampleParticle) {
        const liftCurrent = particle.life - particle.timeToLive
        const currentCycleRatio = (liftCurrent % particle.cycleDuration) / particle.cycleDuration
        const cubicLifeProgress = 1 - currentCycleRatio
        particle.size = particle.maxSize * Math.sin(cubicLifeProgress * Math.PI)
    }
}
