const { leaderboard } = codex
type Leaderboard = Ludex.Plugins.Leaderboards.Leaderboard

class LeaderboardUtilsCore {
    protected static inst$: LeaderboardUtilsCore

    public static get inst() {
        if (!this.inst$) {
            this.inst$ = new this()
        }

        return this.inst$
    }

    public static async getAllLeaderboardAsync(id: string): Promise<Leaderboard | null> {
        if (!leaderboard) return null

        const leaderboardName = leaderboard.getLeaderboardName(id)
        if (!leaderboardName) return null

        const res = await leaderboard.getLeaderboardResponseAsync(id)
        if (!res) return null

        await this.requestLeaderboardAsync(leaderboardName, res.amountPlayer)

        return leaderboard.getLeaderboard(leaderboardName)
    }

    public static async requestLeaderboardAsync(name: string, limit: number): Promise<void> {
        if (this.isLeaderboardRequesting(name)) {
            await this.waitForLeaderboardFinishAsync(name)
            return
        }

        await leaderboard.requestLeaderboardAsync(name, limit)
    }

    public static async waitForLeaderboardFinishAsync(name: string): Promise<void> {
        if (!this.isLeaderboardRequesting(name)) return

        return new Promise((resolve) => {
            const interval = setInterval(() => {
                if (!this.isLeaderboardRequesting(name)) {
                    clearInterval(interval)
                    resolve()
                }
            }, 100)
        })
    }

    public static isLeaderboardRequesting(name: string): boolean {
        return leaderboard.getLeaderboard(name)?.isRequesting || false
    }
}

class LeaderboardUtils extends LeaderboardUtilsCore {
    // * Custom logic here
}

export default LeaderboardUtils
