/**
 * Iterator to iterate through a grid in a spiral from the outside in
 * @param width the width of the grid
 * @param height the height of the grid
 * @yields [x, y] coordinates of the grid, row first
 */
export default function* spiralTraversal(width: number, height: number): Generator<Vec2Like, void, unknown> {
    let startRow = 0
    let endRow = width - 1
    let startCol = 0
    let endCol = height - 1

    while (startRow <= endRow && startCol <= endCol) {
        // top row
        for (let i = startRow; i <= endRow; i++) {
            yield { x: i, y: startCol }
        }
        startCol++

        // right col
        for (let i = startCol; i <= endCol; i++) {
            yield { x: endRow, y: i }
        }
        endRow--

        // bottom row
        for (let i = endRow; i >= startRow; i--) {
            yield { x: i, y: endCol }
        }
        endCol--

        // left col
        for (let i = endCol; i >= startCol; i--) {
            yield { x: startRow, y: i }
        }
        startRow++
    }
}
