import type Grid from '../../grid'
import type Tile from '../../tile'

export default function calcTilePosition(
    grid: Grid<Tile>,
    tileSize: Readonly<[number, number]>,
    coord: Readonly<Vec2Like>,
    padding = 0,
): Vec2Like {
    const { x, y } = coord
    const [width, height] = tileSize

    let posX = width * (x - (grid.width - 1) / 2) + padding * (x - grid.width / 2)
    let posY = height * (y - (grid.height - 1) / 2) + padding * (y - grid.height / 2)

    if (x === -1) {
        posX += tileSize[0] / 4
    }

    if (x === grid.width) {
        posX -= tileSize[0] / 4
    }

    if (y === -1) {
        posY += tileSize[1] / 4
    }

    if (y === grid.height) {
        posY -= tileSize[1] / 4
    }

    return { x: posX, y: posY }
}
