import { Component, type TweenEasing, type Vec3, Widget, _decorator, tween } from 'cc'
import AnimUtils from '../../utils/AnimUtils'
import AnimAbortSignal from '../../utils/common/AnimAbortSignal'
import { PanelAnimationController } from './PanelAnimationController'

const { ccclass, property } = _decorator

const {
    Utils: { Object: O },
} = Ludex

export type UIState = 'hidden' | 'shown'
export type UISwitcher = (newState: UIState, immediate?: boolean) => Promise<void>
export type UIAnimator = {
    switchState: UISwitcher
}

type AnimationDirection = 'top' | 'bottom'

type MovementConfig = WidgetConfig | PositionConfig

type WidgetConfig = {
    mode: AnimationDirection
    value: number
}

type PositionConfig = {
    mode: 'position'
    value: number
}

interface IAnimationConfig {
    nodes: Widget[]
    posOffset: number
    showEase: TweenEasing
    hideEase: TweenEasing
    showDuration: number
    hideDuration: number
}

@ccclass('AnimationConfig')
class AnimationConfig implements IAnimationConfig {
    @property
    public startHidden = true

    @property({ type: [Widget] })
    public nodes: Widget[] = []

    @property
    public posOffset = 0

    @property
    public showEase: TweenEasing = 'backOut'

    @property
    public hideEase: TweenEasing = 'backIn'

    @property
    public showDuration: number = 0

    @property
    public hideDuration: number = 0

    setConfig(config?: Partial<IAnimationConfig>) {
        if (config) {
            O.assign(this, config)
        }
    }
}

@ccclass('PanelAnimator')
export class PanelAnimator extends Component {
    @property({
        type: AnimationConfig,
        displayName: 'Top animation nodes',
    })
    private topConfig = new AnimationConfig()

    @property({
        type: AnimationConfig,
        displayName: 'Bottom animation nodes',
    })
    private botConfig = new AnimationConfig()

    private topAnimator!: UIAnimator
    private botAnimator!: UIAnimator

    private isReady = false

    public onEnable(): void {
        PanelAnimationController.registerAnimator(this)
    }

    public onDisable(): void {
        PanelAnimationController.unregisterAnimator(this)
    }

    public onLoad(): void {
        this.topAnimator = this.createAnimator(this.topConfig, 'top')
        this.botAnimator = this.createAnimator(this.botConfig, 'bottom')

        this.initUIPos()

        this.isReady = true
    }

    private initUIPos(): void {
        this.topAnimator.switchState(this.topConfig.startHidden ? 'hidden' : 'shown', true)
        this.botAnimator.switchState(this.botConfig.startHidden ? 'hidden' : 'shown', true)
    }

    public isReadyForAnimation(): boolean {
        return this.isReady
    }

    public async handleShowUI() {
        if (!this.isReady) {
            console.warn(`${this.node.name}'s ${this.name}: not ready`)
            return
        }

        return Promise.all([this.topAnimator.switchState('shown'), this.botAnimator.switchState('shown')])
    }

    public async handleHideUI() {
        if (!this.isReady) {
            console.warn(`${this.node.name}'s ${this.name}: not ready`)
            return
        }

        return Promise.all([this.topAnimator.switchState('hidden'), this.botAnimator.switchState('hidden')])
    }

    private createAnimator(
        { nodes, posOffset, showEase, hideEase, showDuration, hideDuration }: IAnimationConfig,
        direction: AnimationDirection,
    ): UIAnimator {
        const shownPositions: MovementConfig[] = nodes.map((node) => {
            if (direction === 'top' && node.isAlignTop) {
                return {
                    mode: 'top',
                    value: node.top,
                } as MovementConfig
            }

            if (direction === 'bottom' && node.isAlignBottom) {
                return {
                    mode: 'bottom',
                    value: node.bottom,
                } as MovementConfig
            }

            return {
                mode: 'position',
                value: node.node.position.y,
            } as MovementConfig
        })

        const hiddenPositions: MovementConfig[] = nodes.map((node) => {
            if (direction === 'top' && node.isAlignTop) {
                return {
                    mode: 'top',
                    value: node.top + posOffset,
                } as MovementConfig
            }

            if (direction === 'bottom' && node.isAlignBottom) {
                return {
                    mode: 'bottom',
                    value: node.bottom - posOffset,
                } as MovementConfig
            }

            return {
                mode: 'position',
                value: node.node.position.y + posOffset,
            } as MovementConfig
        })

        let state: UIState = 'shown'
        let signal = new AnimAbortSignal()

        const switchState: UISwitcher = async (newState: UIState, immediate = false) => {
            signal.abort()
            const currentSignal = new AnimAbortSignal()
            signal = currentSignal

            if (state === newState) return

            state = newState

            const startPositions = newState === 'hidden' ? shownPositions : hiddenPositions
            const targetPositions = newState === 'hidden' ? hiddenPositions : shownPositions
            const duration = newState === 'hidden' ? hideDuration : showDuration
            const easing: TweenEasing = newState === 'hidden' ? hideEase : showEase

            const setAllToTargetImmediately = () => {
                for (let index = 0, length = nodes.length; index < length; index++) {
                    const node = nodes[index] as (typeof nodes)[number]
                    const moveConfig = startPositions[index] as (typeof startPositions)[number]
                    const targetConfig = targetPositions[index] as (typeof targetPositions)[number]
                    const target = targetConfig.value
                    PanelAnimator.setToTargetImmediately(node, moveConfig, target)
                }
            }

            if (immediate) {
                setAllToTargetImmediately()
                return
            }

            currentSignal.addAbortListener(setAllToTargetImmediately)

            await Promise.all(
                nodes.map((node, index) => {
                    const moveConfig = startPositions[index] as (typeof startPositions)[number]
                    const targetConfig = targetPositions[index] as (typeof targetPositions)[number]
                    const target = targetConfig.value

                    return AnimUtils.runTweenAsync<Widget | Readonly<Vec3>>(
                        PanelAnimator.getTween({
                            widget: node,
                            config: moveConfig,
                            target,
                            easing,
                            duration,
                        }),
                        currentSignal,
                    )
                }),
            )

            if (!currentSignal.aborted) {
                currentSignal.removeAbortListener(setAllToTargetImmediately)
            }
        }

        return {
            switchState,
        }
    }

    public setAnimator({
        topConfig,
        botConfig,
    }: {
        topConfig?: Partial<IAnimationConfig>
        botConfig?: Partial<IAnimationConfig>
    }): void {
        const topAnimConfig = new AnimationConfig()
        const botAnimConfig = new AnimationConfig()

        topAnimConfig.setConfig(topConfig)
        botAnimConfig.setConfig(botConfig)

        this.topConfig = topAnimConfig
        this.botConfig = botAnimConfig

        this.topAnimator = this.createAnimator(this.topConfig, 'top')
        this.botAnimator = this.createAnimator(this.botConfig, 'bottom')

        this.initUIPos()
    }

    private static getTween({
        widget,
        config,
        target,
        easing,
        duration,
    }: {
        widget: Widget
        config: MovementConfig
        target: number
        easing: TweenEasing
        duration: number
    }) {
        if (config.mode === 'top') {
            return tween(widget)
                .call(() => {
                    widget.top = config.value
                })
                .to(duration, { top: target }, { easing })
        }

        if (config.mode === 'bottom') {
            return tween(widget)
                .call(() => {
                    widget.bottom = config.value
                })
                .to(duration, { bottom: target }, { easing })
        }

        return tween(widget.node.position)
            .call(() => {
                widget.node.setPosition(widget.node.position.x, config.value, widget.node.position.z)
            })
            .to(duration, { y: target }, { easing })
    }

    private static setToTargetImmediately(widget: Widget, config: MovementConfig, target: number) {
        if (config.mode === 'top') {
            widget.top = target
        }

        if (config.mode === 'bottom') {
            widget.bottom = target
        }

        const x = widget.node.position.x
        const y = target
        const z = widget.node.position.z

        widget.node.setPosition(x, y, z)
    }
}
