const LevelGravity = {
    FallDown: 'fall-down',
    FlyUp: 'fly-up',
    PushLeft: 'push-left',
    PushRight: 'push-right',
    PutColumnMiddle: 'put-columns-middle',
    PutRowMiddle: 'put-rows-middle',
} as const

const LevelGravities = [
    LevelGravity.FallDown,
    LevelGravity.FlyUp,
    LevelGravity.PushLeft,
    LevelGravity.PushRight,
    LevelGravity.PutColumnMiddle,
    LevelGravity.PutRowMiddle,
] as const

const VerticalLevelGravities = [LevelGravity.FallDown, LevelGravity.FlyUp, LevelGravity.PutRowMiddle] as const
const HorizontalLevelGravities = [LevelGravity.PushLeft, LevelGravity.PushRight, LevelGravity.PutColumnMiddle] as const

type VerticalLevelGravity = (typeof VerticalLevelGravities)[number]
type HorizontalLevelGravity = (typeof HorizontalLevelGravities)[number]

type LevelGravity = (typeof LevelGravity)[keyof typeof LevelGravity]

export default LevelGravity
export {
    HorizontalLevelGravities,
    LevelGravities,
    VerticalLevelGravities,
    type HorizontalLevelGravity,
    type LevelGravity,
    type VerticalLevelGravity,
}
