import OverlayUtils from 'db://assets/utils/OverlayUtils'
import type { Scene } from 'cc'
import LeaderboardEvents from '../../../../../gameplay/constants/Leaderboard'
import AdsUtils from '../../../../../utils/AdsUtils'

type OverlayView = FBInstant.OverlayView

const {
    Utils: { Time: T, Object: O },
} = Ludex

type ElementInGamePosition = {
    worldX: number
    worldY: number
    width: number
    height: number
}

const DEFAULT_XML_PATH = 'overlays/leaderboard/leaderboard-overlay.xml'
const DEFAULT_CSS_PATH = 'overlays/leaderboard/leaderboard-overlay-styles.css'

type LeaderboardContentPayload = {
    leaderboardPlayers: LeaderboardPlayer[]
    leaderboardPosition: ElementInGamePosition
    currentPlayerId: string
}

type LeaderboardPlayer = {
    playerId: string
    rank: string
    score: number
}

const HEADER_HEIGHT = 295
const FOOTER_HEIGHT = 155

class LeaderboardContentOverlay {
    private mainDiv$!: HTMLDivElement
    private overlayElement$: OverlayView | undefined
    public scene!: Scene

    private currentPayload$: LeaderboardContentPayload | undefined
    private isPageMounted: boolean = false

    constructor(scene: Scene) {
        this.scene = scene
        this.createMainDiv$()
    }

    private createMainDiv$ = (): void => {
        this.mainDiv$ = document.createElement('div')

        const scaleFactor = OverlayUtils.getGameScaleFactor()

        // Apply styles using Object.assign for better performance
        O.assign(this.mainDiv$.style, {
            position: 'absolute',
            transform: 'translateX(-50%)',
            left: '50%',
            top: `${scaleFactor * HEADER_HEIGHT}px`,
            scrollbarWidth: 'none',
            alignItems: 'start',
            overflow: 'auto',
            backgroundColor: 'transparent',
            opacity: '0',
            transition: 'opacity 0.1s ease-in-out',
            pointerEvents: 'none',
            display: 'none',
            userSelect: 'none',
        })

        document.body.appendChild(this.mainDiv$)
    }

    public async updateOverlay(payload: LeaderboardContentPayload): Promise<boolean> {
        try {
            if (!this.isPageMounted) return false

            this.scene.emit(LeaderboardEvents.OVERLAY_SHOW_STARTED)

            const validatedPayload = this.validateOverlayPayload$(payload)

            // Update position if provided
            if (validatedPayload.leaderboardPosition) {
                this.moveMainDiv(validatedPayload.leaderboardPosition)
            }

            // Only update if payload has changed
            if (!this.isPayloadChanged$(validatedPayload)) {
                this.revealMainDiv$()
                return true
            }

            if (this.overlayElement$) {
                await this.overlayElement$.updateAsync(validatedPayload)
            } else {
                this.overlayElement$ = await OverlayUtils.createOverlayViewAsync(
                    DEFAULT_XML_PATH,
                    this.mainDiv$,
                    this.getIframeCss$(payload),
                    DEFAULT_CSS_PATH,
                    validatedPayload,
                )
            }

            this.currentPayload$ = validatedPayload
            await this.showOverlay$()
        } catch (error) {
            console.error('Error creating overlay view:', error)
            return false
        }

        return true
    }

    private isPayloadChanged$ = (payload: LeaderboardContentPayload): boolean => {
        if (!this.currentPayload$) return true

        // Check if the arrays have different lengths
        if (this.currentPayload$.leaderboardPlayers.length !== payload.leaderboardPlayers.length) {
            return true
        }

        // Compare each player object
        for (let i = 0; i < payload.leaderboardPlayers.length; i++) {
            const currentPlayer = this.currentPayload$.leaderboardPlayers[i]
            const newPlayer = payload.leaderboardPlayers[i]

            // Safety check (though should not be needed after length check)
            if (!currentPlayer || !newPlayer) {
                return true
            }

            if (
                currentPlayer.playerId !== newPlayer.playerId ||
                currentPlayer.rank !== newPlayer.rank ||
                currentPlayer.score !== newPlayer.score
            ) {
                return true
            }
        }

        return false
    }

    private showOverlay$ = async (): Promise<void> => {
        if (!this.overlayElement$) return
        await this.overlayElement$.showAsync()
        this.revealMainDiv$()
    }

    private revealMainDiv$ = (): void => {
        if (!this.overlayElement$) return
        if (!this.isPageMounted) return

        this.scene.emit(LeaderboardEvents.OVERLAY_SHOW_COMPLETED)

        // Start fade-in effect
        this.mainDiv$.style.display = 'block'
        this.mainDiv$.scrollTop = 0

        // Trigger fade-in by setting opacity after a brief delay
        requestAnimationFrame(() => {
            if (!this.isPageMounted) {
                this.mainDiv$.style.display = 'none'
                return
            }

            O.assign(this.mainDiv$.style, {
                opacity: '1',
                pointerEvents: 'auto',
            })
        })
    }

    private validateOverlayPayload$ = (payload: LeaderboardContentPayload): LeaderboardContentPayload => {
        const correctedPayload: LeaderboardContentPayload = {
            ...payload,
            leaderboardPlayers: [],
        }

        const validateRegEx = /^\d+$/

        for (const player of payload.leaderboardPlayers) {
            // if player id contains any alphabet characters, delete the player
            if (!validateRegEx.test(player.playerId)) {
                continue
            }

            correctedPayload.leaderboardPlayers.push(player)
        }

        return correctedPayload
    }

    public async hideOverlay(): Promise<void> {
        if (!this.overlayElement$) return

        // Start fade-out effect
        O.assign(this.mainDiv$.style, {
            opacity: '0',
            pointerEvents: 'none',
        })

        try {
            // ? Wait for fade-out transition to complete before hiding
            await T.sleepAsync(100)
            this.mainDiv$.style.display = 'none'
        } catch (error) {
            console.error('Hide overlay failed:', error)
        }
    }

    public async handleHideUICall(): Promise<void> {
        O.assign(this.mainDiv$.style, {
            opacity: '0',
            pointerEvents: 'none',
            display: 'none',
        })
    }

    public async handleShowUICall(): Promise<void> {
        O.assign(this.mainDiv$.style, {
            opacity: '1',
            pointerEvents: 'auto',
            display: 'block',
        })
    }

    private getIframeCss$ = (payload: LeaderboardContentPayload): string => {
        const widthString = 'width: 100%;'
        let widthCss = '100cqw'

        const { leaderboardPosition, leaderboardPlayers } = payload
        const playerCount = leaderboardPlayers.length

        if (leaderboardPosition) {
            widthCss = `${this.getElementDivWidth$(leaderboardPosition)}px`
        }

        // ? Optimal iframe height is based on player tab count and margin to not show scrollbar
        const heightString = `height: calc((${widthCss} * (121 / 1000) + 4px) * ${playerCount});`

        const borderWidthString = 'border-width: 0px;'
        const overflowString = 'overflow: hidden;'
        const displayString = 'display: block;'
        return `${widthString} ${borderWidthString} ${overflowString} ${displayString} ${heightString}   `
    }

    public setIsPageMounted(isPageMounted: boolean): void {
        this.isPageMounted = isPageMounted
    }

    public moveMainDiv(position: ElementInGamePosition): void {
        this.updateMainDivPosition$(position)
    }

    private updateMainDivPosition$ = (position: ElementInGamePosition): void => {
        if (!this.mainDiv$) return

        const cssWidth = `${this.getElementDivWidth$(position)}px`
        const cssHeight = `${this.getElementDivHeight$()}px`
        const cssTopOffset = `${this.getElementDivTopOffset$()}px`

        O.assign(this.mainDiv$.style, {
            width: cssWidth,
            height: `calc(100vh - ${cssHeight})`,
            top: cssTopOffset,
        })
    }

    private getElementDivWidth$ = (position: ElementInGamePosition): number => {
        const { width } = position
        return width * OverlayUtils.getGameScaleFactor()
    }

    private getElementDivHeight$ = (): number => {
        const scaleFactor = OverlayUtils.getGameScaleFactor()
        const adBannerHeight = AdsUtils.getBannerAdHeight()
        return scaleFactor * (HEADER_HEIGHT + FOOTER_HEIGHT + adBannerHeight)
    }

    private getElementDivTopOffset$ = (): number => {
        return HEADER_HEIGHT * OverlayUtils.getGameScaleFactor()
    }
}

export default LeaderboardContentOverlay
