import { _decorator, Enum, type Graphics, randomRange, toRadian, Vec2, Vec3 } from 'cc'
import {
    randomPointBetweenCircleAtFixedAngle,
    randomPointInRect,
    randomSign,
    randomSortArray,
} from '../ParticleFunction'
import type { Particle } from '../ParticleSimulator'

const { ccclass, property } = _decorator

export const Shape = Enum({
    Circle: 0,
    Rectangle: 1,
})

export const EmitLocation = Enum({
    Base: 0,
    Edge: 1,
})

const circleVisible = function (this: ShapeModule): boolean {
    return this.shape === Shape.Circle
}

const rectangleVisible = function (this: ShapeModule): boolean {
    return this.shape === Shape.Rectangle
}

const _tempArr: [number, number] = [0, 0]

@ccclass('ShapeModule')
export class ShapeModule {
    @property({ type: Enum(Shape) })
    public shape: number = Shape.Circle

    @property({ type: Enum(EmitLocation), visible: rectangleVisible })
    public emitFrom: number = EmitLocation.Base

    @property({})
    public position: Vec3 = new Vec3(0, 0, 0)

    @property({})
    public rotation: Vec3 = new Vec3(0, 0, 0)

    @property({})
    public scale: Vec3 = new Vec3(1, 1, 1)

    @property({ range: [0, 360], visible: circleVisible })
    public arc = 360

    @property({ range: [-360, 360], visible: circleVisible })
    public angle = 25

    @property({ min: 0, visible: circleVisible })
    public radius = 1

    @property({ range: [0, 1], step: 0.01, visible: circleVisible })
    public radiusThickness = 1

    @property({ visible: rectangleVisible })
    public rectangleThickness = new Vec2(20, 50)

    public emit(particle: Particle): void {
        switch (this.shape) {
            case Shape.Circle:
                this.circleEmit(this.radius, this.radiusThickness, toRadian(this.generateArcAngle()), particle)
                break
            case Shape.Rectangle:
                this.rectangleEmit(this.emitFrom, this.rectangleThickness, particle)
                break
        }
    }

    private circleEmit(radius: number, radiusThickness: number, theta: number, particle: Particle): void {
        const pos = particle.pos
        randomPointBetweenCircleAtFixedAngle(pos, radius * (1 - radiusThickness), radius, theta + toRadian(this.angle))
        particle.velocity = pos.clone().normalize()
    }

    private rectangleEmit(emitFrom: number, rectangleThickness: Vec2, particle: Particle): void {
        const pos = particle.pos
        switch (emitFrom) {
            case EmitLocation.Base:
                randomPointInRect(pos, rectangleThickness)
                break
            case EmitLocation.Edge:
                _tempArr[0] = randomRange(-1, 1)
                _tempArr[1] = randomSign()
                randomSortArray(_tempArr)
                this.applyRectThickness(_tempArr, rectangleThickness)
                Vec2.set(pos, _tempArr[0], _tempArr[1])
                break
        }

        particle.velocity = pos.clone().normalize()
    }

    private generateArcAngle(): number {
        return randomRange(0, this.arc)
    }

    private applyRectThickness(pos: [number, number], thickness: Vec2) {
        if (thickness.x > 0) {
            pos[0] *= thickness.x
        }
        if (thickness.y > 0) {
            pos[1] *= thickness.y
        }
    }

    public gizmo(g: Graphics) {
        g.strokeColor.fromHEX('#46C4FFB0')
        g.fillColor.fromHEX('#46C4FF77')
        g.lineWidth = 8

        switch (this.shape) {
            case Shape.Circle:
                this.gizmoCircle(g)
                break
            case Shape.Rectangle:
                this.gizmoRectangle(g)
                break
        }
    }

    private gizmoCircle(g: Graphics) {
        g.clear()

        const startAngle = this.angle
        const endAngle = this.angle + this.arc

        this.createArcPath(g, this.radius, startAngle, endAngle)
        this.createArcPath(g, this.radius * (1 - this.radiusThickness), startAngle, endAngle)

        if (this.arc < 360 && this.radius > 0) {
            // draw two line
            const x1 = this.radius * Math.cos(toRadian(this.angle))
            const y1 = this.radius * Math.sin(toRadian(this.angle))

            const x2 = this.radius * Math.cos(toRadian(this.arc + this.angle))
            const y2 = this.radius * Math.sin(toRadian(this.arc + this.angle))

            g.moveTo(x1, y1)
            g.lineTo(0, 0)

            g.moveTo(x2, y2)
            g.lineTo(0, 0)
        }

        g.stroke()
    }

    private gizmoRectangle(g: Graphics) {
        g.clear()

        // draw rectangle
        const width = this.rectangleThickness.x
        const height = this.rectangleThickness.y

        g.moveTo(width, height)
        g.lineTo(-width, height)
        g.lineTo(-width, -height)
        g.lineTo(width, -height)

        g.close()

        g.stroke()
    }

    private createArcPath(g: Graphics, radius: number, startAngle: number, endAngle: number) {
        if (radius <= 0) return

        const segments = 48
        const angelStep = (endAngle - startAngle) / segments

        for (let i = 0; i <= segments; i++) {
            const angle = startAngle + i * angelStep
            const x = radius * Math.cos(toRadian(angle))
            const y = radius * Math.sin(toRadian(angle))
            if (i === 0) {
                g.moveTo(x, y)
            } else {
                g.lineTo(x, y)
            }
        }
    }
}
