import { _decorator, Component } from 'cc'
import { SpecialItem } from '../../../../gameplay/game-objects/item/SpecialItem'
import type { ItemConfig } from '../../../../gameplay/manager/ItemManager'
import AnimAbortSignal from '../../../../utils/common/AnimAbortSignal'
import { Clock } from './clock/Clock'
import { TournamentRail } from './header/tournament-container/TournamentRail'

const { ccclass, property } = _decorator

export type StatusConfig = {
    coins?: number
    score?: number
    level?: number
    time?: number
    leaderData?: {
        score: number
        rank: number
    }[]
    items?: Record<ItemKey, ItemConfig>
}

@ccclass('GameSceneLayout')
export class GameSceneLayout extends Component {
    @property(TournamentRail)
    public tournamentRail!: TournamentRail

    @property(Clock)
    public clock!: Clock

    @property(SpecialItem)
    public items: SpecialItem[] = []

    public isInited: boolean = false

    protected entranceSignal: AnimAbortSignal = new AnimAbortSignal()
    protected exitSignal: AnimAbortSignal = new AnimAbortSignal()

    public async waitForInitAsync(): Promise<void> {
        if (this.isInited) return

        return new Promise((resolve) => {
            const interval = setInterval(() => {
                if (this.isInited) {
                    clearInterval(interval)
                    resolve()
                }
            }, 100)
        })
    }

    public setLevelText(_level: number): void {}

    public disableLayout(): void {}

    public enableLayout(): void {}

    public updateStatus(config: StatusConfig): void {
        this.clock.updateStatus(config)
        this.tournamentRail.updateStatus(config)
        for (const item of this.items) {
            item.updateStatus(config)
        }
    }

    public async playExitAnimUI(_duration: number = 0.17): Promise<void> {}

    public async playEntranceAnimUI(_duration: number = 0.2): Promise<void> {}

    public highlightCoinContainer(): void {}

    public unHighlightCoinContainer(): void {}

    public handleScoreUpdated(_score: number): void {}
}
