import { _decorator, CCInteger, Component, Enum, NodeEventType } from 'cc'
import { EDITOR_NOT_IN_PREVIEW } from 'cc/env'
import { ScreenKeysEnum } from '../../constants/ScreenKeys'

const { ccclass, property, executeInEditMode } = _decorator

@ccclass('ScreenConfig')
export class ScreenConfig {
    @property({ type: Enum(ScreenKeysEnum) })
    public key: ScreenKeysEnum = ScreenKeysEnum.LOADING_SCREEN

    @property({ type: CCInteger })
    public depth: number = 0
}

@ccclass('Screens')
@executeInEditMode
export class Screens extends Component {
    @property({ type: [ScreenConfig] })
    public screens: ScreenConfig[] = []

    @property({
        tooltip: 'Preload all the screens automatically in ScreenDepth when the scene starts.',
    })
    public autoPreload = false

    protected onEnable(): void {
        if (!EDITOR_NOT_IN_PREVIEW) return
        this.onChildrenChanged()
        this.node.on(NodeEventType.CHILD_ADDED, this.onChildrenChanged, this)
        this.node.on(NodeEventType.CHILD_REMOVED, this.onChildrenChanged, this)
    }

    protected onDisable(): void {
        if (!EDITOR_NOT_IN_PREVIEW) return
        this.node.on(NodeEventType.CHILD_ADDED, this.onChildrenChanged, this)
        this.node.on(NodeEventType.CHILD_REMOVED, this.onChildrenChanged, this)
    }

    protected onChildrenChanged() {
        if (!EDITOR_NOT_IN_PREVIEW) return
        const deprecatedScreens = this.node.getComponentsInChildren('BaseScreen')
        if (deprecatedScreens.length > 0) {
            console.warn('Please add Screens to the Screens component instead of adding them directly to the node.')
        }
    }
}
