import { getNearbyTiles, getRandomTile } from '../get'

const { Array: A } = Ludex.Utils

type Tile<I> = {
    id: I | null
    getCoord(): { x: number; y: number }
    isSimilarTo(tile: Tile<I>): boolean
    inSamePositionAs(tile: Tile<I>): boolean
}

type Grid<T> = {
    asArray(): ReadonlyArray<T>
    get(x: number, y: number): T
    isInBounds(x: number, y: number): boolean
}

export default function ensureOneMatch<I, T extends Tile<I>>({
    grid,
    setIdentity,
    seed = Date.now(),
}: { grid: Grid<T>; setIdentity(tile: T, id: I): void; seed?: number }) {
    const arr = grid.asArray()
    const randomTile = getRandomTile(grid, seed)
    const nearbyTiles = getNearbyTiles(grid, randomTile)

    if (nearbyTiles.some((tile) => tile.isSimilarTo(randomTile))) return

    const randomTile2 = getRandomTile(nearbyTiles, seed)
    const sacrifice = A.search(
        arr,
        (tile) =>
            !tile.inSamePositionAs(randomTile) && !tile.inSamePositionAs(randomTile2) && tile.isSimilarTo(randomTile2),
    )

    if (sacrifice === undefined || randomTile.id === null) {
        throw new Error('Could not find a sacrifice tile')
    }

    setIdentity(sacrifice, randomTile.id)
    setIdentity(randomTile2, randomTile.id)
}
