export default function calcTileSize(
    defaultSize: Readonly<[number, number]>,
    trimming: Readonly<[number, number]> = [0, 0],
): [number, number] {
    const [defaultTileW, defaultTileH] = defaultSize
    const [trimmingW, trimmingH] = trimming

    const [trimmedTileW, trimmedTileH] = [1 - trimmingW, 1 - trimmingH]
    const [tileW, tileH] = [defaultTileW * trimmedTileW, defaultTileH * trimmedTileH]

    return [tileW, tileH] as const
}
