import { Component, Node, Prefab, _decorator, instantiate } from 'cc'
import { AFPool } from '../../utils/pool/AFPool'
import { Line } from './Line'
const { ccclass, property } = _decorator

@ccclass('PathDrawer')
export class PathDrawer extends Component {
    @property({ group: 'Line', displayName: 'Prefab', type: Prefab })
    protected linePrefab!: Prefab
    @property({ group: 'Line', displayName: 'Holder', type: Node, tooltip: 'Line Prefab' })
    protected lineHolder!: Node

    protected lineNodes: Node[] = []
    protected linePool!: AFPool<Node>

    @property({ group: 'Point', displayName: 'Prefab', type: Prefab })
    private pointPrefab!: Prefab
    @property({ group: 'Point', displayName: 'Holder', type: Node })
    private pointHolder!: Node

    protected pointPool!: AFPool<Node>
    protected pointNodes: Node[] = []

    public start(): void {
        this.linePool = new AFPool<Node>(
            () => {
                const lineNode = instantiate(this.linePrefab)
                this.lineHolder.addChild(lineNode)
                lineNode.parent = this.lineHolder
                lineNode.active = false
                return lineNode
            },
            0,
            (lineNode: Node) => {
                this.lineNodes.push(lineNode)
                lineNode.active = true
            },
            (lineNode: Node) => {
                lineNode.active = false
            },
            (lineNode: Node) => {
                if (lineNode.isValid) {
                    lineNode.destroy()
                }
            },
        )

        this.pointPool = new AFPool<Node>(
            () => {
                const pointNode = instantiate(this.pointPrefab)
                this.pointHolder.addChild(pointNode)
                pointNode.active = false
                return pointNode
            },
            0,
            (pointNode: Node) => {
                this.pointNodes.push(pointNode)
                pointNode.active = true
            },
            (pointNode: Node) => {
                pointNode.active = false
            },
            (pointNode: Node) => {
                if (pointNode.isValid) {
                    pointNode.destroy()
                }
            },
        )
    }

    public drawPath(path: Path) {
        this.clearPath()
        for (const line of path) {
            const lineNode = this.linePool.alloc()
            lineNode.getComponent(Line)?.setPoints(line.a, line.b)
        }
    }

    public clearPath(): void {
        for (const lineNode of this.lineNodes) {
            this.linePool.free(lineNode)
        }
        this.lineNodes = []

        for (const pointNodes of this.pointNodes) {
            this.pointPool.free(pointNodes)
        }
        this.pointNodes = []
    }
}
