import { CodeSoundKeys } from 'db://assets/constants/SoundKeys'
import AnimUtils from 'db://assets/utils/AnimUtils'
import AudioUtils from 'db://assets/utils/AudioUtils'
import ComponentUtils from 'db://assets/utils/ComponentUtils'
import { _decorator, assert, Component, find, type IVec2Like, randomRange, tween, UITransform } from 'cc'
import GameplayEvents from '../../../constants/GameEvents'
import type { Board, Tile } from '../../../game-objects/board'
import type Cloud from '../../../game-objects/cloud/Cloud'
import clampToBoundingBox from '../../../utils/2d/clampToBoundingBox'
import { getClosestPairIndex } from '../../../utils/2d/getClosestPair'
import toSortedIndices from '../../../utils/array/toSortedIndices'
import randomSign from '../../../utils/number/randomSign'
import type EffectManager from '../../EffectManager'
import CloudEffectProperties from './CloudEffectProperties'

const { ccclass, property } = _decorator

const {
    Utils: { Array: A },
} = Ludex

type MultiTileCloudPosition = {
    type: 'multi-tile-cloud'
    cloud: Cloud
    worldPosition: IVec2Like
    tiles: [Tile, Tile]
}

type SingleTileCloudPosition = {
    type: 'single-tile-cloud'
    clouds: [
        {
            cloud: Cloud
            worldPosition: IVec2Like
            tile: Tile
        },
        {
            cloud: Cloud
            worldPosition: IVec2Like
            tile: Tile
        },
    ]
    tiles: [Tile, Tile]
}

type CloudEffectConfig = MultiTileCloudPosition | SingleTileCloudPosition

@ccclass('CloudEffect')
class CloudEffect extends Component {
    @ComponentUtils.required()
    @property({ type: CloudEffectProperties })
    public readonly properties = new CloudEffectProperties()

    private canvasUiTransform!: UITransform

    protected __preload(): void {
        ComponentUtils.validateRequiredProperties(this, CloudEffect)
    }

    protected onEnable(): void {
        const canvasUiTransform = find('Canvas')?.getComponent(UITransform)
        assert(canvasUiTransform !== undefined && canvasUiTransform !== null, 'Canvas UI transform not found')
        this.canvasUiTransform = canvasUiTransform
    }

    public async playThunderAnimations(config: {
        manager: EffectManager
        pairs: [[Tile, Tile], [Tile, Tile]]
        board: Board
    }) {
        const { manager, pairs, board } = config

        const tiles = pairs.flat() as [Tile, Tile, Tile, Tile]
        const clouds = [this.properties.cloud1, this.properties.cloud2, this.properties.cloud3] as [Cloud, Cloud, Cloud]

        const configs = this.getSimpleCloudPositions(tiles, clouds)

        const animation = tween(this.node).parallel(
            ...configs.map((config, index) =>
                this.getCloudFxTween({
                    manager,
                    board,
                    effect: config,
                    delay: 0.1 * index,
                }),
            ),
        )

        await AnimUtils.runTweenAsync(animation)
    }

    public async playMultiThunderAnimation(config: {
        manager: EffectManager
        pairs: [Tile, Tile][]
        board: Board
        stagger?: number
    }): Promise<void> {
        const { manager, pairs, board, stagger } = config

        const cloud = this.properties.cloud1

        cloud.node.active = true
        await AnimUtils.runTweenAsync(cloud.getMoveToScreenCenterTween())

        await AnimUtils.waitTweenAsync(0.2)

        board.clear({ pairs, stagger, lightning: true })

        const ps = []

        // set of animations and effects
        for (let i = 0, tilesCount = pairs.length * 2; i < tilesCount; i++) {
            const pairIndex = Math.floor(i / 2)

            const pair = pairs[pairIndex]
            if (pair === undefined) continue

            const tile = pair[i % 2]
            if (tile === undefined) continue

            if (i !== 0 && i % 2 === 0) {
                manager.node.scene.emit(GameplayEvents.ADD_COMBO)
                manager.node.scene.emit(GameplayEvents.ADD_SCORE)
            }

            manager.shakeGame()
            manager.shakeScreen(500)
            AudioUtils.playSound(CodeSoundKeys.THUNDERBOLT)
            ps.push(AnimUtils.runTweenAsync(cloud.getLightningTween(tile.node.worldPosition)))

            if (stagger) {
                await AnimUtils.waitTweenAsync(stagger)
            }
        }

        await Promise.all(ps)

        cloud.node.active = false
    }

    private getSimpleCloudPositions(
        tiles: [Tile, Tile, Tile, Tile],
        clouds: [Cloud, Cloud, Cloud],
    ): CloudEffectConfig[] {
        const positions = [
            tiles[0].node.worldPosition,
            tiles[1].node.worldPosition,
            tiles[2].node.worldPosition,
            tiles[3].node.worldPosition,
        ] as const

        const closestPairIndices = getClosestPairIndex(positions)

        const { left: closestPair, right: otherTiles } = A.split(tiles, (_, index) =>
            closestPairIndices.includes(index),
        )

        assert(closestPair.length === 2, 'Closest pair must have 2 tiles')
        assert(otherTiles.length === 2, 'Other tiles must have 2 tiles')

        const closestTile1 = closestPair[0] as Tile
        const closestTile2 = closestPair[1] as Tile

        const otherTile1 = otherTiles[0] as Tile
        const otherTile2 = otherTiles[1] as Tile

        const otherPosition1 = otherTile1.node.worldPosition
        const otherPosition2 = otherTile2.node.worldPosition

        const clamp = clampToBoundingBox(this.canvasUiTransform.getBoundingBoxToWorld(), 100)

        const multiCloudY =
            Math.max(closestTile1.node.worldPosition.y, closestTile2.node.worldPosition.y) + randomRange(150, 200)

        const singleCloud1Y = otherPosition1.y + randomRange(150, 200)
        const singleCloud2Y = otherPosition2.y + randomRange(150, 200)

        const multiCloudX =
            (closestTile1.node.worldPosition.x + closestTile2.node.worldPosition.x) / 2 +
            randomRange(50, 100) * randomSign()

        const singleCloud1X = otherPosition1.x + randomRange(50, 100) * randomSign()
        const singleCloud2X = otherPosition2.x + randomRange(50, 100) * randomSign()

        const multiCloudPosition: IVec2Like = clamp({
            x: multiCloudX,
            y: multiCloudY,
        })

        const singleCloudPosition1: IVec2Like = clamp({
            x: singleCloud1X,
            y: singleCloud1Y,
        })

        const singleCloudPosition2: IVec2Like = clamp({
            x: singleCloud2X,
            y: singleCloud2Y,
        })

        const cloudPositions = [multiCloudPosition, singleCloudPosition1, singleCloudPosition2]

        const sortedIndices = toSortedIndices(cloudPositions, (a, b) => b.y - a.y) as [number, number, number]

        const multiCloud = clouds[sortedIndices[0]] as Cloud
        const singleCloud1 = clouds[sortedIndices[1]] as Cloud
        const singleCloud2 = clouds[sortedIndices[2]] as Cloud

        const configs: CloudEffectConfig[] = [
            {
                type: 'multi-tile-cloud',
                cloud: multiCloud,
                worldPosition: multiCloudPosition,
                tiles: [closestTile1, closestTile2],
            },
            {
                type: 'single-tile-cloud',
                clouds: [
                    {
                        cloud: singleCloud1,
                        worldPosition: singleCloudPosition1,
                        tile: otherTile1,
                    },
                    {
                        cloud: singleCloud2,
                        worldPosition: singleCloudPosition2,
                        tile: otherTile2,
                    },
                ],
                tiles: [otherTile1, otherTile2],
            },
        ]

        return configs
    }

    private getCloudFxTween(config: {
        manager: EffectManager
        board: Board
        effect: CloudEffectConfig
        delay?: number
    }) {
        if (config.effect.type === 'multi-tile-cloud') {
            return this.getMultiTileCloudFxTween(config.effect, config)
        }

        return this.getSingleTileCloudFxTween(config.effect, config)
    }

    private getMultiTileCloudFxTween(
        effect: MultiTileCloudPosition,
        config: {
            manager: EffectManager
            board: Board
            delay?: number
        },
    ) {
        const { manager, board, delay = 0 } = config

        effect.cloud.node.active = true
        const moveInTween = effect.cloud.getMoveToScreenTween(effect.worldPosition, 'left')

        const clearTween = tween(this.node)
            .delay(0.4)
            .call(this.clearTiles.bind(this, manager, board, [effect.tiles], 0.05))

        const explosionTween1 = tween(this.node)
            .delay(0.2)
            .call(manager.createClearExplosionAt.bind(manager, effect.tiles[0].node.worldPosition))

        const lightningTween1 = effect.cloud.getLightningTween(effect.tiles[0].node.worldPosition)

        const lightningTween2 = effect.cloud.getLightningTween(effect.tiles[1].node.worldPosition)

        const explosionTween2 = tween(this.node)
            .delay(0.2)
            .call(manager.createClearExplosionAt.bind(manager, effect.tiles[1].node.worldPosition))

        const cloudTween1 = tween(this.node).parallel(lightningTween1, explosionTween1)

        const cloudTween2 = tween(this.node).delay(0.3).parallel(lightningTween2, explosionTween2)

        const moveOutTween = effect.cloud.getMoveOutScreenTween('left')

        const animation = tween(this.node)
            .delay(delay)
            .then(moveInTween)
            .parallel(clearTween, cloudTween1, cloudTween2)
            .then(moveOutTween)
            .call(() => {
                effect.cloud.node.active = false
            })

        return animation
    }

    private getSingleTileCloudFxTween(
        effect: SingleTileCloudPosition,
        config: {
            manager: EffectManager
            board: Board
            delay?: number
        },
    ) {
        const { manager, board, delay = 0 } = config

        const cloudEffect1 = effect.clouds[0]
        const cloudEffect2 = effect.clouds[1]

        cloudEffect1.cloud.node.active = true
        cloudEffect2.cloud.node.active = true

        const clearTween = tween(this.node)
            .delay(0.6)
            .call(this.clearTiles.bind(this, manager, board, [effect.tiles], 0.1))

        const moveInTween1 = cloudEffect1.cloud.getMoveToScreenTween(cloudEffect1.worldPosition, 'right')

        const lightningTween1 = cloudEffect1.cloud
            .getLightningTween(effect.tiles[0].node.worldPosition)
            .then(cloudEffect1.cloud.getMoveOutScreenTween('right'))

        const explosionTween1 = tween(this.node)
            .delay(0.2)
            .call(manager.createClearExplosionAt.bind(manager, cloudEffect1.tile.node.worldPosition))

        const moveInTween2 = tween(this.node)
            .delay(0.1)
            .then(cloudEffect2.cloud.getMoveToScreenTween(cloudEffect2.worldPosition, 'left'))

        const lightningTween2 = cloudEffect2.cloud
            .getLightningTween(effect.tiles[1].node.worldPosition)
            .then(cloudEffect2.cloud.getMoveOutScreenTween('left'))

        const explosionTween2 = tween(this.node)
            .delay(0.2)
            .call(manager.createClearExplosionAt.bind(manager, cloudEffect2.tile.node.worldPosition))

        const cloudTween1 = tween(this.node)
            .then(moveInTween1)
            .parallel(lightningTween1, explosionTween1)
            .call(() => {
                cloudEffect1.cloud.node.active = false
            })

        const cloudTween2 = tween(this.node)
            .then(moveInTween2)
            .parallel(lightningTween2, explosionTween2)
            .call(() => {
                cloudEffect2.cloud.node.active = false
            })

        const animation = tween(this.node).delay(delay).parallel(cloudTween1, cloudTween2, clearTween)

        return animation
    }

    private clearTiles(manager: EffectManager, board: Board, pairs: [Tile, Tile][], stagger = 0) {
        manager.shakeGame()
        manager.shakeScreen(500)

        AudioUtils.playSound(CodeSoundKeys.THUNDERBOLT)
        board.clear({ pairs, stagger, lightning: true, delay: 1.5 })
        manager.node.scene.emit(GameplayEvents.ADD_COMBO)
        manager.node.scene.emit(GameplayEvents.ADD_SCORE)
    }
}

export default CloudEffect
