import { CodeSoundKeys } from '../../../constants/SoundKeys'
import AudioUtils from '../../../utils/AudioUtils'
import ItemKeys from '../../constants/ItemKeys'
import type EffectManager from '../../manager/EffectManager'
import type Board from '../board'
import type { Grid, Tile } from '../board'
import calcTilePosition from '../board/utils/calc/calcTilePosition'
import withCenteredBoard from './withCenteredBoard'
import withEffectAnimationEvents from './withEffectAnimationEvents'
import withItemAnalytics from './withItemAnalytics'
import withLoweredVolume from './withLoweredVolume'

const {
    Utils: { Function: F },
} = Ludex

const playPadGridAnimation = async (grid: Grid<Tile>, tileSize: Readonly<[number, number]>) => {
    const out = await F.allSettled(
        grid.map((tile, x, y) =>
            tile.sync({
                duration: 0.4,
                easing: 'smooth',
                getTargetPosition: () => calcTilePosition(grid, tileSize, { x, y }, 10),
            }),
        ),
    )

    if (out.some((result) => result.status === 'rejected')) {
        console.error(
            'Error playing padding animation',
            out.filter((result) => result.status === 'rejected'),
        )
    }
}

const playShuffleGridAnimation = async (grid: Grid<Tile>, tileSize: Readonly<[number, number]>) => {
    const array = grid.asArray()
    const count = array.length

    grid.each((tile, x, y) => {
        tile.node.setSiblingIndex(count - 1 - (x + y * grid.width))
    })

    const out = await F.allSettled(
        grid.map((tile) =>
            tile.fly({
                getTilePosition: (pos) => calcTilePosition(grid, tileSize, pos),
            }),
        ),
    )

    if (out.some((result) => result.status === 'rejected')) {
        console.error(
            'Error playing shuffle animation',
            out.filter((result) => result.status === 'rejected'),
        )
    }
}

const playTornadoAnimation = async (
    prevGrid: Grid<Tile>,
    currGrid: Grid<Tile>,
    getTileSize: () => Readonly<[number, number]>,
    playTornadoEffect: () => void,
) => {
    AudioUtils.playSound(CodeSoundKeys.SHUFFLE_ITEM)
    AudioUtils.playSound(CodeSoundKeys.WIND)

    await playPadGridAnimation(prevGrid, getTileSize())
    playTornadoEffect()
    await playShuffleGridAnimation(currGrid, getTileSize())
}

const useTornadoItemLogic = async (board: Board, effectManager: EffectManager) => {
    const getTileSize = () => board.getTileSize()
    const playTornadoEffect = () => effectManager.playTornadoAnimation()

    const playAnimationAndEffect = (prevGrid: Grid<Tile>, currGrid: Grid<Tile>) =>
        playTornadoAnimation(prevGrid, currGrid, getTileSize, playTornadoEffect)

    const useTornado = withEffectAnimationEvents(
        board,
        withCenteredBoard(board, withLoweredVolume(playAnimationAndEffect)),
    )

    return new Promise<void>((resolve) => {
        board.processShuffleRequest(async (prev, curr) => {
            board.clearSelectedTile()
            await useTornado(prev, curr)
            resolve()
        })
    })
}

const useTornadoItem = withItemAnalytics(ItemKeys.THUNDER, withLoweredVolume(useTornadoItemLogic))

export default useTornadoItem
