import type { Component } from 'cc'
import MissingPropertiesError from './MissingPropertiesError'
import MissingPropertyError from './MissingPropertyError'

const {
    Utils: { String: S, Valid: V },
} = Ludex

const { monitorError } = codex

type ValidationStrategy = 'null-check' | 'undefined-check' | 'both' | ((value: unknown) => boolean)

const MAP_KEY = '_compUtils_ReqPropsMap'
const METHOD_KEY = '_compUtils_ReqPropsMethod'

function createHookKey(key: string) {
    return `${METHOD_KEY}_${S.capitalize(key)}`
}

function isNull(value: unknown): value is null {
    return value === null
}

function isUndefined(value: unknown): value is undefined {
    return value === undefined
}

function isNullOrUndefined(value: unknown): value is null | undefined {
    return isNull(value) || isUndefined(value)
}

function getValidator(strategy?: ValidationStrategy) {
    if (typeof strategy === 'function') {
        return strategy
    }

    switch (strategy) {
        case 'null-check':
            return isNull
        case 'undefined-check':
            return isUndefined
        default:
            return isNullOrUndefined
    }
}

function createDecorator(isInvalid: (value: unknown) => boolean) {
    // biome-ignore lint/suspicious/noExplicitAny: needed for experimental decorators
    return (target: Record<string, any>, propertyKey: string) => {
        if (typeof target === 'function') {
            throw new Error('The required decorator can only be applied to non-static class properties.')
        }

        const className = target.constructor.name
        const validator = createHookKey(propertyKey)

        if (target[validator] !== undefined) {
            throw new Error(
                `The required decorator found that property name ${validator} already exists. The reserved method name ${validator} is used already or the decorator was applied multiple times.`,
            )
        }

        target[validator] = function (className: string) {
            if (isInvalid(this[propertyKey])) {
                throw new MissingPropertyError(className, propertyKey)
            }
        }

        if (target[MAP_KEY] === undefined) {
            target[MAP_KEY] = {}
        }

        if (target[MAP_KEY][className] === undefined) {
            target[MAP_KEY][className] = []
        }

        const mapping = target[MAP_KEY] as Record<string, string[]>
        const properties = mapping[className] as string[]

        properties.push(validator)
    }
}

// ? Decorator factory call signature
// biome-ignore lint/suspicious/noExplicitAny: needed for experimental decorators
function required(strategy?: ValidationStrategy): (target: Record<string, any>, propertyKey: string) => void

// ? Decorator call signature
// biome-ignore lint/suspicious/noExplicitAny: needed for experimental decorators
function required(target: Record<string, any>, propertyKey: string): void

// biome-ignore lint/suspicious/noExplicitAny: needed for experimental decorators
function required(strategyOrTarget?: ValidationStrategy | Record<string, any>, propertyKey?: string) {
    if (typeof propertyKey === 'string') {
        // biome-ignore lint/suspicious/noExplicitAny: needed for experimental decorators
        const target = strategyOrTarget as Record<string, any>
        const validator = getValidator()
        const decorate = createDecorator(validator)
        decorate(target, propertyKey)

        return
    }

    const strategy = strategyOrTarget as ValidationStrategy | undefined
    const validator = getValidator(strategy)
    const decorator = createDecorator(validator)
    return decorator
}

function invokeRequiredHook(component: Component, classConstructor?: new () => Component) {
    const className = classConstructor ? classConstructor.name : component.constructor.name
    const mapping: Record<string, string[]> = classConstructor
        ? classConstructor.prototype[MAP_KEY]
        : component.constructor.prototype[MAP_KEY]
    const validators = mapping?.[className] ?? []
    const errors: MissingPropertyError[] = []

    // biome-ignore lint/suspicious/noExplicitAny: needed for experimental decorators
    const target = component as Record<string, any>

    for (const key of validators) {
        if (typeof target[key] !== 'function') continue

        try {
            target[key](className)
        } catch (error) {
            if (error instanceof MissingPropertyError) {
                errors.push(error)
            } else {
                throw error
            }
        }
    }

    if (errors.length === 0) {
        return
    }

    const error = new MissingPropertiesError(className, errors)

    if (V.isDebugger()) {
        throw error
    }

    console.error(error)
    monitorError?.sendException(error)
}

export { invokeRequiredHook, required }
export type { ValidationStrategy }
