import { pseudoRandomRangeInt } from 'cc'
import { LeaderboardTypes } from '../constants/Leaderboard'
import MapData from '../gameplay/constants/MapData'
import ResourcePaths from '../gameplay/constants/ResourcePaths'
import type SkinKeys from '../gameplay/constants/SkinKeys'
import type ThemeKeys from '../gameplay/constants/ThemeKeys'
import ResourceUtils from './ResourceUtils'

const {
    Utils: { Function: F },
} = Ludex

const { player, storage } = codex

const DEFAULT_PLAYER_SKIN_PROGRESS = MapData.mapOrder.map(
    (info) =>
        ({
            skins: info.skins,
            themeId: info.theme,
            currentProgress: 0,
            maxProgress: info.levelCount,
            isLocked: true,
        }) as PlayerSkinProgress[number],
)

class ThemeUtilsCore {
    protected static inst$: ThemeUtilsCore

    public static get inst() {
        if (!this.inst$) {
            this.inst$ = new this()
        }

        return this.inst$
    }
}

class ThemeUtils extends ThemeUtilsCore {
    public playerSkinProgress = DEFAULT_PLAYER_SKIN_PROGRESS.map((info) => ({ ...info }))

    // !! Should be called when changing scenes
    public static init() {
        const inst = this.inst as ThemeUtils

        inst.initDefaultPlayerSkinProgress()
        inst.updatePlayerSkinProgress()
        inst.initDashboardSceneStorage()
    }

    public initDefaultPlayerSkinProgress() {
        this.playerSkinProgress = DEFAULT_PLAYER_SKIN_PROGRESS.map((info) => ({ ...info }))
    }

    protected initDashboardSceneStorage() {
        const dashboardStorage = storage.getStorage('dashboard')
        if (dashboardStorage !== null && dashboardStorage !== undefined) {
            return
        }

        const currMap = this.getCurrentMapProgressInstance()

        storage.addStorage('dashboard', {
            lastMapPayload: currMap,
            numOfMapsPassed: 0,
            animateToCurrentLevel: false,
        })

        const collectionStorage = storage.getStorage('collection')
        if (collectionStorage !== null && collectionStorage !== undefined) {
            return
        }

        storage.addStorage('collection', {
            lastMapPack: currMap.id,
            animateMapPackUnlock: false,
        })

        const leaderboardStorage = storage.getStorage('leaderboard_content')
        if (leaderboardStorage !== null && leaderboardStorage !== undefined) {
            return
        }

        storage.addStorage('leaderboard_content', { lastTabOpened: LeaderboardTypes.DAILY, previousScore: -1 })

        const tournamentStorage = storage.getStorage('tournament')
        if (tournamentStorage !== null && tournamentStorage !== undefined) {
            return
        }

        storage.addStorage('tournament', { animateTournamentLeader: false, previousScore: -1 })
    }

    public static getCurrentMapProgress(): CurrentMapProgressPayload {
        const inst = this.inst as ThemeUtils
        return inst.getCurrentMapProgressInstance()
    }

    protected getCurrentMapProgressInstance(): CurrentMapProgressPayload {
        // get the furthest skins that is not completed. If all skins are completed, get the last skins

        let currentTheme: ThemeKeys | undefined = undefined
        let nextTheme: ThemeKeys | undefined = undefined
        let progress = 0
        let max = 0
        let id = 0

        for (let i = 0, count = MapData.mapOrder.length; i < count; i++) {
            const skin = this.playerSkinProgress[i] as PlayerSkinProgress[number]
            const mapInfo = MapData.mapOrder[i] as (typeof MapData.mapOrder)[number]

            const nextMapInfo = MapData.mapOrder[i + 1]

            // ! if skin is not found, use the first skin
            if (skin.currentProgress < skin.maxProgress) {
                id = mapInfo.id
                currentTheme = mapInfo.theme
                nextTheme = nextMapInfo?.theme ?? currentTheme
                progress = skin.currentProgress
                max = skin.maxProgress
                break
            }
        }

        if (currentTheme === undefined || nextTheme === undefined) {
            const lastTheme = MapData.mapOrder[MapData.mapOrder.length - 1] as (typeof MapData.mapOrder)[number]

            id = lastTheme.id
            currentTheme = lastTheme.theme
            nextTheme = lastTheme.theme
            progress = lastTheme.levelCount
            max = lastTheme.levelCount

            return {
                id,
                currentTheme,
                nextTheme,
                progress,
                max,
            }
        }

        return {
            id,
            currentTheme,
            nextTheme,
            progress,
            max,
        }
    }

    public static getUnlockedSkins(): SkinKeys[] {
        const inst = this.inst as ThemeUtils
        return inst.getUnlockedSkinsInstance()
    }

    protected getUnlockedSkinsInstance(): SkinKeys[] {
        const unlockedSkins: SkinKeys[] = []

        for (const progress of this.playerSkinProgress) {
            if (!progress.isLocked && progress.skins.length === 1) {
                const firstSkin = progress.skins[0]

                if (firstSkin) {
                    unlockedSkins.push(firstSkin)
                }
            }
        }

        return unlockedSkins
    }

    public static getLevelSkins(level?: number): SkinKeys[] {
        const inst = this.inst as ThemeUtils
        return inst.getLevelSkinsInstance(level)
    }

    protected getLevelSkinsInstance(level?: number): SkinKeys[] {
        const playerLevel = level ?? 1

        let count = 0

        for (const mapInfo of MapData.mapOrder) {
            if (count + mapInfo.levelCount >= playerLevel) {
                return mapInfo.skins
            }

            count += mapInfo.levelCount
        }

        const lastMapInfo = MapData.mapOrder[MapData.mapOrder.length - 1] as (typeof MapData.mapOrder)[number]
        return lastMapInfo.skins
    }

    public static getLevelTheme(level?: number): ThemeKeys {
        const inst = this.inst as ThemeUtils
        return inst.getLevelThemeInstance(level)
    }

    protected getLevelThemeInstance(level?: number): ThemeKeys {
        const playerLevel = level ?? 1

        let count = 0

        for (const mapInfo of MapData.mapOrder) {
            if (count + mapInfo.levelCount >= playerLevel) {
                return mapInfo.theme
            }

            count += mapInfo.levelCount
        }

        const lastMapInfo = MapData.mapOrder[MapData.mapOrder.length - 1] as (typeof MapData.mapOrder)[number]
        return lastMapInfo.theme
    }

    public static getThemeSkins(theme: ThemeKeys): SkinKeys[] {
        const inst = this.inst as ThemeUtils
        return inst.getThemeSkinsInstance(theme)
    }

    protected getThemeSkinsInstance(theme: ThemeKeys): SkinKeys[] {
        for (const mapInfo of MapData.mapOrder) {
            if (mapInfo.theme === theme) {
                return mapInfo.skins
            }
        }

        return []
    }

    public static getCollectionUnlockProgress(): CollectionsPayload[] {
        const inst = this.inst as ThemeUtils
        return inst.getCollectionUnlockProgressInstance()
    }

    public static getCurrentUnlockingThemeIndex(): number | null {
        const collectionsPayloads = this.getCollectionUnlockProgress()
        if (collectionsPayloads.length === 0) return null

        let currentIndex = 0
        collectionsPayloads.forEach((element, i) => {
            const { isLocked } = element

            if (!isLocked) {
                currentIndex = i
            }
        })
        return currentIndex
    }

    protected getCollectionUnlockProgressInstance() {
        const payload: CollectionsPayload[] = []

        for (const progress of this.playerSkinProgress) {
            payload.push({
                skins: progress.skins,
                theme: progress.themeId,
                progress: progress.currentProgress,
                max: progress.maxProgress,
                isLocked: progress.isLocked,
            })
        }

        return payload
    }

    protected updatePlayerSkinProgress() {
        const gameData = player.getGameData()
        const levelProgress = (gameData?.level ?? 1) - 1

        let count = levelProgress

        const playerFirstSkin = this.playerSkinProgress[0] as PlayerSkinProgress[number]

        //? unlock first skin
        playerFirstSkin.isLocked = false

        for (let i = 0, length = MapData.mapOrder.length; i < length; i++) {
            const mapInfo = MapData.mapOrder[i]
            if (!mapInfo) continue

            if (count <= 0) continue

            const progress = count - mapInfo.levelCount >= 0 ? mapInfo.levelCount : count

            count -= progress

            this.increaseProgress(i, progress)
        }
    }

    protected increaseProgress(index: number, progress: number) {
        if (index < 0 || index >= this.playerSkinProgress.length) {
            throw new Error('increaseProgress: index out of range')
        }

        const playerSkinProgress = this.playerSkinProgress[index] as PlayerSkinProgress[number]

        const { currentProgress, maxProgress } = playerSkinProgress

        const newProgress = currentProgress + progress > maxProgress ? maxProgress : currentProgress + progress

        if (newProgress > 0) {
            // unlock current skin
            playerSkinProgress.isLocked = false
        }

        if (newProgress === maxProgress) {
            // unlock next skin
            const mapInfo = MapData.mapOrder
            const nextSkin = mapInfo[index + 1]?.skins

            if (nextSkin) {
                const playerSkinProgress = this.playerSkinProgress[index + 1] as PlayerSkinProgress[number]
                playerSkinProgress.isLocked = false
            }
        }

        playerSkinProgress.currentProgress = newProgress
        playerSkinProgress.maxProgress = maxProgress
    }

    public static preloadLevelSpriteFrames(level: number): void {
        const skins = this.getLevelSkins(level)
        this.preloadSkinsFrames(skins)
    }

    public static preloadSkinsFrames(skins: SkinKeys[]): void {
        for (const skin of skins) {
            this.preloadSkinFrames(skin)
        }
    }

    public static preloadSkinFrames(skin: SkinKeys): void {
        const path = `${ResourcePaths.TILE_SKIN_FOLDER}/${skin}`
        ResourceUtils.loadSpriteFramesDirAsync(path)
    }

    public static async loadLevelSpriteFrames(level: number, themeId?: ThemeKeys, seed?: number) {
        const randomRangeInt = F.rng({
            generate: (seed, minInc: number, maxExc: number) => pseudoRandomRangeInt(seed, minInc, maxExc),
            generateSeed: (seed) => pseudoRandomRangeInt(seed, 0, 1e6),
            seed: seed ?? Date.now(),
        })

        if (themeId !== undefined) {
            const useRequestedThemeId = randomRangeInt(0, 2) === 0
            if (useRequestedThemeId) {
                const skins = this.getThemeSkins(themeId)
                const selected = skins[randomRangeInt(0, skins.length)] as (typeof skins)[number]
                return await this.loadSkinFrames(selected)
            }
        }

        const useRandomUnlockedSkin = randomRangeInt(0, 2) === 0
        if (useRandomUnlockedSkin) {
            const unlockedSkins = this.getUnlockedSkins()
            if (unlockedSkins.length > 1) {
                const selected = unlockedSkins[
                    randomRangeInt(0, unlockedSkins.length)
                ] as (typeof unlockedSkins)[number]
                return await this.loadSkinFrames(selected)
            }
        }

        const levelSkins = this.getLevelSkins(level)

        if (levelSkins.length === 0) throw new Error(`No skins found for level ${level}`)
        const loadedSkins = await this.loadSkinsFrames(levelSkins)
        const selected = loadedSkins[randomRangeInt(0, loadedSkins.length)] as (typeof loadedSkins)[number]
        return selected
    }

    public static loadSkinFrames(skin: SkinKeys) {
        const path = `${ResourcePaths.TILE_SKIN_FOLDER}/${skin}`
        return ResourceUtils.loadSpriteFramesDirAsync(path)
    }

    public static async loadSkinsFrames(skins: SkinKeys[]) {
        const ps = skins.map(this.loadSkinFrames)
        const rs = await F.allSettled(ps)
        const { successes, failures } = F.unwrapAllSettled(rs)
        if (failures.length > 0) throw new Error(`Failed to load skins frames: ${failures.join(', ')}`)
        return successes
    }
}

export default ThemeUtils
