import GameplayEvents from '../../constants/GameEvents'
import type Board from '../board'

const withEffectAnimationEvents =
    // biome-ignore lint/suspicious/noExplicitAny: <explanation>
        <F extends (...args: any[]) => any, O = ReturnType<F> extends Promise<infer O> ? O : never>(
            board: Board,
            fn: F,
        ) =>
        async (...args: Parameters<F>): Promise<O> => {
            board.node.scene.emit(GameplayEvents.EFFECTS_ANIMATION_START)
            const out = await fn(...args)
            board.node.scene.emit(GameplayEvents.EFFECTS_ANIMATION_END)
            return out
        }

export default withEffectAnimationEvents
