import AdsUtils from '../../utils/AdsUtils'
import ThemeUtils from '../../utils/ThemeUtils'
import type { StateManager } from '../manager/StateManager'
import BaseState from './BaseState'

const {
    Plugins: {
        Ads: { Types: AdsTypes } = {},
    },
} = Ludex

class PlayingState extends BaseState {
    constructor(manager: StateManager) {
        super(manager)

        this.startCountdown()
        this.preloadNextLevelSkins()

        if (AdsTypes) {
            AdsUtils.preloadAdsByType(AdsTypes.INTERSTITIAL)
        }
    }

    private preloadNextLevelSkins(): void {
        const level = this.data.level ?? 1
        ThemeUtils.preloadLevelSpriteFrames(level + 1)
    }

    private startCountdown(): void {
        if (!this.game) return
        if (this.game.guidelinesManager?.isNewbieGuideLevel()) return

        this.game.clockManager.startCountdown()
        this.game.uiManager.startCountdown()
    }

    public initialize(): void {
        const state = this.manager.createInitializeState(this.manager)
        this.manager.change(state)
    }

    public ready(): void {
        const state = this.manager.createReadyState(this.manager)
        this.manager.change(state)
    }

    public playing(): void {
        // This is playing state, do nothing
    }

    public fail(failKey: FailKey): void {
        const state = this.manager.createFailState(this.manager, failKey)
        this.manager.change(state)
    }

    public finish(): void {
        const state = this.manager.createFinishState(this.manager)
        this.manager.change(state)
    }

    public update(): void {
        // TODO: check data using
        this.updateStateData()
    }
}

export default PlayingState
