import { Component, UIOpacity, _decorator, tween } from 'cc'
import { Localize } from '../../../../components/text/Localize'
import AnimUtils from '../../../../utils/AnimUtils'
import ComponentUtils from '../../../../utils/ComponentUtils'
import AnimAbortSignal from '../../../../utils/common/AnimAbortSignal'

const { ccclass, property } = _decorator

const LOADING_INTER_NOTIFICATION_LOCALE_KEYS = [
    'ads.interNotice.backInSec',
    'ads.interNotice.hangTight',
    'ads.interNotice.quickAd',
] as const satisfies Ludex.Plugins.Language.TextKey[]

function getRandomInterPhrase() {
    const index = Math.floor(Math.random() * LOADING_INTER_NOTIFICATION_LOCALE_KEYS.length)
    return LOADING_INTER_NOTIFICATION_LOCALE_KEYS[index]
}

const NotifyPlayerBeforeShow = Ludex.Configs.Ads.NotifyPlayerBeforeShow

@ccclass('InterstitialAdIndicator')
export class InterstitialAdIndicator extends Component {
    @ComponentUtils.required()
    @property(UIOpacity)
    protected uiOpacity!: UIOpacity

    @ComponentUtils.required()
    @property(Localize)
    protected labelLocalizer!: Localize

    protected signal?: AnimAbortSignal

    protected __preload(): void {
        ComponentUtils.validateRequiredProperties(this)
    }

    public async flash(): Promise<void> {
        const localeKey = getRandomInterPhrase()
        if (NotifyPlayerBeforeShow === false || !localeKey) return

        this.signal?.abort()

        const signal = new AnimAbortSignal()
        this.signal = signal

        this.labelLocalizer.key = localeKey

        const flashes = tween(this.uiOpacity)
            .to(
                0.25,
                {
                    opacity: 255,
                },
                { easing: 'linear' },
            )
            .delay(0.25)
            .to(
                0.25,
                {
                    opacity: 100,
                },
                { easing: 'linear' },
            )
            .delay(0.125)

        await AnimUtils.runTweenAsync(
            tween(this.uiOpacity).repeat(2, flashes).to(0.25, { opacity: 0 }, { easing: 'linear' }),
            signal,
        )

        this.uiOpacity.opacity = 0
    }
}
