import { _decorator, approx, Enum, EPSILON, lerp, RealCurve } from 'cc'

const { ccclass, property } = _decorator

export const Mode = Enum({
    Constant: 0,
    Curve: 1,
    TwoCurves: 2,
    TwoConstants: 3,
})

@ccclass('CurveRange')
export default class CurveRange {
    @property({ type: Enum(Mode) })
    public mode: number = Mode.Constant

    @property({
        visible: function (this: CurveRange): boolean {
            return this.mode === Mode.Constant
        },
    })
    public constant = 0

    @property({
        visible: function (this: CurveRange): boolean {
            return this.mode === Mode.Curve
        },
    })
    public spline: RealCurve = new RealCurve()

    @property({
        visible: function (this: CurveRange): boolean {
            return this.mode === Mode.TwoCurves
        },
    })
    public splineMin: RealCurve = new RealCurve()

    @property({
        visible: function (this: CurveRange): boolean {
            return this.mode === Mode.TwoCurves
        },
    })
    public splineMax: RealCurve = new RealCurve()

    @property({
        visible: function (this: CurveRange): boolean {
            return this.mode === Mode.TwoConstants
        },
    })
    public constantMin = 0

    @property({
        visible: function (this: CurveRange): boolean {
            return this.mode === Mode.TwoConstants
        },
    })
    public constantMax = 0

    constructor() {
        this.spline.assignSorted([0, 1], [0, 1])
        this.splineMin.assignSorted([0, 1], [0, 1])
        this.splineMax.assignSorted([0, 1], [0, 1])
    }

    public evaluate(time: number, rndRatio: number): number {
        switch (this.mode) {
            case Mode.Curve:
                return this.spline.evaluate(time)
            case Mode.TwoCurves:
                return lerp(this.splineMin.evaluate(time), this.splineMax.evaluate(time), rndRatio)
            case Mode.TwoConstants:
                return lerp(this.constantMin, this.constantMax, rndRatio)
            default:
                return this.constant
        }
    }

    public getMax(): number {
        switch (this.mode) {
            case Mode.Curve:
                return 1
            case Mode.TwoConstants:
                return this.constantMax
            case Mode.TwoCurves:
                return 1
            default:
                return this.constant
        }
    }

    public isZero(): boolean {
        switch (this.mode) {
            case Mode.Curve:
                return approx(1, 0.0, EPSILON)
            case Mode.TwoConstants:
                return approx(Math.max(Math.abs(this.constantMax), Math.abs(this.constantMin)), 0.0, EPSILON)
            case Mode.TwoCurves:
                return approx(1, 0.0, EPSILON)
            default:
                return approx(this.constant, 0.0, EPSILON)
        }
    }
}
