import { Vec2 } from 'cc'
import { Direction } from '../../../../constants/Directions'

const { Array: A } = Ludex.Utils

type Tile = {
    isHidden(): boolean
    isSimilarTo(other: Tile): boolean
}

type Grid = {
    get: (x: number, y: number) => Tile
    isInBounds: (x: number, y: number) => boolean
    width: number
    height: number
}

function inSamePosition(a: Readonly<[number, number]>, b: Readonly<[number, number]>): boolean {
    return a[0] === b[0] && a[1] === b[1]
}

function asVec2(coord: Readonly<[number, number]>): Vec2 {
    return new Vec2(coord[0], coord[1])
}

function isTraversablePadding(coord: { x: number; y: number }, grid: Grid) {
    return coord.x >= -1 && coord.x < grid.width + 1 && coord.y >= -1 && coord.y < grid.height + 1
}

function getAdjacentTiles(node: Node, directions: Vec2Like[] = DIRECTIONS4): [number, number][] {
    return directions.map((direction) => [node.tile[0] + direction.x, node.tile[1] + direction.y])
}

const MAX_SEARCHABLE_TURNS = 8

const DIRECTIONS4 = [Direction.Up, Direction.Down, Direction.Left, Direction.Right]

class Node {
    public f = 0
    public g = 0
    public h = 0

    readonly tile: Readonly<[number, number]>
    readonly parent: Node | null
    readonly cache: {
        readonly direction: Vec2 | null
        readonly turns: number
    }

    constructor(tile: Readonly<[number, number]>, parent: Node | null) {
        if (parent && inSamePosition(tile, parent.tile)) {
            throw new Error('Tile cannot be its own parent')
        }

        this.tile = tile
        this.parent = parent

        const direction = Node.calcDirection(this)
        const hasTurned = parent && direction && parent.cache.direction && !direction.equals(parent.cache.direction)
        const turns = hasTurned ? 1 : 0

        this.cache = {
            direction,
            turns: turns + (parent?.cache.turns ?? 0),
        }
    }

    private static calcDirection(node: Node): Vec2 | null {
        if (node.parent) {
            const parentCoord = node.parent.tile
            const currentCoord = node.tile

            return new Vec2(currentCoord[0] - parentCoord[0], currentCoord[1] - parentCoord[1])
        }

        return null
    }

    public getKey(): string {
        /**
         * NOTE: Using either Direction or Parent is enough to make sure node in overlapping paths are unique,
         * Here we only use Direction to avoid having to find the key of the parent node
         */
        return `x:${this.tile[0]},y:${this.tile[1]},d:${
            this.cache.direction?.toString() // direction
        }`
    }

    public equals(node: Node): boolean {
        return this.getKey() === node.getKey()
    }
}

/**
 * Get a path from startTile to endTile.
 *
 * The path only contains traversable tiles,
 * only allows horizontal and vertical movement
 * and only allows two turns.
 *
 * @param grid The grid game component
 * @param startTile The starting tile
 * @param endTile The target tile
 * @returns Returns an array of tiles, including the start and end tiles or null if there is no valid path
 */
export function getBestPath(grid: Grid, startTile: [number, number], endTile: [number, number]): Path {
    const traversableStartDirections = getTraversableDirections(grid, startTile)
    const traversableEndDirections = getTraversableDirections(grid, endTile)

    if (traversableStartDirections.length === 0 || traversableEndDirections.length === 0) {
        return getStraightPath(startTile, endTile)
    }

    const path = getAStarPath(grid, startTile, endTile)
    if (path) return path

    return getStraightPath(startTile, endTile)
}

function getAStarPath(grid: Grid, startTile: [number, number], endTile: [number, number]): Path | null {
    // A* algorithm
    const openList: Node[] = []
    const closedList: Node[] = []

    const startNode = new Node(startTile, null)
    const endNode = new Node(endTile, null)

    // optimization
    const exceptionTiles = [endNode.tile]

    openList.push(startNode)

    while (openList.length > 0) {
        // Get the node with the lowest f score
        const currentNode = openList.shift() as Node

        // Add the current node to the closed list
        closedList.push(currentNode)

        // If the current node is the end node, return the path
        if (inSamePosition(currentNode.tile, endNode.tile)) {
            const path: Path = []

            let endNode = currentNode
            let previousNode = currentNode
            let node: Node | null = currentNode.parent

            while (node) {
                if (node.cache.turns !== previousNode.cache.turns) {
                    path.unshift({
                        a: asVec2(node.tile),
                        b: asVec2(endNode.tile),
                    })

                    endNode = node
                }

                previousNode = node
                node = node.parent
            }

            path.unshift({
                a: asVec2(previousNode.tile),
                b: asVec2(endNode.tile),
            })

            return path
        }

        // Get traversable adjacent tiles of the current node
        const adjacentTiles = getAdjacentTiles(
            currentNode,
            getTraversableDirections(grid, currentNode.tile, exceptionTiles),
        )

        for (const adjacentTile of adjacentTiles) {
            // Create a new node for the adjacent tile
            const adjacentNode = new Node(adjacentTile, currentNode)

            // skip if the adjacent node have turned more than the specified amount for optimization
            if (adjacentNode.cache.turns > MAX_SEARCHABLE_TURNS) {
                continue
            }

            const isEndNode = inSamePosition(adjacentNode.tile, endNode.tile)
            const isDiscovered = closedList.some((node) => node.equals(adjacentNode))
            const isQueued = openList.some((node) => node.equals(adjacentNode))

            /**
             * Skip if the adjacent node is either:
             * * Queued
             * * Discovered (with the exception of the end node)
             */
            if (isQueued || (isDiscovered && !isEndNode)) {
                continue
            }

            // Calculate the f score
            adjacentNode.g = currentNode.g + 1 + adjacentNode.cache.turns
            adjacentNode.h = getHeuristic(adjacentNode, endNode)
            adjacentNode.f = adjacentNode.g + adjacentNode.h

            // Add the adjacent node to the open list
            openList.push(adjacentNode)
        }

        // Sort the open list by f score
        openList.sort((a, b) => a.f - b.f)
    }

    return null
}

function getStraightPath(startTile: [number, number], endTile: [number, number]): Path {
    const startCoord = asVec2(startTile)
    const endCoord = asVec2(endTile)

    if (startCoord.x === endCoord.x || startCoord.y === endCoord.y) {
        return [{ a: startCoord, b: endCoord }]
    }

    return [
        { a: startCoord, b: { x: startCoord.x, y: endCoord.y } },
        { a: { x: startCoord.x, y: endCoord.y }, b: endCoord },
    ]
}

function getHeuristic(node: Node, endNode: Node): number {
    const currentCoord = node.tile
    const endCoord = endNode.tile

    return Math.abs(currentCoord[0] - endCoord[0]) + Math.abs(currentCoord[1] - endCoord[1])
}

function getTraversableDirections(
    grid: Grid,
    tile: Readonly<[number, number]>,
    exceptionTiles: Readonly<[number, number]>[] = [],
): Vec2Like[] {
    const directions: Vec2Like[] = []

    for (const direction of DIRECTIONS4) {
        const coord = {
            x: tile[0] + direction.x,
            y: tile[1] + direction.y,
        }

        if (grid.isInBounds(coord.x, coord.y)) {
            const adjacentTile = grid.get(coord.x, coord.y)

            if (adjacentTile.isHidden() || A.search(exceptionTiles, (t) => inSamePosition(t, [coord.x, coord.y]))) {
                directions.push(direction)
            }

            continue
        }

        if (isTraversablePadding(coord, grid)) {
            directions.push(direction)
        }
    }

    return directions
}
