const {
    Utils: { Array: A },
} = Ludex

const zeroes = (size: number) => A.fillWith(size, () => 0)

const toSortedIndices = <T>(array: ReadonlyArray<T>, compareFn: (a: T, b: T) => number): number[] => {
    const entries = array.map((data, index) => ({ data, index }))
    entries.sort((a, b) => compareFn(a.data, b.data))

    const indices = entries.reduce((acc, entry, index) => {
        acc[entry.index] = index
        return acc
    }, zeroes(array.length))

    return indices
}

export default toSortedIndices
