import { CodeSoundKeys } from '../../constants/SoundKeys'
import AudioUtils from '../../utils/AudioUtils'
import MatchUtils from '../../utils/MatchUtils'
import ResourceUtils from '../../utils/ResourceUtils'
import type { StateManager } from '../manager/StateManager'
import BaseState from './BaseState'

const { analytics } = codex

const {
    Utils: { Valid: V },
} = Ludex

const PRELOAD_SOUNDS = [
    CodeSoundKeys.COMBO_1,
    CodeSoundKeys.COMBO_2,
    CodeSoundKeys.COMBO_3,
    CodeSoundKeys.COMBO_4,
    CodeSoundKeys.COMBO_5,
    CodeSoundKeys.COMBO_6,
    CodeSoundKeys.COMBO_7,
    CodeSoundKeys.COMBO_8,
    CodeSoundKeys.COMBO_9,
    CodeSoundKeys.COMBO_10,
    CodeSoundKeys.COMBO_11,
    CodeSoundKeys.COMBO_12,
    CodeSoundKeys.COMBO_13,
    CodeSoundKeys.TILE_MATCH,
    CodeSoundKeys.BOMB_DEFUSE,
    CodeSoundKeys.BOMB_EXPLODE_1,
    CodeSoundKeys.BOMB_EXPLODE_2,
    CodeSoundKeys.TILE_SELECT,
    CodeSoundKeys.THUNDERBOLT,
    CodeSoundKeys.SHUFFLE_ITEM,
    CodeSoundKeys.LEVEL_START,
    CodeSoundKeys.LEVEL_COMPLETE,
    CodeSoundKeys.LEVEL_FAIL,
]

class ReadyState extends BaseState {
    constructor(manager: StateManager) {
        super(manager)

        AudioUtils.stopAllSounds()
        AudioUtils.turnDownMusic()

        this.preloadAssets()

        this.handleGuideFlow()

        this.setGridPlayable()

        this.logLevelStartAnalytics()
    }

    public initialize(): void {
        const state = this.manager.createInitializeState(this.manager)
        this.manager.change(state)
    }

    public ready(): void {
        // This is ready state, do nothing
    }

    public playing(): void {
        const state = this.manager.createPlayingState(this.manager)
        this.manager.change(state)
    }

    public fail(_message?: string): void {
        // ? Allow fail by dev tools
        if (!V.isDebugger()) return

        const state = this.manager.createFailState(this.manager)
        this.manager.change(state)
    }

    public finish(): void {
        // ? Allow complete by dev tools
        if (!V.isDebugger()) return

        const state = this.manager.createFinishState(this.manager)
        this.manager.change(state)
    }

    public update(): void {
        //
    }

    private preloadAssets(): void {
        this.preloadSounds()
        
        this.game?.effectManager.preloadEffects()
    }

    private preloadSounds(): void {
        for (const sound of PRELOAD_SOUNDS) {
            ResourceUtils.loadAudioAsync(sound)
        }
    }

    private setGridPlayable(): void {
        const gameplay = this.manager.getGameplay()

        if (!gameplay.guidelinesManager?.isNewbieGuideLevel()) {
            this.manager.getGameplay().board.setIgnoreInput(false)
        }
    }

    private handleGuideFlow(): void {
        if (!this.game) return

        if (!this.game.guidelinesManager?.isNewbieGuideLevel()) {
            this.game.handleShowUI()
        }

        this.game.guidelinesManager?.show()
    }

    private logLevelStartAnalytics(): void {
        if (!this.game) return

        const { level = 0 } = this.data

        // ? Score manager is initialized in InitializeState
        // const score = this.game.scoreManager.getCurrentScore()

        const { score = 0 } = MatchUtils.getMatchCustomData() || {}
        const mode = MatchUtils.getMatchMode() || undefined

        analytics?.levelStart(level, score, mode)
    }
}

export default ReadyState
