import { Button, Component, UIOpacity, _decorator, tween } from 'cc'
import GameEvents from '../../../constants/GameEvents'
import AnimUtils from '../../../utils/AnimUtils'
import ComponentUtils from '../../../utils/ComponentUtils'
const { ccclass, property, disallowMultiple } = _decorator

const onEmptyCallback = () => {}

@ccclass('AnimSkipOverlay')
@disallowMultiple
export class AnimSkipOverlay extends Component {
    @property(UIOpacity)
    private containerUIOpacity!: UIOpacity

    @property(UIOpacity)
    private maskUIOpacity!: UIOpacity

    @property(Button)
    private skipButton!: Button

    private isOpened = false
    private currentCallback: () => void = onEmptyCallback

    protected __preload(): void {
        this.validateRequiredComponents()
        this.containerUIOpacity.node.active = false
        this.skipButton.interactable = false
    }

    public onSkipButtonPress(): void {
        this.currentCallback()
        this.close()
    }

    private validateRequiredComponents(): void {
        ComponentUtils.lazyValidateProperties(this)
    }

    @ComponentUtils.onlyRunInLifeCycleAsync
    public async open(payload: AnimSkipOverlayPayload): Promise<void> {
        this.containerUIOpacity.node.active = true

        const { onSkip, opacity = 255, transitionDuration = 0.2 } = payload

        this.maskUIOpacity.opacity = opacity
        await this.animateUIOpacity(0, 255, transitionDuration)

        this.currentCallback = onSkip
        this.skipButton.interactable = true

        this.isOpened = true
        this.node.scene.emit(GameEvents.OPENED_SKIP_OVERLAY, payload)
    }

    @ComponentUtils.onlyRunInLifeCycleAsync
    public async close(transitionDuration = 0.2): Promise<void> {
        this.skipButton.interactable = false
        this.currentCallback = onEmptyCallback

        await this.animateUIOpacity(255, 0, transitionDuration)
        this.maskUIOpacity.opacity = 0

        this.containerUIOpacity.node.active = false

        this.isOpened = false
        this.node.scene.emit(GameEvents.CLOSED_SKIP_OVERLAY)
    }

    private animateUIOpacity(fromOpacity: number, toOpacity: number, duration: number) {
        this.containerUIOpacity.opacity = fromOpacity
        return AnimUtils.runTweenAsync(tween(this.containerUIOpacity).to(duration, { opacity: toOpacity }))
    }

    @ComponentUtils.onlyRunInLifeCycleAsync
    public untilClosed(): Promise<void> {
        if (this.isOpened === false) {
            return Promise.resolve()
        }

        return new Promise((resolve) => {
            this.node.scene.once(GameEvents.CLOSED_SKIP_OVERLAY, resolve)
        })
    }
}
