import { Node as CCNode, Component, UIOpacity, Vec3, _decorator, tween } from 'cc'
import AnimUtils from '../../../../../utils/AnimUtils'
import ComponentUtils from '../../../../../utils/ComponentUtils'
import type AnimAbortSignal from '../../../../../utils/common/AnimAbortSignal'

const { ccclass, property } = _decorator

@ccclass('TournamentBlockSkeleton')
export class TournamentBlockSkeleton extends Component {
    private uiOpacity!: UIOpacity

    @property(CCNode)
    private container!: CCNode

    public __preload(): void {
        this.uiOpacity = this.getComponent(UIOpacity)!
        this.validateRequiredComponents()
    }

    private validateRequiredComponents(): void {
        ComponentUtils.lazyValidateProperties(this)
    }

    public async hide(signal: AnimAbortSignal): Promise<void> {
        await AnimUtils.runTweenAsync(
            tween(this.uiOpacity)
                .to(0.2, {
                    opacity: 0,
                })
                .call(() => {
                    this.node.active = false
                }),
            signal,
        )
    }

    public async show(delay: number, signal: AnimAbortSignal): Promise<void> {
        this.node.active = true

        this.node.scale = new Vec3(0.8, 0.8, 0.8)
        this.uiOpacity.opacity = 0

        const position = this.container.position.clone()
        this.container.position = new Vec3(position.x, position.y - 100, position.z)

        const scale = tween(this.node).delay(delay)
        scale.to(0.2, { scale: Vec3.ONE }, { easing: 'elasticOut' })

        const float = tween(this.container).delay(delay)
        float.to(0.2, { position }, { easing: 'backOut' })

        const opacity = tween(this.uiOpacity).delay(delay)
        opacity.to(0.2, { opacity: 255 }, { easing: 'cubicOut' })

        const cb = () => {
            this.node.setScale(Vec3.ONE)
            this.container.setPosition(position)
            this.uiOpacity.opacity = 255
        }

        signal.addAbortListener(cb)

        await Promise.all([
            AnimUtils.runTweenAsync(scale, signal),
            AnimUtils.runTweenAsync(float, signal),
            AnimUtils.runTweenAsync(opacity, signal),
        ])

        signal.removeAbortListener(cb)
    }
}
