const {
    Utils: { Array: A },
} = Ludex

type VoidFunction<T extends (...args: never[]) => void> = ReturnType<T> extends void ? T : never
type PromiseVoidFunction<T extends (...args: never[]) => Promise<void>> = ReturnType<T> extends Promise<void>
    ? T
    : never

/**
 * @deprecated
 */
class AnimAbortSignal {
    private callbacks$: (() => void)[] = []
    private isAborted$: boolean
    private abortReason$: string | undefined

    constructor() {
        this.isAborted$ = false
        this.abortReason$ = ''
        this.callbacks$ = []
    }

    public get aborted() {
        return this.isAborted$
    }

    public get reason() {
        return this.abortReason$
    }

    public addAbortListener(listener: () => void) {
        this.callbacks$.push(listener)
    }

    public removeAbortListener(listener: () => void) {
        const index = A.searchIndex(this.callbacks$, (cb) => cb === listener)
        if (index === -1) return
        this.callbacks$.splice(index, 1)
    }

    public abort(reason?: string) {
        if (this.isAborted$) return
        this.isAborted$ = true
        this.abortReason$ = reason

        for (const cb of this.callbacks$) {
            typeof cb === 'function' && cb()
        }
        this.callbacks$ = []
    }

    public async untilAborted(): Promise<void> {
        if (this.isAborted$) return

        return new Promise((resolve) => {
            this.addAbortListener(() => resolve())
        })
    }

    public call<T extends (signal: AnimAbortSignal) => unknown>(cb: VoidFunction<T>) {
        if (!this.aborted) {
            cb(this)
        }
    }

    public callAsync<T extends (signal: AnimAbortSignal) => Promise<void>>(cb: PromiseVoidFunction<T>): Promise<void> {
        if (!this.aborted) {
            return cb(this)
        }
        return Promise.resolve()
    }

    public chain(signal: AnimAbortSignal | AbortSignal) {
        if (signal instanceof AnimAbortSignal) {
            signal.addAbortListener(() => this.abort())
            return
        }

        signal.addEventListener('abort', () => this.abort())
    }

    static hasBeenAborted(signal: AnimAbortSignal | undefined | null): boolean {
        return signal === null || signal === undefined || signal.aborted
    }
}

export default AnimAbortSignal
