import { _decorator, CCInteger, CCString, Component, easing, Label, lerp, tween, UIOpacity } from 'cc'
import { TapperedProgressBar } from '../../../../../components/tappered-progress-bar/TapperedProgressBar'
import AnimUtils from '../../../../../utils/AnimUtils'
import ComponentUtils from '../../../../../utils/ComponentUtils'

const { ccclass, property, executeInEditMode } = _decorator

@ccclass('JourneyProgressBar')
@executeInEditMode
export class JourneyProgressBar extends Component {
    @property(TapperedProgressBar)
    private progressBar!: TapperedProgressBar

    @property(Label)
    private progressLabel!: Label

    @property(UIOpacity)
    private labelUIOpacity!: UIOpacity

    private currentProgress: number = 10
    private totalProgress: number = 10
    private labelSeparator: string = '/'

    @property(CCInteger)
    public get total(): number {
        return this.totalProgress
    }

    @property(CCInteger)
    public get current(): number {
        return this.currentProgress
    }

    @property(CCString)
    public get separator(): string {
        return this.labelSeparator
    }

    public set separator(value: string) {
        this.labelSeparator = value
        this.updateProgress()
    }

    public setRange(current: number, total: number): void {
        this.totalProgress = total
        let c = current

        if (c > total) {
            c = total
        }

        this.currentProgress = c

        this.updateProgress()
    }

    public onLoad(): void {
        this.validateRequiredComponents()
    }

    public async animateTo(payload: JourneyProgressSameLevelPayload): Promise<number> {
        const { nextCurr, nextTotal, numberOfProgressBar = 0, onProgressBarPassed, signal } = payload

        const progress = nextCurr / nextTotal
        const fadeOutText = tween(this.labelUIOpacity).to(0.2, { opacity: 0 })
        const fadeInText = tween(this.labelUIOpacity).to(0.2, { opacity: 255 })

        const duration = numberOfProgressBar < 1 ? 1 : Math.sqrt(numberOfProgressBar) + 1

        // [===-------][progress-bars][progress-bars]...[progress-bars][progress----]
        const targetValue = numberOfProgressBar === 0 ? progress : numberOfProgressBar + progress

        let currentProgress = 1

        const start = this.progressBar.progress
        const end = targetValue

        const progressAnimation = AnimUtils.runOnUpdateAsync(
            this.node,
            duration,
            (ratio) => {
                const sineInOutRatio = easing.sineInOut(ratio)
                const curr = lerp(start, end, sineInOutRatio)
                this.progressBar.progress = curr % 1

                if (curr > currentProgress) {
                    currentProgress = curr === 0 ? 1 : Math.ceil(curr)
                    onProgressBarPassed?.()
                }
            },
            signal,
        )

        await Promise.all([AnimUtils.runTweenAsync(fadeOutText, signal), progressAnimation])

        //? in case the animation was aborted, set final values
        this.currentProgress = nextCurr
        this.totalProgress = nextTotal
        this.updateProgress()

        await AnimUtils.runTweenAsync(fadeInText, signal)

        //? in case the animation was aborted, set text opacity to 255
        this.labelUIOpacity.opacity = 255

        return progress
    }

    @ComponentUtils.onlyRunInLifeCycle
    private updateProgress(): void {
        this.progressBar.progress = this.currentProgress / this.totalProgress
        this.progressLabel.string = `${this.currentProgress}${this.separator}${this.totalProgress}`
    }

    private validateRequiredComponents(): void {
        if (!this.progressBar) {
            throw new Error('JourneyProgressBar: Missing required component TapperedProgressBar')
        }

        if (!this.progressLabel) {
            throw new Error('JourneyProgressBar: Missing required component Label')
        }
    }
}
