import Cloud from '../../../game-objects/cloud/Cloud'

const { ccclass, property } = _decorator

@ccclass('CloudEffectProperties')
class CloudEffectProperties {
    @ComponentUtils.required()
    @property({ group: { name: 'Thunder Effect', style: 'section' }, type: Node })
    public readonly cloudContainer!: Node

    @ComponentUtils.required()
    @property({ group: { name: 'Thunder Effect', style: 'section' }, type: Cloud })
    public readonly cloud1!: Cloud

    @ComponentUtils.required()
    @property({ group: { name: 'Thunder Effect', style: 'section' }, type: Cloud })
    public readonly cloud2!: Cloud

    @ComponentUtils.required()
    @property({ group: { name: 'Thunder Effect', style: 'section' }, type: Cloud })
    public readonly cloud3!: Cloud
}

export default CloudEffectProperties
