import { Vec2 } from 'cc'
import spiralTraversal from '../../../../utils/2d/traverseSpiralIn'

const { Array: A } = Ludex.Utils

type Tile = {
    isHidden(): boolean
    isSimilarTo(other: Tile): boolean
}

type Grid = {
    get: (x: number, y: number) => Tile
    width: number
    height: number
}

class PaddingTile implements Tile {
    coord: Vec2
    width!: number
    height!: number

    constructor(x: number, y: number) {
        this.coord = new Vec2(x, y)
    }

    isHidden(): boolean {
        return true
    }

    isSimilarTo(): boolean {
        return false
    }
}

function asVec2(coord: Readonly<[number, number]>): Vec2 {
    return new Vec2(coord[0], coord[1])
}

function getValidPath(grid: Grid): Path | null
function getValidPath(grid: Grid, tile1: Readonly<[number, number]>, tile2: Readonly<[number, number]>): Path | null

function getValidPath(grid: Grid, tile1?: Readonly<[number, number]>, tile2?: Readonly<[number, number]>): Path | null {
    if (tile1 === undefined || tile2 === undefined) {
        return findFirstMatch(grid)
    }

    const startCoord = [tile1[0] + 1, tile1[1] + 1] as const
    const endCoord = [tile2[0] + 1, tile2[1] + 1] as const

    const matrix = toPaddedGrid(grid)

    const exceptionTiles = [startCoord, endCoord]

    if (
        (startCoord[0] === endCoord[0] &&
            checkLineX(matrix, exceptionTiles, startCoord[1], endCoord[1], startCoord[0])) ||
        (startCoord[1] === endCoord[1] && checkLineY(matrix, exceptionTiles, startCoord[0], endCoord[0], startCoord[1]))
    ) {
        return pathOffset([{ a: asVec2(startCoord), b: asVec2(endCoord) }], {
            x: -1,
            y: -1,
        })
    }

    // Check in a rectangle with x-axis or y-axis
    let t: number

    const pMinY = startCoord[1] <= endCoord[1] ? startCoord : endCoord
    const pMaxY = startCoord[1] <= endCoord[1] ? endCoord : startCoord
    const pMinX = startCoord[0] <= endCoord[0] ? startCoord : endCoord
    const pMaxX = startCoord[0] <= endCoord[0] ? endCoord : startCoord

    let moreLinePath: Path = []
    let rectPath: Path = []

    const isMoreLineX = checkMoreLineX(matrix, exceptionTiles, 1, asVec2(pMinY), asVec2(pMaxY))
    const isMoreLineX2 = checkMoreLineX(matrix, exceptionTiles, -1, asVec2(pMinY), asVec2(pMaxY))

    if (isMoreLineX !== -1 || isMoreLineX2 !== -1) {
        if (isMoreLineX !== -1) {
            t = isMoreLineX
        } else {
            t = isMoreLineX2
        }

        moreLinePath = [
            { a: asVec2(startCoord), b: { x: startCoord[0], y: t } },
            { a: { x: startCoord[0], y: t }, b: { x: endCoord[0], y: t } },
            { a: { x: endCoord[0], y: t }, b: asVec2(endCoord) },
        ]
    }

    // Check more down or more up
    const isMoreLineY = checkMoreLineY(matrix, exceptionTiles, 1, asVec2(pMinX), asVec2(pMaxX))
    const isMoreLineY2 = checkMoreLineY(matrix, exceptionTiles, -1, asVec2(pMinX), asVec2(pMaxX))

    if (isMoreLineY !== -1 || isMoreLineY2 !== -1) {
        if (isMoreLineY !== -1) {
            t = isMoreLineY
        } else {
            t = isMoreLineY2
        }

        moreLinePath = [
            { a: asVec2(startCoord), b: { x: t, y: startCoord[1] } },
            { a: { x: t, y: startCoord[1] }, b: { x: t, y: endCoord[1] } },
            { a: { x: t, y: endCoord[1] }, b: asVec2(endCoord) },
        ]
    }

    // Check in a rectangle with x-axis and y-axis
    const rectX = checkRectX(matrix, exceptionTiles, asVec2(pMinY), asVec2(pMaxY))

    if (rectX !== -1) {
        t = rectX
        rectPath = [
            { a: asVec2(startCoord), b: { x: startCoord[0], y: t } },
            { a: { x: startCoord[0], y: t }, b: { x: endCoord[0], y: t } },
            { a: { x: endCoord[0], y: t }, b: asVec2(endCoord) },
        ]
    }

    const rectY = checkRectY(matrix, exceptionTiles, asVec2(pMinX), asVec2(pMaxX))

    if (rectY !== -1) {
        t = rectY
        rectPath = [
            { a: asVec2(startCoord), b: { x: t, y: startCoord[1] } },
            { a: { x: t, y: startCoord[1] }, b: { x: t, y: endCoord[1] } },
            { a: { x: t, y: endCoord[1] }, b: asVec2(endCoord) },
        ]
    }

    if (moreLinePath.length > 0 && rectPath.length > 0) {
        return pathOffset(getPathLength(moreLinePath) <= getPathLength(rectPath) ? moreLinePath : rectPath, {
            x: -1,
            y: -1,
        })
    }

    if (moreLinePath.length > 0) {
        return pathOffset(moreLinePath, { x: -1, y: -1 })
    }

    if (rectPath.length > 0) {
        return pathOffset(rectPath, { x: -1, y: -1 })
    }

    return null
}

function getPathLength(path: Path): number {
    let length = 0

    for (const line of path) {
        length += getLineLength(line)
    }

    return length
}

function getLineLength(line: Line): number {
    if (line.a.x === line.b.x) {
        return Math.abs(line.a.y - line.b.y)
    }
    if (line.a.y === line.b.y) {
        return Math.abs(line.a.x - line.b.x)
    }

    return 0
}

function inSamePosition(a: Readonly<[number, number]>, b: Readonly<[number, number]>): boolean {
    return a[0] === b[0] && a[1] === b[1]
}

function isTraversable(
    matrix: Grid,
    coord: Readonly<[number, number]>,
    exceptionTiles: Readonly<[number, number]>[] = [],
): boolean {
    const tile = matrix.get(...coord)
    return tile.isHidden() || exceptionTiles.some((exceptionTile) => inSamePosition(exceptionTile, coord))
}

function checkLineX(
    matrix: Grid,
    exceptionTiles: Readonly<Readonly<[number, number]>>[],
    y1: number,
    y2: number,
    x: number,
): boolean {
    const min = Math.min(y1, y2)
    const max = Math.max(y1, y2)
    // run column
    for (let y = min; y <= max; y++) {
        if (!isTraversable(matrix, [x, y], exceptionTiles)) {
            return false
        }
    }
    return true
}

function checkLineY(
    matrix: Grid,
    exceptionTiles: Readonly<[number, number]>[],
    x1: number,
    x2: number,
    y: number,
): boolean {
    const min = Math.min(x1, x2)
    const max = Math.max(x1, x2)
    for (let x = min; x <= max; x++) {
        if (!isTraversable(matrix, [x, y], exceptionTiles)) {
            return false
        }
    }
    return true
}

function checkRectX(
    matrix: Grid,
    exceptionTiles: Readonly<[number, number]>[],
    pMinY: Vec2Like,
    pMaxY: Vec2Like,
): number {
    for (let y = pMinY.y + 1; y < pMaxY.y; y++) {
        // check three line
        if (
            checkLineX(matrix, exceptionTiles, pMinY.y, y, pMinY.x) &&
            checkLineY(matrix, exceptionTiles, pMinY.x, pMaxY.x, y) &&
            checkLineX(matrix, exceptionTiles, y, pMaxY.y, pMaxY.x)
        ) {
            return y
        }
    }
    // have a line in three line not true then return -1
    return -1
}

function checkRectY(matrix: Grid, exceptionTiles: Readonly<[number, number]>[], pMinX: Vec2Like, pMaxX: Vec2Like) {
    // find line and y begin
    for (let x = pMinX.x + 1; x < pMaxX.x; x++) {
        if (
            checkLineY(matrix, exceptionTiles, pMinX.x, x, pMinX.y) &&
            checkLineX(matrix, exceptionTiles, pMinX.y, pMaxX.y, x) &&
            checkLineY(matrix, exceptionTiles, x, pMaxX.x, pMaxX.y)
        ) {
            return x
        }
    }
    return -1
}

function checkMoreLineX(
    matrix: Grid,
    exceptionTiles: Readonly<[number, number]>[],
    type: number,
    pMinY: Vec2Like,
    pMaxY: Vec2Like,
): number {
    // find line and y begin
    let y = type === 1 ? pMaxY.y : pMinY.y
    const row = type === 1 ? pMinY.x : pMaxY.x
    // check more
    if (checkLineX(matrix, exceptionTiles, pMinY.y, pMaxY.y, row)) {
        while (
            y >= 0 &&
            y < matrix.height &&
            isTraversable(matrix, [pMinY.x, y], exceptionTiles) &&
            isTraversable(matrix, [pMaxY.x, y], exceptionTiles)
        ) {
            if (checkLineY(matrix, exceptionTiles, pMinY.x, pMaxY.x, y)) {
                return y
            }
            y += type
        }
    }
    return -1
}

function checkMoreLineY(
    matrix: Grid,
    exceptionTiles: Readonly<[number, number]>[],
    type: number,
    pMinX: Vec2Like,
    pMaxX: Vec2Like,
): number {
    let x = type === 1 ? pMaxX.x : pMinX.x
    const col = type === 1 ? pMinX.y : pMaxX.y

    if (checkLineY(matrix, exceptionTiles, pMinX.x, pMaxX.x, col)) {
        while (
            x >= 0 &&
            x < matrix.width &&
            isTraversable(matrix, [x, pMinX.y], exceptionTiles) &&
            isTraversable(matrix, [x, pMaxX.y], exceptionTiles)
        ) {
            if (checkLineX(matrix, exceptionTiles, pMinX.y, pMaxX.y, x)) {
                return x
            }
            x += type
        }
    }
    return -1
}

function pathOffset(path: Path, by: Vec2Like | Vec2): Path {
    return path.map((line) => {
        return {
            a: {
                x: line.a.x + by.x,
                y: line.a.y + by.y,
            },
            b: {
                x: line.b.x + by.x,
                y: line.b.y + by.y,
            },
        }
    })
}

function toPaddedGrid(grid: Grid): Grid {
    const topPadding = A.fillWith(grid.width + 2, (i) => new PaddingTile(i - 1, -1))
    const bottomPadding = A.fillWith(grid.width + 2, (i) => new PaddingTile(i - 1, grid.height))
    const leftPadding = A.fillWith(grid.height, (i) => new PaddingTile(-1, i))
    const rightPadding = A.fillWith(grid.height, (i) => new PaddingTile(grid.width, i))

    const getter = (x: number, y: number) => {
        const realX = x - 1
        const realY = y - 1

        if (realX === -1) {
            return leftPadding[realY]
        }
        if (realX === grid.width) {
            return rightPadding[realY]
        }
        if (realY === -1) {
            return topPadding[realX]
        }
        if (realY === grid.height) {
            return bottomPadding[realX]
        }

        return grid.get(realX, realY)
    }

    return {
        get(x: number, y: number) {
            return getter(x, y)
        },
        width: grid.width + 2,
        height: grid.height + 2,
    }
}

function findFirstMatch(
    grid: Grid,
    traverseFn: (width: number, height: number) => Generator<IVec2Like, void, unknown> = spiralTraversal,
): Path | null {
    // get coords in spiral order to optimize the search
    // NOTE: this search is skewed towards the top left corner of the grid (row first)
    // convert coords to tiles and filter out empty tiles
    // NOTE: this assumes that spiralTraversal() returns valid coords
    // WARNING: this will throw an error if the tile is null
    const tiles: [Tile, [number, number]][] = []

    for (const coord of traverseFn(grid.width, grid.height)) {
        const tile = grid.get(coord.x, coord.y)
        if (tile.isHidden()) continue

        tiles.push([tile, [coord.x, coord.y]])
    }

    const discovered: Readonly<[number, number]>[] = []

    while (tiles.length > 0) {
        const [tile, coord] = tiles.shift()!

        if (A.search(discovered, (dis) => inSamePosition(dis, coord))) continue

        discovered.push(coord)

        // find other tile that is the same and has a valid path
        for (const [otherTile, otherCoord] of tiles) {
            if (!otherTile.isSimilarTo(tile) || A.search(discovered, (dis) => inSamePosition(dis, otherCoord))) continue

            const path = getValidPath(grid, coord, otherCoord)
            if (path !== null) return path
        }
    }

    return null
}

export default getValidPath
