import { _decorator, Component, math } from 'cc'
import CustomModeUtils from '../../utils/CustomModeUtils'
import MatchUtils from '../../utils/MatchUtils'
import ResourceUtils from '../../utils/ResourceUtils'
import CustomGameData from '../constants/CustomGameData'
import GameModes from '../constants/GameMode'
import ResourcePaths from '../constants/ResourcePaths'
import SkinKeys from '../constants/SkinKeys'
import TotalLevel from '../constants/TotalLevel'
import createRandomizedLevelData from '../game-objects/board/utils/create/createRandomizedLevelData'

const { ccclass } = _decorator
const {
    Utils: { Json: J, Array: A },
} = Ludex

const { monitorError } = codex

const AllSkinKeys = Object.values(SkinKeys) as SkinKeys[]

const DIFFICULTY_COLUMN_AND_ROW: [number, number][] = [
    [4, 6],
    [4, 8],
    [5, 8],
    [6, 8],
    [5, 10],
    [6, 10],
    [7, 10],
    [7, 12],
]

const DIFFICULTY_COLORS: number[] = [-2, 0, 2, 5]

export const DEFAULT_LEVEL_DATA = {
    level: 1,
    mode: 'normal',
    time: 900,
    colors: 12,
    specials: {
        bomb: 0,
        rocket: 0,
    },
    rules: {
        maxSpaceOfPair: 1,
        addRandomTilesPerMove: 0,
    },
    columns: 5,
    rows: 6,
    theme: ['flower'],
} as const satisfies ILevelData

const createCachedLevelDataRandomizer = () => {
    const cache: Record<number, ILevelData> = {}

    return (level: number) => {
        if (cache[level]) {
            return cache[level]
        }

        const randomizedData = createRandomizedLevelData(level)

        cache[level] = {
            level,
            theme: AllSkinKeys,
            rows: randomizedData.rows,
            columns: randomizedData.columns,
            colors: randomizedData.colors,
            mode: randomizedData.gravity,
            rules: {
                maxSpaceOfPair: randomizedData.rules.maxSpaceOfPair,
                addRandomTilesPerMove: randomizedData.rules.addRandomTilesPerMove,
            },
            specials: {
                bomb: randomizedData.specials.bomb,
                rocket: randomizedData.specials.rocket,
            },
            time: randomizedData.time,
        }

        return cache[level]
    }
}

@ccclass('LevelManager')
class LevelManager extends Component {
    private levelData!: ILevelData

    private readonly getRandomizedLevelData = createCachedLevelDataRandomizer()

    public async loadLevel(level = 1) {
        const levelData = await this.loadLevelData(level)
        this.levelData = levelData ?? this.levelData ?? DEFAULT_LEVEL_DATA

        monitorError?.addMetadata({
            level,
        })
    }

    public async loadLevelData(level = 1): Promise<ILevelData | null> {
        let levelData: ILevelData | null = null

        if (level >= TotalLevel) {
            levelData = this.getRandomizedLevelData(level)
        } else {
            const levelPerFile = 10
            const levelStr = this.paddingZero(`${Math.floor(level / levelPerFile) + 1}`, 3)
            const levelIndex = level % levelPerFile

            const levelsData = await ResourceUtils.loadJsonAsync(`${ResourcePaths.LEVEL_DATA_FOLDER}/${levelStr}`)

            if (!levelsData) return null
            if (levelsData.json === null) return null

            levelData = levelsData.json[levelIndex] as ILevelData
        }

        levelData = J.clone(levelData)
        if (!levelData) return null

        // ? Add custom data to the level in single mode
        if (MatchUtils.getMatchMode() === GameModes.CUSTOM) {
            const custom = CustomModeUtils.getCustomData()
            const time = levelData.time * (1 + (CustomGameData.time[custom.time] ?? 0) / 100) // Percent
            const bomb = levelData.specials.bomb + (CustomGameData.bombs[custom.bombs] ?? 0)
            const colors = Math.min(levelData.colors + (DIFFICULTY_COLORS[custom.difficulty] ?? 0), 30)

            // ? Add custom difficulty to the level
            let difficulty = this.getDifficulty(levelData.columns, levelData.rows)
            if (difficulty !== undefined) {
                difficulty += custom.difficulty - 1
                const [columns, rows] = this.getCustomColumnAndRow(difficulty) ?? [levelData.columns, levelData.rows]

                levelData.rows = rows
                levelData.columns = columns
            }

            levelData.time = time
            levelData.specials.bomb = bomb
            levelData.colors = colors
        }

        /* // ? When ad service degraded, need make level easier
        if (ads.isServiceDegraded()) {
            levelData.time *= 1.5
        } */

        return levelData
    }

    public getLevelData(): ILevelData {
        return this.levelData
    }

    private getCustomColumnAndRow(difficulty: number): [number, number] | undefined {
        return DIFFICULTY_COLUMN_AND_ROW[math.clamp(difficulty, 0, DIFFICULTY_COLUMN_AND_ROW.length - 1)]
    }

    private getDifficulty(columns: number, rows: number): number | undefined {
        const difficulty = A.searchIndex(DIFFICULTY_COLUMN_AND_ROW, ([c, r]) => c === columns && r === rows)
        return difficulty === -1 ? undefined : difficulty
    }

    private paddingZero(str: string, length: number): string {
        let ret = ''

        for (let i = 0; i < length - str.length; i++) {
            ret += '0'
        }

        return ret + str
    }
}

export default LevelManager
