import Grid from '../../grid'
import type Tile from '../../tile'

function getUncleared(grid: Grid<Tile>): Tile[]
function getUncleared(tiles: ReadonlyArray<Tile>): Tile[]

function getUncleared(gridOrTiles: Grid<Tile> | ReadonlyArray<Tile>): Tile[] {
    const arr = gridOrTiles instanceof Grid ? gridOrTiles.asArray() : gridOrTiles
    return arr.filter((tile) => !tile.isHidden())
}

export default getUncleared
