import { Component, Widget, _decorator, find } from 'cc'
const { ccclass } = _decorator

@ccclass('WidgetCanvasTarget')
export class WidgetCanvasTarget extends Component {
    public __preload(): void {
        const widget = this.getComponent(Widget)
        if (!widget) return

        const canvas = find('Canvas')
        if (!canvas) return

        widget.target = canvas
    }
}
