import Grid from '../../grid'
import type Tile from '../../tile'

const { Array: A } = Ludex.Utils

export default function createGridFromFen<T>({
    customFen,
    identities,
    createTile,
}: {
    customFen: LevelFen
    identities: ReadonlyArray<T>
    createTile: (id: T) => Tile
}) {
    const { idSize, cols, rows, code } = customFen

    if (identities.length < customFen.idSize) {
        throw new Error(
            `Invalid grid code. There are not enough identities/frames ${identities.length} to fill it according to fen code ${idSize}`,
        )
    }

    /**
     * @example `t0t1s2s2:t2t2s0t3:t4t1t5t4:t6..t6:t3..t5`
     *  */
    const fenRows = code.split(':')

    if (fenRows.length !== rows) {
        throw new Error('Invalid grid code')
    }

    const rowRegex = /[a-zA-Z]+\d+|./g
    const tileTypeRegex = /[a-zA-Z]+/
    const tileIdentityRegex = /\d+/

    // ? reduceRight to reverse the order of the rows
    // because the grid was created reversed for readability
    const tiles = fenRows.reduceRight((acc, row) => {
        const rowTiles = []
        const matches = row.matchAll(rowRegex)

        for (const match of matches) {
            const tileString = match[0]

            const itype = tileIdentityRegex.exec(tileString)?.[0]

            const itypeInt = itype !== undefined ? parseInt(itype, 10) : undefined

            const frame =
                itypeInt !== undefined && !isNaN(itypeInt) && itypeInt < identities.length
                    ? identities[itypeInt]
                    : undefined

            const tile = frame !== undefined ? createTile(frame) : createTile(identities[0] as T)

            const ttype = tileTypeRegex.exec(tileString)?.[0]

            const special = ttype !== undefined ? getSpecialTypes(ttype, customFen.encoding) : undefined

            if (special !== undefined && frame !== undefined) {
                tile.setSpecialType(special)
            } else {
                tile.setEmpty()
            }

            rowTiles.push(tile)
        }

        return acc.concat(rowTiles)
    }, [] as Tile[])

    return new Grid({
        width: cols,
        height: rows,
        content: tiles,
    })
}

function getSpecialTypes(characters: string, encoding: TileEncoding): TileSpecialType {
    const specialType = A.search(Object.entries(encoding), ([_, value]) => value === characters)

    if (specialType === undefined) {
        throw new Error(`Invalid special type ${characters}`)
    }

    return specialType[0] as TileSpecialType
}
