import { _decorator, Button, Node as CCNode, Component, Node, tween, Vec3 } from 'cc'
import GameplayEvents from '../../gameplay/constants/GameEvents'
import AdsUtils from '../../utils/AdsUtils'
import AnimUtils from '../../utils/AnimUtils'
import ComponentUtils from '../../utils/ComponentUtils'
import DailyRewardsUtils from '../../utils/DailyRewardsUtils'
import ScreenUtils from '../../utils/ScreenUtils'
import { BaseScreen } from '../BaseScreen'
import { DayBlock } from './components/DayBlock'

const { ccclass, property, requireComponent } = _decorator

const {
    Events,
    Utils: { Array: A, Signal: S },
    Plugins: { Ads: { Types: AdsTypes } = {} },
} = Ludex

const { event, player } = codex

type DailyReward = Ludex.Plugins.DailyRewards.DailyReward
type Signal = InstanceType<typeof S.Signal>

@ccclass('DailyRewardsScreen')
@requireComponent(BaseScreen)
export class DailyRewardsScreen extends Component {
    private rewardsData: DailyReward[] = []
    private currentRewardsData: DailyReward | undefined

    @ComponentUtils.required()
    @property(CCNode)
    private readonly loadingRing!: CCNode

    @ComponentUtils.required()
    @property(Button)
    private readonly claimButton!: Button

    @ComponentUtils.required()
    @property(Button)
    private readonly claimX2Button!: Button

    @ComponentUtils.required()
    @property(Button)
    private readonly closeButton!: Button

    @ComponentUtils.required()
    @property(Button)
    private readonly backgroundButton!: Button

    @ComponentUtils.required()
    @property(Button)
    private readonly continueButton!: Button

    @ComponentUtils.required()
    @property(Node)
    private readonly claimButtonScallableNode!: Node

    @ComponentUtils.required()
    @property(Node)
    private readonly claimX2ButtonScallableNode!: Node

    @ComponentUtils.required()
    @property(Node)
    private readonly continueButtonScallableNode!: Node

    @property([DayBlock])
    private readonly dailyBlocks: DayBlock[] = []

    private readonly dailyLoadingController = new S.SignalController()

    public __preload() {
        this.enabled = false
        ComponentUtils.validateRequiredProperties(this, DailyRewardsScreen)
        this.listenScreenEvents()

        if (AdsTypes) {
            AdsUtils.preloadAdsByType(AdsTypes.REWARDED_VIDEO)
        }
    }

    protected onDestroy(): void {
        this.unListenScreenEvents()
    }

    private listenScreenEvents() {
        // ? Here's a demo of all the events a screen has
        event.on(Events.OPENING_SCREEN, this.handleOpeningScreen)
        event.on(Events.OPENED_SCREEN, this.handleOpenedScreen)
    }

    private unListenScreenEvents() {
        event.off(Events.OPENING_SCREEN, this.handleOpeningScreen)
        event.off(Events.OPENED_SCREEN, this.handleOpenedScreen)
    }

    private readonly handleOpeningScreen = (payload: SwitchScreenPayload) => {
        if (!this.validateEventPayload(payload)) return
        this.enabled = true
        this.setLoading()
    }

    private readonly handleOpenedScreen = async (payload: SwitchScreenPayload) => {
        if (!this.validateEventPayload(payload)) return
        this.open()
    }

    private setLoading() {
        this.loadingRing.active = true

        for (const block of this.dailyBlocks) {
            block.uiOpacity.opacity = 0
        }

        this.continueButtonScallableNode.active = false
        this.claimButtonScallableNode.active = false
        this.claimX2ButtonScallableNode.active = false
    }

    private validateEventPayload(payload: SwitchScreenPayload) {
        const { screenName } = payload ?? {}
        if (screenName !== this.node.name) return false
        return true
    }

    public open() {
        this.setInteractableButtons(true)
        this.node.scene.emit(GameplayEvents.HIDE_UI)
        this.openAsync()
    }

    private async openAsync() {
        const signal = this.dailyLoadingController.renew()
        const shouldPlayAnim = !DailyRewardsUtils.hasShownPopup()

        const rewardsData = await DailyRewardsUtils.getRewardsData()
        if (rewardsData === null) {
            this.close()
            return
        }

        const currentRewardsData = A.search(rewardsData, (reward) => reward.status === 'ready')
        this.updateScreenData(rewardsData, currentRewardsData)
        if (shouldPlayAnim) {
            await this.runPopUpAnim(signal)
        } else {
            this.showElements()
        }
    }

    @ComponentUtils.onlyRunInLifeCycle
    private updateScreenData(rewards: DailyReward[], currentReward: DailyReward | undefined): void {
        this.rewardsData = rewards
        this.currentRewardsData = currentReward

        for (let i = 0, len = this.dailyBlocks.length; i < len; i++) {
            const rewardData = this.rewardsData[i] as DailyReward
            const block = this.dailyBlocks[i] as DayBlock
            const { id: day, type, value, status } = rewardData
            block.updateInfo({ day, type, value, status })
        }
    }

    public close() {
        DailyRewardsUtils.setHasShownPopup()
        this.setInteractableButtons(false)
        this.dailyLoadingController.abort()
        event.emit(Events.CLOSE_SCREEN, { screenName: this.node.name })
        this.node.scene.emit(GameplayEvents.SHOW_UI)
    }

    @ComponentUtils.onlyRunInLifeCycleAsync
    private async runPopUpAnim(signal: Signal): Promise<void> {
        this.loadingRing.active = false
        await Promise.all(this.dailyBlocks.map((block, index) => this.runBlockPopUpAnim(block, index * 0.15, signal)))
        await this.runButtonPopUpAnim(signal)
    }

    @ComponentUtils.onlyRunInLifeCycle
    private showElements(): void {
        this.loadingRing.active = false

        for (const block of this.dailyBlocks) {
            block.uiOpacity.opacity = 255
            block.node.setScale(1, 1, 1)
        }

        if (this.currentRewardsData === undefined) {
            this.continueButtonScallableNode.active = true
            this.claimButtonScallableNode.active = false
            this.claimX2ButtonScallableNode.active = false
            this.continueButtonScallableNode.setScale(1, 1, 1)
        } else {
            this.continueButtonScallableNode.active = false
            this.claimButtonScallableNode.active = true
            this.claimX2ButtonScallableNode.active = true
            this.claimButtonScallableNode.setScale(1, 1, 1)
            this.claimX2ButtonScallableNode.setScale(1, 1, 1)
        }
    }

    @ComponentUtils.onlyRunInLifeCycleAsync
    private async runButtonPopUpAnim(signal?: Signal): Promise<void> {
        if (this.currentRewardsData === undefined) {
            this.continueButtonScallableNode.active = true
            this.claimButtonScallableNode.active = false
            this.claimX2ButtonScallableNode.active = false
            this.continueButtonScallableNode.setScale(0, 0, 0)
            await AnimUtils.runTweenAsync(
                tween(this.continueButtonScallableNode).to(0.3, { scale: Vec3.ONE }, { easing: 'backOut' }),
                signal,
            )
            return
        }

        this.continueButtonScallableNode.active = false
        this.claimButtonScallableNode.active = true
        this.claimX2ButtonScallableNode.active = true

        this.claimButtonScallableNode.setScale(0, 0, 0)
        this.claimX2ButtonScallableNode.setScale(0, 0, 0)

        await Promise.all([
            AnimUtils.runTweenAsync(
                tween(this.claimButtonScallableNode).to(0.3, { scale: Vec3.ONE }, { easing: 'backOut' }),
                signal,
            ),
            AnimUtils.runTweenAsync(
                tween(this.claimX2ButtonScallableNode).delay(0.1).to(0.3, { scale: Vec3.ONE }, { easing: 'backOut' }),
                signal,
            ),
        ])
    }

    @ComponentUtils.onlyRunInLifeCycleAsync
    private async runBlockPopUpAnim(dayBlock: DayBlock, delay: number, signal?: Signal): Promise<void> {
        dayBlock.node.setScale(1.2, 1.2, 1.2)
        dayBlock.uiOpacity.opacity = 0

        const opacity = AnimUtils.runTweenAsync(
            tween(dayBlock.uiOpacity).delay(delay).to(0.3, { opacity: 255 }, { easing: 'quadOut' }),
            signal,
        )

        const scale = AnimUtils.runTweenAsync(
            tween(dayBlock.node).delay(delay).to(0.3, { scale: Vec3.ONE }, { easing: 'backOut' }),
            signal,
        )

        await Promise.all([opacity, scale])
    }

    @ComponentUtils.onlyRunInLifeCycle
    public handleOnClaim(): void {
        if (this.currentRewardsData === undefined) {
            ScreenUtils.showNotificationScreenWithLocalizedKey('error.rewardFail', 2)
            return
        }

        this.setInteractableButtons(false)
        this.claimReward(this.currentRewardsData)
    }

    public async handleOnClaimX2(): Promise<void> {
        if (this.currentRewardsData === undefined) {
            ScreenUtils.showNotificationScreenWithLocalizedKey('error.rewardFail', 2)
            return
        }

        this.setInteractableButtons(false)

        const success = await AdsUtils.showRewardVideoAdAsync()

        if (!success) {
            this.setInteractableButtons(true)
            return
        }

        const x2Reward = true
        this.claimReward(this.currentRewardsData, x2Reward)
    }

    @ComponentUtils.onlyRunInLifeCycle
    private claimReward(rewardData: DailyReward, x2: boolean = false): void {
        DailyRewardsUtils.logLoginReward()

        const coinValue = rewardData.value * (x2 ? 2 : 1)

        this.node.scene.emit(GameplayEvents.ADD_COINS, {
            coins: coinValue,
            collectionAnimation: {
                animateTextOnly: false,
            },
        } as CoinDataPayload)

        const gameData = player.getGameData()

        if (rewardData.items && gameData) {
            const hintValue = rewardData.items.hint * (x2 ? 2 : 1)
            const thunderValue = rewardData.items.thunder * (x2 ? 2 : 1)
            const stormValue = rewardData.items.storm * (x2 ? 2 : 1)

            player.setGameData({
                items: {
                    hint: gameData.items.hint + hintValue,
                    thunder: gameData.items.thunder + thunderValue,
                    storm: gameData.items.storm + stormValue,
                    pliers: gameData.items.pliers,
                },
            })
        }

        this.close()
    }

    @ComponentUtils.onlyRunInLifeCycle
    private setInteractableButtons(interactable: boolean): void {
        this.claimButton.interactable = interactable
        this.claimX2Button.interactable = interactable
        this.closeButton.interactable = interactable
        this.backgroundButton.interactable = interactable
        this.continueButton.interactable = interactable
    }
}
