import { Direction } from '../../../../constants/Directions'

const {
    Utils: { Object: O },
} = Ludex

type Tile = {
    getCoord(): { x: number; y: number }
}

type Grid<T> = {
    get(x: number, y: number): T
    isInBounds(x: number, y: number): boolean
}

export default function getNearbyTiles<T extends Tile>(grid: Grid<T>, tile: T): T[] {
    const { x: tileX, y: tileY } = tile.getCoord()
    const directions = O.vals(Direction) as DirectionType[]

    return directions.reduce((acc, { x, y }) => {
        const [xCoord, yCoord] = [tileX + x, tileY + y]

        if (grid.isInBounds(xCoord, yCoord)) {
            acc.push(grid.get(xCoord, yCoord))
        }

        return acc
    }, [] as T[])
}
