import { _decorator, Component, Label, UITransform, Widget } from 'cc'
import { OpacityToggler } from '../../components/common/OpacityToggler'
import { Localize } from '../../components/text/Localize'
import ComponentUtils from '../../utils/ComponentUtils'

const { ccclass, property } = _decorator

type Key = Ludex.Plugins.Language.TextKey

@ccclass('GuideTextBox')
class GuideTextBox extends Component {
    @ComponentUtils.required()
    @property(Label)
    readonly guideText!: Label

    @ComponentUtils.required()
    @property(Widget)
    readonly widget!: Widget

    @ComponentUtils.required()
    @property(UITransform)
    readonly uiTransform!: UITransform

    @ComponentUtils.required()
    @property(OpacityToggler)
    readonly opacityToggler!: OpacityToggler

    @ComponentUtils.required()
    @property(Localize)
    readonly localizer!: Localize

    protected __preload(): void {
        ComponentUtils.validateRequiredProperties(this)
    }

    public setLocalizedText(key: Key): void {
        this.localizer.key = key
    }

    public async show(): Promise<void> {
        await this.opacityToggler.changeStateFade(true)
    }

    public async hide(): Promise<void> {
        await this.opacityToggler.changeStateFade(false)
    }
}

export default GuideTextBox
