import {
    _decorator,
    CCFloat,
    CCInteger,
    Component,
    game,
    instantiate,
    NodeEventType,
    Prefab,
    ScrollView,
    UITransform,
    Vec2,
    Vec3,
} from 'cc'
import GameEvents from '../../../constants/GameEvents'
import AnimUtils from '../../../utils/AnimUtils'
import ComponentUtils from '../../../utils/ComponentUtils'
import type AnimAbortSignal from '../../../utils/common/AnimAbortSignal'
import { SmoothScrollView } from './SmoothScrollView'
import { ViewNode } from './ViewNode'

const { ccclass, property, requireComponent } = _decorator

const { Array: A, Signal: S } = Ludex.Utils

@ccclass('ScrollViewLayout')
@requireComponent(SmoothScrollView)
export class ScrollViewLayout extends Component {
    @ComponentUtils.required()
    @property(UITransform)
    public viewport!: UITransform

    @property(CCFloat)
    public spacing: number = 0

    @property(Prefab)
    private readonly nodePrefab!: Prefab

    @property(CCInteger)
    private maxNodeCount: number = 0

    @ComponentUtils.required()
    @property(CCInteger)
    private readonly extendCount: number = 0

    @ComponentUtils.required()
    public scrollView!: SmoothScrollView

    @ComponentUtils.required()
    private uiTransform!: UITransform

    @ComponentUtils.required()
    private contentUiTransform!: UITransform

    private readonly nodePool: ViewNode[] = []

    public nodeHeight: number = 0

    private readonly scrollSignalController = new S.SignalController()

    private onNodeVisible: (node: ViewNode) => void = () => {}
    private onNodeInvisible: (node: ViewNode) => void = () => {}

    public __preload(): void {
        this.scrollView = this.getComponent(SmoothScrollView) as SmoothScrollView
        this.uiTransform = this.getComponent(UITransform) as UITransform
        this.contentUiTransform = this.scrollView.content?.getComponent(UITransform) as UITransform
    }

    protected onDestroy(): void {
        game.off(GameEvents.VIEW_RESIZED, this.handleViewResized, this)
    }

    protected start(): void {
        const prefab = instantiate(this.nodePrefab)
        this.nodeHeight = prefab.getComponent(UITransform)!.height
        prefab.destroy()
        ComponentUtils.validateRequiredProperties(this, ScrollViewLayout)
    }

    public init(onNodeVisible?: (node: ViewNode) => void, onNodeInvisible?: (node: ViewNode) => void) {
        this.createNodePool()

        this.onNodeVisible = onNodeVisible ?? this.onNodeVisible
        this.onNodeInvisible = onNodeInvisible ?? this.onNodeInvisible

        for (let i = 0; i < this.nodePool.length; i++) {
            this.nodePool[i]?.setVisible(false)
        }

        this.updateVisibility()
    }

    public updateNodeVisibility() {
        for (let i = 0; i < this.nodePool.length; i++) {
            const node = this.nodePool[i]
            if (!node) continue

            if (node.isVisible()) {
                this.onNodeVisible(node)
            } else {
                this.onNodeInvisible(node)
            }
        }
    }

    public listenEvents() {
        this.scrollView.node.on(ScrollView.EventType.SCROLLING, this.updateVisibility, this)
        this.scrollView.node.on(NodeEventType.TOUCH_MOVE, this.abortScrollSignal, this)
        this.scrollView.node.on(NodeEventType.MOUSE_WHEEL, this.abortScrollSignal, this)
        game.on(GameEvents.VIEW_RESIZED, this.handleViewResized, this)
    }

    public unlistenEvents() {
        this.scrollView.node.off(ScrollView.EventType.SCROLLING, this.updateVisibility, this)
        this.scrollView.node.off(NodeEventType.TOUCH_MOVE, this.abortScrollSignal, this)
        this.scrollView.node.off(NodeEventType.MOUSE_WHEEL, this.abortScrollSignal, this)
        game.off(GameEvents.VIEW_RESIZED, this.handleViewResized, this)
    }

    @ComponentUtils.onlyRunInLifeCycleAsync
    public async scrollToOrder(order: number, signal: AnimAbortSignal, duration = 2) {
        let scrollOffsetY = this.getNodeScrollOffset(order)
        let currentOffsetY = this.scrollView.getScrollOffset().y

        scrollOffsetY = Math.max(scrollOffsetY, 0)
        currentOffsetY = Math.max(currentOffsetY, 0)

        const offsetDiff = scrollOffsetY - currentOffsetY

        if (Math.abs(offsetDiff) < 5) return

        await this.scrollToOffset(new Vec2(0, scrollOffsetY), duration, signal)
    }

    @ComponentUtils.onlyRunInLifeCycleAsync
    public async scrollToTop(signal: AnimAbortSignal, duration = 2) {
        await this.scrollToOffset(new Vec2(0, 0), duration, signal)
    }

    @ComponentUtils.onlyRunInLifeCycleAsync
    public async scrollToOffset(offset: Vec2, duration: number, signal: AnimAbortSignal): Promise<void> {
        const scrollSignal = this.scrollSignalController.renew()

        this.scrollView.scrollToOffset(offset, duration)

        const onAborted = () => this.handleScrollSignalAborted()

        const onAbortedWithOffset = () => {
            if (scrollSignal.aborted) return
            this.handleExternalSignalAborted(offset)
        }

        scrollSignal.once(onAborted)
        signal.addAbortListener(onAbortedWithOffset)
        await AnimUtils.waitTweenAsync(duration, signal)
        scrollSignal.off(onAborted)
        signal.removeAbortListener(onAbortedWithOffset)
    }

    @ComponentUtils.onlyRunInLifeCycle
    public updateVisibility(): void {
        const { yMax: top, yMin: bot } = this.viewport.getBoundingBoxToWorld()

        for (let i = 0; i < this.maxNodeCount; i++) {
            const { top: topNode, bot: botNode } = this.getNodeBoundingBoxWorld(i)
            const isOffScreen = topNode < bot || botNode > top

            if (!isOffScreen) {
                const node = this.getNodeByOrder(i)
                if (!node) {
                    const invisibleNode = this.getInvisibleNode()
                    if (invisibleNode) {
                        invisibleNode.node.setPosition(this.getNodePosition(i))
                        invisibleNode.order = i
                        this.onNodeVisible(invisibleNode)
                        invisibleNode.setVisible(true)
                    }
                } else if (!node.isVisible()) {
                    node.order = i
                    this.onNodeVisible(node)
                    node.setVisible(true)
                }
            } else {
                const node = this.getNodeByOrder(i)
                if (node) {
                    this.onNodeInvisible(node)
                    node.setVisible(false)
                }
            }
        }
    }

    public updateLayout(): void {
        const nodes = this.getVisibleNodes()
        for (const node of nodes) {
            node.node.setPosition(this.getNodePosition(node.order))
        }
    }

    public setMaxNodeCount(count: number): void {
        this.maxNodeCount = count
        this.contentUiTransform.height = (this.nodeHeight + this.spacing) * this.maxNodeCount
    }

    public getAllNodes(): ViewNode[] {
        return this.nodePool
    }

    public getVisibleNodes(): ViewNode[] {
        return this.nodePool.filter((node) => node.isVisible())
    }

    public getNodePosition(order: number): Vec3 {
        return new Vec3(0, -(this.nodeHeight + this.spacing) * order - this.nodeHeight / 2, 0)
    }

    public getNodeScrollOffset(order: number): number {
        const { y } = this.getNodePosition(order)
        return (
            -y -
            this.uiTransform.node.worldPosition.y -
            this.uiTransform.height / 2 +
            this.viewport.node.worldPosition.y
        )
    }

    public getNodeByOrder(order: number): ViewNode | undefined {
        return A.search(this.nodePool, (node) => node.order === order)
    }

    private getInvisibleNode(): ViewNode | undefined {
        return A.search(this.nodePool, (node) => !node.isVisible())
    }

    @ComponentUtils.onlyRunInLifeCycle
    private createNodePool(): void {
        const initCount = Math.ceil(this.viewport.height / this.nodeHeight) + 1 + this.extendCount
        if (this.nodePool.length >= initCount) return

        const nodesToCreate = initCount - this.nodePool.length

        this.setMaxNodeCount(this.maxNodeCount)

        for (let i = 0; i < nodesToCreate; i++) {
            const prefab = instantiate(this.nodePrefab)
            this.scrollView.content?.addChild(prefab)
            prefab.setPosition(this.getNodePosition(this.nodePool.length))

            const node = prefab.addComponent(ViewNode)
            node.order = this.nodePool.length
            node.setVisible(false)

            this.nodePool.push(node)
        }
    }

    private getNodeBoundingBoxWorld(index: number) {
        const nodeWorldPosition = this.getNodePosition(index).y + (this.scrollView.content?.worldPosition.y ?? 0)
        const top = nodeWorldPosition + this.nodeHeight / 2
        const bot = nodeWorldPosition - this.nodeHeight / 2
        return { top, bot }
    }

    private abortScrollSignal() {
        this.scrollSignalController.abort()
    }

    @ComponentUtils.onlyRunInLifeCycle
    private handleScrollSignalAborted() {
        this.scrollView.stopAutoScroll()
        this.updateVisibility()
    }

    @ComponentUtils.onlyRunInLifeCycle
    private handleExternalSignalAborted(offset: Vec2) {
        this.scrollView.stopAutoScroll()
        this.scrollView.scrollToOffset(offset, 0)
        this.updateVisibility()
    }

    private async handleViewResized(): Promise<void> {
        await AnimUtils.waitTweenAsync(0)
        this.createNodePool()
        this.updateVisibility()
    }
}
