import { MaxBoardScale } from '../../constants/BoardScale'

export default function calcBoardScale(
    gridSize: Readonly<[number, number]>,
    tileSize: Readonly<[number, number]>,
    screenSize: Readonly<[number, number]>,
): number {
    const [gridW, gridH] = gridSize
    const [tileW, tileH] = tileSize
    const [uiW, uiH] = screenSize
    const [boardScaleX, boardScaleY] = [uiW / (gridW * tileW), uiH / (gridH * tileH)]
    return Math.min(Math.min(boardScaleX, boardScaleY), MaxBoardScale)
}
