import ComponentUtils from 'db://assets/utils/ComponentUtils'
import { type Node, UIOpacity, Vec3, _decorator, tween } from 'cc'
import AnimUtils from '../../../../utils/AnimUtils'
import { Line } from '../Line'
import { PathDrawer } from '../PathDrawer'

const { ccclass } = _decorator

@ccclass('MatchDrawer')
export class MatchDrawer extends PathDrawer {
    public drawPath(path: Path): void {
        if (path.length === 0) return

        const last = path.length - 1
        if (last < 0) return

        const lastLine = path[last]
        if (!lastLine) return

        path.forEach((line, index) => {
            const lineNode = this.linePool.alloc()
            lineNode.getComponent(Line)?.setPoints(line.a, line.b)
            this.animateLine(lineNode)

            const pointNode = this.pointPool.alloc()
            pointNode.setWorldPosition(line.a.x, line.a.y, 0)
            this.animatePoint(pointNode, index)
        })

        const pointNode = this.pointPool.alloc()
        pointNode.setWorldPosition(lastLine.b.x, lastLine.b.y, 0)
        this.animatePoint(pointNode, path.length)
    }

    private async animateLine(lineNode: Node): Promise<void> {
        const lineOpacity = lineNode.getComponent(UIOpacity)!
        lineOpacity.opacity = 0

        const lineTween = tween(lineOpacity)
            .to(0.25, { opacity: 255 }, { easing: 'quadOut' })
            .delay(0.1)
            .to(0.2, { opacity: 0 })

        await AnimUtils.runTweenAsync(lineTween)

        lineOpacity.opacity = 255
        this.linePool.free(lineNode)
    }

    @ComponentUtils.onlyRunInLifeCycleAsync
    private async animatePoint(pointNode: Node, index: number): Promise<void> {
        pointNode.scale = Vec3.ZERO
        const pointTween = tween(pointNode)
            .delay(0.03 * index)
            .to(0.2, { scale: Vec3.ONE }, { easing: 'quadOut' })
            .delay(0.1)
            .to(0.2, { scale: Vec3.ZERO }, { easing: 'backIn' })

        await AnimUtils.runTweenAsync(pointTween)
        if (!this.node.activeInHierarchy) return

        this.pointPool.free(pointNode)
    }

    public clearPath(): void {
        this.lineNodes = []
        this.pointNodes = []
    }
}
