import { _decorator, Button, CCInteger, Node as CCNode, Component, Label, tween, UIOpacity, Vec3 } from 'cc'
import { Vibrate } from '../../../components/vibrate/Vibrate'
import { ScreenKeysEnum } from '../../../constants/ScreenKeys'
import type { StatusConfig } from '../../../scenes/game-scene/components/ui/GameSceneLayout'
import AnimUtils from '../../../utils/AnimUtils'
import ComponentUtils from '../../../utils/ComponentUtils'
import AnimAbortSignal from '../../../utils/common/AnimAbortSignal'
import ScreenUtils from '../../../utils/ScreenUtils'
import GameplayEvents from '../../constants/GameEvents'
import ItemKeys from '../../constants/ItemKeys'

const { ccclass, property } = _decorator

const RequestItemLocalizedKeys = {
    [ItemKeys.HINT]: 'item.hintDesc',
    [ItemKeys.PLIERS]: 'item.pliersDesc',
    [ItemKeys.STORM]: 'item.stormDesc',
    [ItemKeys.THUNDER]: 'item.thunderDesc',
} as const satisfies Record<ItemKey, Ludex.Plugins.Language.TextKey>

@ccclass('SpecialItem')
export class SpecialItem extends Component {
    @property({
        editorOnly: true,
        tooltip: 'A debug button has infinite quantity',
    })
    protected isDevButton = false

    @ComponentUtils.required()
    @property({ type: Button, tooltip: 'The button that activates the item' })
    readonly button!: Button

    @property({
        type: CCInteger,
        group: 'Quantity',
        tooltip: 'The starting quantity of the item',
        displayName: 'Quantity',
    })
    private quantity: number = 0

    @ComponentUtils.required()
    @property({
        type: Label,
        group: 'Quantity',
        tooltip: 'The label that shows the quantity',
        displayName: 'Label',
    })
    protected quantityLabel!: Label

    @ComponentUtils.required()
    @property({
        type: CCNode,
        group: 'Quantity',
        tooltip: 'The node that shows the quantity of the item',
        displayName: 'Quantity node',
    })
    protected quantityNode!: CCNode

    @ComponentUtils.required()
    @property({
        type: CCNode,
        group: 'Quantity',
        tooltip: 'The node that shows the plus icon when there is no item',
        displayName: 'Plus node',
    })
    protected plusNode!: CCNode

    @ComponentUtils.required()
    @property({
        type: UIOpacity,
        group: 'Animation',
    })
    readonly glowUIOpacity!: UIOpacity

    public get itemType(): ItemKey | undefined {
        return undefined
    }

    @ComponentUtils.required()
    @property({
        type: UIOpacity,
        tooltip: 'The opacity component of the button',
        displayName: 'Button UI Opacity',
    })
    readonly uiOpacity!: UIOpacity

    private signal?: AnimAbortSignal

    private vibrateComp: Vibrate | null = null

    protected onLoad(): void {
        ComponentUtils.validateRequiredProperties(this, SpecialItem)
    }

    public updateStatus(config: StatusConfig): void {
        if (this.itemType === undefined) return

        const itemConfig = config.items?.[this.itemType]
        if (!itemConfig) return

        this.setActiveStatus(itemConfig.canActivate)
        this.setQuantity(itemConfig.quantity, itemConfig.animate)
    }

    private setActiveStatus(isActive: boolean): void {
        this.signal?.abort()
        const signal = new AnimAbortSignal()
        this.signal = signal

        this.button.interactable = isActive
        AnimUtils.runTweenAsync(tween(this.uiOpacity).to(0.2, { opacity: isActive ? 255 : 128 }), signal)
    }

    public async activate(): Promise<void> {
        if (this.quantity <= 0) {
            this.requestItem()
            return
        }

        this.vibrate()
        this.node.scene.emit(GameplayEvents.USE_ITEM, this.itemType)
    }

    public setQuantity(quantity: number, animate = false): void {
        let q = quantity
        if (this.isDevButton) {
            this.quantityLabel.string = 'i'
            this.quantityNode.active = true
            this.plusNode.active = false

            return
        }

        q = Math.max(0, q)

        this.quantity = q
        this.quantityLabel.string = `${this.quantity}`

        if (q <= 0) {
            this.quantityNode.active = false
            this.plusNode.active = true
        } else {
            this.quantityNode.active = true
            this.plusNode.active = false
        }

        if (animate) {
            this.animateAddItem()
        }
    }

    public requestItem(): void {
        if (this.itemType === undefined) {
            console.warn('Item type is not defined')
            return
        }

        ScreenUtils.openScreen(ScreenKeysEnum.GET_ITEM_SCREEN, {
            item: this.itemType,
            localizedMessageKey: RequestItemLocalizedKeys[this.itemType],
            quantity: 1,
        } as GetItemScreenData)
    }

    private vibrate(): void {
        this.vibrateComp = this.vibrateComp ?? this.node.getComponent(Vibrate)
        this.vibrateComp?.vibrate(50)
    }

    protected async animateAddItem() {
        this.glowUIOpacity.opacity = 0

        return Promise.all([
            AnimUtils.runTweenAsync(
                tween(this.node)
                    .to(0.1, { scale: new Vec3(1.15, 1.15, 1.15) }, { easing: 'cubicOut' })
                    .delay(0.2)
                    .to(0.7, { scale: Vec3.ONE }, { easing: 'elasticOut' }),
            ),
            AnimUtils.runTweenAsync(
                tween(this.glowUIOpacity).to(0.1, { opacity: 100 }).delay(0.2).to(0.6, { opacity: 0 }),
            ),
        ])
    }
}
