import DashboardEvents from 'db://assets/constants/DashboardEvents'
import { CustomModeLevel } from 'db://assets/gameplay/constants/CustomGameData'
import { _decorator, Button, Component, Node, Sprite, SpriteFrame } from 'cc'
import { ScreenKeysEnum } from '../../../../../../constants/ScreenKeys'
import ScreenUtils from '../../../../../../utils/ScreenUtils'

const { ccclass, property } = _decorator

@ccclass('CustomButton')
export class CustomButton extends Component {
    @property(Sprite)
    private sprite!: Sprite

    @property(SpriteFrame)
    private lockedSprite!: SpriteFrame

    @property(SpriteFrame)
    private unlockedSprite!: SpriteFrame

    @property(Button)
    private button!: Button

    @property(Node)
    private unlockContainer!: Node

    public set interactable(value: boolean) {
        this.button.interactable = value
    }

    public updateStatus(level: number): void {
        const active = level >= CustomModeLevel

        this.button.interactable = active
        this.unlockContainer.active = !active

        this.sprite.spriteFrame = active ? this.unlockedSprite : this.lockedSprite
    }

    public async handleOnClicked(): Promise<void> {
        const screenPayload = await ScreenUtils.openScreenAndWaitForClose(ScreenKeysEnum.CUSTOM_GAME_SCREEN)
        const { customMode } = screenPayload.screenData ?? {}

        if (customMode) this.node.scene.emit(DashboardEvents.CUSTOM_PLAY_BUTTON_PRESS)
    }
}
