import { CCFloat, Component, Vec2, Vec3, _decorator, tween } from 'cc'
import { OpacityToggler } from '../../../../../components/common/OpacityToggler'
import { PanelAnimator } from '../../../../../components/panel-animator/PanelAnimator'
import GameplayEvents from '../../../../../gameplay/constants/GameEvents'
import ComponentUtils from '../../../../../utils/ComponentUtils'
import type AnimAbortSignal from '../../../../../utils/common/AnimAbortSignal'
import ThemeUtils from '../../../../../utils/ThemeUtils'
import { ScrollViewLayout } from '../../../../common/scroll-view-layout/ScrollViewLayout'
import type { ViewNode } from '../../../../common/scroll-view-layout/ViewNode'
import { ThemeBlock } from './theme-tabs/ThemeBlock'

const { ccclass, property } = _decorator

const { storage } = codex

@ccclass('CollectionsContent')
export class CollectionsContent extends Component {
    private animateThemeBlockUnlockIndex: number | null = null

    @property(PanelAnimator)
    private panelAnimator!: PanelAnimator

    @property(ScrollViewLayout)
    private themeScrollView!: ScrollViewLayout

    @property(OpacityToggler)
    private contentOpacity!: OpacityToggler

    private themeScrollViewInited: boolean = false

    private payloads: CollectionsPayload[] = []

    private preservedScrollPosition: Vec2 = new Vec2()

    @property({
        type: CCFloat,
        displayName: 'Offset from top of scroll view',
        tooltip: 'The offset between the first theme tab and the top of the scroll view',
    })
    public offsetFromTopOfScrollView = 407.5

    public __preload(): void {
        this.validateRequiredComponents()
    }

    public listenEvents() {
        this.node.scene.on(GameplayEvents.HIDE_UI, this.hideContent)
        this.node.scene.on(GameplayEvents.SHOW_UI, this.showContent)
        this.themeScrollView.listenEvents()
    }

    public unlistenEvents() {
        this.unlistenUIEvents()
        this.themeScrollView.unlistenEvents()
    }

    public unlistenUIEvents() {
        this.node.scene.off(GameplayEvents.HIDE_UI, this.hideContent)
        this.node.scene.off(GameplayEvents.SHOW_UI, this.showContent)
    }

    public init(payloads: CollectionsPayload[]): void {
        this.payloads = payloads

        if (this.themeScrollViewInited) return
        this.themeScrollViewInited = true

        this.themeScrollView.init((node: ViewNode) => {
            const isAnimating = this.animateThemeBlockUnlockIndex === node.order
            const payload = this.payloads[node.order]
            const themeBlock = node.getComponent(ThemeBlock)!

            if (!payload) return

            themeBlock.updateInfo({
                ...payload,
                skins: payload.skins ?? [],
                isLocked: isAnimating ? true : payload.isLocked,
            })

            themeBlock.setVisible(true)

            themeBlock.container.scale = new Vec3(0.4, 0.4, 0.4)
            tween(themeBlock.container).to(0.3, { scale: Vec3.ONE }, { easing: 'quadOut' }).start()
        })
    }

    public preserveScrollPosition(): void {
        this.themeScrollView.scrollView.stopAutoScroll()
        this.preservedScrollPosition.set(this.themeScrollView.scrollView.getScrollOffset())
    }

    public restoreScrollPosition(): void {
        this.themeScrollView.scrollView.scrollToOffset(this.preservedScrollPosition, 0)
    }

    public updateUnlockedProgress(collectionsPayloads: CollectionsPayload[]): void {
        const animateUnlock = storage.getStorageData('collection', 'animateMapPackUnlock')
        const lastMapPackId = storage.getStorageData('collection', 'lastMapPack')
        collectionsPayloads.forEach((element, i) => {
            const { progress, max, isLocked } = element
            if (progress < max && !isLocked && animateUnlock && lastMapPackId !== i + 1) {
                // skip the current theme
                // it will be animated when fade-in-complete event is fired
                this.animateThemeBlockUnlockIndex = i
                storage.setStorageData('collection', 'animateMapPackUnlock', false)
                storage.setStorageData('collection', 'lastMapPack', i + 1)

                return
            }
        })
    }

    public shouldAnimateThemeBlockUnlock(): boolean {
        return this.animateThemeBlockUnlockIndex !== null
    }

    @ComponentUtils.onlyRunInLifeCycleAsync
    public async playThemeTabUnlockAnim(signal: AnimAbortSignal): Promise<void> {
        if (!this.animateThemeBlockUnlockIndex) return

        const themeNode = this.themeScrollView.getNodeByOrder(this.animateThemeBlockUnlockIndex)
        if (!themeNode || !themeNode.isVisible()) return

        const themeBlock = themeNode.getComponent(ThemeBlock)
        if (!themeBlock) return

        const payload = ThemeUtils.getCollectionUnlockProgress()[this.animateThemeBlockUnlockIndex]
        if (!payload) return

        await themeBlock.playUnlockAnim(signal, {
            ...payload,
            skins: payload.skins ?? [],
            isLocked: true,
        })

        this.animateThemeBlockUnlockIndex = null
    }

    @ComponentUtils.onlyRunInLifeCycleAsync
    public async scrollToCurrentUnlockingTheme(index: number, signal: AnimAbortSignal) {
        await this.themeScrollView.scrollToOrder(index, signal)
    }

    public enableUIAnimator(): void {
        this.panelAnimator.enabled = true
    }

    public disableUIAnimator(): void {
        this.panelAnimator.enabled = false
    }

    private validateRequiredComponents(): void {
        ComponentUtils.lazyValidateProperties(this)
    }

    private hideContent = () => {
        this.contentOpacity.changeStateFade(false, 0.1)
    }

    private showContent = () => {
        this.contentOpacity.changeStateFade(true, 0.1)
    }
}
