import {
    _decorator,
    Animation,
    Component,
    director,
    instantiate,
    math,
    Node,
    type Prefab,
    randomRange,
    SpriteFrame,
    type sp,
    tween,
    UITransform,
    Vec3,
} from 'cc'
import { OpacityToggler } from '../../components/common/OpacityToggler'
import { Vibrate } from '../../components/vibrate/Vibrate'
import { ParticleSystem } from '../../particles/ParticleSystem'

import { ItemCollectionFX } from '../../screens/get-item-screen/ItemCollectionFX'
import AnimUtils from '../../utils/AnimUtils'
import ComponentUtils from '../../utils/ComponentUtils'
import AnimAbortSignal from '../../utils/common/AnimAbortSignal'
import LayoutUtils from '../../utils/LayoutUtils'
import ResourceUtils from '../../utils/ResourceUtils'
import GameplayEvents from '../constants/GameEvents'
import ItemKeys from '../constants/ItemKeys'
import ResourcePaths from '../constants/ResourcePaths'
import type { Board, Tile } from '../game-objects/board'
import addItemAnimation from '../game-objects/item/addItemEffect'
import type { SpecialItem } from '../game-objects/item/SpecialItem'
import { StarParticles } from '../game-objects/star-particles/StarParticles'
import { AFPool } from '../utils/pool/AFPool'
import CloudEffect from './effects/cloud-effect/CloudEffect'

const {
    Utils: { Valid: V },
} = Ludex

const { ccclass, property } = _decorator
const { monitorError } = codex

@ccclass('EffectManager')
export default class EffectManager extends Component {
    @ComponentUtils.required()
    @property({ group: { name: 'Thunder Effect', style: 'section' }, type: CloudEffect })
    public readonly cloudEffect!: CloudEffect

    @ComponentUtils.required()
    @property({ group: { name: 'Mask Effect', style: 'section' }, type: OpacityToggler })
    private readonly backgroundMask!: OpacityToggler

    @ComponentUtils.required()
    @property({ group: { name: 'Mask Effect', style: 'section' }, type: Node })
    private readonly masks: Node[] = []

    @ComponentUtils.required()
    @property({ group: { name: 'Tornado Effect', style: 'section' }, type: Node })
    private readonly tornadoContainer!: Node

    @ComponentUtils.required()
    @property({
        group: { name: 'Clear Particle Effect', style: 'section' },
        type: Node,
        displayName: 'Particle holder node',
    })
    private readonly clearParticleHolder!: Node

    @ComponentUtils.required()
    @property({
        group: { name: 'Clear Particle Effect', style: 'section' },
        type: ParticleSystem,
        displayName: 'Emitter 1',
    })
    private readonly clearParticle1!: ParticleSystem

    @ComponentUtils.required()
    @property({
        group: { name: 'Clear Particle Effect', style: 'section' },
        type: ParticleSystem,
        displayName: 'Emitter 2',
    })
    private readonly clearParticle2!: ParticleSystem

    @ComponentUtils.required()
    @property({ group: { name: 'Star Particles Effect', style: 'section' }, type: StarParticles })
    private readonly starParticle!: StarParticles

    @ComponentUtils.required()
    @property({ group: { name: 'Confetti Effect', style: 'section' }, type: ParticleSystem })
    private readonly leftConfetti!: ParticleSystem

    @ComponentUtils.required()
    @property({ group: { name: 'Confetti Effect', style: 'section' }, type: ParticleSystem })
    private readonly rightConfetti!: ParticleSystem

    @property({ group: { name: 'Add Item Effect', style: 'section' }, type: ItemCollectionFX })
    private readonly itemEffect!: ItemCollectionFX

    @property({ group: { name: 'Add Item Effect', style: 'section' }, type: SpriteFrame })
    private readonly hintSpriteFrame!: SpriteFrame

    @property({ group: { name: 'Add Item Effect', style: 'section' }, type: SpriteFrame })
    private readonly thunderSpriteFrame!: SpriteFrame

    @property({ group: { name: 'Add Item Effect', style: 'section' }, type: SpriteFrame })
    private readonly tornadoSpriteFrame!: SpriteFrame

    @property({ group: { name: 'Add Item Effect', style: 'section' }, type: SpriteFrame })
    private readonly pliersSpriteFrame!: SpriteFrame

    private tornadoSpineNode!: Node

    private maskTargets: Tile[] = []

    private vibrate: Vibrate | null = null
    private gameShakeSignal = new AnimAbortSignal()

    private bombExplosionPool?: ReturnType<typeof createBombExplosionPool>

    private defuseExplosionPool?: ReturnType<typeof createDefuseExplosionPool>

    @ComponentUtils.required()
    @property({
        type: Node,
        displayName: 'Clear Explosion Container',
        group: { name: 'Clear Explosion Effect', style: 'section' },
    })
    readonly clearExplosionContainer!: Node

    private clearExplosionPool?: ReturnType<typeof createClearExplosionPool>

    private static FULL_ANIM_USED_ON_ADD_ITEM: Record<ItemKey, boolean> = {
        [ItemKeys.HINT]: false,
        [ItemKeys.THUNDER]: false,
        [ItemKeys.STORM]: false,
        [ItemKeys.PLIERS]: false,
    }

    protected __preload(): void {
        ComponentUtils.validateRequiredProperties(this, EffectManager)
    }

    protected onLoad(): void {
        this.clearParticleHolder.active = false
        this.leftConfetti.node.active = false
        this.rightConfetti.node.active = false
    }

    protected update(): void {
        if (this.maskTargets.length > 0) {
            for (let i = 0; i < this.maskTargets.length; i++) {
                const target = this.maskTargets[i] as Tile
                if (target.isHidden()) continue

                const mask = this.masks[i]
                if (!mask) continue

                mask.setWorldPosition(target.node.worldPosition)
            }
        }
    }

    protected onEnable(): void {
        this.node.scene.on(GameplayEvents.GRID_RESIZE, this.handleBoardResize, this)
    }

    protected onDisable(): void {
        this.node.scene.off(GameplayEvents.GRID_RESIZE, this.handleBoardResize, this)
    }

    private handleBoardResize(scale: number): void {
        this.clearParticleHolder.setScale(scale, scale)
        this.clearExplosionContainer.setScale(scale, scale)

        if (LayoutUtils.isLandscape()) {
            const cloudScale = math.clamp(scale, 1, 1.5)
            this.cloudEffect.node.setScale(cloudScale, cloudScale)
        } else {
            this.cloudEffect.node.setScale(1, 1)
        }
    }

    public playClearEffectAt(positions: [Vec3, Vec3]) {
        const { x: x1, y: y1 } = positions[0]
        const { x: x2, y: y2 } = positions[1]
        const { z: z1 } = this.clearParticle1.node.worldPosition
        const { z: z2 } = this.clearParticle2.node.worldPosition

        this.clearParticleHolder.active = true

        this.clearParticle1.node.setWorldPosition(x1, y1, z1)

        this.clearParticle2.node.setWorldPosition(x2, y2, z2)

        this.clearParticle1.play()
        this.clearParticle2.play()

        this.scheduleOnce(() => {
            this.clearParticleHolder.active = false
        }, this.clearParticle1.duration)
    }

    public playStarEffectAt(position: [number, number]) {
        this.starParticle.fireShiningStarAt(position)
    }

    public playConfettiEffect(): void {
        //? Fix for confetti is behind the landscape's sidebar
        this.node.parent?.addChild(this.leftConfetti.node)
        if (this.rightConfetti.node.parent !== this.node.parent) this.node.parent?.addChild(this.rightConfetti.node)

        const isLandscape = LayoutUtils.isLandscape()

        const burstCount = isLandscape ? 50 : 30
        const startSpeedMin = isLandscape ? 5000 : 2000
        const startSpeedMax = isLandscape ? 15000 : 10000
        const leftAngle = isLandscape ? 40 : 60
        const rightAngle = isLandscape ? 110 : 100
        const arc = isLandscape ? 30 : 20

        this.leftConfetti.bursts[0]!.count = this.rightConfetti.bursts[0]!.count = burstCount
        this.leftConfetti.startSpeed.constantMin = this.rightConfetti.startSpeed.constantMin = startSpeedMin
        this.leftConfetti.startSpeed.constantMax = this.rightConfetti.startSpeed.constantMax = startSpeedMax
        this.leftConfetti.shapeModule.angle = leftAngle
        this.rightConfetti.shapeModule.angle = rightAngle
        this.leftConfetti.shapeModule.arc = this.rightConfetti.shapeModule.arc = arc

        this.leftConfetti.node.active = false
        this.rightConfetti.node.active = false

        this.leftConfetti.play()
        this.rightConfetti.play()

        this.scheduleOnce(() => {
            this.leftConfetti.node.active = false
        }, this.leftConfetti.duration)

        this.scheduleOnce(() => {
            this.rightConfetti.node.active = false
        }, this.rightConfetti.duration)
    }

    public async playTornadoAnimation(): Promise<void> {
        const spine = this.tornadoSpineNode.getComponent('sp.Skeleton') as sp.Skeleton
        if (!spine) return

        return new Promise((resolve) => {
            this.tornadoSpineNode.active = true
            spine.setAnimation(0, 'tornado_alone', false)
            spine.setCompleteListener(() => {
                resolve()
                this.tornadoSpineNode.active = false
            })
        })
    }

    public async playThunderAnimation(pairs: [[Tile, Tile], [Tile, Tile]], board: Board): Promise<void> {
        this.resetMasks()

        const [[tile1, tile2], [tile3, tile4]] = pairs

        this.maskTargets = [tile1, tile2, tile3, tile4]
        this.maskTargets.forEach((tile, index) => {
            this.setMaskProperties(index, tile.node.worldPosition, tile.node.worldScale)
        })

        await this.setBackgroundMaskActive(true)

        await this.cloudEffect.playThunderAnimations({ manager: this, board, pairs })

        this.setBackgroundMaskActive(false)
    }

    public async playItemAddAnimation(worldX: number, worldY: number, item: SpecialItem): Promise<void> {
        const canvas = director.getScene()?.getChildByName('Canvas')
        if (canvas === null || canvas === undefined) return

        const { itemType } = item
        if (!itemType) return

        this.itemEffect.node.active = true
        this.itemEffect.node.setParent(canvas)
        this.itemEffect.node.setWorldPosition(worldX, worldY, this.itemEffect.node.worldPosition.z)
        this.itemEffect.node.setScale(Vec3.ONE)

        switch (itemType) {
            case ItemKeys.HINT:
                this.itemEffect.sprite.spriteFrame = this.hintSpriteFrame
                break
            case ItemKeys.THUNDER:
                this.itemEffect.sprite.spriteFrame = this.thunderSpriteFrame
                break
            case ItemKeys.STORM:
                this.itemEffect.sprite.spriteFrame = this.tornadoSpriteFrame
                break
            case ItemKeys.PLIERS:
                this.itemEffect.sprite.spriteFrame = this.pliersSpriteFrame
                break
        }

        const useQuickVersion = EffectManager.FULL_ANIM_USED_ON_ADD_ITEM[itemType]
        EffectManager.FULL_ANIM_USED_ON_ADD_ITEM[itemType] = true

        await addItemAnimation(this.itemEffect, item, useQuickVersion)

        this.itemEffect.node.active = false
    }

    public async playMultiThunderAnimation(pairs: [Tile, Tile][], board: Board, stagger?: number): Promise<void> {
        this.resetMasks()

        await this.cloudEffect.playMultiThunderAnimation({
            manager: this,
            pairs,
            board,
            stagger,
        })

        await this.setBackgroundMaskActive(false)
    }

    @ComponentUtils.onlyRunInLifeCycle
    public shakeScreen(amount = 500) {
        this.vibrate = this.vibrate ?? this.node.getComponent(Vibrate)
        this.vibrate?.vibrate(amount)
    }

    public preloadEffects(): void {
        this.loadDefuseExplosionPool()
        this.loadBombExplosionPool()
        this.loadClearExplosionPool()
        this.loadTornadoSpine()
    }

    private async loadDefuseExplosionPool(): Promise<AFPool<DefuseExplosionObject> | undefined> {
        if (this.defuseExplosionPool) return this.defuseExplosionPool

        const prefab = await ResourceUtils.loadPrefabAsync(ResourcePaths.BOMB_DEFUSE_PREFAB)
        if (!this.isValid || !this.node.activeInHierarchy) return

        if (prefab === null) {
            this.reportOrThrowInDebugger('Failed to load prefab', 'createDefuseExplosionAt')
            return
        }

        this.defuseExplosionPool = createDefuseExplosionPool(prefab, this.node)
        return this.defuseExplosionPool
    }

    private async loadBombExplosionPool(): Promise<AFPool<BombExplosionObject> | undefined> {
        if (this.bombExplosionPool) return this.bombExplosionPool

        const prefab = await ResourceUtils.loadPrefabAsync(ResourcePaths.BOMB_EXPLODE_PREFAB)
        if (!this.isValid || !this.node.activeInHierarchy) return

        if (prefab === null) {
            this.reportOrThrowInDebugger('Failed to load prefab', 'createBombExplosionAt')
            return
        }

        this.bombExplosionPool = createBombExplosionPool(prefab, this.node)
        return this.bombExplosionPool
    }

    private async loadClearExplosionPool(): Promise<AFPool<ClearExplosionObject> | undefined> {
        if (this.clearExplosionPool) return this.clearExplosionPool

        const prefab = await ResourceUtils.loadPrefabAsync(ResourcePaths.CLEAR_EXPLOSION_PREFAB)
        if (!this.isValid || !this.node.activeInHierarchy) return

        if (prefab === null) {
            this.reportOrThrowInDebugger('Failed to load prefab', 'createClearExplosionAt')
            return
        }

        this.clearExplosionPool = createClearExplosionPool(prefab, this.clearExplosionContainer)
        return this.clearExplosionPool
    }

    private async loadTornadoSpine(): Promise<void> {
        const prefab = await ResourceUtils.loadPrefabAsync(ResourcePaths.TORNADO_SPINE_PREFAB)
        if (!this.isValid || !this.node.activeInHierarchy) return

        if (prefab === null) {
            this.reportOrThrowInDebugger('Failed to load prefab', 'loadTornadoSpine')
            return
        }

        const node = instantiate(prefab)
        node.active = false
        this.tornadoContainer.addChild(node)

        this.tornadoSpineNode = node
    }

    public async createDefuseExplosionAt(x: number, y: number): Promise<void> {
        const pool = await this.loadDefuseExplosionPool()

        // ? error already reported in loadDefuseExplosionPool
        if (!pool) return

        const defuseExplosion = pool.alloc()
        const node = defuseExplosion.node
        const anim = defuseExplosion.animation

        node.setWorldPosition(x, y, 0)
        await AnimUtils.runAnimationAsync(anim)
        pool.free(defuseExplosion)
    }

    public async createBombExplosionAt(position: Vec3): Promise<void> {
        const pool = await this.loadBombExplosionPool()

        // ? error already reported in loadBombExplosionPool
        if (!pool) return

        const bombExplosion = pool.alloc()
        const node = bombExplosion.node
        const anim = bombExplosion.animation

        node.setWorldPosition(position)
        await AnimUtils.runAnimationAsync(anim)
        pool.free(bombExplosion)
    }

    public async createClearExplosionAt(position: Vec3): Promise<void> {
        const pool = await this.loadClearExplosionPool()

        // ? error already reported in loadClearExplosionPool
        if (!pool) return

        const clearExplosion = pool.alloc()
        const node = clearExplosion.node
        const anim = clearExplosion.animation

        node.setWorldPosition(position)
        node.getComponentInChildren(ParticleSystem)?.play()
        await AnimUtils.runAnimationAsync(anim)
        pool.free(clearExplosion)
    }

    public async setBackgroundMaskActive(active: boolean): Promise<void> {
        await this.backgroundMask.changeStateFade(active, 0.25)
    }

    public resetMasks(): void {
        for (const mask of this.masks) {
            mask.worldPosition = new Vec3(-9999, -9999, 0)
        }
    }

    public setMaskProperties(index: number, position: Vec3, scale?: Vec3) {
        const mask = this.masks[index]
        if (!mask) return

        if (scale) {
            mask.worldScale = scale
        }

        mask.worldPosition = position
    }

    @ComponentUtils.onlyRunInLifeCycleAsync
    public async shakeGame(): Promise<void> {
        this.gameShakeSignal.abort()
        const signal = new AnimAbortSignal()
        this.gameShakeSignal = signal

        const defaultPos = this.node.position.clone()
        const randX = randomRange(-10, 10)
        const randY = randomRange(-10, 10)

        const targetPosition = defaultPos.clone().add3f(randX, randY, 0)
        const target2Position = defaultPos.clone().add3f(-randX, -randY, 0)

        const shakeTween = tween(this.node)
            .to(0.05, {
                position: targetPosition,
            })
            .to(0.05, {
                position: target2Position,
            })
            .to(0.05, {
                position: defaultPos,
            })

        const cb = () => this.node.setPosition(defaultPos)

        signal.addAbortListener(cb)
        await AnimUtils.runTweenAsync(shakeTween, signal)
        signal.removeAbortListener(cb)
    }

    private reportOrThrowInDebugger(message = 'Unknown error thrown', from = 'Board') {
        const error = new Error(`${from}: ${message}`)
        if (V.isDebugger()) throw error
        monitorError?.sendException(error)
    }
}

type DefuseExplosionObject = {
    node: Node
    animation: Animation
}

function createDefuseExplosionPool(prefab: Prefab, parent: Node) {
    const ctor = (): DefuseExplosionObject => {
        const node = instantiate(prefab)
        const animation = node.getComponent(Animation)

        if (!animation) {
            throw new Error('DefuseExplosion: No animation component found in bombDefuseExplosionPrefab')
        }

        parent.addChild(node)

        node.active = false

        return {
            node,
            animation,
        }
    }

    const alloc = (obj: DefuseExplosionObject) => {
        obj.node.active = true
    }

    const free = (obj: DefuseExplosionObject) => {
        obj.node.active = false
    }

    const destroy = (obj: DefuseExplosionObject) => {
        obj.node.destroy()
    }

    return new AFPool(ctor, 1, alloc, free, destroy)
}

type BombExplosionObject = {
    node: Node
    animation: Animation
}

function createBombExplosionPool(prefab: Prefab, parent: Node) {
    const ctor = (): BombExplosionObject => {
        const node = instantiate(prefab)
        const animation = node.getComponent(Animation)

        if (!animation) {
            throw new Error('BombExplosion: No animation component found in bombExplosionPrefab')
        }

        parent.addChild(node)

        node.active = false

        return {
            node,
            animation,
        }
    }

    const alloc = (obj: BombExplosionObject) => {
        obj.node.active = true
    }

    const free = (obj: BombExplosionObject) => {
        obj.node.active = false
    }

    const destroy = (obj: BombExplosionObject) => {
        obj.node.destroy()
    }

    return new AFPool(ctor, 1, alloc, free, destroy)
}

type ClearExplosionObject = {
    node: Node
    uiTransform: UITransform
    animation: Animation
}

function createClearExplosionPool(prefab: Prefab, parent: Node) {
    const ctor = (): ClearExplosionObject => {
        const node = instantiate(prefab)
        const uiTransform = node.getComponent(UITransform)
        const animation = node.getComponent(Animation)

        if (!uiTransform || !animation) {
            throw new Error('ClearExplosion: Invalid prefab')
        }

        parent.addChild(node)

        node.active = false

        return {
            node,
            uiTransform,
            animation,
        }
    }

    const alloc = (obj: ClearExplosionObject) => {
        obj.node.active = true
    }

    const free = (obj: ClearExplosionObject) => {
        obj.node.active = false
    }

    const destroy = (obj: ClearExplosionObject) => {
        obj.node.destroy()
    }

    return new AFPool(ctor, 2, alloc, free, destroy)
}
