import type { IVec2Like, Rect } from 'cc'

const clampToBoundingBox =
    (box: Rect, padding: number) =>
    (position: IVec2Like): IVec2Like => ({
        x: Math.max(box.xMin + padding, Math.min(box.xMax - padding, position.x)),
        y: Math.max(box.yMin + padding, Math.min(box.yMax - padding, position.y)),
    })

export default clampToBoundingBox
