import {
    _decorator,
    Component,
    game,
    instantiate,
    math,
    Node,
    randomRange,
    type SpriteFrame,
    tween,
    UITransform,
    Vec2,
    Widget,
} from 'cc'
import GameEvents from '../../../constants/GameEvents'
import GameWorld from '../../../constants/GameWorld'
import { CodeSoundKeys } from '../../../constants/SoundKeys'
import AnimUtils from '../../../utils/AnimUtils'
import AudioUtils from '../../../utils/AudioUtils'
import ComponentUtils from '../../../utils/ComponentUtils'
import LayoutUtils from '../../../utils/LayoutUtils'
import { Direction } from '../../constants/Directions'
import GameplayEvents from '../../constants/GameEvents'
import LevelGravity from '../../constants/LevelGravity'
import { DEFAULT_LEVEL_DATA } from '../../manager/LevelManager'
import stagger from '../../utils/2d/stagger'
import Grid from './grid'
import Tile from './tile'
import calcBoardScale from './utils/calc/calcBoardScale'
import calcTilePosition from './utils/calc/calcTilePosition'
import calcTileSize from './utils/calc/calcTileSize'
import parseLevelGravity from './utils/calc/parseLevelGravity'
import rotateGravity from './utils/calc/rotateGravity'
import rotatePath from './utils/calc/rotatePath'
import createGrid, { assignSpecialTiles, createGridFromFen, createRotatedGrid, ensureOneMatch } from './utils/create'
import { getBombTiles, getCleared, getRandomPair, getRandomTiles, getUncleared } from './utils/get'
import { getErrorPath } from './utils/get/getErrorPath'
import getGridFen from './utils/get/getGridFen'
import getOutOfSync from './utils/get/getOutOfSync'
import getValidPath from './utils/get/getValidPath'

const { ccclass, property, requireComponent } = _decorator

const { monitorError } = codex

const MINIMUM_TILES_TO_GRAVITATE = 14
const MINIMUM_UNCLEARED_TO_SPAWN = 2
const MINIMUM_NORMAL_MATCHES_TO_SPAWN = 5
const SPAWN_INTERVAL = 2

const {
    Utils: { Array: A, Function: F, Valid: V },
} = Ludex

const { Layout } = GameWorld

type Layout = (typeof Layout)[keyof typeof Layout]
type ClearPayload = {
    pairs: [Tile, Tile][]
    stagger?: number
    delay?: number
    lightning?: boolean
}

@ccclass('Board')
@requireComponent(UITransform)
export default class Board extends Component {
    @ComponentUtils.required()
    @property(Tile)
    private readonly tilePrefab!: Tile

    @ComponentUtils.required()
    @property(Node)
    public tilesHolder!: Node

    @property({ displayName: 'Tile Spacing', group: 'Create config' })
    public tileTrim = new Vec2(0.025, 0.1)
    private tileSize: [number, number] = [0, 0]

    public widget!: Widget

    private uiTransform!: UITransform

    private grid?: Grid<Tile>
    private selectedTile: Tile | null = null

    // game cycle
    private isPlayingEffect = false
    private levelData?: ILevelData
    private useRotatedGameplay = false
    private dirtyGameplayRotation = false
    private parsedGravityRules: LevelGravity[] = []
    private normalMatches = 0
    private normalMatchesSinceLastSpawn = 0
    private spawnAttempts = 0
    private hintPath: Path | null = null
    private hintTiles: [Tile, Tile] | null = null
    private isAnimating: boolean = false

    // ! Animation handlers must be pure functions to avoid side effects and potential bugs
    private animationQueue: Array<() => Promise<unknown>> = []
    private isConsumingAnimation = false

    protected onLoad(): void {
        this.widget = this.getComponent(Widget) as Widget
        this.uiTransform = this.getComponent(UITransform) as UITransform

        ComponentUtils.validateRequiredProperties(this, Board)

        this.tilePrefab.node.active = false
        this.listenEvents()
    }

    protected onDestroy(): void {
        this.unlistenEvents()
    }

    private shouldRotate(levelData: ILevelData): boolean {
        if (levelData.level === 0) return false

        const isLevelPortrait = levelData.rows > levelData.columns
        const isOrientationPortrait = this.uiTransform.height > this.uiTransform.width
        return isLevelPortrait !== isOrientationPortrait
    }

    public async create(frames: Promise<SpriteFrame[]>, levelData: ILevelData, seed = Date.now()) {
        this.levelData = levelData

        //? Reset the widget for the newbie level
        this.handleOrientationChanged(LayoutUtils.getCurrentLayout())
        await AnimUtils.waitTweenAsync(0)

        //? Shuffle the frames to avoid same board patterns
        const framesPromise = frames.then((unshuffled) => {
            const shuffled = [...unshuffled]
            A.shuffle(shuffled)
            const selected = shuffled.slice(0, levelData.colors)

            monitorError?.addMetadata({
                frames: selected.map((frame) => frame.name),
            })

            return selected
        })

        const identities = A.fillWith(levelData.colors, (i) => i)

        this.useRotatedGameplay = this.shouldRotate(levelData) && this.levelData.level !== 0

        this.parsedGravityRules = parseLevelGravity(levelData.mode)

        const createTile = (id: number) => {
            const tile = instantiate(this.tilePrefab.node)
            const tileComponent = tile.getComponent(Tile) as Tile

            tileComponent.setIdentity(id, framesPromise)
            this.tilesHolder.addChild(tile)

            tile.active = true

            return tileComponent
        }

        let usedFen = false

        if (levelData.customFen !== undefined) {
            try {
                usedFen = true

                this.grid = createGridFromFen({
                    customFen: levelData.customFen,
                    identities,
                    createTile,
                })

                if (this.useRotatedGameplay) this.grid = createRotatedGrid(this.grid)

                this.grid.each((tile, x, y) => {
                    tile.setCoord(x, y)
                    tile.setIgnoreInput(false)
                    tile.node.setSiblingIndex(0)
                })
            } catch (error) {
                console.error('Error creating grid from custom fen', error, 'falling back to random grid')

                usedFen = false
            }
        }

        if (!usedFen) {
            this.grid = createGrid({
                levelData,
                seed,
                identities,
                createTile,
                setTileIdentity: (tile, id) => tile.setIdentity(id, framesPromise),
            })

            if (this.useRotatedGameplay) this.grid = createRotatedGrid(this.grid)

            this.grid.each((tile, x, y) => {
                tile.setCoord(x, y)
                tile.setIgnoreInput(false)
                tile.node.setSiblingIndex(0)
            })

            ensureOneMatch({
                grid: this.grid,
                setIdentity: (tile, id: number) => tile.setIdentity(id, framesPromise),
                seed,
            })
            assignSpecialTiles(this.grid, levelData, seed)
        }

        const trim = [this.tileTrim.x, this.tileTrim.y] as const
        this.tileSize = calcTileSize(this.tilePrefab.getSize(), trim)

        this.updateTilesHolderScale()

        this.node.scene.emit(GameplayEvents.GRID_CREATE, this)

        this.selectedTile = null
        this.animationQueue = []
        this.normalMatches = 0
        this.normalMatchesSinceLastSpawn = 0
        this.spawnAttempts = 0
        this.hintPath = null

        this.processGameplayCycle()
    }

    private updateTilesHolderScale(): void {
        if (!this.grid) return

        const gridSize = [this.grid.width, this.grid.height] as const
        const screenSize = [this.uiTransform.width, this.uiTransform.height] as const

        const scale = calcBoardScale(gridSize, this.tilePrefab.getSize(), screenSize)
        this.tilesHolder.setScale(scale, scale)

        this.node.scene.emit(GameplayEvents.GRID_RESIZE, scale)
    }

    public destroyTiles(): void {
        this.grid?.each((tile) => tile.node.destroy())
    }

    public animateTilesAppear(): Promise<void> {
        if (!this.grid) {
            const error = new Error('animateTilesAppear: Grid is not initialized')
            if (V.isDebugger()) throw error
            monitorError?.sendException(error)
            return Promise.resolve()
        }

        const grid = this.grid

        const getFlyInDirection = (x: number, y: number) => {
            const direction = Vec2.ZERO.clone()
            for (const gravityType of this.parsedGravityRules) {
                const gravity = this.useRotatedGameplay ? rotateGravity(gravityType) : gravityType
                const primitiveDirection = this.getPrimitiveDirection(x, y, gravity)
                direction.subtract2f(primitiveDirection.x, primitiveDirection.y)
            }
            return direction.normalize()
        }

        return new Promise((resolve) => {
            const animHandler = async () => {
                const getTargetPosition = (pos: math.IVec2Like) => calcTilePosition(grid, this.tileSize, pos)

                await Promise.all(
                    grid.map((tile, x, y) => {
                        const delay = randomRange(0, 0.5)
                        const targetScale = tile.node.scale.clone()
                        const appearDirection = getFlyInDirection(x, y)
                        const pos = calcTilePosition(grid, this.tileSize, { x, y })
                        const startPos = appearDirection.multiplyScalar(100).add2f(pos.x, pos.y)

                        tile.node.setPosition(startPos.x, startPos.y)
                        tile.node.setScale(0, 0)

                        return Promise.all([
                            AnimUtils.runTweenAsync(
                                tween(tile.node).delay(delay).to(0.5, { scale: targetScale }, { easing: 'elasticOut' }),
                            ),
                            tile.sync({
                                getTargetPosition,
                                easing: 'backOut',
                                duration: 0.5,
                                delay,
                            }),
                        ])
                    }),
                )

                resolve()
            }

            this.animationQueue.push(animHandler)
            this.consumeAnimationQueue()
        })
    }

    public animateTilesWave(): Promise<void> {
        if (!this.grid) {
            this.reportOrThrowInDebugger('Grid is not initialized', 'animateTilesWave')
            return Promise.resolve()
        }

        const grid = this.grid

        return new Promise((resolve) => {
            const animHandler = async () => {
                const staggers = stagger('center', { x: grid.width, y: grid.height })

                await Promise.all(
                    grid.map((tile, x, y) => {
                        const stagger = staggers[x]?.[y] ?? 0
                        const delay = stagger * 0.1
                        const scale = tile.node.scale.clone()

                        return Promise.all([
                            AnimUtils.runTweenAsync(
                                tween(tile.node)
                                    .delay(delay)
                                    .to(0.25, { scale: scale.clone().multiplyScalar(1.2) }, { easing: 'quadOut' })
                                    .to(0.25, { scale: scale }, { easing: 'quadIn' }),
                            ),
                        ])
                    }),
                )

                resolve()
            }

            this.animationQueue.push(animHandler)
            this.consumeAnimationQueue()
        })
    }

    public async animateTilesError(tiles: Tile[]): Promise<void> {
        if (!this.grid) {
            this.reportOrThrowInDebugger('Grid is not initialized', 'animateTilesError')
            return Promise.resolve()
        }

        if (this.isAnimating) return
        this.isAnimating = true

        await Promise.all(
            tiles.map((tile) => {
                return tile.moveAround()
            }),
        )

        this.isAnimating = false
    }

    public handleTileClick(tile: Tile): void {
        if (tile.isHidden()) return

        if (this.selectedTile === tile) {
            AudioUtils.playSound(CodeSoundKeys.TILE_SELECT)
            this.selectedTile = null
            tile.setFocused(false)
            return
        }

        if (!this.selectedTile) {
            AudioUtils.playSound(CodeSoundKeys.TILE_SELECT)
            this.selectedTile = tile
            tile.setFocused(true)
            return
        }

        if (!this.selectedTile.isSimilarTo(tile)) {
            AudioUtils.playSound(CodeSoundKeys.TILE_SELECT)
            this.selectedTile.setFocused(false)
            this.selectedTile = tile
            tile.setFocused(true)
            return
        }

        this.selectedTile.setFocused(false)

        this.match(this.selectedTile, tile)
        this.selectedTile = null
    }

    public clearSelectedTile(): void {
        if (this.selectedTile) {
            this.selectedTile.setFocused(false)
            this.selectedTile = null
        }
    }

    public match(tileA: Tile, tileB: Tile): void {
        if (!this.grid || !this.levelData) {
            this.reportOrThrowInDebugger('Grid is not initialized', 'match')
            return
        }

        const coordA = [tileA.getCoord().x, tileA.getCoord().y] as [number, number]
        const coordB = [tileB.getCoord().x, tileB.getCoord().y] as [number, number]
        const path = getValidPath(this.grid, coordA, coordB)

        if (path === null) {
            AudioUtils.playSound(CodeSoundKeys.INVALID_MATCH)

            this.animateTilesError([tileA, tileB])

            const isNewbieLevel = this.levelData.level === 0
            const errorPath = getErrorPath(this.grid, coordA, coordB, isNewbieLevel)

            this.node.scene.emit(GameplayEvents.GRID_MATCH, [tileA, tileB])
            this.node.scene.emit(GameplayEvents.GRID_MATCH_FAIL, errorPath)

            return
        }

        AudioUtils.playSound(CodeSoundKeys.TILE_MATCH)

        this.node.scene.emit(GameplayEvents.GRID_MATCH, [tileA, tileB])
        this.node.scene.emit(GameplayEvents.GRID_MATCH_SUCCESS, path, [tileA, tileB])
        this.clear({ pairs: [[tileA, tileB]] })
    }

    public clear(payload: ClearPayload): void {
        const { pairs, stagger = 0, delay = 0, lightning = false } = payload

        this.normalMatches += pairs.length

        for (let i = 0; i < pairs.length; i++) {
            const [a, b] = pairs[i] as [Tile, Tile]

            const delay1 = stagger ? i * 2 * stagger : 0
            const delay2 = stagger ? (i * 2 + 1) * stagger : 0

            a.setEmpty()
            b.setEmpty()

            a.playClearFX(delay1, lightning)
            b.playClearFX(delay2, lightning)

            this.node.scene.emit(GameplayEvents.GRID_CLEAR, [a, b])
        }

        AnimUtils.waitTweenAsync(delay).then(() => {
            this.processGameplayCycle()
        })
    }

    public clearUsingOldLightningFX(pairs: [Tile, Tile][]): void {
        this.normalMatches += pairs.length

        for (const element of pairs) {
            const [a, b] = element

            a.setEmpty()
            b.setEmpty()

            a.playOldLightningClearFX()
            b.playOldLightningClearFX()

            this.node.scene.emit(GameplayEvents.GRID_CLEAR, [a, b])
        }

        this.processGameplayCycle()
    }

    public showHint(): void {
        if (!this.hintPath) return
        const startLine = this.hintPath[0]
        const endLine = this.hintPath[this.hintPath.length - 1]

        if (!startLine || !endLine) {
            this.reportOrThrowInDebugger('Hint path is not valid', 'showHint')
            return
        }

        const coordA = startLine.a
        const coordB = endLine.b

        const tileA = this.getTileAt(coordA.x, coordA.y)
        const tileB = this.getTileAt(coordB.x, coordB.y)

        if (!tileA || !tileB) {
            this.reportOrThrowInDebugger('Hint tiles are not valid', 'showHint')
            return
        }

        tileA.setHint(true)
        tileB.setHint(true)

        this.hintTiles = [tileA, tileB]
    }

    public hideHint(): void {
        if (this.hintTiles) {
            const [tileA, tileB] = this.hintTiles
            tileA.setHint(false)
            tileB.setHint(false)
            this.hintTiles = null
        }
    }

    public getHintPath(): Path | null {
        return this.hintPath
    }

    public getTileAt(x: number, y: number): Tile | null {
        if (!this.grid) return null
        if (!this.grid.isInBounds(x, y)) return null
        return this.grid.get(x, y)
    }

    public getAnyTileWorldPosition(x: number, y: number): Vec2Like {
        if (!this.grid) {
            this.reportOrThrowInDebugger('Grid is not initialized', 'getAnyTileWorldPosition')
            return { x: 0, y: 0 }
        }

        const pos = calcTilePosition(this.grid, this.tileSize, { x, y })
        this.tilePrefab.node.setPosition(pos.x, pos.y)
        const worldPos = this.tilePrefab.node.getWorldPosition()
        return {
            x: worldPos.x,
            y: worldPos.y,
        }
    }

    public getTileSize(): Readonly<[number, number]> {
        return this.tileSize
    }

    public getGridSize(): Readonly<[number, number]> {
        if (!this.grid) {
            this.reportOrThrowInDebugger('Grid is not initialized', 'getGridSize')
            return [0, 0]
        }

        return [this.grid.width, this.grid.height]
    }

    public getUncleared(): Tile[] {
        if (!this.grid) {
            this.reportOrThrowInDebugger('Grid is not initialized', 'getUncleared')
            return []
        }

        return getUncleared(this.grid)
    }

    public getCleared(): Tile[] {
        if (!this.grid) {
            this.reportOrThrowInDebugger('Grid is not initialized', 'getCleared')
            return []
        }

        return getCleared(this.grid)
    }

    public getBombTiles(): Tile[] {
        if (!this.grid) {
            this.reportOrThrowInDebugger('Grid is not initialized', 'getBombTiles')
            return []
        }

        return getBombTiles(this.grid)
    }

    public getPairs(count = 1, uncleared = true): [Tile, Tile][] {
        if (!this.grid) {
            this.reportOrThrowInDebugger('Grid is not initialized', 'getPairs')
            return []
        }

        const pairs: [Tile, Tile][] = []

        let tiles = uncleared ? getUncleared(this.grid) : getCleared(this.grid)

        while (pairs.length < count) {
            const [a, b] = getRandomPair(tiles)
            pairs.push([a, b])
            tiles = tiles.filter((tile) => tile !== a && tile !== b)
        }

        return pairs
    }

    public getBoundingBoxWorld(): math.Rect {
        const boardScale = this.tilesHolder.getWorldScale().x
        const gridSize = this.getGridSize()

        const width = this.tileSize[0] * boardScale * gridSize[0]
        const height = this.tileSize[1] * boardScale * gridSize[1]

        const topBottomPos = this.node.getWorldPosition().subtract3f(width / 2, height / 2, 0)

        return new math.Rect(topBottomPos.x, topBottomPos.y, width, height)
    }

    public setIgnoreInput(ignore = true): void {
        this.grid?.each((tile) => tile.setIgnoreInput(ignore))
    }

    public processShuffleRequest(
        handleShuffleAnimation: (prevGrid: Grid<Tile>, currGrid: Grid<Tile>) => Promise<void>,
    ): void {
        if (!this.grid) {
            this.reportOrThrowInDebugger('Grid is not initialized', 'processShuffleRequest')
            return
        }

        const newGridAndPath = this.processShuffleStep(this.grid, true, handleShuffleAnimation)
        this.grid = newGridAndPath[0]
        this.hintPath = newGridAndPath[1]

        this.consumeAnimationQueue()
    }

    public generateFenFromCurrentLevel(data?: ILevelData): LevelFen {
        if (!this.levelData) {
            this.reportOrThrowInDebugger('Level data is not initialized', 'generateFenFromCurrentLevel')
        }

        return Board.generateFen(data ?? this.levelData ?? DEFAULT_LEVEL_DATA)
    }

    public static generateFen(data: ILevelData): LevelFen {
        const levelData = data

        const frames = []
        for (let i = 0; i < levelData.colors; i++) {
            frames.push(i + 1)
        }

        const seed = Date.now()

        /**
         * Fake tile with barely enough logic to create a grid
         */
        type FakeTile = {
            id: number
            special: { type: TileSpecialType } | undefined
            coord: Vec2
            setIdentity: (id: number) => void
            isHidden: () => boolean
            setSpecialType: (t: TileSpecialType) => void
            setCoord: (x: number, y: number) => void
            getCoord: () => Vec2
            isSimilarTo: (tile: FakeTile) => boolean
            inSamePositionAs: (tile: FakeTile) => boolean
        }

        const grid = createGrid({
            levelData,
            seed,
            identities: frames,
            setTileIdentity: (tile: FakeTile, id) => tile.setIdentity(id),
            createTile: (frame) => {
                let identity = frame
                let special: { type: TileSpecialType } | undefined = undefined
                const coord = new Vec2(0, 0)

                return {
                    get id() {
                        return identity
                    },
                    get special() {
                        return special
                    },
                    get coord() {
                        return coord
                    },
                    setIdentity: (id) => {
                        identity = id
                    },
                    isHidden: () => false,
                    setSpecialType: (t: TileSpecialType) => {
                        special = { type: t }
                    },
                    setCoord: (x: number, y: number) => coord.set(x, y),
                    getCoord: () => coord,
                    isSimilarTo: (tile: FakeTile) => tile.id === identity,
                    inSamePositionAs: (tile: FakeTile) => tile.getCoord().equals(coord),
                } as FakeTile
            },
        })

        grid.each((tile, x, y) => {
            tile.setCoord(x, y)
        })

        ensureOneMatch({ grid, seed, setIdentity: (tile, id: number) => tile.setIdentity(id) })
        assignSpecialTiles(grid, levelData, seed)

        return getGridFen(grid)
    }

    private listenEvents() {
        game.on(GameEvents.VIEW_RESIZED, this.handleViewResized)
        game.on(GameEvents.WIDGET_RESIZED, this.handleViewResized)
        // @ts-expect-error - This event is custom event
        game.on(GameEvents.ORIENTATION_CHANGED, this.handleOrientationChanged, this)
        this.node.scene.on(GameplayEvents.EFFECTS_ANIMATION_START, this.onEffectAnimStarted, this)
        this.node.scene.on(GameplayEvents.EFFECTS_ANIMATION_END, this.onEffectAnimEnded, this)
    }

    private unlistenEvents() {
        game.off(GameEvents.VIEW_RESIZED, this.handleViewResized)
        game.off(GameEvents.WIDGET_RESIZED, this.handleViewResized)
        game.off(GameEvents.ORIENTATION_CHANGED, this.handleOrientationChanged, this)
        this.node.scene.off(GameplayEvents.EFFECTS_ANIMATION_START, this.onEffectAnimStarted, this)
        this.node.scene.off(GameplayEvents.EFFECTS_ANIMATION_END, this.onEffectAnimEnded, this)
    }

    private onEffectAnimStarted(): void {
        this.isPlayingEffect = true
    }

    private onEffectAnimEnded(): void {
        this.isPlayingEffect = false
        this.handleOrientationChanged(LayoutUtils.getCurrentLayout())
        this.handleViewResized()
    }

    private readonly handleViewResized = F.debounce(() => {
        if (this.isPlayingEffect) return

        this.updateTilesHolderScale()

        if (this.levelData) {
            const shouldRotate = this.shouldRotate(this.levelData)
            this.dirtyGameplayRotation = this.useRotatedGameplay !== shouldRotate
            if (this.dirtyGameplayRotation) {
                this.consumeAnimationQueue()
            }
        }
    }, 100)

    public getWidgetMargins(layout: Layout) {
        if (layout === Layout.Landscape && (this.levelData === undefined || this.levelData.level !== 0)) {
            return {
                top: 80,
                bottom: 80,
                left: 640,
                right: 60,
            }
        }

        return {
            top: 160,
            bottom: 160,
            left: 20,
            right: 20,
        }
    }

    private handleOrientationChanged(layout: Layout): void {
        if (this.isPlayingEffect) return
        const { top, bottom, left, right } = this.getWidgetMargins(layout)
        this.widget.top = top
        this.widget.bottom = bottom
        this.widget.left = left
        this.widget.right = right
    }

    private processGameplayCycle(): void {
        if (!this.node.activeInHierarchy || !this.grid || !this.levelData) {
            this.reportOrThrowInDebugger('Grid is not initialized', 'processGameplayCycle')
            return
        }

        const uncleared = getUncleared(this.grid)

        if (uncleared.length === 0) {
            return
        }

        let grid = this.grid

        const shouldTrySpawning =
            uncleared.length >= MINIMUM_UNCLEARED_TO_SPAWN &&
            this.spawnAttempts < this.levelData.rules.addRandomTilesPerMove &&
            this.normalMatches >= MINIMUM_NORMAL_MATCHES_TO_SPAWN

        const canSpawn = this.normalMatchesSinceLastSpawn >= SPAWN_INTERVAL

        if (shouldTrySpawning) {
            if (canSpawn) {
                this.spawnAttempts++
                this.normalMatchesSinceLastSpawn = 0
                this.processSpawnStep()
            } else {
                this.normalMatchesSinceLastSpawn++
            }
        }

        if (uncleared.length % 2 === 1) {
            //? If there is an odd number of uncleared tiles, spawn a single tile
            this.processSpawnSingleTileStep()
        }

        if (uncleared.length > MINIMUM_TILES_TO_GRAVITATE) {
            grid = this.processGravitationStep(grid)
        }

        const newGridAndPath = this.processShuffleStep(grid)

        this.consumeAnimationQueue()

        this.grid = newGridAndPath[0]
        this.hintPath = newGridAndPath[1]
    }

    private processSpawnStep(): void {
        if (!this.grid) {
            this.reportOrThrowInDebugger('Grid is not initialized', 'processSpawnStep')
            return
        }

        const [a, b] = getRandomTiles(getCleared(this.grid), 2)
        const id = a?.id ?? undefined
        const frames = a?.frames ?? undefined

        if (a === undefined || id === undefined || frames === undefined || b === undefined) {
            this.reportOrThrowInDebugger('Failed to get random tiles', 'processSpawnStep')
            return
        }

        a.restore()
        b.restore(id, frames)
        this.node.scene.emit(GameplayEvents.GRID_SPAWN, [a, b])
    }

    private processGravitationStep(grid: Grid<Tile>): Grid<Tile> {
        let newGrid = grid
        const sequences: Array<() => Promise<unknown>> = []

        for (const parsedGravity of this.parsedGravityRules) {
            const gravity = this.useRotatedGameplay ? rotateGravity(parsedGravity) : parsedGravity
            newGrid = this.toGravitated(newGrid, gravity)

            const affectedTiles = getOutOfSync(newGrid)

            if (affectedTiles.length === 0) continue

            for (const [tile, [x, y]] of affectedTiles) {
                tile.setCoord(x, y)
            }

            newGrid.each((tile) => {
                tile.node.setSiblingIndex(0)
            })

            const animHandler = async () => {
                await Promise.all(
                    affectedTiles.map(([tile, [x, y]]) => {
                        return tile.sync({
                            delay: 0.25,
                            duration: 0.25,
                            getTargetPosition: () => calcTilePosition(newGrid, this.tileSize, { x, y }),
                        })
                    }),
                )
            }

            sequences.push(animHandler)
        }

        const animHandler = async () => {
            for (const handler of sequences) {
                await handler()
            }
        }

        this.animationQueue.push(animHandler)

        return newGrid
    }

    private toGravitated(grid: Grid<Tile>, gravityType: LevelGravity): Grid<Tile> {
        const copy = new Grid<Tile>(grid)

        const top = 0
        const bottom = grid.height - 1
        const left = 0
        const right = grid.width - 1

        const fullRows = [top, bottom] as const
        const fullCols = [left, right] as const

        const halfRows = Math.floor(grid.height / 2)
        const halfCols = Math.floor(grid.width / 2)

        const isLighter = (cell: Tile) => cell.isHidden()

        switch (gravityType) {
            case LevelGravity.FallDown:
                copy.gravitate({
                    rowRange: fullRows,
                    colRange: fullCols,
                    direction: Direction.Down,
                    isLighter,
                })

                break

            case LevelGravity.FlyUp:
                copy.gravitate({
                    rowRange: fullRows,
                    colRange: fullCols,
                    direction: Direction.Up,
                    isLighter,
                })

                break

            case LevelGravity.PushLeft:
                copy.gravitate({
                    rowRange: fullRows,
                    colRange: fullCols,
                    direction: Direction.Left,
                    isLighter,
                })

                break

            case LevelGravity.PushRight:
                copy.gravitate({
                    rowRange: fullRows,
                    colRange: fullCols,
                    direction: Direction.Right,
                    isLighter,
                })

                break

            case LevelGravity.PutColumnMiddle:
                copy.gravitate({
                    rowRange: fullRows,
                    colRange: [halfCols, right],
                    direction: Direction.Left,
                    isLighter,
                    out: copy.gravitate({
                        rowRange: fullRows,
                        colRange: [left, halfCols - 1],
                        direction: Direction.Right,
                        isLighter,
                    }),
                })

                break

            case LevelGravity.PutRowMiddle:
                copy.gravitate({
                    rowRange: [halfRows, bottom],
                    colRange: fullCols,
                    direction: Direction.Down,
                    isLighter,
                    out: copy.gravitate({
                        rowRange: [top, halfRows - 1],
                        colRange: fullCols,
                        direction: Direction.Up,
                        isLighter,
                    }),
                })

                break
        }

        return copy
    }

    private getPrimitiveDirection(x: number, y: number, gravityType: LevelGravity): DirectionType {
        if (!this.grid) {
            this.reportOrThrowInDebugger('Grid is not initialized', 'getPrimitiveDirection')
            return Direction.Down
        }

        const halfRows = Math.floor(this.grid.height / 2)
        const halfCols = Math.floor(this.grid.width / 2)

        switch (gravityType) {
            case LevelGravity.FallDown:
                return Direction.Down
            case LevelGravity.FlyUp:
                return Direction.Up
            case LevelGravity.PushLeft:
                return Direction.Left
            case LevelGravity.PushRight:
                return Direction.Right
            case LevelGravity.PutColumnMiddle:
                return x >= halfCols ? Direction.Left : Direction.Right
            case LevelGravity.PutRowMiddle:
                return y >= halfRows ? Direction.Down : Direction.Up
        }
    }

    private processShuffleStep(
        grid: Grid<Tile>,
        force = false,
        handleShuffleAnimation?: (prevGrid: Grid<Tile>, newGrid: Grid<Tile>) => Promise<void>,
    ): [Grid<Tile>, Path] {
        let path = getValidPath(grid)
        const uncleared = getUncleared(grid).map((tile) => {
            const coord = tile.getCoord()
            return [coord.x, coord.y] as [number, number]
        })

        if (path !== null && !force) {
            return [grid, path]
        }

        let newGrid: Grid<Tile>
        let DEBUG_SANITY_CHECK = 100

        do {
            ;[newGrid] = grid.toShuffled(uncleared)
            path = getValidPath(newGrid)
            DEBUG_SANITY_CHECK--
        } while (path === null && DEBUG_SANITY_CHECK > 0)

        if (DEBUG_SANITY_CHECK <= 0) {
            const gridAsArray = newGrid.asArray().map((tile) => {
                return {
                    name: tile.id,
                    type: tile.special?.type,
                    active: !tile.isHidden(),
                }
            })

            monitorError?.sendException(new Error('No paths found after 100 shuffle attempts'), {
                message: 'No paths found after 100 shuffle attempts',
                level: this.levelData?.level,
                rules: this.levelData?.rules,
                mode: this.levelData?.mode,
                gridAsArray,
            })

            this.node.scene.emit(GameplayEvents.GAME_EXIT)
            return [grid, path ?? []] //? This line is unreachable
        }

        const affectedTiles = getOutOfSync(newGrid)

        for (const [tile, [x, y]] of affectedTiles) {
            tile.setCoord(x, y)
        }

        const animHandler =
            handleShuffleAnimation !== undefined
                ? () => handleShuffleAnimation(grid, newGrid)
                : this.createShuffleAnimationHandler(grid, newGrid)

        this.animationQueue.push(animHandler)

        return [newGrid, path ?? []]
    }

    private createShuffleAnimationHandler(prevGrid: Grid<Tile>, currGrid: Grid<Tile>): () => Promise<void> {
        const paddingAnimHandler = async () => {
            await Promise.all(
                prevGrid.map((tile, x, y) =>
                    tile.sync({
                        duration: 0.3,
                        easing: 'smooth',
                        getTargetPosition: () => calcTilePosition(currGrid, this.tileSize, { x, y }, 10),
                    }),
                ),
            )
        }

        const returnToPlaceAnim = async () => {
            currGrid.each((tile) => {
                tile.node.setSiblingIndex(0)
            })

            await Promise.all(
                currGrid.map((tile) =>
                    tile.sync({
                        duration: 0.4,
                        easing: 'smooth',
                        delay: randomRange(0, 0.2),
                        getTargetPosition: (pos) => calcTilePosition(currGrid, this.tileSize, pos),
                    }),
                ),
            )
        }

        const animHandler = async () => {
            await paddingAnimHandler()
            await returnToPlaceAnim()
        }

        return animHandler
    }

    private processSpawnSingleTileStep(): void {
        if (!this.grid) {
            this.reportOrThrowInDebugger('Grid is not initialized', 'processSpawnSingleTileStep')
            return
        }

        const [clearTile] = getRandomTiles(getCleared(this.grid), 1)
        const unclearTiles = getUncleared(this.grid)

        // Count occurrences of each tile ID
        const tileCountMap: Record<string, number> = {}
        for (const tile of unclearTiles) {
            const id = tile.id

            if (id !== null) tileCountMap[id] = (tileCountMap[id] ?? 0) + 1
        }

        // Find an uncleared tile with an odd count
        const unclearTile = A.search(unclearTiles, (tile) => {
            const id = tile.id
            if (id === null) return false

            const tileCount = tileCountMap[id]
            return tileCount !== undefined && tileCount % 2 === 1
        })

        if (!clearTile || !unclearTile || unclearTile.id === null) {
            this.reportOrThrowInDebugger('Failed to get a random tile', 'processSpawnSingleTileStep')
            return
        }

        clearTile.restore(unclearTile.id, unclearTile.frames)
        this.node.scene.emit(GameplayEvents.GRID_SPAWN, [clearTile])
    }

    private async consumeAnimationQueue(): Promise<void> {
        if (this.isConsumingAnimation) return

        this.isConsumingAnimation = true

        while (this.animationQueue.length > 0) {
            if (this.getUncleared().length === 0) {
                this.animationQueue = []
                break
            }

            const handler = this.animationQueue.shift()
            await handler?.()
        }

        // ? this.dirtyGameplayRotation might be set to true during the animation
        while (this.dirtyGameplayRotation && this.levelData !== undefined) {
            this.dirtyGameplayRotation = false

            const shouldRotate = this.shouldRotate(this.levelData)

            if (shouldRotate !== this.useRotatedGameplay) {
                await this.updateGameplayRotation()
            }
        }

        this.isConsumingAnimation = false
    }

    public async updateGameplayRotation() {
        if (!this.levelData) return

        const shouldRotate = this.shouldRotate(this.levelData)
        const isRevert = this.useRotatedGameplay && !shouldRotate
        this.useRotatedGameplay = shouldRotate

        if (!this.grid || !this.levelData || !this.hintPath) {
            return
        }

        this.node.scene.emit(GameplayEvents.GRID_ORIENTATION_CHANGE, this.useRotatedGameplay)

        this.hintPath = rotatePath(this.grid.width, this.grid.height, this.hintPath, isRevert)

        this.setIgnoreInput(true)

        const newGrid = createRotatedGrid(this.grid, isRevert)
        this.grid = newGrid

        this.updateTilesHolderScale()

        await Promise.all(
            newGrid.map((tile) => {
                return tile.sync({
                    delay: 0.3,
                    duration: 0.3,
                    easing: 'quartInOut',
                    getTargetPosition: () => calcTilePosition(newGrid, this.tileSize, tile.getCoord(), 10),
                })
            }),
        )

        newGrid.each((tile) => {
            tile.node.setSiblingIndex(0)
        })

        await Promise.all(
            newGrid.map((tile) => {
                return tile.sync({
                    duration: 0.2,
                    easing: 'sineInOut',
                    getTargetPosition: () => calcTilePosition(newGrid, this.tileSize, tile.getCoord()),
                })
            }),
        )

        this.setIgnoreInput(false)
    }

    public clearAnimationQueue(): void {
        this.isConsumingAnimation = false
        this.animationQueue = []
    }

    private reportOrThrowInDebugger(message = 'Unknown error thrown', from = 'Board') {
        const error = new Error(`${from}: ${message}`)
        if (V.isDebugger()) throw error
        monitorError?.sendException(error)
    }
}
