import { _decorator, CCBoolean, pseudoRandom } from 'cc'
import { isCurveTwoValues } from '../ParticleFunction'
import type { Particle } from '../ParticleSimulator'
import CurveRange from '../particle-editor/CurveRange'
import { ModuleRandSeed } from './ModuleRandSeed'

const { ccclass, property } = _decorator

const ROTATION_MODULE_RAND_SEED = ModuleRandSeed.ROTATION

@ccclass('RotationOvertimeModule')
export class RotationOvertimeModule {
    @property({ type: CCBoolean })
    private enable!: boolean

    @property(CCBoolean)
    private separateAxes = false

    @property({
        type: CurveRange,
        visible: function (this: RotationOvertimeModule): boolean {
            return this.separateAxes
        },
    })
    private x = new CurveRange()

    @property({
        type: CurveRange,
        visible: function (this: RotationOvertimeModule): boolean {
            return this.separateAxes
        },
    })
    private y = new CurveRange()

    @property({ type: CurveRange })
    private z = new CurveRange()

    public update(particle: Particle, deltaTime: number): void {
        if (!this.enable) return

        const normalizedTime = 1 - particle.remainingLifetime / particle.startLifetime

        const seed = particle.randomSeed + ROTATION_MODULE_RAND_SEED

        const randZ = isCurveTwoValues(this.z) ? pseudoRandom(seed) : 0
        const rotationZ = this.z.evaluate(normalizedTime, randZ) * deltaTime

        let rotationX = 0
        let rotationY = 0

        if (this.separateAxes) {
            const randX = isCurveTwoValues(this.x) ? pseudoRandom(seed) : 0
            const randY = isCurveTwoValues(this.y) ? pseudoRandom(seed) : 0

            rotationX = this.x.evaluate(normalizedTime, randX) * deltaTime
            rotationY = this.y.evaluate(normalizedTime, randY) * deltaTime
        }

        particle.rotation.add3f(rotationX, rotationY, rotationZ)
    }
}
