import { Sprite, SpriteFrame, Vec3, _decorator } from 'cc'
import { Line } from '../Line'
import { PathDrawer } from '../PathDrawer'

const { ccclass, property } = _decorator

@ccclass('ErrorDrawer')
export class ErrorDrawer extends PathDrawer {
    private pathAppearDuration!: number

    @property(SpriteFrame)
    private one!: SpriteFrame
    @property(SpriteFrame)
    private two!: SpriteFrame
    @property(SpriteFrame)
    private error!: SpriteFrame

    public start(): void {
        super.start()
        this.pathAppearDuration = 0
    }

    public drawPath(path: Path): void {
        this.clearPath()

        path.forEach((line, index) => {
            const lineNode = this.linePool.alloc()
            lineNode.getComponent(Line)?.setPoints(line.a, line.b)

            if (index !== 0) {
                const pointNode = this.pointPool.alloc()
                pointNode.setWorldPosition(new Vec3(line.a.x, line.a.y, 0))

                const sprite = pointNode.getComponent(Sprite)!

                if (index === 1 && path.length > 3) {
                    sprite.spriteFrame = this.one
                } else if (index === 2 && path.length > 3) {
                    sprite.spriteFrame = this.two
                } else {
                    sprite.spriteFrame = this.error
                }
            }
        })

        if (path.length === 1) {
            const pointNode = this.pointPool.alloc()
            pointNode.setWorldPosition(
                new Vec3(path[0].a.x + path[0].b.x, path[0].a.y + path[0].b.y, 0).multiplyScalar(0.5),
            )
            pointNode.getComponent(Sprite)!.spriteFrame = this.error
        }

        this.pathAppearDuration = 0.8
    }

    public update(dt: number): void {
        if (this.pathAppearDuration > 0) {
            this.pathAppearDuration -= dt
            if (this.pathAppearDuration <= 0) {
                this.clearPath()
            }
        }
    }
}
