import {
    type __private,
    _decorator,
    bits,
    CCBoolean,
    CCClass,
    CCFloat,
    Enum,
    type IAssembler,
    type MeshRenderData,
    ParticleSystem2D,
    pseudoRandom,
    randomRangeInt,
    SpriteFrame,
    UIRenderer,
    Vec3,
} from 'cc'
import { EDITOR, EDITOR_NOT_IN_PREVIEW } from 'cc/env'
import { Burst } from './Burst'
import { type Particle, ParticleSimulator } from './ParticleSimulator'
import CurveRange from './particle-editor/CurveRange'
import GradientRange from './particle-editor/GradientRange'
import { ColorOvertimeModule } from './particle-modules/ColorOvertimeModule'
import { LimitVelocityOvertimeModule } from './particle-modules/LimitVelocityOvertimeModule'
import { RotationOvertimeModule } from './particle-modules/RotationOvertimeModule'
import { ShapeModule } from './particle-modules/ShapeModule'
import { SizeOvertimeModule } from './particle-modules/SizeOvertimeModule'
import { VelocityOvertimeModule } from './particle-modules/VelocityOvertimeModule'

const { ccclass, property, executeInEditMode, disallowMultiple, playOnFocus } = _decorator

type IBatcher = __private._cocos_2d_renderer_i_batcher__IBatcher

const setClassAttr = CCClass.Attr.setClassAttr

//? Avoid rendering issues in 2D
const Z_OFFSET = 100

export const Space = Enum({
    World: 0,
    Local: 1,
})

//? Add this type to extend MeshRenderData with particleInitRenderDrawInfo
type ParticleMeshRenderData = MeshRenderData & {
    particleInitRenderDrawInfo: (renderEntity: any) => void
}

@ccclass('ParticleSystem')
@executeInEditMode(true)
@disallowMultiple(true)
@playOnFocus(true)
export class ParticleSystem extends UIRenderer {
    @property({})
    public get preview(): boolean {
        return this._preview
    }

    public set preview(value: boolean) {
        this._preview = value
        if (value) {
            this._startPreview()
        } else {
            this._stopPreview()
        }
    }

    @property({ formerlySerializedAs: 'preview' })
    private _preview: boolean = false

    @property(CCBoolean)
    private playOnAwake: boolean = true

    @property({ type: Enum(Space) })
    public get simulationSpace() {
        return this._simulationSpace
    }

    public set simulationSpace(val: number) {
        this._simulationSpace = val
        this._updateMaterial()
        this._updateSimulationSpace()
    }

    @property({ formerlySerializedAs: 'simulationSpace' })
    private _simulationSpace = Space.Local

    @property(CCFloat)
    public duration: number = 5

    @property(CCBoolean)
    private loop: boolean = false

    @property({ type: CurveRange })
    public startDelay: CurveRange = new CurveRange()

    @property({ type: CurveRange })
    private startLifetime: CurveRange = new CurveRange()

    @property(GradientRange)
    private startColor: GradientRange = new GradientRange()

    @property(CCBoolean)
    private startSize2D: boolean = false

    @property({ type: CurveRange, formerlySerializedAs: 'startSize' })
    private startSizeX: CurveRange = new CurveRange()

    @property({
        type: CurveRange,
        visible: function (this: ParticleSystem): boolean {
            return this.startSize2D
        },
    })
    private startSizeY: CurveRange = new CurveRange()

    @property({ type: CurveRange })
    public startSpeed: CurveRange = new CurveRange()

    @property(CCBoolean)
    private startRotation3D: boolean = false

    @property({
        type: CurveRange,
        visible: function (this: ParticleSystem): boolean {
            return this.startRotation3D
        },
    })
    private startRotationX: CurveRange = new CurveRange()

    @property({
        type: CurveRange,
        visible: function (this: ParticleSystem): boolean {
            return this.startRotation3D
        },
    })
    private startRotationY: CurveRange = new CurveRange()

    @property({ type: CurveRange, formerlySerializedAs: 'startRotation' })
    private startRotationZ: CurveRange = new CurveRange()

    @property({ type: CurveRange })
    private gravityModifier: CurveRange = new CurveRange()

    @property({ type: CurveRange })
    public rateOverTime: CurveRange = new CurveRange()

    @property({ type: ShapeModule })
    public shapeModule: ShapeModule = new ShapeModule()

    @property({ type: VelocityOvertimeModule })
    public velocityOvertimeModule: VelocityOvertimeModule = new VelocityOvertimeModule()

    @property({ type: SizeOvertimeModule })
    public sizeOvertimeModule: SizeOvertimeModule = new SizeOvertimeModule()

    @property({ type: RotationOvertimeModule })
    public rotationOvertimeModule: RotationOvertimeModule = new RotationOvertimeModule()

    @property({ type: ColorOvertimeModule })
    public colorOvertimeModule: ColorOvertimeModule = new ColorOvertimeModule()

    @property({ type: LimitVelocityOvertimeModule })
    public limitVelocityOvertimeModule: LimitVelocityOvertimeModule = new LimitVelocityOvertimeModule()

    @property
    @property(Burst)
    public bursts: Burst[] = []

    @property({ serializable: true })
    private _spriteFrame: SpriteFrame | null = null

    @property({ type: SpriteFrame })
    public get spriteFrame(): SpriteFrame | null {
        return this._spriteFrame
    }

    public set spriteFrame(value: SpriteFrame | null) {
        const lastSprite = this._renderSpriteFrame
        if (lastSprite === value) {
            return
        }
        this._renderSpriteFrame = value

        if (!value || value._uuid) {
            this._spriteFrame = value
        }

        this._applySpriteFrame()

        if (EDITOR) {
            this.node.emit('spriteframe-changed', this)
        }
    }

    public get assembler(): IAssembler | null {
        return this._assembler
    }

    public _renderSpriteFrame: SpriteFrame | null = null

    private _simulator!: ParticleSimulator

    public time: number = 0
    private emitRateTimeCounter: number = 0

    private isPlaying: boolean = false
    private isStopped: boolean = true
    private isEmitting: boolean = false

    constructor() {
        super()

        this.startLifetime.constant = 5
        this.startSizeX.constant = 1
        this.startSpeed.constant = 5
        this.rateOverTime.constant = 10

        this.initProperties()
    }
    public onEnable(): void {
        super.onEnable()
        this._updateMaterial()
        this._updateSimulationSpace()
    }

    public onDestroy(): void {
        super.onDestroy()
    }

    private initProperties(): void {
        this._simulator = new ParticleSimulator(this)
    }

    public onFocusInEditor(): void {
        this._startPreview()
    }

    public onLostFocusInEditor(): void {
        this._stopPreview()
    }

    private _startPreview(): void {
        if (this._preview) {
            this.play()
        }
    }

    private _stopPreview(): void {
        if (!this._preview) {
            this.play()
            this.stop()
        }
    }

    public __preload(): void {
        super.__preload()

        if (this.spriteFrame && !this._renderSpriteFrame) {
            this._applySpriteFrame()
        }

        // auto play
        if (!EDITOR_NOT_IN_PREVIEW) {
            if (this.playOnAwake) {
                this.play()
            }
        }
    }

    protected start(): void {
        this.node.position = new Vec3(this.node.position.x, this.node.position.y, Z_OFFSET)
    }

    public play(): void {
        if (this.isStopped) {
            this.isStopped = false
        }

        this.time = 0
        this.isPlaying = true
        this.isStopped = false
        this.isEmitting = true

        this._simulator.reset()
        this.markForUpdateRenderData()
    }

    public stop(): void {
        if (this.isPlaying) {
            this.isPlaying = false
        }

        if (this.isEmitting) {
            this.isEmitting = false
        }

        this.isStopped = true

        this._simulator.reset()
        this._simulator.stop()
    }

    protected _flushAssembler(): void {
        const assembler = ParticleSystem2D.Assembler.getAssembler(this)

        if (this._assembler !== assembler) {
            this._assembler = assembler
        }

        if (this._assembler?.createData) {
            const simulator = this._simulator
            let renderData = simulator.renderData as ParticleMeshRenderData
            if (!renderData) {
                renderData = simulator.renderData = this._assembler.createData(this) as ParticleMeshRenderData
                simulator.uvFilled = 0
                renderData.particleInitRenderDrawInfo(this.renderEntity)
                simulator.initDrawInfo()
            }
        }
    }

    public _applySpriteFrame(): void {
        this._renderSpriteFrame = this._renderSpriteFrame || this._spriteFrame
        if (this._renderSpriteFrame) {
            if (this._renderSpriteFrame.texture) {
                if (this._simulator) {
                    this._simulator.updateUVs(true)
                }
                this._updateMaterial()
                this.isStopped = false
                this.markForUpdateRenderData()
            }
        } else {
            this.stop()
        }
    }

    public _updateMaterial(): void {
        if (this._customMaterial) {
            this.setSharedMaterial(this._customMaterial, 0)
            const target = this.getRenderMaterial(0)!.passes[0]!.blendState.targets[0]
            if (target) {
                this._dstBlendFactor = target.blendDst
                this._srcBlendFactor = target.blendSrc
            }
        }

        const mat = this.getMaterialInstance(0)
        if (mat) mat.recompileShaders({ USE_LOCAL: this.simulationSpace === Space.Local })
        if (mat && mat.passes.length > 0) {
            this._updateBlendFunc()
        }
    }

    public _finishedSimulation(): void {
        this.stop()
        this.markForUpdateRenderData()
    }

    protected _canRender(): boolean {
        return (
            super._canRender() &&
            !this.isStopped &&
            this._renderSpriteFrame !== null &&
            this._renderSpriteFrame !== undefined
        )
    }

    protected _render(render: IBatcher): void {
        if (this.simulationSpace === Space.Local) {
            render.commitComp(this, this._simulator.renderData, this._renderSpriteFrame, this._assembler, this.node)
        } else if (this.simulationSpace === Space.World) {
            render.commitComp(this, this._simulator.renderData, this._renderSpriteFrame, this._assembler, null)
        }
    }

    protected _updateSimulationSpace(): void {
        if (typeof this._renderEntity?.setRenderTransform !== 'function') return

        if (this.simulationSpace === Space.Local) {
            this._renderEntity.setRenderTransform(this.node)
            this._renderEntity.setUseLocal(true)
        } else if (this.simulationSpace === Space.World) {
            this._renderEntity.setRenderTransform(null)
            this._renderEntity.setUseLocal(false)
        }
    }

    protected lateUpdate(dt: number): void {
        if (this.isPlaying) {
            this.time += dt
            this._simulator.step(dt)
        }
    }

    public emitting(deltaTime: number): void {
        const startDelay = this.startDelay.evaluate(0, 1)
        if (this.time > startDelay) {
            if (this.time > this.duration + startDelay) {
                if (!this.loop) {
                    this.isEmitting = false
                }
            }
        }

        if (this.isEmitting) {
            this.emitRateTimeCounter += this.rateOverTime.evaluate(this.time / this.duration, 1) * deltaTime

            if (this.emitRateTimeCounter > 1) {
                const emitNum = Math.floor(this.emitRateTimeCounter)
                this.emitRateTimeCounter -= emitNum

                this.emit(emitNum, deltaTime)
            }

            for (const burst of this.bursts) {
                burst.update(this, deltaTime)
            }
        }

        const gravityModifier = this.gravityModifier.evaluate(this.time / this.duration, 1)

        if (this._simulator.particles.length === 0 && !this.isEmitting && this.isPlaying) {
            this._finishedSimulation()
            return
        }

        this._simulator.particles.forEach((particle, _) => {
            this.sizeOvertimeModule.update(particle)
            this.rotationOvertimeModule.update(particle, deltaTime)
            this.colorOvertimeModule.update(particle)
            // Apply velocity
            this.velocityOvertimeModule.update(particle, deltaTime)
            this.applyGravityModifier(particle, gravityModifier)
            this.limitVelocityOvertimeModule.update(particle, deltaTime)

            particle.pos.add(particle.velocity.clone().multiplyScalar(deltaTime))
        })
    }

    public emit(count: number, deltaTime: number) {
        if (this.spriteFrame === null) return

        const loopDelta = (this.time % this.duration) / this.duration

        for (let i = 0; i < count; i++) {
            const particle = this._simulator.emitParticle()

            particle.init()

            // apply shapeModule
            this.shapeModule.emit(particle)

            // apply world space
            if (this.simulationSpace === Space.World) {
                const m = this.node.worldMatrix
                particle.pos.x += m.m12
                particle.pos.y += m.m13
            }

            // apply startSpeed
            const curveStartSpeed = this.startSpeed.evaluate(loopDelta, this.rand())
            particle.velocity.multiplyScalar(curveStartSpeed)

            // apply width and height
            particle.width = this.spriteFrame.width
            particle.height = this.spriteFrame.height

            // apply startSize
            const sizeX = this.startSizeX.evaluate(loopDelta, this.rand())
            const scale = this.startSize2D
                ? new Vec3(sizeX, this.startSizeY.evaluate(loopDelta, this.rand()), 1)
                : new Vec3(sizeX, sizeX, 1)
            particle.startSize = scale.clone()
            particle.size = scale.clone()

            // apply startRotation
            const rotationZ = this.startRotationZ.evaluate(loopDelta, this.rand())
            const rotation = this.startRotation3D
                ? new Vec3(
                      this.startRotationX.evaluate(loopDelta, this.rand()),
                      this.startRotationY.evaluate(loopDelta, this.rand()),
                      rotationZ,
                  )
                : new Vec3(0, 0, rotationZ)
            particle.rotation = rotation

            // apply startColor
            particle.startColor = this.startColor.evaluate(loopDelta, this.rand()).clone()
            particle.color = particle.startColor.clone()

            // apply startLifetime
            particle.startLifetime = this.startLifetime.evaluate(loopDelta, this.rand()) + deltaTime
            particle.remainingLifetime = particle.startLifetime

            particle.randomSeed = randomRangeInt(0, 233280)
        }
    }

    private applyGravityModifier(particle: Particle, gravityModifier: number): void {
        particle.velocity.add(new Vec3(0, -9.8 * gravityModifier, 0))
    }

    private rand(): number {
        return pseudoRandom(randomRangeInt(0, bits.INT_MAX))
    }
}

setClassAttr(ParticleSystem, 'color', 'visible', false)
