import ComponentUtils from 'db://assets/utils/ComponentUtils'
import type AnimAbortSignal from 'db://assets/utils/common/AnimAbortSignal'
import { _decorator, CCInteger, Component, UITransform, Vec3 } from 'cc'
import { ScrollViewLayout } from '../../../../../../common/scroll-view-layout/ScrollViewLayout'
import type { ViewNode } from '../../../../../../common/scroll-view-layout/ViewNode'
import { Leader, type LeaderConfig } from './Leader'

const { ccclass, property } = _decorator
const { player } = codex

const {
    Utils: { Array: A },
} = Ludex

@ccclass('Leaders')
export class Leaders extends Component {
    @property(ScrollViewLayout)
    public scrollView!: ScrollViewLayout

    @property(Leader)
    private tempLeader!: Leader

    @property(CCInteger)
    public maxLeader!: number

    private leaderData: Ludex.Plugins.Leaderboards.Leader[] = []

    private displayLeaderData: Ludex.Plugins.Leaderboards.Leader[] = []
    private displayPlayerLeader: Ludex.Plugins.Leaderboards.Leader | null = null

    private currentPlayerId!: string

    private isHidden = true

    public uiTransform!: UITransform

    protected onLoad() {
        this.uiTransform = this.getComponent(UITransform)!

        this.validateRequiredComponents()
    }

    private validateRequiredComponents(): void {
        ComponentUtils.lazyValidateProperties(this)
    }

    public __preload() {
        this.currentPlayerId = player.getPlayerId()
    }

    @ComponentUtils.onlyRunInLifeCycleAsync
    public async updateData(data: Ludex.Plugins.Leaderboards.Leader[]): Promise<void> {
        this.isHidden = false
        this.leaderData = data
        this.leaderData.forEach((leader, index) => {
            leader.rank = index + 1
        })

        this.setupDisplayLeadersData()
    }

    public initScrollViews(): void {
        this.scrollView.setMaxNodeCount(this.leaderData.length)

        this.scrollView.init((node: ViewNode) => {
            const leader = node.getComponent(Leader)!
            const leaderData = this.displayLeaderData[node.order]
            if (!leaderData) return

            const { playerId, photo, name, score } = leaderData

            leader.setVisible(true)

            leader.updateInfo(
                {
                    playerId,
                    isHighlight: this.currentPlayerId === playerId,
                    name,
                    photo,
                    rank: node.order + 1,
                    score,
                },
                this.maxLeader,
            )
        })
    }

    @ComponentUtils.onlyRunInLifeCycleAsync
    public async playRankChangeAnimation(
        signal: AnimAbortSignal,
        oldScore: number,
        newScore: number,
        newRank: number,
    ): Promise<void> {
        if (oldScore >= newScore) return

        const oldLeaderData = this.displayLeaderData

        const playerLeaderData = A.search(oldLeaderData, (leader) => leader.playerId === player.getPlayerId())
        if (!playerLeaderData) return

        const oldRank = playerLeaderData.rank

        const permutedLeaderData = oldLeaderData[newRank - 1]
        if (!permutedLeaderData) return

        const playerLeaderNode = this.scrollView.getNodeByOrder(playerLeaderData.rank - 1)
        if (!playerLeaderNode) return
        const playerLeader = playerLeaderNode?.getComponent(Leader)
        if (!playerLeader) return

        playerLeader.setVisible(false)
        this.tempLeader.setVisible(true)
        this.tempLeader.updatePhoto({
            playerId: player.getPlayerId(),
            photo: playerLeaderData.photo,
        })
        this.tempLeader.node.worldPosition = playerLeaderNode.node.worldPosition.clone()
        await this.tempLeader.runScaleAnimation(1.1, 0.5, signal)

        const scrollDuration = Math.min((oldRank - newRank) * 0.5, 2)

        const scrollAnimations = [
            this.tempLeader.runUpdateDataAnimation(oldRank, newRank, oldScore, newScore, scrollDuration, signal),
            this.scrollToLeaderRank(newRank, scrollDuration, signal),
        ]

        const maxScrollOffsetY = this.scrollView.scrollView.getMaxScrollOffset().y
        const scrollOffsetY = this.scrollView.getNodeScrollOffset(newRank - 1)

        let endWorldPosition = Vec3.ZERO

        if (scrollOffsetY < 0) {
            endWorldPosition = new Vec3(
                this.tempLeader.node.worldPosition.x,
                this.node.worldPosition.y +
                    this.scrollView.getNodePosition(newRank - 1).y +
                    this.uiTransform.height / 2,
                this.tempLeader.node.worldPosition.z,
            )
        } else if (this.scrollView.scrollView.getScrollOffset().y >= maxScrollOffsetY - 5) {
            if (scrollOffsetY < maxScrollOffsetY) {
                endWorldPosition = new Vec3(
                    this.tempLeader.node.worldPosition.x,
                    this.scrollView.viewport.node.worldPosition.y,
                    this.tempLeader.node.worldPosition.z,
                )
            } else {
                const permutedLeader = this.scrollView.getNodeByOrder(newRank - 1)
                endWorldPosition = permutedLeader?.node.worldPosition.clone() ?? Vec3.ZERO
            }
        }

        if (!endWorldPosition.equals(Vec3.ZERO)) {
            scrollAnimations.push(this.tempLeader.runWorldPositionAnimation(endWorldPosition, scrollDuration, signal))
        }

        await Promise.all(scrollAnimations)
        await Promise.all([
            this.tempLeader.runScaleAnimation(1, 0.25, signal),
            this.playPostRankChangeAnimation(newRank, oldRank, signal),
        ])

        this.tempLeader.setVisible(false)
        playerLeaderNode.node.worldPosition = this.tempLeader.node.worldPosition.clone()

        this.setupDisplayLeadersData()
        this.scrollView.updateNodeVisibility()
        this.scrollView.updateLayout()
    }

    public async playShowLeadersAnimation(signal: AnimAbortSignal, stagger = 0.1): Promise<void> {
        if (this.isHidden) return
        let visibleLeaders: Leader[] = []

        const viewNodes = this.scrollView.getAllNodes()

        viewNodes
            .sort((a, b) => a.order - b.order)
            .forEach((node, index) => {
                const leader = node.getComponent(Leader)!
                leader.setVisible(index < this.leaderData.length)

                if (node.isVisible()) {
                    visibleLeaders.push(leader)
                }
            })

        if (visibleLeaders.length < 1) return
        visibleLeaders = visibleLeaders.slice(0, viewNodes.length - 1)

        const duration = 0.3
        await Promise.all(
            visibleLeaders.map(async (leader, index) => {
                if (this.isHidden) return
                await leader.runShowUpAnimation(signal, index * stagger, duration)
            }),
        )
    }

    @ComponentUtils.onlyRunInLifeCycleAsync
    public async scrollToPlayerLeader(duration: number, signal: AnimAbortSignal): Promise<void> {
        if (!this.displayPlayerLeader) return

        await this.scrollToLeaderRank(this.displayPlayerLeader.rank, duration, signal)
    }

    public updateDisplayPlayerLeaderForRankChanging(
        previousScore: number,
        currentScore: number,
        currentRank: number,
    ): boolean {
        if (!this.displayPlayerLeader) return false

        this.displayPlayerLeader.score = previousScore

        this.displayLeaderData.sort((a, b) => b.score - a.score)
        const previousRank = this.displayLeaderData.indexOf(this.displayPlayerLeader) + 1

        if (currentRank === previousRank) {
            this.displayPlayerLeader.score = currentScore
            this.displayPlayerLeader.rank = currentRank
            return false
        }

        this.displayPlayerLeader.rank = previousRank

        this.tempLeader.updateInfo({
            playerId: player.getPlayerId(),
            isHighlight: true,
            name: this.displayPlayerLeader.name,
            photo: this.displayPlayerLeader.photo,
            rank: this.displayPlayerLeader.rank,
            score: previousScore,
        })

        return true
    }

    public hideLeaders(): void {
        this.isHidden = true

        const leaders = this.scrollView.getAllNodes().map((node) => node.getComponent(Leader))

        for (const leader of leaders) {
            leader?.setVisible(false)
        }
    }

    public getLeaderData(): Ludex.Plugins.Leaderboards.Leader[] {
        return this.leaderData
    }

    private setupDisplayLeadersData(): void {
        const playerData = A.search(this.leaderData, (leader) => leader.playerId === this.currentPlayerId)

        if (playerData) {
            this.displayLeaderData = this.leaderData.filter((leader) => leader.playerId !== this.currentPlayerId)
            this.displayPlayerLeader = { ...playerData }
            this.displayLeaderData.splice(playerData.rank - 1, 0, this.displayPlayerLeader)
        } else {
            this.displayPlayerLeader = null
            this.displayLeaderData = this.leaderData
        }
    }

    private async playPostRankChangeAnimation(start: number, end: number, signal: AnimAbortSignal): Promise<void> {
        const startIndex = start ?? 0
        const nodes = this.scrollView
            .getVisibleNodes()
            .filter((node) => node.order >= startIndex - 1 && node.order < end)
        const leaders = nodes.map((node) => node.getComponent(Leader)!)

        // Distance between each leader
        const offset = this.scrollView.nodeHeight + this.scrollView.spacing

        const leaderAnimations: Promise<void>[] = []

        leaders.forEach((leader, index) => {
            const node = nodes[index]
            if (!node) return

            leader.updateRank({
                rank: node.order + 2,
                isHighlight: false,
            } as LeaderConfig)

            const leaderWorldPosition = leader.node.worldPosition.clone()
            leaderAnimations.push(
                leader.runWorldPositionAnimation(leaderWorldPosition.add3f(0, -offset, 0), 0.25, signal),
            )
        })

        await Promise.all(leaderAnimations)
    }

    @ComponentUtils.onlyRunInLifeCycleAsync
    private async scrollToLeaderRank(rank: number, duration: number, signal: AnimAbortSignal): Promise<void> {
        await this.scrollView.scrollToOrder(rank - 1, signal, duration)
    }
}
