import { _decorator, type animation, Component, Label, Node, tween, Vec3 } from 'cc'
import { TapperedProgressBar } from '../../../../../../components/tappered-progress-bar/TapperedProgressBar'
import { ScreenKeysEnum, ScreenNames } from '../../../../../../constants/ScreenKeys'
import { ParticleSystem } from '../../../../../../particles/ParticleSystem'
import AnimUtils from '../../../../../../utils/AnimUtils'
import ComponentUtils from '../../../../../../utils/ComponentUtils'
import DailyRewardsUtils from '../../../../../../utils/DailyRewardsUtils'

const { ccclass, property } = _decorator
const {
    Utils: { Signal: S, String: Str },
    Configs: {
        DailyRewards: { MockTime },
    },
    Events,
} = Ludex
const { event } = codex
type Signal = InstanceType<typeof S.Signal>

const DATE_LENGTH = MockTime > 0 ? MockTime : 864e5

@ccclass('DailyRewardsWidget')
export class DailyRewardsWidget extends Component {
    @ComponentUtils.required()
    @property('cc.animation.AnimationController')
    readonly animationController!: animation.AnimationController

    @ComponentUtils.required()
    @property(ParticleSystem)
    readonly particles!: ParticleSystem

    @ComponentUtils.required()
    @property(Label)
    readonly timeLabel!: Label

    @ComponentUtils.required()
    @property(TapperedProgressBar)
    readonly progressBar!: TapperedProgressBar

    @ComponentUtils.required()
    @property(Node)
    readonly progressBarAndLabel!: Node

    protected readonly signalController = new S.SignalController()

    protected __preload(): void {
        ComponentUtils.validateRequiredProperties(this, DailyRewardsWidget)
    }

    protected onEnable(): void {
        event.on(Events.CLOSED_SCREEN, this.handleScreenClosed)
    }

    protected onDisable(): void {
        event.off(Events.CLOSED_SCREEN, this.handleScreenClosed)
        this.signalController.abort()
    }

    protected onDestroy(): void {
        this.signalController.abort()
    }

    protected handleScreenClosed = ({ screenName }: SwitchScreenPayload) => {
        if (screenName !== ScreenNames[ScreenKeysEnum.DAILY_REWARDS_SCREEN]) return
        this.refresh()
    }

    protected setParticles(play: boolean): void {
        if (play) {
            this.particles.node.active = true
            this.particles.play()
        } else {
            this.particles.stop()
            this.particles.node.active = false
        }
    }

    protected setProgressBarAndLabelVisibility(visible: boolean, signal: Signal): void {
        AnimUtils.runTweenAsync(
            tween(this.progressBarAndLabel).to(
                visible ? 0.5 : 0.2,
                { scale: visible ? Vec3.ONE : Vec3.ZERO },
                { easing: visible ? 'backOut' : 'sineIn' },
            ),
            signal,
        )
    }

    protected startUpdateRemainingTimeSchedule(signal: Signal) {
        const controller = new S.SignalController()

        const callback = async () => {
            const sig = controller.renew()
            sig.chain(signal)

            const remaining = await DailyRewardsUtils.getRemainingTime()
            if (sig.aborted) return

            this.updateRemainingTime(remaining)

            if (remaining === 0) {
                this.unschedule(callback)
            }
        }

        callback()
        this.schedule(callback, 1)
        signal.once(() => this.unschedule(callback))
    }

    protected async startSelfRefreshSchedule(signal: Signal) {
        const remaining = await DailyRewardsUtils.getRemainingTime()
        if (signal.aborted) return

        if (remaining === 0) return

        const callback = () => {
            if (!signal.aborted) this.refresh()
            this.unschedule(callback)
        }

        this.schedule(callback, remaining / 1e3)
        signal.once(() => this.unschedule(callback))
    }

    @ComponentUtils.onlyRunInLifeCycle
    protected updateRemainingTime(remainingTime: number) {
        if (remainingTime === 0) {
            this.updateTimeLabel('00:00:00')
        } else {
            const hours = Str.padStart(Math.floor(remainingTime / 36e5).toString(), 2, '0')
            const minutes = Str.padStart(Math.floor((remainingTime % 36e5) / 6e4).toString(), 2, '0')
            const seconds = Str.padStart(Math.floor((remainingTime % 6e4) / 1e3).toString(), 2, '0')
            const text = `${hours}:${minutes}:${seconds}`
            this.updateTimeLabel(text)
        }

        const progress = 1 - remainingTime / DATE_LENGTH
        this.updateProgressBar(progress)
    }

    @ComponentUtils.onlyRunInLifeCycle
    protected updateTimeLabel(text: string) {
        this.timeLabel.string = text
    }

    @ComponentUtils.onlyRunInLifeCycle
    protected updateProgressBar(progress: number) {
        this.progressBar.progress = progress
    }

    public async refresh() {
        const signal = this.signalController.renew()
        const claimable = await DailyRewardsUtils.hasClaimableRewards()
        if (signal.aborted) return
        this.animationController.setValue('claimable', claimable)
        this.setParticles(claimable)
        this.setProgressBarAndLabelVisibility(!claimable, signal)
        this.startUpdateRemainingTimeSchedule(signal)
        this.startSelfRefreshSchedule(signal)
    }
}
