export class PerfCounter {
    private time: number = 0
    private _value: number = 0

    public get value(): number {
        return this._value
    }

    constructor() {
        this.time = 0
        this._value = 0
    }

    public start(now: number = 0): void {
        this.time = now
    }

    public end(now: number = 0): void {
        this._value = now - this.time
    }
}
