import { Component, ProgressBar, UIOpacity, _decorator, tween } from 'cc'
import { Localize } from '../../components/text/Localize'
import AnimUtils from '../../utils/AnimUtils'
import ComponentUtils from '../../utils/ComponentUtils'
const { ccclass, property } = _decorator

type Locale = Ludex.Plugins.Language.Locale
type Locales = Ludex.Plugins.Language.Locales

type Key = keyof Locales[Locale]['text']

const PROGRESS_AMOUNT = 0.9
const LOADING_DURATION = 8
const HINT_UPDATE_INTERVAL = 5

const LOCALIZED_MESSAGE_KEYS = [
    // ADS:
    'interMsg.reviveAd',
    'interMsg.doubleAd',

    // GAMEMODE
    'interMsg.ranked',
    'interMsg.friendChallenge',
    'interMsg.bragChallenge',

    // GAMEPLAY
    'interMsg.clearTiles',
    'interMsg.beatClock',
    'interMsg.clearBombs',
    'interMsg.seasonPack',
    'interMsg.collectStars',

    // LEADERBOARDS
    'interMsg.competeInLb',
    'interMsg.competeWithFriends',

    // POWERUPS
    'interMsg.thunder',
    'interMsg.hint',
    'interMsg.pliers',
    'interMsg.tornado',

    // REWARDS
    'interMsg.coins',
    'interMsg.seasonTiles',
    'interMsg.dailyLogin',
    'interMsg.levelUp',
    'interMsg.seasonRewards',
] as const satisfies Key[]

@ccclass('LoadingFullScreen')
export class LoadingFullScreen extends Component {
    @ComponentUtils.required()
    @property(ProgressBar)
    private progressBar!: ProgressBar

    @ComponentUtils.required()
    private uiOpacity!: UIOpacity

    @ComponentUtils.required()
    @property(Localize)
    private loadingLabelLocalizer!: Localize

    public __preload(): void {
        this.uiOpacity = this.getComponent(UIOpacity) as UIOpacity
        ComponentUtils.validateRequiredProperties(this)
    }

    public onLoad(): void {
        this.uiOpacity.opacity = 0
        this.progressBar.progress = 0
    }

    public async show(_sceneName: string): Promise<void> {
        this.uiOpacity.opacity = 0
        this.progressBar.progress = 0

        const fadeTween = tween(this.uiOpacity).to(0.5, { opacity: 255 }, { easing: 'cubicOut' })
        const loadingTween = tween(this.progressBar).to(
            LOADING_DURATION,
            { progress: PROGRESS_AMOUNT },
            { easing: 'cubicOut' },
        )

        this.runLoadingTextAnimation()

        AnimUtils.runTweenAsync(fadeTween)
        await AnimUtils.runTweenAsync(loadingTween)
    }

    public async hide(_sceneName: string): Promise<void> {
        if (!this.uiOpacity || !this.progressBar) return

        this.uiOpacity.opacity = 255
        this.progressBar.progress = PROGRESS_AMOUNT

        const fadeTween = tween(this.uiOpacity).to(0.5, { opacity: 0 }, { easing: 'cubicIn' })
        const loadingTween = tween(this.progressBar).to(0.2, { progress: 1 }, { easing: 'cubicIn' })

        this.runLoadingTextAnimation()
        await AnimUtils.runTweenAsync(loadingTween)
        this.stopLoadingTextAnimation()

        await AnimUtils.runTweenAsync(fadeTween)
        this.node.active = false
    }

    private runLoadingTextAnimation() {
        this.updateHintText()
        this.schedule(() => this.updateHintText(), HINT_UPDATE_INTERVAL)
    }

    private updateHintText() {
        if (!this.loadingLabelLocalizer) return
        const hintText = this.getHintText()
        if (!hintText) return
        this.loadingLabelLocalizer.key = hintText
    }

    private stopLoadingTextAnimation() {
        this.unscheduleAllCallbacks()
    }

    private getHintText() {
        return LOCALIZED_MESSAGE_KEYS[Math.floor(Math.random() * LOCALIZED_MESSAGE_KEYS.length)]
    }
}
