import { _decorator, Component, Label, Node } from 'cc'
import { Localize } from '../../components/text/Localize'
import ComponentUtils from '../../utils/ComponentUtils'
import GuideHand from './GuideHand'
import GuideItemButton from './GuideItemButton'
import GuideMasks from './GuideMasks'
import GuideTextBox from './GuideTextBox'

const { ccclass, property } = _decorator

@ccclass('GuideUI')
export class GuideUI extends Component {
    @ComponentUtils.required()
    @property(GuideHand)
    readonly guideHand!: GuideHand

    @ComponentUtils.required()
    @property(GuideTextBox)
    readonly guideTextBox!: GuideTextBox

    @ComponentUtils.required()
    @property(Label)
    readonly guideInfoText!: Label

    @ComponentUtils.required()
    @property(Localize)
    readonly guideInfoTextLocalizer!: Localize

    @ComponentUtils.required()
    @property(GuideMasks)
    readonly guideMasks!: GuideMasks

    @ComponentUtils.required()
    @property(Node)
    readonly guideSkipButton!: Node

    @ComponentUtils.required()
    @property(GuideItemButton)
    readonly guideItemButton!: GuideItemButton

    protected __preload(): void {
        ComponentUtils.validateRequiredProperties(this)
    }
}
