import { _decorator, CCBoolean, Component, game, View, Widget } from 'cc'
import GameEvents from '../../constants/GameEvents'
import AdsUtils from '../../utils/AdsUtils'
import CocosUtils from '../../utils/CocosUtils'
import ComponentUtils from '../../utils/ComponentUtils'

const { ccclass, requireComponent, property } = _decorator
const { ads, event } = codex

const {
    Events,
    Plugins: { Ads: { Types: AdsTypes, Status: AdStatus } = {} },
    Utils: { Array: A, Function: F, Device: D },
} = Ludex

type AdEventPayload = Ludex.Plugins.Ads.AdEventPayload

const TOP_POSITIONS = ['top', 'topleft', 'topright']
const BOTTOM_POSITIONS = ['bottom', 'bottomleft', 'bottomright']
const LEFT_POSITIONS = ['left', 'topleft', 'bottomleft']
const RIGHT_POSITIONS = ['right', 'topright', 'bottomright']

@ccclass('ResizeWithBannerAd')
@requireComponent(Widget)
export class ResizeWithBannerAd extends Component {
    @ComponentUtils.required()
    @property(CCBoolean)
    private includeSAB = true

    @ComponentUtils.required()
    @property(CCBoolean)
    private includeRightBanner = true

    @ComponentUtils.required()
    @property(CCBoolean)
    private includeBottomBanner = true

    @ComponentUtils.required()
    @property(CCBoolean)
    private includeLeftBanner = true

    @ComponentUtils.required()
    @property(CCBoolean)
    private closeWhenAdFailed = false

    @ComponentUtils.required()
    @property(Widget)
    private widget!: Widget

    public __preload(): void {
        ComponentUtils.validateRequiredProperties(this)

        this.setupEventListeners$('on')
    }

    public onDestroy(): void {
        this.setupEventListeners$('off')
    }

    public start(): void {
        this.handleOnResize$()
    }

    private setupEventListeners$ = (state: 'on' | 'off'): void => {
        const handlers = [
            [Events.AD_SHOWING, this.handleOnAdShowing$],
            [Events.AD_CLOSED, this.handleOnAdClosed$],
            [Events.AD_SHOW_FAILED, this.handleOnAdFailed$],
            [GameEvents.VIEW_RESIZED, this.handleOnResize$],
        ] as const

        handlers.forEach(([evt, func]) => {
            event[state](evt, func)
        })
    }

    private handleOnAdShowing$ = (payload: AdEventPayload): void => {
        const { placementId, type } = payload
        if (!this.checkValid$() || !placementId || !AdsTypes) return
        if (type !== AdsTypes.BANNER) return

        try {
            const ad = ads.getAd(AdsTypes.BANNER, placementId)
            if (!ad) return
        } catch (error) {
            console.debug('Error getting banner ad:', error)
            return
        }

        this.resizeWhenBannerAdShow$(placementId)
        this.emitResizeEvent()
    }

    private handleOnAdClosed$ = (payload: AdEventPayload): void => {
        const { placementId, type } = payload
        if (!this.checkValid$() || !placementId || !AdsTypes) return
        if (type !== AdsTypes.BANNER) return

        try {
            const ad = ads.getAd(AdsTypes.BANNER, placementId)
            if (!ad) return
        } catch (error) {
            console.debug('Error getting banner ad:', error)
            return
        }

        this.resizeWhenBannerAdClose$(placementId)
        this.emitResizeEvent()
    }

    private handleOnAdFailed$ = (payload: AdEventPayload): void => {
        const { placementId, type } = payload
        if (!this.closeWhenAdFailed || !this.checkValid$() || !placementId || !AdsTypes) return
        if (type !== AdsTypes.BANNER) return

        this.resizeWhenBannerAdClose$(placementId)
        this.emitResizeEvent()
    }

    private handleOnResize$ = (): void => {
        if (!this.checkValid$() || !AdsTypes || !AdStatus) return
        if (!AdsUtils.isAdEnabled(AdsTypes.BANNER)) {
            this.resizeSafeAreaBottom$()
            return
        }

        const bannerAds = ads.getAdsByType(AdsTypes.BANNER)
        if (bannerAds.length === 0) {
            this.resizeSafeAreaBottom$()
            return
        }

        bannerAds.forEach((ad) => {
            const status = ads.getAdStatus(AdsTypes.BANNER, ad.placementId)
            if (!status) return

            if (status === AdStatus.SHOWING || D.isIOS()) {
                this.resizeWhenBannerAdShow$(ad.placementId)
            } else if (status === AdStatus.IDLE) {
                this.resizeWhenBannerAdClose$(ad.placementId)
            }
        })
    }

    private getBannerHeight$ = (placementId: string, gameZoom: number): number => {
        try {
            const { PlacementId, Position } = ads.getBannerConfig(placementId)

            const includeSAB = this.includeSAB && Position === 'bottom'
            return ads.getBannerHeight(PlacementId, gameZoom, includeSAB)
        } catch (error) {
            console.debug('[ResizeWithBannerAd] Error getting banner ad:', error)
            return 0
        }
    }

    private resizeWhenBannerAdShow$ = (placementId: string) => {
        let top: number | undefined
        let bottom: number | undefined
        let left: number | undefined
        let right: number | undefined

        const gameZoom = CocosUtils.cssSizeToWorldSize(1)
        const bannerHeight = this.getBannerHeight$(placementId, gameZoom)

        const { BannerWidth, Position } = ads.getBannerConfig(placementId)

        if (Position === 'top') {
            top = bannerHeight
        } else if (Position === 'bottom') {
            if (this.includeBottomBanner) {
                bottom = bannerHeight
            } else if (this.includeSAB) {
                bottom = ads.getSafeAreaBottom(gameZoom)
            }
        } else if (Position === 'left' || Position === 'right') {
            if (BannerWidth === undefined) return

            let bannerWidth = BannerWidth
            if ('getBannerOffset' in GameSDK.extra) {
                bannerWidth += GameSDK.extra.getBannerOffset()
            }

            if (Position === 'left' && this.includeLeftBanner) {
                left = bannerWidth * gameZoom
            } else if (Position === 'right' && this.includeRightBanner) {
                right = bannerWidth * gameZoom
            }
        }

        this.updateWidgets$({ top, bottom, left, right })
    }

    private resizeWhenBannerAdClose$ = (placementId: string): void => {
        const { Position } = ads.getBannerConfig(placementId)

        const gameZoom = CocosUtils.cssSizeToWorldSize(1)
        const bottom = this.includeSAB ? ads.getSafeAreaBottom(gameZoom) : 0

        this.updateWidgets$({
            top: A.has(TOP_POSITIONS, Position) ? 0 : undefined,
            bottom: A.has(BOTTOM_POSITIONS, Position) ? bottom : undefined,
            left: A.has(LEFT_POSITIONS, Position) ? 0 : undefined,
            right: A.has(RIGHT_POSITIONS, Position) ? 0 : undefined,
        })
    }

    private resizeSafeAreaBottom$ = (): void => {
        const gameZoom = CocosUtils.cssSizeToWorldSize(1)
        const bottom = this.includeSAB ? ads.getSafeAreaBottom(gameZoom) : 0

        this.updateWidgets$({ bottom })
    }

    private updateWidgets$ = (config: { top?: number; bottom?: number; left?: number; right?: number }): void => {
        const { top, bottom, left, right } = config
        this.widget.top = top ?? this.widget.top
        this.widget.bottom = bottom ?? this.widget.bottom
        this.widget.left = left ?? this.widget.left
        this.widget.right = right ?? this.widget.right

        View.instance.emit('canvas-resize')
    }

    private emitResizeEvent = F.debounce((): void => {
        game.emit(GameEvents.WIDGET_RESIZED)
    }, 100)

    private checkValid$ = (): boolean => {
        return this.node.activeInHierarchy && this.node.parent !== null && this.node.parent !== undefined
    }
}
