import { _decorator } from 'cc'
import { DayBlock, type IDayPayload } from './DayBlock'

const { ccclass } = _decorator

@ccclass('DayBlockRed')
class DayBlockRed extends DayBlock {
    protected updateBackground(payload: IDayPayload): void {
        if (!this.blueTopBackground || !this.blueBackground) {
            return
        }

        switch (payload.status) {
            case 'ready':
                this.background.spriteFrame = this.blueBackground
                this.topBackground.spriteFrame = this.blueTopBackground
                break
            case 'waiting':
                this.background.spriteFrame = this.defaultBackground
                this.topBackground.spriteFrame = this.defaultTopBackground
                break
        }
    }

    protected updateDayLabel(payload: IDayPayload): void {
        this.dayLocalizer.setParams([payload.day.toString()])

        let dayLabelColor = this.defaultDayLabelOutlineColor

        if (payload.status === 'ready') {
            dayLabelColor = this.blueDayLabelOutlineColor
        }

        this.dayLabel.shadowColor = dayLabelColor
        this.dayLabel.outlineColor = dayLabelColor
    }
}

export default DayBlockRed
