import { type IVec2Like, Vec2 } from 'cc'

function getClosestPairIndex(points: ReadonlyArray<IVec2Like>): [number, number] {
    if (points.length < 2) {
        throw new Error('Not enough points to find a closest pair')
    }

    const first = points[0] as IVec2Like
    const second = points[1] as IVec2Like
    const distance = Vec2.distance(first, second)

    let matrix: Array<number> = [distance]
    let maxCoords: [number, number] = [0, 1]

    const getClosestPairLoopWithPoints = getClosestPairLoop(points)

    for (let i = 0; i < points.length - 1; i++) {
        for (let j = i + 1; j < points.length; j++) {
            const [nextMatrix, nextMaxCoords] = getClosestPairLoopWithPoints(matrix, maxCoords, i, j)
            matrix = nextMatrix
            maxCoords = nextMaxCoords
        }
    }

    return maxCoords
}

function getClosestPair(points: ReadonlyArray<IVec2Like>): [IVec2Like, IVec2Like] {
    const [i, j] = getClosestPairIndex(points)
    return [points[i] as IVec2Like, points[j] as IVec2Like]
}

const getClosestPairLoop = (points: ReadonlyArray<IVec2Like>) => {
    const getDistanceFromPoints = getDistance(points)

    return (
        matrix: Array<number>,
        maxCoords: [number, number],
        i: number,
        j: number,
    ): [Array<number>, [number, number]] => {
        if (i === j) return [matrix, maxCoords]
        if (i > j) return getClosestPairLoop(points)(matrix, maxCoords, j, i)

        const newDistance = getDistanceFromPoints(i, j)
        const oldDistance = getCell(matrix, points.length)(maxCoords[0], maxCoords[1])

        if (newDistance >= oldDistance) return [matrix, maxCoords]

        return [setCell(matrix, points.length)(i, j, newDistance), [i, j]]
    }
}

const getFlatCoord =
    (size: number) =>
    (i: number, j: number): number =>
        (i * (2 * size - i - 1)) / 2 + (j - i - 1)

const setCell =
    (matrix: Array<number>, size: number) =>
    (i: number, j: number, value: number): Array<number> => {
        if (i === j) return matrix
        if (i > j) return setCell(matrix, size)(j, i, value)
        const index = getFlatCoord(size)(i, j)
        return [...matrix.slice(0, index), value, ...matrix.slice(index + 1)]
    }

const getCell =
    (matrix: Array<number>, size: number) =>
    (i: number, j: number): number => {
        if (i === j) return Infinity
        if (i > j) return getCell(matrix, size)(j, i)
        const index = getFlatCoord(size)(i, j)
        return matrix[index] ?? Infinity
    }

const getDistance =
    (points: ReadonlyArray<IVec2Like>) =>
    (i: number, j: number): number => {
        const a = points[i] as IVec2Like
        const b = points[j] as IVec2Like
        return Vec2.distance(a, b)
    }

export { getClosestPair, getClosestPairIndex }
