import { _decorator, Component, ScrollView, UITransform } from 'cc'
import { OpacityToggler } from '../../../../../components/common/OpacityToggler'
import { PanelAnimator } from '../../../../../components/panel-animator/PanelAnimator'
import TournamentEvents from '../../../../../constants/Tournament'
import GameplayEvents from '../../../../../gameplay/constants/GameEvents'
import ComponentUtils from '../../../../../utils/ComponentUtils'
import AnimAbortSignal from '../../../../../utils/common/AnimAbortSignal'
import MatchUtils from '../../../../../utils/MatchUtils'
import ScreenUtils from '../../../../../utils/ScreenUtils'
import TournamentUtils, { type TournamentData } from '../../../../../utils/TournamentUtils'
import { TournamentBlockSkeleton } from './TournamentBlockSkeleton'
import { TournamentsBoard } from './TournamentsBoard'
import { TournamentsNotAvailable } from './TournamentsNotAvailable'

const { ccclass, property } = _decorator
const { storage, match } = codex

const {
    Utils: { Object: O, Array: A },
} = Ludex

export interface ITournamentLeadersPayload {
    leaders: Ludex.Plugins.Leaderboards.Leader[]
    player?: Ludex.Plugins.Leaderboards.Leader
    amountPlayer: number
}

@ccclass('TournamentContent')
export class TournamentContent extends Component {
    @property({ type: PanelAnimator })
    private panelAnimator!: PanelAnimator

    @property(TournamentsBoard)
    private tournamentsBoard!: TournamentsBoard

    @property(TournamentsNotAvailable)
    private tournamentsNotAvailable!: TournamentsNotAvailable

    @property(ScrollView)
    private scrollView!: ScrollView

    @property({ type: [TournamentBlockSkeleton] })
    private skeletons: TournamentBlockSkeleton[] = []

    @property(OpacityToggler)
    private contentOpacity!: OpacityToggler

    private uiTransform!: UITransform

    private animAbortSignal: AnimAbortSignal | null = null

    //? soft abort signal should only be used for non-critical animations. called immediately upon page transitionOut
    private softAbortSignal: AnimAbortSignal | null = null

    public onLoad(): void {
        this.uiTransform = this.node.getComponent(UITransform)!

        this.validateRequiredComponents()
    }

    private validateRequiredComponents() {
        ComponentUtils.lazyValidateProperties(this)
    }

    public listenEvents(): void {
        this.scrollView.node.on(ScrollView.EventType.SCROLLING, this.onScrolling, this)

        this.node.scene.on(TournamentEvents.RELOAD_TOURNAMENT, this.reload, this)
        this.node.scene.on(GameplayEvents.HIDE_UI, this.hideContent)
        this.node.scene.on(GameplayEvents.SHOW_UI, this.showContent)
    }

    private unlistenEvents(): void {
        this.scrollView.node.off(ScrollView.EventType.SCROLLING, this.onScrolling, this)

        this.node.scene.off(TournamentEvents.RELOAD_TOURNAMENT, this.reload, this)
        this.unlistenUIEvents()
    }

    public unlistenUIEvents(): void {
        this.node.scene.off(GameplayEvents.HIDE_UI, this.hideContent)
        this.node.scene.off(GameplayEvents.SHOW_UI, this.showContent)
    }

    private hideContent = () => {
        this.contentOpacity.changeStateFade(false, 0.1)
    }

    private showContent = () => {
        this.contentOpacity.changeStateFade(true, 0.1)
    }

    private onScrolling(): void {
        this.tournamentsBoard.updateVisibility()
    }

    public enableUIAnimator(): void {
        this.panelAnimator.enabled = true
    }

    public disableUIAnimator(): void {
        this.panelAnimator.enabled = false
    }

    public async enter(): Promise<void> {
        if (this.animAbortSignal !== null || this.softAbortSignal !== null) {
            return
        }

        this.animAbortSignal = new AnimAbortSignal()
        this.softAbortSignal = new AnimAbortSignal()

        await this.showLoading(this.animAbortSignal)
        if (this.isAborted()) return

        const tournamentData: TournamentData[] = await TournamentUtils.requestTournamentDataAsync()
        if (this.isAborted()) return

        await this.hideLoading(this.animAbortSignal)
        if (this.isAborted()) return

        if (tournamentData.length === 0 && !this.softAbortSignal?.aborted) {
            this.tournamentsNotAvailable.show()
            return
        }

        this.tournamentsNotAvailable.hide()

        this.tryShowTournamentResult()

        this.scrollView.enabled = true

        this.tournamentsBoard.updateData(tournamentData)

        await this.tournamentsBoard.showTournamentBlocks(
            this.uiTransform.contentSize.height - this.tournamentsBoard.layout.paddingTop,
            this.animAbortSignal,
        )

        this.tournamentsBoard.updateVisibility()
    }

    public async showLoading(signal: AnimAbortSignal): Promise<void> {
        this.scrollView.scrollToTop(0)
        this.scrollView.enabled = false

        await Promise.all([this.skeletons.map((skeleton) => skeleton.show(0, signal))])
    }

    private async hideLoading(signal: AnimAbortSignal): Promise<void> {
        await Promise.all([this.skeletons.map((skeleton) => skeleton.hide(signal))])
    }

    private tryShowTournamentResult(): void {
        try {
            const { animateTournamentLeader } = storage.getStorage('tournament')
            if (!animateTournamentLeader) return

            const tournamentData = TournamentUtils.getAllTournamentData()

            const { tournamentId } = match.getMatchState().customData
            if (!tournamentId) throw new Error('Tournament ID not found')

            const tournament = A.search(tournamentData, (tournament) => tournament.id === tournamentId)
            if (!tournament) throw new Error('Tournament not found')

            const data = JSON.stringify(tournament)
            ScreenUtils.showTournamentDetailScreen(data, 'tournament_end')
        } catch (error) {
            storage.setStorageData('tournament', 'animateTournamentLeader', false)
            console.warn('showTournamentResult', error)
        }
    }

    public async processCreateTournament() {
        try {
            await MatchUtils.processCreateTournamentAsync()
        } catch (error) {
            if (!O.hasOwn(error, 'code')) {
                ScreenUtils.showNotificationScreenWithLocalizedKey('error.createTourFail', 2)
                return
            }

            if (error.code === 'USER_INPUT') return

            if (error.code === 'PLAYER_HAS_TOURNAMENT') {
                ScreenUtils.showNotificationScreenWithLocalizedKey('error.tourExists', 2)
                return
            }

            if (error.code === 'INVALID_OPERATION' && O.hasOwn(error, 'message') && typeof error.message === 'string') {
                ScreenUtils.showNotificationScreen(error.message, 2)
            }

            ScreenUtils.showNotificationScreenWithLocalizedKey('error.createTourFail', 2)
        }

        this.reload()
    }

    public isAborted(): boolean {
        return this.animAbortSignal?.aborted ?? true
    }

    public cleanup(): void {
        this.unlistenEvents()

        this.animAbortSignal?.abort()
        this.animAbortSignal = null

        this.tournamentsBoard.removeTournamentBlocks()
        this.tournamentsNotAvailable.hide()
    }

    public softAbort(): void {
        this.softAbortSignal?.abort()
        this.softAbortSignal = null
    }

    public reload(): void {
        this.softAbort()
        this.cleanup()
        this.listenEvents()
        this.enter()
    }
}
