import pseudoRandomIterable from '../random/pseudoRandomIterable'
import getRandomTile from './getRandomTile'

type Grid<T> = {
    asArray(): ReadonlyArray<T>
}

function isArray<T>(i: unknown): i is ReadonlyArray<T> {
    return Array.isArray(i)
}

function getRandomTiles<T>(grid: Grid<T>, count: number, seed?: number): Set<T>
function getRandomTiles<T>(tiles: ReadonlyArray<T>, count: number, seed?: number): Set<T>

function getRandomTiles<T>(gridOrTiles: Grid<T> | ReadonlyArray<T>, count: number, seed = Date.now()): Set<T> {
    const arr = isArray(gridOrTiles) ? gridOrTiles : gridOrTiles.asArray()

    if (arr.length < count) return new Set(arr)

    const result: Set<T> = new Set()
    const rng = pseudoRandomIterable(-1, seed)
    let rngSeed = rng.next()

    while (result.size < count) {
        // ? The generator should never be exhausted when -1 is the size
        if (rngSeed.done) throw new Error('RNG exhausted')
        result.add(getRandomTile(arr, rngSeed.value))

        rngSeed = rng.next()
    }

    return result
}

export default getRandomTiles
