import { CCFloat, Component, Enum, UIOpacity, UITransform, Vec3, View, _decorator, easing, game, tween } from 'cc'
import DashboardEvents from '../../../../constants/DashboardEvents'
import DashboardScreens from '../../../../constants/DashboardScreens'
import GameEvents from '../../../../constants/GameEvents'
import AnimUtils from '../../../../utils/AnimUtils'
import ComponentUtils from '../../../../utils/ComponentUtils'
import type AnimAbortSignal from '../../../../utils/common/AnimAbortSignal'
import ScreenUtils from '../../../../utils/ScreenUtils'
import { CollectionsPage } from './collections/CollectionsPage'
import { DashboardPage } from './DashboardPage'

const { ccclass, property, disallowMultiple, requireComponent } = _decorator

const { Array: A } = Ludex.Utils

@ccclass('PageManagerItem')
class PageManagerItem {
    @property(DashboardPage)
    public page!: DashboardPage

    @property({ type: Enum(DashboardScreens) })
    public pageType!: DashboardScreens
}

@ccclass('PageManager')
@disallowMultiple
@requireComponent(UITransform)
export class PageManager extends Component {
    @ComponentUtils.required()
    @property({ type: UIOpacity, displayName: 'Background Mask', group: 'Background Mask' })
    private backgroundMask!: UIOpacity

    @property({
        type: CCFloat,
        displayName: 'Transition duration',
        group: 'Background Mask',
    })
    private backgroundMaskTransitionDuration: number = 0.5

    @property({
        type: CCFloat,
        displayName: 'Transition delay',
        group: 'Background Mask',
    })
    private backgroundMaskTransitionDelay: number = 0.3

    @ComponentUtils.required()
    @property({ type: UIOpacity, displayName: 'Nav Bar Shadow', group: 'Nav Bar Shadow' })
    private navBarShadow!: UIOpacity

    @property({
        type: CCFloat,
        displayName: 'Transition duration',
        group: 'Nav Bar Shadow',
    })
    private navBarShadowTransitionDuration: number = 0.5

    @property({
        type: CCFloat,
        displayName: 'Transition delay',
        group: 'Nav Bar Shadow',
    })
    private navBarShadowTransitionDelay: number = 0.3

    @property({ type: [PageManagerItem] })
    private pageItems: PageManagerItem[] = []

    @property({ type: Enum(DashboardScreens) })
    private currentScreen: DashboardScreens = DashboardScreens.home

    @ComponentUtils.required()
    private uiTransform!: UITransform

    public unmountAndHideAll(): void {
        // biome-ignore lint/complexity/noForEach: simple
        this.pageItems.forEach((item) => {
            const { page, pageType: screen } = item

            if (screen === this.currentScreen) {
                return
            }

            page.unmount()
            page.hide()
        })
    }

    public onLoad(): void {
        this.uiTransform = this.getComponent(UITransform) as UITransform
        ComponentUtils.validateRequiredProperties(this)

        game.on(GameEvents.VIEW_RESIZED, this.init, this)
    }

    protected onDestroy(): void {
        game.off(GameEvents.VIEW_RESIZED, this.init, this)
    }

    public init(): void {
        this.pageItems.forEach((pageItem, index) => {
            const { page } = pageItem
            const widget = page.getWidget()

            const currentIndex = this.getPageIndex(this.currentScreen)

            widget.right = -(index - currentIndex) * this.uiTransform.width
            widget.left = (index - currentIndex) * this.uiTransform.width
            widget.updateAlignment()
        })
    }

    public updateCollectionsContentPayload(data: CollectionsPayload[]): void {
        const collectionsPage = this.getPage(DashboardScreens.collections)
        if (collectionsPage && collectionsPage instanceof CollectionsPage) {
            collectionsPage.setPayload(data)
        }
    }

    @ComponentUtils.onlyRunInLifeCycleAsync
    public async navigate(
        payload: NavigatePayload,
        signal: AnimAbortSignal,
        onMount?: (page: DashboardPage) => Promise<void>,
        onTransitionIn?: (page: DashboardPage) => Promise<void>,
    ): Promise<void> {
        const { from, to } = payload

        if (from === null) {
            await this.navigateFromNull(to, onMount, onTransitionIn)
            return
        }

        await this.navigateFromPage(from, to, signal, onMount, onTransitionIn)
    }

    @ComponentUtils.onlyRunInLifeCycleAsync
    private async navigateFromPage(
        from: DashboardScreens,
        to: DashboardScreens,
        signal: AnimAbortSignal,
        onMount?: (page: DashboardPage) => Promise<void>,
        onTransitionIn?: (page: DashboardPage) => Promise<void>,
    ) {
        const { toPage, fromPage } = this.validateNavigationRequest(from, to)

        this.currentScreen = to

        const width = this.uiTransform.width

        const startIndex = this.getPageIndex(from)
        const endIndex = this.getPageIndex(to)

        const leftIndex = Math.min(startIndex, endIndex)
        const rightIndex = Math.max(startIndex, endIndex)

        const distance = (rightIndex - leftIndex) * width
        const direction = startIndex < endIndex ? 1 : -1

        const items = this.pageItems.slice(leftIndex, rightIndex + 1)

        items.forEach(({ page }, index) => {
            page.show()

            const widget = page.getWidget()

            widget.enabled = false

            page.node.position = new Vec3(
                (index + leftIndex - startIndex) * width,
                page.node.position.y,
                page.node.position.z,
            )
        })

        const isPageLoading = !toPage.hasLoadedPage()

        if (isPageLoading) {
            this.showLoadingScreen()
        }

        const remainingItems = this.pageItems.filter((item) => item.pageType !== to)

        const cleanup = () => this.cleanupPages(toPage, remainingItems)

        signal.addAbortListener(cleanup)

        await this.mountPage(toPage, onMount)

        await signal.callAsync(() =>
            this.performFullTransition({ from, to, fromPage, toPage, distance, direction, items }),
        )

        signal.removeAbortListener(cleanup)
        signal.call(cleanup)

        await signal.callAsync(() => this.transitionInPage(toPage, onTransitionIn))

        if (isPageLoading) {
            this.hideLoadingScreen()
        }
    }

    @ComponentUtils.onlyRunInLifeCycle
    private hideLoadingScreen() {
        ScreenUtils.closeLoadingScreen()
    }

    @ComponentUtils.onlyRunInLifeCycle
    private showLoadingScreen() {
        ScreenUtils.showLoadingScreen()
    }

    @ComponentUtils.onlyRunInLifeCycle
    private cleanupPages(toPage: DashboardPage, items: PageManagerItem[]) {
        const widget = toPage.getWidget()
        widget.enabled = true
        widget.left = 0
        widget.right = 0
        View.instance.emit('canvas-resize')

        for (const item of items) {
            item.page.unmount()
            item.page.hide()
        }
    }

    @ComponentUtils.onlyRunInLifeCycleAsync
    private async performFullTransition(config: {
        from: DashboardScreens
        to: DashboardScreens
        fromPage: DashboardPage
        toPage: DashboardPage
        distance: number
        direction: 1 | -1
        items: PageManagerItem[]
    }) {
        const { from, to, fromPage, distance, direction, items, toPage } = config

        this.node.scene.emit(DashboardEvents.REQUEST_NAVIGATION_ANIMATION, {
            from,
            to,
        })

        await Promise.all([
            fromPage.transitionOut(),
            this.transition(distance, direction, items),
            this.handleBackgroundMask(this.backgroundMaskTransitionDuration, toPage.getDarkenBackground()),

            this.handleNavBarShadow(this.navBarShadowTransitionDuration, toPage.getNavbarShadow()),
        ])
    }

    @ComponentUtils.onlyRunInLifeCycleAsync
    private async mountPage(toPage: DashboardPage, onMount?: (page: DashboardPage) => Promise<void>) {
        await toPage.mount()
        await onMount?.(toPage)
    }

    private validateNavigationRequest(from: DashboardScreens, to: DashboardScreens) {
        if (from !== this.currentScreen) {
            throw new Error(
                'PageManager: Cannot navigate from a different screen. Perhaps you forgot to update the currentScreen property.',
            )
        }

        const fromPage = this.getPage(this.currentScreen)
        const toPage = this.getPage(to)

        if (!toPage || !fromPage) {
            throw new Error(
                'PageManager: Cannot navigate to/from a null page. Perhaps you forgot to add the page to the pageItems array in the editor.',
            )
        }
        return { toPage, fromPage }
    }

    @ComponentUtils.onlyRunInLifeCycleAsync
    private async navigateFromNull(
        to: DashboardScreens,
        onMount?: (page: DashboardPage) => Promise<void>,
        onTransitionIn?: (page: DashboardPage) => Promise<void>,
    ) {
        const currentPage = this.getPage(this.currentScreen)!
        currentPage.show()

        this.currentScreen = to
        await this.mountPage(currentPage, onMount)

        if (!this.node?.scene) return
        this.node.scene.emit(DashboardEvents.REQUEST_NAVIGATION_ANIMATION, {
            from: null,
            to,
        })

        await this.transitionInPage(currentPage, onTransitionIn)
    }

    private async transitionInPage(
        currentPage: DashboardPage,
        onTransitionIn?: (page: DashboardPage) => Promise<void>,
    ) {
        await currentPage.transitionIn()
        await onTransitionIn?.(currentPage)
    }

    public getPage(screen: DashboardScreens): DashboardPage | null {
        const index = this.getPageIndex(screen)
        return index !== -1 ? (this.pageItems[index]?.page ?? null) : null
    }

    private async handleBackgroundMask(duration: number, nextOpacity: number): Promise<void> {
        await AnimUtils.runTweenAsync(
            tween(this.backgroundMask)
                .delay(this.backgroundMaskTransitionDelay)
                .to(duration, {
                    opacity: nextOpacity,
                })
                .start(),
        )
    }

    private async handleNavBarShadow(duration: number, nextOpacity: number): Promise<void> {
        await AnimUtils.runTweenAsync(
            tween(this.navBarShadow)
                .delay(this.navBarShadowTransitionDelay)
                .to(duration, {
                    opacity: nextOpacity,
                })
                .start(),
        )
    }

    private getPageIndex(screen: DashboardScreens): number {
        const index = A.searchIndex(this.pageItems, (item) => item.pageType === screen)
        return index
    }

    private async transition(distance: number, direction: 1 | -1, items: PageManagerItem[]): Promise<void> {
        await Promise.all(
            items.map(({ page }) =>
                AnimUtils.runTweenAsync(
                    tween(page.node).to(
                        0.5,
                        {
                            position: new Vec3(
                                page.node.position.x - distance * direction,
                                page.node.position.y,
                                page.node.position.z,
                            ),
                        },
                        {
                            easing: easing.quintInOut,
                        },
                    ),
                ),
            ),
        )
    }
}
