import PlayerAvatarOverlay from 'db://assets/scenes/common/PlayerAvatarOverlay'
import OverlayUtils from 'db://assets/utils/OverlayUtils'
import { _decorator, Component, Label, Node, Sprite, tween, UIOpacity, UITransform } from 'cc'
import { ParticleSystem } from '../../../../../../particles/ParticleSystem'
import AnimUtils from '../../../../../../utils/AnimUtils'
import CacheUtils from '../../../../../../utils/CacheUtils'

const { ccclass, property } = _decorator

export interface LeaderRailPayload {
    score?: number
    rank?: number
}

type Signal = Ludex.Utils.Signal.Signal

const TOURNAMENT_RAIL_OVERLAY_CSS = 'overlays/avatar/tournament-rail-overlay-styles.css'

@ccclass('LeaderRail')
export class LeaderRail extends Component {
    @property(Label)
    private scoreLabel!: Label

    @property(Node)
    private crown!: Node

    @property(Sprite)
    private avatar!: Sprite

    @property(ParticleSystem)
    private spark!: ParticleSystem

    private defaultAvatar!: SpriteFrame

    private uiOpacity!: UIOpacity

    private score: number = 0

    private isOverlayMode: boolean = false
    private playerAvatarOverlay: PlayerAvatarOverlay | null = null

    protected __preload(): void {
        this.defaultAvatar = this.avatar.spriteFrame!
        this.uiOpacity = this.getComponent(UIOpacity)!
    }

    public onLoad(): void {
        this.createAvatarOverlay$()
    }

    public onDisable(): void {
        this.cleanupOverlay()
    }

    public async updateScore(score: number, signal: Signal, immediately: boolean = false): Promise<void> {
        const addScore = score - this.score
        const duration = immediately ? 0 : 0.5

        const scoreTween = tween(this.node).to(
            duration,
            {},
            {
                onUpdate: (_, ratio) => {
                    if (!ratio) return
                    const value = Math.floor(addScore * ratio) + this.score
                    this.scoreLabel.string = value.toString()
                },
            },
        )

        await AnimUtils.runTweenAsync(scoreTween, signal)
        this.score = score
        this.scoreLabel.string = score.toString()
    }

    public async updatePhoto(): Promise<void> {
        const id = GameSDK.player.getID()
        if (!id) return

        if (this.isOverlayMode) {
            const result = await this.showAvatarOverlay$(id)
            if (result) return

            this.isOverlayMode = false
        }

        const url = GameSDK.player.getPhoto()
        if (!url) return

        const frame = await CacheUtils.getPlayerAvatar(url, id, 50)
        if (!frame) {
            this.avatar.spriteFrame = this.defaultAvatar
            return
        }

        frame.packable = true
        this.avatar.spriteFrame = frame
    }

    public setCrown(active: boolean): void {
        this.crown.active = active
    }

    public setOpacity(value: number): void {
        this.uiOpacity.opacity = value
    }

    public playSparkParticle(): void {
        this.spark.play()
    }

    public stopSparkParticle(): void {
        this.spark.stop()
    }

    private showAvatarOverlay$ = async (playerId: string): Promise<boolean> => {
        const { worldX, worldY } = this.getAvatarPosition$()
        const result = await this.playerAvatarOverlay?.updateOverlay({
            playerId,
            worldX,
            worldY,
            avatarWidth: this.getAvatarWidth$(),
            customCss: TOURNAMENT_RAIL_OVERLAY_CSS,
        })

        return result ?? false
    }

    private getAvatarWidth$ = (): number => {
        const uiTransform = this.avatar.node.getComponent(UITransform)
        if (!uiTransform) return 0
        const { width } = uiTransform
        return width
    }

    private getAvatarPosition$ = (): { worldX: number; worldY: number } => {
        const { worldPositionX, worldPositionY } = this.avatar.node
        return { worldX: worldPositionX, worldY: worldPositionY }
    }

    private createAvatarOverlay$ = (): void => {
        try {
            OverlayUtils.isOverlayViewAPIAvailable(GameSDK)

            this.isOverlayMode = true
            this.playerAvatarOverlay = new PlayerAvatarOverlay(this.node.scene)
        } catch {
            return
        }
    }

    public cleanupOverlay(): void {
        this.playerAvatarOverlay?.hideOverlay()
    }

    public update(_dt: number): void {
        this.handleAvatarPositionChanged$()
    }

    private handleAvatarPositionChanged$ = (): void => {
        if (!this.isOverlayMode) return

        const { worldX, worldY } = this.getAvatarPosition$()
        this.playerAvatarOverlay?.moveOverlayTo(worldX, worldY)
    }
}
