import { Component, UIOpacity, _decorator } from 'cc'
const { ccclass, requireComponent } = _decorator

const changeElementMaskAlpha = (alpha: number): void => {
    const appMask = document.getElementById('app-mask')
    if (!appMask) return

    appMask.style.background = `rgb(0 0 0 / ${alpha})`
}

/**
 * Yandex only
 */
@ccclass('BackgroundMask')
@requireComponent([UIOpacity])
export class BackgroundMask extends Component {
    private opacity!: UIOpacity

    public onLoad() {
        const opacity = this.getComponent(UIOpacity)
        if (!opacity) {
            console.warn('BackgroundMask: UIOpacity not found')
            return
        }

        this.opacity = opacity
        this.updateMaskAlpha()
    }

    public updateMaskAlpha() {
        const nodeAlpha = this.opacity.opacity / 255
        changeElementMaskAlpha(nodeAlpha)
    }
}
