import { _decorator, Button, Component, Label } from 'cc'
import { ScreenKeysEnum } from '../../constants/ScreenKeys'
import GameplayEvents from '../../gameplay/constants/GameEvents'
import ComponentUtils from '../../utils/ComponentUtils'
import MatchUtils from '../../utils/MatchUtils'
import ScreenUtils from '../../utils/ScreenUtils'
import TournamentUtils from '../../utils/TournamentUtils'
import { BaseScreen } from '../BaseScreen'

const { ccclass, property, requireComponent } = _decorator

const {
    Events,
    Match: { Modes },
} = Ludex

const { event, storage } = codex

@ccclass('QuitLevelScreen')
@requireComponent(BaseScreen)
export class QuitLevelScreen extends Component {
    private screen!: BaseScreen

    @property(Label)
    private singleDescription!: Label

    @property(Label)
    private tournamentDescription!: Label

    @property(Button)
    private quitButton!: Button

    @property(Label)
    private quitLabel!: Label

    @property(Label)
    private endLabel!: Label

    @property(Button)
    private continueButton!: Button

    public __preload() {
        this.enabled = false
        this.screen = this.getComponent(BaseScreen)!
        this.validateRequiredComponents()
        this.listenScreenEvents()
    }

    public onDestroy(): void {
        this.unListenScreenEvents()
    }

    private validateRequiredComponents() {
        ComponentUtils.lazyValidateProperties(this)
    }

    private listenScreenEvents() {
        // ? Here's a demo of all the events a screen has
        event.on(Events.OPENING_SCREEN, this.handleOpeningScreen)
        event.on(Events.OPENED_SCREEN, this.handleOpenedScreen)
    }

    private unListenScreenEvents() {
        event.off(Events.OPENING_SCREEN, this.handleOpeningScreen)
        event.off(Events.OPENED_SCREEN, this.handleOpenedScreen)
    }

    private handleOpeningScreen = (payload: SwitchScreenPayload) => {
        if (!this.validateEventPayload(payload)) return

        this.setInteractableButtons(true)
    }

    private handleOpenedScreen = (payload: SwitchScreenPayload) => {
        if (!this.validateEventPayload(payload)) return
        this.open()
    }

    private validateEventPayload(payload: SwitchScreenPayload) {
        const { screenName } = payload ?? {}

        if (screenName !== this.node.name) return false

        return true
    }

    public open() {
        this.updateStatus()
        this.runPopUpAnimation()
        this.node.scene.emit(GameplayEvents.HIDE_UI)
    }

    public close() {
        this.setInteractableButtons(false)

        ScreenUtils.closeScreen(ScreenKeysEnum.QUIT_LEVEL_SCREEN)

        this.node.scene.emit(GameplayEvents.SHOW_UI)
        this.node.scene.emit(GameplayEvents.GAME_RESUME)
    }

    private updateStatus() {
        const isTournament = MatchUtils.getMatchMode() === Modes.TOURNAMENT

        this.singleDescription.node.active = !isTournament
        this.tournamentDescription.node.active = isTournament
        this.quitLabel.node.active = !isTournament
        this.endLabel.node.active = isTournament
    }

    private runPopUpAnimation(): void {
        if (!this.screen.container) {
            console.warn('StartLevelScreen: screen not found')
        }
    }

    private setInteractableButtons(interactable: boolean) {
        this.quitButton.interactable = interactable
        this.continueButton.interactable = interactable
        this.screen.backgroundMaskButton.interactable = interactable
    }

    public handleClickedContinue() {
        this.close()
    }

    public async handleClickedQuit(): Promise<void> {
        this.setInteractableButtons(false)

        if (MatchUtils.getMatchMode() === Modes.TOURNAMENT) {
            await this.handleTournamentQuit()
        }

        this.node.scene.emit(GameplayEvents.GAME_EXIT)
    }

    private async handleTournamentQuit(): Promise<void> {
        const { score: currentScore = 0, level = 1, tournamentId } = MatchUtils.getMatchCustomData() ?? {}
        const previousScore = storage.getStorageData('tournament', 'previousScore') ?? 0
        const isAnimateTournament = currentScore > previousScore

        if (isAnimateTournament && tournamentId) {
            TournamentUtils.saveTournamentProgress(level, currentScore, tournamentId)
        }

        storage.setStorageData('tournament', 'animateTournamentLeader', isAnimateTournament)

        await MatchUtils.finishMatchAsync()
    }
}
