import { Component, Node, Prefab, Vec3, _decorator, easing, instantiate, randomRange, tween } from 'cc'
import { CodeSoundKeys } from '../../constants/SoundKeys'
import type { CoinContainer } from '../../scenes/common/coin-container/CoinContainer'
import { Coin } from '../../scenes/game-scene/components/coin-manager/components/Coin'
import { CoinTextPopup } from '../../scenes/game-scene/components/coin-manager/components/CoinTextPopup'
import AnimUtils from '../../utils/AnimUtils'
import AudioUtils from '../../utils/AudioUtils'
import ComponentUtils from '../../utils/ComponentUtils'
import AnimAbortSignal from '../../utils/common/AnimAbortSignal'
import GameplayEvents from '../constants/GameEvents'

const { ccclass, disallowMultiple, property } = _decorator

const { player, monitorError } = codex

const MAX_COINS = 999999999
const COIN_COLLECT_ANIM = {
    MAX_COIN_COUNT: 10,
    DISTANCE_RANGE: [1000, 1500],
    STAGGER: 0.03,
    FLOAT_DURATION: 1,
    FLY_DURATION: 1,
} as const

@ccclass('CoinManager')
@disallowMultiple
export class CoinManager extends Component {
    private coins: number = 0

    @ComponentUtils.required()
    @property(Prefab)
    private coinPrefab!: Prefab

    @ComponentUtils.required()
    @property(Prefab)
    private coinTextPopup!: Prefab

    private coinTextPopupNode!: CoinTextPopup

    @ComponentUtils.required()
    @property(Node)
    private coinAnimContainer!: Node

    private collectionAnimSignal?: AnimAbortSignal

    private coinObjects: Coin[] = []
    private targetContainer: CoinContainer | undefined

    protected onLoad(): void {
        ComponentUtils.validateRequiredProperties(this)
        this.getCoinsFromPlayerData()
        this.prepareCoinCollection()
        this.prepareCoinTextPopup()
        this.listenToCoinEvents()
    }

    protected start(): void {
        this.emitCoinAmount()
    }

    public init(): void {
        this.updateCoins({ coins: this.coins })
    }

    public listenToCoinEvents(): void {
        this.node.scene.on(GameplayEvents.ADD_COINS, this.addCoins, this)
        this.node.scene.on(GameplayEvents.REMOVE_COINS, this.removeCoins, this)
        this.node.scene.on(GameplayEvents.ABORT_COIN_ANIMATION, this.abortCoinAnimation, this)
    }

    private prepareCoinCollection(): void {
        for (let i = 0; i < COIN_COLLECT_ANIM.MAX_COIN_COUNT; i++) {
            const coin = instantiate(this.coinPrefab)
            coin.setParent(this.coinAnimContainer)
            coin.setPosition(0, 0)
            coin.active = false
            this.coinObjects.push(coin.getComponent(Coin) as Coin)
        }
    }

    private prepareCoinTextPopup(): void {
        const textPopupNode = instantiate(this.coinTextPopup)
        textPopupNode.setParent(this.node)
        textPopupNode.active = false

        this.coinTextPopupNode = textPopupNode.getComponent(CoinTextPopup) as CoinTextPopup
    }

    @ComponentUtils.onlyRunInLifeCycleAsync
    private async handleCollectionAnim(
        coins: number,
        targetContainer: CoinContainer | undefined,
        floatDuration: number,
        flyDuration: number,
        signal: AnimAbortSignal,
    ): Promise<void> {
        if (!targetContainer) {
            console.warn(
                'No target coin container assigned to CoinManager. No coin collection animation will be played',
            )
            return
        }

        const animatedCoins = this.coinObjects.slice(0, this.getShortAmount(coins))

        if (animatedCoins.length === 0) {
            console.warn(
                `Requested coins amount is ${coins}. Shortened to ${this.getShortAmount(
                    coins,
                )}. No coin objects was found to animate collection.`,
            )

            monitorError?.sendException(
                new Error(
                    `Requested coins amount is ${coins}. Shortened to ${this.getShortAmount(
                        coins,
                    )}. No coin objects was found to animate collection.`,
                ),
                {
                    coins,
                    animatedCoins: animatedCoins.length,
                    hasCoinTextPopup: !!this.coinTextPopupNode,
                    hasTargetContainer: !!targetContainer,
                    coinCollectionCount: this.coinObjects.length,
                },
            )

            return
        }

        AudioUtils.playSound(CodeSoundKeys.COLLECT_STOCK_COINS)

        await Promise.all([
            this.playCoinFlyAndexplodeAnimation(targetContainer, animatedCoins, floatDuration, flyDuration, signal),
            AnimUtils.waitTweenAsync(floatDuration + flyDuration / 2, signal).then(() =>
                this.animateCoinTextPopup(coins, signal),
            ),
        ])

        signal.abort()
    }

    @ComponentUtils.onlyRunInLifeCycleAsync
    private async playCoinFlyAndexplodeAnimation(
        container: CoinContainer,
        coins: Coin[],
        floatDuration: number,
        flyDuration: number,
        signal?: AnimAbortSignal,
    ) {
        const explodeAndFlybackAvgDuration = floatDuration + flyDuration

        await Promise.all([
            ...coins.flatMap((coin) => {
                coin.node.active = true

                coin.stopParticles()

                const TIME_RANGE = [0, Math.min(floatDuration, flyDuration) * 0.5] as const
                const offset = randomRange(...TIME_RANGE)

                const explodeDuration = floatDuration - offset
                const flybackDuration = flyDuration + offset

                return [
                    this.animateCoinExplode(coin.node, explodeDuration, signal),
                    AnimUtils.waitTweenAsync(explodeDuration, signal).then(() =>
                        this.animateCoinFlyBack(container, coin, flybackDuration, signal),
                    ),
                ]
            }),
            AnimUtils.waitTweenAsync(explodeAndFlybackAvgDuration, signal).then(() =>
                container.emitExplodeEffect(signal),
            ),
        ])
    }

    @ComponentUtils.onlyRunInLifeCycleAsync
    private animateCoinTextPopup(coin: number, signal: AnimAbortSignal) {
        return this.coinTextPopupNode.show(coin, signal)
    }

    @ComponentUtils.onlyRunInLifeCycleAsync
    private async animateCoinFlyBack(
        container: CoinContainer,
        coin: Coin,
        flyDuration: number,
        signal?: AnimAbortSignal,
    ) {
        const startPosition = coin.node.worldPosition.clone()
        const lerpedPosition = Vec3.ZERO.clone()
        const targetScale = container.getCoinIconWorldScale()

        await AnimUtils.runTweenAsync(
            tween(coin.node).to(
                flyDuration,
                {
                    scale: targetScale,
                },
                {
                    onStart: () => {
                        coin.playSpinAnimationAfterDelay(0, signal) // ? x2 to make the offset more obvious
                        coin.playTrailParticles()
                    },
                    onUpdate: (_, ratio) => {
                        if (ratio === undefined) {
                            return
                        }

                        const cubicIn = easing.cubicIn(ratio)
                        const position = container.getCoinIconWorldPosition()

                        coin.node.worldPosition = Vec3.lerp(lerpedPosition, startPosition, position, cubicIn)
                    },
                    onComplete: () => {
                        coin.emittingParticles.stopSystem()
                        coin.stopAnimation()
                        coin.hide()
                    },
                },
            ),
            signal,
        )

        await AnimUtils.waitTweenAsync(1, signal)
        coin.resetDisabled()
    }

    @ComponentUtils.onlyRunInLifeCycleAsync
    private async animateCoinExplode(coin: Node, floatDuration: number, signal?: AnimAbortSignal) {
        const RANDOM_RANGE_X = [-200, 200] as const
        const RANDOM_RANGE_Y = [-200, 200] as const
        const targetX = randomRange(RANDOM_RANGE_X[0], RANDOM_RANGE_X[1])
        const targetY = randomRange(RANDOM_RANGE_Y[0], RANDOM_RANGE_Y[1])

        coin.setPosition(0, 0, coin.position.z)
        coin.setScale(Vec3.ZERO)

        const targetPos = new Vec3(targetX, targetY, coin.position.z)

        await Promise.all([
            AnimUtils.runTweenAsync(
                tween(coin)
                    // ? `floatDuration` to make the delay follows staggered timing
                    // ? `* 0.1` to make the delay shorter,
                    // ? making the position animation more obvious
                    .delay(floatDuration * 0.1)
                    .to(
                        floatDuration * 0.8,
                        {
                            scale: Vec3.ONE,
                        },
                        {
                            easing: 'elasticOut',
                        },
                    ),
                signal,
            ),
            AnimUtils.runTweenAsync(
                tween(coin).to(
                    floatDuration,
                    {
                        position: targetPos,
                    },
                    {
                        easing: 'circOut',
                    },
                ),
                signal,
            ),
        ])
    }

    private getCoinsFromPlayerData(): void {
        const gameData = player.getGameData()

        if (!gameData) {
            this.coins = 0
            return
        }

        this.coins = gameData.coins
    }

    public getCoins(): number {
        return this.coins
    }

    public updateCoins(payload: CoinDataPayload): void {
        if (this.coins > MAX_COINS) {
            this.coins = MAX_COINS
        }

        if (payload.coins === this.coins) {
            return
        }

        const changes = Math.max(0, payload.coins - this.coins)
        this.coins = payload.coins

        player.setGameData({ coins: this.coins })

        const { collectionAnimation } = payload

        this.collectionAnimSignal?.abort()
        const newSignal = new AnimAbortSignal()
        this.collectionAnimSignal = newSignal

        // let's signal from payload to cancel this signal too
        collectionAnimation?.signal?.chain(newSignal)

        if (collectionAnimation === undefined || collectionAnimation.animateTextOnly) {
            this.node.scene.emit(GameplayEvents.COIN_CHANGED, {
                ...payload,
                collectionAnimation: {
                    animateTextOnly: true,
                    signal: collectionAnimation?.signal,
                },
            })
            return
        }

        const {
            floatDuration = COIN_COLLECT_ANIM.FLOAT_DURATION,
            flyDuration = COIN_COLLECT_ANIM.FLY_DURATION,
            target = this.targetContainer,
        } = collectionAnimation

        this.collectionAnimSignal = newSignal
        this.targetContainer = target

        const withCollectionAnim = {
            ...payload,
            collectionAnimation: {
                floatDuration,
                flyDuration,
                animateTextOnly: false,
                signal: newSignal,
                target: target,
            },
        }

        this.node.scene.emit(GameplayEvents.COIN_CHANGED, withCollectionAnim)
        this.handleCollectionAnim(changes, this.targetContainer, floatDuration, flyDuration, newSignal)
    }

    public addCoins(payload: CoinDataPayload): void {
        this.updateCoins({
            ...payload,
            coins: this.coins + payload.coins,
        })
    }

    public removeCoins(payload: CoinDataPayload): void {
        if (this.coins < payload.coins) {
            this.node.scene.emit(GameplayEvents.REMOVE_COINS_FAILED)
            return
        }

        this.updateCoins({
            ...payload,
            coins: this.coins - payload.coins,
        })
        this.node.scene.emit(GameplayEvents.REMOVE_COINS_SUCCESS)
    }

    public emitCoinAmount(): void {
        this.node.scene.emit(GameplayEvents.COIN_CHANGED, {
            coins: this.coins,
        } as CoinDataPayload)
    }

    private getShortAmount(count: number): number {
        return Math.min(count, COIN_COLLECT_ANIM.MAX_COIN_COUNT)
    }

    private abortCoinAnimation(): void {
        this.collectionAnimSignal?.abort()
    }

    @ComponentUtils.onlyRunInLifeCycleAsync
    public untilFinishesCollectAnim(): Promise<void> {
        if (this.collectionAnimSignal === undefined) return Promise.resolve()
        return this.collectionAnimSignal.untilAborted()
    }

    public setTargetContainer(targetContainer: CoinContainer): void {
        this.targetContainer = targetContainer
    }

    public getTargetContainer(): CoinContainer | undefined {
        return this.targetContainer
    }

    protected onDestroy(): void {
        this.collectionAnimSignal?.abort()
    }
}
