const ButtonPlusEvents = {
    // Transitions
    TRANSITION_DOWN: 'TRANSITION_DOWN',
    TRANSITION_HOVER: 'TRANSITION_HOVER',
    TRANSITION_RESET: 'TRANSITION_RESET',
    TRANSITION_DISABLED: 'TRANSITION_DISABLED',

    // Interactions
    CLICK_START: 'CLICK_START',
    CLICK: 'CLICK',
    CLICK_END: 'CLICK_END',
    CLICK_CANCEL: 'CLICK_CANCEL',
} as const

type ButtonPlusEvent = (typeof ButtonPlusEvents)[keyof typeof ButtonPlusEvents]

export { ButtonPlusEvents }
export type { ButtonPlusEvent }
