import Ads from '../../constants/Ads'
import { ScreenKeysEnum } from '../../constants/ScreenKeys'
import { CodeSoundKeys } from '../../constants/SoundKeys'
import AdsUtils from '../../utils/AdsUtils'
import AnimUtils from '../../utils/AnimUtils'
import AudioUtils from '../../utils/AudioUtils'
import GeneralUtils from '../../utils/GeneralUtils'
import LayoutUtils from '../../utils/LayoutUtils'
import MatchUtils from '../../utils/MatchUtils'
import ScreenUtils from '../../utils/ScreenUtils'
import ThemeUtils from '../../utils/ThemeUtils'
import type { StateManager } from '../manager/StateManager'
import BaseState from './BaseState'

const { player, match } = codex
const {
    Match: { Modes },
    Plugins: { Ads: { Types: AdsTypes } = {} },
} = Ludex

class InitializeState extends BaseState {
    constructor(manager: StateManager) {
        super(manager)

        this.clearBoard()
        this.initAdPreload()
        this.initCountdown()
        this.initScore()

        this.loadLevelAndUI()
    }

    private loadLevelAndUI(): void {
        this.setAdVisibility()
        this.loadLevel().then(() => {
            this.updateUI()
        })
    }

    private clearBoard(): void {
        if (!this.game) return

        this.game.rescueTiles = []
        this.game.board.destroyTiles()
        this.game.board.clearAnimationQueue()
        this.game.pathDraw.clearMatchPath()
    }

    private setAdVisibility(): void {
        if (!AdsTypes) return

        const level = this.data.level
        const showBannerAds = level !== undefined && level > Ads.NUM_LEVEL_WITHOUT_BANNER_ADS

        AdsUtils.setAdEnable(AdsTypes.BANNER, !showBannerAds)

        AdsUtils.updateBannerAdsByLayout(LayoutUtils.getCurrentLayout())
        AdsUtils.refreshBannerAdVisibility()
    }

    private async loadLevel(): Promise<void> {
        if (!this.game) return

        const mode = MatchUtils.getMatchMode()

        try {
            if (mode === Modes.CHALLENGE_FRIEND) {
                const challengeData = await this.loadChallengeFriendMode()
                AudioUtils.playSound(CodeSoundKeys.LEVEL_START)
                this.handleChallengeFriendModeStart(challengeData)
                return
            }

            if (mode === Modes.TOURNAMENT) {
                await this.loadSingleMode()
                AudioUtils.playSound(CodeSoundKeys.LEVEL_START)
                this.handleTournamentModeStart()
                return
            }
        } catch (error) {
            console.error('Error loading level:', error)
        }

        await this.loadSingleMode()
        AudioUtils.playSound(CodeSoundKeys.LEVEL_START)
        this.handleSingleModeStart()
    }

    private async loadChallengeFriendMode() {
        if (!this.game) throw new Error('Game is not initialized')

        await MatchUtils.getMatchCustomDataAsync()

        const challengeData = match.getMatchState().customData.challengeData

        if (challengeData === undefined || challengeData === null) {
            throw new Error('Challenge friend mode need to have data.')
        }

        const levelManager = this.game.levelManager
        await levelManager.loadLevel(challengeData.level)

        this.updateGameData({
            level: challengeData.level,
            levelData: levelManager.getLevelData(),
        })

        return challengeData
    }

    private async loadSingleMode() {
        if (this.game === null) throw new Error('Game is not initialized')

        const levelManager = this.game.levelManager
        await levelManager.loadLevel(this.data.level)
        this.updateGameData({
            levelData: levelManager.getLevelData(),
        })
    }

    private async handleChallengeFriendModeStart(challengeData: ChallengeData) {
        if (!this.game) return

        const levelData = this.game.levelManager.getLevelData()
        const { level, fen, gravity, rules, time } = challengeData

        const playerId = player.getPlayerId()

        await Promise.all([
            MatchUtils.setMatchCustomDataAsync({ level, score: 0 }),
            MatchUtils.setProfileMatchDataAsync({ id: playerId, score: 0 }),
        ])

        const frames = ThemeUtils.loadLevelSpriteFrames(challengeData.level)

        await this.game.board.create(frames, {
            ...levelData,
            level,
            customFen: fen,
            mode: gravity,
            rules,
            time,
        })

        this.game.board.setIgnoreInput(true)
        await this.game.board.animateTilesAppear()
        this.game.board.setIgnoreInput(false)

        await frames

        this.ready()
    }

    private async handleSingleModeStart() {
        if (!this.game) return

        const levelData = this.game.levelManager.getLevelData()
        const level = this.data.level ?? 1
        const playerId = player.getPlayerId()

        await Promise.all([
            MatchUtils.setMatchCustomDataAsync({ level, score: 0 }),
            MatchUtils.setProfileMatchDataAsync({ id: playerId, score: 0 }),
        ])

        const frames = ThemeUtils.loadLevelSpriteFrames(level)
        await this.game.board.create(frames, levelData)
        this.game.board.setIgnoreInput(true)

        await this.game.board.animateTilesAppear()

        if (!this.game.guidelinesManager?.isNewbieGuideLevel()) {
            this.game.board.setIgnoreInput(false)
        }

        const isHardLevel = level && level > 0 && level % 5 === 0

        if (isHardLevel) {
            //? Show hard level screen
            if (LayoutUtils.isLandscape()) {
                this.game.handleShowUI()
            }

            await ScreenUtils.openScreen(ScreenKeysEnum.HARD_LEVEL_SCREEN)
            await AnimUtils.waitTweenAsync(1.3)
            ScreenUtils.closeScreen(ScreenKeysEnum.HARD_LEVEL_SCREEN)

            GeneralUtils.setBlockInput(true)
            await this.game.board.animateTilesWave()
            GeneralUtils.setBlockInput(false)
        }

        this.ready()
    }

    private async handleTournamentModeStart() {
        if (!this.game) return

        const levelData = this.game.levelManager.getLevelData()
        const level = this.data.level ?? 1
        const playerId = player.getPlayerId()

        await Promise.all([
            MatchUtils.setMatchCustomDataAsync({ level }),
            MatchUtils.setProfileMatchDataAsync({ id: playerId }),
        ])

        const frames = ThemeUtils.loadLevelSpriteFrames(level, this.data.themeId)
        await this.game.board.create(frames, levelData)
        this.game.board.setIgnoreInput(true)

        await this.game.board.animateTilesAppear()

        if (!this.game.guidelinesManager?.isNewbieGuideLevel()) {
            this.game.board.setIgnoreInput(false)
        }

        await frames

        this.ready()
    }

    private initAdPreload(): void {
        if (!this.game) return

        this.game.isAdPreloadedForCurrentLevel = false
    }

    private initScore(): void {
        if (!this.game) return

        this.game.scoreManager.init()
    }

    private initCountdown(): void {
        if (!this.game) return

        this.game.clockManager.init()
    }

    private updateUI(): void {
        if (!this.game) return
        const gameData = this.game.getGameData()
        if (!gameData) return

        const { level = 0, levelData } = gameData
        this.game.uiManager.setLevelText(level)

        if (!levelData) return
        this.game.clockManager.setTime(levelData.time)
    }

    public initialize(): void {
        // This is initialize state, do nothing
    }

    public ready(): void {
        const state = this.manager.createReadyState(this.manager)
        this.manager.change(state)
    }

    public playing(): void {
        // Don't allow play in initialize state
    }

    public fail(): void {
        // Don't allow fail in initialize state
    }

    public finish(): void {
        // Don't allow finish in initialize state
    }

    public update(): void {
        //
    }
}

export default InitializeState
