import { math } from 'cc'
import PlayerHasTournamentError from '../exceptions/PlayerHasTournamentError'
import ThemeUtils from './ThemeUtils'

const { match, player, leaderboard } = codex

const {
    Utils: { Object: O, Valid: V, String: S, Array: A },
    Match: { Modes },
} = Ludex

type SetProfilePayload = Ludex.Plugins.Match.Handler.Payload.SetProfile
type SetCustomDataMatchPayload = Ludex.Plugins.Match.Handler.Payload.SetCustomDataMatch
type SetLeaderboardScorePayload = Ludex.Plugins.Match.Handler.Payload.SetLeaderboardScore

type CreateTournamentPayload = Ludex.Plugins.Match.Concrete.Payload.Tournament.Create

type FinishChallengePayload = Ludex.Plugins.Match.Concrete.Payload.Challenge.Finish

const PREFIX_TOURNAMENT_TITLES = [
    'Champion',
    'Ultimate',
    'Grand',
    'Elite',
    "Master's",
    'Premier',
    'Epic',
    "Pro's",
    'Supreme',
    'Legendary',
    'Victory',
    'Prime',
    "Titan's",
    "Hero's",
    "Star's",
]

class MatchUtilsCore {
    protected static inst$: MatchUtilsCore

    public static get inst() {
        if (!this.inst$) {
            this.inst$ = new this()
        }

        return this.inst$
    }

    public static async startMatchAsync(mode?: string): Promise<void> {
        // ? This method is called when the player starts a new game.
        // ? If no input mode, the current match mode will be used (Replaying game)
        try {
            const matchMode = mode ?? this.getMatchMode()

            switch (matchMode) {
                case Modes.SINGLE:
                    await this.inst.processStartSingleModeAsync$()
                    break
                case Modes.TOURNAMENT:
                    await this.inst.processStartTournamentModeAsync$()
                    break
                case Modes.CHALLENGE_FRIEND:
                    await this.inst.processStartChallengeModeAsync$()
                    break
                /* case Modes.MATCHING_GROUP:
                    await this.inst.processStartMatchingGroupModeAsync()
                    break */
                default:
                    // ? Default to single mode
                    if (matchMode) {
                        await this.inst.processStartGameModeAsync$(matchMode)
                    } else {
                        await this.inst.processStartSingleModeAsync$()
                    }
            }
        } catch (error) {
            console.warn('startMatchAsync', error)
        }
    }

    public static async setProfileMatchDataAsync(payload: SetProfilePayload): Promise<void> {
        try {
            await match.handler.setProfileData(payload)
        } catch (error) {
            console.warn('setProfileMatchDataAsync', error)
        }
    }

    public static async setMatchCustomDataAsync(payload: SetCustomDataMatchPayload): Promise<void> {
        try {
            // ? This not set to extraData, only set to customData
            await match.handler.setMatchCustomData(payload)
        } catch (error) {
            console.warn('setMatchCustomDataAsync', error)
        }
    }

    public static async getMatchCustomDataAsync(): Promise<void> {
        try {
            // * This will update match custom data to match state
            // ? Fetch extraData from match and merge to customData
            await match.handler.getMatchCustomData()
        } catch (error) {
            console.warn('getMatchCustomDataAsync', error)
        }
    }

    public static async setLeaderboardScoreAsync(payload: SetLeaderboardScorePayload): Promise<void> {
        try {
            await match.handler.setLeaderboardScore(payload)
        } catch (error) {
            console.warn('setLeaderboardScoreAsync', error)
        }
    }

    public static async finishMatchAsync(payload?: FinishChallengePayload): Promise<void> {
        // ? This method is called when the player end game, not finish a level.
        // ? It should clean up the match and reset the game state.

        try {
            const matchMode = this.getMatchMode()

            switch (matchMode) {
                case Modes.SINGLE:
                    await this.inst.processFinishSingleModeAsync$()
                    break
                case Modes.TOURNAMENT:
                    await this.inst.processFinishTournamentModeAsync$()
                    break
                case Modes.CHALLENGE_FRIEND:
                    await this.inst.processFinishChallengeModeAsync$(payload)
                    break
                /* case Modes.MATCHING_GROUP:
                    await this.inst.processFinishGroupModeAsync()
                    break */
                default:
                    if (matchMode) await this.inst.processFinishGameModeAsync$()
                    // ? Default to single mode
                    else await this.inst.processFinishSingleModeAsync$()
            }
        } catch (error) {
            console.warn('finishMatchAsync', error)
        }
    }

    public static async joinTournamentAsync(tournamentId: string): Promise<void> {
        const playerId = player.getPlayerId()
        return match.tournament.join.processAsync({ tournamentId, playerId })
    }

    public static async continueTournamentAsync(contextId: string): Promise<void> {
        const playerId = player.getPlayerId()
        return match.tournament.continue.processAsync({ playerId, contextId })
    }

    protected processStartSingleModeAsync$ = async (): Promise<void> => {
        const playerId = player.getPlayerId()
        return match.single.start.processAsync({ playerId })
    }

    protected processFinishSingleModeAsync$ = async (): Promise<void> => {
        return match.single.finish.processAsync()
    }

    protected processStartTournamentModeAsync$ = async (): Promise<void> => {
        const playerId = player.getPlayerId()
        const contextId = GameSDK.context.getID()

        return match.tournament.continue.processAsync({
            playerId,
            contextId,
        })
    }

    protected processFinishTournamentModeAsync$ = async (): Promise<void> => {
        const playerId = player.getPlayerId()

        // ? Get best score from player or from leaderboard of this tournament
        const bestScore = player.getBestScore()

        return match.tournament.finish.processAsync({
            playerId,
            bestScore,
        })
    }

    protected processStartChallengeModeAsync$ = async (): Promise<void> => {
        try {
            // ? Try to continue current challenge match
            return this.processContinueChallengeAsync$()
        } catch (error) {
            if (!O.hasOwn(error, 'name') || V.isString(error.name)) throw error

            // ? Start a new one with new opponent
            if (error.name === 'PlayerHasFinishedMatch') {
                return this.processChallengeNewOpponentAsync$()
            }

            // ? In same match, but not join in
            if (error.name === 'PlayerNotJoinInMatch') {
                return this.processJoinChallengeAsync$()
            }
        }
    }

    protected processFinishChallengeModeAsync$ = async (payload?: FinishChallengePayload): Promise<void> => {
        return match.challenge.finish.processAsync(payload)
    }

    protected processContinueChallengeAsync$ = async (): Promise<void> => {
        const { id, customData } = this.getMatchState$()
        const { playerId, opponentId } = customData

        if (!V.isString(id)) {
            throw new Error('Invalid match id')
        }

        if (!V.isString(playerId)) {
            throw new Error('Invalid player id')
        }

        if (!V.isString(opponentId)) {
            throw new Error('Invalid opponent id')
        }

        const contextId = GameSDK.context.getID()
        if (!contextId) {
            throw new Error('Cannot get contextId')
        }

        return match.challenge.continue.processAsync({
            matchId: id,
            contextId,
            playerId,
            opponentId,
        })
    }

    protected processChallengeNewOpponentAsync$ = async (): Promise<void> => {
        const friendIds = player.getConnectedPlayerIds(100, 0)
        const rand = Math.floor(Math.random() * friendIds.length)

        const playerId = player.getPlayerId()
        const opponentId = friendIds[rand]

        if (!V.isString(opponentId) || V.isEmpty(opponentId)) {
            throw new Error('Invalid opponent id')
        }

        return match.challenge.friend.processAsync({ playerId, opponentId })
    }

    protected processJoinChallengeAsync$ = async (): Promise<void> => {
        const { id, customData } = this.getMatchState$()
        const { playerId, opponentId } = customData

        if (!V.isString(id)) {
            throw new Error('Invalid match id')
        }

        if (!V.isString(playerId)) {
            throw new Error('Invalid player id')
        }

        if (!V.isString(opponentId)) {
            throw new Error('Invalid opponent id')
        }

        return match.challenge.join.processAsync({ matchId: id, playerId, opponentId })
    }

    protected processStartGameModeAsync$ = async (gameMode: string): Promise<void> => {
        const playerId = player.getPlayerId()

        return match.strategy.start.processAsync({
            matchId: '123456',
            gameMode,
            playerId,
        })
    }

    protected processFinishGameModeAsync$ = async (): Promise<void> => {
        return match.strategy.finish.processAsync({
            postGlobalLeaderboard: true,
            setPlayerBestScore: true,
        })
    }

    protected getMatchState$ = (): Ludex.Plugins.Match.State | never => {
        const matchState = match.getMatchState()
        if (!matchState) {
            throw new Error('Match state not found')
        }

        return matchState
    }

    public static getMatchMode(): string | null {
        const matchState = this.inst.getMatchState$()

        if (!V.isString(matchState.mode)) {
            return null
        }

        return matchState.mode
    }

    public static getMatchCustomData() {
        const matchState = this.inst.getMatchState$()

        if (!V.isObject(matchState.customData)) {
            return null
        }

        return matchState.customData
    }

    public static updateMatchStateAfterLevelFinish(level: number, score: number): void {
        this.setMatchCustomDataAsync({ level, score })

        const playerId = player.getPlayerId()
        this.setProfileMatchDataAsync({ id: playerId, score })
    }
    protected createTournamentName = (): string => {
        const prefix_tournament_title =
            PREFIX_TOURNAMENT_TITLES[math.randomRangeInt(0, PREFIX_TOURNAMENT_TITLES.length - 1)]
        const tournamentName = `${prefix_tournament_title} Tournament`
        const normalizedName = S.removeDiacritics(tournamentName)
        return `${normalizedName}`
    }

    protected createTournamentPayload(): CreateTournamentPayload {
        const leaderboardId = S.generateObjectId()

        const playerId = player.getPlayerId()
        const { name: playerName } = player.getPlayer()

        const gameData = player.getGameData()
        const level = gameData?.level ?? 1
        const themeId = ThemeUtils.getLevelTheme(level)

        const matchData = this.getMatchState$()
        const { profiles = {} } = matchData
        const score = profiles[playerId]?.score ?? 0

        return {
            score,
            playerId,
            mode: Modes.TOURNAMENT,
            name: this.createTournamentName(),
            leaderboardId,
            payload: {
                playerId,
                playerName,
                leaderboardId,
                themeId,
            },
        }
    }

    protected getTournamentsAsync$ = async (): Promise<GameSDK.Tournament[]> => {
        try {
            return await GameSDK.tournament.getTournamentsAsync()
        } catch (error) {
            console.error(error)
            return []
        }
    }

    public static async processCreateTournamentAsync(): Promise<void> {
        const tournaments = await this.inst.getTournamentsAsync$()

        const playerTournament = A.search(tournaments, (tournament) => {
            const payloadStr = tournament.getPayload()
            const payload = JSON.parse(V.isString(payloadStr) ? payloadStr : '{}')
            const isDefault = tournament.getTournamentType() === 'DEFAULT'
            return isDefault && payload.playerId === player.getPlayerId()
        })

        if (playerTournament) {
            throw new PlayerHasTournamentError(player.getPlayerId(), playerTournament.getID())
        }

        const tournamentCreatePayload = this.inst.createTournamentPayload()

        player.setGameData({
            lastCreateTournamentShown: Date.now(),
        })

        await match.tournament.create.processAsync(tournamentCreatePayload)

        const { leaderboardId } = tournamentCreatePayload

        leaderboard.addLeaderboard({
            name: leaderboardId,
            type: 'tournament',
            autoSortRank: true,
            leaderboardId,
        })
    }
}

class MatchUtils extends MatchUtilsCore {
    protected createTournamentPayload(): CreateTournamentPayload {
        return super.createTournamentPayload()
    }
}

export default MatchUtils
