import { _decorator, Button, Component, Node, UIOpacity } from 'cc'
import { OpacityToggler } from '../../components/common/OpacityToggler'
import DevTools from '../../constants/DevTools'
import { ScreenKeysEnum } from '../../constants/ScreenKeys'
import { CodeSoundKeys } from '../../constants/SoundKeys'
import { CustomModeLevel } from '../../gameplay/constants/CustomGameData'
import GameplayEvents from '../../gameplay/constants/GameEvents'
import GameModes from '../../gameplay/constants/GameMode'
import AdsUtils from '../../utils/AdsUtils'
import AudioUtils from '../../utils/AudioUtils'
import ComponentUtils from '../../utils/ComponentUtils'
import MatchUtils from '../../utils/MatchUtils'
import ScreenUtils from '../../utils/ScreenUtils'
import { BaseScreen } from '../BaseScreen'
import { SwitchButton } from './components/switch-button/SwitchButton'

const { ccclass, property, requireComponent } = _decorator

const {
    Events,
    Utils: { Device: D },
    Match: { Modes },
} = Ludex
const { player, event } = codex

@ccclass('PauseScreen')
@requireComponent(BaseScreen)
export class PauseScreen extends Component {
    private screen!: BaseScreen

    @property(Node)
    public musicSetting!: Node

    @property(Node)
    public soundSetting!: Node

    @property(Node)
    public vibrateSetting!: Node

    @property(SwitchButton)
    private musicSwitch!: SwitchButton

    @property(SwitchButton)
    private soundSwitch!: SwitchButton

    @property(SwitchButton)
    private vibrateSwitch!: SwitchButton

    @property(Button)
    private closeButtonComponent!: Button

    @property({ type: Button, group: 'Button Container' })
    private resumeButton!: Button

    @property({ type: Button, group: 'Button Container' })
    private restartButton!: Button

    @property({ type: Button, group: 'Button Container' })
    private challengeFriendsButton!: Button

    @property({ type: Button, group: 'Button Container' })
    private createShortcutButton!: Button

    @property({ type: Button, group: 'Button Container' })
    private customModeButton!: Button

    @property(UIOpacity)
    private containerUIOpacity!: UIOpacity

    // ! For Unlocking DevTools
    private triggerSequence = ''

    public onLoad() {
        this.enabled = false
        this.screen = this.getComponent(BaseScreen)!
        this.validateRequiredComponents()

        this.listenScreenEvents()
        this.listenUnlockDevTools()
    }

    public onDestroy(): void {
        this.unListenScreenEvents()
        this.unListenUnlockDevTools()
    }

    private validateRequiredComponents() {
        ComponentUtils.lazyValidateProperties(this)
    }

    private listenScreenEvents() {
        event.on(Events.OPENING_SCREEN, this.handleOpeningScreen)
        event.on(Events.OPENED_SCREEN, this.handleOpenedScreen)
    }

    private unListenScreenEvents() {
        event.off(Events.OPENING_SCREEN, this.handleOpeningScreen)
        event.off(Events.OPENED_SCREEN, this.handleOpenedScreen)
    }

    private handleOpeningScreen = (payload: SwitchScreenPayload) => {
        if (!this.validateSwitchScreenPayload(payload)) return

        this.setInteractableButtons(true)
        this.updateStatus()
        this.updateButtonsStatus()
    }

    private handleOpenedScreen = (payload: SwitchScreenPayload) => {
        if (!this.validateSwitchScreenPayload(payload)) return
        this.open()
    }

    private validateSwitchScreenPayload(payload: SwitchScreenPayload) {
        const { screenName } = payload
        if (screenName !== this.node.name) return false
        return true
    }

    // * Music setting switch on/off will have a code: 0 for off, 1 for on
    // * Sound setting switch on/off will have a code: 2 for off, 3 for on
    private listenUnlockDevTools() {
        this.node.off(DevTools.Events.TRIGGER_UNLOCK_KEY)

        this.node.on(DevTools.Events.TRIGGER_RESET_SEQUENCE, this.handleResetSequence)
        this.node.on(DevTools.Events.TRIGGER_UNLOCK_KEY, this.handleTriggerUnlockKey)
    }

    private unListenUnlockDevTools() {
        this.node.off(DevTools.Events.TRIGGER_RESET_SEQUENCE, this.handleResetSequence)
        this.node.off(DevTools.Events.TRIGGER_UNLOCK_KEY, this.handleTriggerUnlockKey)
    }

    private handleResetSequence = () => {
        this.triggerSequence = ''
    }

    private handleTriggerUnlockKey = (payload: { code: number }) => {
        if (!('code' in payload)) return

        const { code } = payload

        const { UNLOCK_SEQUENCE } = DevTools

        if (typeof code !== 'number') return
        if (UNLOCK_SEQUENCE.indexOf(code.toString()) === -1) return

        this.triggerSequence += code.toString()

        console.log(
            'SettingsScreen: handleTriggerUnlockKey',
            code,
            this.triggerSequence,
            this.triggerSequence === UNLOCK_SEQUENCE,
        )

        if (this.triggerSequence === UNLOCK_SEQUENCE) {
            this.node.scene.emit(DevTools.Events.UNLOCK_DEV_TOOLS)
            this.node.off(DevTools.Events.TRIGGER_UNLOCK_KEY)
        }
    }

    public open() {
        this.updateStatus()
        this.node.emit(DevTools.Events.TRIGGER_RESET_SEQUENCE)
        this.node.scene.emit(GameplayEvents.HIDE_UI)
    }

    public close() {
        this.setInteractableButtons(false)
        this.node.emit(DevTools.Events.TRIGGER_RESET_SEQUENCE)
        ScreenUtils.closeScreen(ScreenKeysEnum.PAUSE_SCREEN)
    }

    private updateStatus() {
        const settings = {
            music: player.getPlayerSetting('music') ?? false,
            sound: player.getPlayerSetting('sound') ?? false,
            vibrate: player.getPlayerSetting('vibrate') ?? false,
        }

        if (!this.musicSwitch) {
            console.warn('SettingsScreen: musicSwitch not found')
            return
        }

        if (!this.soundSwitch) {
            console.warn('SettingsScreen: soundSwitch not found')
            return
        }

        this.musicSwitch.setEnabled(settings.music)
        this.soundSwitch.setEnabled(settings.sound)

        if (GameSDK.isGameSDK('GameDistribution')) return

        this.updateVibrateButton()
    }

    private setInteractableButtons(interactable: boolean): void {
        this.resumeButton.interactable = interactable
        this.closeButtonComponent.interactable = interactable
        this.restartButton.interactable = interactable
        this.challengeFriendsButton.interactable = interactable
        this.screen.backgroundMaskButton.interactable = interactable
        this.customModeButton.interactable = interactable
    }

    private updateButtonsStatus() {
        const isYandex = GameSDK.isGameSDK('Yandex')
        const isSingleCustomMode =
            MatchUtils.getMatchMode() === Modes.SINGLE || MatchUtils.getMatchMode() === GameModes.CUSTOM
        const { level = 1 } = player.getGameData() ?? {}

        this.setActiveButton(this.challengeFriendsButton, !isYandex)
        this.setActiveButton(this.createShortcutButton, isYandex && D.isDesktop())
        this.setActiveButton(this.customModeButton, isSingleCustomMode && level >= CustomModeLevel)
        this.setActiveButton(this.resumeButton, !isSingleCustomMode)
    }

    private setActiveButton(button: Button, active: boolean): void {
        if (!button || !button.node.parent) return

        button.node.parent.active = active
    }

    public switchMusicButton(): void {
        if (!this.musicSwitch) {
            console.warn('SettingsScreen: musicSwitch not found')
            return
        }

        const state = this.musicSwitch.switch()
        player.setSetting('music', state)

        // ! For Unlocking DevTools
        this.node.emit(DevTools.Events.TRIGGER_UNLOCK_KEY, { code: state ? 1 : 0 })

        if (state) {
            const mode = MatchUtils.getMatchMode()
            AudioUtils.playMusic(mode === Modes.TOURNAMENT ? CodeSoundKeys.TOURNAMENT_MUSIC : CodeSoundKeys.BG_MUSIC)
        } else {
            AudioUtils.pauseMusic()
        }
    }

    public switchSoundButton(): void {
        if (!this.soundSwitch) {
            console.warn('SettingsScreen: soundSwitch not found')
            return
        }

        const state = this.soundSwitch.switch()
        player.setSetting('sound', state)

        // ! For Unlocking DevTools
        this.node.emit(DevTools.Events.TRIGGER_UNLOCK_KEY, { code: state ? 3 : 2 })
    }

    public switchVibrateButton(): void {
        if (!this.vibrateSwitch) {
            console.warn('SettingsScreen: vibrateSwitch not found')
            return
        }

        const state = this.vibrateSwitch.switch()
        player.setSetting('vibrate', state)
    }

    private updateVibrateButton = (): void => {
        const button = this.vibrateSetting.getComponentInChildren(Button)
        const switchState = this.vibrateSetting.getComponentInChildren(SwitchButton)
        const opacityState = this.vibrateSetting.getComponentInChildren(OpacityToggler)

        if (!button || !switchState || !opacityState) {
            console.warn('SettingsScreen: vibrate button missing components')
            return
        }

        if (!D.isMobile()) {
            button.interactable = false
            switchState.setEnabled(false)
            opacityState.changeState(false)
        } else {
            const vibrate = player.getPlayerSetting('vibrate') ?? false
            // switchState.updateStatus(vibrate)
            switchState.setEnabled(vibrate)
        }
    }

    // ? Callback for click event of ResumeButton
    public handleClickedResume() {
        this.resumeGame()
    }

    // ? Callback for click event of CustomModeButton
    public async handleClickedCustomMode() {
        this.setVisible(false)
        const screenPayload = await ScreenUtils.openScreenAndWaitForClose(ScreenKeysEnum.CUSTOM_GAME_SCREEN)

        const { customMode } = screenPayload.screenData ?? {}
        if (!customMode) {
            this.node.scene.emit(GameplayEvents.HIDE_UI)
            this.setVisible(true)
            return
        }

        this.close()
        this.node.scene.emit(GameplayEvents.GAME_RESUME)
        this.node.scene.emit(GameplayEvents.GAME_REPLAY, customMode ? GameModes.CUSTOM : undefined)
    }

    // ? Callback for click event of CloseButton
    public handleClickedClose() {
        this.resumeGame()
    }

    // ? Callback for click event of RestartButton
    public async handleClickedRestart(): Promise<void> {
        this.close()

        const { level = 1 } = player.getGameData() ?? {}

        if (MatchUtils.getMatchMode() === GameModes.CUSTOM) {
            await AdsUtils.showRewardedInterstitialAdAsync({ showError: false })
        } else {
            await AdsUtils.showInterstitialAdAsync({ forLevel: level })
        }

        if (MatchUtils.getMatchMode() === Modes.TOURNAMENT) {
            ScreenUtils.openScreen(ScreenKeysEnum.START_OVER_SCREEN)
            return
        }

        this.node.scene.emit(GameplayEvents.GAME_RESUME)
        this.node.scene.emit(GameplayEvents.GAME_REPLAY)
    }

    // ? Callback for click event of InviteFriendsButton
    public async handleClickedChallengeFriends(): Promise<void> {
        if (GameSDK.isGameSDK('MsGames')) {
            //? Sharing game to friends if SDK is MsGames
            ScreenUtils.showLoadingScreen()
            await GameSDK.extra.shareAsync()
            ScreenUtils.closeLoadingScreen()
            return
        }

        this.close()
        this.node.scene.emit(GameplayEvents.CHALLENGE_FRIENDS)
    }

    // ? Callback for click event of CreateShortcutButton
    public async handleClickedCreateShortcut(): Promise<void> {
        try {
            await GameSDK.extra.checkAndCreateShortcutAsync()
        } catch {
            ScreenUtils.showNotificationScreenWithLocalizedKey('error.shortcutFail')
        }
    }

    private resumeGame() {
        this.close()
        this.node.scene.emit(GameplayEvents.GAME_RESUME)
        this.node.scene.emit(GameplayEvents.SHOW_UI)
    }

    private setVisible(visible: boolean) {
        this.containerUIOpacity.opacity = visible ? 255 : 0
    }
}
