import ComponentUtils from 'db://assets/utils/ComponentUtils'
import { _decorator, CCFloat, Component, Sprite, UIOpacity, UITransform } from 'cc'

const { ccclass, property } = _decorator

@ccclass('Ray')
export default class Ray extends Component {
    @ComponentUtils.required()
    @property(UITransform)
    public readonly uiTransform!: UITransform

    @ComponentUtils.required()
    @property(Sprite)
    public readonly sprite!: Sprite

    @property({ type: UIOpacity })
    public readonly uiOpacity!: UIOpacity

    @property({ type: CCFloat })
    public readonly widthOffset = 0

    protected __preload(): void {
        ComponentUtils.validateRequiredProperties(this, Ray)
    }
}
