import GameModes from 'db://assets/gameplay/constants/GameMode'
import MatchUtils from 'db://assets/utils/MatchUtils'
import { _decorator, Component, Label, Node, RealCurve, tween, UIOpacity, UITransform, Vec3, Widget } from 'cc'
import AnimUtils from '../../../../../utils/AnimUtils'
import ComponentUtils from '../../../../../utils/ComponentUtils'
import type AnimAbortSignal from '../../../../../utils/common/AnimAbortSignal'
import { CustomScoreContainer } from '../custom/CustomScoreContainer'
import type { StatusConfig } from '../GameSceneLayout'
import { ScoreBlock } from '../header/score-block/ScoreBlock'

const { ccclass, property, disallowMultiple } = _decorator

const NORMAL_POSITIONS = {
    score: -589.003,
    item: -861.836,
    sidebar: 1114,
}

const CUSTOM_POSITIONS = {
    score: -568.518,
    item: -981.836,
    sidebar: 1222,
}

@ccclass('Sidebar')
@disallowMultiple
export class Sidebar extends Component {
    @property(ScoreBlock)
    private scoreBlock!: ScoreBlock

    @property(Node)
    private itemContainer!: Node

    @property(Label)
    private levelNumber!: Label

    @property(Node)
    private sidebarContainer!: Node

    @property(Widget)
    private sidebarBackground!: Widget

    @property(Node)
    private leavesContainer!: Node

    @property(CustomScoreContainer)
    private customScoreContainer!: CustomScoreContainer

    @property(RealCurve)
    private entranceCurve: RealCurve = new RealCurve()

    @property(UIOpacity)
    private uiOpacity!: UIOpacity

    private originSidebarPosition: number = 0
    private originLeavesPosition: number = 0

    public saveOriginPos(): void {
        this.originSidebarPosition = this.sidebarContainer.position.y
        this.originLeavesPosition = this.leavesContainer.position.y
    }

    public initUIPos(): void {
        this.sidebarContainer.setPosition(this.sidebarContainer.position.x, this.originSidebarPosition + 1250)
        this.leavesContainer.setPosition(this.leavesContainer.position.x, this.originLeavesPosition + 150)
    }

    public async playExitAnimUI(entranceSignal: AnimAbortSignal) {
        const cb = () => this.setActive(false)

        entranceSignal.addAbortListener(cb)

        const sidebarTween = tween(this.sidebarContainer).to(
            0.2,
            {
                position: new Vec3(this.sidebarContainer.position.x, this.originSidebarPosition + 1250, 0),
            },
            { easing: 'quadIn' },
        )

        const leavesTween = tween(this.leavesContainer)
        leavesTween.delay(0.1).to(
            0.2,
            {
                position: new Vec3(this.leavesContainer.position.x, this.originLeavesPosition + 150, 0),
            },
            { easing: 'quadIn' },
        )

        await Promise.all([
            AnimUtils.runTweenAsync(sidebarTween, entranceSignal),
            AnimUtils.runTweenAsync(leavesTween, entranceSignal),
        ])

        entranceSignal.removeAbortListener(cb)

        if (!entranceSignal.aborted) {
            this.setActive(false)
        }
    }

    public async playEntranceAnimUI(entranceSignal: AnimAbortSignal) {
        this.setActive(true)

        const sidebarTween = tween(this.sidebarContainer).to(
            0.3,
            {
                position: new Vec3(this.sidebarContainer.position.x, this.originSidebarPosition, 0),
            },
            {
                easing: (t: number) => {
                    return this.entranceCurve.evaluate(t)
                },
            },
        )

        const leavesTween = tween(this.leavesContainer)
        leavesTween.to(
            0.2,
            {
                position: new Vec3(this.leavesContainer.position.x, this.originLeavesPosition, 0),
            },
            { easing: 'quadIn' },
        )

        await Promise.all([
            AnimUtils.runTweenAsync(sidebarTween, entranceSignal),
            AnimUtils.runTweenAsync(leavesTween, entranceSignal),
        ])
    }

    @ComponentUtils.onlyRunInLifeCycle
    public setActive(active: boolean): void {
        if (!this.uiOpacity) return
        this.uiOpacity.opacity = active ? 255 : 0
    }

    public setLevelText(level: number) {
        this.levelNumber.string = level.toString()
    }

    public updateStatus(config: StatusConfig): void {
        if (config.score !== undefined) this.scoreBlock.setScore(config.score)
        if (config.level !== undefined) this.setLevelText(config.level)

        this.customScoreContainer.updateStatus()
        this.updateUIByMode()
    }

    public updateScore(score: number): void {
        this.scoreBlock.updateScore(score)
    }

    private updateUIByMode(): void {
        const positions = MatchUtils.getMatchMode() === GameModes.CUSTOM ? CUSTOM_POSITIONS : NORMAL_POSITIONS

        this.scoreBlock.node.position.set(this.scoreBlock.node.position.x, positions.score)
        this.itemContainer.position.set(this.itemContainer.position.x, positions.item)

        this.sidebarContainer.getComponent(UITransform)!.height = positions.sidebar
        this.sidebarBackground.updateAlignment()
    }
}
