import { Component, Label, Vec3, _decorator } from 'cc'
import GameplayEvents from 'db://assets/gameplay/constants/GameEvents'
import GuideMasks from 'db://assets/gameplay/guides/GuideMasks'
import AnimUtils from 'db://assets/utils/AnimUtils'
import ComponentUtils from 'db://assets/utils/ComponentUtils'
import { CustomButton } from '../custom-button/CustomButton'

const { ccclass, property } = _decorator

@ccclass('CustomGuideUI')
export class CustomGuideUI extends Component {
    @ComponentUtils.required()
    @property(CustomButton)
    private customButton!: CustomButton

    @ComponentUtils.required()
    @property(GuideMasks)
    private guideMasks!: GuideMasks

    @ComponentUtils.required()
    @property(Label)
    private guideInfoText!: Label

    private guideShown: boolean = false

    public update(): void {
        if (!this.guideShown) return

        const position = this.customButton.node.worldPosition
        this.guideInfoText.node.worldPosition = new Vec3(position.x - 100, position.y, 0)
        this.guideMasks.setOtherMaskTransform(position.x, position.y, 150, 150)
    }

    @ComponentUtils.onlyRunInLifeCycleAsync
    public async startGuide(): Promise<void> {
        this.guideShown = true
        this.guideInfoText.node.active = true

        await this.guideMasks.show()
        await Promise.race([this.waitForClick(), AnimUtils.waitTweenAsync(3)])
        await this.guideMasks?.hide()

        this.guideInfoText.node.active = false
        this.guideShown = false
    }

    @ComponentUtils.onlyRunInLifeCycleAsync
    private waitForClick(): Promise<void> {
        return new Promise<void>((resolve) => {
            this.node.scene.once(GameplayEvents.GUIDE_MASK_CLICKED, () => {
                resolve()
            })
        })
    }
}
