import { Component, _decorator } from 'cc'
import LeaderboardUtils from '../../utils/LeaderboardUtils'
import MatchUtils from '../../utils/MatchUtils'
import TournamentUtils, { type TournamentData } from '../../utils/TournamentUtils'

const { ccclass } = _decorator
const {
    Utils: { Object: O },
} = Ludex

const { player, leaderboard } = codex

type Leader = {
    score: number
    rank: number
}

type LeaderboardData = Ludex.Plugins.Leaderboards.Leaderboard

@ccclass('TournamentManager')
export class TournamentManager extends Component {
    public leaderData: Leader[] = []
    public tournamentData: TournamentData | null = null
    public leaderboardData: LeaderboardData | null = null
    public leaderboardName: string | null = null

    public async init(): Promise<void> {
        await this.requestTournamentData()
        await this.requestLeaderData()
    }

    private async requestTournamentData(): Promise<void> {
        const { tournamentId } = MatchUtils.getMatchCustomData() ?? {}
        if (!tournamentId) return

        await TournamentUtils.requestTournamentDataAsync()
        this.tournamentData = TournamentUtils.getTournamentDataById(tournamentId) ?? null
    }

    public async requestLeaderData(): Promise<void> {
        const { leaderboardId } = MatchUtils.getMatchCustomData() ?? {}
        if (!leaderboardId) return

        await this.requestLeaderboardData(leaderboardId)
        if (!this.leaderboardData) return

        const playerId = player.getPlayerId()

        this.leaderData = O.vals(this.leaderboardData.leaders)
            .reduce((acc, leader) => {
                if (leader.playerId !== playerId && leader.score > 0) {
                    const l = {
                        score: leader.score,
                        rank: leader.rank,
                    }
                    acc.push(l)
                }
                return acc
            }, [] as Leader[])
            .sort((a, b) => a.score - b.score)

        this.leaderData.forEach((leader, index) => {
            leader.rank = this.leaderData.length - index
        })
    }

    private async requestLeaderboardData(leaderboardId: string): Promise<void> {
        this.leaderboardName = leaderboard.getLeaderboardName(leaderboardId)
        this.leaderboardData = await LeaderboardUtils.getAllLeaderboardAsync(leaderboardId)
    }
}
