import { Component, UITransform, _decorator, game } from 'cc'
import GameEvents from '../../constants/GameEvents'
import CocosUtils from '../../utils/CocosUtils'
const { ccclass } = _decorator

@ccclass('Background')
export class Background extends Component {
    private uiTransform!: UITransform

    private ratio!: number

    public __preload(): void {
        this.uiTransform = this.getComponent(UITransform)!
        this.ratio = this.uiTransform.width / this.uiTransform.height

        this.listenGameEvents()
        this.handleViewResized()
    }

    private listenGameEvents() {
        game.on(GameEvents.ORIENTATION_CHANGED, this.handleViewResized, this)
        game.on(GameEvents.VIEW_RESIZED, this.handleViewResized, this)
    }

    private unlistenGameEvents() {
        game.off(GameEvents.VIEW_RESIZED, this.handleViewResized, this)
        game.off(GameEvents.ORIENTATION_CHANGED, this.handleViewResized, this)
    }

    private handleViewResized(): void {
        const width = CocosUtils.cssSizeToWorldSize(CocosUtils.getScreenWidthCss())
        const height = CocosUtils.cssSizeToWorldSize(CocosUtils.getScreenHeightCss())

        const screenRatio = width / height

        if (screenRatio < this.ratio) {
            this.uiTransform.width = height * this.ratio
            this.uiTransform.height = height
        } else {
            this.uiTransform.width = width
            this.uiTransform.height = width / this.ratio
        }
    }

    protected onDestroy(): void {
        this.unlistenGameEvents()
    }
}
