import { Component, Label, UIOpacity, Vec3, _decorator, tween } from 'cc'
import AnimUtils from '../../../../../utils/AnimUtils'
import ComponentUtils from '../../../../../utils/ComponentUtils'
import type AnimAbortSignal from '../../../../../utils/common/AnimAbortSignal'

const { ccclass, property } = _decorator

const START_POSITION = Vec3.ZERO.clone().add3f(0, -100, 0)
const END_POSITION = Vec3.ZERO.clone().add3f(0, 100, 0)

const {
    Utils: { Number: N },
} = Ludex

@ccclass('CoinTextPopup')
export class CoinTextPopup extends Component {
    @property(Label)
    public label!: Label

    @property(UIOpacity)
    public uiOpacity!: UIOpacity

    protected __preload(): void {
        ComponentUtils.lazyValidateProperties(this)
        this.node.active = false
    }

    public async show(amount: number, signal: AnimAbortSignal): Promise<void> {
        this.enableNode()

        this.initializePopup(amount)
        await this.showTextPopupAnimation(signal)
        await this.hidePopup()

        this.disableNode()
    }

    public enableNode(): void {
        this.node.active = true
    }

    @ComponentUtils.onlyRunInLifeCycle
    public disableNode(): void {
        this.node.active = false
    }

    @ComponentUtils.onlyRunInLifeCycle
    private initializePopup(amount: number) {
        const text = N.getShortNumber(amount, 3)
        this.label.string = text
        this.uiOpacity.opacity = 255

        this.node.setScale(0.5, 0.5, 0.5)
        this.node.setPosition(START_POSITION)
    }

    @ComponentUtils.onlyRunInLifeCycleAsync
    private async hidePopup() {
        this.node.setScale(Vec3.ONE)
        this.node.setPosition(END_POSITION)

        //! not subscribed to signal because in the case the user skipped the animation we'd still want them to see this node disappear
        await AnimUtils.runTweenAsync(
            tween(this.uiOpacity).to(0.5, {
                opacity: 0,
            }),
        )
    }

    @ComponentUtils.onlyRunInLifeCycleAsync
    private async showTextPopupAnimation(signal: AnimAbortSignal) {
        await Promise.all([
            AnimUtils.runTweenAsync(
                tween(this.node).to(
                    1.5,
                    {
                        position: END_POSITION,
                    },
                    {
                        easing: 'sineOut',
                    },
                ),
                signal,
            ),
            AnimUtils.runTweenAsync(
                tween(this.node).to(
                    0.5,
                    {
                        scale: Vec3.ONE,
                    },
                    {
                        easing: 'backOut',
                    },
                ),
                signal,
            ),
        ])
    }
}
