import AudioUtils from '../../../utils/AudioUtils'

const withLoweredVolume =
    <F extends (...args: any[]) => any, O = ReturnType<F> extends Promise<infer O> ? O : never>(fn: F) =>
    async (...args: Parameters<F>): Promise<O> => {
        AudioUtils.setVolumeMusic(0, true)
        const out = await fn(...args)
        AudioUtils.setVolumeMusic(0.3, true)
        return out
    }

export default withLoweredVolume
