import { CCBoolean, Enum, _decorator } from 'cc'
import { EDITOR_NOT_IN_PREVIEW } from 'cc/env'
import { ExtendedButton } from '../../../../../../components/button/ExtendedButton'
import DashboardEvents from '../../../../../../constants/DashboardEvents'
import DashboardEventButtonTypes from '../../../../../../gameplay/constants/DashboardEventButtonTypes'
const { ccclass, property } = _decorator

@ccclass('EventButton')
export class EventButton extends ExtendedButton {
    @property({ type: Enum(DashboardEventButtonTypes) })
    public eventType: DashboardEventButtonTypes = DashboardEventButtonTypes.STAR_CHEST

    private _isAvailable = false

    @property(CCBoolean)
    public get isAvailable() {
        return this._isAvailable
    }

    public set isAvailable(value: boolean) {
        this._isAvailable = value
        this.fakeClick()
    }

    public handleClick(): void {
        this.node.scene.emit(DashboardEvents.DASHBOARD_EVENTS_BUTTON_PRESS, {
            type: this.eventType,
        } as DashboardEventButtonPayload)
    }

    protected override setAnimationState(state: 'DOWN' | 'HOVER' | 'RESET'): void {
        if (EDITOR_NOT_IN_PREVIEW) return
        this.animationController.setValue('RESET-SPECIAL', this._isAvailable)
        super.setAnimationState(state)
    }
}
