import { CCString, Component, Label, _decorator } from 'cc'
import { TapperedProgressBar } from '../../../../../../components/tappered-progress-bar/TapperedProgressBar'
const { ccclass, property } = _decorator

@ccclass('EventProgressBar')
export class EventProgressBar extends Component {
    @property(TapperedProgressBar)
    private progressBar!: TapperedProgressBar

    @property(Label)
    private progressLabel!: Label

    @property(CCString)
    public get text(): string {
        return this.progressLabel.string
    }

    @property({
        range: [0, 1, 0.01],
        slide: true,
        tooltip: 'i18n:progress.progress',
    })
    public get progress(): number {
        return this.progressBar.progress
    }

    public set progress(value: number) {
        this.progressBar.progress = value
    }

    public set text(value: string) {
        this.progressLabel.string = value
    }

    public __preload(): void {
        // called before onLoad, use for initialization
    }

    public onLoad(): void {
        this.validateRequiredComponents()
    }

    private validateRequiredComponents(): void {
        if (!this.progressBar) {
            throw new Error('EventProgressBar: Missing required component TapperedProgressBar')
        }

        if (!this.progressLabel) {
            throw new Error('EventProgressBar: Missing required component Label')
        }
    }
}
