import { _decorator, Button, Component, Vec3 } from 'cc'
import { OpacityToggler } from '../../components/common/OpacityToggler'
import ComponentUtils from '../../utils/ComponentUtils'
import GameplayEvents from '../constants/GameEvents'
import { AlphaMasks } from '../game-objects/mask/AlphaMasks'
import type { GameMask } from '../game-objects/mask/GameMask'

const { ccclass, property } = _decorator

@ccclass('GuideMasks')
class GuideMasks extends Component {
    @property(OpacityToggler)
    private background!: OpacityToggler

    @property(AlphaMasks)
    private alphaMasks!: AlphaMasks

    @property(Button)
    private maskButtonComponent!: Button

    public __preload(): void {
        this.validateRequiredComponents()
    }

    private validateRequiredComponents(): void {
        ComponentUtils.lazyValidateProperties(this)
    }

    public async show(): Promise<void> {
        this.maskButtonComponent.enabled = true
        await this.background.changeStateFade(true, 0.25)
    }

    public async hide(): Promise<void> {
        this.maskButtonComponent.enabled = false
        await this.background.changeStateFade(false, 0.25)
    }

    public onClick(): void {
        this.node.scene.emit(GameplayEvents.GUIDE_MASK_CLICKED)
    }

    public setOtherMaskTransform(x: number, y: number, width: number, height: number): void {
        this.alphaMasks.masks[1]!.node.worldPosition = new Vec3(-9999, -9999, 0)
        const mask = this.alphaMasks.masks[0]!

        this.setMaskTransform(mask, x, y, width, height)
    }

    public setItemMaskTransform(x: number, y: number, width: number, height: number): void {
        this.alphaMasks.masks[0]!.node.worldPosition = new Vec3(-9999, -9999, 0)
        const mask = this.alphaMasks.masks[1]!

        this.setMaskTransform(mask, x, y, width, height)
    }

    private setMaskTransform(mask: GameMask, x: number, y: number, width: number, height: number): void {
        mask.uiTransform.setContentSize(width, height)
        mask.node.setWorldPosition(x, y, 0)
    }
}

export default GuideMasks
