import ItemKeys from '../../constants/ItemKeys'
import type EffectManager from '../../manager/EffectManager'
import type Board from '../board'
import withBlockedInput from './withBlockedInput'
import withItemAnalytics from './withItemAnalytics'
import withLoweredVolume from './withLoweredVolume'

const useTornadoThunderCombinedLogic = async (board: Board, effectManager: EffectManager) => {
    const uncleared = board.getUncleared()
    if (uncleared.length <= 2 * 2) return

    const pairCount = uncleared.length / 2
    const halfPairCount = Math.ceil(pairCount / 2)
    const pairs = board.getPairs(halfPairCount)

    board.clearSelectedTile()

    await effectManager.playMultiThunderAnimation(pairs, board, 0.125)
}

const useTornadoThunderCombined = withItemAnalytics(
    ItemKeys.THUNDER,
    withLoweredVolume(withBlockedInput(useTornadoThunderCombinedLogic)),
)

export default useTornadoThunderCombined
