import { type Component, isCCObject } from 'cc'
import {
    type FnThatReturnVoid,
    type FnThatReturnVoidPromise,
    onlyRunInLifeCycle,
    onlyRunInLifeCycleAsync,
} from './component-utils/life-cycle'
import { invokeRequiredHook, required, type ValidationStrategy } from './component-utils/required'

const {
    Utils: { Array: A, Object: O, Valid: V },
} = Ludex

type ComponentValid = {
    [key: string]: boolean
}

// TODO: Add more base components
const BaseComponentNames = [
    'cc.Mask',
    'cc.Label',
    'cc.Canvas',
    'cc.Camera',
    'cc.Layout',
    'cc.Button',
    'cc.Sprite',
    'cc.Widget',
    'cc.Renderer',
    'cc.Animation',
    'cc.UIOpacity',
]

class ComponentUtilsCore {
    protected static inst$: ComponentUtilsCore

    public static get inst() {
        if (!this.inst$) {
            this.inst$ = new this()
        }

        return this.inst$
    }

    protected validComponentCalled$: ComponentValid = {}

    // biome-ignore lint/suspicious/noExplicitAny: needed for experimental decorators
    public static required(strategy?: ValidationStrategy): (target: Record<string, any>, propertyKey: string) => void
    // biome-ignore lint/suspicious/noExplicitAny: needed for experimental decorators
    public static required(target: Record<string, any>, propertyKey: string): void
    // biome-ignore lint/suspicious/noExplicitAny: needed for experimental decorators
    public static required(strategyOrTarget?: ValidationStrategy | Record<string, any>, propertyKey?: string) {
        if (typeof propertyKey === 'string') {
            // biome-ignore lint/suspicious/noExplicitAny: needed for experimental decorators
            const target = strategyOrTarget as Record<string, any>
            return required(target, propertyKey)
        }

        const strategy = strategyOrTarget as ValidationStrategy | undefined
        return required(strategy)
    }

    // biome-ignore lint/suspicious/noExplicitAny: needed for generic functions
    public static onlyRunInLifeCycle<T extends (...args: any[]) => any>(
        customIsValid: () => boolean,
    ): (_target: object, propertyKey: string, descriptor: TypedPropertyDescriptor<FnThatReturnVoid<T>>) => void

    // biome-ignore lint/suspicious/noExplicitAny: needed for generic functions
    public static onlyRunInLifeCycle<T extends (...args: any[]) => any>(
        _target: Component,
        propertyKey: string,
        descriptor: TypedPropertyDescriptor<FnThatReturnVoid<T>>,
    ): void

    // biome-ignore lint/suspicious/noExplicitAny: needed for generic functions
    public static onlyRunInLifeCycle<T extends (...args: any[]) => any>(
        compOrCustomValidator: Component | (() => boolean),
        propertyKey?: string,
        descriptor?: TypedPropertyDescriptor<FnThatReturnVoid<T>>,
    ) {
        if (typeof compOrCustomValidator === 'object' && propertyKey !== undefined && descriptor !== undefined) {
            return onlyRunInLifeCycle(compOrCustomValidator, propertyKey, descriptor)
        }

        const customIsValid = compOrCustomValidator as () => boolean
        return onlyRunInLifeCycle(customIsValid)
    }

    // biome-ignore lint/suspicious/noExplicitAny: needed for generic functions
    public static onlyRunInLifeCycleAsync<T extends (...args: any[]) => any>(
        customIsValid: () => boolean,
    ): (_target: object, propertyKey: string, descriptor: TypedPropertyDescriptor<FnThatReturnVoidPromise<T>>) => void

    // biome-ignore lint/suspicious/noExplicitAny: needed for generic functions
    public static onlyRunInLifeCycleAsync<T extends (...args: any[]) => any>(
        _target: Component,
        propertyKey: string,
        descriptor: TypedPropertyDescriptor<FnThatReturnVoidPromise<T>>,
    ): void

    // biome-ignore lint/suspicious/noExplicitAny: needed for generic functions
    public static onlyRunInLifeCycleAsync<T extends (...args: any[]) => any>(
        compOrCustomValidator: Component | (() => boolean),
        propertyKey?: string,
        descriptor?: TypedPropertyDescriptor<FnThatReturnVoidPromise<T>>,
    ) {
        if (typeof compOrCustomValidator === 'object' && propertyKey !== undefined && descriptor !== undefined) {
            return onlyRunInLifeCycleAsync(compOrCustomValidator, propertyKey, descriptor)
        }

        const customIsValid = compOrCustomValidator as () => boolean
        return onlyRunInLifeCycleAsync(customIsValid)
    }

    /** @deprecated Put `ComponentUtils.required()` on required properties and use `ComponentUtils.validateRequiredProperties` */
    public static lazyValidateProperties(component: Component, enabled = true): void {
        if (!enabled) return

        // biome-ignore lint/suspicious/noShadowRestrictedNames: needed for experimental decorators
        const { constructor } = component
        const { name } = constructor

        if (this.inst.validComponentCalled$[name]) return

        // console.info('lazyValidateProperties', constructor.name, { component })

        try {
            this.inst.validateRequiredComponents(component)

            this.inst.validateRequiredProperties(component)

            this.inst.validateCustomProperties(component)
        } catch (error) {
            component.enabled = false

            console.error(error)
        } finally {
            this.inst.validComponentCalled$[name] = true
        }
    }

    protected validateRequiredComponents(component: Component): void {
        // biome-ignore lint/suspicious/noShadowRestrictedNames: needed for experimental decorators
        const { name, constructor } = component

        const componentNames = this.getRequiredComponentNames(constructor)
        // console.info('componentNames', componentNames)

        for (const componentName of componentNames) {
            if (!component.getComponent(componentName)) {
                throw new Error(`${name}: Missing required ${componentName} component`)
            }

            /* ? Should we check the property name?
            const propName = S.toCamelCase(componentName)
            if (!O.hasOwn(component, propName)) {
                throw new Error(`${name}: Missing required ${propName} property`)
            } */
        }
    }

    public static validateRequiredProperties(component: Component, classConstructor?: new () => Component) {
        invokeRequiredHook(component, classConstructor)
    }

    protected validateRequiredProperties(component: Component): void {
        // biome-ignore lint/suspicious/noShadowRestrictedNames: needed for experimental decorators
        const { name, constructor } = component

        const propNames = this.getRequiredPropNames(constructor)
        // console.info('propNames', propNames)

        for (const propName of propNames) {
            if (!O.hasOwn(component, propName)) {
                throw new Error(`${name}: Missing required ${propName} property`)
            }

            const property = this.getProperty(propName, component)
            if (!isCCObject(property)) {
                throw new Error(`${name}: ${propName} property is not a cc object`)
            }
        }
    }

    protected validateCustomProperties(component: Component): void {
        const { name } = component

        const customPropNames = this.getCustomPropNames(component)
        // console.info('customPropNames', customPropNames)

        for (const propName of customPropNames) {
            if (!O.hasOwn(component, propName)) {
                throw new Error(`${name}: Missing required ${propName} property`)
            }

            const property = this.getProperty(propName, component)
            if (!isCCObject(property)) {
                throw new Error(`${name}: ${propName} property is not a cc object`)
            }
        }
    }

    protected getProperty(name: string, object: unknown): unknown {
        if (!O.hasOwn(object, name)) return undefined

        const { value } = Object.getOwnPropertyDescriptor(object, name) ?? {}

        return value
    }

    protected getProps(construct: unknown): string[] {
        if (!O.hasOwn(construct, '__props__')) return []

        const { __props__: props } = construct

        if (!Array.isArray(props)) return []

        return props
    }

    protected getRequiredPropNames(construct: unknown): string[] {
        const props = this.getProps(construct)
        return props.filter((name) => {
            if (name === 'node') return false
            if (!O.hasOwn(construct, name)) return false
            if (!isCCObject(construct[name])) return false
            return !name.startsWith('_')
        })
    }

    protected getBaseComponentName(componentName: string): string {
        for (const baseComponentName of BaseComponentNames) {
            const index = baseComponentName.indexOf(componentName)
            if (index >= 0) {
                return baseComponentName
            }
        }

        return componentName
    }

    protected getRequiredComponentNames(construct: unknown): string[] {
        if (!O.hasOwn(construct, '_requireComponent')) return []

        const { _requireComponent: components } = construct

        if (!Array.isArray(components)) return []

        return components.map((component) => {
            return this.getBaseComponentName(component)
        })
    }

    protected getCustomPropNames(component: Component): string[] {
        const keys = Object.keys(component)
        const props = this.getProps(component.constructor)

        const isNotUnderscore = (name: string) => name.indexOf('_') < 0

        const customPropNames = A.difference(keys, props).filter(isNotUnderscore)

        return customPropNames.filter((name: string) => {
            if (!O.hasOwn(component, name)) return false

            const property = this.getProperty(name, component)
            if (V.isObject(property) && isCCObject(property)) return true

            return false
        })
    }
}

class ComponentUtils extends ComponentUtilsCore {
    // * Custom logic here
}

export default ComponentUtils
