import { type Rect, Vec2 } from 'cc'

export type Point = {
    position: IVec2Like
    radius: number
    isStatic: boolean
}

export default function separatePoints(points: Point[], minDistance: number, boundingBox: Rect): void {
    const maxIterations = 30
    let iterations = 0

    while (iterations++ < maxIterations) {
        let adjusted = false

        for (let i = 0; i < points.length; i++) {
            for (let j = i + 1; j < points.length; j++) {
                const a = points[i]!
                const b = points[j]!

                // Calculate effective minimum distance based on their radii
                const effectiveDistance = minDistance + a.radius + b.radius

                // Adjust only if they are too close
                if (Vec2.distance(a.position, b.position) < effectiveDistance) {
                    adjusted ||= adjustPosition(a, b, effectiveDistance, boundingBox)
                }
            }
        }

        if (!adjusted) break
    }

    // Ensure all points remain within the bounding box
    for (const point of points) clampToBoundingBox(point, boundingBox)
}

function adjustPosition(a: Point, b: Point, minDistance: number, boundingBox: Rect): boolean {
    const posA = a.position
    const posB = b.position
    const distance = Vec2.distance(posA, posB)

    if (distance >= minDistance) return false

    const direction = new Vec2(posB.x - posA.x, posB.y - posA.y).normalize()
    const overlap = minDistance - distance

    // Static points should not move
    if (!a.isStatic && !b.isStatic) {
        posA.x -= direction.x * overlap * 0.5
        posA.y -= direction.y * overlap * 0.5
        posB.x += direction.x * overlap * 0.5
        posB.y += direction.y * overlap * 0.5
    } else if (!a.isStatic) {
        posA.x -= direction.x * overlap
        posA.y -= direction.y * overlap
    } else if (!b.isStatic) {
        posB.x += direction.x * overlap
        posB.y += direction.y * overlap
    }

    // Clamp adjusted positions to the bounding box
    clampToBoundingBox(a, boundingBox)
    clampToBoundingBox(b, boundingBox)

    return true
}

function clampToBoundingBox(point: Point, boundingBox: Rect): void {
    const { position, radius } = point

    position.x = Math.max(boundingBox.xMin + radius, Math.min(boundingBox.xMax - radius, position.x))
    position.y = Math.max(boundingBox.yMin + radius, Math.min(boundingBox.yMax - radius, position.y))
}
