import { _decorator, Component } from 'cc'
import type AnimAbortSignal from '../../../utils/common/AnimAbortSignal'
import { TopLeader } from './TopLeader'

const { ccclass, property } = _decorator

export interface LeaderConfig {
    playerId: string
    name: string
    photo: string
    score: number
}

@ccclass('TopLeaders')
export class TopLeaders extends Component {
    @property(TopLeader)
    private topLeaders: TopLeader[] = []

    private leaderData: Ludex.Plugins.Leaderboards.Leader[] = []

    public updateData(data: Ludex.Plugins.Leaderboards.Leader[]): void {
        this.leaderData = data.slice(0, 3)
        this.leaderData.slice(0, 3).forEach((leader, index) => {
            this.topLeaders[index].updateInfo({
                playerId: leader.playerId,
                name: leader.name,
                photo: leader.photo,
                score: leader.score,
            })
        })
    }

    public async playShowLeadersAnimation(signal: AnimAbortSignal, stagger = 0.1): Promise<void> {
        const duration = 0.3

        await Promise.all(
            this.leaderData.map(async (_, index) => {
                const leader = this.topLeaders[index]
                await leader.runShowUpAnimation(index * stagger, duration, signal)
            }),
        )
    }

    public hideLeaders(): void {
        for (const leader of this.topLeaders) {
            leader.setVisible(false)
        }
    }
}
