import type { Vec2 } from 'cc'
import { _decorator, Component, ParticleSystem2D } from 'cc'
import ComponentUtils from '../../utils/ComponentUtils'

const { ccclass, disallowMultiple, executeInEditMode, requireComponent } = _decorator

@ccclass('ParticleSystem2DExtra')
@executeInEditMode(true)
@disallowMultiple(true)
@requireComponent(ParticleSystem2D)
export abstract class ParticleSystem2DExtra extends Component {
    protected particleSystem!: ParticleSystem2D

    public __preload(): void {
        // called before onLoad, use for initialization

        this.particleSystem = this.getComponent(ParticleSystem2D)!
        this.validateRequiredComponents()

        this.overrideFunctions()
    }

    /**
     * Call when a new particle is emitted
     */
    public abstract initParticle(particle: Particle2DExtra): void

    /**
     * Call every frame to update a particle
     */
    public abstract updateParticle(particle: Particle2DExtra, dt: number): void

    private overrideFunctions() {
        const emitParticle = this.particleSystem._simulator.emitParticle.bind(this.particleSystem._simulator)
        this.particleSystem._simulator.emitParticle = this.emitParticle(emitParticle)

        const stepFunction = this.particleSystem._simulator.step.bind(this.particleSystem._simulator)
        this.particleSystem._simulator.step = this.step(stepFunction)
    }

    private emitParticle =
        (func: (pos: Vec2) => void) =>
        (position: Vec2): void => {
            func(position)

            //? customize in here
            const length = this.particleSystem._simulator.particles.length
            const newParticle = this.particleSystem._simulator.particles[length - 1]
            this.initParticle(newParticle)
        }

    private step =
        (func: (dt: number) => void) =>
        (dt: number): void => {
            //? customize in here
            const particles = this.particleSystem._simulator.particles
            for (let i = 0; i < particles.length; i++) {
                const particle = particles[i]
                this.updateParticle(particle, dt)
            }

            func(dt)
        }

    private validateRequiredComponents() {
        ComponentUtils.lazyValidateProperties(this)
    }
}
