import { _decorator, Component, Vec3 } from 'cc'
import { OpacityToggler } from '../components/common/OpacityToggler'
import { ScreenKeysEnum, ScreenNames } from '../constants/ScreenKeys'
import { CodeSoundKeys } from '../constants/SoundKeys'
import AdsUtils from '../utils/AdsUtils'
import AudioUtils from '../utils/AudioUtils'
import ComponentUtils from '../utils/ComponentUtils'
import GeneralUtils from '../utils/GeneralUtils'
import MatchUtils from '../utils/MatchUtils'
import SceneUtils from '../utils/SceneUtils'
import ScreenUtils from '../utils/ScreenUtils'
import ThemeUtils from '../utils/ThemeUtils'
import FailKeys from './constants/FailKeys'
import GameplayEvents from './constants/GameEvents'
import GameModes from './constants/GameMode'
import ItemKeys from './constants/ItemKeys'
import { InputController } from './controller/InputController'
import type { Tile } from './game-objects/board'
import Board from './game-objects/board/Board'
import useHintItem from './game-objects/item/useHintItem'
import usePliersItem from './game-objects/item/usePliersItem'
import useThunderItem from './game-objects/item/useThunderItem'
import useTornadoItem from './game-objects/item/useTornadoItem'
import { PathDraw } from './game-objects/path-draw/PathDraw'
import { ClockManager } from './manager/ClockManager'
import { CoinManager } from './manager/CoinManager'
import { ComboManager } from './manager/ComboManager'
import EffectManager from './manager/EffectManager'
import GuidelinesManager from './manager/GuidelinesManager'
import { ItemManager } from './manager/ItemManager'
import LevelManager from './manager/LevelManager'
import { ScoreManager } from './manager/ScoreManager'
import { StateManager } from './manager/StateManager'
import { TournamentManager } from './manager/TournamentManager'
import { UIManager } from './manager/UIManager'

const { player, event, match, storage, analytics, monitorError, leaderboard } = codex

const {
    Events,
    Utils: { Time: T, Array: A, Valid: V },
    Configs: { Gameplay: GameplayConfig },
    Match: { Modes },
    Plugins: {
        Ads: { Types: AdTypes } = {},
    },
} = Ludex

const { ccclass, property } = _decorator

const DEFAULT_GAME_DATA = {
    level: 1,
    isFromLoader: false,
} as const satisfies GameData

@ccclass('Gameplay')
export class Gameplay extends Component {
    // Managers (System)
    public uiManager!: UIManager
    public levelManager!: LevelManager
    public stateManager!: StateManager
    public effectManager!: EffectManager
    public itemManager!: ItemManager
    public coinManager!: CoinManager
    public comboManager!: ComboManager
    public clockManager!: ClockManager
    public scoreManager!: ScoreManager
    public guidelinesManager: GuidelinesManager | undefined
    public tournamentManager: TournamentManager | undefined

    // Controllers (System)
    public inputController!: InputController

    // Components
    @property(Board)
    public board!: Board

    @property(OpacityToggler)
    public opacityToggler!: OpacityToggler

    @property(PathDraw)
    public pathDraw!: PathDraw

    private gameData: GameData | null = null

    public rescueTiles: Tile[] = []

    private isAutoPlayEnabled: boolean = false

    public isAdPreloadedForCurrentLevel: boolean = false

    public __preload(): void {
        this.initComponents()

        this.validateRequiredComponents()

        this.listenCoreEvents()
        this.listenInputEvents()
        this.listenFlowEvents()
    }

    public onLoad(): void {
        ThemeUtils.init()
    }

    protected onDestroy(): void {
        this.unlistenCoreEvents()
    }

    private initComponents() {
        this.initUIManager()
        this.initLevelManager()
        this.initStateManager()
        this.initEffectManager()
        this.initItemManager()
        this.initCoinManager()
        this.initComboManager()
        this.initClockManager()
        this.initScoreManager()
        this.initInputController()
        this.initGuidelinesManager()
        this.initPathDraw()
    }

    private initUIManager(): void {
        this.uiManager = this.node.parent?.getComponentInChildren(UIManager)!
        this.uiManager.init(this)
    }

    private initLevelManager(): void {
        this.levelManager = this.getComponent(LevelManager)!
    }

    private initStateManager(): void {
        this.stateManager = this.getComponent(StateManager)!
    }

    private initInputController(): void {
        this.inputController = this.getComponent(InputController)!
    }

    private initEffectManager(): void {
        this.effectManager = this.getComponent(EffectManager)!
    }

    private initItemManager(): void {
        this.itemManager = this.getComponent(ItemManager)!
        this.itemManager.init(this)
    }

    private initCoinManager(): void {
        this.coinManager = this.getComponent(CoinManager)!
    }

    private initComboManager(): void {
        this.comboManager = this.getComponent(ComboManager)!
        this.comboManager.init()
    }

    private initClockManager(): void {
        this.clockManager = this.getComponent(ClockManager)!
        this.clockManager.init()
    }

    private initScoreManager(): void {
        this.scoreManager = this.getComponent(ScoreManager)!
        this.scoreManager.init()
    }

    private initGuidelinesManager(): void {
        if (!GameplayConfig.Tutorial.Enabled) return

        this.guidelinesManager = this.getComponent(GuidelinesManager)!
        this.guidelinesManager.init(this)
    }

    private initPathDraw(): void {
        this.pathDraw.init(this.board)
    }

    private validateRequiredComponents(): void {
        ComponentUtils.lazyValidateProperties(this)
    }

    private listenCoreEvents(): void {
        event.on(Events.OPENING_SCREEN, this.handleOpenScreen)
        event.on(Events.CLOSING_SCREEN, this.handleCloseScreen)
    }

    private unlistenCoreEvents(): void {
        event.off(Events.OPENING_SCREEN, this.handleOpenScreen)
        event.off(Events.CLOSING_SCREEN, this.handleCloseScreen)
    }

    private readonly handleOpenScreen = (payload: SwitchScreenPayload) => {
        const { screenName } = payload

        if (screenName === ScreenNames[ScreenKeysEnum.GET_ITEM_SCREEN]) {
            this.uiManager.highlightCoinContainer()
        }
    }

    public handleShowUI = async () => {
        this.uiManager.updateStatus()
        this.uiManager.disableLayout()
        this.uiManager.setInteractableItems(false)

        await this.uiManager.playEntranceAnimUI()

        this.uiManager.enableLayout()
        this.uiManager.setInteractableItems(true)
    }

    public handleHideUI = async () => {
        this.uiManager.disableLayout()
        this.uiManager.setInteractableItems(false)

        await this.uiManager.playExitAnimUI()
    }

    private readonly handleCloseScreen = ({ screenName }: { screenName: string }) => {
        if (screenName === ScreenNames[ScreenKeysEnum.GET_ITEM_SCREEN]) {
            this.uiManager.unHighlightCoinContainer()
        }
    }

    private listenInputEvents(): void {
        this.node.scene.on(GameplayEvents.SHOW_UI, this.handleShowUI)
        this.node.scene.on(GameplayEvents.HIDE_UI, this.handleHideUI)

        this.node.scene.on(GameplayEvents.GAME_PAUSE, this.handlePauseGame)
        this.node.scene.on(GameplayEvents.GAME_RESUME, this.handleResumeGame)
        this.node.scene.on(GameplayEvents.GAME_REPLAY, this.handleReplayGame)
        this.node.scene.on(GameplayEvents.GAME_FINISH, this.handleFinishGame)
        this.node.scene.on(GameplayEvents.NEXT_LEVEL, this.handleNextLevel)
        this.node.scene.on(GameplayEvents.START_OVER, this.handleStartOver)
        this.node.scene.on(GameplayEvents.MAX_LEVEL, this.handleMaxLevel)
        this.node.scene.on(GameplayEvents.GAME_EXIT, this.handleExitGame)

        this.node.scene.on(GameplayEvents.TILE_CLICK, this.handleTileClick)
        this.node.scene.on(GameplayEvents.GRID_MATCH_SUCCESS, this.handleGridMatchSuccess)
        this.node.scene.on(GameplayEvents.GRID_MATCH_FAIL, this.handleGridMatchFail)
        this.node.scene.on(GameplayEvents.GRID_ORIENTATION_CHANGE, this.handleGridOrientationChange)

        this.node.scene.on(GameplayEvents.PAUSE_TIME, this.handlePauseTime)
        this.node.scene.on(GameplayEvents.RESUME_TIME, this.handleResumeTime)

        this.node.scene.on(GameplayEvents.TIME_OUT, this.handleClockTimeout)
        this.node.scene.on(GameplayEvents.HALF_TIME, this.handleHalfTime)
        this.node.scene.on(GameplayEvents.RESCUE_START, this.handleStartRescue)
        this.node.scene.on(GameplayEvents.RESCUE_FINISHED, this.handleFinishRescue)
        this.node.scene.on(GameplayEvents.BOMB_EXPLODE, this.handleBombExplode)
        this.node.scene.on(GameplayEvents.SHOW_HINT, this.handleShowHint)

        this.node.scene.on(GameplayEvents.ADD_SCORE, this.handleAddScore)

        this.node.scene.on(GameplayEvents.GRID_CLEAR, this.handleGridClear)

        this.node.scene.on(GameplayEvents.USE_ITEM, this.handleUseItem)

        this.node.scene.on(GameplayEvents.ADD_ITEM, this.handleAddItem)

        this.node.scene.on(GameplayEvents.AUTO_PLAY, this.handleAutoPlay)

        this.node.scene.on(GameplayEvents.ADD_STARS, this.handleAddStars)
    }

    private listenFlowEvents(): void {
        this.node.scene.on(GameplayEvents.CHALLENGE_FRIENDS, this.handleChallengeFriends)
    }

    public async start(): Promise<void> {
        this.initDataFirstTime()
        await this.tryInitTournamentMode()
        this.initGame()
        // this.initDevTools()
    }

    private initDataFirstTime(): void {
        this.initData()

        const sceneData = this.getSceneData()
        const { isFromLoader = false } = sceneData ?? {}

        let { level } = sceneData ?? {}
        if (level === undefined) {
            if (MatchUtils.getMatchMode() === Modes.TOURNAMENT) {
                level = 1
            } else {
                level = player.getGameData()?.level ?? 0
            }
        }

        this.setGameData({ level, isFromLoader })
    }

    private initData(): void {
        const sceneData = this.getSceneData()
        const { isFromLoader = false } = sceneData ?? {}

        const { level = 1, items } = player.getGameData() ?? {}
        const isFirstLogin = level === 0 && isFromLoader

        if (isFirstLogin && items) {
            items.hint = 2
            items.thunder = 1
            items.storm = 1
            items.pliers = 0

            player.setGameData({
                items,
                coins: 500,
                lastRewardedItems: Date.now(),
            })

            this.node.scene.emit(GameplayEvents.ADD_COINS, { coins: 500, playSound: false } as CoinDataPayload)
        } else if (items && this.checkItemsRewardable()) {
            if (items.hint < 5) items.hint += 1
            if (items.thunder < 5) items.thunder += 1
            if (items.storm < 5) items.storm += 1
            if (items.pliers < 5) items.pliers += 1

            player.setGameData({
                items,
                lastRewardedItems: Date.now(),
            })
        }

        this.setGameData({
            ...DEFAULT_GAME_DATA,
            level,
        })
    }

    private checkItemsRewardable(): boolean {
        const { lastRewardedItems = 0 } = player.getGameData() ?? {}

        if (T.isToday(lastRewardedItems)) return false

        return true
    }

    private initGame(): void {
        this.stateManager.init(this)
        this.stateManager.initialize()
    }

    private async tryInitTournamentMode(): Promise<void> {
        const mode = MatchUtils.getMatchMode()
        if (mode !== Modes.TOURNAMENT) return

        this.tournamentManager = this.getComponent(TournamentManager)!
        await this.tournamentManager.init()

        const { leaderboardData, leaderboardName, tournamentData } = this.tournamentManager

        //? Set game theme id
        if (!tournamentData) return
        const { themeId } = tournamentData.payload
        this.setGameData({ themeId })

        //? Request leaderboard data and update player's previous score
        if (!leaderboardData || !leaderboardName) return
        const playerId = player.getPlayerId()
        let playerLeader = leaderboardData.leaders[playerId]

        if (!playerLeader && leaderboard) {
            const playerLeaders = await leaderboard.getLeadersByPlayerIdsAsync(leaderboardName, [playerId])
            if (playerLeaders.length === 0) return

            playerLeader = playerLeaders[0]
        }

        storage.setStorageData('tournament', 'previousScore', playerLeader?.score ?? 0)
    }

    public setGameData(data: GameData): void {
        const correctLevel = data.level ?? this.gameData?.level ?? 1

        this.gameData = {
            ...this.gameData,
            ...data,
            level: correctLevel,
        }
    }

    public getGameData(): GameData | null {
        return this.gameData
    }

    private getSceneData(): SceneData | null {
        return SceneUtils.getSceneData(this.node.scene)
    }

    private readonly handlePauseGame = (): void => {
        this.node.scene.emit(GameplayEvents.PAUSE_TIME)
        this.node.pauseSystemEvents(true)
        this.opacityToggler.changeStateFade(false, 0.1)
        this.uiManager.setComboUI(false)
        this.processCrazyGamesGameplayStop()
    }

    private readonly handleResumeGame = (): void => {
        this.node.resumeSystemEvents(true)

        if (this.stateManager.isPlaying()) {
            this.node.scene.emit(GameplayEvents.RESUME_TIME)
        }

        this.processCrazyGamesGameplayStart()
        this.opacityToggler.changeStateFade(true, 0.1)
        this.uiManager.setComboUI(true)

        this.guidelinesManager?.startAutoGuides()
    }

    private readonly handleReplayGame = async (forceMode?: string): Promise<void> => {
        this.handleHideHint()

        try {
            const mode = MatchUtils.getMatchMode()
            if (mode === Modes.SINGLE || mode === GameModes.CUSTOM) {
                await MatchUtils.finishMatchAsync()
            }

            if (mode !== Modes.CHALLENGE_FRIEND) {
                await MatchUtils.startMatchAsync(forceMode)
            }

            if (mode === Modes.TOURNAMENT) {
                await MatchUtils.setMatchCustomDataAsync({ level: 1, score: 0 })
                this.setGameData({ level: 1 })
            }
        } catch (error) {
            console.warn('handleReplayGame', error)
        }

        this.stateManager.initialize()
    }

    private readonly handleFinishGame = () => {
        this.attemptCallCreateShortcut()

        if (this.guidelinesManager?.isNewbieGuideLevel()) {
            this.guidelinesManager.guideUi.guideSkipButton.active = false
            this.handleNextLevel()
        } else {
            this.stateManager.finish()
        }
    }

    private readonly handleNextLevel = async (): Promise<void> => {
        if (!this.gameData) return

        const mode = MatchUtils.getMatchMode()

        try {
            if (mode === Modes.SINGLE || mode === GameModes.CUSTOM) {
                await MatchUtils.finishMatchAsync()
            }
        } catch (error) {
            console.warn('handleNextLevel', error)
        }

        const correctLevel = this.gameData.level ?? 1
        const nextLevel = correctLevel + 1

        this.setGameData({ level: nextLevel })

        if (mode === Modes.TOURNAMENT) {
            await MatchUtils.setMatchCustomDataAsync({ level: nextLevel })
        } else {
            await MatchUtils.setMatchCustomDataAsync({ level: nextLevel, score: 0 })
            await MatchUtils.startMatchAsync()
        }

        this.stateManager.initialize()

        console.log('Next level', this.gameData.level)
    }

    private readonly handleStartOver = async (level?: number): Promise<void> => {
        player.setGameData({
            level: level ?? 1,
        })

        await MatchUtils.setMatchCustomDataAsync({ level, score: 0 })

        this.gameData = player.getGameData()

        this.stateManager.initialize()

        console.info('Start over', this.gameData?.level)
    }

    private readonly handleMaxLevel = (): void => {
        if (!this.gameData) return

        this.initData()

        ScreenUtils.openScreen(ScreenKeysEnum.CONGRATULATION_SCREEN, {
            level: this.gameData.level,
        })

        console.info('Max level', this.gameData.level)
    }

    private readonly handleExitGame = async () => {
        try {
            storage.setStorageData('dashboard', 'animateToCurrentLevel', true)
            storage.setStorageData('collection', 'animateMapPackUnlock', true)
        } catch (error) {
            console.warn('handleExitGame', error)
        }
    }

    private readonly handleTileClick = (tile: Tile) => {
        this.stateManager.playing()
        this.board.handleTileClick(tile)
        this.handleHideHint()
    }

    private readonly handleGridMatchSuccess = (path: Path, match: [Tile, Tile]) => {
        this.pathDraw.drawMatchPath(path, ({ x, y }) => this.board.getAnyTileWorldPosition(x, y))

        this.comboManager.addCombo()

        this.handleAddScore()

        this.effectManager.playClearEffectAt([match[0].node.worldPosition, match[1].node.worldPosition])
    }

    private readonly handleGridMatchFail = (path: Path) => {
        if (!this.guidelinesManager?.isShowErrorPathLevel()) return

        this.pathDraw.drawErrorPath(path, ({ x, y }) => this.board.getAnyTileWorldPosition(x, y))
    }

    private readonly handleGridClear = async () => {
        const uncleared = this.board.getUncleared()

        //? Preload interstitial ads when 10 tiles remain
        if (AdTypes && uncleared.length === 10 && !this.isAdPreloadedForCurrentLevel) {
            this.isAdPreloadedForCurrentLevel = true
            AdsUtils.preloadAdsByType(AdTypes.INTERSTITIAL)
        }

        if (uncleared.length === 0) {
            this.node.scene.emit(GameplayEvents.GAME_FINISH)
        }

        //? Request tournament leaderboard data when 8 tiles remain
        if (uncleared.length === 8) {
            await this.tournamentManager?.requestLeaderData()
            this.uiManager?.currentLayout.tournamentRail.handleScoreUpdated({
                score: this.scoreManager.getCurrentScore(),
                leaderData: this.tournamentManager?.leaderData,
            })
        }
    }

    private handlePauseTime = () => {
        this.clockManager.pause()
        this.uiManager.pauseTime()
    }

    private handleResumeTime = () => {
        this.clockManager.resume()
        this.uiManager.resumeTime()
    }

    private readonly handleClockTimeout = () => {
        this.stateManager.fail(FailKeys.TIME)
    }

    private readonly handleHalfTime = () => {
        if (this.guidelinesManager === undefined) return

        if (this.board.getUncleared().length >= this.board.getCleared().length) {
            if (this.guidelinesManager.startThunderGuide()) return
        }

        if (this.board.getBombTiles().length > 0) {
            if (this.guidelinesManager.startPliersGuide()) return
        }
    }

    private readonly handleStartRescue = () => {
        AudioUtils.turnDownMusic()
        GeneralUtils.setBlockInput(true)
        this.board.setIgnoreInput(true)

        if (this.rescueTiles.length > 0) {
            for (const tile of this.rescueTiles) {
                tile.restore()
            }
            this.rescueTiles = []
        }

        const mode = MatchUtils.getMatchMode() ?? undefined

        analytics?.levelRescue(this.gameData?.level ?? 1, undefined, mode)
    }

    private readonly handleFinishRescue = () => {
        GeneralUtils.setBlockInput(false)
        this.board.setIgnoreInput(false)
    }

    private readonly handleBombExplode = async (tile: Tile) => {
        this.rescueTiles.push(tile)
        this.board.setIgnoreInput(true)

        if (!GeneralUtils.getBlockInput()) {
            AudioUtils.playSound(CodeSoundKeys.BOMB_EXPLODE_1)
            GeneralUtils.setBlockInput(true)
        }

        const position = tile.node.worldPosition
        await this.effectManager.createBombExplosionAt(new Vec3(position.x, position.y, 0))

        GeneralUtils.setBlockInput(false)

        if (this.stateManager.isFail()) return
        this.stateManager.fail(FailKeys.BOMB)
    }

    private readonly handleShowHint = () => {
        if (this.pathDraw.isDrawingHint) return

        const path = this.board.getHintPath()
        if (!path) return

        AudioUtils.playSound(CodeSoundKeys.HINT_ITEM)

        this.pathDraw.drawHintPath(path, (coords) => this.board.getAnyTileWorldPosition(coords.x, coords.y))

        this.board.showHint()

        this.node.scene.emit(GameplayEvents.REFRESH_ITEMS)
    }

    private readonly handleGridOrientationChange = () => {
        this.handleHideHint()
    }

    private readonly handleHideHint = () => {
        if (!this.pathDraw.isDrawingHint) return

        this.pathDraw.clearHintPath()
        this.board.hideHint()

        this.node.scene.emit(GameplayEvents.REFRESH_ITEMS)
    }

    private readonly handleAddScore = () => {
        const currentLevel = this.levelManager.getLevelData().level
        const currentCombo = this.comboManager.getCombo()
        const remainingTime = this.clockManager.getRemainingTime()

        this.scoreManager.addScore(currentLevel, currentCombo, remainingTime)
    }

    private readonly handleUseItem = async (item: ItemKey) => {
        this.stateManager.playing()

        if (item !== ItemKeys.HINT) {
            this.handleHideHint()
        }

        this.itemManager.setQuantity(item, this.itemManager.getQuantity(item) - 1)

        switch (item) {
            case ItemKeys.HINT:
                useHintItem(this.board, this.pathDraw)
                this.node.scene.emit(GameplayEvents.REFRESH_ITEMS)
                break
            case ItemKeys.PLIERS:
                usePliersItem(this.board, this.effectManager)
                this.node.scene.emit(GameplayEvents.REFRESH_ITEMS)
                break
            case ItemKeys.STORM:
                this.node.scene.emit(GameplayEvents.HIDE_UI)
                this.node.scene.emit(GameplayEvents.PAUSE_TIME)
                await useTornadoItem(this.board, this.effectManager)
                this.node.scene.emit(GameplayEvents.RESUME_TIME)
                this.node.scene.emit(GameplayEvents.SHOW_UI)
                break
            case ItemKeys.THUNDER: {
                this.node.scene.emit(GameplayEvents.HIDE_UI)
                this.node.scene.emit(GameplayEvents.PAUSE_TIME)
                await useThunderItem(this.board, this.effectManager)
                const uncleared = this.board.getUncleared()
                if (uncleared.length > 0) {
                    this.node.scene.emit(GameplayEvents.RESUME_TIME)
                    this.node.scene.emit(GameplayEvents.SHOW_UI)
                }
            }
        }
    }

    private readonly handleAutoPlay = () => {
        if (this.isAutoPlayEnabled) {
            this.disableAutoPlay()
        } else {
            this.enableAutoPlay()
        }
    }

    private readonly enableAutoPlay = () => {
        this.isAutoPlayEnabled = true
        this.schedule(this.autoPlay, 0.5)
    }

    private readonly disableAutoPlay = () => {
        this.isAutoPlayEnabled = false
        this.unschedule(this.autoPlay)
    }

    private readonly autoPlay = () => {
        if (!this.stateManager.isReady() && !this.stateManager.isPlaying()) return

        const path = this.board.getHintPath()

        if (path) {
            const startLine = path[0]
            const endLine = path.slice(-1)[0]

            if (startLine === undefined || endLine === undefined) {
                const error = new Error('No path found')
                if (V.isDebugger()) throw error
                monitorError?.sendException(error)
                return
            }

            const start = startLine.a
            const end = endLine.b

            const startTile = this.board.getTileAt(start.x, start.y)
            const endTile = this.board.getTileAt(end.x, end.y)

            if (!startTile || !endTile) throw new Error('Tile is null')

            startTile.onClick()
            endTile.onClick()
        } else {
            throw new Error('No path found')
        }
    }

    private readonly handleAddStars = (payload: AddStarsPayload) => {
        const gameData = player.getGameData()
        if (!gameData) return Promise.resolve([])

        player.setGameData({
            stars: gameData.stars + 1,
        })

        const { startAnimationAt } = payload
        if (!startAnimationAt) return

        this.effectManager.playStarEffectAt(startAnimationAt)
    }

    private readonly handleChallengeFriends = async () => {
        try {
            ScreenUtils.showLoadingScreen()

            const fen = this.board.generateFenFromCurrentLevel()
            let { level = 1 } = player.getGameData() ?? {}
            //? Avoid playing level 0 (Newbie Guide)
            level = Math.max(1, level)

            const {
                mode: gravity = '',
                rules = { addRandomTilesPerMove: 0, maxSpaceOfPair: 0 },
                time = 0,
            } = this.gameData?.levelData ?? {}

            await match?.context.choose.processAsync({
                playerId: player.getPlayerId(),
                fallbackSingleMode: false,
                extraData: {
                    challengeData: {
                        level,
                        fen,
                        gravity,
                        rules,
                        time,
                    },
                },
            })

            if (MatchUtils.getMatchMode() === Modes.TOURNAMENT) {
                //? If player choose tournament context
                await this.tryInitTournamentMode()
            }

            ScreenUtils.closeLoadingScreen()

            this.node.scene.emit(GameplayEvents.UPDATE_MUSIC)
            this.node.scene.emit(GameplayEvents.GAME_RESUME)
            this.node.scene.emit(GameplayEvents.GAME_REPLAY, {
                level,
            })
        } catch (e) {
            const error = e instanceof Error ? e : new Error('Unable to invite friends')
            monitorError?.sendException(error)

            ScreenUtils.closeLoadingScreen()
            ScreenUtils.showNotificationScreenWithLocalizedKey('error.tryLater')

            this.node.scene.emit(GameplayEvents.GAME_RESUME)
            this.node.scene.emit(GameplayEvents.SHOW_UI)
        }
    }

    private attemptCallCreateShortcut(): void {
        const { level = 0, nextLevelCount = 0 } = this.getGameData() ?? {}

        // ? Don't create shortcut for first level
        if (level < 2) return

        // ? Don't create shortcut for first 2 next levels
        if (nextLevelCount < 2) return

        GameSDK.extra.checkAndCreateShortcutAsync().catch((error: Error) => {
            console.warn('checkAndCreateShortcutAsync', error)
        })
    }

    private processCrazyGamesGameplayStart(): void {
        if ('gameplayStart' in GameSDK.extra) {
            GameSDK.extra.gameplayStart()
        }
    }

    private processCrazyGamesGameplayStop(): void {
        if ('gameplayStop' in GameSDK.extra) {
            GameSDK.extra.gameplayStop()
        }
    }

    public disableRewardAdsItemForCrazyGames(): void {
        if (!GameSDK.isGameSDK('CrazyGames')) return

        //TODO: implement for CG
    }

    private readonly handleAddItem = async (payload: AddItemPayload): Promise<void> => {
        const { animate, item: itemType, quantity: addedQuantity } = payload
        const item = A.search(this.uiManager.items, (item) => item.itemType === itemType)

        if (item === undefined) {
            console.warn(`Item ${itemType} not found`)
            return
        }

        const quantity = this.itemManager.getQuantity(itemType)
        const newQuantity = quantity + addedQuantity

        this.itemManager.saveQuantity(itemType, newQuantity)
        this.uiManager.setInteractableItems(false)

        if (animate) {
            const { startWorldPosition } = animate
            await this.effectManager.playItemAddAnimation(startWorldPosition.x, startWorldPosition.y, item)
        }

        this.itemManager.setQuantity(itemType, newQuantity, true)
        this.uiManager.setInteractableItems(true)
    }
}
