import { Component, _decorator, find } from 'cc'
import { Vibrate } from '../../components/vibrate/Vibrate'
import { CodeSoundKeys } from '../../constants/SoundKeys'
import AudioUtils from '../../utils/AudioUtils'
import GameplayEvents from '../constants/GameEvents'
const { ccclass } = _decorator

const COMBO_SOUNDS = [
    CodeSoundKeys.COMBO_1,
    CodeSoundKeys.COMBO_2,
    CodeSoundKeys.COMBO_3,
    CodeSoundKeys.COMBO_4,
    CodeSoundKeys.COMBO_5,
    CodeSoundKeys.COMBO_6,
    CodeSoundKeys.COMBO_7,
    CodeSoundKeys.COMBO_8,
    CodeSoundKeys.COMBO_9,
    CodeSoundKeys.COMBO_10,
    CodeSoundKeys.COMBO_11,
    CodeSoundKeys.COMBO_12,
    CodeSoundKeys.COMBO_13,
]

@ccclass('ComboManager')
export class ComboManager extends Component {
    private currentCombo!: number

    private emitEvent!: boolean

    public init(): void {
        this.currentCombo = 0
        this.setEmitEvent(true)

        this.node.scene.on(GameplayEvents.COMBO_ENDED, this.resetCombo, this)
        this.node.scene.on(GameplayEvents.GAME_FINISH, this.resetCombo, this)
        this.node.scene.on(GameplayEvents.GAME_REPLAY, this.resetCombo, this)
        this.node.scene.on(GameplayEvents.NEXT_LEVEL, this.resetCombo, this)

        this.node.scene.on(GameplayEvents.ADD_COMBO, this.addCombo, this)
    }

    public addCombo(): void {
        this.setCombo(this.currentCombo + 1)
    }

    public resetCombo(): void {
        this.setCombo(0)
    }

    public getCombo(): number {
        return this.currentCombo
    }

    private setCombo(combo: number): void {
        this.currentCombo = combo

        if (combo > 0) {
            const soundIndex = Math.min(combo - 1, COMBO_SOUNDS.length - 1)
            AudioUtils.playSound(COMBO_SOUNDS[soundIndex])
            this.vibrate(combo)
        }

        if (!this.emitEvent) return

        this.node.scene.emit(GameplayEvents.COMBO_UPDATED, combo)
    }

    private vibrate(combo: number): void {
        find('Vibrate')
            ?.getComponent(Vibrate)
            ?.vibrate(Math.min(20 + combo * 20, 11 * 20))
    }

    public setEmitEvent(enabled: boolean): void {
        this.emitEvent = enabled
    }
}
