// biome-ignore lint/suspicious/noExplicitAny: needed for generic functions
export type FnThatReturnVoidPromise<T extends (...args: any[]) => any> = ReturnType<T> extends Promise<void> ? T : never

// biome-ignore lint/suspicious/noExplicitAny: needed for generic functions
function decorateComponent<T extends (...args: any[]) => any>(
    _target: Component,
    propertyKey: string,
    descriptor: TypedPropertyDescriptor<FnThatReturnVoidPromise<T>>,
) {
    const original = descriptor.value

    const wrapped = async function (this: Component, ...args: Parameters<FnThatReturnVoidPromise<T>>) {
        if (this.isValid) return await original?.apply(this, args)
        console.debug(
            `Skipping method call ${String(propertyKey)} because component ${
                this.constructor.name
            } is not enabled in hierarchy`,
        )
    }

    descriptor.value = wrapped as FnThatReturnVoidPromise<T>
}

// biome-ignore lint/suspicious/noExplicitAny: needed for generic functions
function createNonComponentDecorator<T extends (...args: any[]) => any>(
    isValid: () => boolean,
): (_target: object, propertyKey: string, descriptor: TypedPropertyDescriptor<FnThatReturnVoidPromise<T>>) => void {
    return (_target: object, propertyKey: string, descriptor: TypedPropertyDescriptor<FnThatReturnVoidPromise<T>>) => {
        const original = descriptor.value

        const wrapped = async function (this: object, ...args: Parameters<FnThatReturnVoidPromise<T>>) {
            if (isValid.call(this)) return await original?.apply(this, args)
            console.debug(
                `Skipping method call ${String(propertyKey)} because component ${
                    this.constructor.name
                } is not enabled in hierarchy`,
            )
        }

        descriptor.value = wrapped as FnThatReturnVoidPromise<T>
    }
}

// biome-ignore lint/suspicious/noExplicitAny: needed for generic functions
export default function onlyRunInLifeCycleAsync<T extends (...args: any[]) => any>(
    customIsValid: () => boolean,
): (_target: object, propertyKey: string, descriptor: TypedPropertyDescriptor<FnThatReturnVoidPromise<T>>) => void

// biome-ignore lint/suspicious/noExplicitAny: needed for generic functions
export default function onlyRunInLifeCycleAsync<T extends (...args: any[]) => any>(
    _target: Component,
    propertyKey: string,
    descriptor: TypedPropertyDescriptor<FnThatReturnVoidPromise<T>>,
): void

// biome-ignore lint/suspicious/noExplicitAny: needed for generic functions
export default function onlyRunInLifeCycleAsync<T extends (...args: any[]) => any>(
    compOrCustomValidator: Component | (() => boolean),
    propertyKey?: string,
    descriptor?: TypedPropertyDescriptor<FnThatReturnVoidPromise<T>>,
) {
    if (typeof compOrCustomValidator === 'object' && propertyKey !== undefined && descriptor !== undefined) {
        decorateComponent(compOrCustomValidator, propertyKey, descriptor)
        return
    }

    const customIsValid = compOrCustomValidator as () => boolean
    const decorator = createNonComponentDecorator(customIsValid)
    return decorator
}
