import { easing, lerp, tween, Vec3 } from 'cc'
import { CodeSoundKeys } from '../../../constants/SoundKeys'
import type { ItemCollectionFX } from '../../../screens/get-item-screen/ItemCollectionFX'
import AnimUtils from '../../../utils/AnimUtils'
import AudioUtils from '../../../utils/AudioUtils'
import type { SpecialItem } from './SpecialItem'

const VEC3_SCALE_UP_OVERSHOOT_Y = new Vec3(0.8, 1.2, 1)
const VEC3_SCALE_UP_OVERSHOOT_X = new Vec3(1.2, 0.8, 1)
const VEC3_HALF = new Vec3(0.5, 0.5, 0.5)
const VEC3_6_5 = new Vec3(0.65, 0.65, 0.65)

const addItemAnimation = async (itemEffect: ItemCollectionFX, item: SpecialItem, useQuickVersion = false) => {
    itemEffect.playExplosionParticles(useQuickVersion)
    AudioUtils.playSound(CodeSoundKeys.ITEM_REWARD)

    if (useQuickVersion) {
        await playQuickAnimation(itemEffect, item)
    } else {
        await playStandardAnimation(itemEffect, item)
    }

    AudioUtils.playSound(CodeSoundKeys.BUTTON_CLICK)
}

async function playQuickAnimation(itemEffect: ItemCollectionFX, item: SpecialItem) {
    const scaleUp = AnimUtils.runTweenAsync(
        tween(itemEffect.node)
            .to(0.1, { scale: VEC3_SCALE_UP_OVERSHOOT_Y })
            .to(0.1, { scale: VEC3_SCALE_UP_OVERSHOOT_X })
            .to(0.1, { scale: Vec3.ONE })
            .to(0.3, { scale: VEC3_6_5 }),
    )

    const startX = itemEffect.node.worldPosition.x
    const startY = itemEffect.node.worldPosition.y
    const tempTargetPosition = new Vec3()

    const move = AnimUtils.runTweenAsync(
        tween(itemEffect.node).update(0.8, (_, ratio) => {
            item.node.getWorldPosition(tempTargetPosition)

            const progressX = easing.quadOut(ratio)
            const x = lerp(startX, tempTargetPosition.x, progressX)
            const midY = Math.max(startY, tempTargetPosition.y)
            const interpolatedMidY = lerp(startY, midY + 300, 0.5)

            if (ratio < 0.5) {
                const progressY = easing.cubicOut(ratio * 2)
                const y = lerp(startY, interpolatedMidY, progressY)
                itemEffect.node.setWorldPosition(x, y, 0)
            } else {
                const progressY = easing.cubicIn((ratio - 0.5) * 2)
                const y = lerp(interpolatedMidY, tempTargetPosition.y, progressY)
                itemEffect.node.setWorldPosition(x, y, 0)
            }
        }),
    )

    await Promise.all([scaleUp, move])
}

async function playStandardAnimation(itemEffect: ItemCollectionFX, item: SpecialItem) {
    await scaleUpAnimation(itemEffect)
    await AnimUtils.waitTweenAsync(0.8)
    await moveCloseAnimation(itemEffect, item)
    await jumpInAnimation(itemEffect, item)
}

const scaleUpAnimation = (itemEffect: ItemCollectionFX) => {
    return AnimUtils.runTweenAsync(
        tween(itemEffect.node)
            .to(0.1, { scale: VEC3_SCALE_UP_OVERSHOOT_Y })
            .to(0.1, { scale: VEC3_SCALE_UP_OVERSHOOT_X })
            .to(0.1, { scale: Vec3.ONE }),
    )
}

const moveCloseAnimation = (itemEffect: ItemCollectionFX, item: SpecialItem) => {
    return AnimUtils.runTweenAsync(
        tween(itemEffect.node).to(
            1,
            {
                worldPosition: new Vec3(
                    item.node.worldPosition.x,
                    item.node.worldPosition.y + 30,
                    item.node.worldPosition.z,
                ),
                scale: VEC3_HALF,
            },
            { easing: 'cubicInOut' },
        ),
    )
}

const jumpInAnimation = (itemEffect: ItemCollectionFX, item: SpecialItem) => {
    const targetPosition = item.node.worldPosition.clone()
    const jumpPosition = new Vec3(targetPosition.x, targetPosition.y + 70, targetPosition.z)

    return AnimUtils.runTweenAsync(
        tween(itemEffect.node)
            .to(0.3, { worldPosition: jumpPosition, scale: VEC3_6_5 }, { easing: 'cubicOut' })
            .to(0.5, { worldPosition: targetPosition }, { easing: 'cubicIn' }),
    )
}

export default addItemAnimation
