import { Component, _decorator } from 'cc'
import { ScreenKeysEnum } from '../../constants/ScreenKeys'
import ComponentUtils from '../../utils/ComponentUtils'
import ScreenUtils from '../../utils/ScreenUtils'
import { BaseScreen } from '../BaseScreen'
const { ccclass, disallowMultiple, requireComponent } = _decorator

const { Events } = Ludex
const { event } = codex

@ccclass('LoadingScreen')
@disallowMultiple
@requireComponent(BaseScreen)
export class LoadingScreen extends Component {
    private screen!: BaseScreen
    private isClosing: boolean = false

    public onLoad() {
        this.enabled = false
        this.screen = this.getComponent(BaseScreen) as BaseScreen
        this.validateRequiredComponents()
        this.listenScreenEvents()
    }

    public onDestroy(): void {
        this.unListenScreenEvents()
    }

    private validateRequiredComponents() {
        ComponentUtils.validateRequiredProperties(this, LoadingScreen)
    }

    private listenScreenEvents() {
        // ? Here's a demo of all the events a screen has
        event.on(Events.OPENING_SCREEN, this.handleOpeningScreen)
        event.on(Events.OPENED_SCREEN, this.handleOpenedScreen)
        event.on(Events.CLOSING_SCREEN, this.handleClosingScreen)
        event.on(Events.CLOSED_SCREEN, this.handleClosedScreen)
    }

    private unListenScreenEvents() {
        event.off(Events.OPENING_SCREEN, this.handleOpeningScreen)
        event.off(Events.OPENED_SCREEN, this.handleOpenedScreen)
        event.off(Events.CLOSING_SCREEN, this.handleClosingScreen)
        event.off(Events.CLOSED_SCREEN, this.handleClosedScreen)
    }

    private readonly handleOpeningScreen = (payload: SwitchScreenPayload) => {
        if (!this.validateEventPayload(payload)) return
        this.enabled = true
        this.isClosing = false
    }

    private readonly handleOpenedScreen = (payload: SwitchScreenPayload) => {
        if (!this.validateEventPayload(payload)) return
        this.open()
    }

    private readonly handleClosingScreen = (payload: SwitchScreenPayload) => {
        if (!this.validateEventPayload(payload)) return
        this.isClosing = true
        this.unscheduleAllCallbacks()
    }

    private readonly handleClosedScreen = (payload: SwitchScreenPayload) => {
        if (!this.validateEventPayload(payload)) return
        this.enabled = false
        this.isClosing = false
    }

    private validateEventPayload(payload: SwitchScreenPayload) {
        const { screenName } = payload
        if (screenName !== this.node.name) return false
        return true
    }

    public open() {
        if (!this.enabled || this.isClosing) return

        const duration = this.screen.data.duration
        if (!duration) return

        if (duration > 0) {
            this.scheduleOnce(this.handleOnCloseScreen, duration)
        }
    }

    private readonly handleOnCloseScreen = () => {
        ScreenUtils.closeScreen(ScreenKeysEnum.LOADING_SCREEN)
    }
}
