import { Button, Component, Label, _decorator } from 'cc'
import { Localize } from '../../components/text/Localize'
import { ScreenKeysEnum } from '../../constants/ScreenKeys'
import ComponentUtils from '../../utils/ComponentUtils'
import ScreenUtils from '../../utils/ScreenUtils'
import { BaseScreen } from '../BaseScreen'
const { ccclass, disallowMultiple, requireComponent, property } = _decorator

const {
    Events,
    Utils: { Valid: V },
} = Ludex
const { event } = codex

@ccclass('NotificationScreen')
@disallowMultiple
@requireComponent(BaseScreen)
export class NotificationScreen extends Component {
    private screen!: BaseScreen

    @property(Label)
    public message!: Label

    @property(Localize)
    public messageLocalizer!: Localize

    @property(Button)
    public touchZone!: Button

    public enableCloseOnTouch = true

    public __preload() {
        this.listenScreenEvents()
    }

    public onDestroy(): void {
        this.unListenScreenEvents()
    }

    public onLoad() {
        this.screen = this.getComponent(BaseScreen)!
        this.validateRequiredComponents()
    }

    private validateRequiredComponents() {
        ComponentUtils.lazyValidateProperties(this)
    }

    private listenScreenEvents() {
        event.on(Events.OPENING_SCREEN, this.handleOpeningScreen)
        event.on(Events.CLOSING_SCREEN, this.handleClosingScreen)
    }

    private unListenScreenEvents() {
        event.off(Events.OPENING_SCREEN, this.handleOpeningScreen)
        event.off(Events.CLOSING_SCREEN, this.handleClosingScreen)
    }

    private handleOpeningScreen = (payload: SwitchScreenPayload) => {
        if (!this.validateEventPayload(payload)) return

        // ? this.screen.data only available after OPENING_SCREEN event
        // ? Or use this.screen.data as payload.screenData
        this.updateScreenData(payload)
        this.screen.backgroundMaskButton.interactable = this.enableCloseOnTouch
    }

    private handleClosingScreen = (payload: SwitchScreenPayload) => {
        if (!this.validateEventPayload(payload)) return
        this.unscheduleAllCallbacks()
    }

    private validateEventPayload(payload: SwitchScreenPayload) {
        const { screenName } = payload
        if (screenName !== this.node.name) return false
        return true
    }

    private updateScreenData(payload: SwitchScreenPayload) {
        const { screenData } = payload

        if (!V.isObject(screenData)) {
            console.warn('NotificationScreen: screenData not found')
            return
        }

        if (screenData.message !== undefined) {
            this.setMessageManually(screenData.message)
        } else if (screenData.localizedMessageKey !== undefined) {
            this.setMessageUsingLocalizeKey(screenData.localizedMessageKey)
        }

        if (screenData.duration && screenData.duration > 0) {
            this.scheduleOnce(() => {
                ScreenUtils.closeScreen(ScreenKeysEnum.NOTIFICATION_SCREEN)
            }, screenData.duration)
        }

        if (screenData.enableCloseOnTouch === false) {
            this.enableCloseOnTouch = false
        } else {
            this.enableCloseOnTouch = true
        }
    }

    private setMessageManually(message: string) {
        this.messageLocalizer.enabled = false
        this.message.string = message
    }

    private setMessageUsingLocalizeKey(key: Ludex.Plugins.Language.TextKey) {
        this.messageLocalizer.enabled = true
        this.messageLocalizer.key = key
    }

    // ? Callback for click event of CloseButton
    public handleClickedClose() {
        this.unscheduleAllCallbacks()
        this.screen.backgroundMaskButton.interactable = false
        ScreenUtils.closeScreen(ScreenKeysEnum.NOTIFICATION_SCREEN)
    }

    public closeOnTouch() {
        if (!this.enableCloseOnTouch) return

        this.handleClickedClose()
    }
}
