import { CodeSoundKeys } from '../../../constants/SoundKeys'
import AudioUtils from '../../../utils/AudioUtils'
import ItemKeys from '../../constants/ItemKeys'
import type Board from '../board'
import type { PathDraw } from '../path-draw/PathDraw'
import withItemAnalytics from './withItemAnalytics'

const useHintItemLogic = (board: Board, pathDraw: PathDraw) => {
    if (pathDraw.isDrawingHint) return

    const path = board.getHintPath()
    if (path === null) return

    AudioUtils.playSound(CodeSoundKeys.HINT_ITEM)
    pathDraw.drawHintPath(path, (coords) => board.getAnyTileWorldPosition(coords.x, coords.y))
    board.showHint()
}

const useHintItem = withItemAnalytics(ItemKeys.PLIERS, useHintItemLogic)

export default useHintItem
