import {
    CCBoolean,
    CCFloat,
    Node as CCNode,
    Component,
    Enum,
    type EventTouch,
    Node,
    UIOpacity,
    UITransform,
    Vec3,
    _decorator,
    easing,
    lerp,
    tween,
} from 'cc'
import { EDITOR_NOT_IN_PREVIEW } from 'cc/env'
import DashboardScreens from '../../../../../../constants/DashboardScreens'
import { CodeSoundKeys } from '../../../../../../constants/SoundKeys'
import AnalyticsUtils from '../../../../../../utils/AnalyticsUtils'
import AnimUtils from '../../../../../../utils/AnimUtils'
import AudioUtils from '../../../../../../utils/AudioUtils'
import ComponentUtils from '../../../../../../utils/ComponentUtils'
import { NavManager } from './NavManager'
const { ccclass, property, executeInEditMode } = _decorator

const { analytics } = codex
const { Plugins } = Ludex

const { Events: AnalyticsEvents } = Plugins.Analytics ?? {}

@ccclass('NavButton')
@executeInEditMode
export class NavButton extends Component {
    // ! Remove this when other screens are ready
    @property(CCBoolean)
    private wip = false

    public get isWIP(): boolean {
        return this.wip
    }

    @property({
        group: { name: 'Animation' },
        displayName: 'Is Selected',
        tooltip: 'This is for the editor only and will be ignored at runtime',
        editorOnly: true,
    })
    private isSelectedInEditor = false

    @property({ type: CCFloat, group: { name: 'Animation' } })
    private animationDuration = 0.3

    @property({ type: CCFloat, group: { name: 'Animation' } })
    private selectedSpacing = 150

    @property({ type: CCFloat, group: { name: 'Animation' } })
    private deselectedSpacing = 88

    @property({ type: CCFloat, group: { name: 'Animation' } })
    private selectedIconY = 20

    @property({ type: CCFloat, group: { name: 'Animation' } })
    private selectedIconX = 0

    @property({ group: { name: 'Animation' } })
    private selectedIconScale = new Vec3(1, 1, 1)

    @property({ type: CCFloat, group: { name: 'Animation' } })
    private deselectedIconY = 0

    @property({ type: CCFloat, group: { name: 'Animation' } })
    private deselectedIconX = 0

    @property({ group: { name: 'Animation' } })
    private deselectedIconScale = new Vec3(0.8, 0.8, 1)

    @property({ type: CCFloat, group: { name: 'Animation' } })
    private initTextY = -100

    @property({ type: CCFloat, group: { name: 'Animation' } })
    private selectedTextY = -10

    @property({ type: CCFloat, group: { name: 'Animation' } })
    private selectedTextX = 0

    @property({ type: CCFloat, group: { name: 'Animation' } })
    private deselectedTextY = 0

    @property({ type: CCFloat, group: { name: 'Animation' } })
    private deselectedTextX = 0

    @property({ type: UITransform })
    private spacer!: UITransform

    @property(CCNode)
    private icon!: CCNode

    @property(UIOpacity)
    private text!: UIOpacity

    @property({ type: UITransform })
    private uiTransform!: UITransform

    @property({ type: Enum(DashboardScreens) })
    private screenType!: DashboardScreens

    public get buttonType(): DashboardScreens {
        return this.screenType
    }

    @property(NavManager)
    public navManager!: NavManager

    private isSelected: boolean = false

    public update() {
        if (EDITOR_NOT_IN_PREVIEW) {
            this.onEditorUpdate()
        }
    }

    public onEnable() {
        //? Support for window
        this.node.on(Node.EventType.MOUSE_ENTER, this.onMouseEnter)
        this.node.on(Node.EventType.MOUSE_LEAVE, this.onMouseLeave)
    }

    public onDisable() {
        //? Support for window
        this.node.off(Node.EventType.MOUSE_ENTER, this.onMouseEnter)
        this.node.off(Node.EventType.MOUSE_LEAVE, this.onMouseLeave)
    }

    private onMouseEnter = (_event: EventTouch) => {
        if (this.isSelected) return

        this.playHoverOnAnimation()
    }

    private onMouseLeave = (_event: EventTouch) => {
        if (this.isSelected) return

        this.playHoverOffAnimation()
    }

    private onEditorUpdate() {
        if (this.icon === null || this.icon === undefined) {
            return
        }
        if (this.text === null || this.icon === undefined) {
            return
        }
        if (this.spacer === null || this.spacer === undefined) {
            return
        }

        if (this.isSelectedInEditor) {
            this.icon.setScale(this.selectedIconScale)
            this.icon.setPosition(this.selectedIconX, this.selectedIconY, 0)

            this.text.node.setPosition(this.selectedTextX, this.selectedTextY, 0)

            this.spacer.width = this.selectedSpacing

            this.text.opacity = 255
        } else {
            this.icon.setScale(this.deselectedIconScale)
            this.icon.setPosition(this.deselectedIconX, this.deselectedIconY, 0)

            this.text.node.setPosition(this.deselectedTextX, this.initTextY, 0)

            this.spacer.width = this.deselectedSpacing

            this.text.opacity = 0
        }
    }

    public setSelected() {
        if (this.isSelected) {
            return
        }

        AudioUtils.playSound(CodeSoundKeys.PATCH_SHIFT)
        this.navManager.selectUsingSceneTransition(this)

        if (AnalyticsEvents) {
            analytics.event(AnalyticsEvents.BUTTON_CLICK, {
                button_name: this.node.parent?.name ?? this.node.name,
                screen_name: AnalyticsUtils.getCurrentPage(),
            })
        }
    }

    public async select(animateSelect = true): Promise<void> {
        if (this.isSelected) {
            throw new Error('NavButton: Already selected')
        }

        this.isSelected = true

        if (animateSelect) {
            await AnimUtils.runTweenAsync(this.playSelectAnimation())
        } else {
            this.icon.setScale(this.selectedIconScale)
            this.icon.setPosition(this.selectedIconX, this.selectedIconY, 0)

            this.text.node.setPosition(this.selectedTextX, this.selectedTextY, 0)

            this.spacer.width = this.selectedSpacing

            this.text.opacity = 255
        }
    }

    public async deselect(animate = true, force = false): Promise<void> {
        if (!this.isSelected && !force) {
            throw new Error('NavButton: Already deselected')
        }

        this.isSelected = false
        if (animate) {
            await AnimUtils.runTweenAsync(this.deselectAnimation())
        } else {
            this.icon.setScale(this.deselectedIconScale)
            this.icon.setPosition(this.deselectedIconX, this.deselectedIconY, 0)

            this.text.node.setPosition(this.deselectedTextX, this.deselectedTextY, 0)

            this.spacer.width = this.deselectedSpacing

            this.text.opacity = 0
        }
    }

    public getSelectedWorldDistanceX(): number {
        const deselPos = new Vec3(this.selectedIconX, this.selectedIconY, 0)
        const selPos = new Vec3(this.deselectedIconX, this.deselectedIconY, 0)

        const ogScale = this.node.scale.clone()

        //? This is to get the world position of the icon as if it was selected
        this.node.scale = this.selectedIconScale
        const deselWorldPos = this.uiTransform.convertToWorldSpaceAR(deselPos)
        const selWorldPos = this.uiTransform.convertToWorldSpaceAR(selPos)

        //? Restore the original scale
        this.node.scale = ogScale

        return selWorldPos.x - deselWorldPos.x
    }

    private playHoverOnAnimation() {
        const t = tween(this.icon)
        t.to(
            0.1,
            {
                scale: this.deselectedIconScale.clone().add3f(0.1, 0.1, 0),
            },
            {
                easing: 'cubicIn',
            },
        )

        return AnimUtils.runTweenAsync(t)
    }

    private playHoverOffAnimation() {
        const t = tween(this.icon)
        t.to(
            0.1,
            {
                scale: this.deselectedIconScale,
            },
            {
                easing: 'cubicOut',
            },
        )

        return AnimUtils.runTweenAsync(t)
    }

    private playSelectAnimation() {
        return tween(this.spacer).to(
            this.animationDuration,
            {
                width: this.selectedSpacing,
            },
            {
                easing: 'linear',
                onUpdate: (_target, ratio) => {
                    if (ratio === undefined) return

                    const easedRatio = easing.quintInOut(ratio)
                    const backRatio = easing.backOut(ratio)
                    const linearRatio = easing.linear(ratio)
                    const halfRatio = ratio < 0.5 ? ratio * 2 : 1

                    const iconNextY = this.deselectedIconY + (this.selectedIconY - this.deselectedIconY) * backRatio

                    const iconNextX = this.deselectedIconX + (this.selectedIconX - this.deselectedIconX) * linearRatio

                    this.icon.setPosition(iconNextX, iconNextY, 0)

                    const iconNextScale = this.deselectedIconScale.clone().lerp(this.selectedIconScale, easedRatio)

                    this.icon.setScale(iconNextScale.x, iconNextScale.y, this.icon.scale.z)

                    const textNextOpacity = halfRatio * 255
                    const textNextY = this.deselectedTextY + (this.selectedTextY - this.deselectedTextY) * linearRatio

                    const textNextX = this.deselectedTextX + (this.selectedTextX - this.deselectedTextX) * linearRatio

                    this.text.opacity = textNextOpacity
                    this.text.node.setPosition(textNextX, textNextY, 0)
                },
            },
        )
    }

    private deselectAnimation() {
        return tween(this.spacer).to(
            this.animationDuration,
            {
                width: this.deselectedSpacing,
            },
            {
                easing: 'linear',
                onUpdate: (_target, ratio) => {
                    if (ratio === undefined) return

                    const iconNextY = lerp(this.selectedIconY, this.deselectedIconY, ratio)
                    const iconNextX = lerp(this.selectedIconX, this.deselectedIconX, ratio)
                    this.icon.setPosition(iconNextX, iconNextY, 0)

                    const halfRatio = ratio < 0.5 ? ratio * 2 : 1
                    const textNextOpacity = lerp(255, 0, halfRatio)
                    this.text.opacity = textNextOpacity

                    const textNextY = lerp(this.selectedTextY, this.deselectedTextY, ratio)
                    const textNextX = lerp(this.selectedTextX, this.deselectedTextX, ratio)
                    this.text.node.setPosition(textNextX, textNextY, 0)

                    const iconNextScale = this.selectedIconScale
                        .clone()
                        .lerp(this.deselectedIconScale, easing.quintInOut(ratio))
                    this.icon.setScale(iconNextScale.x, iconNextScale.y, this.icon.scale.z)
                },
            },
        )
    }

    public onLoad(): void {
        this.uiTransform = this.getComponent(UITransform)!
        this.validateRequiredComponents()
    }

    private validateRequiredComponents() {
        ComponentUtils.lazyValidateProperties(this)
    }
}
