import { _decorator, Component, Node, tween, UITransform, Vec3, Widget } from 'cc'
import AnimUtils from '../../../../../utils/AnimUtils'
import ComponentUtils from '../../../../../utils/ComponentUtils'
import type AnimAbortSignal from '../../../../../utils/common/AnimAbortSignal'

const { ccclass, property } = _decorator

@ccclass('JoinLeaderboardContainer')
export class JoinLeaderboardContainer extends Component {
    @ComponentUtils.required()
    @property(Widget)
    readonly widget!: Widget

    @ComponentUtils.required()
    @property(UITransform)
    readonly uiTransform!: UITransform

    @ComponentUtils.required()
    @property(Node)
    private readonly joinButton!: Node

    protected __preload(): void {
        ComponentUtils.validateRequiredProperties(this, JoinLeaderboardContainer)
    }

    public updatePosition(worldY?: number): void {
        if (worldY !== undefined) {
            this.widget.enabled = false
            this.node.setWorldPosition(this.node.worldPosition.x, worldY, this.node.worldPosition.z)
        } else {
            this.widget.enabled = true
            this.widget.updateAlignment()
        }
    }

    public async show(signal?: AnimAbortSignal): Promise<void> {
        if (signal?.aborted) return

        this.joinButton.active = true

        const scaleTween = tween(this.joinButton)
            .set({ scale: Vec3.ZERO })
            .to(0.5, { scale: Vec3.ONE }, { easing: 'backOut' })

        await AnimUtils.runTweenAsync(scaleTween, signal)
    }

    public hide(): void {
        this.joinButton.active = false
    }
}
