import { Component, EventTarget, _decorator, type Node } from 'cc'
import GameplayEvents from '../../gameplay/constants/GameEvents'
import type { PanelAnimator } from './PanelAnimator'
const { ccclass } = _decorator

@ccclass('PanelAnimationController')
export class PanelAnimationController extends Component {
    private animators: Set<PanelAnimator> = new Set()
    private eventTarget: EventTarget = new EventTarget()

    private handleRegistration = (animator?: PanelAnimator): void => {
        if (!animator) {
            throw new Error('Invalid event payload.')
        }

        if (this.animators.has(animator)) {
            console.warn('Animator already registered.')
        }

        this.animators.add(animator)
    }

    private handleUnregistration = (animator?: PanelAnimator): void => {
        if (!animator) {
            throw new Error('Invalid event payload.')
        }

        if (!this.animators.has(animator)) {
            console.warn('Animator not registered.')
        }

        this.animators.delete(animator)
    }

    private handleShowUIs = async (payload?: UIPanelPayload): Promise<void> => {
        let hasFellBack = false

        const animators = [] as PanelAnimator[]
        for (const animator of this.animators) {
            animators.push(animator)
        }

        const animations = [] as Promise<unknown>[]
        for (const animator of animators) {
            if (!animator.isReadyForAnimation() && !hasFellBack) {
                hasFellBack = true
                payload?.fallback?.()
            }

            animations.push(animator.handleShowUI())
        }

        await Promise.all(animations)
        payload?.onComplete?.()
        this.eventTarget?.emit(GameplayEvents.SHOW_UI)
    }

    private handleHideUIs = async (payload?: UIPanelPayload): Promise<void> => {
        let hasFellBack = false

        const animators = [] as PanelAnimator[]
        for (const animator of this.animators) {
            animators.push(animator)
        }

        const animations = [] as Promise<unknown>[]
        for (const animator of animators) {
            if (!animator.isReadyForAnimation() && !hasFellBack) {
                hasFellBack = true
                payload?.fallback?.()
            }

            animations.push(animator.handleHideUI())
        }

        await Promise.all(animations)
        payload?.onComplete?.()
        this.eventTarget?.emit(GameplayEvents.HIDE_UI)
    }

    protected onEnable(): void {
        this.node.scene.on(GameplayEvents.SHOW_UI, this.handleShowUIs, this)
        this.node.scene.on(GameplayEvents.HIDE_UI, this.handleHideUIs, this)
    }

    protected onDisable(): void {
        this.node.scene.off(GameplayEvents.SHOW_UI, this.handleShowUIs, this)
        this.node.scene.off(GameplayEvents.HIDE_UI, this.handleHideUIs, this)
    }

    public showUI() {
        return this.handleShowUIs()
    }

    public hideUI() {
        return this.handleHideUIs()
    }

    public on(
        type: GameplayEvents.SHOW_UI | GameplayEvents.HIDE_UI,
        callback: (...args: unknown[]) => void,
        thisArg?: unknown,
        once?: boolean,
    ) {
        this.eventTarget.on(type, callback, thisArg, once)
    }

    public off(
        type: GameplayEvents.SHOW_UI | GameplayEvents.HIDE_UI,
        callback?: (...args: unknown[]) => void,
        thisArg?: unknown,
    ) {
        this.eventTarget.off(type, callback, thisArg)
    }

    public static getController(node: Node): PanelAnimationController | null {
        let comp: PanelAnimationController | null = null
        let parent: Node | null = node

        while (parent !== null) {
            comp = parent.getComponent(PanelAnimationController)

            if (comp) {
                return comp
            }

            parent = parent.parent
        }

        return comp
    }

    public static registerAnimator(animator: PanelAnimator): void {
        const controller = this.getController(animator.node)
        if (controller) {
            controller.handleRegistration(animator)
        } else {
            console.warn(`No PanelAnimationController found for animator ${animator.node.name}`)
        }
    }

    public static unregisterAnimator(animator: PanelAnimator): void {
        const controller = this.getController(animator.node)
        if (controller) {
            controller.handleUnregistration(animator)
        } else {
            console.warn(`No PanelAnimationController found for animator ${animator.node.name}`)
        }
    }
}
