import { Animation, Component, Label, Vec3, _decorator, game, tween } from 'cc'
import SceneKeys from '../../constants/SceneKeys'
import { ParticleSystem } from '../../particles/ParticleSystem'
import AnimUtils from '../../utils/AnimUtils'
import ComponentUtils from '../../utils/ComponentUtils'
import SceneUtils from '../../utils/SceneUtils'
const { ccclass, property, disallowMultiple } = _decorator

const { analytics } = codex

@ccclass('PlayableScene')
@disallowMultiple
export class PlayableScene extends Component {
    @ComponentUtils.required()
    @property({ type: Label })
    private label!: Label

    @ComponentUtils.required()
    @property({ type: ParticleSystem })
    private particleLeft!: ParticleSystem

    @ComponentUtils.required()
    @property({ type: ParticleSystem })
    private particleRight!: ParticleSystem

    @ComponentUtils.required()
    @property({ type: Animation })
    private backgroundAnimation!: Animation

    constructor() {
        super()

        // ? Make scene flow correct for test scene
        // * Need call this in constructor to make sure SceneManager can get correct scene
        SceneUtils.markIsTestScene(SceneKeys.PLAYABLE_SCENE)
    }

    public __preload() {
        this.listenEvents()
    }

    public onLoad() {
        ComponentUtils.validateRequiredProperties(this)
    }

    private listenEvents() {
        //
    }

    public async start() {
        analytics.event('ready_playable')

        await this.waitViewable()

        this.playTweens()
        this.playParticles()
        this.playAnimations()

        analytics.event('showing_playable')

        setTimeout(() => {
            if (!('openAppStore' in GameSDK.extra)) return
            GameSDK.extra.openAppStore()
        }, 10000)
    }

    private async waitViewable() {

        if (!('isViewable' in GameSDK.extra)) return
        if (GameSDK.extra.isViewable()) return

        if (!('waitViewableChange' in GameSDK.extra)) return

        game.pause()

        const success = await GameSDK.extra.waitViewableChange()

        if (!success) {
            await this.waitViewable()
            return
        }

        game.resume()
    }

    @ComponentUtils.onlyRunInLifeCycleAsync
    private async playTweens() {
        const labelTween = tween(this.label.node)
            .to(10, { position: new Vec3(0, 200, 0) }, { easing: 'cubicInOut' })
            .to(6, { position: new Vec3(0, -100, 0) }, { easing: 'cubicInOut' })

        AnimUtils.runTweenAsync(tween(this.label.node).repeat(999, labelTween))
    }

    @ComponentUtils.onlyRunInLifeCycle
    private playParticles() {
        console.log('playParticles', this.particleLeft, this.particleRight)
        this.particleLeft.play()
        this.particleRight.play()
    }

    @ComponentUtils.onlyRunInLifeCycle
    private playAnimations() {
        this.backgroundAnimation.play()
    }
}
