/**
 * Staggering values based a 2D array that allow "ripple" effects.
 * @param from The coordinate to start from
 * @param grid The grid size
 */
export default function stagger(from: IVec2Like | 'center', grid: IVec2Like): number[][] {
    const { x: width, y: height } = grid
    const { x: fromX, y: fromY } = from === 'center' ? { x: (width - 1) / 2, y: (height - 1) / 2 } : from

    const stagger = []
    for (let toX = 0; toX < width; toX++) {
        const row = []
        for (let toY = 0; toY < height; toY++) {
            const distanceX = fromX - toX
            const distanceY = fromY - toY
            const value = Math.sqrt(distanceX * distanceX + distanceY * distanceY)
            row.push(value)
        }
        stagger.push(row)
    }

    return stagger
}
