import type { Gameplay } from '../Gameplay'
import type { StateManager } from '../manager/StateManager'

abstract class BaseState {
    protected game: Gameplay | null = null
    protected data: GameData = {}
    protected manager: StateManager

    constructor(manager: StateManager) {
        this.game = manager.getGameplay()
        this.manager = manager

        this.updateStateData()

        this.logDebug()
    }

    private logDebug(): void {
        const stateName = this.constructor.name
        console.debug(`[STATE] ${stateName}`, this.data)
    }

    protected updateStateData(): void {
        if (!this.game) return

        const gameData = this.game.getGameData()
        if (!gameData) return

        this.data = gameData
        // console.info('[STATE] updateStateData', this.data)
    }

    protected updateGameData(data: GameData): void {
        this.game?.setGameData(data)
    }

    public abstract initialize(): void
    public abstract ready(): void
    public abstract playing(): void
    public abstract fail(failKey?: FailKey): void
    public abstract finish(): void
    public abstract update(deltaTime: number): void
}

export default BaseState
