import OverlayUtils from 'db://assets/utils/OverlayUtils'
import { _decorator, Node, Sprite, SpriteFrame, UITransform } from 'cc'
import CacheUtils from '../../../../../../utils/CacheUtils'
import PlayerAvatarOverlay from '../../../../../common/PlayerAvatarOverlay'
import { ScoreBlock } from './ScoreBlock'

const { ccclass, property } = _decorator

@ccclass('ChallengeScoreBlock')
export class ChallengeScoreBlock extends ScoreBlock {
    @property(Sprite)
    private avatar!: Sprite

    @property(SpriteFrame)
    private defaultAvatar!: SpriteFrame

    @property(Node)
    private defaultAvatarNode!: Node

    private playerAvatarOverlay: PlayerAvatarOverlay | null = null

    private isOverlayMode: boolean = false

    public onLoad(): void {
        super.onLoad()
        this.createAvatarOverlay$()
    }

    public onDisable(): void {
        this.cleanupOverlay()
    }

    public async updateInfo(photo: string, playerId: string, score?: number): Promise<void> {
        this.setScore(score)
        // ? Wait one frame for score update UITransform
        this.scheduleOnce(() => this.resizeWidth(), 0)

        try {
            if (this.isOverlayMode) {
                const result = await this.showAvatarOverlay(playerId)
                if (result) return

                this.isOverlayMode = false
            }

            const spriteFrame = await CacheUtils.getPlayerAvatar(photo, playerId, 50)
            if (!spriteFrame) return

            spriteFrame.packable = true
            this.setAvatarSpriteFrame$(spriteFrame)
        } catch {
            this.setAvatarSpriteFrame$(this.defaultAvatar)
        }
    }

    private async showAvatarOverlay(playerId: string): Promise<boolean> {
        const { worldX, worldY } = this.getDefaultAvatarPosition$()
        const result = await this.playerAvatarOverlay?.updateOverlay({
            playerId,
            worldX,
            worldY,
            avatarWidth: this.getDefaultAvatarWidth$(),
        })

        return result ?? false
    }

    private setAvatarSpriteFrame$ = (spriteFrame: SpriteFrame): void => {
        if (!this.isValid) return
        this.avatar.spriteFrame = spriteFrame
    }

    private createAvatarOverlay$ = (): void => {
        try {
            OverlayUtils.isOverlayViewAPIAvailable(GameSDK)

            this.isOverlayMode = true
            this.playerAvatarOverlay = new PlayerAvatarOverlay(this.node.scene)
        } catch {
            return
        }
    }

    public cleanupOverlay(): void {
        this.playerAvatarOverlay?.hideOverlay()
    }

    public update(_dt: number): void {
        this.handleScoreBlockPositionChange$()
    }

    private handleScoreBlockPositionChange$ = (): void => {
        if (!this.isOverlayMode) return

        const { worldX, worldY } = this.getDefaultAvatarPosition$()
        this.playerAvatarOverlay?.moveOverlayTo(worldX, worldY)
    }

    private getDefaultAvatarPosition$ = (): { worldX: number; worldY: number } => {
        const { worldPositionX, worldPositionY } = this.defaultAvatarNode
        return {
            worldX: worldPositionX,
            worldY: worldPositionY,
        }
    }

    private getDefaultAvatarWidth$ = (): number => {
        const uiTransform = this.defaultAvatarNode.getComponent(UITransform)
        if (!uiTransform) return 0
        const { width } = uiTransform
        return width
    }
}
