const { LeaderboardList } = Ludex.Configs.Leaderboards

const LeaderboardIds = {
    DAILY: LeaderboardList[0]?.Id ?? 'daily-leaderboard-id',
    WEEKLY: LeaderboardList[1]?.Id ?? 'weekly-leaderboard-id',
}

const LeaderboardTypes = {
    DAILY: 'daily',
    WEEKLY: 'weekly',
}
type LeaderboardType = (typeof LeaderboardTypes)[keyof typeof LeaderboardTypes]

export { LeaderboardIds, LeaderboardTypes }
export type { LeaderboardType }
