import { _decorator, CCBoolean, pseudoRandom } from 'cc'
import { isGradientTwoValues } from '../ParticleFunction'
import type { Particle } from '../ParticleSimulator'
import GradientRange from '../particle-editor/GradientRange'
import { ModuleRandSeed } from './ModuleRandSeed'

const { ccclass, property } = _decorator

const COLOR_MODULE_RAND_SEED = ModuleRandSeed.COLOR

@ccclass('ColorOvertimeModule')
export class ColorOvertimeModule {
    @property({ type: CCBoolean })
    private enable!: boolean

    @property({ type: GradientRange })
    private color: GradientRange = new GradientRange()

    public update(particle: Particle): void {
        if (!this.enable) return

        particle.color.set(particle.startColor)
        const rand = isGradientTwoValues(this.color) ? pseudoRandom(particle.randomSeed + COLOR_MODULE_RAND_SEED) : 0
        particle.color.multiply(this.color.evaluate(1.0 - particle.remainingLifetime / particle.startLifetime, rand))
    }
}
