import { Button, EventHandler, type EventTouch, Input, Node, type __private, _decorator, type math } from 'cc'
import { type ButtonPlusEvent, ButtonPlusEvents } from '../../constants/ButtonPlusEvents'
import SoundKeys, { CodeSoundKeys, SoundKeysMapping } from '../../constants/SoundKeys'
import AudioUtils from '../../utils/AudioUtils'
import CocosUtils from '../../utils/CocosUtils'

const { ccclass, property, disallowMultiple, menu } = _decorator
const DisabledState: __private._cocos_ui_button__State.DISABLED = 3

@ccclass('ButtonPlus')
@disallowMultiple
@menu('Ludex/ButtonPlus/ButtonPlus')
export class ButtonPlus extends Button {
    @property(EventHandler)
    public onClickStartEvents: EventHandler[] = []

    @property(EventHandler)
    public onClickEndEvents: EventHandler[] = []

    @property(EventHandler)
    public onClickCancelEvents: EventHandler[] = []

    /**
     * @deprecated ButtonPlus overrides and disables native Cocos Creator Button transitions
     */
    @property({ override: true, visible: false })
    override get transition(): typeof Button.Transition.NONE {
        return Button.Transition.NONE
    }

    override set transition(_value: typeof Button.Transition) {
        super.transition = Button.Transition.NONE
    }

    /**
     * @deprecated ButtonPlus overrides and disables native Cocos Creator Button transitions
     */
    @property({ override: true, visible: false })
    override get normalColor() {
        return super.normalColor
    }

    override set normalColor(value) {
        super.normalColor = value
    }

    /**
     * @deprecated ButtonPlus overrides and disables native Cocos Creator Button transitions
     */
    @property({ override: true, visible: false })
    override get pressedColor() {
        return super.pressedColor
    }

    override set pressedColor(value) {
        super.pressedColor = value
    }

    /**
     * @deprecated ButtonPlus overrides and disables native Cocos Creator Button transitions
     */
    @property({ override: true, visible: false })
    override get hoverColor() {
        return super.hoverColor
    }

    override set hoverColor(value) {
        super.hoverColor = value
    }

    /**
     * @deprecated ButtonPlus overrides and disables native Cocos Creator Button transitions
     */
    @property({ override: true, visible: false })
    override get disabledColor() {
        return super.disabledColor
    }

    override set disabledColor(value) {
        super.disabledColor = value
    }

    /**
     * @deprecated ButtonPlus overrides and disables native Cocos Creator Button transitions
     */
    @property({ override: true, visible: false })
    override get normalSprite() {
        return super.normalSprite
    }

    override set normalSprite(value) {
        super.normalSprite = value
    }

    /**
     * @deprecated ButtonPlus overrides and disables native Cocos Creator Button transitions
     */
    @property({ override: true, visible: false })
    override get pressedSprite() {
        return super.pressedSprite
    }

    override set pressedSprite(value) {
        super.pressedSprite = value
    }

    /**
     * @deprecated ButtonPlus overrides and disables native Cocos Creator Button transitions
     */
    @property({ override: true, visible: false })
    override get hoverSprite() {
        return super.hoverSprite
    }

    override set hoverSprite(value) {
        super.hoverSprite = value
    }

    /**
     * @deprecated ButtonPlus overrides and disables native Cocos Creator Button transitions
     */
    @property({ override: true, visible: false })
    override get disabledSprite() {
        return super.disabledSprite
    }

    override set disabledSprite(value) {
        super.disabledSprite = value
    }

    /**
     * @deprecated ButtonPlus overrides and disables native Cocos Creator Button transitions
     */
    @property({ override: true, visible: false })
    override get duration() {
        return super.duration
    }

    override set duration(value) {
        super.duration = value
    }

    /**
     * @deprecated ButtonPlus overrides and disables native Cocos Creator Button transitions
     */
    @property({ override: true, visible: false })
    override get zoomScale() {
        return super.zoomScale
    }

    override set zoomScale(value) {
        super.zoomScale = value
    }

    @property
    public makeSound = true

    @property({
        visible(this: ButtonPlus) {
            return this.makeSound
        },
        type: SoundKeys,
    })
    public soundKey = SoundKeys[CodeSoundKeys.BUTTON_CLICK] as number

    @property({
        tooltip: 'Add a delay after click to prevent spamming (not apply for cancel click)',
    })
    public disableSpamClick = true

    @property({
        visible(this: ButtonPlus) {
            return this.disableSpamClick
        },
        tooltip: 'Delay time in seconds',
    })
    public disableSpamDelay = 0.5

    @property
    public performBoundsCheckOnClickCancel = true

    /**
     * Handle anim scales the click area result in cancelling click
     */
    protected currentClickBoundingBox: math.Rect | null = null

    /**
     * Handle double click case
     */
    protected hasDown = false
    /**
     * Handle double click case
     */
    protected hasUp = false

    /**
     * Whether the button has been Down and Up
     *
     * For check onMouseLeave only
     */
    protected hasFinishClickCycle = false

    protected hasBeenDisabled = false

    public override __preload() {
        super.__preload()
    }

    public override onDestroy(): void {
        super.onDestroy?.()
    }

    public override onEnable() {
        super.onEnable?.()

        this.hasBeenDisabled = !this.interactable

        //? remove origin Button Event
        this.node.off(Input.EventType.TOUCH_END, this._onTouchEnded, this)

        //? add onClick custom handle
        this.node.on(Node.EventType.TOUCH_START, this.onTouchStart)
        this.node.on(Node.EventType.TOUCH_END, this.onTouchEnd)
        this.node.on(Node.EventType.TOUCH_CANCEL, this.onTouchCancel)

        //? Support for window
        this.node.on(Node.EventType.MOUSE_ENTER, this.onMouseEnter)
        this.node.on(Node.EventType.MOUSE_LEAVE, this.onMouseLeave)
    }

    public override onDisable() {
        super.onDisable?.()

        this.node.off(Node.EventType.TOUCH_START, this.onTouchStart)
        this.node.off(Node.EventType.TOUCH_END, this.onTouchEnd)
        this.node.off(Node.EventType.TOUCH_CANCEL, this.onTouchCancel)

        //? Support for window
        this.node.off(Node.EventType.MOUSE_ENTER, this.onMouseEnter)
        this.node.off(Node.EventType.MOUSE_LEAVE, this.onMouseLeave)

        this.unschedule(this.handleResetButtonState)
        this.handleResetButtonState()
    }

    protected override _updateState(): void {
        super._updateState()
        this.patchEmitTransitionDisabled()
    }

    private patchEmitTransitionDisabled() {
        const state = this._getButtonState()
        const isDisabled = state === DisabledState
        if (this.hasBeenDisabled !== isDisabled) {
            if (isDisabled) {
                this.emitTransitionEvent(ButtonPlusEvents.TRANSITION_DISABLED)
            } else {
                this.emitTransitionEvent(ButtonPlusEvents.TRANSITION_RESET)
            }
            this.hasBeenDisabled = isDisabled
        }
    }

    protected onTouchStart = (event: EventTouch) => {
        if (!this.interactable) return
        if (this.hasDown) return

        this.currentClickBoundingBox = CocosUtils.getBoundingBoxToWorld(this.node)
        this.handleDown(event)
    }

    protected onTouchEnd = (event: EventTouch) => {
        // ? Sometime onTouchEnd is called with onTouchStart (~1ms)
        this.scheduleOnce(() => {
            if (!this.interactable) return
            if (!this.hasDown) return
            if (this.hasUp) return

            this.handleUp(event)
        }, 0)
    }

    protected handleDown(event: EventTouch) {
        this.hasDown = true

        if (this.makeSound) {
            AudioUtils.playSound(SoundKeysMapping[this.soundKey] as string)
        }

        this.emitTransitionEvent(ButtonPlusEvents.TRANSITION_DOWN)
        this.emitClickStartEvents(event)
    }

    protected onTouchCancel = (event: EventTouch) => {
        if (!this.interactable) return
        if (!this.hasDown) return
        if (this.hasUp) return

        if (this.performBoundsCheckOnClickCancel) {
            const uiLocation = event.getUILocation()
            const inClickBoundingBox = this.currentClickBoundingBox?.contains(uiLocation) ?? false

            if (inClickBoundingBox) {
                this.handleUp(event)
                return
            }
        }

        this.hasUp = true
        this.hasFinishClickCycle = true

        this.handleResetButtonState()
        this.emitClickCancelEvents(event)
        this.emitClickEndEvents(event)
    }

    protected onMouseEnter = (_event: EventTouch) => {
        if (!this.interactable) return
        if (this.hasDown) return

        this.hasFinishClickCycle = false

        this.emitTransitionEvent(ButtonPlusEvents.TRANSITION_HOVER)
    }

    protected onMouseLeave = (_event: EventTouch) => {
        if (!this.interactable) return
        if (this.hasDown) return
        if (this.hasUp) return
        if (this.hasFinishClickCycle) return

        this.handleResetButtonState()
    }

    private emitClickStartEvents(event: EventTouch) {
        for (const clickEvent of this.onClickStartEvents) {
            clickEvent.emit([event, clickEvent.customEventData])
        }

        this.node.emit(ButtonPlusEvents.CLICK_START, this)
    }

    private emitClickEndEvents(event: EventTouch) {
        for (const clickEvent of this.onClickEndEvents) {
            clickEvent.emit([event, clickEvent.customEventData])
        }

        this.node.emit(ButtonPlusEvents.CLICK_END, this)
    }

    private emitClickCancelEvents(event: EventTouch) {
        for (const clickEvent of this.onClickCancelEvents) {
            clickEvent.emit([event, clickEvent.customEventData])
        }

        this.node.emit(ButtonPlusEvents.CLICK_CANCEL, this)
    }

    protected handleUp(event: EventTouch) {
        this.hasUp = true

        // @ts-expect-error accessing a private prop in Button
        this._pressed = false

        this.hasFinishClickCycle = true

        this.emitTransitionEvent(ButtonPlusEvents.TRANSITION_RESET)

        if (this.disableSpamClick) {
            this.scheduleOnce(this.handleResetButtonState, this.disableSpamDelay)
        } else {
            this.handleResetButtonState()
        }

        this.emitButtonEvents(event)
        this.emitClickEndEvents(event)
    }

    protected emitButtonEvents(event: EventTouch) {
        if (!this.interactable) return

        for (const clickEvent of this.clickEvents) {
            clickEvent.emit([event, clickEvent.customEventData])
        }

        this.node.emit(ButtonPlusEvents.CLICK, this)
        this.node.emit(Button.EventType.CLICK, this)
    }

    protected handleResetButtonState = () => {
        this.hasDown = false
        this.hasUp = false
        this.emitTransitionEvent(ButtonPlusEvents.TRANSITION_RESET)
    }

    protected emitTransitionEvent(event: ButtonPlusEvent) {
        this.node.emit(event, this)
    }

    /** Fake a click to reset the animation controller of the button.
     * You might want to use this method when you need the button to
     * reflect a different reset state.
     *
     * * Note: This method is only for visual purposes and does not emit any events.
     * Use `resetButtonState()` to reset the button's internal logical state.
     * */
    public fakeClick(): void {
        this.emitTransitionEvent(ButtonPlusEvents.TRANSITION_DOWN)
        this.scheduleOnce(() => {
            this.emitTransitionEvent(ButtonPlusEvents.TRANSITION_RESET)
        }, 0)
    }

    /** Reset the button's internal logical state.
     * This method affect the natural flow of the button's interaction.
     *
     * * Note: Please use `fakeClick()` if you only need to reset the button's visual state.
     */
    public resetButtonState() {
        this.handleResetButtonState()
    }

    public setInteractable(interactable = true, alsoChangeEnableStatus = true) {
        this.interactable = interactable
        if (alsoChangeEnableStatus) {
            this.enabled = interactable
        }
    }
}
