import { _decorator, CCString, Component, director } from 'cc'
import AudioChannel from './AudioChannel'

const { ccclass, property } = _decorator

const { audio, player } = codex

type SoundKeys = Ludex.Plugins.Audio.SoundKeys
type PlayConfig = Ludex.Plugins.Audio.PlayConfig

@ccclass('Sound')
export class Sound extends Component {
    @property(CCString)
    public channelId = ''

    private channel: AudioChannel | null = null

    public __preload() {
        if (!audio) return

        // * Only call new a class (not component) in __preload method
        this.channel = new AudioChannel(this.node, this.channelId)
        audio.addChannel(this.channel)
    }

    public onLoad(): void {
        if (this.channelId === '') {
            this.channelId = this.node.uuid
        }
    }

    // ? Sometime we need some sound effect must be play when scene is changed
    public makePersistent() {
        director.addPersistRootNode(this.node)
    }

    public removePersistent() {
        director.removePersistRootNode(this.node)
    }

    public play(key: SoundKeys, options?: PlayConfig) {
        if (!this.isSoundEnable()) return

        return audio.play(key, options, this.channelId)
    }

    private isSoundEnable(): boolean {
        return !!player.getPlayerSetting('sound')
    }

    public setVolume(volume: number) {
        audio.setVolume(volume, this.channelId)
    }

    public pause(key: SoundKeys) {
        audio.pause(key, this.channelId)
    }

    public resume(key: SoundKeys) {
        if (!this.isSoundEnable()) return

        audio.resume(key, this.channelId)
    }

    public stop(key: SoundKeys) {
        audio.stop(key, this.channelId)
    }

    public stopAll() {
        audio.stopAll(this.channelId)
    }

    public mute(boolean: boolean) {
        if (boolean) {
            audio.mute(this.channelId)
        } else {
            audio.unmute(this.channelId)
        }
    }
}
