import { _decorator, CCFloat, CCInteger, repeat } from 'cc'
import type { ParticleSystem } from './ParticleSystem'

const { ccclass, property } = _decorator

@ccclass('Burst')
export class Burst {
    @property(CCFloat)
    private time: number = 0

    @property(CCInteger)
    private repeatCount: number = 1

    @property(CCFloat)
    private repeatInterval: number = 1

    @property(CCInteger)
    public count: number = 1

    private remainingCount!: number
    private curTime!: number

    constructor() {
        this.remainingCount = 0
        this.curTime = 0.0
    }

    public update(psys: ParticleSystem, dt: number): void {
        if (this.remainingCount === 0) {
            this.remainingCount = this.repeatCount
            this.curTime = this.time
        }

        if (this.remainingCount > 0) {
            let preFrameTime = repeat(psys.time - psys.startDelay.evaluate(0, 1), psys.duration) - dt
            preFrameTime = preFrameTime > 0.0 ? preFrameTime : 0.0
            const curFrameTime = repeat(psys.time - psys.startDelay.evaluate(0, 1), psys.duration)
            if (this.curTime >= preFrameTime && this.curTime < curFrameTime) {
                psys.emit(this.count, dt - (curFrameTime - this.curTime))
                this.curTime += this.repeatInterval
                --this.remainingCount
            }
        }
    }

    public reset(): void {
        this.remainingCount = 0
        this.curTime = 0.0
    }
}
