import { _decorator, Component, Label, ParticleSystem2D, Sprite, tween, UIOpacity, Vec3 } from 'cc'
import CoinData from '../../../constants/CoinData'
import { ScreenKeysEnum } from '../../../constants/ScreenKeys'
import { CodeSoundKeys } from '../../../constants/SoundKeys'
import GameplayEvents from '../../../gameplay/constants/GameEvents'
import AnimUtils from '../../../utils/AnimUtils'
import AudioUtils from '../../../utils/AudioUtils'
import ComponentUtils from '../../../utils/ComponentUtils'
import AnimAbortSignal from '../../../utils/common/AnimAbortSignal'
import ScreenUtils from '../../../utils/ScreenUtils'

const { ccclass, property } = _decorator
const {
    Utils: { Number: N },
} = Ludex

@ccclass('CoinContainer')
export class CoinContainer extends Component {
    @property(Label)
    private coinLabel!: Label

    @property(Sprite)
    private coinIcon!: Sprite

    @property(UIOpacity)
    private explosionCircle!: UIOpacity

    @property(ParticleSystem2D)
    private explosionParticles!: ParticleSystem2D

    private currentCoin = 0

    private animAbortSignal: AnimAbortSignal | undefined

    private setCoinTextFromNumber(value: number): void {
        const text = N.getShortNumber(value, 3)
        this.coinLabel.string = text
    }

    public handleAddCoinButton(): void {
        ScreenUtils.openScreen(ScreenKeysEnum.GET_COIN_SCREEN, {
            quantity: CoinData.GET_COIN,
        })
    }

    public onLoad(): void {
        this.validateRequiredComponents()
        this.listenToEvents()
        this.explosionParticles.node.active = false
    }

    private validateRequiredComponents(): void {
        ComponentUtils.lazyValidateProperties(this)
    }

    private listenToEvents(): void {
        this.node.scene.on(GameplayEvents.COIN_CHANGED, this.updateCoins, this)
    }

    public async updateCoins(payload: CoinDataPayload): Promise<void> {
        const { coins, collectionAnimation: animConfig, playSound } = payload

        if (animConfig === undefined || this.node.activeInHierarchy === false) {
            this.setCoins(coins)
            return
        }

        const { signal } = animConfig

        this.animAbortSignal?.abort()
        this.animAbortSignal = signal ?? new AnimAbortSignal()

        if (animConfig.animateTextOnly) {
            await this.animateCoinText(coins, 0, playSound, signal)
            this.setCoins(coins)
            return
        }

        const flyDuration = animConfig.flyDuration ?? 0
        const floatDuration = animConfig.floatDuration ?? 0

        const delay = floatDuration + flyDuration

        await this.animateCoinText(coins, delay, playSound, signal)
        this.setCoins(coins)
    }

    @ComponentUtils.onlyRunInLifeCycle
    public setCoins(coins: number) {
        this.setCoinTextFromNumber(coins)
        this.currentCoin = coins
    }

    @ComponentUtils.onlyRunInLifeCycleAsync
    private async animateCoinText(
        coins: number,
        delay: number,
        playSound = true,
        signal?: AnimAbortSignal,
    ): Promise<void> {
        const startCoin = this.currentCoin

        await AnimUtils.waitTweenAsync(delay, signal)

        if (playSound) AudioUtils.playSound(CodeSoundKeys.COLLECT_COINS)

        await AnimUtils.runOnUpdateAsync(
            this.node,
            1,
            (ratio) => {
                const currentCoin = Math.round(startCoin + (coins - startCoin) * ratio)
                this.setCoinTextFromNumber(currentCoin)
            },
            signal,
        )
    }

    @ComponentUtils.onlyRunInLifeCycleAsync
    public async emitExplodeEffect(signal?: AnimAbortSignal): Promise<void> {
        this.initializeExplosionCircle()
        this.explosionParticles.resetSystem()

        await Promise.all([
            AnimUtils.runTweenAsync(
                tween(this.explosionCircle.node).to(0.4, { scale: new Vec3(3, 3, 3) }, { easing: 'quadOut' }),
                signal,
            ),
            // ? Make sure that delay + duration add up to the duration of the tween above
            AnimUtils.runTweenAsync(
                tween(this.explosionCircle).delay(0.2).to(0.2, {
                    opacity: 0,
                }),
                signal,
            ),
        ])

        this.resetExplosionCircle()
    }

    @ComponentUtils.onlyRunInLifeCycle
    private resetExplosionCircle() {
        this.explosionCircle.node.setScale(0, 0)
        this.explosionCircle.opacity = 0
        this.explosionCircle.node.active = false
        this.explosionParticles.node.active = false
    }

    @ComponentUtils.onlyRunInLifeCycle
    private initializeExplosionCircle() {
        this.explosionCircle.node.active = true
        this.explosionParticles.node.active = true
        this.explosionCircle.opacity = 255
        this.explosionCircle.node.setScale(0, 0)
    }

    public getCoinIconWorldPosition(): Vec3 {
        return this.coinIcon.node.worldPosition
    }

    public getCoinIconWorldScale(): Vec3 {
        return this.coinIcon.node.worldScale
    }

    protected onDestroy(): void {
        this.animAbortSignal?.abort()
    }
}
