import { _decorator, Component, game, UITransform } from 'cc'
import { OpacityToggler } from '../../../../../components/common/OpacityToggler'
import GameEvents from '../../../../../constants/GameEvents'
const { ccclass, property } = _decorator

@ccclass('LandscapeMask')
export class LandscapeMask extends Component {
    @property(OpacityToggler)
    private opacityToggler!: OpacityToggler

    @property(UITransform)
    private uiTransform!: UITransform

    @property(UITransform)
    private leftMask!: UITransform

    @property(UITransform)
    private rightMask!: UITransform

    protected __preload(): void {
        game.on(GameEvents.VIEW_RESIZED, this.onViewResized, this)
    }

    protected onDestroy(): void {
        game.off(GameEvents.VIEW_RESIZED, this.onViewResized, this)
    }

    private onViewResized(): void {
        this.updateMaskPosition()
    }

    public updateMaskPosition() {
        const { width: screenWidth, height: screenHeight } = this.uiTransform.contentSize

        // ? Masks is rotated 90 degrees so width is actually height and vice versa
        this.leftMask.width = this.rightMask.width = screenHeight * 2
        const margin = this.leftMask.height > screenWidth * 0.3 ? this.leftMask.height - screenWidth * 0.3 : 0

        this.leftMask.node.setWorldPosition(-margin, this.leftMask.node.worldPosition.y, 0)
        this.rightMask.node.setWorldPosition(screenWidth + margin, this.rightMask.node.worldPosition.y, 0)
    }

    public show(): void {
        this.opacityToggler.changeStateFade(true, 0.2)
    }

    public hide(): void {
        this.opacityToggler.changeStateFade(false, 0.1)
    }
}
