import ComponentUtils from 'db://assets/utils/ComponentUtils'
import { _decorator, Component, type Material, Sprite, UITransform, Vec2 } from 'cc'
import { GameMask } from './GameMask'

const { ccclass, property, executeInEditMode } = _decorator

@ccclass('AlphaMasks')
@executeInEditMode(true)
export class AlphaMasks extends Component {
    @property(GameMask)
    public masks: GameMask[] = []

    @ComponentUtils.required()
    private UITransform!: UITransform

    private material!: Material

    public __preload(): void {
        this.UITransform = this.node.getComponent(UITransform)!
    }

    public onLoad(): void {
        ComponentUtils.validateRequiredProperties(this)
    }

    public start(): void {
        this.material = this.getComponent(Sprite)?.getMaterialInstance(0)!

        console.log('Alphamasks', this.masks, this.node.parent?.name)
        for (let index = 0; index < this.masks.length; index++) {
            const mask = this.masks[index]
            if (!mask) continue
            console.log('mask', mask.texture)
            this.material.setProperty(`mask${index}Texture`, mask.texture)
        }
    }

    protected update(): void {
        if (!this.material) return

        const { width, height } = this.UITransform

        this.masks.forEach((mask, index) => {
            const { width: maskUIWidth, height: maskUIHeight, anchorX, anchorY } = mask.uiTransform

            const maskWidth = maskUIWidth * mask.node.scale.x
            const maskHeight = maskUIHeight * mask.node.scale.y

            const ratio = new Vec2(maskWidth / width, maskHeight / height)

            const offset = new Vec2(0.5 + (0.5 - anchorX) * ratio.x, 0.5 - (0.5 - anchorY) * ratio.y)

            const center = new Vec2(
                offset.x + (mask.node.worldPosition.x - this.node.worldPosition.x) / width,
                offset.y - (mask.node.worldPosition.y - this.node.worldPosition.y) / height,
            )

            this.material.setProperty(`ratio${index}`, ratio)
            this.material.setProperty(`center${index}`, center)
        })
    }

    public setMaskActive(index: number, active: boolean): void {
        if (index >= this.masks.length || !this.material) return

        this.material.setProperty(`useMask${index}`, active ? 1 : 0)
    }
}
