import {
    Animation,
    AnimationClip,
    Component,
    _decorator as decor,
    math,
    Node,
    randomRange,
    Sprite,
    SpriteFrame,
    type Tween,
    type TweenEasing,
    tween,
    UIOpacity,
    UITransform,
    Vec2,
    Vec3,
} from 'cc'
import { ExtendedButton } from '../../../../components/button/ExtendedButton'
import { CodeSoundKeys } from '../../../../constants/SoundKeys'
import AnimUtils from '../../../../utils/AnimUtils'
import AudioUtils from '../../../../utils/AudioUtils'
import ComponentUtils from '../../../../utils/ComponentUtils'
import GameplayEvents from '../../../constants/GameEvents'
import TileSpecialTypes from '../constants/TileSpecialTypes'

const { ccclass, property, requireComponent } = decor

const {
    Utils: { Signal: S, Function: F },
} = Ludex

type Signal = InstanceType<typeof S.Signal>

@ccclass('Tile')
@requireComponent([UITransform])
@requireComponent([UIOpacity])
export default class Tile extends Component {
    @ComponentUtils.required()
    @property({ type: Sprite, group: 'Sprite' })
    public readonly iconPlaceholder!: Sprite

    @ComponentUtils.required()
    @property({ type: Sprite, group: 'Sprite' })
    public readonly icon!: Sprite

    @ComponentUtils.required()
    @property({ type: Sprite, group: 'Sprite' })
    public readonly bombComponent!: Sprite

    @ComponentUtils.required()
    @property({ type: Sprite, group: 'Sprite' })
    public readonly starComponent!: Sprite

    @ComponentUtils.required()
    @property({ type: Sprite, group: 'Sprite' })
    private readonly background!: Sprite

    @ComponentUtils.required()
    @property({ type: UITransform, displayName: 'Background UI Transform' })
    private readonly bgUITransform!: UITransform

    @ComponentUtils.required()
    @property(SpriteFrame)
    private readonly defaultBackground!: SpriteFrame

    @ComponentUtils.required()
    @property({ type: UIOpacity, group: 'Opacity' })
    private readonly uiOpacity!: UIOpacity

    @ComponentUtils.required()
    @property({ type: UIOpacity, group: 'Opacity' })
    private readonly shineOpacity!: UIOpacity

    @ComponentUtils.required()
    @property({ type: UIOpacity, group: 'Opacity' })
    private readonly glowOpacity!: UIOpacity

    @ComponentUtils.required()
    @property({ type: UIOpacity, group: 'Opacity' })
    private readonly iconOpacity!: UIOpacity

    @ComponentUtils.required()
    @property({ type: UIOpacity, group: 'Opacity' })
    private readonly iconPlaceholderOpacity!: UIOpacity

    @ComponentUtils.required()
    @property({ type: UIOpacity, group: 'Opacity' })
    public readonly bombUiOpacity!: UIOpacity

    @ComponentUtils.required()
    @property({ type: UIOpacity, group: 'Opacity' })
    public readonly startUiOpacity!: UIOpacity

    @ComponentUtils.required()
    @property(ExtendedButton)
    readonly button!: ExtendedButton

    @ComponentUtils.required()
    @property(Node)
    private readonly container!: Node

    private glowOpacityTween!: Tween<UIOpacity>

    @ComponentUtils.required()
    @property({ type: Animation, group: 'Animation' })
    readonly animationComponent!: Animation

    @ComponentUtils.required()
    @property({ type: AnimationClip, group: 'Animation' })
    private readonly clearClip!: AnimationClip

    @ComponentUtils.required()
    @property({ type: AnimationClip, group: 'Animation' })
    private readonly shakeClip!: AnimationClip

    @ComponentUtils.required()
    @property({ type: AnimationClip, group: 'Animation' })
    private readonly breakClip!: AnimationClip

    @ComponentUtils.required()
    @property({ type: AnimationClip, group: 'Animation' })
    private readonly lightningClearClip!: AnimationClip

    public id: number | null = null

    public frames: Promise<Array<SpriteFrame>> | Array<SpriteFrame> = []

    public special: ReturnType<typeof this.createSpecialHandler> | undefined

    private isEmpty = false

    private readonly coord: Vec2 = new Vec2()

    private readonly mainSignalController = new S.SignalController()
    private readonly identitySignalController = new S.SignalController()
    private readonly placeholderSignalController = new S.SignalController()
    private readonly specialSignalController = new S.SignalController()

    protected onLoad(): void {
        ComponentUtils.validateRequiredProperties(this, Tile)
        this.special = this.createSpecialHandler(TileSpecialTypes.none)
    }

    protected update(dt: number): void {
        if (this.special?.type === TileSpecialTypes.bomb) {
            this.special.update(dt)
        }
    }

    protected onDestroy(): void {
        if (this.special?.type !== TileSpecialTypes.none) {
            this.special?.onDestroy()
        }
    }

    @ComponentUtils.onlyRunInLifeCycle
    public setIgnoreInput(ignore = true): void {
        this.button.interactable = !ignore
    }

    public getCoord(): Readonly<Vec2> {
        return this.coord
    }

    public setCoord(x: number, y: number): void {
        this.coord.set(x, y)
    }

    public isSimilarTo(tile: Readonly<Tile>): boolean {
        return this.id === tile.id
    }

    public inSamePositionAs(tile: Readonly<Tile>): boolean
    public inSamePositionAs(x: number, y: number): boolean

    public inSamePositionAs(tileOrX: Readonly<Tile> | number, y?: number): boolean {
        if (tileOrX instanceof Tile) {
            return this.coord.equals(tileOrX.getCoord())
        }

        return this.coord.x === tileOrX && this.coord.y === y
    }

    public isHidden(): boolean {
        return this.isEmpty
    }

    public getSize(): [number, number] {
        return [this.bgUITransform.width, this.bgUITransform.height]
    }

    public onClick(): void {
        if (this.isHidden()) return
        this.node.scene.emit(GameplayEvents.TILE_CLICK, this)
    }

    public setIdentity(id: number, frames: Promise<Array<SpriteFrame>> | Array<SpriteFrame>): void {
        this.id = id
        this.frames = frames
        this.updateIdentityFrame()
    }

    public async updateIdentityFrame() {
        if (this.id === null) return

        const signal = this.identitySignalController.renew()

        if (!Array.isArray(this.frames)) {
            this.setPlaceholderVisible(true, signal)
            const frames = await this.frames
            if (signal.aborted) return
            this.frames = frames
        }

        const frame = this.frames[this.id]

        if (!frame) {
            console.error(`Failed to load identity frame. ${this.id} is not in the frames array.`)
            return
        }

        this.setPlaceholderVisible(false, signal)
        signal.wrap(() => this.setIdentityFrame(frame))?.()
    }

    @ComponentUtils.onlyRunInLifeCycle
    private setPlaceholderVisible(visible = true, identitySignal?: Signal) {
        const signal = this.placeholderSignalController.renew()

        if (identitySignal) {
            signal.chain(identitySignal)
        }

        if (visible) {
            AnimUtils.runTweenAsync(
                tween(this.iconPlaceholder.node).to(0.15, { scale: Vec3.ONE }, { easing: 'backOut' }),
                signal,
            )

            AnimUtils.runTweenAsync(
                tween(this.iconPlaceholderOpacity)
                    .delay(randomRange(0, 0.75))
                    .to(0.15, { opacity: 255 })
                    .repeatForever(
                        tween(this.iconPlaceholderOpacity)
                            .to(0.25, { opacity: 80 })
                            .delay(0.25)
                            .to(0.25, { opacity: 255 })
                            .delay(0.75),
                    ),
                signal,
            )

            return
        }

        AnimUtils.runTweenAsync(
            tween(this.iconPlaceholder.node).to(0.15, { scale: Vec3.ZERO }, { easing: 'backIn' }),
            signal,
        )

        AnimUtils.runTweenAsync(tween(this.iconPlaceholderOpacity).to(0.15, { opacity: 0 }), signal)
    }

    @ComponentUtils.onlyRunInLifeCycle
    private setIdentityFrame(frame: SpriteFrame, signal?: Signal) {
        this.icon.spriteFrame = frame

        //? fit icon into 60% Tile
        const fillRatio = 0.7
        const [tileWidth, tileHeight] = this.getSize()

        const { width: iconW, height: iconH } = frame.originalSize
        const scale = Math.max(iconW / (tileWidth * fillRatio), iconH / (tileHeight * fillRatio))

        this.icon.node.setScale(0, 0, 0)

        const onCancel = () => {
            this.icon.node.setScale(1 / scale, 1 / scale, 1)
        }

        signal?.once(onCancel)

        AnimUtils.runTweenAsync(
            tween(this.icon.node)
                .delay(randomRange(0, 0.2))
                .to(
                    0.15,
                    {
                        scale: new Vec3(1 / scale, 1 / scale, 1),
                    },
                    {
                        easing: 'sineOut',
                    },
                ),
            signal,
        )

        signal?.off(onCancel)
    }

    public setSpecialType(specialType: TileSpecialType = TileSpecialTypes.none, bombDuration?: number): void {
        this.special?.switch(specialType, bombDuration)

        if (this.special?.type === TileSpecialTypes.bomb || this.special?.type === TileSpecialTypes.star) {
            const special = this.special
            if (!Array.isArray(this.frames)) {
                const signal = this.specialSignalController.renew()
                special.hide(signal, true)
                this.frames.then(() => {
                    if (special.isDestroyed) return
                    special.show(signal)
                })
            }
        }
    }

    public async setFocused(focused: boolean): Promise<void> {
        const shineOpacityTween = tween(this.shineOpacity).to(
            0.15,
            { opacity: focused ? 255 : 0 },
            { easing: 'cubicOut' },
        )

        this.shineOpacity.node.scale = focused ? Vec3.ZERO : Vec3.ONE

        const scaleTween = tween(this.shineOpacity.node).to(
            0.15,
            { scale: focused ? Vec3.ONE : Vec3.ZERO },
            { easing: 'cubicOut' },
        )

        if (!focused) {
            if (this.glowOpacityTween) this.glowOpacityTween.stop()
            this.glowOpacity.opacity = 0
        } else {
            const glowOpacityTween = tween(this.glowOpacity).to(2, { opacity: 150 })
            glowOpacityTween.to(2, { opacity: 20 })

            this.glowOpacityTween = tween(this.glowOpacity).repeatForever(glowOpacityTween).start()
        }

        await Promise.all([AnimUtils.runTweenAsync(shineOpacityTween), AnimUtils.runTweenAsync(scaleTween)])
    }

    public setEmpty(quietly = false) {
        if (this.isEmpty) {
            return
        }

        this.mainSignalController.abort()

        this.isEmpty = true
        this.setIgnoreInput(true)

        /* Manually call reset button state
        because the clear animation hide the button immediately
        which might cause the mouse up event to not be triggered.
        This will cause the button to be stuck in the pressed state.
        */
        this.button.resetButtonState()

        if (
            !quietly &&
            (this.special?.type === TileSpecialTypes.star || this.special?.type === TileSpecialTypes.bomb)
        ) {
            this.special.clear()
        }

        this.setOpacity(0)
        this.special?.switch(TileSpecialTypes.none)
    }

    @ComponentUtils.onlyRunInLifeCycle
    private setOpacity(opacity: number) {
        this.uiOpacity.opacity = opacity
    }

    public async setHint(hint: boolean) {
        const signal = this.mainSignalController.renew()

        if (hint) {
            await this.runAnimation(this.shakeClip.name, signal)
            return
        }

        this.animationComponent?.stop()
        if (!this.container) return

        const normalTween = tween(this.container).to(0.1, {
            scale: Vec3.ONE,
            eulerAngles: Vec3.ZERO,
        })

        await AnimUtils.runTweenAsync(normalTween, signal)
    }

    public async playClearFX(delay?: number, lightning: boolean = false): Promise<void> {
        const signal = this.mainSignalController.renew()

        this.setOpacity(255)

        const cb = () => this.setOpacity(0)
        const clearClip = lightning ? this.lightningClearClip : this.clearClip

        signal.once(cb)
        await this.runAnimationAfterDelay(clearClip.name, delay, signal)
        signal.off(cb)

        if (!signal.aborted) cb()
    }

    public async playOldLightningClearFX(): Promise<void> {
        const signal = this.mainSignalController.renew()

        this.setOpacity(255)

        const cb = () => this.setOpacity(0)

        signal.once(cb)
        await this.runAnimation(this.breakClip.name, signal)
        signal.off(cb)

        if (!signal.aborted) cb()
    }

    @ComponentUtils.onlyRunInLifeCycleAsync
    private async runAnimationAfterDelay(name: string, delay?: number, signal?: Signal) {
        if (delay) await AnimUtils.waitTweenAsync(delay, signal)
        await this.runAnimation(name, signal)
    }

    @ComponentUtils.onlyRunInLifeCycleAsync
    private runAnimation(name: string, signal?: Signal) {
        return AnimUtils.runAnimationAsync(this.animationComponent, name, signal)
    }

    public async restore(id?: number, frames?: Promise<Array<SpriteFrame>> | Array<SpriteFrame>): Promise<void> {
        const signal = this.mainSignalController.renew()

        this.animationComponent.stop()

        this.isEmpty = false
        this.setIgnoreInput(true)

        if (id !== undefined && frames) {
            this.setIdentity(id, frames)
        }

        this.setOpacity(255)
        this.iconOpacity.opacity = 255
        this.node.setScale(Vec3.ZERO)
        this.background.spriteFrame = this.defaultBackground
        this.button.fakeClick()

        await AnimUtils.runTweenAsync(
            tween(this.node).to(
                0.3,
                {
                    scale: Vec3.ONE,
                },
                {
                    easing: 'backOut',
                },
            ),
            signal,
        )

        this.node.setScale(Vec3.ONE)
        this.setIgnoreInput(false)
    }

    public async sync(config: {
        duration?: number
        delay?: number
        easing?: TweenEasing
        getTargetPosition: (pos: Vec2Like) => Vec2Like
    }): Promise<void> {
        const { duration = 0.3, delay = 0, easing = 'cubicOut', getTargetPosition } = config

        const targetPos = getTargetPosition(this.coord)
        const position = new Vec3(targetPos.x, targetPos.y, this.node.position.z)

        await AnimUtils.runTweenAsync(tween(this.node).delay(delay).to(duration, { position }, { easing }))
    }

    public async fly(config: { getTilePosition: (pos: Readonly<Vec2Like>) => Vec2Like }) {
        const MAX_HEIGHT = 300
        const distanceToCenter = Math.sqrt(this.node.position.x ** 2 + this.node.position.y ** 2)
        const height = Math.max(MAX_HEIGHT - distanceToCenter * 0.7, 0)
        const delay = distanceToCenter / 1000

        const curPosition = this.node.position.clone()
        const targetPos = config.getTilePosition(this.coord)
        const newPosition = new Vec3(targetPos.x, targetPos.y, this.node.position.z)

        const spriteNode = this.container
        // const shadowNode = this.node.getChildByName('Shadow')!

        let shakeAmount = 2

        const duration = height * 0.0025

        let initOrbitSpeed = 1 //? PI/s
        let initSelfOrbitSpeed = 360 //? degree/s

        const phase1Duration = duration

        let phase2StartAngle = 0
        const phase2CenterPos = new Vec3(0, 0, 0)
        const phase2Duration = 0.75 - phase1Duration

        const tweenObject = { value: 0, prevElapsed: 0 }
        const t = tween(tweenObject)
        t.delay(delay)
        t.to(
            0.5,
            { value: 0 },
            {
                progress: (_start: number, _end: number, current: number, ratio: number): number => {
                    shakeAmount *= -1

                    const deltaShake = shakeAmount * ratio

                    const newPos = curPosition.clone()
                    newPos.x += deltaShake
                    this.node.setPosition(newPos)

                    return current
                },
            },
        )
        t.to(
            phase1Duration,
            { value: 1 },
            {
                progress: (_start: number, _end: number, current: number, ratio: number): number => {
                    const expoRatio = AnimUtils.easeOutExpo(ratio)
                    const deltaTime = phase1Duration * ratio - tweenObject.prevElapsed
                    const elapsedTime = phase1Duration * ratio

                    //? tween height
                    const newH = height * expoRatio
                    this.setHeight(newH, MAX_HEIGHT)

                    //? tween position
                    const currentPos = this.node.position.clone()
                    initOrbitSpeed += 0.1
                    Vec3.rotateZ(currentPos, currentPos, new Vec3(0, 0, 0), initOrbitSpeed * Math.PI * deltaTime)

                    this.node.setPosition(new Vec3(currentPos.x, currentPos.y, currentPos.z))

                    //? tween angle
                    initSelfOrbitSpeed += 10
                    const addAngle = initSelfOrbitSpeed * deltaTime
                    spriteNode.angle += addAngle
                    // shadowNode.angle += addAngle

                    tweenObject.prevElapsed = elapsedTime

                    return current
                },
            },
        )
        t.call(() => {
            phase2CenterPos.set(
                (this.node.position.x + newPosition.x) / 2,
                (this.node.position.y + newPosition.y) / 2,
                0,
            )
            tweenObject.prevElapsed = 0
            //? transform angle to negative value for easy tween
            spriteNode.angle %= 360
            spriteNode.angle -= 360
            // shadowNode.angle %= 360
            // shadowNode.angle -= 360
            phase2StartAngle = spriteNode.angle
        })
        t.to(
            phase2Duration,
            { value: 0 },
            {
                progress: (_start: number, _end: number, current: number, ratio: number): number => {
                    const deltaTime = phase2Duration * ratio - tweenObject.prevElapsed
                    const elapsedTime = phase2Duration * ratio

                    //? tween height
                    const newH = height * (1 - ratio)
                    this.setHeight(newH, MAX_HEIGHT)

                    //? tween position
                    const currentPos = this.node.position.clone()
                    Vec3.rotateZ(currentPos, currentPos, phase2CenterPos, (Math.PI / phase2Duration) * deltaTime)

                    this.node.setPosition(new Vec3(currentPos.x, currentPos.y, currentPos.z))

                    //? tween angle
                    spriteNode.angle += (-phase2StartAngle / phase2Duration) * deltaTime
                    // shadowNode.angle += (-phase2StartAngle / phase2Duration) * deltaTime

                    tweenObject.prevElapsed = elapsedTime

                    return current
                },
            },
        )
        t.call(() => {
            this.node.setPosition(newPosition)
            this.setHeight(0, MAX_HEIGHT)
            spriteNode.angle = 0
            // shadowNode.angle = 0
        })
        return AnimUtils.runTweenAsync(t)
    }

    public async moveAround(): Promise<void> {
        const moveDirections: Vec2Like[] = [
            { x: 1, y: 1 },
            { x: -1, y: 1 },
            { x: -1, y: -1 },
            { x: 1, y: -1 },
        ]

        const defaultPosition = this.container.position.clone()
        let prevDirectionIndex = -1

        for (let i = 0; i < 5; i++) {
            let moveDirectionIndex = math.randomRangeInt(0, 4)
            while (prevDirectionIndex === moveDirectionIndex) {
                moveDirectionIndex = math.randomRangeInt(0, 4)
            }
            prevDirectionIndex = moveDirectionIndex

            const moveDirection = moveDirections[moveDirectionIndex]
            if (!moveDirection) continue

            const movePosition = defaultPosition.clone().add(new Vec3(moveDirection.x * 5, moveDirection.y * 5))
            const moveTween = tween(this.container).to(0.1, { position: movePosition }, { easing: 'smooth' })

            await AnimUtils.runTweenAsync(moveTween)
        }

        const resetTween = tween(this.container).to(0.1, { position: defaultPosition }, { easing: 'smooth' })
        await AnimUtils.runTweenAsync(resetTween)
    }

    private setHeight(height: number, maxHeight: number): void {
        if (!this.container) return

        const spriteNode = this.container
        // const shadowNode = this.node.getChildByName('Shadow')!

        spriteNode.setPosition(new Vec3(spriteNode.position.x, height, 0))
        // shadowNode.setPosition(new Vec3(shadowNode.position.x, -4 - height / 10, 0))
        //? max: 0.3 (height), min: 0 (height 0)
        spriteNode.setScale(new Vec3(1 + (height / maxHeight) * 0.3, 1 + (height / maxHeight) * 0.3, 1))
    }

    private createSpecialHandler(type: TileSpecialType, bombDuration?: number) {
        switch (type) {
            case TileSpecialTypes.bomb:
                return this.createBombHandler(bombDuration ?? 90)
            case TileSpecialTypes.star:
                return this.createStarHandler()
            default:
                return {
                    type: TileSpecialTypes.none,
                    switch: (to: TileSpecialType, bombDuration?: number) => {
                        this.special = this.createSpecialHandler(to, bombDuration)
                    },
                } as const
        }
    }

    private createBombHandler(duration: number) {
        let isDestroyed = false
        let isCountingDown = false
        let remaining = duration

        const setCounting = () => {
            isCountingDown = true
        }

        const setNotCounting = () => {
            isCountingDown = false
        }

        this.node.scene.on(GameplayEvents.TILE_CLICK, setCounting)
        this.node.scene.on(GameplayEvents.USE_ITEM, setCounting)

        this.node.scene.on(GameplayEvents.RESUME_TIME, setCounting)

        this.node.scene.on(GameplayEvents.PAUSE_TIME, setNotCounting)
        this.node.scene.on(GameplayEvents.TIME_OUT, setNotCounting)

        this.bombComponent.node.active = true

        const detonate = () => {
            isCountingDown = false
            this.bombComponent.node.active = false
            this.setEmpty(true)
            this.node.scene.emit(GameplayEvents.BOMB_EXPLODE, this)
        }

        const update = (dt: number) => {
            if (isCountingDown) {
                remaining = remaining - dt
                this.bombComponent.fillRange = -remaining / duration

                if (remaining <= 0) {
                    detonate()
                }
            }
        }

        const clear = () => {
            AudioUtils.playSound(CodeSoundKeys.BOMB_DEFUSE)
        }

        const onDestroy = () => {
            isDestroyed = true

            this.node.scene.off(GameplayEvents.TILE_CLICK, setCounting)
            this.node.scene.off(GameplayEvents.USE_ITEM, setCounting)

            this.node.scene.off(GameplayEvents.GAME_RESUME, setCounting)

            this.node.scene.off(GameplayEvents.GAME_PAUSE, setNotCounting)
            this.node.scene.off(GameplayEvents.TIME_OUT, setNotCounting)
        }

        const cleanup = () => {
            isDestroyed = true
            isCountingDown = false
            this.bombComponent.node.active = false
            remaining = duration

            this.node.scene.off(GameplayEvents.TILE_CLICK, setCounting)
            this.node.scene.off(GameplayEvents.USE_ITEM, setCounting)

            this.node.scene.off(GameplayEvents.GAME_RESUME, setCounting)

            this.node.scene.off(GameplayEvents.GAME_PAUSE, setNotCounting)
            this.node.scene.off(GameplayEvents.TIME_OUT, setNotCounting)
        }

        const hide = (signal?: Signal, immediate = false) => {
            if (immediate) {
                this.bombUiOpacity.opacity = 0
                return
            }

            signal?.once(() => {
                this.bombUiOpacity.opacity = 0
            })

            return F.allSettled([
                AnimUtils.runTweenAsync(
                    tween(this.bombUiOpacity).to(0.1, { opacity: 0 }, { easing: 'cubicOut' }),
                    signal,
                ),
            ])
        }

        const show = (signal?: Signal, immediate = false) => {
            if (immediate) {
                this.bombUiOpacity.opacity = 255
                return
            }

            signal?.once(() => {
                this.bombUiOpacity.opacity = 255
            })

            return F.allSettled([
                AnimUtils.runTweenAsync(
                    tween(this.bombUiOpacity).to(0.1, { opacity: 255 }, { easing: 'cubicOut' }),
                    signal,
                ),
            ])
        }

        const sw = (to: TileSpecialType, bombDuration?: number) => {
            cleanup()
            this.special = this.createSpecialHandler(to, bombDuration)
        }

        const detonateImmediately = () => {
            remaining = 0
        }

        return {
            type: TileSpecialTypes.bomb,
            get isDestroyed() {
                return isDestroyed
            },
            update,
            clear,
            cleanup,
            onDestroy,
            switch: sw,
            show,
            hide,
            detonateImmediately,
        } as const
    }

    private createStarHandler() {
        let isDestroyed = false
        this.starComponent.node.active = true

        const clear = () => {
            this.starComponent.node.active = false

            AudioUtils.playSound(CodeSoundKeys.COLLECT_STARS)
            const { x, y } = this.node.worldPosition
            this.node.scene.emit(GameplayEvents.ADD_STARS, {
                startAnimationAt: [x, y],
            } as AddStarsPayload)
        }

        const onDestroy = () => {
            isDestroyed = true
        }

        const cleanup = () => {
            isDestroyed = true
            this.starComponent.node.active = false
        }

        const hide = (signal?: Signal, immediate = false) => {
            if (immediate) {
                this.startUiOpacity.opacity = 0
                return
            }

            signal?.once(() => {
                this.startUiOpacity.opacity = 0
            })

            return F.allSettled([
                AnimUtils.runTweenAsync(
                    tween(this.startUiOpacity).to(0.1, { opacity: 0 }, { easing: 'cubicOut' }),
                    signal,
                ),
            ])
        }

        const show = (signal?: Signal, immediate = false) => {
            if (immediate) {
                this.startUiOpacity.opacity = 255
                return
            }

            signal?.once(() => {
                this.startUiOpacity.opacity = 255
            })

            return F.allSettled([
                AnimUtils.runTweenAsync(
                    tween(this.startUiOpacity).to(0.1, { opacity: 255 }, { easing: 'cubicOut' }),
                    signal,
                ),
            ])
        }

        const sw = (to: TileSpecialType) => {
            cleanup()
            this.special = this.createSpecialHandler(to)
        }

        return {
            type: TileSpecialTypes.star,
            get isDestroyed() {
                return isDestroyed
            },
            clear,
            cleanup,
            onDestroy,
            show,
            hide,
            switch: sw,
        } as const
    }
}
