import { _decorator, Component, tween, Vec3 } from 'cc'
import { ButtonPlusEvents } from '../../constants/ButtonPlusEvents'
import AnimUtils from '../../utils/AnimUtils'
import ComponentUtils from '../../utils/ComponentUtils'
import { ButtonPlus } from './ButtonPlus'

const { ccclass, property, disallowMultiple, requireComponent, menu } = _decorator

const {
    Utils: { Signal: S },
} = Ludex

@ccclass('ButtonPlusScale')
@disallowMultiple
@requireComponent(ButtonPlus)
@menu('Ludex/ButtonPlus/ButtonPlusScale')
export class ButtonPlusScale extends Component {
    @ComponentUtils.required()
    @property(ButtonPlus)
    readonly button!: ButtonPlus

    @property({
        group: { name: 'Normal', style: 'section' },
        displayName: 'Scale',
    })
    public normalScale = new Vec3(1, 1, 1)

    @property({
        group: { name: 'Normal', style: 'section' },
        displayName: 'Duration',
    })
    public normalDuration = 0.2

    @property({
        group: { name: 'Hover', style: 'section' },
        displayName: 'Scale',
    })
    public hoverScale = new Vec3(1.1, 1.1, 1.1)

    @property({
        group: { name: 'Hover', style: 'section' },
        displayName: 'Duration',
    })
    public hoverDuration = 0.2

    @property({
        group: { name: 'Click', style: 'section' },
        displayName: 'Scale',
    })
    public clickScale = new Vec3(0.9, 0.9, 0.9)

    @property({
        group: { name: 'Click', style: 'section' },
        displayName: 'Duration',
    })
    public clickDuration = 0.2

    protected animStateController = new S.SignalController()

    protected start(): void {
        this.button.node.setScale(this.normalScale)
    }

    protected onEnable(): void {
        this.button.node.on(ButtonPlusEvents.TRANSITION_DOWN, this.onTransitionDown, this)
        this.button.node.on(ButtonPlusEvents.TRANSITION_HOVER, this.onTransitionHover, this)
        this.button.node.on(ButtonPlusEvents.TRANSITION_RESET, this.onTransitionReset, this)
    }

    protected onDisable(): void {
        this.button.node.off(ButtonPlusEvents.TRANSITION_DOWN, this.onTransitionDown, this)
        this.button.node.off(ButtonPlusEvents.TRANSITION_HOVER, this.onTransitionHover, this)
        this.button.node.off(ButtonPlusEvents.TRANSITION_RESET, this.onTransitionReset, this)
    }

    private onTransitionDown() {
        AnimUtils.runTweenAsync(
            tween(this.button.node).to(this.clickDuration, { scale: this.clickScale }, { easing: 'cubicOut' }),
            this.animStateController.renew(),
        )
    }

    private onTransitionHover() {
        AnimUtils.runTweenAsync(
            tween(this.button.node).to(this.hoverDuration, { scale: this.hoverScale }, { easing: 'backOut' }),
            this.animStateController.renew(),
        )
    }

    private onTransitionReset() {
        AnimUtils.runTweenAsync(
            tween(this.button.node).to(this.normalDuration, { scale: this.normalScale }, { easing: 'cubicOut' }),
            this.animStateController.renew(),
        )
    }
}
