import LevelGravity from '../../../../constants/LevelGravity'

export default function rotateGravity(levelGravity: LevelGravity): LevelGravity {
    switch (levelGravity) {
        case LevelGravity.FallDown:
            return LevelGravity.PushRight
        case LevelGravity.FlyUp:
            return LevelGravity.PushLeft
        case LevelGravity.PushLeft:
            return LevelGravity.FallDown
        case LevelGravity.PushRight:
            return LevelGravity.FlyUp
        case LevelGravity.PutColumnMiddle:
            return LevelGravity.PutRowMiddle
        case LevelGravity.PutRowMiddle:
            return LevelGravity.PutColumnMiddle
    }
}
