import { Component, UITransform, _decorator } from 'cc'
import GameplayEvents from '../constants/GameEvents'

const { ccclass } = _decorator

@ccclass('GuideItemButton')
class GuideItemButton extends Component {
    private uiTransform!: UITransform

    public __preload(): void {
        this.uiTransform = this.node.getComponent(UITransform)!
    }

    public setTransform(x: number, y: number, width: number, height: number): void {
        this.node.setWorldPosition(x, y, 0)
        this.uiTransform.setContentSize(width, height)
    }

    public onClick(): void {
        this.node.scene.emit(GameplayEvents.ITEM_MASK_CLICKED)
    }
}

export default GuideItemButton
