import { _decorator, CCFloat, CCInteger } from 'cc'

const { ccclass, property } = _decorator

@ccclass('RayManagerProperties')
class RayManagerProperties {
    @property({ type: CCFloat })
    public thicknessStart = 10

    @property({ type: CCFloat })
    public thicknessEnd = 10

    @property({ type: CCInteger, min: 1 })
    public segments = 10

    @property({ type: CCFloat, min: 0, max: 1, slide: true, step: 0.01 })
    public jaggedness = 0.1
}

export default RayManagerProperties
