import { find } from 'cc'
import { Vibrate } from '../../components/vibrate/Vibrate'
import { ScreenKeysEnum } from '../../constants/ScreenKeys'
import { CodeSoundKeys } from '../../constants/SoundKeys'
import AudioUtils from '../../utils/AudioUtils'
import GeneralUtils from '../../utils/GeneralUtils'
import MatchUtils from '../../utils/MatchUtils'
import ScreenUtils from '../../utils/ScreenUtils'
import TournamentUtils from '../../utils/TournamentUtils'
import FailKeys from '../constants/FailKeys'
import GameplayEvents from '../constants/GameEvents'
import GameModes from '../constants/GameMode'
import type { StateManager } from '../manager/StateManager'
import BaseState from './BaseState'

const { analytics, storage } = codex
const {
    Match: { Modes },
} = Ludex

class FailState extends BaseState {
    constructor(manager: StateManager, failKey?: FailKey) {
        super(manager)

        AudioUtils.stopAllSounds()
        this.vibrateDevice()
        this.pauseCountdown()

        this.updateMatchCustomData()
        this.handleGameOver(failKey)

        this.logLevelFailAnalytics()
    }

    private pauseCountdown(): void {
        this.game?.uiManager.pauseTime()
    }

    private vibrateDevice(): void {
        find('Vibrate')?.getComponent(Vibrate)?.vibrate(500)
    }

    private handleGameOver(failKey?: FailKey): void {
        const mode = MatchUtils.getMatchMode()

        if (mode === Modes.SINGLE || mode === GameModes.CUSTOM) {
            this.handleSingleGameOver(failKey)
        } else if (mode === Modes.TOURNAMENT) {
            this.handleTournamentGameOver()
        } else if (mode === Modes.CHALLENGE_FRIEND) {
            this.handleChallengeGameOver()
        } else {
            this.handleSingleGameOver(failKey)
        }
    }

    private async handleSingleGameOver(failKey?: FailKey): Promise<void> {
        if (!this.game) return

        const failScreenPayload = await this.openFailScreenAndWaitForClose(failKey)
        const { customMode: openCustomGameScreen, rescue } = failScreenPayload.screenData ?? {}
        if (!openCustomGameScreen) {
            if (rescue) {
                this.game.node.scene.emit(GameplayEvents.SHOW_UI)
                this.game.node.scene.emit(GameplayEvents.RESCUE_START)
            } else {
                this.game.node.scene.emit(GameplayEvents.GAME_REPLAY)
            }
            return
        }

        const customScreenPayload = await ScreenUtils.openScreenAndWaitForClose(ScreenKeysEnum.CUSTOM_GAME_SCREEN)
        const { customMode } = customScreenPayload.screenData ?? {}

        const forceMode = customMode ? GameModes.CUSTOM : Modes.SINGLE

        this.game.node.scene.emit(GameplayEvents.GAME_REPLAY, forceMode)
    }

    private async handleTournamentGameOver(): Promise<void> {
        if (!this.game) return

        const { tournamentId } = MatchUtils.getMatchCustomData() ?? {}
        if (!tournamentId) return

        const isTournamentExpired = TournamentUtils.validateTournamentExpired(tournamentId)

        if (isTournamentExpired) {
            storage.setStorageData('tournament', 'animateTournamentLeader', false)

            const screenDuration = 2
            ScreenUtils.showNotificationScreenWithLocalizedKey('error.tourEnded', screenDuration)

            this.game?.scheduleOnce(() => {
                this.game?.node.scene.emit(GameplayEvents.GAME_EXIT)
            }, screenDuration)

            return
        }

        GeneralUtils.setBlockInput(true)

        try {
            const previousScore = storage.getStorageData('tournament', 'previousScore') ?? 0
            const currentScore = this.game.scoreManager.getCurrentScore()

            const isAnimateTournament = currentScore > previousScore

            if (isAnimateTournament) {
                const { tournamentId } = MatchUtils.getMatchCustomData() ?? {}
                if (!tournamentId) return

                TournamentUtils.saveTournamentProgress(this.data.level ?? 1, currentScore, tournamentId)
            }

            storage.setStorageData('tournament', 'animateTournamentLeader', isAnimateTournament)

            await MatchUtils.finishMatchAsync()
        } catch (error) {
            console.warn(error)
        }

        GeneralUtils.setBlockInput(false)

        this.game?.node.scene.emit(GameplayEvents.GAME_EXIT)
    }

    private async handleChallengeGameOver(): Promise<void> {
        if (!this.game) return

        GeneralUtils.setBlockInput(true)

        try {
            const { challengeData } = MatchUtils.getMatchCustomData() ?? {}
            await MatchUtils.finishMatchAsync({ extraData: { challengeData } })
        } catch (error) {
            console.warn(error)
        }

        GeneralUtils.setBlockInput(false)

        this.game.node.scene.emit(GameplayEvents.SHOW_CHALLENGE_RESULTS)
    }

    private async openFailScreenAndWaitForClose(failKey?: FailKey): Promise<SwitchScreenPayload> {
        const { level } = this.data

        if (failKey === FailKeys.TIME) AudioUtils.playSound(CodeSoundKeys.LEVEL_FAIL_TIMEOUT)

        AudioUtils.playSound(CodeSoundKeys.LEVEL_FAIL)

        return ScreenUtils.openScreenAndWaitForClose(ScreenKeysEnum.RESCUES_SCREEN, {
            level,
            failKey,
        })
    }

    private updateMatchCustomData(): void {
        if (!this.game) return

        const { level = 1 } = this.data
        const score = this.game?.scoreManager.getCurrentScore()

        MatchUtils.updateMatchStateAfterLevelFinish(level, score)
    }

    private logLevelFailAnalytics(): void {
        if (!this.game) return

        const { level = 0 } = this.data
        const score = this.game.scoreManager.getCurrentScore()
        const mode = MatchUtils.getMatchMode() || undefined

        analytics?.levelFail(level, score, mode)
    }

    public initialize(): void {
        const state = this.manager.createInitializeState(this.manager)
        this.manager.change(state)
    }

    public ready(): void {
        // Don't allow ready in fail state
    }

    public playing(): void {
        const state = this.manager.createPlayingState(this.manager)
        this.manager.change(state)
    }

    public fail(): void {
        // This is fail state, do nothing
    }

    public finish(): void {
        const state = this.manager.createFinishState(this.manager)
        this.manager.change(state)
    }

    public update(): void {
        //
    }
}

export default FailState
