import { _decorator, CCFloat, Node as CCNode, CCString, Component, Sprite, SpriteFrame, tween, Vec3 } from 'cc'
import AnimUtils from '../../../../../utils/AnimUtils'
import ComponentUtils from '../../../../../utils/ComponentUtils'
import type AnimAbortSignal from '../../../../../utils/common/AnimAbortSignal'
import ResourceUtils from '../../../../../utils/ResourceUtils'
import { JourneyLevelAvatar } from '../journey-level-avatar/JourneyLevelAvatar'
import { JourneyProgressBar } from '../journey-progress-bar/JourneyProgressBar'

const { ccclass, property } = _decorator

@ccclass('ProgressConfiguration')
export class ProgressConfiguration {
    @property(SpriteFrame)
    public avatar!: SpriteFrame

    @property(CCString)
    public themeId!: string
}

@ccclass('JourneyProgressAnimator')
export class JourneyProgressAnimator extends Component {
    @ComponentUtils.required()
    @property(JourneyProgressBar)
    private readonly progressBar!: JourneyProgressBar

    @ComponentUtils.required()
    @property(CCNode)
    private readonly currentLevelAvatarNode!: CCNode

    @ComponentUtils.required()
    @property(Sprite)
    private readonly currentLevelAvatar!: Sprite

    @ComponentUtils.required()
    @property(JourneyLevelAvatar)
    private readonly nextLevelAvatar!: JourneyLevelAvatar

    @property({
        type: CCFloat,
        displayName: 'Next Level Y Offset on Animation',
        group: 'Unlocked Animation',
    })
    private readonly nextLevelYOffset: number = 0

    private originalNextLevelAvatarPosition!: Vec3

    private isAnimating = false

    public setRange(current: number, total: number): void {
        this.progressBar.setRange(current, total)
    }

    public async animateTo(current: number, signal: AnimAbortSignal): Promise<void> {
        if (this.isAnimating) {
            console.warn('JourneyProgressAnimator: animation already in progress')
            return Promise.resolve()
        }

        this.isAnimating = true
        if (current !== this.progressBar.current) {
            await this.progressBar.animateTo({
                nextCurr: current,
                nextTotal: this.progressBar.total,
                signal,
            })
        }

        this.isAnimating = false
    }

    public async animateToNextLevel({
        nextCurr,
        nextTotal,
        mapsPassed,
        signal,
    }: JourneyProgressLevelUpPayload): Promise<void> {
        if (this.isAnimating) {
            console.warn('JourneyProgressAnimator: animation already in progress')
            return
        }

        this.isAnimating = true

        const numberOfProgressBar = mapsPassed.length - 2

        if (numberOfProgressBar === 0) {
            await this.progressBar.animateTo({ nextCurr, nextTotal, signal })
            this.isAnimating = false
            return
        }

        await this.animateTargetAvaToCenter(signal)

        const onProgressBarPassed = () => {
            if (currThemeIndex === 1) {
                this.nextLevelAvatar.playUnlockAnimation()
                currThemeIndex += 1
                return
            }

            if (currThemeIndex >= mapsPassed.length) {
                throw new Error('JourneyProgressAnimator: currThemeIndex out of bounds')
            }

            this.nextLevelAvatar.playFlashAnimation(signal)
            this.setNextLevelAvatar((<(typeof mapsPassed)[number]>mapsPassed[currThemeIndex]).theme)
            currThemeIndex += 1
        }

        let currThemeIndex = 1
        await this.progressBar.animateTo({
            signal,
            nextCurr,
            nextTotal,
            numberOfProgressBar,
            onProgressBarPassed,
        })

        await this.animateTargetAvaToPrev(signal)

        const nextTheme = (<(typeof mapsPassed)[number]>mapsPassed[mapsPassed.length - 1]).theme
        const currentTheme = (<(typeof mapsPassed)[number]>mapsPassed[mapsPassed.length - 2]).theme
        this.setCurrentLevelAvatar(currentTheme)

        this.nextLevelAvatar.hideImmediately()
        this.moveTargetAvaToOriginal()
        this.nextLevelAvatar.avatarSprite = await this.getSpriteFrameByThemeId(nextTheme)
        this.nextLevelAvatar.playSpawnAnimation()

        this.isAnimating = false
    }

    private async animateTargetAvaToCenter(signal: AnimAbortSignal): Promise<void> {
        const targetPos = new Vec3(
            0,
            this.nextLevelYOffset + this.nextLevelAvatar.node.position.y,
            this.nextLevelAvatar.node.position.z,
        )

        await AnimUtils.runTweenAsync(
            tween(this.nextLevelAvatar.node).to(
                1,
                {
                    position: targetPos,
                },
                {
                    easing: 'cubicInOut',
                },
            ),
            signal,
        )

        //? in case the animation was aborted, set final values
        this.nextLevelAvatar.node.position = targetPos
    }

    private moveTargetAvaToOriginal(): void {
        this.nextLevelAvatar.node.position = this.originalNextLevelAvatarPosition.clone()
    }

    private async animateTargetAvaToPrev(signal: AnimAbortSignal): Promise<void> {
        const targetPos = this.currentLevelAvatarNode.position.clone()

        await AnimUtils.runTweenAsync(
            tween(this.nextLevelAvatar.node).to(
                1,
                {
                    position: targetPos,
                },
                {
                    easing: 'cubicInOut',
                },
            ),
            signal,
        )

        //? in case the animation was aborted, set final values
        this.nextLevelAvatar.node.position = targetPos
    }

    private async getSpriteFrameByThemeId(themeId: ThemeKeys): Promise<SpriteFrame | null> {
        const spriteFrame = ResourceUtils.loadThemeThumbnailAsync(themeId)

        return spriteFrame
    }

    @ComponentUtils.onlyRunInLifeCycleAsync
    private async loadAndSetAvatar(themeId: ThemeKeys, avatar: JourneyLevelAvatar | Sprite) {
        const spriteFrame = await this.getSpriteFrameByThemeId(themeId)

        if (!spriteFrame) {
            console.warn(`JourneyProgressAnimator: ${themeId} is null`)
            return
        }

        this.setAvatar(spriteFrame, avatar)
    }

    @ComponentUtils.onlyRunInLifeCycle
    private setAvatar(spriteFrame: SpriteFrame, avatar: JourneyLevelAvatar | Sprite) {
        if (avatar instanceof JourneyLevelAvatar) {
            avatar.avatarSprite = spriteFrame
            return
        }

        avatar.spriteFrame = spriteFrame
    }

    public setCurrentLevelAvatar(themeId: ThemeKeys) {
        this.loadAndSetAvatar(themeId, this.currentLevelAvatar)
    }

    public setNextLevelAvatar(themeId: ThemeKeys) {
        this.loadAndSetAvatar(themeId, this.nextLevelAvatar)
    }

    public onLoad(): void {
        ComponentUtils.validateRequiredProperties(this, JourneyProgressAnimator)
        this.originalNextLevelAvatarPosition = this.nextLevelAvatar.node.position.clone()
    }
}
