import OverlayUtils from 'db://assets/utils/OverlayUtils'
import { Scene } from 'cc'
import LayoutUtils from '../../utils/LayoutUtils'

type OverlayView = FBInstant.OverlayView

const {
    Utils: { Time: T, Object: O },
} = Ludex

type AvatarPayload = {
    playerId: string
    worldX: number
    worldY: number
    avatarWidth?: number
    customCss?: string
}

const DEFAULT_AVATAR_WIDTH = 55
//? Make the div bigger than the avatar to avoid scrollbar
const DIV_SCALE_FACTOR = 1.2
const LANDSCAPE_SCALE_FACTOR = 2

const DEFAULT_XML_PATH = 'overlays/avatar/avatar-overlay.xml'
const DEFAULT_CSS_PATH = 'overlays/avatar/avatar-overlay-styles.css'

class PlayerAvatarOverlay {
    private mainDiv$!: HTMLDivElement
    private overlayView: OverlayView | undefined
    public scene!: Scene

    private currentPlayerId: string | undefined

    constructor(scene: Scene) {
        this.scene = scene
        this.createMainDiv$()
    }

    private createMainDiv$ = (): void => {
        this.mainDiv$ = document.createElement('div')

        O.assign(this.mainDiv$.style, {
            position: 'absolute',
            transform: 'translate(-50%, -50%)',
            scrollbarWidth: 'none',
            alignItems: 'center',
            overflow: 'hidden',
            backgroundColor: 'transparent',
            opacity: '0',
            transition: 'opacity 0.3s ease-in-out',
            pointerEvents: 'none',
            display: 'none',
        })

        this.updateContainerWidth$(DEFAULT_AVATAR_WIDTH)
        document.body.appendChild(this.mainDiv$)
    }

    public moveOverlayTo(worldX: number, worldY: number): void {
        const scaleFactor = OverlayUtils.getGameScaleFactor()
        Object.assign(this.mainDiv$.style, {
            top: `calc(100vh - ${worldY * scaleFactor}px)`,
            left: `calc(${worldX * scaleFactor}px)`,
        })
    }

    public async updateOverlay(payload: AvatarPayload): Promise<boolean> {
        try {
            const {
                playerId,
                worldX,
                worldY,
                avatarWidth = DEFAULT_AVATAR_WIDTH,
                customCss = DEFAULT_CSS_PATH,
            } = payload

            if (!this.isPlayerIdValid$(playerId)) return false

            this.updateContainerWidth$(avatarWidth)
            this.moveOverlayTo(worldX, worldY)

            if (this.currentPlayerId === playerId) {
                await this.showOverlay$()
                return true
            }

            this.overlayView = await OverlayUtils.createOverlayViewAsync(
                DEFAULT_XML_PATH,
                this.mainDiv$,
                this.getIframeCss$(),
                customCss,
                {
                    playerId: playerId,
                },
            )

            this.currentPlayerId = playerId
            await this.showOverlay$()
        } catch (error) {
            console.error('Error creating overlay view:', error)
            return false
        }

        return true
    }

    private isPlayerIdValid$ = (playerId: string): boolean => {
        const validateRegEx = /^\d+$/

        return validateRegEx.test(playerId)
    }

    private showOverlay$ = async (): Promise<void> => {
        if (!this.overlayView) return

        O.assign(this.mainDiv$.style, {
            opacity: '0',
            display: 'none',
            pointerEvents: 'none',
        })

        await this.overlayView.showAsync()

        // Use the fadeIn utility method for smooth transition
        await this.fadeIn()
    }

    public async hideOverlay(): Promise<void> {
        if (!this.overlayView) return

        // Start fade-out effect
        O.assign(this.mainDiv$.style, {
            opacity: '0',
            pointerEvents: 'none',
        })

        try {
            // ? Wait for fade-out transition to complete before hiding
            await T.sleepAsync(300)
            this.mainDiv$.style.display = 'none'
            await this.overlayView.dismissAsync()
        } catch (error) {
            console.error('Hide overlay failed:', error)
        }
    }

    /**
     * Utility method to fade in the main div with custom duration and easing
     */
    public async fadeIn(duration: number = 300, easing: string = 'ease-in-out'): Promise<void> {
        O.assign(this.mainDiv$.style, {
            transition: `opacity ${duration}ms ${easing}`,
            display: 'block',
        })

        requestAnimationFrame(() => {
            O.assign(this.mainDiv$.style, {
                opacity: '1',
                pointerEvents: 'auto',
            })
        })

        await T.sleepAsync(duration)
    }

    /**
     * Utility method to fade out the main div with custom duration and easing
     */
    public async fadeOut(duration: number = 300, easing: string = 'ease-in-out'): Promise<void> {
        O.assign(this.mainDiv$.style, {
            transition: `opacity ${duration}ms ${easing}`,
            opacity: '0',
            pointerEvents: 'none',
        })

        await T.sleepAsync(duration)
        this.mainDiv$.style.display = 'none'
    }

    private updateContainerWidth$ = (avatarWidth: number): void => {
        const finalScaleFactor = LayoutUtils.isLandscape()
            ? LANDSCAPE_SCALE_FACTOR * DIV_SCALE_FACTOR
            : DIV_SCALE_FACTOR
        const size = `${avatarWidth * finalScaleFactor * OverlayUtils.getGameScaleFactor()}px`

        O.assign(this.mainDiv$.style, {
            width: size,
            height: size,
        })
    }

    private getIframeCss$ = (): string => {
        const widthString = 'width: 100%;'
        const heightString = 'height: 100%;'
        const overflowString = 'overflow: hidden;'
        const backgroundColorString = 'background-color: transparent;'
        const borderWidthString = 'border-width: 0px;'
        const alignItemsString = 'align-items: center;'

        return `${widthString} ${heightString} ${overflowString} ${backgroundColorString} ${borderWidthString} ${alignItemsString}`
    }
}

export default PlayerAvatarOverlay
