import { Component, Node, Sprite, SpriteFrame, Vec3, _decorator, tween } from 'cc'
import { CodeSoundKeys } from '../../constants/SoundKeys'
import { ParticleSystem } from '../../particles/ParticleSystem'
import AnimUtils from '../../utils/AnimUtils'
import AudioUtils from '../../utils/AudioUtils'
import ComponentUtils from '../../utils/ComponentUtils'

const { ccclass, property } = _decorator

const ANGLE = 30
const DISTANCE = 300
const DELAY = 0.15

@ccclass('GetRewardsEffect')
export class GetRewardsEffect extends Component {
    @property(SpriteFrame)
    private hint!: SpriteFrame

    @property(SpriteFrame)
    private thunder!: SpriteFrame

    @property(SpriteFrame)
    private storm!: SpriteFrame

    @property(SpriteFrame)
    private pliers!: SpriteFrame

    @property(ParticleSystem)
    private particles: ParticleSystem[] = []

    public __preload() {
        this.validateRequiredComponents()
    }

    private validateRequiredComponents() {
        ComponentUtils.lazyValidateProperties(this)
    }

    public async playAnimation(items: ItemKey[], onItemFinish: () => void): Promise<void> {
        const totalAngle = (items.length - 1) * ANGLE
        const startAngle = -totalAngle / 2 + 90

        await Promise.all(
            items.map((item, index) => {
                const sprite = this.createSprite(item)
                const angle = startAngle + index * ANGLE
                const delay = index * DELAY

                sprite.node.position = new Vec3(
                    Math.cos(angle * (Math.PI / 180)) * DISTANCE,
                    Math.sin(angle * (Math.PI / 180)) * DISTANCE,
                    0,
                )

                const nodeTween = tween(sprite.node)
                nodeTween.delay(delay)
                nodeTween.to(0.6, { position: Vec3.ZERO, scale: new Vec3(0.7, 0.7, 0.7) }, { easing: 'backIn' })

                return AnimUtils.runTweenAsync(nodeTween).then(() => {
                    onItemFinish()
                    AudioUtils.playSound(CodeSoundKeys.COLLECT_STARS)
                    this.playExplodeEffect()
                    sprite.node.destroy()
                })
            }),
        )
    }

    private playExplodeEffect(): void {
        for (const particle of this.particles) {
            particle.stop()
            particle.play()
        }
    }

    private createSprite(item: ItemKey): Sprite {
        const spriteFrame = this.getSpriteFrame(item)
        const sprite = new Node().addComponent(Sprite)

        sprite.spriteFrame = spriteFrame
        sprite.node.parent = this.node
        sprite.node.scale = new Vec3(1.5, 1.5, 1.5)

        return sprite
    }

    private getSpriteFrame(item: ItemKey) {
        switch (item) {
            case 'hint':
                return this.hint
            case 'thunder':
                return this.thunder
            case 'storm':
                return this.storm
            case 'pliers':
                return this.pliers
            default:
                return null
        }
    }
}
