import { CCFloat, CCInteger, Layout, type Tween, UITransform, Widget, _decorator, tween } from 'cc'
import { EDITOR_NOT_IN_PREVIEW } from 'cc/env'
import AnimUtils from '../../utils/AnimUtils'

const { ccclass, property } = _decorator

interface IAutoLayoutWrappee {
    wrapeeContainer: UITransform
    wrapee: UITransform
}

@ccclass('AutoLayoutWrappee')
class AutoLayoutWrappee implements IAutoLayoutWrappee {
    @property({ type: UITransform })
    public wrapeeContainer!: UITransform

    @property({ type: UITransform })
    public wrapee!: UITransform
}

@ccclass('AutoLayout')
export default class AutoLayout extends Layout {
    @property({ displayName: 'Use Auto Layout' })
    private useAutoLayout = true

    @property({
        type: [AutoLayoutWrappee],
        displayName: 'Children',
        visible(this: AutoLayout) {
            return this.useAutoLayout
        },
    })
    private children: AutoLayoutWrappee[] = []

    private mappedChildren: (IAutoLayoutWrappee & {
        wrapeeContainerWidget: Widget
    })[] = []

    @property({
        visible: false,
        serializable: true,
    })
    private selected: number = 0

    @property({
        type: CCInteger,
        displayName: 'Selected Index',
        visible(this: AutoLayout) {
            return this.useAutoLayout
        },
    })
    get selectedIndex(): number {
        return this.selected
    }
    set selectedIndex(value: number) {
        this.selected = value
        this.updateLayout(true)
    }

    @property({
        type: CCFloat,
        displayName: 'Deselected Icon Width',
        group: 'Edge Icons',
        visible(this: AutoLayout) {
            return this.useAutoLayout
        },
    })
    get deselectedIconWidth(): number {
        return this.deselectedIconW
    }

    set deselectedIconWidth(value: number) {
        this.deselectedIconW = value
        this.updateLayout(true)
    }

    @property({
        visible: false,
        serializable: true,
    })
    private deselectedIconW = 0

    private tweens: Tween<unknown>[] = []

    public __preload() {
        this.mappedChildren = this.children.map((child: IAutoLayoutWrappee) => {
            const widget = child.wrapeeContainer.getComponent(Widget)

            if (!widget) {
                throw new Error('AutoLayout: Widget component not found')
            }

            return {
                ...child,
                wrapeeContainerWidget: widget,
            }
        })
    }

    public override updateLayout(force = false) {
        if (this._layoutDirty || force) {
            if (EDITOR_NOT_IN_PREVIEW) {
                this.updateChildrenAnchorsEditor()
            } else {
                this.updateChildrenAnchors()
            }

            // ! Cocos engine doesn't update the alignment of the children when the layout is dirty
            for (const child of this.mappedChildren) {
                child.wrapeeContainerWidget.updateAlignment()
            }
        }

        super.updateLayout(force)
    }

    public getSelectedWorldX(): number {
        const wrapeeContainer = this.mappedChildren[this.selectedIndex].wrapeeContainer

        if (this.selectedIndex === 0 || this.selectedIndex === this.children.length - 1) {
            const { xMin } = wrapeeContainer.getBoundingBoxToWorld()
            const anchorX = this.getAnchorX(this.selectedIndex)
            return xMin + anchorX * wrapeeContainer.width
        }

        return wrapeeContainer.getBoundingBoxToWorld().center.x
    }

    private getAnchorX(index: number): number {
        if (index < this.selectedIndex) return 0
        if (index > this.selectedIndex) return 1

        if (index === 0) {
            //   <left-padding> [i] <space> [   i   ] <space> [i] <right-padding>
            // { <left-padding> [i] <space> [i } / i] <space> [i] <right-padding> <- get anchor of content between {...}

            const containerWidth = this.mappedChildren[index].wrapeeContainer.width
            const halfIcon = this.deselectedIconWidth / 2
            const leftPadding = this.paddingLeft
            const rightPadding = containerWidth - halfIcon + this.spacingX

            const totalSpace = leftPadding + containerWidth + rightPadding
            const midPoint = totalSpace / 2
            const offset = midPoint - leftPadding
            const newAnchor = offset / containerWidth
            return newAnchor
        }

        if (index === this.children.length - 1) {
            // <left-padding> [i] <space> [   i   ] <space> [i] <right-padding>
            // <left-padding> [i] <space> [i / { i] <space> [i] <right-padding> } <- get anchor of content between {...}

            const containerWidth = this.mappedChildren[index].wrapeeContainer.width
            const halfIcon = this.deselectedIconWidth / 2
            const leftPadding = containerWidth - halfIcon + this.spacingX
            const rightPadding = this.paddingRight

            const totalSpace = leftPadding + containerWidth + rightPadding
            const midPoint = totalSpace / 2
            const offset = midPoint - leftPadding
            const newAnchor = offset / containerWidth
            return newAnchor
        }

        return 0.5
    }

    private async updateChildrenAnchors(): Promise<void> {
        for (const tween of this.tweens) {
            tween.stop()
        }

        this.tweens = []

        await Promise.all([
            this.children
                .flatMap((child, index) => this.getChildrenAnchorsTweens(child, this.getAnchorX(index)))
                .map((t) => AnimUtils.runTweenAsync(t)),
        ])
    }

    private updateChildrenAnchorsEditor(): void {
        this.children.forEach((child, index) => {
            const anchor = this.getAnchorX(index)
            child.wrapee.setAnchorPoint(anchor, child.wrapee.anchorY)
            child.wrapeeContainer.setAnchorPoint(anchor, child.wrapeeContainer.anchorY)
        })
    }

    private getChildrenAnchorsTweens(child: AutoLayoutWrappee, to: number): Tween<unknown>[] {
        return [
            tween(child.wrapee)
                .to(0.3, { anchorX: to }, { easing: 'sineInOut' })
                .call(() => {
                    child.wrapee.setAnchorPoint(to, child.wrapee.anchorY)
                }),
            tween(child.wrapeeContainer)
                .to(0.3, { anchorX: to }, { easing: 'sineInOut' })
                .call(() => {
                    child.wrapeeContainer.setAnchorPoint(to, child.wrapeeContainer.anchorY)
                }),
        ]
    }
}
