import { Component, UITransform, Vec2, Vec3, _decorator } from 'cc'
const { ccclass } = _decorator

@ccclass('Line')
export class Line extends Component {
    private uiTransform!: UITransform

    public onLoad(): void {
        this.uiTransform = this.getComponent<UITransform>(UITransform)!
    }

    public setPoints(a: Vec2Like, b: Vec2Like): void {
        this.node.setWorldPosition((a.x + b.x) / 2, (a.y + b.y) / 2, 0)

        this.uiTransform.width = Vec2.distance(a, b) + 10
        this.uiTransform.width /= this.node.worldScale.x

        const angleRadians = Math.atan2(b.y - a.y, b.x - a.x)
        const angleDegree = (angleRadians * 180) / Math.PI

        this.node.eulerAngles = new Vec3(0, 0, angleDegree)
    }
}
