import { Component, type Node, Prefab, _decorator, director, find, instantiate } from 'cc'
import { EDITOR_NOT_IN_PREVIEW } from 'cc/env'
import ComponentUtils from '../../utils/ComponentUtils'
import { LoadingFullScreen } from './LoadingFullScreen'
const { ccclass, property } = _decorator

@ccclass('Loading')
export class Loading extends Component {
    @property(Prefab)
    private loadingPrefab!: Prefab
    private loadingFullScreen!: LoadingFullScreen

    private loadingNode: Node | null = null

    public __preload(): void {
        this.validateRequiredComponents()
    }

    private validateRequiredComponents() {
        ComponentUtils.lazyValidateProperties(this)
    }

    public onLoad() {
        this.makePersistent()
    }

    private makePersistent() {
        if (EDITOR_NOT_IN_PREVIEW) return
        if (director.isPersistRootNode(this.node)) return

        director.addPersistRootNode(this.node)
    }

    public createLoadingFullScreen() {
        const canvas = find('Canvas')
        if (!canvas) return

        if (canvas.getChildByName('Loading')) return

        if (!this.loadingNode) {
            this.loadingNode = instantiate(this.loadingPrefab)
        }

        const node = instantiate._clone(this.loadingNode)
        this.loadingFullScreen = node.getComponent(LoadingFullScreen)!

        find('Canvas')?.addChild(node)
    }

    public async show(sceneName: string): Promise<void> {
        this.createLoadingFullScreen()
        await this.loadingFullScreen.show(sceneName)
    }

    public async hide(sceneName: string): Promise<void> {
        if (!this.loadingFullScreen) return
        await this.loadingFullScreen.hide(sceneName)
    }
}
