import { pseudoRandom } from 'cc'

const identity = (i: number) => i

export default function* pseudoRandomIterable(
    size: number,
    startingSeed: number,
    rng: (seed: number) => number = identity,
) {
    let s = size
    let nseed = startingSeed

    if (s <= 0) {
        s = Number.POSITIVE_INFINITY
    }

    for (let i = 0; i < s; i++) {
        yield rng(nseed)
        // ? pseudoRandom uses LCGs and LCGs are bad at small numbers
        // but pseudoRandom generates numbers between 0 and 1
        nseed = pseudoRandom(nseed) * 1000000000
    }
}
