import { CodeSoundKeys } from '../../../constants/SoundKeys'
import AudioUtils from '../../../utils/AudioUtils'
import ItemKeys from '../../constants/ItemKeys'
import type EffectManager from '../../manager/EffectManager'
import type Board from '../board'
import TileSpecialTypes from '../board/constants/TileSpecialTypes'
import { getRandomTile } from '../board/utils/get'
import withItemAnalytics from './withItemAnalytics'

const usePliersItemLogic = (board: Board, effectManager: EffectManager) => {
    const tiles = board.getBombTiles()
    const tile = getRandomTile(tiles)
    const position = tile.node.worldPosition

    effectManager.createDefuseExplosionAt(position.x, position.y)
    AudioUtils.playSound(CodeSoundKeys.BOMB_DEFUSE)
    tile.special?.switch(TileSpecialTypes.none)
}

const usePliersItem = withItemAnalytics(ItemKeys.PLIERS, usePliersItemLogic)

export default usePliersItem
