import TileSpecialTypes from '../../constants/TileSpecialTypes'
import Grid from '../../grid'
import type Tile from '../../tile'
import getUncleared from './getUncleared'

function getBombTiles(grid: Grid<Tile>): Tile[]
function getBombTiles(tiles: ReadonlyArray<Tile>): Tile[]

function getBombTiles(gridOrTiles: Grid<Tile> | ReadonlyArray<Tile>): Tile[] {
    const arr = gridOrTiles instanceof Grid ? gridOrTiles.asArray() : gridOrTiles
    const uncleared = getUncleared(arr)
    return uncleared.filter((tile) => tile.special?.type === TileSpecialTypes.bomb)
}

export default getBombTiles
