import { Button, Component, Node, UIOpacity, _decorator, find } from 'cc'
import { CoinContainer } from 'db://assets/scenes/common/coin-container/CoinContainer'
import { Localize } from '../../../../../components/text/Localize'
import ComponentUtils from '../../../../../utils/ComponentUtils'
import { CustomScoreContainer } from '../custom/CustomScoreContainer'
import type { StatusConfig } from '../GameSceneLayout'
import { ScoreContainer } from './ScoreContainer'
const { ccclass, property } = _decorator

@ccclass('Header')
export class Header extends Component {
    @property(Localize)
    private levelTextLocalizer!: Localize

    @property(Node)
    private settingNode!: Node

    @property(Node)
    private homeNode!: Node

    @property(Node)
    private leftNode!: Node

    @property(Node)
    private coinNode!: Node

    @property(ScoreContainer)
    public scoreContainer!: ScoreContainer

    @property(CoinContainer)
    public coinContainer!: CoinContainer

    @property(CustomScoreContainer)
    public customScoreContainer!: CustomScoreContainer

    private uiOpacity!: UIOpacity

    public onLoad() {
        this.uiOpacity = this.node.getComponent(UIOpacity)!

        this.validateRequiredComponents()
    }

    private validateRequiredComponents() {
        ComponentUtils.lazyValidateProperties(this)
    }

    public updateStatus(config: StatusConfig) {
        this.scoreContainer.updateStatus(config)

        if (config.coins !== undefined) this.coinContainer.setCoins(config.coins)
        if (config.level !== undefined) this.setLevelText(config.level)
    }

    public highlightCoinContainer() {
        const inputBlocker = find('Canvas/Screens')
        if (!inputBlocker) return

        const coinPosition = this.coinNode.worldPosition.clone()

        this.coinNode.parent = inputBlocker
        this.coinNode.worldPosition = coinPosition
    }

    public removeCoinContainerHighlight() {
        const coinPosition = this.coinNode.worldPosition.clone()

        this.coinNode.parent = this.leftNode
        this.coinNode.worldPosition = coinPosition
    }

    public setLevelText(level: number) {
        this.levelTextLocalizer.setParams([level.toString()])
    }

    public setButtonsInteractable(interactable = true): void {
        this.settingNode.getComponent(Button)!.interactable = interactable
        this.homeNode.getComponent(Button)!.interactable = interactable
    }

    @ComponentUtils.onlyRunInLifeCycle
    public setActive(active: boolean): void {
        this.uiOpacity.opacity = active ? 255 : 0
    }
}
