const {
    Utils: { Browser: B },
} = Ludex
const { player } = codex

interface VibrationProfiles {
    default: VibratePattern
    [key: string]: VibratePattern
}

class VibrateUtilsCore {
    protected static inst$: VibrateUtilsCore
    protected static vibrationProfiles$: VibrationProfiles = {
        default: [100],
    }

    public static get inst() {
        if (!this.inst$) {
            this.inst$ = new this()
        }
        return this.inst$
    }

    public static addVibrationProfile(name: string, profile: VibratePattern): void {
        this.vibrationProfiles$[name] = profile
    }

    public static vibrate(profileName: string): void {
        if (!this.isVibrateEnable()) return

        let profile = this.vibrationProfiles$[profileName]
        if (!profile) {
            console.warn(`Vibration profile ${profileName} not found. Using default profile.`)
            profile = this.vibrationProfiles$.default
        }

        const isSuccess = B.vibrate(profile)

        if (!isSuccess) {
            if ('performHapticFeedbackAsync' in GameSDK.extra) {
                GameSDK.extra.performHapticFeedbackAsync()
                return
            }

            console.warn('Vibrate failed')
        }
    }

    public static isVibrateEnable(): boolean {
        return !!player.getPlayerSetting('vibrate')
    }
}

class VibrateUtils extends VibrateUtilsCore {
    // * Custom logic here
}

export default VibrateUtils
