function rotateVec2(vec: Vec2Like, boardWidth: number, boardHeight: number, revert = false): Vec2Like {
    const { x, y } = vec
    const newX = revert ? y : boardHeight - 1 - y
    const newY = revert ? boardWidth - 1 - x : x
    return { x: newX, y: newY }
}

export default function rotatePath(boardWidth: number, boardHeight: number, path: Path, revert = false): Path {
    const newPath: Path = []

    for (const line of path) {
        const { a, b } = line
        const newA = rotateVec2(a, boardWidth, boardHeight, revert)
        const newB = rotateVec2(b, boardWidth, boardHeight, revert)
        newPath.push({ a: newA, b: newB })
    }

    return newPath
}
