import Grid from '../../grid'
import type Tile from '../../tile'

function getCleared(grid: Grid<Tile>): Tile[]
function getCleared(tiles: ReadonlyArray<Tile>): Tile[]

function getCleared(gridOrTiles: Grid<Tile> | ReadonlyArray<Tile>): Tile[] {
    const arr = gridOrTiles instanceof Grid ? gridOrTiles.asArray() : gridOrTiles
    return arr.filter((tile) => tile.isHidden())
}

export default getCleared
