import { SpriteFrame } from 'cc'
import type AnimAbortSignal from './common/AnimAbortSignal'

const {
    Utils: { Image: I },
} = Ludex

const { frameCapture } = codex

type Signal = Ludex.Utils.Signal.Signal

class CacheUtilsCore {
    protected static inst$: CacheUtilsCore

    public static get inst() {
        if (!this.inst$) {
            this.inst$ = new this()
        }

        return this.inst$
    }

    protected static playerAvatarCache$: Map<string, SpriteFrame> = new Map()

    public static async getPlayerAvatar(
        url: string,
        playerId: string,
        size: number,
        signal?: AnimAbortSignal | Signal,
    ): Promise<SpriteFrame | undefined> {
        let avatar = this.playerAvatarCache$.get(`${playerId}:${size}`)
        if (!avatar) {
            avatar = await this.loadPlayerAvatar(url, playerId, size, signal)
        }
        return avatar
    }

    public static async loadPlayerAvatar(
        url: string,
        playerId: string,
        size: number,
        signal?: AnimAbortSignal | Signal,
    ): Promise<SpriteFrame | undefined> {
        const image = await this.loadImage(url, size, size, `${playerId}:${size}`)
        if (signal?.aborted || !image) return

        const spriteFrame = SpriteFrame.createWithImage(image)
        this.playerAvatarCache$.set(`${playerId}:${size}`, spriteFrame)

        return spriteFrame
    }

    public static isAvatarCached(playerId: string, size: number): boolean {
        return this.playerAvatarCache$.has(`${playerId}:${size}`)
    }

    protected static async loadImage(
        url: string,
        width: number,
        height: number,
        id: string,
    ): Promise<HTMLImageElement | null> {
        try {
            const data = await frameCapture.captureAsync({
                width,
                height,
                name: 'LeaderAvatar',
                renderOptions: {
                    [id]: {
                        name: id,
                        type: 'image',
                        depth: 0,
                        position: [0, 0],
                        size: [width, height],
                        image: url,
                    },
                },
            })
            if (!data) return null

            return await I.loadImage(data)
        } catch {
            return null
        }
    }
}

class CacheUtils extends CacheUtilsCore {
    // * Custom logic here
}

export default CacheUtils
