import { HorizontalTextAlignment, UITransform, game } from 'cc'
import GameEvents from '../../constants/GameEvents'
import LayoutUtils from '../../utils/LayoutUtils'
import GameplayEvents from '../constants/GameEvents'
import GuideKeys from '../constants/GuideKeys'
import type { Gameplay } from '../Gameplay'
import type GuidelinesManager from '../manager/GuidelinesManager'

class Guide02Timer {
    private gameplay: Gameplay
    private manager: GuidelinesManager

    constructor(manager: GuidelinesManager) {
        this.manager = manager
        this.gameplay = manager.gameplay
    }

    public async setup(): Promise<void> {
        const {
            guideUi: { guideMasks, guideInfoText, guideInfoTextLocalizer },
        } = this.manager

        await this.gameplay.handleShowUI()

        guideMasks.node.active = true

        guideInfoText.node.active = true
        guideInfoTextLocalizer.key = 'guide.timer'
        guideInfoText.horizontalAlign = HorizontalTextAlignment.CENTER
        guideInfoText.getComponent(UITransform)!.setAnchorPoint(0.5, 0.5)

        this.handleViewResized()

        await guideMasks.show()

        this.manager.node.scene.once(GameplayEvents.GUIDE_MASK_CLICKED, this.close, this)
        this.manager.node.scene.emit(GameplayEvents.PAUSE_TIME)

        game.on(GameEvents.VIEW_RESIZED, this.handleViewResized, this)
        game.on(GameEvents.ORIENTATION_CHANGED, this.handleViewResized, this)
    }

    private handleViewResized(): void {
        const {
            guideUi: { guideMasks, guideInfoText },
        } = this.manager
        const { clock } = this.gameplay.uiManager.currentLayout
        let { width, height } = clock.getComponent(UITransform)!.contentSize

        const scale = 1.5
        const clockPosition = clock.node.worldPosition
        const guideInfoTextY = LayoutUtils.isPortrait()
            ? clockPosition.y - height / 2 - 50
            : clockPosition.y + height / 2 + 70

        width *= scale
        height *= scale

        guideMasks.setOtherMaskTransform(clockPosition.x, clockPosition.y, width, height)
        guideInfoText.node.setWorldPosition(clockPosition.x, guideInfoTextY, 0)
    }

    private async close(): Promise<void> {
        const {
            guideUi: { guideInfoText, guideMasks },
        } = this.manager

        this.gameplay.comboManager.setEmitEvent(true)

        if (this.gameplay.stateManager.isPlaying()) {
            this.manager.node.scene.emit(GameplayEvents.RESUME_TIME)
        }

        await guideMasks.hide()

        guideInfoText.node.active = false
        this.manager.completeGuide(GuideKeys.TIMER)

        this.unlistenEvents()
    }

    public unlistenEvents(): void {
        game.off(GameEvents.VIEW_RESIZED, this.handleViewResized, this)
        game.off(GameEvents.ORIENTATION_CHANGED, this.handleViewResized, this)
    }
}

export default Guide02Timer
