import { CCFloat, Component, UITransform, Widget, _decorator } from 'cc'
import GameEvents from '../../constants/GameEvents'
import ComponentUtils from '../../utils/ComponentUtils'
import { Localize } from './Localize'

const { ccclass, property, executeInEditMode, disallowMultiple, requireComponent } = _decorator

@ccclass('LocalizedLayout')
@disallowMultiple
@executeInEditMode(true)
@requireComponent(UITransform)
export class LocalizedLayout extends Component {
    @property(Localize)
    readonly label?: Localize

    @ComponentUtils.required()
    @property(UITransform)
    public layout!: UITransform

    @property({ visible: false })
    private _horizontal: number = 0

    @property({ visible: false })
    private _vertical: number = 0

    @property({ type: CCFloat, group: 'Padding' })
    get horizontal(): number {
        return this._horizontal
    }

    set horizontal(value: number) {
        this._horizontal = value
        this.updateContentSize()
    }

    @property({ type: CCFloat, group: 'Padding' })
    get vertical(): number {
        return this._vertical
    }

    set vertical(value: number) {
        this._vertical = value
        this.updateContentSize()
    }

    protected __preload(): void {
        this.layout = this.getComponent(UITransform) as UITransform
        ComponentUtils.validateRequiredProperties(this)
    }

    protected start(): void {
        this.updateContentSize()
    }

    protected onEnable(): void {
        this.listenEvent()
    }

    protected onDisable(): void {
        this.unlistenEvent()
    }

    private listenEvent() {
        this.label?.node.on(GameEvents.LOCALIZE_APPLIED, this.updateContentSize)
    }

    private unlistenEvent() {
        this.label?.node.off(GameEvents.LOCALIZE_APPLIED, this.updateContentSize)
    }

    public updateContentSize = () => {
        if (this.label === undefined) return

        const size = this.label.getComponent(UITransform)?.contentSize
        if (size === undefined) return

        this.layout.setContentSize(size.width + this.horizontal, size.height + this.vertical)

        const widgets = this.node.getComponentsInChildren(Widget)

        for (const widget of widgets) {
            widget.updateAlignment()
        }
    }
}
