import { _decorator, CCBoolean, pseudoRandom } from 'cc'
import { isCurveTwoValues } from '../ParticleFunction'
import type { Particle } from '../ParticleSimulator'
import CurveRange from '../particle-editor/CurveRange'
import { ModuleRandSeed } from './ModuleRandSeed'

const { ccclass, property } = _decorator

const SIZE_MODULE_RAND_SEED = ModuleRandSeed.SIZE

@ccclass('SizeOvertimeModule')
export class SizeOvertimeModule {
    @property({ type: CCBoolean })
    private enable!: boolean

    @property(CCBoolean)
    private separateAxes = false

    @property({
        type: CurveRange,
        visible: function (this: SizeOvertimeModule): boolean {
            return !this.separateAxes
        },
    })
    private size = new CurveRange()

    @property({
        type: CurveRange,
        visible: function (this: SizeOvertimeModule): boolean {
            return this.separateAxes
        },
    })
    private x = new CurveRange()

    @property({
        type: CurveRange,
        visible: function (this: SizeOvertimeModule): boolean {
            return this.separateAxes
        },
    })
    private y = new CurveRange()

    constructor() {
        this.size.constant = 1
        this.x.constant = 1
        this.y.constant = 1
    }

    public update(particle: Particle): void {
        if (!this.enable) return

        const currLifetime = 1 - particle.remainingLifetime / particle.startLifetime
        const seed = particle.randomSeed + SIZE_MODULE_RAND_SEED

        if (!this.separateAxes) {
            const rand = isCurveTwoValues(this.size) ? pseudoRandom(seed) : 0
            const size = this.size.evaluate(currLifetime, rand)

            particle.size.multiplyScalar(size)
        } else {
            const randX = isCurveTwoValues(this.x) ? pseudoRandom(seed) : 0
            const randY = isCurveTwoValues(this.y) ? pseudoRandom(seed) : 0

            particle.size.x = particle.startSize.x * this.x.evaluate(currLifetime, randX)
            particle.size.y = particle.startSize.y * this.y.evaluate(currLifetime, randY)
        }
    }
}
