import type Grid from '../../grid'
import type Tile from '../../tile'

export default function getOutOfSync(grid: Grid<Tile>): [Tile, [number, number]][] {
    return grid.reduce(
        (acc, tile, x, y) => {
            if (tile.inSamePositionAs(x, y)) return acc
            acc.push([tile, [x, y]] as const)
            return acc
        },
        [] as [Tile, [number, number]][],
    )
}
