import { _decorator, CCBoolean, math, pseudoRandom } from 'cc'
import { isCurveTwoValues } from '../ParticleFunction'
import type { Particle } from '../ParticleSimulator'
import CurveRange from '../particle-editor/CurveRange'
import { ModuleRandSeed } from './ModuleRandSeed'

const { ccclass, property } = _decorator

const LIMIT_VELOCITY_MODULE_RAND_SEED = ModuleRandSeed.LIMIT

@ccclass('LimitVelocityOvertimeModule')
export class LimitVelocityOvertimeModule {
    @property({ type: CCBoolean })
    private enable!: boolean

    @property({ type: CurveRange })
    private limit: CurveRange = new CurveRange()

    @property({ type: CurveRange })
    private dampen: CurveRange = new CurveRange()

    constructor() {
        this.limit.constant = 500
        this.dampen.constant = 0.2
    }

    public update(particle: Particle, dt: number): void {
        if (!this.enable) return

        const normalizedTime = 1 - particle.remainingLifetime / particle.startLifetime
        const dampedVel = particle.velocity.clone().normalize()

        const randLimit = isCurveTwoValues(this.limit)
            ? pseudoRandom(particle.randomSeed + LIMIT_VELOCITY_MODULE_RAND_SEED)
            : 0
        const randDampen = isCurveTwoValues(this.dampen)
            ? pseudoRandom(particle.randomSeed + LIMIT_VELOCITY_MODULE_RAND_SEED + 1)
            : 0

        const vel = particle.velocity.length()
        const limit = this.limit.evaluate(normalizedTime, randLimit)
        const dampen = this.dampen.evaluate(normalizedTime, randDampen)
        particle.velocity = dampedVel.multiplyScalar(this.dampenBeyondLimit(vel, limit, dampen, dt))
    }

    private dampenBeyondLimit(vel: number, limit: number, dampen: number, dt: number): number {
        if (vel <= limit) return vel
        const t = 1 - Math.exp(-dampen * dt)
        return math.lerp(vel, limit, t)
    }
}
