import { _decorator, Button, Component, Label, Node } from 'cc'
import GameplayEvents from '../../gameplay/constants/GameEvents'
import AdsUtils from '../../utils/AdsUtils'
import AudioUtils from '../../utils/AudioUtils'
import ComponentUtils from '../../utils/ComponentUtils'
import SortNodeUtils from '../../utils/SortNodeUtils'
import { BaseScreen } from '../BaseScreen'

const { ccclass, property, requireComponent } = _decorator

const { event } = codex
const {
    Events,
    Plugins: { Ads: { Types: AdsTypes } = {} },
} = Ludex

@ccclass('GetCoinScreen')
@requireComponent(BaseScreen)
export class GetCoinScreen extends Component {
    private screen!: BaseScreen
    private payload!: SwitchScreenPayload

    @ComponentUtils.required()
    @property(Label)
    private quantityLabel!: Label

    @ComponentUtils.required()
    @property(Button)
    public closeButtonComponent!: Button

    @ComponentUtils.required()
    @property(Button)
    public getNowButtonComponent!: Button

    @ComponentUtils.required()
    @property(Node)
    private particle!: Node

    public __preload() {
        this.enabled = false
        this.screen = this.getComponent(BaseScreen)!
        this.listenScreenEvents()
    }

    protected onLoad(): void {
        SortNodeUtils.setNodeToLayer(this.particle, 'Particles')

        if (AdsTypes) {
            AdsUtils.preloadAdsByType(AdsTypes.REWARDED_VIDEO)
        }
    }

    public onDestroy(): void {
        this.unListenScreenEvents()
    }

    private listenScreenEvents() {
        // ? Here's a demo of all the events a screen has
        event.on(Events.OPENING_SCREEN, this.handleOpeningScreen)
        event.on(Events.OPENED_SCREEN, this.handleOpenedScreen)
    }

    private unListenScreenEvents() {
        event.off(Events.OPENING_SCREEN, this.handleOpeningScreen)
        event.off(Events.OPENED_SCREEN, this.handleOpenedScreen)
    }

    private handleOpeningScreen = (payload: SwitchScreenPayload) => {
        console.log('GetCoinScreen.OPENING_SCREEN', payload)
        if (!this.validateEventPayload(payload)) return

        this.setInteractableButtons(true)
        this.updateStatus()
    }

    private handleOpenedScreen = (payload: SwitchScreenPayload) => {
        if (!this.validateEventPayload(payload)) return
        this.open()
    }

    private validateEventPayload(payload: SwitchScreenPayload) {
        if (!('screenName' in payload)) return false

        const { screenName } = payload
        if (screenName !== this.node.name) return false

        this.payload = payload
        return true
    }

    public open() {
        this.updateStatus()
        this.runPopUpAnimation()
        this.node.scene.emit(GameplayEvents.HIDE_UI)
    }

    public close() {
        this.setInteractableButtons(false)

        event.emit(Events.CLOSE_SCREEN, { screenName: this.node.name })
        this.node.scene.emit(GameplayEvents.SHOW_UI)
    }

    private updateStatus() {
        this.updateCoinQuantity()
    }

    private runPopUpAnimation(): void {
        if (!this.screen.container) {
            console.warn('GetCoinScreen: screen not found')
        }
    }

    private updateCoinQuantity() {
        const quantity = this.getQuantity()

        if (quantity) {
            this.updateQuantityLabel(quantity)
        }
    }

    private updateQuantityLabel(quantity: number) {
        this.quantityLabel.string = `x${quantity.toString()}`
    }

    private getQuantity(): number {
        const { screenData } = this.payload

        if (!screenData) return 0

        if ('quantity' in screenData && screenData.quantity && typeof screenData.quantity === 'number') {
            return screenData.quantity
        }

        return 0
    }

    public handleAddCoin() {
        const quantity = this.getQuantity()
        this.node.scene.emit(GameplayEvents.ADD_COINS, {
            coins: quantity,
            collectionAnimation: {
                animateTextOnly: false,
            },
            skipAnimation: true,
        } as CoinDataPayload)
        this.close()
    }

    // ? Callback for click event of GetNowButton
    public async handleGetNowButtonClicked(): Promise<void> {
        AudioUtils.stopAllSounds()

        const success = await AdsUtils.showRewardVideoAdAsync()
        if (!success) return

        this.handleAddCoin()
    }

    private setInteractableButtons(interactable: boolean) {
        this.closeButtonComponent.interactable = interactable
        this.getNowButtonComponent.interactable = interactable
        this.screen.backgroundMaskButton.interactable = interactable
    }
}
