import { Node as CCNode, Component, Graphics, Vec2, Vec3, _decorator } from 'cc'
import SecondOrderDynamics, { SODConfig } from './SecondOrderDynamics'
const { ccclass, property } = _decorator

@ccclass('Trail')
export class Trail extends Component {
    @property({ type: CCNode, displayName: 'Target' })
    private target!: CCNode

    @property({ type: Graphics, displayName: 'Graphics' })
    private graphics!: Graphics

    @property({ type: [SODConfig], displayName: 'Trail Nodes' })
    private trailNodes: SODConfig[] = []

    private trailNodeInstances: SecondOrderDynamics[] = []

    public reset() {
        for (const instance of this.trailNodeInstances) {
            instance.reset(new Vec2(this.node.worldPosition.x, this.node.worldPosition.y))
        }

        this.graphics.clear()
    }

    public onLoad(): void {
        this.validateRequiredComponents()

        this.spawnTrailNodes()
    }

    protected update(dt: number): void {
        this.graphics.clear()

        const firstNode = this.trailNodeInstances[0]
        this.drawTrail(dt, firstNode, this.target.worldPosition.x, this.target.worldPosition.y)

        for (let i = 1; i < this.trailNodeInstances.length; i++) {
            const prevNode = this.trailNodeInstances[i - 1]
            const currNode = this.trailNodeInstances[i]

            this.drawTrail(dt, currNode, prevNode.currentState.x, prevNode.currentState.y)
        }
    }

    private drawTrail(dt: number, instance: SecondOrderDynamics, towardsX: number, towardsY: number): void {
        const { graphics } = this

        if (!this.target.activeInHierarchy) return

        const targetPosition = new Vec2(towardsX, towardsY)
        const nextPosition = instance.update(dt, targetPosition)

        const localTargetPosition = new Vec3(targetPosition.x, targetPosition.y, 0)
        const localNextPosition = new Vec3(nextPosition.x, nextPosition.y, 0)

        graphics.node.inverseTransformPoint(localTargetPosition, localTargetPosition)
        graphics.node.inverseTransformPoint(localNextPosition, localNextPosition)

        graphics.moveTo(localTargetPosition.x, localTargetPosition.y)
        graphics.lineTo(localNextPosition.x, localNextPosition.y)

        graphics.stroke()
    }

    private spawnTrailNodes(): void {
        for (const trailNodeConfig of this.trailNodes) {
            const startPosistion = new Vec2(this.node.worldPosition.x, this.node.worldPosition.y)
            const dynamics = new SecondOrderDynamics(startPosistion, trailNodeConfig)
            this.trailNodeInstances.push(dynamics)
        }
    }

    private validateRequiredComponents(): void {
        if (!this.target) {
            throw new Error('Target is not set')
        }

        if (!this.graphics) {
            throw new Error('Graphics is not set')
        }

        if (this.trailNodes.length === 0) {
            console.warn('No trail nodes are set')
        }
    }
}
