import { CCBoolean, CCInteger, CCString, Component, EventHandler, Label, _decorator } from 'cc'

const { ccclass, property } = _decorator

@ccclass('HorizontalSelector')
export class HorizontalSelector extends Component {
    @property({
        type: CCBoolean,
        tooltip:
            'Component initializes on its own in its preload method. Turn off if you want to initialize it manually.',
    })
    private selfInitialize!: boolean

    @property({
        type: CCString,
        visible: function (this: HorizontalSelector): boolean {
            return !this.selfInitialize
        },
    })
    private items: string[] = []

    @property({
        type: CCInteger,
        visible: function (this: HorizontalSelector): boolean {
            return !this.selfInitialize
        },
    })
    private currentIndex: number = 0

    @property(CCBoolean)
    private loop!: boolean

    @property(Label)
    private selectedItemLabel: Label | null = null

    @property(EventHandler)
    private onSelectEvents: EventHandler[] = []

    private selectedItem: string = ''

    public __preload() {
        if (this.selfInitialize) return

        this.init(this.items, this.currentIndex)
    }

    public init(items: string[], currentIndex: number, loop?: boolean) {
        this.items = items
        this.currentIndex = currentIndex
        if (loop !== undefined) this.loop = loop

        this.selectedItem = this.items[this.currentIndex]
        if (this.selectedItemLabel) this.selectedItemLabel.string = this.selectedItem
    }

    public selectNext() {
        if (!this.loop && this.currentIndex === this.items.length - 1) return
        this.currentIndex = (this.currentIndex + 1) % this.items.length
        this.selectedItem = this.items[this.currentIndex]
        this.emitOnSelectedEvents()
    }

    public selectPrevious() {
        if (!this.loop && this.currentIndex === 0) return
        this.currentIndex = (this.currentIndex - 1 + this.items.length) % this.items.length
        this.selectedItem = this.items[this.currentIndex]
        this.emitOnSelectedEvents()
    }

    public getSelectedItem() {
        return this.selectedItem
    }

    public getSelectedIndex() {
        return this.currentIndex
    }

    public getItems() {
        return this.items
    }

    public setSelectedItem(index: number) {
        this.selectedItem = this.items[index]
        this.currentIndex = index
        this.emitOnSelectedEvents()
    }

    private emitOnSelectedEvents() {
        if (this.selectedItemLabel) this.selectedItemLabel.string = this.selectedItem

        for (const selectEvent of this.onSelectEvents) {
            selectEvent.emit([this.selectedItem, selectEvent.customEventData])
        }
    }
}
