import { Component, _decorator } from 'cc'
import type { Gameplay } from '../Gameplay'
import GameplayEvents from '../constants/GameEvents'
import ItemKeys from '../constants/ItemKeys'
import canUseHintItem from '../game-objects/item/canUseHintItem'
import canUsePliersItem from '../game-objects/item/canUsePliersItem'
import canUseThunderItem from '../game-objects/item/canUseThunderItem'
import canUseTornadoItem from '../game-objects/item/canUseTornadoItem'
const { ccclass } = _decorator

const { player } = codex

export type ItemConfig = {
    canActivate: boolean
    quantity: number
    animate?: boolean
}

const itemConfigs: Record<ItemKey, ItemConfig> = {
    [ItemKeys.HINT]: {
        canActivate: false,
        quantity: 0,
    },
    [ItemKeys.THUNDER]: {
        canActivate: false,
        quantity: 0,
    },
    [ItemKeys.STORM]: {
        canActivate: false,
        quantity: 0,
    },
    [ItemKeys.PLIERS]: {
        canActivate: false,
        quantity: 0,
    },
}

@ccclass('ItemManager')
export class ItemManager extends Component {
    public itemConfigs: Record<ItemKey, ItemConfig> = itemConfigs

    private game!: Gameplay

    protected onLoad(): void {
        this.node.scene.on(GameplayEvents.GRID_CREATE, this.updateItemsConfig, this)
        this.node.scene.on(GameplayEvents.GRID_MATCH, this.updateItemsConfig, this)
        this.node.scene.on(GameplayEvents.GRID_CLEAR, this.updateItemsConfig, this)
        this.node.scene.on(GameplayEvents.GRID_SPAWN, this.updateItemsConfig, this)
        this.node.scene.on(GameplayEvents.BOMB_EXPLODE, this.updateItemsConfig, this)
        this.node.scene.on(GameplayEvents.REFRESH_ITEMS, this.updateItemsConfig, this)
    }

    public init(game: Gameplay): void {
        this.game = game
    }

    public getQuantity(item: ItemKey): number {
        return this.itemConfigs[item].quantity
    }

    public setQuantity(item: ItemKey, quantity: number, animate: boolean = false): void {
        const q = Math.max(0, quantity)

        this.itemConfigs[item].quantity = q

        this.saveQuantity(item, quantity)
        this.updateItemConfig(item, animate)
    }

    public saveQuantity(item: ItemKey, quantity: number): void {
        const gameData = player.getGameData()
        if (!gameData) return

        player.setGameData({
            items: {
                ...gameData.items,
                [item]: quantity,
            },
        })
    }

    public canUseItem(item: ItemKey) {
        const { board, pathDraw } = this.game

        switch (item) {
            case ItemKeys.HINT:
                return canUseHintItem(board, pathDraw)
            case ItemKeys.PLIERS:
                return canUsePliersItem(board)
            case ItemKeys.STORM:
                return canUseTornadoItem(board)
            case ItemKeys.THUNDER:
                return canUseThunderItem(board)
            default:
                return false
        }
    }

    private updateItemsConfig(): void {
        const gameData = player.getGameData()
        if (!gameData || !gameData.items) return

        for (const itemKey of Object.keys(this.itemConfigs)) {
            const itemConfig = this.itemConfigs[itemKey as ItemKey]
            itemConfig.canActivate = this.canUseItem(itemKey as ItemKey)
            itemConfig.quantity = gameData.items[itemKey as ItemKey] || 0
        }

        this.game.uiManager.currentLayout.updateStatus({ items: this.itemConfigs })
    }

    private updateItemConfig(item: ItemKey, animate: boolean = false): void {
        const gameData = player.getGameData()
        if (!gameData) return

        this.itemConfigs[item].canActivate = this.canUseItem(item)
        this.itemConfigs[item].quantity = gameData.items[item] || 0

        this.game.uiManager.currentLayout.updateStatus({
            items: {
                ...this.itemConfigs,
                [item]: {
                    animate,
                    ...this.itemConfigs[item],
                },
            },
            ...this.game.uiManager.config,
        })
    }
}
