// biome-ignore lint/suspicious/noExplicitAny: needed for generic functions
export type FnThatReturnVoid<T extends (...args: any[]) => any> = ReturnType<T> extends void ? T : never

// biome-ignore lint/suspicious/noExplicitAny: needed for generic functions
function decorateComponent<T extends (...args: any[]) => any>(
    _target: Component,
    propertyKey: string,
    descriptor: TypedPropertyDescriptor<FnThatReturnVoid<T>>,
) {
    const original = descriptor.value

    const wrapped = function (this: Component, ...args: Parameters<FnThatReturnVoid<T>>) {
        if (this.isValid) return original?.apply(this, args)
        console.debug(
            `Skipping method call ${String(propertyKey)} because component ${
                this.constructor.name
            } is not enabled in hierarchy`,
        )
    }

    descriptor.value = wrapped as FnThatReturnVoid<T>
}

// biome-ignore lint/suspicious/noExplicitAny: needed for generic functions
function createNonComponentDecorator<T extends (...args: any[]) => any>(
    isValid: () => boolean,
): (_target: object, propertyKey: string, descriptor: TypedPropertyDescriptor<FnThatReturnVoid<T>>) => void {
    return (_target: object, propertyKey: string, descriptor: TypedPropertyDescriptor<FnThatReturnVoid<T>>) => {
        const original = descriptor.value

        const wrapped = function (this: object, ...args: Parameters<FnThatReturnVoid<T>>) {
            if (isValid.call(this)) return original?.apply(this, args)
            console.debug(
                `Skipping method call ${String(propertyKey)} because component ${
                    this.constructor.name
                } is not enabled in hierarchy`,
            )
        }

        descriptor.value = wrapped as FnThatReturnVoid<T>
    }
}

// biome-ignore lint/suspicious/noExplicitAny: needed for generic functions
export default function onlyRunInLifeCycle<T extends (...args: any[]) => any>(
    customIsValid: () => boolean,
): (_target: object, propertyKey: string, descriptor: TypedPropertyDescriptor<FnThatReturnVoid<T>>) => void

// biome-ignore lint/suspicious/noExplicitAny: needed for generic functions
export default function onlyRunInLifeCycle<T extends (...args: any[]) => any>(
    _target: Component,
    propertyKey: string,
    descriptor: TypedPropertyDescriptor<FnThatReturnVoid<T>>,
): void

// biome-ignore lint/suspicious/noExplicitAny: needed for generic functions
export default function onlyRunInLifeCycle<T extends (...args: any[]) => any>(
    compOrCustomValidator: Component | (() => boolean),
    propertyKey?: string,
    descriptor?: TypedPropertyDescriptor<FnThatReturnVoid<T>>,
) {
    if (typeof compOrCustomValidator === 'object' && propertyKey !== undefined && descriptor !== undefined) {
        decorateComponent(compOrCustomValidator, propertyKey, descriptor)
        return
    }

    const customIsValid = compOrCustomValidator as () => boolean
    const decorator = createNonComponentDecorator(customIsValid)
    return decorator
}
