import { Component, type Node, Tween, Vec2, _decorator, tween } from 'cc'
import MatchUtils from '../../utils/MatchUtils'
import type { Gameplay } from '../Gameplay'
import GameplayEvents from '../constants/GameEvents'
import GuideKeys from '../constants/GuideKeys'
import ItemKeys from '../constants/ItemKeys'
import Guide01Newbie from '../guides/Guide01Newbie'
import Guide02Timer from '../guides/Guide02Timer'
import Guide03Combo from '../guides/Guide03Combo'
import Guide04Hint from '../guides/Guide04Hint'
import Guide05Thunder from '../guides/Guide05Thunder'
import Guide06Storm from '../guides/Guide06Storm'
import Guide07Pliers from '../guides/Guide07Pliers'
import { GuideUI } from '../guides/GuideUI'

const { player, analytics } = codex
const { ccclass, property } = _decorator

const {
    Match: { Modes },
    Plugins: {
        Analytics: { Events: AnalyticsEvents } = {},
    },
    Utils: { Array: A },
} = Ludex

const RANGE_HINT_GUIDE_LEVELS = new Vec2(0, 10)
const RANGE_ERROR_PATH_LEVELS = new Vec2(0, 10)

const HINT_GUIDE_DELAY = 5
const STORM_GUIDE_DELAY = 10

const STATUS = {
    READY: 'READY',
    RUNNING: 'RUNNING',
}

type Status = (typeof STATUS)[keyof typeof STATUS]

@ccclass('GuidelinesManager')
class GuidelinesManager extends Component {
    public gameplay!: Gameplay

    @property(GuideUI)
    readonly guideUi!: GuideUI

    private guide1!: Guide01Newbie
    private guide2!: Guide02Timer
    private guide3!: Guide03Combo
    private guide4!: Guide04Hint
    private guide5!: Guide05Thunder
    private guide6!: Guide06Storm
    private guide7!: Guide07Pliers

    private status!: Status

    private guideHintDelay: Tween<Node> | null = null
    private guideStormDelay: Tween<Node> | null = null

    public init(gameplay: Gameplay): void {
        this.gameplay = gameplay

        this.status = STATUS.READY

        this.node.scene.on(GameplayEvents.COMBO_UPDATED, this.startComboGuide, this)
        this.node.scene.on(GameplayEvents.TILE_CLICK, this.startAutoGuides, this)
        this.node.scene.on(GameplayEvents.USE_ITEM, this.handleUseItem, this)

        this.node.scene.on(GameplayEvents.GAME_FINISH, this.stopAutoGuides, this)
        this.node.scene.on(GameplayEvents.GAME_REPLAY, this.stopAutoGuides, this)
        this.node.scene.on(GameplayEvents.GAME_PAUSE, this.stopAutoGuides, this)
    }

    public show(): void {
        if (this.isNewbieGuideLevel()) {
            if (this.hasGuideItemShowed(GuideKeys.NEWBIE)) {
                this.resetGuideItemShowed()
            }
            this.startGuide(GuideKeys.NEWBIE)
        } else {
            this.startGuide(GuideKeys.TIMER)
        }
    }

    public startGuide(id: GuideKey): boolean {
        if (this.status === STATUS.RUNNING) return false

        if (this.hasGuideItemShowed(id)) return false

        this.addGuideItemShowed(id)

        this.status = STATUS.RUNNING

        switch (id) {
            case GuideKeys.NEWBIE:
                AnalyticsEvents && analytics.event(AnalyticsEvents.TUTORIAL_BEGIN)
                this.guide1 = new Guide01Newbie(this)
                this.guide1.setup()
                break
            case GuideKeys.TIMER:
                this.guide2 = new Guide02Timer(this)
                this.guide2.setup()
                break
            case GuideKeys.COMBO:
                this.guide3 = new Guide03Combo(this)
                this.guide3.setup()
                break
            case GuideKeys.HINT_ITEM:
                this.guide4 = new Guide04Hint(this)
                this.guide4.setup()
                break
            case GuideKeys.THUNDER_ITEM:
                this.guide5 = new Guide05Thunder(this)
                this.guide5.setup()
                break
            case GuideKeys.STORM_ITEM:
                this.guide6 = new Guide06Storm(this)
                this.guide6.setup()
                break
            case GuideKeys.PLIERS_ITEM:
                this.guide7 = new Guide07Pliers(this)
                this.guide7.setup()
                break
            default:
                break
        }

        return true
    }

    public completeGuide(id: GuideKey): void {
        AnalyticsEvents && analytics.event(AnalyticsEvents.TUTORIAL_STEP, { step: id })

        this.status = STATUS.READY
        this.addGuideItemShowed(id)

        if (!this.gameplay.stateManager.isPlaying()) return
        this.startAutoGuides()
    }

    private isHintGuideLevel(): boolean {
        const currentLevel = this.getCurrentLevel()
        const { x: min, y: max } = RANGE_HINT_GUIDE_LEVELS
        return currentLevel >= min && currentLevel <= max
    }

    public isShowErrorPathLevel(): boolean {
        const currentGameMode = MatchUtils.getMatchMode()
        if (currentGameMode === Modes.TOURNAMENT) return false

        const currentLevel = this.getCurrentLevel()
        const { x: min, y: max } = RANGE_ERROR_PATH_LEVELS
        return currentLevel >= min && currentLevel <= max
    }

    public isNewbieGuideLevel(): boolean {
        return this.getCurrentLevel() === 0
    }

    public hasGuideItemShowed(id: GuideKey): boolean {
        return A.has(this.getGuideItemShowed(), id)
    }

    private addGuideItemShowed(id: GuideKey): void {
        if (this.hasGuideItemShowed(id)) return

        const guideItemShowed = this.getGuideItemShowed()
        guideItemShowed.push(id)

        player.setGameData({ guideItemShowed })
    }

    private resetGuideItemShowed(): void {
        player.setGameData({ guideItemShowed: [] })
    }

    private getGuideItemShowed(): GuideKey[] {
        return player.getGameData()?.guideItemShowed ?? []
    }

    private getCurrentLevel(): number {
        return this.gameplay.levelManager.getLevelData().level
    }

    private startComboGuide(combo: number): void {
        if (this.isNewbieGuideLevel()) return
        if (combo < 2) return

        this.startGuide(GuideKeys.COMBO)

        this.node.scene.off(GameplayEvents.COMBO_UPDATED, this.startComboGuide, this)
    }

    public startAutoGuides(): void {
        this.startHintGuide()
        this.startStormGuide()
    }

    private stopAutoGuides(): void {
        if (this.guideHintDelay !== null) {
            this.guideHintDelay.stop()
        }

        if (this.guideStormDelay !== null) {
            this.guideStormDelay.stop()
        }
    }

    private startHintGuide(): void {
        if (!this.isHintGuideLevel()) return

        if (this.guideHintDelay === null) {
            this.guideHintDelay = tween(this.node)
                .delay(HINT_GUIDE_DELAY)
                .call(() => {
                    if (this.isNewbieGuideLevel()) return
                    if (this.hasGuideItemShowed(GuideKeys.HINT_ITEM) && this.status === STATUS.READY) {
                        this.node.scene.emit(GameplayEvents.SHOW_HINT)
                    } else {
                        this.startGuide(GuideKeys.HINT_ITEM)
                    }

                    this.startHintGuide()
                })
        }

        this.guideHintDelay.stop()
        this.guideHintDelay.start()
    }

    private startStormGuide(): void {
        if (this.guideStormDelay === null) {
            this.guideStormDelay = new Tween(this.node).delay(STORM_GUIDE_DELAY).call(() => {
                if (this.isNewbieGuideLevel()) return
                if (this.hasGuideItemShowed(GuideKeys.STORM_ITEM)) return

                if (!this.gameplay.itemManager.canUseItem(ItemKeys.STORM)) return

                if (!this.startGuide(GuideKeys.STORM_ITEM)) {
                    this.startStormGuide()
                }
            })
        }

        this.guideStormDelay.stop()
        this.guideStormDelay.start()
    }

    public startThunderGuide(): boolean {
        if (this.isNewbieGuideLevel()) return false
        return this.startGuide(GuideKeys.THUNDER_ITEM)
    }

    public startPliersGuide(): boolean {
        if (this.isNewbieGuideLevel()) return false
        return this.startGuide(GuideKeys.PLIERS_ITEM)
    }

    private handleUseItem(item: ItemKey): void {
        this.startAutoGuides()

        switch (item) {
            case ItemKeys.HINT:
                this.addGuideItemShowed(GuideKeys.HINT_ITEM)
                break
            case ItemKeys.THUNDER:
                this.addGuideItemShowed(GuideKeys.THUNDER_ITEM)
                break
            case ItemKeys.STORM:
                this.addGuideItemShowed(GuideKeys.STORM_ITEM)
                break
            case ItemKeys.PLIERS:
                this.addGuideItemShowed(GuideKeys.PLIERS_ITEM)
                break
        }
    }

    public async skipNewbieGuide(): Promise<void> {
        this.guideUi.guideSkipButton.active = false
        await this.guide1.close()
        const level = this.getCurrentLevel() + 1
        player.setGameData({ level })
        this.node.scene.emit(GameplayEvents.NEXT_LEVEL)
    }

    protected onDestroy(): void {
        this.guide2?.unlistenEvents()
        this.guide3?.unlistenEvents()
        this.guide4?.unlistenEvents()
        this.guide5?.unlistenEvents()
        this.guide6?.unlistenEvents()
        this.guide7?.unlistenEvents()
    }
}

export default GuidelinesManager
