import { Trail } from 'db://assets/components/trail/Trail'
import AnimUtils from 'db://assets/utils/AnimUtils'
import ComponentUtils from 'db://assets/utils/ComponentUtils'
import type AnimAbortSignal from 'db://assets/utils/common/AnimAbortSignal'
import {
    _decorator,
    Animation,
    AnimationClip,
    CCInteger,
    Component,
    instantiate,
    ParticleSystem2D,
    randomRange,
    tween,
    Vec3,
} from 'cc'

const { ccclass, disallowMultiple, property } = _decorator

const DISTANCE_RANGE = [50, 160]

@ccclass('LockAnimator')
@disallowMultiple
export class LockAnimator extends Component {
    @property(ParticleSystem2D)
    private floatingParticles!: ParticleSystem2D

    @property(Animation)
    private iconAnimation!: Animation

    @property(AnimationClip)
    private lockClip!: AnimationClip

    @property(AnimationClip)
    private unlockClip!: AnimationClip

    @property(AnimationClip)
    private tryToUnlockClip!: AnimationClip

    @property(Trail)
    private trailTemplate!: Trail

    @property({ type: CCInteger, min: 0 })
    private trailCount: number = 0

    private trails: Trail[] = []

    public onLoad(): void {
        this.validateRequiredComponents()

        this.prepareTrails()

        this.floatingParticles.node.active = false
    }

    public async unlock(signal?: AnimAbortSignal): Promise<void> {
        this.floatingParticles.node.active = true

        this.floatingParticles.resetSystem()

        this.scheduleOnce(() => {
            this.explodeTrails(signal)
        }, 1.1)

        await this.playAnimation(this.unlockClip.name, signal)

        this.floatingParticles.stopSystem()
    }

    public lock(): void {
        this.playAnimation(this.lockClip.name)

        if (this.floatingParticles.node.active) {
            this.floatingParticles.stopSystem()
        }
    }

    private async explodeTrails(signal?: AnimAbortSignal): Promise<void> {
        await Promise.all(this.trails.map((trail) => this.getExplodeTrail(trail, 0, signal)))
    }

    private async getExplodeTrail(trail: Trail, delay = 0, signal?: AnimAbortSignal): Promise<void> {
        const rotation = randomRange(0, 2 * Math.PI)

        const explodeX = randomRange(DISTANCE_RANGE[0], DISTANCE_RANGE[1]) * Math.cos(rotation)
        const explodeY = randomRange(DISTANCE_RANGE[0], DISTANCE_RANGE[1]) * Math.sin(rotation)

        const explodePosition = new Vec3(explodeX, explodeY, trail.node.position.z)

        const ogScale = trail.node.scale.clone()

        // animate coin from 0, 0 of the screen, appearing, moving in a random direction, for a random amount of distance, and then move to the target position
        const moveTween = tween(trail.node)
            .delay(delay)
            .call(() => {
                trail.node.setPosition(0, 0)
                trail.reset()
                trail.node.active = true
            })
            .to(
                0.5,
                {
                    position: explodePosition.clone(),
                },
                {
                    easing: 'expoOut',
                },
            )
            .to(
                0.5,
                {
                    scale: new Vec3(0, 0, 1),
                },
                {
                    easing: 'expoIn',
                },
            )

        await AnimUtils.runTweenAsync(moveTween, signal)

        trail.node.setPosition(0, 0)
        trail.node.scale = ogScale
        trail.reset()
        trail.node.active = false
    }

    private prepareTrails() {
        this.trailTemplate.node.active = false

        for (let i = 0; i < this.trailCount; i++) {
            const trail = instantiate(this.trailTemplate.node)
            trail.setParent(this.node)
            trail.active = false

            this.trails.push(trail.getComponent(Trail)!)
        }
    }

    private validateRequiredComponents() {
        ComponentUtils.lazyValidateProperties(this)
    }

    private async playAnimation(clipName: string, signal?: AnimAbortSignal): Promise<void> {
        if (signal?.aborted) {
            return Promise.resolve()
        }

        return new Promise((resolve) => {
            this.iconAnimation.once(Animation.EventType.FINISHED, () => {
                resolve()
            })

            signal?.addAbortListener(() => {
                this.iconAnimation.off(Animation.EventType.FINISHED)
                this.iconAnimation.stop()
                resolve()
            })

            this.iconAnimation.play(clipName)
        })
    }

    public async playTryToUnlockAnimation(): Promise<void> {
        return this.playAnimation(this.tryToUnlockClip.name)
    }
}
