import { _decorator, Component } from 'cc'
import { ButtonPlusEvents } from '../../constants/ButtonPlusEvents'
import AnalyticsUtils from '../../utils/AnalyticsUtils'
import ComponentUtils from '../../utils/ComponentUtils'
import { ButtonPlus } from './ButtonPlus'

const { ccclass, property, disallowMultiple, requireComponent, menu } = _decorator

const {
    Plugins: { Analytics: { Events } = {} },
} = Ludex
const { analytics } = codex

@ccclass('ButtonPlusAnalytics')
@disallowMultiple
@requireComponent(ButtonPlus)
@menu('Ludex/ButtonPlus/ButtonPlusAnalytics')
export class ButtonPlusAnalytics extends Component {
    @ComponentUtils.required()
    @property(ButtonPlus)
    readonly button!: ButtonPlus

    @property
    readonly useCustomButtonName = false

    @property({
        visible(this: ButtonPlusAnalytics) {
            return this.useCustomButtonName
        },
    })
    readonly customButtonName = ''

    protected onEnable(): void {
        this.button.node.on(ButtonPlusEvents.CLICK, this.onClick, this)
    }

    protected onDisable(): void {
        this.button.node.off(ButtonPlusEvents.TRANSITION_DOWN, this.onClick, this)
    }

    private onClick() {
        if (!Events) return

        analytics.event(Events.BUTTON_CLICK, {
            button_name: this.useCustomButtonName ? this.customButtonName : this.node.name,
            screen_name: AnalyticsUtils.getCurrentPage(),
        })
    }
}
