import { HorizontalTextAlignment, UITransform, Vec3, game } from 'cc'
import GameEvents from '../../constants/GameEvents'
import LayoutUtils from '../../utils/LayoutUtils'
import type { Gameplay } from '../Gameplay'
import GameplayEvents from '../constants/GameEvents'
import GuideKeys from '../constants/GuideKeys'
import ItemKeys from '../constants/ItemKeys'
import type GuidelinesManager from '../manager/GuidelinesManager'

const { Array: A } = Ludex.Utils

class Guide06Storm {
    private gameplay: Gameplay
    private manager: GuidelinesManager

    constructor(manager: GuidelinesManager) {
        this.manager = manager
        this.gameplay = manager.gameplay
    }

    public async setup(): Promise<void> {
        const {
            guideUi: { guideMasks, guideInfoText, guideInfoTextLocalizer },
        } = this.manager

        const storm = A.search(this.gameplay.uiManager.items, (item) => item.itemType === ItemKeys.STORM)
        if (!storm) return

        guideMasks.node.active = true

        guideInfoText.node.active = true
        guideInfoTextLocalizer.key = 'guide.storm'
        guideInfoText.horizontalAlign = HorizontalTextAlignment.CENTER
        guideInfoText.getComponent(UITransform)!.setAnchorPoint(0.5, 0.5)

        this.handleViewResized()

        await guideMasks.show()

        this.manager.node.scene.once(GameplayEvents.GUIDE_MASK_CLICKED, this.close, this)
        this.manager.node.scene.once(GameplayEvents.ITEM_MASK_CLICKED, this.useItem, this)
        this.manager.node.scene.emit(GameplayEvents.PAUSE_TIME)
        game.on(GameEvents.VIEW_RESIZED, this.handleViewResized, this)
        game.on(GameEvents.ORIENTATION_CHANGED, this.handleViewResized, this)
    }

    private handleViewResized(): void {
        const {
            guideUi: { guideMasks, guideInfoText, guideItemButton },
        } = this.manager
        const storm = A.search(this.gameplay.uiManager.items, (item) => item.itemType === ItemKeys.STORM)
        if (!storm) return

        const itemPosition = storm.node.worldPosition
        let { width, height } = storm.getComponent(UITransform)!.contentSize

        const padding = 40
        width += padding
        height += padding

        guideMasks.setItemMaskTransform(itemPosition.x, itemPosition.y, width, height)
        guideItemButton.node.active = true
        guideItemButton.setTransform(itemPosition.x, itemPosition.y, width, height)
        
        if (LayoutUtils.isPortrait()) {
            guideInfoText.node.setPosition(0, 0)
            guideInfoText.node.setWorldPosition(guideInfoText.node.worldPosition.x, itemPosition.y + height / 2 + 30, 0)
        } else if (LayoutUtils.isLandscape()) {
            const { clock } = this.gameplay.uiManager.currentLayout
            const guideInfoTextPosition = new Vec3(clock.node.worldPosition.x, clock.node.worldPosition.y - 750, 0)
            guideInfoText.node.worldPosition = guideInfoTextPosition
        }
    }

    private async close(): Promise<void> {
        const {
            guideUi: { guideMasks, guideInfoText, guideItemButton },
        } = this.manager

        this.manager.node.scene.emit(GameplayEvents.RESUME_TIME)

        await guideMasks.hide()

        guideInfoText.node.active = false
        guideItemButton.node.active = false

        this.manager.completeGuide(GuideKeys.STORM_ITEM)

        this.unlistenEvents()
    }

    private useItem(): void {
        this.manager.node.scene.emit(GameplayEvents.USE_ITEM, ItemKeys.STORM)
        this.close()
    }

    public unlistenEvents(): void {
        this.manager.node.scene.off(GameplayEvents.GUIDE_MASK_CLICKED, this.close, this)
        this.manager.node.scene.off(GameplayEvents.ITEM_MASK_CLICKED, this.useItem, this)
        game.off(GameEvents.VIEW_RESIZED, this.handleViewResized, this)
        game.off(GameEvents.ORIENTATION_CHANGED, this.handleViewResized, this)
    }
}

export default Guide06Storm
