import { AudioSource } from 'cc'
type SoundKeys = Ludex.Plugins.Audio.SoundKeys

let BaseAudioPlayer = Ludex.Plugins.Audio?.BaseAudioPlayer

if (!BaseAudioPlayer) {
    BaseAudioPlayer = class BaseAudioPlayer {
        constructor(key: SoundKeys) {
            console.warn('BaseAudioPlayer is not defined', key)
        }
    } as unknown as typeof Ludex.Plugins.Audio.BaseAudioPlayer
}

class AudioPlayer extends BaseAudioPlayer {
    private audioSource: AudioSource

    constructor(key: SoundKeys, audioSource: AudioSource) {
        super(key)

        this.audioSource = audioSource
    }

    public play(config?: Ludex.Plugins.Audio.PlayConfig): void {
        if (config) {
            const { volume = 1, loop = false } = config
            this.audioSource.volume = volume
            this.audioSource.loop = loop
        }
        this.audioSource.play()
    }

    public pause(): void {
        this.audioSource.pause()
    }

    public resume(): void {
        if (this.audioSource.state === AudioSource.AudioState.PAUSED) {
            this.audioSource.play()
        }
    }

    public stop(): void {
        this.audioSource.stop()
    }

    public setVolume(volume: number): void {
        this.audioSource.volume = volume
    }

    public getVolume(): number {
        return this.audioSource.volume
    }

    public getAudioSource(): AudioSource {
        return this.audioSource
    }
}

export default AudioPlayer
