import { _decorator, Color, Enum, Gradient } from 'cc'

const { ccclass, property } = _decorator

export const Mode = Enum({
    Color: 0,
    Gradient: 1,
    TwoColors: 2,
    TwoGradients: 3,
    RandomColor: 4,
})

const tempColor = new Color()
const tempColor2 = new Color()

@ccclass('GradientRange')
export default class GradientRange {
    @property({ type: Enum(Mode) })
    public mode: number = Mode.Color

    @property({
        visible: function (this: GradientRange): boolean {
            return this.mode === Mode.Color
        },
    })
    public color = Color.WHITE.clone()

    @property({
        visible: function (this: GradientRange): boolean {
            return this.mode === Mode.TwoColors
        },
    })
    public colorMin = Color.WHITE.clone()

    @property({
        visible: function (this: GradientRange): boolean {
            return this.mode === Mode.TwoColors
        },
    })
    public colorMax = Color.WHITE.clone()

    @property({
        visible: function (this: GradientRange): boolean {
            return this.mode === Mode.Gradient || this.mode === Mode.RandomColor
        },
    })
    public gradient = new Gradient()

    @property({
        visible: function (this: GradientRange): boolean {
            return this.mode === Mode.TwoGradients
        },
    })
    public gradientMin = new Gradient()

    @property({
        visible: function (this: GradientRange): boolean {
            return this.mode === Mode.TwoGradients
        },
    })
    public gradientMax = new Gradient()

    private _color = Color.WHITE.clone()

    public evaluate(time: number, rndRatio: number): Color {
        switch (this.mode) {
            case Mode.Color:
                return this.color
            case Mode.TwoColors:
                Color.lerp(this._color, this.colorMin, this.colorMax, rndRatio)
                return this._color
            case Mode.RandomColor:
                return this.gradient.getRandomColor(this._color)
            case Mode.Gradient:
                return this.gradient.evaluateFast(this._color, time)
            case Mode.TwoGradients:
                Color.lerp(
                    this._color,
                    this.gradientMin.evaluateFast(tempColor, time),
                    this.gradientMax.evaluateFast(tempColor2, time),
                    rndRatio,
                )
                return this._color
            default:
                return this.color
        }
    }
}
