import { Component, Node, UIOpacity, _decorator, game, tween } from 'cc'
import { OpacityToggler } from '../../../../../components/common/OpacityToggler'
import GameEvents from '../../../../../constants/GameEvents'
import GameplayEvents from '../../../../../gameplay/constants/GameEvents'
import MapData from '../../../../../gameplay/constants/MapData'
import AnimUtils from '../../../../../utils/AnimUtils'
import ComponentUtils from '../../../../../utils/ComponentUtils'
import type AnimAbortSignal from '../../../../../utils/common/AnimAbortSignal'
import LayoutUtils from '../../../../../utils/LayoutUtils'
import ThemeUtils from '../../../../../utils/ThemeUtils'
import { CoinContainer } from '../../../../common/coin-container/CoinContainer'
import { DashboardHeader } from '../../ui/header/DashboardHeader'
import { JourneyProgressAnimator } from '../../ui/journey-progress/JourneyProgressAnimator'
import { LandscapeMask } from '../../ui/landscape-mask/LandscapeMask'
import { CustomButton } from './custom-button/CustomButton'
import { CustomGuideUI } from './custom-guide-ui/CustomGuideUI'
import { PlayButton } from './play-button/PlayButton'

const { ccclass, property } = _decorator

const { storage, monitorError, player } = codex
const {
    Utils: { Array: A, Json: J },
} = Ludex

@ccclass('HomeContent')
export class HomeContent extends Component {
    @ComponentUtils.required()
    @property({ type: PlayButton, group: 'Play Button' })
    private playButton!: PlayButton

    @ComponentUtils.required()
    @property({ type: CustomButton, group: 'Play Button' })
    private customButton!: CustomButton

    @ComponentUtils.required()
    @property(OpacityToggler)
    private contentOpacity!: OpacityToggler

    @ComponentUtils.required()
    @property(JourneyProgressAnimator)
    private journeyProgressAnimator!: JourneyProgressAnimator

    @ComponentUtils.required()
    @property(DashboardHeader)
    private dashboardHeader!: DashboardHeader

    @ComponentUtils.required()
    @property(UIOpacity)
    private overlayDuringAnim!: UIOpacity

    @ComponentUtils.required()
    @property(CoinContainer)
    private coinContainer!: CoinContainer

    @ComponentUtils.required()
    @property(Node)
    private leavesContainer!: Node

    @ComponentUtils.required()
    @property(LandscapeMask)
    private landscapeMask!: LandscapeMask

    @ComponentUtils.required()
    @property(CustomGuideUI)
    private customGuideUI!: CustomGuideUI

    protected onLoad(): void {
        game.on(GameEvents.ORIENTATION_CHANGED, this.handleOrientationChanged, this)

        this.handleOrientationChanged()

        ComponentUtils.validateRequiredProperties(this)
    }

    protected onDestroy(): void {
        game.off(GameEvents.ORIENTATION_CHANGED, this.handleOrientationChanged, this)
    }

    private listenEvents() {
        this.node.scene.on(GameplayEvents.HIDE_UI, this.hideContent, this)
        this.node.scene.on(GameplayEvents.SHOW_UI, this.showContent, this)
    }

    private unlistenEvents() {
        this.node.scene.off(GameplayEvents.HIDE_UI, this.hideContent, this)
        this.node.scene.off(GameplayEvents.SHOW_UI, this.showContent, this)
    }

    public getCoinContainer(): CoinContainer {
        return this.coinContainer
    }

    public updateStatus(): void {
        const { level = 1, stars = 0 } = player.getGameData() ?? {}

        this.playButton.updateStatus(level)
        this.customButton.updateStatus(level)
        this.dashboardHeader.updateStarProgress(stars)
        this.dashboardHeader.updateDailyRewardsButton()
    }

    public updateDailyRewardsButton(): void {
        this.dashboardHeader.updateDailyRewardsButton()
    }

    public setUnlockProgress(currProgress: CurrentMapProgressPayload, resetAnimationQueue = false): void {
        this.setProgress(currProgress)
        this.updateCurrentMapInStorage(currProgress, resetAnimationQueue)
    }

    public shouldPlayUnlockAnimation(): boolean {
        const dashboardStorage = storage.getStorage('dashboard')

        if (dashboardStorage === null || dashboardStorage === undefined) {
            monitorError?.sendException(new Error('DashboardScene: dashboardStorage not found'))
            return false
        }

        const animateToCurrentLevel = dashboardStorage.animateToCurrentLevel ?? false

        if (!animateToCurrentLevel) {
            return false
        }

        const tournamentStorage = storage.getStorage('tournament')

        if (tournamentStorage === null || tournamentStorage === undefined) {
            monitorError?.sendException(new Error('DashboardScene: tournamentStorage not found'))
            return false
        }

        const animateTournamentLeader = tournamentStorage.animateTournamentLeader ?? false

        if (animateTournamentLeader) {
            return false
        }

        const lastProgress = dashboardStorage.lastMapPayload
        const currProgress = ThemeUtils.getCurrentMapProgress()
        const isSameProgress = J.encode(lastProgress) === J.encode(currProgress)

        if (isSameProgress) {
            return false
        }

        const lastMapIndex = A.searchIndex(MapData.mapOrder, (map) => map.id === lastProgress.id)
        const currMapIndex = A.searchIndex(MapData.mapOrder, (map) => map.id === currProgress.id)

        const isDevToolTimeTravel = lastMapIndex > currMapIndex
        return !isDevToolTimeTravel
    }

    @ComponentUtils.onlyRunInLifeCycleAsync
    public playUnlockProgressAnimation(signal: AnimAbortSignal) {
        const dashboardStorage = storage.getStorage('dashboard')
        if (dashboardStorage === null || dashboardStorage === undefined) {
            throw new Error('DashboardScene: dashboardStorage not found')
        }

        const lastProgress = dashboardStorage.lastMapPayload
        const currProgress = ThemeUtils.getCurrentMapProgress()

        const lastMapIndex = A.searchIndex(MapData.mapOrder, (map) => map.id === lastProgress.id)
        const currMapIndex = A.searchIndex(MapData.mapOrder, (map) => map.id === currProgress.id)

        const mapsPassed = MapData.mapOrder.slice(lastMapIndex, currMapIndex + 2)
        const isSamePack = lastProgress.id === currProgress.id

        if (isSamePack) {
            return this.playUnlockProgressAnimationOnly(currProgress, lastProgress, signal)
        }

        return this.playUnlockProgressAndPackAnimation(lastProgress, currProgress, mapsPassed, signal)
    }

    @ComponentUtils.onlyRunInLifeCycleAsync
    private async playUnlockProgressAndPackAnimation(
        lastProgress: CurrentMapProgressPayload,
        currProgress: CurrentMapProgressPayload,
        mapsPassed: MapInfo[],
        signal: AnimAbortSignal,
    ) {
        this.setProgress({ ...lastProgress })

        this.updateCurrentMapInStorage({ ...currProgress }, true)

        this.playButton.interactable = false

        await this.showOwnOverlay(signal)
        await this.journeyProgressAnimator.animateToNextLevel({
            nextCurr: currProgress.progress,
            nextTotal: currProgress.max,
            mapsPassed,
            signal,
        })
        await this.hideOwnOverlay(signal)

        this.playButton.interactable = true
    }

    @ComponentUtils.onlyRunInLifeCycleAsync
    private async playUnlockProgressAnimationOnly(
        currProgress: CurrentMapProgressPayload,
        lastProgress: CurrentMapProgressPayload,
        signal: AnimAbortSignal,
    ) {
        this.setProgress({
            ...currProgress,
            progress: lastProgress.progress,
        })

        this.updateCurrentMapInStorage({ ...currProgress }, true)

        const progress = currProgress.progress

        await this.showOwnOverlay(signal)
        await this.journeyProgressAnimator.animateTo(progress, signal)
        await this.hideOwnOverlay(signal)
    }

    @ComponentUtils.onlyRunInLifeCycleAsync
    private async hideOwnOverlay(signal: AnimAbortSignal) {
        await AnimUtils.runTweenAsync(tween(this.overlayDuringAnim).to(0.3, { opacity: 0 }), signal)
        if (!this.node.activeInHierarchy) return

        //? in case the animation was aborted, set final values
        this.overlayDuringAnim.opacity = 0
    }

    @ComponentUtils.onlyRunInLifeCycleAsync
    private async showOwnOverlay(signal: AnimAbortSignal) {
        await AnimUtils.runTweenAsync(tween(this.overlayDuringAnim).to(0.3, { opacity: 150 }), signal)
    }

    private setProgress(payload: CurrentMapProgressPayload): void {
        const { currentTheme, nextTheme, progress, max } = payload

        this.journeyProgressAnimator.setCurrentLevelAvatar(currentTheme)

        // ! Handle case where nextMap is undefined. (i.e. last map)
        this.journeyProgressAnimator.setNextLevelAvatar(nextTheme ?? currentTheme)

        this.journeyProgressAnimator.setRange(progress, max)
    }

    private updateCurrentMapInStorage(currProgress: CurrentMapProgressPayload, resetAnimationQueue = false): void {
        const dashboardStorage = storage.getStorage('dashboard')

        if (dashboardStorage === null || dashboardStorage === undefined) {
            monitorError?.sendException(new Error('DashboardScene: dashboardStorage not found'))
            return
        }

        const { animateToCurrentLevel } = dashboardStorage

        storage.setStorageData(
            'dashboard',
            'animateToCurrentLevel',
            resetAnimationQueue ? false : animateToCurrentLevel,
        )

        storage.setStorageData('dashboard', 'lastMapPayload', currProgress)
    }

    private handleOrientationChanged(): void {
        this.leavesContainer.active = LayoutUtils.isLandscape()
        this.landscapeMask.node.active = LayoutUtils.isLandscape()
    }
    public transitionIn(): void {
        this.listenEvents()
        if (!this.landscapeMask.node.active) return
        this.landscapeMask.updateMaskPosition()
        this.landscapeMask.show()
    }

    public transitionOut(): void {
        this.unlistenEvents()
        if (!this.landscapeMask.node.active) return
        this.landscapeMask.hide()
    }

    @ComponentUtils.onlyRunInLifeCycle
    private hideContent(): void {
        this.contentOpacity.changeStateFade(false, 0.5)
    }

    @ComponentUtils.onlyRunInLifeCycle
    private showContent(): void {
        this.contentOpacity.changeStateFade(true, 0.1)
    }

    @ComponentUtils.onlyRunInLifeCycleAsync
    public async showCustomGuide(): Promise<void> {
        await this.customGuideUI.startGuide()
    }
}
