import {
    _decorator,
    Animation,
    AnimationClip,
    Node as CCNode,
    Component,
    Sprite,
    SpriteFrame,
    tween,
    UIOpacity,
    Vec3,
} from 'cc'
import AnimUtils from '../../../../../utils/AnimUtils'
import type AnimAbortSignal from '../../../../../utils/common/AnimAbortSignal'

const { ccclass, property, executeInEditMode } = _decorator

@ccclass('JourneyLevelAvatar')
@executeInEditMode
export class JourneyLevelAvatar extends Component {
    @property({ type: Sprite, displayName: 'Avatar node' })
    private sprite: Sprite | null = null

    @property(CCNode)
    private lockIcon!: CCNode

    @property(Animation)
    private animation!: Animation

    @property(UIOpacity)
    private opacity!: UIOpacity

    @property({ type: AnimationClip, group: 'Animation' })
    private flashAnimation!: AnimationClip

    @property({ type: AnimationClip, group: 'Animation' })
    private unlockAnimation!: AnimationClip

    @property({ type: AnimationClip, group: 'Animation' })
    private spawnAnimation!: AnimationClip

    @property({ type: SpriteFrame, displayName: 'Avatar sprite' })
    public get avatarSprite(): SpriteFrame | null {
        return this.sprite?.spriteFrame ?? null
    }

    public set avatarSprite(value: SpriteFrame | null) {
        if (this.sprite) {
            this.sprite.spriteFrame = value
        }
    }

    public async playUnlockAnimation(): Promise<void> {
        const anim = this.playAnimation(this.unlockAnimation)

        const startX = this.lockIcon.position.x
        const startY = this.lockIcon.position.y

        const randAngleRange = [45, 135] as const
        const randAngle =
            (Math.random() * (randAngleRange[1] - randAngleRange[0]) + randAngleRange[0]) * (Math.PI / 180)
        const distance = 150
        const randX = this.lockIcon.position.x + distance * Math.cos(randAngle)
        const randY = this.lockIcon.position.y + distance * Math.sin(randAngle)

        const endY = startY - 400

        // shoot the lock icon up and let it fall down to endY
        const movement = AnimUtils.runTweenAsync(
            tween(this.lockIcon)
                .delay(0.3)
                .to(
                    0.2,
                    {
                        position: new Vec3(randX, randY, this.lockIcon.position.z),
                    },
                    {
                        easing: 'sineOut',
                    },
                )
                .to(
                    0.4,
                    {
                        position: new Vec3(randX, endY, this.lockIcon.position.z),
                    },
                    {
                        easing: 'cubicIn',
                    },
                ),
        )

        // spin the lock icon to random angle
        const randRotation = [0, 360] as const
        const rotation = AnimUtils.runTweenAsync(
            tween(this.lockIcon)
                .delay(0.3)
                .to(
                    0.6,
                    {
                        eulerAngles: new Vec3(
                            0,
                            0,
                            Math.random() * (randRotation[1] - randRotation[0]) * (randX > startX ? -1 : 1),
                        ),
                    },
                    {
                        easing: 'sineOut',
                    },
                ),
        )

        await Promise.all([anim, movement, rotation])

        this.lockIcon.position = new Vec3(startX, startY, this.lockIcon.position.z)
        this.lockIcon.eulerAngles = new Vec3(0, 0, 0)
    }

    public async playFlashAnimation(signal: AnimAbortSignal): Promise<void> {
        const y = this.node.position.y
        const anim = this.playAnimation(this.flashAnimation)
        const t = AnimUtils.runTweenAsync(
            tween(this.node)
                .to(
                    0.1,
                    {
                        position: new Vec3(this.node.position.x, this.node.position.y + 10, this.node.position.z),
                    },
                    {
                        easing: 'sineOut',
                    },
                )
                .to(
                    0.1,
                    {
                        position: new Vec3(this.node.position.x, y, this.node.position.z),
                    },
                    {
                        easing: 'sineIn',
                    },
                ),
            signal,
        )

        await Promise.all([anim, t])
    }

    public playSpawnAnimation(): void {
        this.opacity.opacity = 255

        // ? assume animation is short and won't block main logic
        this.playAnimation(this.spawnAnimation)
    }

    public hideImmediately(): void {
        this.opacity.opacity = 0
        this.node.setScale(Vec3.ZERO)
    }

    private async playAnimation(clip: AnimationClip): Promise<void> {
        await AnimUtils.runAnimationAsync(this.animation, clip.name)
    }

    public onLoad(): void {
        this.validateRequiredComponents()
    }

    private validateRequiredComponents(): void {
        if (!this.sprite) {
            throw new Error('JourneyLevelAvatar: Avatar node is required')
        }

        if (!this.lockIcon) {
            throw new Error('JourneyLevelAvatar: Lock icon is required')
        }

        if (!this.flashAnimation) {
            throw new Error('JourneyLevelAvatar: Flash animation is required')
        }

        if (!this.unlockAnimation) {
            throw new Error('JourneyLevelAvatar: Unlock animation is required')
        }

        if (!this.animation) {
            throw new Error('JourneyLevelAvatar: Animation component is required')
        }

        if (
            !this.animation.clips.some((clip) => {
                if (clip === null) return false

                return (
                    clip.name === this.flashAnimation.name ||
                    clip.name === this.unlockAnimation.name ||
                    clip.name === this.spawnAnimation.name
                )
            })
        ) {
            throw new Error('JourneyLevelAvatar: Animation component does not contain required animations')
        }
    }
}
