const { player } = codex

class CustomModeUtils {
    protected static inst$: CustomModeUtils

    public static get inst() {
        if (!this.inst$) {
            this.inst$ = new this()
        }
        return this.inst$
    }

    public static getCustomBonus(): number {
        const { time, difficulty, bombs, ads } = this.getCustomData()

        return (
            this.getTimeBonus(time) +
            this.getDifficultyBonus(difficulty) +
            this.getBombsBonus(bombs) +
            this.getExtraAdsBonus(ads)
        )
    }

    public static getCustomData(): { time: number; difficulty: number; bombs: number; ads: boolean } {
        const { custom = { time: 4, difficulty: 1, bombs: 0, ads: false } } = player.getGameData() ?? {}

        return custom
    }

    protected static getTimeBonus(timeIndex: number): number {
        return (timeIndex - 4) * 5
    }

    protected static getDifficultyBonus(difficultyIndex: number): number {
        if (difficultyIndex === 3) return 30
        return (difficultyIndex - 1) * 20
    }

    protected static getBombsBonus(bombIndex: number): number {
        return bombIndex * 5
    }

    protected static getExtraAdsBonus(extraAds: boolean): number {
        return extraAds ? 75 : 0
    }
}

export default CustomModeUtils
