import { _decorator, CCBoolean, CCFloat, ScrollView, Vec3, type EventMouse, type Node } from 'cc'
const { ccclass, property } = _decorator

@ccclass('SmoothScrollView')
export class SmoothScrollView extends ScrollView {
    @property({ type: CCBoolean, group: 'Mouse Scroll' })
    private mouseScroll!: boolean

    @property({
        type: CCFloat,
        group: 'Mouse Scroll',
        visible: function (this: SmoothScrollView) {
            return this.mouseScroll
        },
    })
    private mouseScrollSpeed: number = 300

    @property({
        type: CCFloat,
        group: 'Mouse Scroll',
        visible: function (this: SmoothScrollView) {
            return this.mouseScroll
        },
    })
    private mouseScrollDuration: number = 0.5

    protected _onMouseWheel(event: EventMouse, captureListeners?: Node[]): void {
        if (!this.mouseScroll) {
            super._onMouseWheel(event, captureListeners)
            return
        }

        if (!this.enabledInHierarchy) {
            return
        }

        if (this._hasNestedViewGroup(event, captureListeners)) {
            return
        }

        const deltaMove = new Vec3()
        const scrollY = event.getScrollY()
        const direction = scrollY < 0 ? 1 : -1

        if (this.vertical) {
            deltaMove.set(0, direction * this.mouseScrollSpeed, 0)
        } else if (this.horizontal) {
            deltaMove.set(direction * this.mouseScrollSpeed, 0, 0)
        }

        if (!this._stopMouseWheel) {
            this._handlePressLogic()
            this.schedule(this._checkMouseWheel, 1.0 / 60)
            this._stopMouseWheel = true
        }

        this._startAutoScroll(deltaMove, this.mouseScrollDuration, true)
        this._stopPropagationIfTargetIsMe(event)
    }
}
