import { randomRangeInt } from 'cc'
import { Direction } from '../../../constants/Directions'

function bubbleUpCells<T>(config: {
    array: { length: number; get: (index: number) => T; swap: (a: number, b: number) => void }
    isLighter: (cell: T) => boolean
    out?: Set<T>
}): Set<T> {
    const { array, isLighter: isEmptyCell, out = new Set<T>() } = config
    for (let i = 0; i < array.length; i++) {
        if (isEmptyCell(array.get(i))) {
            for (let j = i + 1; j < array.length; j++) {
                if (!isEmptyCell(array.get(j))) {
                    array.swap(i, j)
                    out.add(array.get(i))
                    break
                }
            }
        }
    }

    return out
}

type GridConfig<T> = {
    width?: number
    height?: number
    content: ReadonlyArray<T>
}

export default class Grid<T> {
    private array: T[] = []
    public readonly width: number
    public readonly height: number

    public constructor()
    public constructor(config: GridConfig<T>)
    public constructor(copy: Grid<T>)

    public constructor(configOrGrid?: GridConfig<T> | Grid<T>) {
        const { width = 1, height = 1 } = configOrGrid ?? {}
        const content = configOrGrid instanceof Grid ? configOrGrid.array : (configOrGrid?.content ?? [])

        if (content.length !== width * height) {
            throw new Error('Invalid content length')
        }

        this.width = width
        this.height = height
        this.array = [...content]
    }

    public asArray(): ReadonlyArray<T> {
        return this.array
    }

    public get(x: number, y: number): T {
        if (!this.isInBounds(x, y)) {
            throw new Error('Out of bounds')
        }

        return this.array[y * this.width + x] as T
    }

    public set(x: number, y: number, value: Readonly<T>): void {
        this.array[y * this.width + x] = value
    }

    public isInBounds(x: number, y: number): boolean {
        return x >= 0 && x < this.width && y >= 0 && y < this.height
    }

    public each(callback: (value: T, x: number, y: number) => void): void {
        for (let y = 0; y < this.height; y++) {
            for (let x = 0; x < this.width; x++) {
                callback(this.get(x, y), x, y)
            }
        }
    }

    public map<U>(callback: (value: T, x: number, y: number) => U): U[] {
        const result: U[] = []
        for (let y = 0; y < this.height; y++) {
            for (let x = 0; x < this.width; x++) {
                result.push(callback(this.get(x, y), x, y))
            }
        }
        return result
    }

    public reduce<U>(callback: (accumulator: U, value: T, x: number, y: number) => U, initialValue: U): U {
        let result = initialValue
        for (let y = 0; y < this.height; y++) {
            for (let x = 0; x < this.width; x++) {
                result = callback(result, this.get(x, y), x, y)
            }
        }
        return result
    }

    public swap(a: [number, number], b: [number, number]): void {
        const [x1, y1] = a
        const [x2, y2] = b

        const temp = this.get(x1, y1)
        this.set(x1, y1, this.get(x2, y2))
        this.set(x2, y2, temp)
    }

    public shuffle(cells?: ReadonlyArray<[number, number]>): Set<T> {
        const c = cells ?? this.map((_, x, y) => [x, y])
        const shuffledCells = [...c]

        const affected = new Set<T>()

        for (let i = shuffledCells.length - 1; i > 0; i--) {
            const j = randomRangeInt(0, i + 1)
            const left = shuffledCells[i] as [number, number]
            const right = shuffledCells[j] as [number, number]

            shuffledCells[i] = right
            shuffledCells[j] = left

            affected.add(this.get(left[0], left[1]))
            affected.add(this.get(right[0], right[1]))
        }

        // Apply the shuffled cells to the grid
        for (let i = 0; i < shuffledCells.length; i++) {
            const [x, y] = c[i] as [number, number]
            const [newX, newY] = shuffledCells[i] as [number, number]
            this.swap([x, y], [newX, newY])
        }

        return affected
    }

    public toShuffled(cells?: ReadonlyArray<[number, number]>): [Grid<T>, Set<T>] {
        const newGrid = new Grid<T>({
            width: this.width,
            height: this.height,
            content: this.array,
        })
        const affected = newGrid.shuffle(cells)
        return [newGrid, affected]
    }

    public gravitate(config: {
        rowRange: Readonly<[number, number]>
        colRange: Readonly<[number, number]>
        direction: DirectionType
        isLighter: (cell: T) => boolean
        onSwap?: (cellA: T, cellB: T) => void
        out?: Set<T>
    }): Set<T> {
        const {
            rowRange: [minRow, maxRow],
            colRange: [minCol, maxCol],
            direction,
            isLighter,
            onSwap,
            out = new Set(),
        } = config

        const width = maxCol - minCol + 1
        const height = maxRow - minRow + 1

        switch (direction) {
            case Direction.Up:
                for (let x = minCol; x <= maxCol; x++) {
                    bubbleUpCells({
                        array: {
                            length: height,
                            get: (i) => this.get(x, maxRow - i),
                            swap: (a, b) => {
                                const cellA = this.get(x, maxRow - a)
                                const cellB = this.get(x, maxRow - b)
                                this.swap([x, maxRow - a], [x, maxRow - b])
                                onSwap?.(cellA, cellB)
                            },
                        },
                        isLighter,
                        out,
                    })
                }

                return out

            case Direction.Down:
                for (let x = minCol; x <= maxCol; x++) {
                    bubbleUpCells({
                        array: {
                            length: height,
                            get: (i) => this.get(x, minRow + i),
                            swap: (a, b) => {
                                const cellA = this.get(x, minRow + a)
                                const cellB = this.get(x, minRow + b)
                                this.swap([x, minRow + a], [x, minRow + b])
                                onSwap?.(cellA, cellB)
                            },
                        },
                        isLighter,
                        out,
                    })
                }

                return out

            case Direction.Left:
                for (let y = minRow; y <= maxRow; y++) {
                    bubbleUpCells({
                        array: {
                            length: width,
                            get: (i) => this.get(minCol + i, y),
                            swap: (a, b) => {
                                const cellA = this.get(minCol + a, y)
                                const cellB = this.get(minCol + b, y)
                                this.swap([minCol + a, y], [minCol + b, y])
                                onSwap?.(cellA, cellB)
                            },
                        },
                        isLighter,
                        out,
                    })
                }

                return out

            case Direction.Right:
                for (let y = minRow; y <= maxRow; y++) {
                    bubbleUpCells({
                        array: {
                            length: width,
                            get: (i) => this.get(maxCol - i, y),
                            swap: (a, b) => {
                                const cellA = this.get(maxCol - a, y)
                                const cellB = this.get(maxCol - b, y)
                                this.swap([maxCol - a, y], [maxCol - b, y])
                                onSwap?.(cellA, cellB)
                            },
                        },
                        isLighter,
                        out,
                    })
                }

                return out

            default:
                throw new Error('Invalid direction')
        }
    }

    public toGravitated(...args: Parameters<Grid<T>['gravitate']>): [Grid<T>, ReturnType<Grid<T>['gravitate']>] {
        const newGrid = new Grid<T>({
            width: this.width,
            height: this.height,
            content: this.array,
        })
        const affected = newGrid.gravitate(...args)
        return [newGrid, affected]
    }

    public toRotated(revert = false): Grid<T> {
        const newGrid = new Grid<T>({
            width: this.height,
            height: this.width,
            content: new Array(this.width * this.height),
        })

        if (!revert) {
            for (let y = this.height - 1; y >= 0; y--) {
                for (let x = 0; x < this.width; x++) {
                    newGrid.set(this.height - 1 - y, x, this.get(x, y))
                }
            }
        } else {
            for (let y = 0; y < this.height; y++) {
                for (let x = this.width - 1; x >= 0; x--) {
                    newGrid.set(y, this.width - 1 - x, this.get(x, y))
                }
            }
        }

        return newGrid
    }
}
