import { Component, Label, Node, _decorator } from 'cc'
import GameModes from 'db://assets/gameplay/constants/GameMode'
import CustomModeUtils from 'db://assets/utils/CustomModeUtils'
import MatchUtils from 'db://assets/utils/MatchUtils'

const { ccclass, property, disallowMultiple } = _decorator

@ccclass('CustomScoreContainer')
@disallowMultiple
export class CustomScoreContainer extends Component {
    @property(Node)
    private customScoreBlock!: Node

    @property(Label)
    private scoreText!: Label

    public updateStatus(): void {
        if (MatchUtils.getMatchMode() !== GameModes.CUSTOM) {
            this.customScoreBlock.active = false
            return
        }

        this.customScoreBlock.active = true
        this.scoreText.string = `${CustomModeUtils.getCustomBonus()}%`
    }
}
