import {
    CCInteger,
    CCString,
    type CacheMode,
    Component,
    Director,
    Label,
    Layers,
    Node,
    Sprite,
    UIOpacity,
    _decorator,
    director,
} from 'cc'
const { ccclass, executionOrder, property } = _decorator

const {
    Utils: { Decorator, Array: A },
} = Ludex

type Nodes = {
    [key: string]: Node
}

const BBN = 'Batch By Name'
const BL = 'Batch Label'
const BS = 'Batch Sprite'

@ccclass('BatchNodes')
@executionOrder(99999)
export class BatchNodes extends Component {
    @property({
        group: { name: BBN },
    })
    public batchByName = false

    @property({
        type: [CCInteger],
        group: { name: BBN },
    })
    public depthNames: number[] = []

    @property({
        type: [CCString],
        group: { name: BBN },
    })
    public listName: string[] = []

    @property({
        group: { name: BL },
    })
    public batchLabel = false

    @property({
        group: { name: BL },
    })
    public depthLabel = 1

    @property({
        group: { name: BL },
    })
    public typeLabel = 'char'

    @property({
        group: { name: BS },
    })
    public batchSprite = false

    @property({
        group: { name: BS },
    })
    public depthSprite = 0

    @property({
        readonly: true,
    })
    public batchAllNodes = false

    @property({
        readonly: true,
    })
    public batchDepth = 0

    @property({
        readonly: true,
    })
    public checkOverlap = false

    private batchNode: Nodes = {}

    private labelChildren: Label[] = []
    private spriteChildren: Sprite[] = []
    private allChildren: Component[] = []
    private nameChildren: Node[] = []

    private originalParents: Nodes = {}

    private isInited = false

    public __preload(): void {
        if (!this.enabled) return
        director.on(Director.EVENT_AFTER_UPDATE, this.onAfterUpdate)
        director.on(Director.EVENT_AFTER_RENDER, this.onAfterRender)
    }

    public onDestroy(): void {
        director.off(Director.EVENT_AFTER_UPDATE, this.onAfterUpdate)
        director.off(Director.EVENT_AFTER_RENDER, this.onAfterRender)
    }

    public onLoad(): void {
        if (!this.enabled) return
        this.setupBatchNodes(this.node)
        this.isInited = true
    }

    public onEnable(): void {
        if (this.isInited) return

        director.on(Director.EVENT_AFTER_UPDATE, this.onAfterUpdate)
        director.on(Director.EVENT_AFTER_RENDER, this.onAfterRender)

        this.setupBatchNodes(this.node)
        this.isInited = true
    }

    private handleActiveChange(node: Node): void {
        node.once(Node.EventType.ACTIVE_IN_HIERARCHY_CHANGED, this.handleNodeChange)
    }

    @Decorator.debounce(1000)
    private handleNodeChange = (): void => {
        if (!this.isInited) return
        console.warn('handleNodeChange', this.node.name)
        this.setupBatchNodes(this.node)
    }

    public onDisable(): void {
        this.restoreOriginalChildren()
        director.off(Director.EVENT_AFTER_UPDATE, this.onAfterUpdate)
        director.off(Director.EVENT_AFTER_RENDER, this.onAfterRender)
        this.isInited = false
    }

    private setupBatchNodes(node: Node): void {
        this.allChildren = []
        this.nameChildren = []
        this.labelChildren = []
        this.spriteChildren = []

        this.saveOriginalChildren(node)

        this.createGlobalNodes(node)
        this.createGlobalNameNodes(node)
        this.createGlobalLabelNode(node)
        this.createGlobalSpriteNode(node)

        this.sortGlobalNodes()
    }

    private sortGlobalNodes(): void {
        this.batchNode.node?.setSiblingIndex(0)
        this.batchNode.label?.setSiblingIndex(this.depthLabel)
        this.batchNode.sprite?.setSiblingIndex(this.depthSprite)

        this.listName.forEach((name, idx) => {
            const depth = this.depthNames[idx]
            if (depth === undefined) {
                console.warn('BatchNodes: depth is undefined', name, idx, depth)
                return
            }

            this.batchNode[name]?.setSiblingIndex(depth)
        })
    }

    private createGlobalNodes(node: Node): void {
        if (!this.batchAllNodes) return

        const batchNode = new Node('GlobalBatchNode')
        batchNode.active = true

        this.batchNode.node = batchNode

        node.addChild(batchNode)
    }

    private createGlobalNameNodes(node: Node): void {
        if (!this.batchByName) return

        for (const name of this.listName) {
            if (this.batchNode[name]) continue

            const batchNode = new Node(name)
            batchNode.active = true

            this.batchNode[name] = batchNode

            node.addChild(batchNode)
        }
    }

    private createGlobalLabelNode(node: Node): void {
        if (!this.batchLabel) return

        const labelNode = new Node('GlobalLabelNode')
        labelNode.active = true
        this.batchNode.label = labelNode

        node.addChild(labelNode)
    }

    private createGlobalSpriteNode(node: Node): void {
        if (!this.batchSprite) return

        const spriteNode = new Node('GlobalSpriteNode')
        spriteNode.active = true

        this.batchNode.sprite = spriteNode

        node.addChild(spriteNode)
    }

    private saveOriginalParents(): void {
        const componentChildren = [...this.labelChildren, ...this.spriteChildren]
        const nodeChildren = this.nameChildren

        for (const child of componentChildren) {
            if (child.node.parent) {
                this.handleActiveChange(child.node)
                this.originalParents[child.uuid] = child.node.parent
            }
        }

        for (const child of nodeChildren) {
            if (child.parent) {
                this.handleActiveChange(child)
                this.originalParents[child.uuid] = child.parent
            }
        }
    }

    private saveOriginalChildren(node: Node): void {
        const children = node.children

        for (const child of children) {
            this.saveLabelChildren(child)
            this.saveSpriteChildren(child)
            this.saveNodeChildrenByName(child)
            this.saveNodeChildrenByType(child)
        }

        this.saveOriginalParents()
    }

    private saveLabelChildren(node: Node): void {
        if (!this.batchLabel) return

        let childLabels = node.getComponentsInChildren(Label)

        const cacheModeMap: Record<string, CacheMode> = {
            none: Label.CacheMode.NONE,
            char: Label.CacheMode.CHAR,
            bitmap: Label.CacheMode.BITMAP,
        }

        const cacheMode = cacheModeMap[this.typeLabel]

        if (cacheMode) {
            childLabels = childLabels.filter((label) => label.cacheMode === cacheMode)
        }

        if (childLabels.length === 0) return

        this.labelChildren.push(...childLabels)
    }

    private saveSpriteChildren(node: Node): void {
        if (!this.batchSprite) return

        const childSprites = node.getComponentsInChildren(Sprite)

        if (childSprites.length === 0) return

        this.spriteChildren.push(...childSprites)
    }

    private saveNodeChildrenByName(node: Node): void {
        if (!this.batchByName) return

        for (const child of node.children) {
            if (A.has(this.listName, child.name)) {
                this.nameChildren.push(child)
            } else {
                this.saveNodeChildrenByName(child)
            }
        }
    }

    private saveNodeChildrenByType(node: Node, depth = 0): void {
        if (!this.batchAllNodes) return

        const nodes = node.getComponentsInChildren(Component)
        this.allChildren.push(...nodes)

        if (this.batchDepth < 1 || depth >= this.batchDepth) return

        for (const child of this.allChildren) {
            this.saveNodeChildrenByType(child.node, this.batchDepth)
        }
    }

    public onAfterUpdate = (): void => {
        if (!this.isInited) return
        this.hierarchyChildren()
    }

    private onAfterRender = (): void => {
        if (!this.isInited) return
        this.restoreOriginalChildren()
    }

    private hierarchyChildren(): void {
        const updateParent = (child: Component, parent: Node | undefined): void => {
            if (!child.node.isValid || !child.node.activeInHierarchy || !parent || child.node.isChildOf(parent)) return
            child.node.setParent(parent, true)
            child.node.layer = Layers.Enum.UI_2D
        }

        const skipNodes = ['GlobalLabelNode', 'GlobalSpriteNode', 'GlobalBatchNode']

        for (const label of this.labelChildren) {
            updateParent(label, this.batchNode.label)
        }

        for (const sprite of this.spriteChildren) {
            updateParent(sprite, this.batchNode.sprite)
        }

        for (const child of this.allChildren) {
            updateParent(child, this.batchNode.node)
        }

        // sort by type of component
        this.allChildren.sort((a, b) => a.constructor.name.localeCompare(b.constructor.name))

        // sort label by cacheMode
        this.labelChildren.sort((a, b) => a.cacheMode - b.cacheMode)

        for (const child of this.nameChildren) {
            if (!child.isValid || !child.activeInHierarchy || !child.isTransformDirty()) continue

            const opacity = child.getComponent(UIOpacity)
            if (opacity && opacity.opacity < 1) continue

            // Skip specific nodes
            if (A.has(skipNodes, child.name)) continue

            // Update parent for Node type
            const batchNode = this.batchNode[child.name]
            if (!batchNode) continue

            if (!child.isChildOf(batchNode)) {
                child.setParent(batchNode, true)
                child.layer = Layers.Enum.UI_2D
            }
        }
    }

    private restoreOriginalChildren(): void {
        const children = [...this.labelChildren, ...this.spriteChildren]
        if (children.length > 0) {
            for (const child of children) {
                const parent = this.originalParents[child.uuid]
                if (!parent) continue

                if (child.node.isChildOf(parent)) continue

                child.node.setParent(parent, true)
            }
        }

        for (const child of this.nameChildren) {
            const parent = this.originalParents[child.uuid]
            if (!parent) continue

            if (child.isChildOf(parent)) continue

            child.setParent(parent, true)
        }
    }
}
