import { HorizontalLevelGravities, LevelGravities, VerticalLevelGravities } from '../../../../constants/LevelGravity'
import { RandomizedLevelDataConfig } from '../../constants/RandomizedLevelDataConfig'

type DifficultyGenerator = (t: number) => number
type RangeReducer = (t: number) => [number, number]

const createRangeReducerRight =
    (a: number, b: number): RangeReducer =>
    (t: number) => {
        if (t <= 0) return [a, b]
        if (t >= 1) return [b, b]
        return [Math.floor(a + t * (b - a)), b]
    }

const createRangeReducerLeft = (b: number, a: number): RangeReducer => {
    const l = createRangeReducerRight(a, b)
    return (t: number) => l(t)
}

const getRandomIntInclusive = (min: number, max: number) => {
    const newMin = Math.ceil(min)
    const newMax = Math.floor(max)
    return Math.floor(Math.random() * (newMax - newMin + 1)) + newMin
}

const createRangedReducingGenerator = (rangeReducer: RangeReducer): DifficultyGenerator => {
    return (t: number) => {
        return getRandomIntInclusive(...rangeReducer(t))
    }
}

const createDifficultyGenerator = (loopAfter: number) => (level: number) => {
    if (loopAfter <= 1) return 0
    return ((level - 1) % loopAfter) / (loopAfter - 1)
}

const generateGravity = () => {
    const dontPickAny = Math.random() > 0.75
    if (dontPickAny) return 'normal'

    const pickOne = Math.random() > 0.5
    if (pickOne) return LevelGravities[Math.floor(Math.random() * LevelGravities.length)] as string

    const vertical = VerticalLevelGravities[Math.floor(Math.random() * VerticalLevelGravities.length)] as string
    const horizontal = HorizontalLevelGravities[Math.floor(Math.random() * HorizontalLevelGravities.length)] as string

    return [vertical, horizontal].join('-')
}

const LevelDataGenerators = {
    generateDifficulty: createDifficultyGenerator(RandomizedLevelDataConfig.loopLevelDifficulty),
    generateTime: createRangedReducingGenerator(createRangeReducerLeft(...RandomizedLevelDataConfig.timeRange)),
    generateColors: createRangedReducingGenerator(createRangeReducerRight(...RandomizedLevelDataConfig.colorsRange)),
    specials: {
        generateBomb: createRangedReducingGenerator(
            createRangeReducerRight(...RandomizedLevelDataConfig.specials.bombRange),
        ),
        generateRocket: createRangedReducingGenerator(
            createRangeReducerRight(...RandomizedLevelDataConfig.specials.rocketRange),
        ),
    },
    rules: {
        generateMaxSpaceOfPair: createRangedReducingGenerator(
            createRangeReducerRight(...RandomizedLevelDataConfig.rules.maxSpaceOfPairRange),
        ),
        generateAddRandomTilesPerMove: createRangedReducingGenerator(
            createRangeReducerRight(...RandomizedLevelDataConfig.rules.addRandomTilesPerMoveRange),
        ),
    },
    generateColumns: createRangedReducingGenerator(createRangeReducerRight(...RandomizedLevelDataConfig.columnsRange)),
    generateRows: createRangedReducingGenerator(createRangeReducerRight(...RandomizedLevelDataConfig.rowsRange)),
} as const

export type RandomizedLevelData = {
    gravity: string
    time: number
    colors: number
    specials: {
        bomb: number
        rocket: number
    }
    rules: {
        maxSpaceOfPair: number
        addRandomTilesPerMove: number
    }
    columns: number
    rows: number
}

export default function createRandomizedLevelData(level: number): RandomizedLevelData {
    const difficulty = LevelDataGenerators.generateDifficulty(level)

    const cols = LevelDataGenerators.generateColumns(difficulty)
    const rows = LevelDataGenerators.generateRows(difficulty)

    // ? ensures that there are an even number of tiles
    const safeRows = cols % 2 === 1 && rows % 2 === 1 ? rows + 1 : rows

    return {
        rows: safeRows,
        columns: cols,
        colors: LevelDataGenerators.generateColors(difficulty),
        gravity: generateGravity(),
        rules: {
            addRandomTilesPerMove: LevelDataGenerators.rules.generateAddRandomTilesPerMove(difficulty),
            maxSpaceOfPair: LevelDataGenerators.rules.generateMaxSpaceOfPair(difficulty),
        },
        specials: {
            bomb: LevelDataGenerators.specials.generateBomb(difficulty),
            rocket: LevelDataGenerators.specials.generateRocket(difficulty),
        },
        time: LevelDataGenerators.generateTime(difficulty),
    }
}
