import { _decorator, Color, Component, Label, Node, Sprite, SpriteFrame, UIOpacity } from 'cc'
import { Localize } from '../../../components/text/Localize'
import ComponentUtils from '../../../utils/ComponentUtils'

const { ccclass, property } = _decorator

export interface IDayPayload {
    day: number
    type: string
    value: number
    status?: string
}

const DAY_LABEL_BLUE_OUTLINE_COLOR = '#3469bf'

@ccclass('DayBlock')
export class DayBlock extends Component {
    @ComponentUtils.required()
    @property({ type: Sprite, group: 'Background' })
    protected background!: Sprite

    @ComponentUtils.required()
    @property({ type: Sprite, group: 'Background' })
    protected topBackground!: Sprite

    @ComponentUtils.required()
    @property({ type: SpriteFrame, group: 'Background' })
    protected blueBackground!: SpriteFrame

    @ComponentUtils.required()
    @property({ type: SpriteFrame, group: 'Background' })
    protected blueTopBackground!: SpriteFrame

    protected defaultBackground!: SpriteFrame

    protected defaultTopBackground!: SpriteFrame

    @ComponentUtils.required()
    @property(Label)
    protected dayLabel!: Label

    @ComponentUtils.required()
    @property(Localize)
    protected dayLocalizer!: Localize

    protected defaultDayLabelOutlineColor!: Color

    protected blueDayLabelOutlineColor!: Color

    @ComponentUtils.required()
    @property(Label)
    private coinLabel!: Label

    @ComponentUtils.required()
    @property(Node)
    private rewardItemsNode!: Node

    @ComponentUtils.required()
    @property(Node)
    private checkNode!: Node

    @ComponentUtils.required()
    @property(Node)
    protected glowNodes: Node[] = []

    @ComponentUtils.required()
    @property({ type: UIOpacity })
    public uiOpacity!: UIOpacity

    public onLoad() {
        ComponentUtils.validateRequiredProperties(this, DayBlock)

        this.initDefaultProperties()
    }

    private initDefaultProperties(): void {
        this.defaultBackground = this.background.spriteFrame!
        this.defaultTopBackground = this.topBackground.spriteFrame!

        this.defaultDayLabelOutlineColor = new Color(this.dayLabel.outlineColor)
        this.blueDayLabelOutlineColor = new Color(DAY_LABEL_BLUE_OUTLINE_COLOR)
    }

    public updateInfo(payload: IDayPayload): void {
        this.updateBackground(payload)
        this.updateDayLabel(payload)

        this.coinLabel.string = payload.value.toString()

        this.updateRewardItems(payload)
    }

    protected updateBackground(payload: IDayPayload): void {
        if (!this.blueTopBackground || !this.blueBackground) {
            return
        }

        switch (payload.status) {
            case 'ready':
                this.background.spriteFrame = this.blueBackground
                this.topBackground.spriteFrame = this.blueTopBackground
                break
            case 'waiting':
                this.background.spriteFrame = this.defaultBackground
                this.topBackground.spriteFrame = this.blueTopBackground
                break
            case 'rewarded':
                this.background.spriteFrame = this.defaultBackground
                this.topBackground.spriteFrame = this.defaultTopBackground
                break
        }
    }

    protected updateDayLabel(payload: IDayPayload): void {
        this.dayLocalizer.setParams([payload.day.toString()])

        let dayLabelColor = this.defaultDayLabelOutlineColor

        if (payload.status !== 'rewarded') {
            dayLabelColor = this.blueDayLabelOutlineColor
        }

        this.dayLabel.outlineColor = dayLabelColor
        this.dayLabel.shadowColor = dayLabelColor
    }

    private updateRewardItems(payload: IDayPayload): void {
        switch (payload.status) {
            case 'ready':
                this.rewardItemsNode.active = true
                this.checkNode.active = false
                this.setGlowNodesActive(true)
                break
            case 'waiting':
                this.rewardItemsNode.active = true
                this.checkNode.active = false
                this.setGlowNodesActive(false)
                break
            case 'rewarded':
                this.rewardItemsNode.active = false
                this.checkNode.active = true
                this.setGlowNodesActive(false)
                break
        }
    }

    protected setGlowNodesActive(active: boolean): void {
        if (!this.glowNodes) return

        // biome-ignore lint/complexity/noForEach: simple
        this.glowNodes.forEach((node) => {
            node.active = active
        })
    }
}
