import { _decorator, CCBoolean, Component, ccenum, game, Sprite } from 'cc'
import GameEvents from '../../constants/GameEvents'
import GameWorld from '../../constants/GameWorld'
import { ResourcePathsEnum, ResourcePathsMapping } from '../../gameplay/constants/ResourcePaths'
import ComponentUtils from '../../utils/ComponentUtils'
import LayoutUtils from '../../utils/LayoutUtils'
import ResourceUtils from '../../utils/ResourceUtils'

const { ccclass, property, requireComponent } = _decorator
const { Layout } = GameWorld

enum LayoutEnum {
    Portrait = 0,
    Landscape = 1,
}

const LayoutMapping = [Layout.Portrait, Layout.Landscape]

ccenum(LayoutEnum)

@ccclass('PreloadSprite')
@requireComponent(Sprite)
export class PreloadSprite extends Component {
    @property({ type: LayoutEnum })
    public layout!: LayoutEnum

    @property({ type: ResourcePathsEnum })
    public path = 0

    @property({ type: CCBoolean })
    public packable!: boolean

    @ComponentUtils.required()
    private sprite!: Sprite

    protected __preload(): void {
        this.sprite = this.getComponent(Sprite) as Sprite
        ComponentUtils.validateRequiredProperties(this, PreloadSprite)

        game.on(GameEvents.ORIENTATION_CHANGED, this.loadSpriteFrame, this)
        this.loadSpriteFrame()
    }

    protected onDestroy(): void {
        game.off(GameEvents.ORIENTATION_CHANGED, this.loadSpriteFrame, this)
    }

    private async loadSpriteFrame(): Promise<void> {
        if (this.sprite.spriteFrame) {
            this.unlistenEvent()
            return
        }
        if (LayoutUtils.getCurrentLayout() !== LayoutMapping[this.layout]) return

        if (!this.path) return
        const path = `${ResourcePathsMapping[this.path]}/spriteFrame`
        if (!path) return

        const spriteFrame = await ResourceUtils.loadSpriteFrameAsync(path, this.packable)

        if (!this.enabledInHierarchy || this.sprite.spriteFrame) {
            this.unlistenEvent()
            return
        }

        this.sprite.spriteFrame = spriteFrame
        this.unlistenEvent()
    }

    private unlistenEvent(): void {
        game.off(GameEvents.ORIENTATION_CHANGED, this.loadSpriteFrame, this)
    }
}
