import LeaderboardEvents from 'db://assets/gameplay/constants/Leaderboard'
import { SwitchButton } from 'db://assets/screens/settings-screen/components/switch-button/SwitchButton'
import AnalyticsUtils from 'db://assets/utils/AnalyticsUtils'
import { Button, Component, _decorator } from 'cc'
import { type LeaderboardType, LeaderboardTypes } from '../../../../../../../../constants/Leaderboard'
import { CodeSoundKeys } from '../../../../../../../../constants/SoundKeys'
import AudioUtils from '../../../../../../../../utils/AudioUtils'
const { ccclass, property } = _decorator

const { analytics } = codex
const {
    Plugins: {
        Analytics: { Events: AnalyticsEvents } = {},
    },
} = Ludex


@ccclass('Tabs')
export class Tabs extends Component {
    @property(SwitchButton)
    public tabDaily!: SwitchButton

    @property(SwitchButton)
    public tabWeekly!: SwitchButton

    public setInteractableButtons(interactable: boolean): void {
        this.tabDaily.getComponent(Button)!.interactable = interactable
        this.tabWeekly.getComponent(Button)!.interactable = interactable
    }

    public updateStatus(type: LeaderboardType): void {
        this.tabDaily.setEnabled(type === LeaderboardTypes.DAILY)
        this.tabWeekly.setEnabled(type === LeaderboardTypes.WEEKLY)
    }

    public handleTabDailySelect(): void {
        this.tabDaily.setEnabled(true)
        this.tabWeekly.setEnabled(false)

        AudioUtils.playSound(CodeSoundKeys.BUTTON_CLICK)

        if (AnalyticsEvents) {
            analytics.event(AnalyticsEvents.BUTTON_CLICK, {
                button_name: this.node.name,
                screen_name: AnalyticsUtils.getCurrentPage(),
            })
        }

        this.node.scene.emit(LeaderboardEvents.WORLD_TAB_SELECTED)
    }

    public handleTabWeeklySelect(): void {
        this.tabDaily.setEnabled(false)
        this.tabWeekly.setEnabled(true)

        AudioUtils.playSound(CodeSoundKeys.BUTTON_CLICK)

        if (AnalyticsEvents) {
            analytics.event(AnalyticsEvents.BUTTON_CLICK, {
                button_name: this.node.name,
                screen_name: AnalyticsUtils.getCurrentPage(),
            })
        }

        this.node.scene.emit(LeaderboardEvents.WEEKLY_TAB_SELECTED)
    }
}
