import { Component, UIOpacity, _decorator } from 'cc'
import { TapperedProgressBar } from '../../../../../components/tappered-progress-bar/TapperedProgressBar'
import GameplayEvents from '../../../../../gameplay/constants/GameEvents'

const { ccclass } = _decorator

const COMBO_DURATION = 2

@ccclass('ComboProgressBar')
export class ComboProgressBar extends Component {
    private progressBar!: TapperedProgressBar
    private comboTimer!: number
    private isActive!: boolean
    private opacity!: UIOpacity

    public start(): void {
        this.progressBar = this.getComponent<TapperedProgressBar>(TapperedProgressBar)!
        this.opacity = this.getComponent<UIOpacity>(UIOpacity)!

        this.comboTimer = 0
        this.isActive = false

        this.node.scene.on(GameplayEvents.COMBO_UPDATED, this.handleComboUpdated, this)
    }

    public update(dt: number): void {
        if (!this.isActive) return

        if (this.comboTimer > 0) {
            this.comboTimer -= dt
            this.progressBar.progress = this.comboTimer / COMBO_DURATION
        } else {
            this.isActive = false
            this.opacity.opacity = 0
            this.node.scene.emit(GameplayEvents.COMBO_ENDED)
        }
    }

    public handleComboUpdated(combo: number): void {
        if (combo === 0) this.opacity.opacity = 0
        else if (combo > 0) {
            this.isActive = true
            this.comboTimer = COMBO_DURATION
        }
        if (combo > 1) {
            this.opacity.opacity = 255
        }
    }

    public pause(): void {
        this.isActive = false
    }

    public resume(): void {
        this.isActive = true
    }
}
