import { pseudoRandomRangeInt } from 'cc'
import Grid from '../../grid'
import pseudoRandomIterable from '../random'

const {
    Utils: { Array: A },
} = Ludex

type LevelData = {
    columns: number
    rows: number
    rules: {
        maxSpaceOfPair: number
    }
}

export default function createGrid<I, T>(config: {
    levelData: LevelData
    identities: ReadonlyArray<I>
    setTileIdentity: (tile: T, id: I) => void
    createTile: (id: I) => T
    seed?: number
}): Grid<T> {
    const {
        levelData: { columns, rows },
        identities,
        setTileIdentity,
        createTile,
        seed = Date.now(),
    } = config

    const pairs = createShuffledIdentityPairs(seed, columns * rows, identities)

    const firstSort = sortTilesByMaxSpace(config.levelData, identities, pairs).reverse()
    const secondSort = sortTilesByMaxSpace(config.levelData, identities, firstSort).reverse()

    const tiles = secondSort.map((id) => {
        const tile = createTile(id)
        setTileIdentity(tile, id)
        return tile
    })

    return new Grid({
        width: columns,
        height: rows,
        content: tiles,
    })
}

function createShuffledIdentityPairs<I>(seed: number, size: number, identities: ReadonlyArray<I>): I[] {
    const pairs: I[] = []
    const half = size / 2
    const rng = (s: number) => pseudoRandomRangeInt(s, 0, identities.length)

    for (const index of pseudoRandomIterable(half, seed, rng)) {
        pairs.push(identities[index] as (typeof identities)[number])
        pairs.push(identities[index] as (typeof identities)[number])
    }

    A.shuffle(pairs)

    return pairs
}

function sortTilesByMaxSpace<I>(levelData: LevelData, sortColors: ReadonlyArray<I>, randomTiles: I[]): I[] {
    const { rows, columns, rules } = levelData

    if (!rules.maxSpaceOfPair) return randomTiles

    const tileGrid = convertSingleListToGrid(randomTiles, rows, columns)

    for (const element of sortColors) {
        let previousTileRow: number | null = null
        let previousTileColumn: number | null = null

        for (let row = 0; row < rows; row++) {
            for (let column = 0; column < columns; column++) {
                const tile = (tileGrid[row] as I[])[column] as I
                if (tile !== element) continue

                if (!previousTileRow || !previousTileColumn) {
                    previousTileRow = row
                    previousTileColumn = column
                    continue
                }

                const allowedDistance = rules.maxSpaceOfPair + 1
                const distance = Math.abs(row - previousTileRow) + Math.abs(column - previousTileColumn)

                if (distance <= allowedDistance) {
                    previousTileColumn = column
                    previousTileRow = row
                    continue
                }

                const newPos = getTileNewPosition(previousTileRow, previousTileColumn, row, column)

                const temp = (tileGrid[newPos.row] as I[])[newPos.column] as I
                ;(tileGrid[newPos.row] as I[])[newPos.column] = (tileGrid[row] as I[])[column] as I
                ;(tileGrid[row] as I[])[column] = temp
            }
        }
    }

    return convertGridToSingleList(tileGrid)
}

function convertSingleListToGrid<I>(list: I[], rows: number, columns: number): I[][] {
    const grid: I[][] = []

    for (let i = 0; i < rows; i++) {
        grid[i] = []

        for (let j = 0; j < columns; j++) {
            ;(grid[i] as I[])[j] = list[i * columns + j] as I
        }
    }

    return grid
}

function convertGridToSingleList<I>(grid: I[][]): I[] {
    const list: I[] = []

    for (const row of grid) {
        for (const element of row) {
            list.push(element)
        }
    }

    return list
}

function getTileNewPosition(
    row1: number,
    column1: number,
    row2: number,
    column2: number,
): { row: number; column: number } {
    let newRow = row1
    let newColumn = column1

    if (row2 > row1) {
        newRow = row1 + 1
    }
    if (row2 < row1) {
        newRow = row1 - 1
    }

    if (column2 > column1) {
        newColumn = column1 + 1
    }
    if (column2 < column1) {
        newColumn = column1 - 1
    }

    return { row: newRow, column: newColumn }
}
