import { CCBoolean, CCFloat, Canvas, Component, UITransform, Vec2, Vec3, _decorator } from 'cc'

const { ccclass, property } = _decorator

@ccclass('RigidBodyObject')
export class RigidBodyObject extends Component {
    @property(CCFloat)
    public gravityScale = 1

    @property(Vec2)
    public linearVelocity = new Vec2(0, 0)

    @property(CCFloat)
    private bounce!: number

    @property(CCBoolean)
    public simulate!: boolean

    private uiTransform!: UITransform

    private canvas!: UITransform

    public onLoad() {
        this.canvas = this.node.scene.getComponentInChildren(Canvas)!.getComponent(UITransform)!

        this.uiTransform = this.node.getComponent(UITransform)!
    }

    public update(dt: number) {
        if (!this.simulate) {
            return
        }

        this.applyGravity()

        let position = this.node.worldPosition.clone()
        position.add(new Vec3(this.linearVelocity.x * dt, this.linearVelocity.y * dt, 0))

        position = this.applyBounce(position)

        this.node.setWorldPosition(position)
    }

    /**
     * Apply a force to the center
     */
    public applyForce(force: Vec2) {
        this.linearVelocity.add(force)
    }

    private applyGravity(): void {
        const gravity = new Vec2(0, -9.8 * this.gravityScale)
        this.applyForce(gravity)
    }

    private applyBounce(position: Vec3): Vec3 {
        const boundingBox = this.uiTransform.getBoundingBoxToWorld()

        if (boundingBox.yMin < 0) {
            position.y = boundingBox.height / 2
            this.linearVelocity.y *= -this.bounce
        }

        if (boundingBox.xMin < 0) {
            position.x = boundingBox.width / 2
            this.linearVelocity.x *= -1
        }

        const canvasWidth = this.canvas.width

        if (boundingBox.xMax > canvasWidth) {
            position.x = canvasWidth - boundingBox.width / 2
            this.linearVelocity.x *= -1
        }

        return position
    }
}
