import { _decorator, Button, Component, tween, UIOpacity } from 'cc'
import { Localize } from '../../components/text/Localize'
import AdsUtils from '../../utils/AdsUtils'
import AnimUtils from '../../utils/AnimUtils'
import ComponentUtils from '../../utils/ComponentUtils'
import { BaseScreen } from '../BaseScreen'

const { ccclass, requireComponent, property } = _decorator

const {
    Events,
    Plugins: {
        Ads: { Types: AdsTypes } = {},
    },
} = Ludex
const { event } = codex

@ccclass('GetRewardsScreen')
@requireComponent(BaseScreen)
export class GetRewardsScreen extends Component {
    private screen!: BaseScreen

    @property(Localize)
    private countDownLocalizer!: Localize

    @property(Button)
    private skipButton!: Button

    @property(Button)
    private claimButton!: Button

    public __preload() {
        this.enabled = false
        this.screen = this.getComponent(BaseScreen)!

        this.validateRequiredComponents()
        this.listenScreenEvents()

        if (AdsTypes) {
            AdsUtils.preloadAdsByType(AdsTypes.REWARDED_VIDEO)
        }
    }

    public onDestroy(): void {
        this.unListenScreenEvents()
    }

    private validateRequiredComponents() {
        ComponentUtils.lazyValidateProperties(this)
    }

    private listenScreenEvents() {
        // ? Here's a demo of all the events a screen has
        event.on(Events.OPENING_SCREEN, this.handleOpeningScreen)
        event.on(Events.OPENED_SCREEN, this.handleOpenedScreen)
    }

    private unListenScreenEvents() {
        event.off(Events.OPENING_SCREEN, this.handleOpeningScreen)
        event.off(Events.OPENED_SCREEN, this.handleOpenedScreen)
    }

    private handleOpeningScreen = (payload: SwitchScreenPayload) => {
        if (!this.validateEventPayload(payload)) return
        this.setInteractableButtons(false)
    }

    private handleOpenedScreen = (payload: SwitchScreenPayload) => {
        if (!this.validateEventPayload(payload)) return
        this.open()
    }

    private validateEventPayload(payload: SwitchScreenPayload) {
        const { screenName } = payload ?? {}

        if (screenName !== this.node.name) return false

        return true
    }

    public open() {
        this.updateStatus()
        this.runPopUpAnimation()
        this.runCountdownAnimation()
    }

    public close(getRewardsSuccess: boolean, rewardsAdShown: boolean = false) {
        this.setInteractableButtons(false)

        event.emit(Events.CLOSE_SCREEN, {
            screenName: this.node.name,
            screenData: { getRewardsSuccess, rewardsAdShown },
        } as SwitchScreenPayload)
    }

    private runCountdownAnimation(): void {
        let time = 5
        this.countDownLocalizer.setParams([time.toString()])

        const updateTime = () => {
            if (time <= 0) {
                this.stopCountdownAnimation()
                return
            }

            time--

            if (time === 3) this.runButtonAnimation()

            this.countDownLocalizer.setParams([time.toString()])
        }

        this.schedule(updateTime, 1.0, 6)
    }

    private async stopCountdownAnimation(): Promise<void> {
        this.unscheduleAllCallbacks()

        if (GameSDK.isGameSDK('Yandex')) {
            this.close(false)
            return
        }

        const success = await AdsUtils.showRewardedInterstitialAdAsync()
        this.close(success, true)
    }

    public handleClickedSkip(): void {
        this.unscheduleAllCallbacks()
        this.close(false)
    }

    public async handleClickedClaim(): Promise<void> {
        this.unscheduleAllCallbacks()
        const success = await AdsUtils.showRewardedInterstitialAdAsync()
        this.close(success, true)
    }

    private runPopUpAnimation(): void {
        if (!this.screen.container) {
            console.warn('StartLevelScreen: screen not found')
        }
    }

    private async runButtonAnimation(): Promise<void> {
        const button = GameSDK.isGameSDK('Yandex') ? this.claimButton : this.skipButton
        const buttonOpacity = button.node.getComponent(UIOpacity)

        if (!buttonOpacity) return

        const opacityTween = tween(buttonOpacity).to(0.3, { opacity: 255 })

        await AnimUtils.runTweenAsync(opacityTween)

        this.setInteractableButtons(true)
    }

    private updateStatus(): void {
        const isYandex = GameSDK.isGameSDK('Yandex')

        this.skipButton.node.active = !isYandex
        this.claimButton.node.active = isYandex
        this.countDownLocalizer.key = isYandex ? 'ads.levelAfterT' : 'ads.adAfterT'

        this.skipButton.getComponent(UIOpacity)!.opacity = 0
        this.claimButton.getComponent(UIOpacity)!.opacity = 0
    }

    private setInteractableButtons(interactable: boolean) {
        this.skipButton.interactable = interactable
        this.claimButton.interactable = interactable
    }
}
