import { Animation, Button, Component, UIOpacity, _decorator, game, tween } from 'cc'
import { Localize } from '../../components/text/Localize'
import CoinData from '../../constants/CoinData'
import DashboardEvents from '../../constants/DashboardEvents'
import { CodeSoundKeys } from '../../constants/SoundKeys'
import GameplayEvents from '../../gameplay/constants/GameEvents'
import { AnimSkipOverlay } from '../../scenes/common/anim-skip-overlay/AnimSkipOverlay'
import AnimUtils from '../../utils/AnimUtils'
import AudioUtils from '../../utils/AudioUtils'
import ComponentUtils from '../../utils/ComponentUtils'
import AnimAbortSignal from '../../utils/common/AnimAbortSignal'
import { BaseScreen } from '../BaseScreen'
import { StarChest } from './components/StarChest'
import { StarProgressBar } from './StarProgressBar'

const { ccclass, property, requireComponent } = _decorator

type Key = Ludex.Plugins.Language.TextKey

const { Events } = Ludex
const { event, player } = codex

const STARS_REQUIRED_FOR_CHEST = 20

const CHEST_AVAILABLE_LOCALIZED_KEY: Key = 'reward.starChestAvailable'
const CHEST_UNAVAILABLE_MESSAGE: Key = 'reward.starChestUnavailable'

@ccclass('StarChestScreen')
@requireComponent(BaseScreen)
export class StarChestScreen extends Component {
    private screen!: BaseScreen

    @property(StarProgressBar)
    private starProgressBar!: StarProgressBar

    @property(Button)
    private openButton!: Button

    @property(Button)
    private continueButton!: Button

    @property(Animation)
    private chestAnimation!: Animation

    @property(StarChest)
    private starChest!: StarChest

    @property(UIOpacity)
    private chestUnlockFXMask!: UIOpacity

    @property(AnimSkipOverlay)
    private skipOverlay!: AnimSkipOverlay

    @property(Localize)
    private messageLabelLocalizer!: Localize

    private numChestAvailable!: number

    public __preload() {
        this.enabled = false
        this.screen = this.getComponent(BaseScreen)!
        this.validateRequiredComponents()
        this.listenScreenEvents()
    }

    public onDestroy(): void {
        this.unListenScreenEvents()
    }

    private validateRequiredComponents() {
        ComponentUtils.lazyValidateProperties(this)
    }

    private listenScreenEvents() {
        // ? Here's a demo of all the events a screen has
        event.on(Events.OPENING_SCREEN, this.handleOpeningScreen)
        event.on(Events.OPENED_SCREEN, this.handleOpenedScreen)
    }

    private unListenScreenEvents() {
        event.off(Events.OPENING_SCREEN, this.handleOpeningScreen)
        event.off(Events.OPENED_SCREEN, this.handleOpenedScreen)
    }

    private handleOpeningScreen = (payload: SwitchScreenPayload) => {
        if (!this.screen) {
            throw new Error('StarChestScreen: screen not found')
        }

        if (!this.validateEventPayload(payload)) return
    }

    private handleOpenedScreen = (payload: SwitchScreenPayload) => {
        if (!this.validateEventPayload(payload)) return
        this.open()
    }

    private validateEventPayload(payload: SwitchScreenPayload) {
        const { screenName } = payload ?? {}

        if (screenName !== this.node.name) return false

        return true
    }

    public open() {
        this.runPopUpAnimation()
        this.node.scene.emit(GameplayEvents.HIDE_UI)

        const gameData = player.getGameData()
        if (!gameData) return

        const { stars } = gameData

        this.numChestAvailable = Math.floor(stars / STARS_REQUIRED_FOR_CHEST)

        this.starProgressBar.setRange(stars, STARS_REQUIRED_FOR_CHEST)

        const hasChest = this.numChestAvailable > 0

        this.setInteractableButtons(true)

        this.openButton.node.active = hasChest
        this.openButton.interactable = hasChest

        this.continueButton.node.active = !hasChest
        this.continueButton.interactable = !hasChest

        this.messageLabelLocalizer.key = this.numChestAvailable
            ? CHEST_AVAILABLE_LOCALIZED_KEY
            : CHEST_UNAVAILABLE_MESSAGE

        this.numChestAvailable ? this.chestAnimation.play() : this.chestAnimation.stop()

        this.chestUnlockFXMask.node.active = false
        this.chestUnlockFXMask.opacity = 0
        this.starChest.respawnAtStartingState()
    }

    public close() {
        this.setInteractableButtons(false)
        event.emit(Events.CLOSE_SCREEN, { screenName: this.node.name })
        this.node.scene.emit(GameplayEvents.SHOW_UI)
    }

    private runPopUpAnimation(): void {
        if (!this.screen.container) {
            console.warn('StarChestScreen: screen not found')
        }
    }

    public async handleClickButton(): Promise<void> {
        if (this.numChestAvailable === 0) {
            this.close()
            return
        }

        AudioUtils.turnDownMusic()

        const gameData = player.getGameData()
        if (!gameData) return

        this.setInteractableButtons(false)

        const { stars: prevStars } = gameData

        player.setGameData({
            stars: gameData.stars - STARS_REQUIRED_FOR_CHEST * this.numChestAvailable,
        })

        const signal = new AnimAbortSignal()

        await this.skipOverlay.open({
            opacity: 200,
            onSkip: () => signal.abort(),
        })

        await this.handleUnlockAnimation(prevStars, gameData.stars, signal)
        await this.skipOverlay.close()

        AudioUtils.turnUpMusic()

        game.emit(DashboardEvents.STARS_UPDATED)

        this.close()
    }

    private async handleUnlockAnimation(prevStars: number, nextStars: number, signal: AnimAbortSignal): Promise<void> {
        await Promise.all([this.starChest.moveToCenter(signal), this.fadeInChestUnlockFX(signal)])

        this.chestAnimation.stop()

        this.starChest.startShaking()

        // start playing drum roll
        await AudioUtils.playSound(CodeSoundKeys.STAR_CHEST_DRUM_ROLL, {
            loop: true,
        })

        // Audio only plays after a frame
        // So we wait for a frame before starting the animation
        // to cover the case where animations was skipped
        await AnimUtils.waitTweenAsync(0)

        let lastAmount = prevStars
        let currChest = this.numChestAvailable

        const getProgress = (curr: number) => {
            if (this.numChestAvailable > 2) {
                return 1 - curr / this.numChestAvailable
            }

            // ? Rescaled so the animation doesn't look too fast going straight to the largest scale
            return (1 - curr / this.numChestAvailable) * 0.35
        }

        const onProgress = (current: number) => {
            if (lastAmount - current >= STARS_REQUIRED_FOR_CHEST) {
                lastAmount = current
                currChest--
                this.starChest.scale(getProgress(currChest), signal)
                AudioUtils.playSound(CodeSoundKeys.STAR_CHEST_OPEN)
            }
        }

        await Promise.all([this.starProgressBar.animateTo(nextStars, signal, onProgress)])

        await AnimUtils.waitTweenAsync(1, signal)

        this.starChest.stopShaking()

        await this.starChest.glow(signal)

        // Stop drum roll
        AudioUtils.stopSound(CodeSoundKeys.STAR_CHEST_DRUM_ROLL)

        const coins = CoinData.STAR_CHEST * this.numChestAvailable

        this.node.scene.emit(GameplayEvents.ADD_COINS, {
            coins,
            collectionAnimation: {
                animateTextOnly: false,
                floatDuration: 3,
                signal,
            },
            skipAnimation: true,
        } as CoinDataPayload)

        await this.starChest.explode(signal)
        await AnimUtils.waitTweenAsync(1, signal)

        await Promise.all([this.fadeOutChestUnlockFX(signal)])
    }

    private async fadeInChestUnlockFX(signal: AnimAbortSignal): Promise<void> {
        this.chestUnlockFXMask.node.active = true
        await AnimUtils.runTweenAsync(tween(this.chestUnlockFXMask).to(0.5, { opacity: 150 }), signal)
    }

    private async fadeOutChestUnlockFX(signal: AnimAbortSignal): Promise<void> {
        await AnimUtils.runTweenAsync(tween(this.chestUnlockFXMask).to(0.3, { opacity: 0 }), signal)
        this.chestUnlockFXMask.opacity = 0
        this.chestUnlockFXMask.node.active = false
    }

    private setInteractableButtons(interactable: boolean) {
        this.openButton.interactable = interactable
        this.continueButton.interactable = interactable
        this.screen.backgroundMaskButton.interactable = interactable
    }
}
