import { _decorator, Button, Component, Label } from 'cc'
import CustomGameData from '../../gameplay/constants/CustomGameData'
import GameplayEvents from '../../gameplay/constants/GameEvents'
import ComponentUtils from '../../utils/ComponentUtils'
import CustomModeUtils from '../../utils/CustomModeUtils'
import { BaseScreen } from '../BaseScreen'
import { SwitchButton } from '../settings-screen/components/switch-button/SwitchButton'
import { HorizontalSelector } from './components/horizontal-selector/HorizontalSelector'

const { ccclass, property, requireComponent } = _decorator

const { Events } = Ludex
const { event, player } = codex

@ccclass('CustomGameScreen')
@requireComponent(BaseScreen)
export class CustomGameScreen extends Component {
    private screen!: BaseScreen

    @property(Button)
    private playButton!: Button

    @property({ type: HorizontalSelector, group: 'Selectors' })
    private timeSelector!: HorizontalSelector

    @property({ type: HorizontalSelector, group: 'Selectors' })
    private difficultySelector!: HorizontalSelector

    @property({ type: HorizontalSelector, group: 'Selectors' })
    private bombsSelector!: HorizontalSelector

    @property({ type: SwitchButton, group: 'Selectors' })
    private extraAdsSwitch!: SwitchButton

    @property(Label)
    private bonusLabel!: Label

    public __preload() {
        this.enabled = false
        this.screen = this.getComponent(BaseScreen)!
        this.validateRequiredComponents()
        this.listenScreenEvents()
    }

    public onDestroy(): void {
        this.unListenScreenEvents()
    }

    private validateRequiredComponents() {
        ComponentUtils.lazyValidateProperties(this)
    }

    private listenScreenEvents() {
        // ? Here's a demo of all the events a screen has
        event.on(Events.OPENING_SCREEN, this.handleOpeningScreen)
        event.on(Events.OPENED_SCREEN, this.handleOpenedScreen)
    }

    private unListenScreenEvents() {
        event.off(Events.OPENING_SCREEN, this.handleOpeningScreen)
        event.off(Events.OPENED_SCREEN, this.handleOpenedScreen)
    }

    private handleOpeningScreen = (payload: SwitchScreenPayload) => {
        if (!this.screen) {
            throw new Error('StartLevelScreen: screen not found')
        }

        if (!this.validateEventPayload(payload)) return

        this.setInteractableButtons(true)
    }

    private handleOpenedScreen = (payload: SwitchScreenPayload) => {
        if (!this.validateEventPayload(payload)) return
        this.open()
    }

    private validateEventPayload(payload: SwitchScreenPayload) {
        const { screenName } = payload ?? {}

        if (screenName !== this.node.name) return false

        return true
    }

    public open() {
        this.updateStatus()
        this.runPopUpAnimation()
        this.node.scene.emit(GameplayEvents.HIDE_UI)
    }

    public close(customMode?: boolean): void {
        this.setInteractableButtons(false)
        this.node.scene.emit(GameplayEvents.SHOW_UI)

        event.emit(Events.CLOSE_SCREEN, { screenName: this.node.name, screenData: { customMode } })
    }

    private runPopUpAnimation(): void {
        if (!this.screen.container) {
            console.warn('StartLevelScreen: screen not found')
        }
    }

    private updateStatus(): void {
        const { time, difficulty, bombs } = CustomGameData
        const custom = CustomModeUtils.getCustomData()

        this.timeSelector.init(
            time.map((t) => (t < 0 ? `${t}%` : `+${t}%`)),
            custom.time,
        )
        this.difficultySelector.init([...difficulty], custom.difficulty)
        this.bombsSelector.init(
            bombs.map((b) => `${b}`),
            custom.bombs,
        )

        this.extraAdsSwitch.setEnabled(custom.ads)

        this.calculateBonus()
    }

    // ? Callback for click event of PlayButton
    public async handleClickedPlay(): Promise<void> {
        this.close(true)
    }

    public handleClickedClose(): void {
        this.close()
    }

    // ? Callback for select event of TimeSelector
    public handleSelectedTime(time: string): void {
        this.updateCustomGameData()
        this.calculateBonus()
    }

    // ? Callback for select event of TimeSelector
    public handleSelectedDifficulty(difficulty: string): void {
        this.updateCustomGameData()
        this.calculateBonus()
    }

    // ? Callback for select event of BombSelector
    public handleSelectedBombs(bombs: string): void {
        this.updateCustomGameData()
        this.calculateBonus()
    }

    // ? Callback for switch event of ExtraAdsSwitch
    public handleSwitchedExtraAds(): void {
        this.extraAdsSwitch.switch()
        this.updateCustomGameData()
        this.calculateBonus()
    }

    private updateCustomGameData(): void {
        const timeIndex = this.timeSelector.getSelectedIndex()
        const difficultyIndex = this.difficultySelector.getSelectedIndex()
        const bombIndex = this.bombsSelector.getSelectedIndex()

        player.setGameData({
            custom: {
                time: timeIndex,
                difficulty: difficultyIndex,
                bombs: bombIndex,
                ads: this.extraAdsSwitch.enable,
            },
        })
    }

    private setInteractableButtons(interactable: boolean): void {
        this.playButton.interactable = interactable
        this.screen.backgroundMask.getComponent(Button)!.interactable = interactable
    }

    private calculateBonus(): void {
        const bonus = CustomModeUtils.getCustomBonus()
        this.bonusLabel.string = bonus >= 0 ? `+${bonus}%` : `${bonus}%`
    }
}
