import { Director, Node, Vec3, _decorator, director, tween } from 'cc'
import AnimUtils from '../../../../../utils/AnimUtils'
import AnimAbortSignal from '../../../../../utils/common/AnimAbortSignal'
import MatchUtils from '../../../../../utils/MatchUtils'
import { GameSceneLayout, type StatusConfig } from '../GameSceneLayout'
import { ChallengeScoreBlock } from '../header/score-block/ChallengeScoreBlock'
import { Sidebar } from './Sidebar'

const { ccclass, property } = _decorator
const { match } = codex
const {
    Match: { Modes },
} = Ludex
@ccclass('LandscapeLayout')
export class LandscapeLayout extends GameSceneLayout {
    @property({ type: Sidebar, group: 'Sidebar' })
    private sidebar!: Sidebar

    @property({ type: ChallengeScoreBlock, group: 'ChallengeScore' })
    private playerScoreBlock!: ChallengeScoreBlock

    @property({ type: ChallengeScoreBlock, group: 'ChallengeScore' })
    private opponentScoreBlock!: ChallengeScoreBlock

    @property(Node)
    private topAnimNodes: Node[] = []
    private originTopNodesPos: Vec3[] = []

    public onLoad(): void {
        //? wait LayoutComponent finish layout (not dirty)
        director.once(Director.EVENT_AFTER_UPDATE, () => {
            this.saveOriginPos()
            this.initUIPos()
            this.isInited = true
        })
    }

    private saveOriginPos(): void {
        this.sidebar.saveOriginPos()

        for (const node of this.topAnimNodes) {
            this.originTopNodesPos.push(node.position.clone())
        }
    }

    private initUIPos(): void {
        this.sidebar.initUIPos()

        this.topAnimNodes.forEach((node, idx) => {
            const startPos = this.originTopNodesPos[idx].clone()
            startPos.y += 100
            node.setPosition(startPos)
        })
    }

    public async playExitAnimUI(duration: number = 0.2): Promise<void> {
        this.entranceSignal.abort()
        this.exitSignal.abort()
        this.exitSignal = new AnimAbortSignal()

        const exitTweens: Promise<unknown>[] = []

        this.topAnimNodes.forEach((node, idx) => {
            const des = new Vec3(this.node.position.x, this.originTopNodesPos[idx].y + 100, 0)
            const t = tween(node)
            t.to(duration, { position: des }, { easing: 'backIn' })

            exitTweens.push(AnimUtils.runTweenAsync(t, this.exitSignal))
        })

        exitTweens.push(this.sidebar.playExitAnimUI(this.exitSignal))

        await Promise.all(exitTweens)
    }

    public async playEntranceAnimUI(duration: number = 0.17): Promise<void> {
        this.exitSignal.abort()
        this.entranceSignal.abort()
        this.entranceSignal = new AnimAbortSignal()

        const entranceTweens: Promise<unknown>[] = []

        this.topAnimNodes.forEach((node, idx) => {
            const des = new Vec3(this.node.position.x, this.originTopNodesPos[idx].y, 0)
            const t = tween(node)
            t.to(duration, { position: des }, { easing: 'backOut' })

            entranceTweens.push(AnimUtils.runTweenAsync(t, this.entranceSignal))
        })

        entranceTweens.push(this.sidebar.playEntranceAnimUI(this.entranceSignal))

        await Promise.all(entranceTweens)
    }

    public setLevelText(level: number): void {
        this.sidebar.setLevelText(level)
    }

    public updateStatus(config: StatusConfig): void {
        super.updateStatus(config)
        this.sidebar.updateStatus(config)
        this.updateScoreBlockStatus(config)
    }

    private updateScoreBlockStatus(config: StatusConfig): void {
        if (MatchUtils.getMatchMode() !== Modes.CHALLENGE_FRIEND) {
            this.playerScoreBlock.node.active = false
            this.opponentScoreBlock.node.active = false
            return
        }

        if (this.playerScoreBlock.node.active) return

        const state = match.getMatchState()
        const { playerId, opponentId } = MatchUtils.getMatchCustomData() ?? {}

        if (!playerId || !opponentId) return

        this.playerScoreBlock.node.active = true
        this.opponentScoreBlock.node.active = true

        const player = state.profiles[playerId]
        const opponent = state.profiles[opponentId]

        if (player) this.playerScoreBlock.updateInfo(player.photo, playerId, player.score)
        if (opponent) this.opponentScoreBlock.updateInfo(opponent.photo, opponentId, opponent.score)

        if (config.score !== undefined) this.playerScoreBlock.setScore(config.score)
    }

    public handleScoreUpdated(score: number): void {
        this.sidebar.updateScore(score)

        if (!this.playerScoreBlock.node.active) return
        this.playerScoreBlock.updateScore(score)
    }
}
