import OverlayUtils from 'db://assets/utils/OverlayUtils'
import { _decorator, Component, game, Node, UITransform } from 'cc'
import { OpacityToggler } from '../../../../../components/common/OpacityToggler'
import GameEvents from '../../../../../constants/GameEvents'
import { LeaderboardIds, type LeaderboardType, LeaderboardTypes } from '../../../../../constants/Leaderboard'
import GameplayEvents from '../../../../../gameplay/constants/GameEvents'
import LeaderboardEvents from '../../../../../gameplay/constants/Leaderboard'
import ComponentUtils from '../../../../../utils/ComponentUtils'
import AnimAbortSignal from '../../../../../utils/common/AnimAbortSignal'
import ScreenUtils from '../../../../../utils/ScreenUtils'
import { Tabs } from './components/header/tabs/Tabs'
import { LeaderboardTimer } from './components/header/timer/LeaderboardTimer'
import { LeaderSkeleton } from './components/leader/LeaderSkeleton'
import { Leaders } from './components/leader/Leaders'
import { JoinLeaderboardContainer } from './JoinLeaderboardContainer'
import LeaderboardContentOverlay from './LeaderboardContentOverlay'
import { LeaderboardIsEmpty } from './LeaderboardIsEmpty'

const { ccclass, property } = _decorator
const { storage, leaderboard, player, monitorError } = codex

const {
    Utils: { Object: O },
    Configs: { Leaderboards: LeaderboardConfigs },
} = Ludex

class EmptyLeaderboardError extends Error {
    constructor(message: string) {
        super(message)
        this.name = 'EmptyLeaderboardError'
    }
}

@ccclass('LeaderboardContent')
export class LeaderboardContent extends Component {
    @ComponentUtils.required()
    @property(Tabs)
    private tabs!: Tabs

    @ComponentUtils.required()
    @property(LeaderboardTimer)
    private timer!: LeaderboardTimer

    @ComponentUtils.required()
    @property({ type: Leaders, group: 'Leaderboard' })
    public leaders!: Leaders

    @property({ type: [LeaderSkeleton], group: 'Leaderboard' })
    private leaderSkeletons: LeaderSkeleton[] = []

    @ComponentUtils.required()
    @property({ type: LeaderboardIsEmpty, group: 'Leaderboard' })
    private leaderboardIsEmpty!: LeaderboardIsEmpty

    @ComponentUtils.required()
    @property(OpacityToggler)
    private contentOpacity!: OpacityToggler

    @ComponentUtils.required()
    @property(JoinLeaderboardContainer)
    private joinLeaderboardContainer!: JoinLeaderboardContainer

    @ComponentUtils.required()
    @property(Node)
    private loadingNode!: Node

    private isHidden = true
    private isOverlayMode = false

    private currentLb: Ludex.Plugins.Leaderboards.Leaderboard | null = null
    private currentTab: LeaderboardType = LeaderboardTypes.DAILY

    private animAbortSignal: AnimAbortSignal = new AnimAbortSignal()

    private hasPlayedAppearAnim: Record<LeaderboardType, boolean> = {
        friends: false,
        global: false,
        global_weekly: false,
    }

    private dailyFBOverlay: LeaderboardContentOverlay | null = null
    private weeklyFBOverlay: LeaderboardContentOverlay | null = null

    protected __preload(): void {
        this.leaders.maxLeader = LeaderboardConfigs.LeaderboardList[0]?.Limit ?? 0
    }

    public onLoad(): void {
        ComponentUtils.validateRequiredProperties(this)
        this.createFBOverlay$()
    }

    public listenEvents(): void {
        game.on(GameEvents.VIEW_RESIZED, this.handleViewResized$)

        this.node.scene.on(LeaderboardEvents.WEEKLY_TAB_SELECTED, this.handleWeeklyTabSelected$)
        this.node.scene.on(LeaderboardEvents.WORLD_TAB_SELECTED, this.handleDailyTabSelected$)
        this.node.scene.on(LeaderboardEvents.OVERLAY_SHOW_STARTED, this.enableOverlayLoadingNode$)
        this.node.scene.on(LeaderboardEvents.OVERLAY_SHOW_COMPLETED, this.disableOverlayLoadingNode$)

        this.node.scene.on(GameplayEvents.HIDE_UI, this.hideContent$)
        this.node.scene.on(GameplayEvents.SHOW_UI, this.showContent$)
    }

    private unlistenEvents$ = (): void => {
        game.off(GameEvents.VIEW_RESIZED, this.handleViewResized$)

        this.node.scene.off(LeaderboardEvents.WEEKLY_TAB_SELECTED, this.handleWeeklyTabSelected$)
        this.node.scene.off(LeaderboardEvents.WORLD_TAB_SELECTED, this.handleDailyTabSelected$)
        this.node.scene.off(LeaderboardEvents.OVERLAY_SHOW_STARTED, this.enableOverlayLoadingNode$)
        this.node.scene.off(LeaderboardEvents.OVERLAY_SHOW_COMPLETED, this.disableOverlayLoadingNode$)

        this.unlistenUIEvents()
    }

    public unlistenUIEvents(): void {
        this.node.scene.off(GameplayEvents.HIDE_UI, this.hideContent$)
        this.node.scene.off(GameplayEvents.SHOW_UI, this.showContent$)
    }

    @ComponentUtils.onlyRunInLifeCycleAsync
    public async loadLeaderboard(signal: AnimAbortSignal, type: LeaderboardType): Promise<void> {
        const leaderboardName = this.getLeaderboardName$(type)
        if (leaderboardName === null) return

        await signal.callAsync(() => leaderboard.requestLeaderboardAsync(leaderboardName, this.leaders.maxLeader))

        signal.call(() => {
            const lb = leaderboard.getLeaderboard(leaderboardName)
            this.currentLb = lb ?? this.currentLb
        })

        await signal.callAsync(async () => this.loadPlayerLeaderData(type))
    }

    private getLeaderboardName$ = (type: LeaderboardType): string | null => {
        const leaderboardId = type === LeaderboardTypes.WEEKLY ? LeaderboardIds.WEEKLY : LeaderboardIds.DAILY
        return leaderboard.getLeaderboardName(leaderboardId)
    }

    @ComponentUtils.onlyRunInLifeCycleAsync
    private async showLoading(signal: AnimAbortSignal, type: LeaderboardType, skeletonDelay = 0): Promise<void> {
        if (this.isOverlayMode) {
            this.enableOverlayLoadingNode$()
            this.cleanupOverlay()
            return
        }

        await Promise.all([
            this.leaders.scrollView.unlistenEvents(),
            this.leaders.scrollView.scrollToTop(signal).then(() => {
                if (!this.leaders) return

                this.leaders.scrollView.scrollView.enabled = false
                this.leaders.scrollView.listenEvents()
            }),
            this.leaderSkeletons.map((skeleton, index) => skeleton.show(index * skeletonDelay, signal)),
        ])
    }

    @ComponentUtils.onlyRunInLifeCycleAsync
    private async hideLoading(signal: AnimAbortSignal, _type: LeaderboardType): Promise<void> {
        if (this.isOverlayMode) {
            return
        }

        await Promise.all(this.leaderSkeletons.map((skeleton) => skeleton.hide(signal)))
    }

    public entering(): void {
        this.timer.reset()

        this.isHidden = false
        const lastTabOpened = storage.getStorageData('leaderboard_content', 'lastTabOpened')

        if (lastTabOpened === LeaderboardTypes.WEEKLY) {
            this.handleWeeklyTabSelected$()
        } else if (lastTabOpened === LeaderboardTypes.DAILY) {
            this.handleDailyTabSelected$()
        }
    }

    public hide(): void {
        this.isHidden = true
    }

    public cleanup(): void {
        this.leaders.scrollView.unlistenEvents()
        this.unlistenEvents$()
        this.cleanupSideEffects$()
        this.cleanupOverlay()
    }

    public cleanupOverlay(): void {
        this.dailyFBOverlay?.hideOverlay()
        this.weeklyFBOverlay?.hideOverlay()
    }

    private cleanupSideEffects$ = (): void => {
        this.animAbortSignal.abort()
        this.animAbortSignal = new AnimAbortSignal()

        this.leaders.hideLeaders()
        this.leaderboardIsEmpty.hide()
        this.joinLeaderboardContainer.hide()
    }

    public updateOverlayMountStatus(isPageMounted: boolean): void {
        this.dailyFBOverlay?.setIsPageMounted(isPageMounted)
        this.weeklyFBOverlay?.setIsPageMounted(isPageMounted)
    }

    @ComponentUtils.onlyRunInLifeCycleAsync
    private async updateStatus(signal: AnimAbortSignal, type: LeaderboardType): Promise<void> {
        if (signal === undefined || this.isHidden) {
            return
        }

        this.initializeStatusUpdate$(type)

        try {
            await this.loadLeaderboardData$(signal, type)
            await this.handleSuccessfulLoad$(signal, type)
        } catch (error) {
            this.handleLoadError$(signal, type, error)
        }
    }

    private initializeStatusUpdate$ = (type: LeaderboardType): void => {
        this.tabs.updateStatus(type)
        this.timer.setTimeLabel()
    }

    private loadLeaderboardData$ = async (signal: AnimAbortSignal, type: LeaderboardType): Promise<void> => {
        await this.showLoading(signal, type)
        await this.loadLeaderboard(signal, type)
        this.hideLoading(signal, type)
    }

    private handleSuccessfulLoad$ = async (signal: AnimAbortSignal, type: LeaderboardType): Promise<void> => {
        if (this.isOverlayMode) {
            await this.handleOverlayMode$(signal, type)
        } else {
            await this.handleRegularMode$(signal, type)
        }

        await this.handleGuestUserFlow$(signal)
    }

    private handleOverlayMode$ = async (signal: AnimAbortSignal, type: LeaderboardType): Promise<void> => {
        signal.call(() => {
            this.timer?.show(signal)
            this.handleUpdateOverlayAfterSignal$(signal, type)
        })
    }

    private handleUpdateOverlayAfterSignal$ = async (signal: AnimAbortSignal, type: LeaderboardType): Promise<void> => {
        const result = await this.updateOverlay$(type)

        if (!result) {
            this.isOverlayMode = false
            this.disableOverlayLoadingNode$()
            this.handleRegularMode$(signal, type)
            return
        }

        this.setupTimerWithLeaderboard$(signal)
    }

    private handleRegularMode$ = async (signal: AnimAbortSignal, type: LeaderboardType): Promise<void> => {
        signal.call(() => {
            this.setupTimerWithLeaderboard$(signal)
        })

        await signal.callAsync((signal) => this.playLeadersShowupAnimation(type, signal))
    }

    private setupTimerWithLeaderboard$ = (signal: AnimAbortSignal): void => {
        this.timer?.setTimeLabel(this.currentLb?.resettable)
        this.timer?.show(signal)
    }

    private handleGuestUserFlow$ = async (signal: AnimAbortSignal): Promise<void> => {
        if (GameSDK.extra.isGuest) {
            this.updateJoinLeaderboardButton()
            await this.joinLeaderboardContainer?.show(signal)
        }
    }

    private handleLoadError$ = (signal: AnimAbortSignal, type: LeaderboardType, error: unknown): void => {
        if (!(error instanceof EmptyLeaderboardError)) {
            throw error
        }

        signal.call(() => {
            this.handleEmptyLeaderboard$(signal, type)
        })
    }

    private handleEmptyLeaderboard$ = (signal: AnimAbortSignal, type: LeaderboardType): void => {
        this.leaderboardIsEmpty.show()
        this.leaders.scrollView.setMaxNodeCount(0)
        this.hideLoading(signal, type)
        this.setupTimerWithLeaderboard$(signal)
    }

    @ComponentUtils.onlyRunInLifeCycleAsync
    private async playLeadersShowupAnimation(type: LeaderboardType, signal: AnimAbortSignal) {
        if (!this.currentLb) return

        const playerId = player.getPlayerId()
        const playerLeaderData = this.currentLb.leaders[playerId]

        const { previousScore } = storage.getStorage('leaderboard_content')

        let leaders = O.vals(this.currentLb.leaders)
        leaders.sort((a, b) => b.score - a.score)

        if (GameSDK.extra.isGuest) {
            leaders = leaders.slice(0, 5)
        }

        const scoreChanging =
            playerLeaderData &&
            previousScore !== -1 &&
            previousScore < playerLeaderData.score &&
            playerLeaderData.rank <= this.leaders.maxLeader

        this.leaders.updateData(leaders)

        let rankChanging = false
        if (scoreChanging) {
            rankChanging = this.leaders.updateDisplayPlayerLeaderForRankChanging(
                previousScore,
                playerLeaderData.score,
                playerLeaderData.rank,
            )
        }

        this.leaders.initScrollViews()
        this.leaders.scrollView.scrollView.enabled = true

        const stagger = this.hasPlayedAppearAnim[type] ? 0 : 0.1
        await signal.callAsync(() => this.leaders.playShowLeadersAnimation(signal, stagger))
        this.hasPlayedAppearAnim[type] = true

        if (!playerLeaderData) return

        await signal.callAsync(() => this.leaders.scrollToPlayerLeader(1, signal))

        if (!rankChanging || previousScore === -1) return

        await signal.callAsync((s) =>
            this.leaders.playRankChangeAnimation(s, previousScore, playerLeaderData.score, playerLeaderData.rank),
        )

        storage.setStorageData('leaderboard_content', 'previousScore', playerLeaderData.score)
    }

    private updateOverlay$ = async (type: LeaderboardType): Promise<boolean> => {
        if (!this.currentLb) return false

        const sortedLeaders = O.vals(this.currentLb.leaders)
        sortedLeaders.sort((a, b) => b.score - a.score)

        const leaderboardPlayers = sortedLeaders.map((leader) => {
            const correctRankText = leader.rank > 50 ? '50+' : `${leader.rank}`

            return {
                playerId: leader.playerId,
                rank: correctRankText,
                score: leader.score,
            }
        })

        const correctOverlay = this.getCorrectOverlay$(type)
        if (!correctOverlay) return false

        const result = await correctOverlay?.updateOverlay({
            leaderboardPlayers,
            leaderboardPosition: this.getLeaderboardSize$(),
            currentPlayerId: player.getPlayerId(),
        })

        return result ?? false
    }

    @ComponentUtils.onlyRunInLifeCycleAsync
    private async loadPlayerLeaderData(type: LeaderboardType): Promise<void> {
        const playerId = player.getPlayerId()

        if (this.currentLb === null) {
            throw new Error('LeaderboardContent: Leaderboard is not found.')
        }

        const leaders = this.currentLb.leaders

        if (Object.keys(leaders).length === 0) {
            throw new EmptyLeaderboardError('LeaderboardContent: Leaderboard is empty.')
        }

        const leaderData = leaders[playerId]
        if (leaderData) return

        const leaderboardName = this.getLeaderboardName$(type)
        if (leaderboardName === null) {
            throw new Error('LeaderboardContent: Leaderboard name is not found.')
        }

        const leadersByIds = await leaderboard.getLeadersByPlayerIdsAsync(leaderboardName, [playerId])

        if (leadersByIds.length === 0) {
            return
        }

        const data = leadersByIds[0]
        if (!data) return

        leaders[playerId] = data
    }

    @ComponentUtils.onlyRunInLifeCycle
    private updateJoinLeaderboardButton(): void {
        if (!GameSDK.extra.isGuest) return
        if (this.isOverlayMode) return

        let y: number | undefined = undefined
        const nodes = this.leaders.scrollView.getVisibleNodes()

        if (nodes.length === 0) {
            y = this.leaders.scrollView.viewport.getBoundingBoxToWorld().center.y
        } else {
            const lastNode = nodes[nodes.length - 1] as (typeof nodes)[number]
            const containerHeight = this.joinLeaderboardContainer.uiTransform.height
            const spacing = 20
            const newY = lastNode.node.worldPosition.y - containerHeight - spacing
            const bottomY = newY - containerHeight / 2
            const { yMin: bot } = this.leaders.scrollView.viewport.getBoundingBoxToWorld()

            if (bottomY >= bot) y = newY
        }

        this.joinLeaderboardContainer.updatePosition(y)
    }

    private handleViewResized$ = (): void => {
        this.updateJoinLeaderboardButton()
    }

    private handleWeeklyTabSelected$ = async (): Promise<void> => {
        storage.setStorageData('leaderboard_content', 'lastTabOpened', LeaderboardTypes.WEEKLY)
        this.cleanupSideEffects$()

        this.currentTab = LeaderboardTypes.WEEKLY
        const signal = this.animAbortSignal

        try {
            await this.updateStatus(signal, LeaderboardTypes.WEEKLY)
        } catch (error) {
            signal.call(() => {
                const err =
                    error instanceof Error
                        ? error
                        : new Error('Unknown error during loading global weekly leaderboard.')

                console.error('LeaderboardContent', err)
                monitorError?.sendException(err)
                ScreenUtils.showNotificationScreenWithLocalizedKey('error.noFriendsLb', 2)
            })
        }
    }

    private handleDailyTabSelected$ = async (): Promise<void> => {
        storage.setStorageData('leaderboard_content', 'lastTabOpened', LeaderboardTypes.DAILY)
        this.cleanupSideEffects$()

        this.currentTab = LeaderboardTypes.DAILY
        const signal = this.animAbortSignal

        try {
            await this.updateStatus(signal, LeaderboardTypes.DAILY)
        } catch (error) {
            signal.call(() => {
                const err =
                    error instanceof Error ? error : new Error('Unknown error during loading global leaderboard.')

                console.error('LeaderboardContent', err)
                monitorError?.sendException(err)
                ScreenUtils.showNotificationScreenWithLocalizedKey('error.noWorldLb', 2)
            })
        }
    }

    private hideContent$ = () => {
        this.contentOpacity.changeStateFade(false, 0.1)
        this.updateOverlayMountStatus(false)
        this.dailyFBOverlay?.handleHideUICall()
        this.weeklyFBOverlay?.handleHideUICall()
    }

    private showContent$ = () => {
        this.contentOpacity.changeStateFade(true, 0.1)
        this.updateOverlayMountStatus(true)
        this.handleOverlayShowUICall$()
    }

    private handleOverlayShowUICall$ = () => {
        if (!this.isOverlayMode) return

        if (this.currentTab === LeaderboardTypes.WEEKLY) {
            this.weeklyFBOverlay?.handleShowUICall()
        } else {
            this.dailyFBOverlay?.handleShowUICall()
        }
    }

    // ? Callback for join leaderboard button
    public async handleJoinLeaderboard(): Promise<void> {
        try {
            if (!('initPlayerAsync' in GameSDK.player)) return

            ScreenUtils.showLoadingScreen()
            await GameSDK.player.initPlayerAsync()
            ScreenUtils.closeLoadingScreen()

            if (GameSDK.extra.isGuest) {
                ScreenUtils.showNotificationScreenWithLocalizedKey('error.joinLb', 1)
                return
            }
        } catch (error) {
            console.warn('Join leaderboard failed', error)
            ScreenUtils.closeLoadingScreen()
            ScreenUtils.showNotificationScreenWithLocalizedKey('error.joinLb', 1)
        }
    }

    private createFBOverlay$ = (): void => {
        try {
            OverlayUtils.isOverlayViewAPIAvailable(GameSDK)

            this.isOverlayMode = true
            this.dailyFBOverlay = new LeaderboardContentOverlay(this.node.scene)
            this.weeklyFBOverlay = new LeaderboardContentOverlay(this.node.scene)
        } catch {
            return
        }
    }

    private getLeaderboardSize$ = (): { worldX: number; worldY: number; width: number; height: number } => {
        const leaderboardUITransform = this.leaders.getComponent(UITransform)
        if (!leaderboardUITransform)
            return {
                worldX: 0,
                worldY: 0,
                width: 0,
                height: 0,
            }

        const { width, height } = leaderboardUITransform
        const { worldPositionX, worldPositionY } = this.leaders.node

        return {
            worldX: worldPositionX,
            worldY: worldPositionY,
            width,
            height,
        }
    }

    private getCorrectOverlay$ = (type: LeaderboardType): LeaderboardContentOverlay | null => {
        return type === LeaderboardTypes.DAILY ? this.dailyFBOverlay : this.weeklyFBOverlay
    }

    private enableOverlayLoadingNode$ = (): void => {
        this.loadingNode.active = true
    }

    private disableOverlayLoadingNode$ = (): void => {
        this.loadingNode.active = false
    }
}
