import { Component, _decorator } from 'cc'
import GameplayEvents from '../../constants/GameEvents'
import type Board from '../board'
import { ErrorDrawer } from './error/ErrorDrawer'
import { HintDrawer } from './hint/HintDrawer'
import { MatchDrawer } from './match/MatchDrawer'
const { ccclass, property } = _decorator

@ccclass('PathDraw')
export class PathDraw extends Component {
    @property({ group: { name: 'Path Drawer' }, type: MatchDrawer })
    private matchPath!: MatchDrawer

    @property({ group: { name: 'Path Drawer' }, type: HintDrawer })
    private hintPath!: HintDrawer

    @property({ group: { name: 'Path Drawer' }, type: ErrorDrawer })
    private errorPath!: ErrorDrawer

    private board!: Board

    public isDrawingHint = false

    public onLoad(): void {
        this.node.scene.on(GameplayEvents.GRID_RESIZE, (scale: number) => {
            this.node.setScale(scale, scale)
            this.node.position = this.board.node.position.clone()
        })
    }

    public init(board: Board): void {
        this.board = board
    }

    public drawMatchPath(path: Path, getTileWorldPosition: (coords: Vec2Like) => Vec2Like): void {
        // clone path to avoid modifying the original path
        const p = path.map((line) => ({ ...line }))

        for (const line of p) {
            line.a = getTileWorldPosition(line.a)
            line.b = getTileWorldPosition(line.b)
        }

        this.matchPath.drawPath(p)
    }

    public clearMatchPath(): void {
        this.matchPath.clearPath()
    }

    public drawHintPath(path: Path, getTileWorldPosition: (coords: Vec2Like) => Vec2Like): void {
        if (this.isDrawingHint) return

        this.isDrawingHint = true
        // clone path to avoid modifying the original path
        const p = path.map((line) => ({ ...line }))

        for (const line of p) {
            line.a = getTileWorldPosition(line.a)
            line.b = getTileWorldPosition(line.b)
        }

        this.hintPath.drawPath(p)
    }

    public clearHintPath(): void {
        if (!this.isDrawingHint) return

        this.isDrawingHint = false
        this.hintPath.clearPath()
    }

    public drawErrorPath(path: Path, getTileWorldPosition: (coords: Vec2Like) => Vec2Like): void {
        // clone path to avoid modifying the original path
        const p = path.map((line) => ({ ...line }))

        for (const line of p) {
            line.a = getTileWorldPosition(line.a)
            line.b = getTileWorldPosition(line.b)
        }

        this.errorPath.drawPath(p)
    }
}
