import {
    CCInteger,
    CCString,
    type CacheMode,
    Component,
    Director,
    Label,
    Layers,
    Node,
    Sprite,
    _decorator,
    director,
} from 'cc'
const { ccclass, executionOrder, property } = _decorator

const {
    Utils: { Array: A, Object: O },
} = Ludex

interface BatchNodes {
    [key: string]: Node
}

interface NodeSets {
    [key: string]: Node[]
}

@ccclass('BatchNodesV2')
@executionOrder(99999)
export class BatchNodesV2 extends Component {
    @property({ group: 'Batch Settings' })
    private batchByName = false

    @property({
        type: [CCString],
        group: 'Batch Settings',
        visible: function (this: BatchNodesV2) {
            return this.batchByName
        },
    })
    private listName: string[] = []

    @property({
        type: [CCInteger],
        group: 'Batch Settings',
        visible: function (this: BatchNodesV2) {
            return this.batchByName
        },
    })
    private depthNames: number[] = []

    @property({ group: 'Label Settings' })
    private batchLabel = false

    @property({
        group: 'Label Settings',
        visible: function (this: BatchNodesV2) {
            return this.batchLabel
        },
    })
    private typeLabel: 'all' | 'char' | 'bitmap' | 'none' = 'char'

    @property({
        group: 'Label Settings',
        visible: function (this: BatchNodesV2) {
            return this.batchLabel
        },
    })
    private depthLabel = 1

    private get depthLabelBitmap(): number {
        return this.depthLabel
    }

    private get depthLabelChar(): number {
        return this.depthLabel + 1
    }

    private get depthLabelNone(): number {
        return this.depthLabel + 2
    }

    @property({ group: 'Sprite Settings' })
    private batchSprite = false

    @property({
        group: 'Sprite Settings',
        visible: function (this: BatchNodesV2) {
            return this.batchSprite
        },
    })
    private depthSprite = 0

    private batchNodes: BatchNodes = {}
    private originalParents: { [key: string]: Node } = {}
    private labelNodes: Node[] = []
    private spriteNodes: Node[] = []
    private namedNodes: NodeSets = {}
    private isInitialized = false

    private labelNodesByType = {
        bitmap: [] as Node[],
        char: [] as Node[],
        none: [] as Node[],
    }

    private isEventListenerSetup = false

    private onAfterUpdate = (): void => {
        if (!this.isInitialized) return
        this.batchActiveNodes()
    }

    private onAfterRender = (): void => {
        if (!this.isInitialized) return
        this.restoreNodes()
    }

    protected onLoad(): void {
        if (!this.enabled) return
        this.initializeDataStructures()
    }

    protected start(): void {
        if (!this.enabled) return
        this.initialize()
    }

    protected onEnable(): void {
        if (!this.isEventListenerSetup) {
            this.setupEventListeners()
            this.isEventListenerSetup = true
        }
    }

    protected onDisable(): void {
        if (this.isEventListenerSetup) {
            this.removeEventListeners()
            this.isEventListenerSetup = false
        }
        this.restoreNodes()
    }

    protected onDestroy(): void {
        this.removeEventListeners()
        this.restoreNodes()
        this.clearData()
    }

    private initializeDataStructures(): void {
        this.batchNodes = {}
        this.originalParents = {}
        this.labelNodes = []
        this.spriteNodes = []
        this.namedNodes = {}
        this.labelNodesByType = {
            bitmap: [],
            char: [],
            none: [],
        }
    }

    private initialize(): void {
        if (this.isInitialized) return
        this.createBatchContainers()
        this.scanNodes(this.node)
        this.sortBatchNodes()
        this.isInitialized = true
    }

    private clearData(): void {
        this.batchNodes = {}
        this.originalParents = {}
        this.labelNodes = []
        this.spriteNodes = []
        this.namedNodes = {}
        this.labelNodesByType = {
            bitmap: [],
            char: [],
            none: [],
        }
        this.isInitialized = false
    }

    private categorizeNode(node: Node): void {
        if (!node.parent) return

        this.originalParents[node.uuid] = node.parent

        if (this.batchLabel) {
            const label = node.getComponent(Label)
            if (label) {
                if (this.typeLabel === 'all') {
                    if (label.cacheMode === Label.CacheMode.BITMAP) {
                        this.labelNodesByType.bitmap.push(node)
                    } else if (label.cacheMode === Label.CacheMode.CHAR) {
                        this.labelNodesByType.char.push(node)
                    } else {
                        this.labelNodesByType.none.push(node)
                    }
                } else if (this.isValidLabel(label)) {
                    this.labelNodes.push(node)
                }
            }
        }

        if (this.batchSprite) {
            const sprite = node.getComponent(Sprite)
            if (sprite) {
                this.spriteNodes.push(node)
            }
        }

        if (this.batchByName) {
            const nodeName = node.name
            if (this.listName.includes(nodeName)) {
                if (!this.namedNodes[nodeName]) {
                    this.namedNodes[nodeName] = []
                }
                this.namedNodes[nodeName].push(node)
            }
        }
    }

    private isValidLabel(label: Label): boolean {
        const cacheModeMap: Record<string, CacheMode> = {
            none: Label.CacheMode.NONE,
            char: Label.CacheMode.CHAR,
            bitmap: Label.CacheMode.BITMAP,
        }
        return label.cacheMode === cacheModeMap[this.typeLabel]
    }

    private sortBatchNodes(): void {
        if (this.batchLabel) {
            if (this.typeLabel === 'all') {
                this.batchNodes['label_bitmap']?.setSiblingIndex(this.depthLabelBitmap)
                this.batchNodes['label_char']?.setSiblingIndex(this.depthLabelChar)
                this.batchNodes['label_none']?.setSiblingIndex(this.depthLabelNone)
            } else {
                const depthMap = {
                    bitmap: this.depthLabelBitmap,
                    char: this.depthLabelChar,
                    none: this.depthLabelNone,
                }
                const depth = depthMap[this.typeLabel] || this.depthLabelChar
                this.batchNodes['label']?.setSiblingIndex(depth)
            }
        }

        if (this.batchNodes['sprite']) {
            this.batchNodes['sprite'].setSiblingIndex(this.depthSprite)
        }

        for (let i = 0; i < this.listName.length; i++) {
            const name = this.listName[i]
            if (!name) continue

            const depth = this.depthNames[i] || 0
            const node = this.batchNodes[name]
            if (node) {
                node.setSiblingIndex(depth)
            }
        }
    }

    private batchNodeArray(nodes: Node[], parent: Node): void {
        for (let i = 0; i < nodes.length; i++) {
            const node = nodes[i]
            if (!node?.isValid || !node.activeInHierarchy) continue

            if (node.parent === parent) continue
            if (this.isBatchContainer(node)) continue

            if (!node.isTransformDirty()) continue

            if (!this.isParentChainActive(node)) continue

            node.setParent(parent, true)
            node.layer = Layers.Enum.UI_2D
        }
    }

    private isBatchContainer(node: Node): boolean {
        const containers = O.vals(this.batchNodes)
        return A.has(containers, node)
    }

    private isParentChainActive(node: Node): boolean {
        let currentNode: Node | null = node
        const rootNode = this.node

        while (currentNode && currentNode !== rootNode) {
            if (!currentNode.active || !currentNode.isValid) {
                return false
            }
            currentNode = currentNode.parent
        }

        return currentNode === rootNode
    }

    private restoreNodes(): void {
        const restoreNode = (node: Node): void => {
            const originalParent = this.originalParents[node.uuid]
            if (!originalParent?.isValid) return

            if (node.parent === originalParent) return

            // if (!node.isTransformDirty()) return

            if (!this.isParentChainActive(originalParent)) return

            node.setParent(originalParent, true)
        }

        if (this.batchLabel) {
            if (this.typeLabel === 'all') {
                for (const type in this.labelNodesByType) {
                    const nodes = this.labelNodesByType[type as keyof typeof this.labelNodesByType]
                    for (let i = 0; i < nodes.length; i++) {
                        const node = nodes[i]
                        if (!node) continue
                        restoreNode(node)
                    }
                }
            } else {
                for (let i = 0; i < this.labelNodes.length; i++) {
                    const node = this.labelNodes[i]
                    if (!node) continue
                    restoreNode(node)
                }
            }
        }

        if (this.batchSprite) {
            for (let i = 0; i < this.spriteNodes.length; i++) {
                const node = this.spriteNodes[i]
                if (!node) continue
                restoreNode(node)
            }
        }

        if (this.batchByName) {
            for (const name in this.namedNodes) {
                const nodes = this.namedNodes[name]
                if (!nodes) continue

                for (let i = 0; i < nodes.length; i++) {
                    const node = nodes[i]
                    if (!node) continue
                    restoreNode(node)
                }
            }
        }
    }

    private setupEventListeners(): void {
        director.on(Director.EVENT_AFTER_UPDATE, this.onAfterUpdate)
        director.on(Director.EVENT_AFTER_RENDER, this.onAfterRender)
    }

    private removeEventListeners(): void {
        director.off(Director.EVENT_AFTER_UPDATE, this.onAfterUpdate)
        director.off(Director.EVENT_AFTER_RENDER, this.onAfterRender)
    }

    private createBatchContainers(): void {
        if (this.batchLabel) {
            if (this.typeLabel === 'all') {
                const types: ('bitmap' | 'char' | 'none')[] = ['bitmap', 'char', 'none']
                for (const type of types) {
                    const labelNode = new Node(`BatchLabel_${type}`)
                    labelNode.layer = Layers.Enum.UI_2D
                    this.node.addChild(labelNode)
                    this.batchNodes[`label_${type}`] = labelNode
                }
            } else {
                const labelNode = new Node('BatchLabel')
                labelNode.layer = Layers.Enum.UI_2D
                this.node.addChild(labelNode)
                this.batchNodes['label'] = labelNode
            }
        }

        if (this.batchSprite) {
            const spriteNode = new Node('BatchSprite')
            spriteNode.layer = Layers.Enum.UI_2D
            this.node.addChild(spriteNode)
            this.batchNodes['sprite'] = spriteNode
        }

        if (this.batchByName) {
            for (let i = 0; i < this.listName.length; i++) {
                const name = this.listName[i]
                if (!name) continue

                const namedNode = new Node(`Batch_${name}`)
                namedNode.layer = Layers.Enum.UI_2D
                this.node.addChild(namedNode)
                this.batchNodes[name] = namedNode
                this.namedNodes[name] = []
            }
        }
    }

    private scanNodes(node: Node): void {
        const children = node.children
        for (let i = 0; i < children.length; i++) {
            const child = children[i]
            if (!child) continue

            this.categorizeNode(child)
            if (child.children.length > 0) {
                this.scanNodes(child)
            }
        }
    }

    private batchActiveNodes(): void {
        if (this.batchLabel) {
            if (this.typeLabel === 'all') {
                this.batchNodesByType(this.labelNodesByType.bitmap, 'label_bitmap')
                this.batchNodesByType(this.labelNodesByType.char, 'label_char')
                this.batchNodesByType(this.labelNodesByType.none, 'label_none')
            } else {
                this.batchNodesByType(this.labelNodes, 'label')
            }
        }

        if (this.batchSprite) {
            this.batchNodesByType(this.spriteNodes, 'sprite')
        }

        if (this.batchByName) {
            for (const name in this.namedNodes) {
                const nodes = this.namedNodes[name]
                if (!nodes) continue

                const batchParent = this.batchNodes[name]
                if (batchParent) {
                    this.batchNodeArray(nodes, batchParent)
                }
            }
        }
    }

    private batchNodesByType(nodes: Node[], type: string): void {
        const batchParent = this.batchNodes[type]
        if (!batchParent) return
        this.batchNodeArray(nodes, batchParent)
    }
}
