import { Director, Node, Vec3, _decorator, director, tween } from 'cc'
import AnimUtils, { type TweenObj } from '../../../../../utils/AnimUtils'
import ComponentUtils from '../../../../../utils/ComponentUtils'
import AnimAbortSignal from '../../../../../utils/common/AnimAbortSignal'
import { GameSceneLayout, type StatusConfig } from '../GameSceneLayout'
import { Header } from '../header/Header'

const { ccclass, property } = _decorator

@ccclass('PortraitLayout')
export class PortraitLayout extends GameSceneLayout {
    @property(Header)
    private header!: Header

    @property([Node])
    private topAnimNodes: Node[] = []
    private originTopNodesPos: Vec3[] = []

    @property([Node])
    private botAnimNodes: Node[] = []
    private originBotNodesPos: Vec3[] = []

    private hideBotWorldY = -80

    public onLoad(): void {
        this.validateRequiredComponents()

        //? wait LayoutComponent finish layout (not dirty)
        director.once(Director.EVENT_AFTER_UPDATE, () => {
            this.saveOriginPos()
            this.initUIPos()
            this.isInited = true
        })
    }

    private validateRequiredComponents(): void {
        ComponentUtils.lazyValidateProperties(this)
    }

    private saveOriginPos(): void {
        for (const node of this.topAnimNodes) {
            this.originTopNodesPos.push(node.position.clone())
        }

        for (const node of this.botAnimNodes) {
            this.originBotNodesPos.push(node.position.clone())
        }
    }

    private initUIPos(): void {
        this.topAnimNodes.forEach((node, idx) => {
            const startPos = this.originTopNodesPos[idx]?.clone() ?? new Vec3(0, 0, 0)
            startPos.y += 200
            node.setPosition(startPos)
        })

        const temp = new Vec3(0, this.hideBotWorldY, 0)
        this.botAnimNodes.forEach((node, idx) => {
            const startPos = this.originBotNodesPos[idx]?.clone() ?? new Vec3(0, 0, 0)
            temp.y = this.hideBotWorldY
            const y = node.parent?.inverseTransformPoint(temp, temp).y ?? 200
            startPos.y = y
            node.setPosition(startPos)
        })
    }

    public setLevelText(level: number): void {
        this.header.setLevelText(level)
    }

    public disableLayout(): void {
        this.header.setButtonsInteractable(false)
    }

    public enableLayout(): void {
        this.header.setButtonsInteractable()
    }

    public updateStatus(config: StatusConfig): void {
        super.updateStatus(config)
        this.header.updateStatus(config)
    }

    public async playExitAnimUI(duration: number = 0.17): Promise<void> {
        this.entranceSignal.abort()
        this.exitSignal.abort()
        this.exitSignal = new AnimAbortSignal()

        const exitTweens: Promise<TweenObj<Node>>[] = []

        this.topAnimNodes.forEach((node, idx) => {
            const des = new Vec3(node.position.x, (this.originTopNodesPos[idx]?.y ?? 0) + 200, 0)
            const t = tween(node)
            t.to(duration, { position: des }, { easing: 'backIn' })

            exitTweens.push(AnimUtils.runTweenAsync(t, this.exitSignal))
        })

        const temp = new Vec3(0, this.hideBotWorldY, 0)

        for (const node of this.botAnimNodes) {
            temp.y = this.hideBotWorldY
            const y = node.parent?.inverseTransformPoint(temp, temp).y ?? 200
            const des = new Vec3(node.position.x, y, 0)
            const t = tween(node)
            t.to(duration, { position: des }, { easing: 'backIn' })

            exitTweens.push(AnimUtils.runTweenAsync(t, this.exitSignal))
        }

        await Promise.all(exitTweens)

        if (this.exitSignal.aborted) return

        this.header.setActive(false)
    }

    public async playEntranceAnimUI(duration: number = 0.2): Promise<void> {
        this.exitSignal.abort()
        this.entranceSignal.abort()
        this.entranceSignal = new AnimAbortSignal()

        this.header.setActive(true)

        const entranceTweens: Promise<TweenObj<Node>>[] = []

        this.topAnimNodes.forEach((node, idx) => {
            const des = new Vec3(node.position.x, this.originTopNodesPos[idx]?.y ?? 0, 0)
            const t = tween(node)
            t.to(duration, { position: des }, { easing: 'backOut' })

            entranceTweens.push(AnimUtils.runTweenAsync(t, this.entranceSignal))
        })

        this.botAnimNodes.forEach((node, idx) => {
            const des = new Vec3(node.position.x, this.originBotNodesPos[idx]?.y ?? 0, 0)
            const t = tween(node)
            t.to(duration, { position: des }, { easing: 'backOut' })

            entranceTweens.push(AnimUtils.runTweenAsync(t, this.entranceSignal))
        })

        await Promise.all(entranceTweens)
    }

    public highlightCoinContainer(): void {
        this.header.highlightCoinContainer()
    }

    public unHighlightCoinContainer(): void {
        this.header.removeCoinContainerHighlight()
    }

    public handleScoreUpdated(score: number): void {
        this.header.scoreContainer.updateScore(score)
    }
}
