import Grid from '../../grid'
import type Tile from '../../tile'
import getRandomTile from './getRandomTile'

const { Array: A } = Ludex.Utils

function getRandomPair(grid: Grid<Tile>, seed?: number): [Tile, Tile]
function getRandomPair(tiles: ReadonlyArray<Tile>, seed?: number): [Tile, Tile]

function getRandomPair(gridOrTiles: Grid<Tile> | ReadonlyArray<Tile>, seed = Date.now()) {
    const arr = gridOrTiles instanceof Grid ? gridOrTiles.asArray() : gridOrTiles
    const randomTile = getRandomTile(arr, seed)
    const sameTypeTile = A.search(arr, (tile) => tile.isSimilarTo(randomTile) && !tile.inSamePositionAs(randomTile))

    if (sameTypeTile === undefined) throw new Error('Could not find a matching tile')

    return [randomTile, sameTypeTile]
}

export default getRandomPair
