import { Color, type IColorLike, js, MeshRenderData, misc, Vec3 } from 'cc'
import type { ParticleSystem } from './ParticleSystem'

const ZERO_VEC3 = new Vec3(0, 0, 0)
const formatBytes = 9

export class Particle {
    public pos: Vec3 = new Vec3(0, 0, 0)
    public color = new Color(255, 255, 255, 255)
    public startColor!: Color
    public size: Vec3 = new Vec3(0, 0, 0)
    public startSize: Vec3 = new Vec3(0, 0, 0)
    public rotation = new Vec3(0, 0, 0)
    public startLifetime = 0
    public remainingLifetime = 0
    public randomSeed = 0
    public velocity = new Vec3(0, 0)
    public width = 0
    public height = 0

    public init(): void {
        this.pos.set(ZERO_VEC3)

        this.remainingLifetime = 0
        this.startLifetime = 0
        this.startSize.set(ZERO_VEC3)
        this.startColor = Color.WHITE.clone()
        this.velocity.set(ZERO_VEC3)

        this.randomSeed = 0
    }
}

class ParticlePool extends js.Pool<Particle> {
    public get(): Particle {
        return this._get() || new Particle()
    }
}

const pool = new ParticlePool((par: Particle): void => {
    par.pos.set(ZERO_VEC3)
    par.color.set(255, 255, 255, 255)
    par.size.set(ZERO_VEC3)
    par.rotation.set(ZERO_VEC3)
}, 1024)

export class ParticleSimulator {
    public particles: Particle[] = []
    public active = false
    public uvFilled = 0
    public renderData: MeshRenderData = new MeshRenderData()
    private sys: ParticleSystem

    constructor(system: ParticleSystem) {
        this.sys = system
        this.particles = []
        this.active = false
        this.uvFilled = 0
    }

    public stop(): void {
        this.active = false
    }

    public reset(): void {
        this.active = true
        const particles = this.particles
        for (const particle of particles) {
            pool.put(particle)
        }
        particles.length = 0
        this.renderData.resize(0, 0)
    }

    public emitParticle(): Particle {
        const particle = pool.get()
        this.particles.push(particle)

        return particle
    }

    public updateUVs(force?: boolean): void {
        const renderData = this.renderData
        if (renderData && this.sys) {
            const vbuf = renderData.vData
            const spriteFrame = this.sys.spriteFrame
            if (!spriteFrame) return
            const uv = spriteFrame.uv

            const start = force ? 0 : this.uvFilled
            const particleCount = this.particles.length
            for (let i = start; i < particleCount; i++) {
                const offset = i * formatBytes * 4
                vbuf[offset + 3] = uv[0] ?? 0
                vbuf[offset + 4] = uv[1] ?? 0
                vbuf[offset + 12] = uv[2] ?? 0
                vbuf[offset + 13] = uv[3] ?? 0
                vbuf[offset + 21] = uv[4] ?? 0
                vbuf[offset + 22] = uv[5] ?? 0
                vbuf[offset + 30] = uv[6] ?? 0
                vbuf[offset + 31] = uv[7] ?? 0
            }
            this.uvFilled = particleCount
        }
    }

    public updateParticleBuffer(particle: Particle, pos: Vec3, buffer: MeshRenderData, offset: number): void {
        const vbuf = buffer.vData

        const x: number = pos.x
        const y: number = pos.y
        const z: number = pos.z

        const width = particle.width * particle.size.x
        const height = particle.height * particle.size.y

        const halfWidth = width / 2
        const halfHeight = height / 2

        const rotX = particle.rotation.x || 0
        const rotY = particle.rotation.y || 0
        const rotZ = particle.rotation.z || 0

        if (rotX === 0 && rotY === 0 && rotZ === 0) {
            // No rotation: direct assignment
            vbuf[offset] = x - halfWidth
            vbuf[offset + 1] = y - halfHeight
            vbuf[offset + 2] = z

            vbuf[offset + 9] = x + halfWidth
            vbuf[offset + 10] = y - halfHeight
            vbuf[offset + 11] = z

            vbuf[offset + 18] = x - halfWidth
            vbuf[offset + 19] = y + halfHeight
            vbuf[offset + 20] = z

            vbuf[offset + 27] = x + halfWidth
            vbuf[offset + 28] = y + halfHeight
            vbuf[offset + 29] = z
        } else {
            // Rotation in radians
            const radX = -misc.degreesToRadians(rotX)
            const radY = -misc.degreesToRadians(rotY)
            const radZ = -misc.degreesToRadians(rotZ)

            const cosX = Math.cos(radX)
            const sinX = Math.sin(radX)
            const cosY = Math.cos(radY)
            const sinY = Math.sin(radY)
            const cosZ = Math.cos(radZ)
            const sinZ = Math.sin(radZ)

            function rotatePoint(px: number, py: number, pz: number): Vec3 {
                // Rotate around X-axis
                const y1 = py * cosX - pz * sinX
                const z1 = py * sinX + pz * cosX

                // Rotate around Y-axis
                const x2 = px * cosY + z1 * sinY
                const z2 = z1 * cosY - px * sinY

                // Rotate around Z-axis
                const x3 = x2 * cosZ - y1 * sinZ
                const y3 = x2 * sinZ + y1 * cosZ

                return new Vec3(x3, y3, z2)
            }

            const positions: [Vec3, Vec3, Vec3, Vec3] = [
                rotatePoint(-halfWidth, -halfHeight, 0), // bottom-left
                rotatePoint(halfWidth, -halfHeight, 0), // bottom-right
                rotatePoint(-halfWidth, halfHeight, 0), // top-left
                rotatePoint(halfWidth, halfHeight, 0), // top-right
            ]

            vbuf[offset] = positions[0].x + x
            vbuf[offset + 1] = positions[0].y + y
            vbuf[offset + 2] = positions[0].z + z

            vbuf[offset + 9] = positions[1].x + x
            vbuf[offset + 10] = positions[1].y + y
            vbuf[offset + 11] = positions[1].z + z

            vbuf[offset + 18] = positions[2].x + x
            vbuf[offset + 19] = positions[2].y + y
            vbuf[offset + 20] = positions[2].z + z

            vbuf[offset + 27] = positions[3].x + x
            vbuf[offset + 28] = positions[3].y + y
            vbuf[offset + 29] = positions[3].z + z
        }

        // Color data
        Color.toArray(vbuf, particle.color as IColorLike, offset + 5)
        Color.toArray(vbuf, particle.color as IColorLike, offset + 14)
        Color.toArray(vbuf, particle.color as IColorLike, offset + 23)
        Color.toArray(vbuf, particle.color as IColorLike, offset + 32)
    }

    public step(dt: number): void {
        const psys = this.sys
        const node = psys.node
        const particles = this.particles
        // dt = dt > assembler.maxParticleDeltaTime ? assembler.maxParticleDeltaTime : dt

        // Calculate pos
        node.updateWorldTransform()

        // Emission
        if (this.active) {
            this.sys.emitting(dt)
        }

        // Request buffer for particles
        const renderData = this.renderData
        const particleCount = particles.length
        renderData.reset()
        this.requestData(particleCount * 4, particleCount * 6)

        // Fill up uvs
        if (particleCount > this.uvFilled) {
            this.updateUVs()
        }

        // Used to reduce memory allocation / creation within the loop
        let particleIdx = 0
        while (particleIdx < particles.length) {
            const particle = particles[particleIdx]
            if (!particle) continue

            particle.remainingLifetime -= dt
            if (particle.remainingLifetime > 0) {
                const offset = formatBytes * particleIdx * 4
                this.updateParticleBuffer(particle, particle.pos, renderData, offset)

                // update particle counter
                ++particleIdx
            } else {
                const deadParticle = particles[particleIdx]
                if (!deadParticle) continue

                if (particleIdx !== particles.length - 1) {
                    const lastParticle = particles[particles.length - 1]
                    if (lastParticle) {
                        particles[particleIdx] = lastParticle
                    }
                }

                pool.put(deadParticle)

                particles.length--
                renderData.resize(renderData.vertexCount - 4, renderData.indexCount - 6)
            }
        }

        this.renderData.material = this.sys.getRenderMaterial(0) // hack
        this.renderData.frame = this.sys.spriteFrame // hack
        renderData?.setRenderDrawInfoAttributes?.()
    }

    requestData(vertexCount: number, indexCount: number): void {
        let offset = this.renderData.indexCount
        this.renderData.request(vertexCount, indexCount)
        const count = this.renderData.indexCount / 6
        const buffer = this.renderData.iData
        for (let i = offset; i < count; i++) {
            const vId = i * 4
            buffer[offset++] = vId
            buffer[offset++] = vId + 1
            buffer[offset++] = vId + 2
            buffer[offset++] = vId + 1
            buffer[offset++] = vId + 3
            buffer[offset++] = vId + 2
        }
    }

    public initDrawInfo(): void {
        const renderData = this.renderData
        renderData?.setRenderDrawInfoAttributes?.()
    }
}
