import { _decorator, CCBoolean, Component, Node, Sprite, SpriteFrame, tween, UIOpacity } from 'cc'
import { Localize } from '../../../../../../components/text/Localize'
import SkinKeys from '../../../../../../gameplay/constants/SkinKeys'
import ThemeKeys from '../../../../../../gameplay/constants/ThemeKeys'
import ThemeLocaleKeys from '../../../../../../gameplay/constants/ThemeLocaleKeys'
import { ThemePreviewResourcesMapping } from '../../../../../../gameplay/constants/ThemeResourcesPath'
import AnimUtils from '../../../../../../utils/AnimUtils'
import ComponentUtils from '../../../../../../utils/ComponentUtils'
import AnimAbortSignal from '../../../../../../utils/common/AnimAbortSignal'
import ResourceUtils from '../../../../../../utils/ResourceUtils'
import { LockAnimator } from './common/lock/LockAnimator'
import { ThemeProgressBar } from './common/ThemeProgressBar'

const { ccclass, property } = _decorator

const defaultBlock: CollectionsPayload = {
    progress: 0,
    max: 10,
    isLocked: true,
    theme: ThemeKeys.SUMMER,
    skins: [SkinKeys.FRUIT],
}

const { Array: A } = Ludex.Utils
@ccclass('ThemeBlock')
export class ThemeBlock extends Component {
    @ComponentUtils.required()
    @property(Node)
    private readonly mask!: Node

    @ComponentUtils.required()
    @property(Node)
    private readonly progressBar!: Node

    @ComponentUtils.required()
    @property(LockAnimator)
    private readonly lockAnimator!: LockAnimator

    @ComponentUtils.required()
    @property({ type: Node, group: 'Container' })
    public container!: Node

    @ComponentUtils.required()
    @property({ type: UIOpacity, group: 'Container' })
    private readonly containerOpacity!: UIOpacity

    @ComponentUtils.required()
    @property({ type: Node, group: 'Theme' })
    private readonly themeLoadingRing!: Node

    @ComponentUtils.required()
    @property({ type: Sprite, group: 'Theme' })
    private readonly themeSprite!: Sprite

    @ComponentUtils.required()
    @property({ type: Localize, group: 'Title' })
    private readonly titleLocalizer!: Localize

    @ComponentUtils.required()
    @property({ type: UIOpacity, group: 'Title' })
    private readonly titleOpacity!: UIOpacity

    @ComponentUtils.required()
    @property({ type: Sprite, group: 'Tile' })
    private readonly tileBlockIconSprite: Sprite[] = []

    @ComponentUtils.required()
    @property({ type: UIOpacity, group: 'Tile' })
    private readonly newTilesOpacity!: UIOpacity

    private maskUIOpacity!: UIOpacity

    private signal: AnimAbortSignal = new AnimAbortSignal()

    @property(CCBoolean)
    public set isLocked(value: boolean) {
        this.payload.isLocked = value
        this.updateInfo()
    }

    public get isLocked(): boolean {
        return this.payload.isLocked
    }

    private payload: CollectionsPayload = defaultBlock

    public __preload() {
        this.maskUIOpacity = this.mask.getComponent(UIOpacity)!

        ComponentUtils.validateRequiredProperties(this, ThemeBlock)
    }

    public updateInfo(payload: CollectionsPayload = defaultBlock): void {
        this.payload = payload
        const { progress, max, isLocked, theme, skins } = payload

        this.signal.abort()
        const signal = new AnimAbortSignal()
        this.signal = signal

        this.updateTitle(theme)
        this.updateSkinAsync(skins, signal)
        this.updateThemeAsync(theme, signal)

        const progressBarComponent = this.progressBar.getComponent(ThemeProgressBar)

        if (!progressBarComponent) {
            throw new Error('ThemeBlock: progressBarComponent not found')
        }

        this.mask.active = isLocked
        this.progressBar.active = !isLocked
        this.lockAnimator.node.active = isLocked
        if (isLocked) this.lockAnimator.lock()
        this.maskUIOpacity.opacity = isLocked ? 128 : 0

        if (isLocked) return

        progressBarComponent.setProgress(progress, max)
    }

    public async playUnlockAnim(signal: AnimAbortSignal, payload: CollectionsPayload = defaultBlock): Promise<void> {
        this.payload = { ...payload, isLocked: false }
        const { progress, max } = payload

        const progressBarComponent = this.progressBar.getComponent(ThemeProgressBar)

        if (!progressBarComponent) throw new Error('ThemeBlock: progressBarComponent not found')

        this.mask.active = true
        this.progressBar.active = false
        this.lockAnimator.node.active = true

        progressBarComponent.setProgress(progress, max)
        await this.animateUnlockedState(signal)
    }

    private updateTitle(theme: ThemeKeys) {
        const localeKey = ThemeLocaleKeys[theme]
        this.titleLocalizer.key = localeKey
    }

    @ComponentUtils.onlyRunInLifeCycleAsync
    private async updateSkinAsync(skinKeys: SkinKeys[], signal: AnimAbortSignal): Promise<void> {
        const tileCount = this.tileBlockIconSprite.length
        const skinCount = skinKeys.length

        const spritesPromises = skinKeys.map(async (skinKey) => {
            const sprites = await ResourceUtils.loadSkinPreviewAsync(skinKey)
            return sprites
        })

        const skinSprites = await Promise.all(spritesPromises)

        if (signal.aborted || !this.tileBlockIconSprite) return

        if (skinCount <= tileCount) {
            const spritesPerSkin = Math.floor(tileCount / skinCount)
            const remainingSprites = tileCount % skinCount

            const spritesEachSkins = [...A.fillWith(skinCount, () => spritesPerSkin)]
            for (let i = 0; i < remainingSprites; i++) {
                const count = spritesEachSkins[i] as (typeof spritesEachSkins)[number]
                spritesEachSkins[i] = count + 1
            }

            let tileIndex = 0

            for (let i = 0; i < skinCount; i++) {
                const count = spritesEachSkins[i] as (typeof spritesEachSkins)[number]
                for (let j = 0; j < count && tileIndex < tileCount; j++, tileIndex++) {
                    const currentSkinSprites = skinSprites[i] as (typeof skinSprites)[number]
                    const skinSprite = currentSkinSprites[j] as (typeof currentSkinSprites)[number]
                    const tileIcon = this.tileBlockIconSprite[tileIndex] as (typeof this.tileBlockIconSprite)[number]
                    tileIcon.spriteFrame = skinSprite
                }
            }
        } else {
            for (let index = 0, len = this.tileBlockIconSprite.length; index < len; index++) {
                const tile = this.tileBlockIconSprite[index] as (typeof this.tileBlockIconSprite)[number]
                const currentSkinSprites = skinSprites[index % skinCount] as (typeof skinSprites)[number]
                const firstSkinSprite = currentSkinSprites[0] as (typeof currentSkinSprites)[number]
                tile.spriteFrame = firstSkinSprite
            }
        }
    }

    private async updateThemeAsync(theme: ThemeKeys, signal: AnimAbortSignal): Promise<void> {
        this.themeSprite.spriteFrame = null

        let sprite = ResourceUtils.getAsset(`${ThemePreviewResourcesMapping[theme]}/spriteFrame`, SpriteFrame)

        if (!sprite) {
            this.themeLoadingRing.active = true

            //? Wait for a short time to prevent load image too fast
            await AnimUtils.waitTweenAsync(0.1, signal)
            if (signal.aborted) return

            sprite = await ResourceUtils.loadThemePreviewAsync(theme)
        }

        if (!sprite) {
            //? Fallback to summer theme
            sprite = await ResourceUtils.loadThemePreviewAsync(ThemeKeys.SUMMER)
        }

        if (!this.themeSprite || !sprite || signal.aborted) return

        this.themeSprite.spriteFrame = sprite
        this.themeLoadingRing.active = false
    }

    private async animateUnlockedState(signal: AnimAbortSignal) {
        const progressBarUIOpacity = this.progressBar.getComponent(UIOpacity)

        if (!progressBarUIOpacity) {
            throw new Error('ThemeBlock: progressBarUIOpacity not found')
        }

        this.mask.active = true
        this.maskUIOpacity.opacity = this.maskUIOpacity.opacity ?? 255
        this.progressBar.active = true
        progressBarUIOpacity.opacity = 0

        await this.lockAnimator.unlock(signal)
        this.lockAnimator.node.active = false

        await Promise.all([
            AnimUtils.runTweenAsync(tween(this.maskUIOpacity).to(0.25, { opacity: 0 }), signal),
            AnimUtils.runTweenAsync(tween(progressBarUIOpacity).to(0.25, { opacity: 255 }), signal),
        ])

        this.mask.active = false
        this.maskUIOpacity.opacity = 0
        progressBarUIOpacity.opacity = 255
    }

    public setVisible(value: boolean) {
        this.containerOpacity.opacity = value ? 255 : 0
        this.titleOpacity.opacity = value ? 255 : 0
        this.newTilesOpacity.opacity = value ? 255 : 0
    }
}
