import shuffleArray from '../array/shuffle'

/**
 * Iterator to iterate through a grid in a spiral from the outside in
 * @param width the width of the grid
 * @param height the height of the grid
 * @yields [x, y] coordinates of the grid, row first
 */
export default function* randomTraversal(width: number, height: number): Generator<Vec2Like, void, unknown> {
    const tldr = []

    for (let i = 0; i < width; i++) {
        for (let j = 0; j < height; j++) {
            tldr.push({ x: i, y: j })
        }
    }

    const random = shuffleArray(tldr)

    for (const { x, y } of random) {
        yield { x, y }
    }
}
