import DailyRewardsData from '../constants/DailyRewardsData'
import { ScreenKeysEnum } from '../constants/ScreenKeys'
import ScreenUtils from './ScreenUtils'

const { player, storage, dailyRewards } = codex

const {
    Utils: { Array: A },
    Configs: {
        DailyRewards: { MockTime, MaxDays, CheckInterrupt },
    },
} = Ludex

const DATE_LENGTH = MockTime > 0 ? MockTime : 864e5

type DailyReward = Ludex.Plugins.DailyRewards.DailyReward

class DailyRewardsUtilsCore {
    protected static lastUpdate = 0
    protected static updateInterval = 0

    protected static setDirty$ = () => {
        this.lastUpdate = 0
    }

    public static logLoginReward() {
        dailyRewards.logLoginReward()
        this.setDirty$()
        this.tryUpdateData()
    }

    public static async tryUpdateData() {
        if (!this.shouldUpdateData$()) return
        this.lastUpdate = Date.now()
        await dailyRewards.requestDailyRewardsAsync()
        const remaining = this.getCurrentRemainingTime$()
        this.updateInterval = remaining / 2
    }

    public static async hasClaimableRewards() {
        await this.tryUpdateData()
        const rewards = dailyRewards.getDailyRewards()
        if (rewards === null) return false
        return A.search(Object.values(rewards), (i) => i.status === 'ready') !== undefined
    }

    public static async getRewardsData() {
        await this.tryUpdateData()
        const dailyFromPlugin = dailyRewards.getDailyRewards()
        if (dailyFromPlugin === null) return null

        const rewardsData: DailyReward[] = []

        for (let i = 0; i < MaxDays; i++) {
            const dailyRewardsData = DailyRewardsData[i]
            if (!dailyRewardsData) continue

            //? Data from plugin is 1-based
            const dayData = dailyFromPlugin[i + 1]

            rewardsData.push({
                id: dailyRewardsData.id,
                type: dailyRewardsData.type,
                status: dailyRewardsData.status,
                ...dayData,
                value: dailyRewardsData.value,
                items: dailyRewardsData.items ? { ...dailyRewardsData.items } : undefined,
            })
        }

        return rewardsData
    }

    public static async getRemainingTime() {
        await this.tryUpdateData()
        return this.getCurrentRemainingTime$()
    }

    protected static getCurrentRemainingTime$ = () => {
        const rewards = dailyRewards.getDailyRewards()
        if (rewards === null) return 0

        const { firstReward = 0, lastReward = 0 } = player.getPlayerData().dailyRewardedData ?? {}

        if (CheckInterrupt) {
            const lastRewardIndex = this.findLastRewardedIndex$()
            if (lastRewardIndex === -1) return 0
            const nextReward = firstReward + (lastRewardIndex + 1) * DATE_LENGTH
            return Math.max(nextReward - Date.now(), 0)
        }

        const nextReward = lastReward + DATE_LENGTH
        return Math.max(nextReward - Date.now(), 0)
    }

    protected static findLastRewardedIndex$ = () => {
        const { logDays = [] } = player.getPlayerData().dailyRewardedData ?? {}

        for (let i = logDays.length - 1; i >= 0; i--) {
            if (logDays[i]) return i
        }

        return -1
    }

    protected static shouldUpdateData$ = () => {
        return Date.now() - this.lastUpdate > this.updateInterval
    }
}

class DailyRewardsUtils extends DailyRewardsUtilsCore {
    public static hasShownPopup() {
        const store = storage.getStorage('dashboardDailyRewards')
        return store.hasShownPopup
    }

    public static setHasShownPopup(shown = true) {
        storage.setStorageData('dashboardDailyRewards', 'hasShownPopup', shown)
    }

    public static async openPopupScreen() {
        await ScreenUtils.openScreen(ScreenKeysEnum.DAILY_REWARDS_SCREEN)
    }

    public static async openPopupScreenAndWaitForClose() {
        await ScreenUtils.openScreenAndWaitForClose(ScreenKeysEnum.DAILY_REWARDS_SCREEN)
    }
}

export default DailyRewardsUtils
