import { Component, _decorator } from 'cc'
import CustomModeUtils from '../../utils/CustomModeUtils'
import MatchUtils from '../../utils/MatchUtils'
import GameplayEvents from '../constants/GameEvents'
import GameModes from '../constants/GameMode'

const { ccclass } = _decorator
const {
    Match: { Modes: MatchModes },
} = Ludex

@ccclass('ScoreManager')
export class ScoreManager extends Component {
    private currentScore: number = 0

    public addScore(level: number, combo: number, remainingTime: number): void {
        const levelMultiplier = this.getLevelMultiplier(level)
        const comboMultiplier = this.getComboMultiplier(combo)
        const timeMultiplier = this.getTimeMultiplier(level, remainingTime)
        const customBonusScoreMultiplier = this.getCustomBonusScoreMultiplier()

        const newScore = Math.floor(
            this.currentScore + 10 * levelMultiplier * comboMultiplier * timeMultiplier * customBonusScoreMultiplier,
        )
        const roundScore = Math.ceil(newScore / 10) * 10

        this.setScore(roundScore)
    }

    public init(): void {
        this.initCurrentScore()
        this.updateScore()
    }

    private initCurrentScore(): void {
        const mode = MatchUtils.getMatchMode()

        if (mode === MatchModes.TOURNAMENT) {
            this.currentScore = MatchUtils.getMatchCustomData()?.score || 0
            return
        }

        this.currentScore = 0
    }

    private setScore(score: number): void {
        this.currentScore = score
        this.updateScore()
    }

    private async updateScore(): Promise<void> {
        this.node.scene.emit(GameplayEvents.SCORE_UPDATED, this.currentScore)
    }

    public getCurrentScore(): number {
        return this.currentScore
    }

    private getLevelMultiplier(level: number): number {
        return 1 + (level - 1) * 0.1
    }

    private getComboMultiplier(combo: number): number {
        return 1 + (combo - 1) * 0.1
    }

    private getTimeMultiplier(level: number, remainingTime: number): number {
        if (level <= 2) {
            return 1 + 60 * 0.05
        }
        return 1 + remainingTime * 0.05
    }

    private getCustomBonusScoreMultiplier(): number {
        if (MatchUtils.getMatchMode() !== GameModes.CUSTOM) return 1

        const customBonus = CustomModeUtils.getCustomBonus()
        return 1 + customBonus / 100
    }
}
