import OverlayUtils from 'db://assets/utils/OverlayUtils'
import type { Scene } from 'cc'

type OverlayView = FBInstant.OverlayView

const {
    Utils: { Time: T, Object: O },
} = Ludex

const TOP_PLAYER_OVERLAY_PATH = 'overlays/tournament/top-player-overlay.xml'
const TOP_PLAYER_OVERLAY_CSS = 'overlays/tournament/top-player-overlay-styles.css'

const LEADERBOARD_OVERLAY_PATH = 'overlays/tournament/leaderboard-overlay.xml'
const LEADERBOARD_OVERLAY_CSS = 'overlays/tournament/leaderboard-overlay-styles.css'

type TournamentDetailPayload = {
    tournamentPlayers: TournamentPlayer[]
    leaderboardPosition: ElementInGamePosition
    topPlayerPosition: ElementInGamePosition
    currentPlayerId: string
}

type ElementInGamePosition = {
    worldX: number
    worldY: number
    width: number
    height: number
}

type TournamentPlayer = {
    playerId: string
    rank: number
    score: number
}

class TournamentDetailOverlay {
    private topPlayerDiv$!: HTMLDivElement
    private topPlayerElement$: OverlayView | undefined

    private leaderboardDiv$!: HTMLDivElement
    private leaderboardElement$: OverlayView | undefined
    public scene!: Scene

    private currentPayload$: TournamentDetailPayload | undefined
    private isScreenActive: boolean = false

    constructor(scene: Scene) {
        this.scene = scene
        this.createLeaderboardDiv$()
        this.createTopPlayerDiv$()
    }

    private createLeaderboardDiv$ = (): void => {
        this.leaderboardDiv$ = document.createElement('div')

        // Apply styles using Object.assign for better performance
        O.assign(this.leaderboardDiv$.style, {
            position: 'absolute',
            transform: 'translate(-50%, -50%)',
            left: '50%',
            scrollbarWidth: 'none',
            alignItems: 'start',
            overflow: 'auto',
            backgroundColor: 'transparent',
            opacity: '0',
            transition: 'opacity 0.3s ease-in-out',
            pointerEvents: 'none',
            display: 'none',
            userSelect: 'none',
        })

        document.body.appendChild(this.leaderboardDiv$)
    }

    private createTopPlayerDiv$ = (): void => {
        this.topPlayerDiv$ = document.createElement('div')

        // Apply styles using Object.assign for better performance
        O.assign(this.topPlayerDiv$.style, {
            position: 'absolute',
            transform: 'translate(-50%, -50%)',
            left: '50%',
            scrollbarWidth: 'none',
            alignItems: 'start',
            overflow: 'hidden',
            backgroundColor: 'transparent',
            opacity: '0',
            transition: 'opacity 0.3s ease-in-out',
            pointerEvents: 'none',
            display: 'none',
            userSelect: 'none',
        })

        document.body.appendChild(this.topPlayerDiv$)
    }

    public async updateOverlay(payload: TournamentDetailPayload): Promise<boolean> {
        try {
            if (!this.isScreenActive) return false

            const validatedPayload = this.validateOverlayPayload$(payload)

            this.moveAndResizeOverlays$(validatedPayload)

            // Only update if payload has changed
            if (!this.isPayloadChanged$(validatedPayload)) {
                await this.revealMainDivs$()
                return true
            }

            this.topPlayerElement$ = await this.updateTopPlayerElement$(validatedPayload)
            this.leaderboardElement$ = await this.updateLeaderboardElement$(validatedPayload)

            this.currentPayload$ = validatedPayload
            await this.showOverlay$()
        } catch (error) {
            console.error('Error creating overlay view:', error)
            return false
        }

        return true
    }

    private updateTopPlayerElement$ = async (
        validatedPayload: TournamentDetailPayload,
    ): Promise<OverlayView | undefined> => {
        // Get top 3 players sorted by rank (ascending)
        const top3Players = validatedPayload.tournamentPlayers
            .slice() // Create a copy to avoid mutating the original array
            .sort((a, b) => a.rank - b.rank) // Sort by rank ascending (rank 1 is first)
            .slice(0, 3) // Take only the first 3 players

        // Create a new payload with only the top 3 players
        const top3Payload: TournamentDetailPayload = {
            ...validatedPayload,
            tournamentPlayers: top3Players,
            leaderboardPosition: validatedPayload.leaderboardPosition,
            topPlayerPosition: validatedPayload.topPlayerPosition,
        }

        if (this.topPlayerElement$) {
            await this.topPlayerElement$.updateAsync(top3Payload)
            return this.topPlayerElement$
        }

        // Create the top player overlay element
        const overlay = await OverlayUtils.createOverlayViewAsync(
            TOP_PLAYER_OVERLAY_PATH,
            this.topPlayerDiv$,
            this.getTopPlayerIframeCss$(),
            TOP_PLAYER_OVERLAY_CSS,
            top3Payload,
        )

        return overlay
    }

    private updateLeaderboardElement$ = async (
        validatedPayload: TournamentDetailPayload,
    ): Promise<OverlayView | undefined> => {
        if (this.leaderboardElement$) {
            await this.leaderboardElement$.updateAsync(validatedPayload)

            const iframe = this.leaderboardElement$.getIFrameElement()
            const newIframeStyleString = this.getLeaderboardIframeCss$(validatedPayload)
            iframe.style.cssText = newIframeStyleString

            return this.leaderboardElement$
        }

        const overlay = await OverlayUtils.createOverlayViewAsync(
            LEADERBOARD_OVERLAY_PATH,
            this.leaderboardDiv$,
            this.getLeaderboardIframeCss$(validatedPayload),
            LEADERBOARD_OVERLAY_CSS,
            validatedPayload,
        )

        return overlay
    }

    private isPayloadChanged$ = (payload: TournamentDetailPayload): boolean => {
        if (!this.currentPayload$) return true

        // Check if the arrays have different lengths
        if (this.currentPayload$.tournamentPlayers.length !== payload.tournamentPlayers.length) {
            return true
        }

        // Compare each player object
        for (let i = 0; i < payload.tournamentPlayers.length; i++) {
            const currentPlayer = this.currentPayload$.tournamentPlayers[i]
            const newPlayer = payload.tournamentPlayers[i]

            // Safety check (though should not be needed after length check)
            if (!currentPlayer || !newPlayer) {
                return true
            }

            if (
                currentPlayer.playerId !== newPlayer.playerId ||
                currentPlayer.rank !== newPlayer.rank ||
                currentPlayer.score !== newPlayer.score
            ) {
                return true
            }
        }

        return false
    }

    private showOverlay$ = async (): Promise<void> => {
        if (!this.leaderboardElement$ || !this.topPlayerElement$) return

        const leaderboardPromise = this.leaderboardElement$.showAsync()
        const topPlayerPromise = this.topPlayerElement$.showAsync()

        await Promise.all([leaderboardPromise, topPlayerPromise])
        this.revealMainDivs$()
    }

    private revealMainDivs$ = (): void => {
        if (!this.isScreenActive) return
        if (!this.leaderboardElement$ || !this.topPlayerElement$) return

        O.assign(this.leaderboardDiv$.style, {
            display: 'block',
        })
        this.leaderboardDiv$.scrollTop = 0

        O.assign(this.topPlayerDiv$.style, {
            display: 'block',
        })

        requestAnimationFrame(() => {
            if (!this.isScreenActive) {
                this.leaderboardDiv$.style.display = 'none'
                this.topPlayerDiv$.style.display = 'none'
                return
            }

            O.assign(this.leaderboardDiv$.style, {
                opacity: '1',
                pointerEvents: 'auto',
            })
            O.assign(this.topPlayerDiv$.style, {
                opacity: '1',
                pointerEvents: 'auto',
            })
        })
    }

    private validateOverlayPayload$ = (payload: TournamentDetailPayload): TournamentDetailPayload => {
        const correctedPayload: TournamentDetailPayload = {
            ...payload,
            tournamentPlayers: [],
        }

        const validateRegEx = /^\d+$/

        for (const player of payload.tournamentPlayers) {
            // if player id contains any alphabet characters, delete the player
            if (!validateRegEx.test(player.playerId)) {
                continue
            }

            correctedPayload.tournamentPlayers.push(player)
        }

        return correctedPayload
    }

    public async hideOverlay(): Promise<void> {
        if (!this.leaderboardElement$) return
        if (!this.topPlayerElement$) return

        // Start fade-out effect
        O.assign(this.leaderboardDiv$.style, {
            opacity: '0',
            pointerEvents: 'none',
            display: 'none',
        })

        O.assign(this.topPlayerDiv$.style, {
            opacity: '0',
            pointerEvents: 'none',
            display: 'none',
        })
    }

    private getLeaderboardIframeCss$ = (payload: TournamentDetailPayload): string => {
        const widthString = 'width: 100%;'

        const { tournamentPlayers, leaderboardPosition } = payload

        const containerWidth = this.getElementDivWidth$(leaderboardPosition)
        const playerCount = tournamentPlayers.length

        // ? Optimal iframe height is based on player tab count and margin to not show scrollbar
        const heightString = `height: calc((${containerWidth}px * (107 / 593) - 2px) * ${playerCount} + 15px);`

        const borderWidthString = 'border-width: 0px;'
        const overflowString = 'overflow: hidden;'
        const displayString = 'display: block;'
        return `${widthString} ${borderWidthString} ${overflowString} ${displayString} ${heightString}   `
    }

    private getTopPlayerIframeCss$ = (): string => {
        const widthString = 'width: 100%;'

        // ? Prevent scrollbar from showing
        const positionString = 'position: absolute;'
        const heightString = 'height: 130%;'
        const topString = 'top: -23%;'

        const borderWidthString = 'border-width: 0px;'
        const overflowString = 'overflow: hidden;'
        const displayString = 'display: block;'
        return `${widthString} ${borderWidthString} ${overflowString} ${displayString} ${heightString} ${topString} ${positionString}`
    }

    private getElementDivWidth$ = (payload: ElementInGamePosition): number => {
        const { width } = payload
        return width * OverlayUtils.getGameScaleFactor()
    }

    private getElementDivHeight$ = (payload: ElementInGamePosition): number => {
        const { height } = payload
        return height * OverlayUtils.getGameScaleFactor()
    }

    private getElementDivTopOffset$ = (payload: ElementInGamePosition): number => {
        const { worldY } = payload
        return worldY * OverlayUtils.getGameScaleFactor()
    }

    private getElementDivLeftOffset$ = (payload: ElementInGamePosition): number => {
        const { worldX } = payload
        return worldX * OverlayUtils.getGameScaleFactor()
    }

    private moveAndResizeOverlays$ = (payload: TournamentDetailPayload): void => {
        const { leaderboardPosition, topPlayerPosition } = payload

        this.updateDivPosition$(this.leaderboardDiv$, leaderboardPosition)
        this.updateDivPosition$(this.topPlayerDiv$, topPlayerPosition)
    }

    private updateDivPosition$ = (element: HTMLDivElement, payload: ElementInGamePosition): void => {
        if (!element) return

        const cssWidth = `${this.getElementDivWidth$(payload)}px`
        const cssHeight = `${this.getElementDivHeight$(payload)}px`
        const cssLeftOffset = `${this.getElementDivLeftOffset$(payload)}px`
        const cssTopOffset = `${this.getElementDivTopOffset$(payload)}px`

        O.assign(element.style, {
            width: cssWidth,
            height: cssHeight,
            left: cssLeftOffset,
            top: `calc(100vh - ${cssTopOffset})`,
        })
    }

    public setIsScreenActive(isScreenActive: boolean): void {
        this.isScreenActive = isScreenActive
    }
}

export default TournamentDetailOverlay
