import {
    _decorator,
    Color,
    Component,
    Director,
    director,
    type EventTouch,
    game,
    Input,
    Label,
    Sprite,
    sys,
    UIOpacity,
} from 'cc'
import GameEvents from '../../constants/GameEvents'
import ComponentUtils from '../../utils/ComponentUtils'
import { PerfCounter } from './PerfCounter'

const { ccclass, executionOrder, property, requireComponent } = _decorator

const {
    Utils: { Valid: V, Array: A, Device: D },
    Configs: {
        Debugger: { ListPlayerDevIds },
        // GameEngine: { DynamicAssets, HybridFileCache },
    },
} = Ludex

const { player } = codex

const DANGER_COLOR = Color.RED
const WARNING_COLOR = Color.YELLOW

const WHITE_COLOR = Color.WHITE
const BLACK_COLOR = Color.BLACK

@ccclass('GameStats')
@executionOrder(-1000)
@requireComponent([UIOpacity])
export class GameStats extends Component {
    @ComponentUtils.required()
    @property(Label)
    public dprLabel!: Label

    @ComponentUtils.required()
    @property(Label)
    public fpsLabel!: Label

    @ComponentUtils.required()
    @property(Label)
    public fpsRawLabel!: Label

    @ComponentUtils.required()
    @property(Label)
    public drawCountLabel!: Label

    @ComponentUtils.required()
    @property(Label)
    public rafTimeLabel!: Label

    @ComponentUtils.required()
    @property(Label)
    public deltaTimeLabel!: Label

    @ComponentUtils.required()
    @property(Label)
    public logicTimeLabel!: Label

    @ComponentUtils.required()
    @property(Label)
    public physicTimeLabel!: Label

    @ComponentUtils.required()
    @property(Label)
    public renderTimeLabel!: Label

    @ComponentUtils.required()
    @property(Label)
    public objectMemoryUsageLabel!: Label

    @ComponentUtils.required()
    @property(Label)
    public textureMemoryUsageLabel!: Label

    @ComponentUtils.required()
    @property(Sprite)
    public bg!: Sprite

    @ComponentUtils.required()
    @property(UIOpacity)
    public opacity!: UIOpacity

    private rafTimeCounter$!: PerfCounter
    private logicTimeCounter$!: PerfCounter
    private physicTimeCounter$!: PerfCounter
    private renderTimeCounter$!: PerfCounter

    private lastRafTime$ = 0
    private slowRefreshInterval$ = 1.0 // dpr, memory, draw-calls
    private mediumRefreshInterval$ = 0.5 // fps, logic time, render time, physic time
    private fastRefreshInterval$ = 0.16 // fps raw (~60fps)
    private isBackgroundEnabled$ = false

    private touchMs$ = 0
    private isDragging$ = false

    public __preload(): void {
        this.listenInputEvents$()
    }

    public onLoad(): void {
        ComponentUtils.validateRequiredProperties(this)
    }

    private listenInputEvents$ = () => {
        this.node.on(Input.EventType.TOUCH_START, this.handleTouchStart$)
        this.node.on(Input.EventType.TOUCH_MOVE, this.handleTouchMove$)
        this.node.on(Input.EventType.TOUCH_END, this.handleTouchEnd$)
        this.node.on(Input.EventType.TOUCH_CANCEL, this.handleTouchEnd$)

        this.node.on(Input.EventType.MOUSE_WHEEL, () => {
            console.info(this)
            game.emit(GameEvents.REQUEST_DYNAMIC_ATLAS)
        })
    }

    public start(): void {
        this.bg.enabled = false

        if (!this.canDisplayStats$()) {
            this.node.active = false
            return
        }

        this.node.active = true

        this.rafTimeCounter$ = new PerfCounter()
        this.logicTimeCounter$ = new PerfCounter()
        this.physicTimeCounter$ = new PerfCounter()
        this.renderTimeCounter$ = new PerfCounter()

        director.on(Director.EVENT_BEFORE_UPDATE, this.beforeUpdate$, this)
        director.on(Director.EVENT_AFTER_UPDATE, this.afterUpdate$, this)
        director.on(Director.EVENT_BEFORE_PHYSICS, this.beforePhysics$, this)
        director.on(Director.EVENT_AFTER_PHYSICS, this.afterPhysics$, this)
        director.on(Director.EVENT_BEFORE_DRAW, this.beforeDraw$, this)
        director.on(Director.EVENT_AFTER_DRAW, this.afterDraw$, this)

        this.startUpdateStats$()
    }

    private handleTouchStart$ = (event: EventTouch) => {
        event.preventSwallow = this.opacity.opacity === 0
        if (!event.touch || !this.opacity) return

        this.isDragging$ = false

        const isDoubleTap = Date.now() - this.touchMs$ < 300

        if (isDoubleTap) {
            if (this.opacity.opacity === 255) {
                this.opacity.opacity = 0
            } else {
                this.opacity.opacity = 255
                game.emit(GameEvents.REQUEST_DYNAMIC_ATLAS)
            }
        }

        this.touchMs$ = Date.now()
    }

    private handleTouchMove$ = (event: EventTouch) => {
        event.preventSwallow = this.opacity.opacity === 0
        if (!event.touch || !this.opacity) return

        const deltaPosition = event.touch.getUIDelta()

        // Start dragging if moved enough distance
        if (!this.isDragging$ && deltaPosition.length() > 10) {
            this.isDragging$ = true
        }

        if (this.isDragging$) {
            this.node.setPosition(this.node.position.x + deltaPosition.x, this.node.position.y + deltaPosition.y)
        }
    }

    private handleTouchEnd$ = (event: EventTouch) => {
        event.preventSwallow = this.opacity.opacity === 0
        if (!event.touch || !this.opacity) return

        // Toggle background only if not dragging
        if (!this.isDragging$) {
            this.bg.enabled = !this.bg.enabled
            this.isBackgroundEnabled$ = this.bg.enabled
        }

        this.isDragging$ = false
    }

    protected onDestroy(): void {
        if (!this.canDisplayStats$()) return

        director.off(Director.EVENT_BEFORE_UPDATE, this.beforeUpdate$, this)
        director.off(Director.EVENT_AFTER_UPDATE, this.afterUpdate$, this)
        director.off(Director.EVENT_BEFORE_PHYSICS, this.beforePhysics$, this)
        director.off(Director.EVENT_AFTER_PHYSICS, this.afterPhysics$, this)
        director.off(Director.EVENT_BEFORE_DRAW, this.beforeDraw$, this)
        director.off(Director.EVENT_AFTER_DRAW, this.afterDraw$, this)

        // Cleanup schedule timers
        this.unschedule(this.updateSlowStats$)
        this.unschedule(this.updateFastStats$)
        this.unschedule(this.updateMediumStats$)

        // Cleanup input event listeners
        this.node.off(Input.EventType.TOUCH_START, this.handleTouchStart$)
        this.node.off(Input.EventType.TOUCH_MOVE, this.handleTouchMove$)
        this.node.off(Input.EventType.TOUCH_END, this.handleTouchEnd$)
        this.node.off(Input.EventType.TOUCH_CANCEL, this.handleTouchEnd$)
    }

    private canDisplayStats$ = (): boolean => {
        return V.isDebugger() || A.has(ListPlayerDevIds, player.getPlayerId())
    }

    private startUpdateStats$ = () => {
        this.schedule(this.updateFastStats$, this.fastRefreshInterval$)
        this.schedule(this.updateSlowStats$, this.slowRefreshInterval$)
        this.schedule(this.updateMediumStats$, this.mediumRefreshInterval$)

        requestAnimationFrame(this.handleRequestAnimationFrame$)
    }

    private updateFastStats$ = () => {
        this.updateFpsRaw$()
        this.updateRafTime$()
        this.updateDeltaTime$()
    }

    private updateSlowStats$ = () => {
        this.updateDPR$()
        this.updateDrawCalls$()
        this.updateObjectMemoryUsage$()
        this.updateTextureMemoryUsage$()
    }

    private updateMediumStats$ = () => {
        this.updateFps$()
        this.updateLogicTime$()
        this.updatePhysicTime$()
        this.updateRenderTime$()
    }

    private calcRafTime$ = (timestamp: DOMHighResTimeStamp) => {
        if (!this.node?.isValid) return

        this.rafTimeCounter$.end(timestamp)
        this.lastRafTime$ = timestamp

        this.handleRequestAnimationFrame$()
    }

    private handleRequestAnimationFrame$ = () => {
        this.rafTimeCounter$.start(this.lastRafTime$)
        requestAnimationFrame(this.calcRafTime$)
    }

    private updateFps$ = () => {
        const fps = this.getFps$()
        this.updateFpsColor$(fps)
        this.fpsLabel.string = `FPS: ${fps}`
    }

    // ? Good fps is 60, so 1 frame is 16.6ms, that mean renderTime + logicTime + physicTime
    // ? If renderTime, logicTime or physicTime is bigger than 5ms, it's not good
    private updateFpsColor$ = (fps: number) => {
        if (fps < 30) {
            this.fpsLabel.color = DANGER_COLOR
        } else if (fps < 48) {
            this.fpsLabel.color = WARNING_COLOR
        } else {
            this.setColorWithBackground$(this.fpsLabel)
        }
    }

    private updateFpsRaw$ = () => {
        const fpsRaw = this.getFpsRaw$()
        this.updateFpsRawColor$(fpsRaw)
        this.fpsRawLabel.string = `FPS Raw: ${fpsRaw}`
    }

    private updateFpsRawColor$ = (fpsRaw: number) => {
        if (fpsRaw < 30) {
            this.fpsRawLabel.color = DANGER_COLOR
        } else if (fpsRaw < 48) {
            this.fpsRawLabel.color = WARNING_COLOR
        } else {
            this.setColorWithBackground$(this.fpsRawLabel)
        }
    }

    // ? The draw calls is the number of draw calls in the scene
    // ? If draw calls is bigger than 15, it's not good
    private updateDrawCalls$ = () => {
        const drawCount = this.getDrawCalls$()
        this.updateDrawCallsColor$(drawCount)
        this.drawCountLabel.string = `Draw Calls: ${drawCount}`
    }

    private updateDrawCallsColor$ = (drawCount: number) => {
        // * Special case: on iOS, drawCount higher will be ok.
        // ! But! only good to use with AutoAtlas and disable DynamicAtlas, if not, it's not good
        // ? Double check: setupDynamicAtlas in SystemManager.ts

        const isIOS = sys.os === sys.OS.IOS

        const dangerDrawCount = isIOS ? 80 : 40
        const warningDrawCount = isIOS ? 60 : 25

        if (drawCount > dangerDrawCount) {
            this.drawCountLabel.color = DANGER_COLOR
        } else if (drawCount > warningDrawCount) {
            this.drawCountLabel.color = WARNING_COLOR
        } else {
            this.setColorWithBackground$(this.drawCountLabel)
        }
    }

    private updateRafTime$ = () => {
        const rafTime = this.getRafTime$()
        this.updateRafTimeColor$(rafTime)
        this.rafTimeLabel.string = `RAF Time: ${rafTime}ms`
    }

    private updateRafTimeColor$ = (rafTime: number) => {
        // * 30fps: 33ms, 48fps: 20ms, 60fps: 16ms
        if (rafTime > 33) {
            this.rafTimeLabel.color = DANGER_COLOR
        } else if (rafTime > 20) {
            this.rafTimeLabel.color = WARNING_COLOR
        } else {
            this.setColorWithBackground$(this.rafTimeLabel)
        }
    }

    private updateDeltaTime$ = () => {
        const deltaTime = this.getDeltaTime$()
        this.updateDeltaTimeColor$(deltaTime)
        this.deltaTimeLabel.string = `Delta Time: ${deltaTime}ms`
    }

    private updateDeltaTimeColor$ = (deltaTime: number) => {
        // * 30fps: 33ms, 48fps: 20ms, 60fps: 16ms
        if (deltaTime > 33) {
            this.deltaTimeLabel.color = DANGER_COLOR
        } else if (deltaTime > 20) {
            this.deltaTimeLabel.color = WARNING_COLOR
        } else {
            this.setColorWithBackground$(this.deltaTimeLabel)
        }
    }

    private updateLogicTime$ = () => {
        const logicTime = this.getLogicTime$()
        this.updateLogicTimeColor$(logicTime)
        this.logicTimeLabel.string = `Logic Time: ${logicTime}ms`
    }

    private updateLogicTimeColor$ = (logicTime: number) => {
        if (logicTime > 10) {
            this.logicTimeLabel.color = DANGER_COLOR
        } else if (logicTime > 5) {
            this.logicTimeLabel.color = WARNING_COLOR
        } else {
            this.setColorWithBackground$(this.logicTimeLabel)
        }
    }

    private updatePhysicTime$ = () => {
        const physicTime = this.getPhysicTime$()
        this.updatePhysicTimeColor$(physicTime)
        this.physicTimeLabel.string = `Physic Time: ${physicTime}ms`
    }

    private updatePhysicTimeColor$ = (physicTime: number) => {
        if (physicTime > 10) {
            this.physicTimeLabel.color = DANGER_COLOR
        } else if (physicTime > 5) {
            this.physicTimeLabel.color = WARNING_COLOR
        } else {
            this.setColorWithBackground$(this.physicTimeLabel)
        }
    }

    private updateRenderTime$ = () => {
        const renderTime = this.getRenderTime$()
        this.updateRenderTimeColor$(renderTime)
        this.renderTimeLabel.string = `Render Time: ${renderTime}ms`
    }

    private updateRenderTimeColor$ = (renderTime: number) => {
        if (renderTime > 10) {
            this.renderTimeLabel.color = DANGER_COLOR
        } else if (renderTime > 5) {
            this.renderTimeLabel.color = WARNING_COLOR
        } else {
            this.setColorWithBackground$(this.renderTimeLabel)
        }
    }

    // ? The object memory usage is the memory used by the objects in the scene
    // ? If it's bigger than 40MB, it's not good
    private updateObjectMemoryUsage$ = () => {
        const objectMemoryUsage = this.getObjectMemoryUsage$()
        this.updateObjectMemoryUsageColor$(objectMemoryUsage)
        this.objectMemoryUsageLabel.string = `Object Mem: ${objectMemoryUsage}MB`
    }

    private updateObjectMemoryUsageColor$ = (objectMemoryUsage: number) => {
        if (objectMemoryUsage > 40) {
            this.objectMemoryUsageLabel.color = DANGER_COLOR
        } else if (objectMemoryUsage > 25) {
            this.objectMemoryUsageLabel.color = WARNING_COLOR
        } else {
            this.setColorWithBackground$(this.objectMemoryUsageLabel)
        }
    }

    // ? The texture memory usage is the memory used by the textures in the scene
    // ? If it's bigger than 50MB, it's not good
    private updateTextureMemoryUsage$ = () => {
        const textureMemoryUsage = this.getTextureMemoryUsage$()
        this.updateTextureMemoryUsageColor$(textureMemoryUsage)

        this.textureMemoryUsageLabel.string = `Texture Mem: ${textureMemoryUsage}MB`
    }

    private updateTextureMemoryUsageColor$ = (textureMemoryUsage: number) => {
        if (textureMemoryUsage > 200) {
            this.textureMemoryUsageLabel.color = DANGER_COLOR
        } else if (textureMemoryUsage > 100) {
            this.textureMemoryUsageLabel.color = WARNING_COLOR
        } else {
            this.setColorWithBackground$(this.textureMemoryUsageLabel)
        }
    }

    private updateDPR$ = () => {
        const devicePixelRatio = D.pixelRatio()
        this.updateDPRColor$(devicePixelRatio)
        this.dprLabel.string = `DPR: ${devicePixelRatio}`
    }

    private updateDPRColor$ = (devicePixelRatio: number) => {
        if (devicePixelRatio > 2) {
            this.dprLabel.color = DANGER_COLOR
        } else if (devicePixelRatio <= 1) {
            this.dprLabel.color = WARNING_COLOR
        } else {
            this.setColorWithBackground$(this.dprLabel)
        }
    }

    private setColorWithBackground$ = (label: Label) => {
        const color = this.isBackgroundEnabled$ ? BLACK_COLOR : WHITE_COLOR
        if (label.color.equals(color)) return
        label.color = color
    }

    // Frames per second
    private getFps$ = (): number => {
        if (!director.root) return 0

        const { fps } = director.root
        return Math.round(fps) || 0
    }

    private getDrawCalls$ = (): number => {
        if (!director.root) return 0

        const { numDrawCalls } = director.root.device
        return numDrawCalls || 0
    }

    private toFixed$ = (value: number, precision: number = 2): number => {
        // keep format: 0.0000ms, 0.0000MB, 0.0000fps, etc
        return Number(value.toFixed(precision)) || 0
    }

    private getFpsRaw$ = (): number => {
        return this.toFixed$(1000 / this.rafTimeCounter$.value, 0)
    }

    private getRafTime$ = (): number => {
        return this.toFixed$(this.rafTimeCounter$.value)
    }

    private getDeltaTime$ = (): number => {
        return this.toFixed$(game.deltaTime * 1000)
    }

    private getLogicTime$ = (): number => {
        return this.toFixed$(this.logicTimeCounter$.value)
    }

    private getPhysicTime$ = (): number => {
        return this.toFixed$(this.physicTimeCounter$.value)
    }

    private getRenderTime$ = (): number => {
        return this.toFixed$(this.renderTimeCounter$.value)
    }

    private getObjectMemoryUsage$ = (): number => {
        if (!director.root) return 0

        const { memoryStatus } = director.root.device
        return this.toFixed$(memoryStatus.bufferSize / (1024 * 1024), 4) || 0
    }

    private getTextureMemoryUsage$ = (): number => {
        if (!director.root) return 0

        const { memoryStatus } = director.root.device
        return this.toFixed$(memoryStatus.textureSize / (1024 * 1024), 4) || 0
    }

    private beforeUpdate$ = () => {
        const now = performance.now()
        this.logicTimeCounter$.start(now)
    }

    private afterUpdate$ = () => {
        const now = performance.now()
        this.logicTimeCounter$.end(now)
    }

    private beforePhysics$ = () => {
        const now = performance.now()
        this.physicTimeCounter$.start(now)
    }

    private afterPhysics$ = () => {
        const now = performance.now()
        this.physicTimeCounter$.end(now)
    }

    private beforeDraw$ = () => {
        const now = performance.now()
        this.renderTimeCounter$.start(now)
    }

    private afterDraw$ = () => {
        const now = performance.now()
        this.renderTimeCounter$.end(now)
    }
}
