import { pseudoRandomRangeInt } from 'cc'

type Grid<T> = {
    asArray(): ReadonlyArray<T>
}

function isArray<T>(i: unknown): i is ReadonlyArray<T> {
    return Array.isArray(i)
}

function getRandomTile<T>(grid: Grid<T>, seed?: number): T
function getRandomTile<T>(tiles: ReadonlyArray<T>, seed?: number): T

function getRandomTile<T>(gridOrTiles: Grid<T> | ReadonlyArray<T>, seed = Date.now()): T {
    const arr = isArray(gridOrTiles) ? gridOrTiles : gridOrTiles.asArray()
    return arr[pseudoRandomRangeInt(seed, 0, arr.length)]
}

export default getRandomTile
