import LeaderboardUtils from 'db://assets/utils/LeaderboardUtils'
import { _decorator, Component, Node, tween, Vec3 } from 'cc'
import AnimUtils from '../../../../../../utils/AnimUtils'
import ComponentUtils from '../../../../../../utils/ComponentUtils'
import TournamentUtils from '../../../../../../utils/TournamentUtils'

const {
    Utils: { Signal: S },
} = Ludex

type Signal = InstanceType<typeof S.Signal>

const { ccclass, property } = _decorator

const VEC3_1_1 = new Vec3(1.1, 1.1)

@ccclass('TournamentButton')
export class TournamentButton extends Component {
    @ComponentUtils.required()
    @property(Node)
    public readonly hotIcon!: Node

    private readonly requestController = new S.SignalController()

    protected __preload(): void {
        ComponentUtils.validateRequiredProperties(this, TournamentButton)
    }

    protected async onLoad(): Promise<void> {
        await this.handleHotIconAppearance()
    }

    protected onDestroy(): void {
        this.requestController.abort()
    }

    private async handleHotIconAppearance() {
        if (GameSDK.isGameSDK('MsGames')) {
            this.handleMsGamesHotIconCheck()
        } else {
            this.handleNormalHotIconCheck()
        }
    }

    private async handleMsGamesHotIconCheck() {
        try {
            const signal = this.requestController.renew()

            const tournaments = await TournamentUtils.requestTournamentDataAsync()
            if (signal.aborted) return

            const activeTournaments = tournaments.filter((tour) => !TournamentUtils.validateTournamentExpired(tour.id))

            if (activeTournaments.length >= 2) {
                this.showHotIcon(signal)
            }
        } catch (error) {
            console.info('Error in TournamentButton', error)
        }
    }

    private async handleNormalHotIconCheck() {
        try {
            const signal = this.requestController.renew()

            const tournaments = await TournamentUtils.requestTournamentDataAsync()
            if (signal.aborted) return

            for (const tournament of tournaments) {
                const { leaderboardId, playerId } = tournament.payload
                if (!leaderboardId || playerId !== 'global') continue

                const leaderboardData = await LeaderboardUtils.getAllLeaderboardAsync(leaderboardId)
                if (!leaderboardData) continue

                if (leaderboardData.amountPlayer >= 30) {
                    this.showHotIcon(signal)
                    break
                }
            }
        } catch (error) {
            console.info('Error in TournamentButton', error)
        }
    }

    @ComponentUtils.onlyRunInLifeCycle
    private showHotIcon(signal: Signal) {
        this.hotIcon.active = true
        this.hotIcon.setScale(Vec3.ZERO)

        const pulse = tween(this.hotIcon)
            .delay(1)
            .to(0.3, { scale: VEC3_1_1 }, { easing: 'backIn' })
            .to(0.3, { scale: Vec3.ONE }, { easing: 'backOut' })

        AnimUtils.runTweenAsync(
            tween(this.hotIcon).to(0.5, { scale: Vec3.ONE }, { easing: 'backOut' }).repeatForever(pulse),
            signal,
        )
    }
}
