import { _decorator, Component, Label, Layout, Node, Sprite, SpriteFrame, UITransform, View } from 'cc'
import { Localize } from '../../components/text/Localize'
import { CodeSoundKeys } from '../../constants/SoundKeys'
import TournamentEvents from '../../constants/Tournament'
import GameplayEvents from '../../gameplay/constants/GameEvents'
import ThemeKeys from '../../gameplay/constants/ThemeKeys'
import { ThemeTournamentResourcesMapping } from '../../gameplay/constants/ThemeResourcesPath'
import { ParticleSystem } from '../../particles/ParticleSystem'
import { Leaders } from '../../scenes/dashboard-scene/components/pages/leaderboard/components/leader/Leaders'
import type { ITournamentLeadersPayload } from '../../scenes/dashboard-scene/components/pages/tournaments/TournamentContent'
import AdsUtils from '../../utils/AdsUtils'
import AudioUtils from '../../utils/AudioUtils'
import ComponentUtils from '../../utils/ComponentUtils'
import AnimAbortSignal from '../../utils/common/AnimAbortSignal'
import LayoutUtils from '../../utils/LayoutUtils'
import LeaderboardUtils from '../../utils/LeaderboardUtils'
import MatchUtils from '../../utils/MatchUtils'
import OverlayUtils from '../../utils/OverlayUtils'
import ResourceUtils from '../../utils/ResourceUtils'
import SceneUtils from '../../utils/SceneUtils'
import ScreenUtils from '../../utils/ScreenUtils'
import TournamentUtils, { type TournamentData } from '../../utils/TournamentUtils'
import { BaseScreen } from '../BaseScreen'
import { TopLeaders } from './components/TopLeaders'
import TournamentDetailOverlay from './components/TournamentDetailOverlay'
import { TournamentLeaderSkeleton } from './components/TournamentLeaderSkeleton'

const { ccclass, property, requireComponent } = _decorator

const {
    Events,
    Utils: { Object: O, Time: T, Number: N },
} = Ludex

const { event, storage, player, leaderboard, language } = codex

const CONTAINER_HEIGHT = 1166

type PlayTournamentPayload = {
    level: number
    score: number
    replay: boolean
}

@ccclass('TournamentDetailScreen')
@requireComponent(BaseScreen)
export class TournamentDetailScreen extends Component {
    @ComponentUtils.required()
    private screen!: BaseScreen

    @ComponentUtils.required()
    @property(Label)
    private readonly title!: Label

    @ComponentUtils.required()
    @property(Localize)
    private readonly titleLocalizer!: Localize

    @ComponentUtils.required()
    @property(Localize)
    private readonly timeLocalizer!: Localize

    @ComponentUtils.required()
    @property(Localize)
    private readonly playerCountLocalizer!: Localize

    @ComponentUtils.required()
    @property(Leaders)
    private readonly tournamentLeaders!: Leaders

    @ComponentUtils.required()
    @property(TopLeaders)
    private readonly topLeaders!: TopLeaders

    @ComponentUtils.required()
    @property({ type: Node, group: 'Buttons' })
    private readonly playButton!: Node

    @ComponentUtils.required()
    @property({ type: Node, group: 'Buttons' })
    private readonly continueButton!: Node

    @ComponentUtils.required()
    @property({ type: Node, group: 'Buttons' })
    private readonly replayButton!: Node

    @ComponentUtils.required()
    @property({ type: Node, group: 'Buttons' })
    private readonly logInButton!: Node

    @ComponentUtils.required()
    @property({ type: Layout, group: 'Buttons' })
    private readonly buttonsLayout!: Layout

    @ComponentUtils.required()
    @property({ type: Sprite })
    private readonly banner!: Sprite

    @ComponentUtils.required()
    @property({ type: Node })
    private readonly bannerLoadingRing!: Node

    @ComponentUtils.required()
    @property({ type: Node })
    private readonly bannerMask!: Node

    @property({ type: [TournamentLeaderSkeleton] })
    private readonly leaderSkeletons: TournamentLeaderSkeleton[] = []

    @property({ type: ParticleSystem, group: 'Confetti' })
    private readonly leftConfetti!: ParticleSystem

    @property({ type: ParticleSystem, group: 'Confetti' })
    private readonly rightConfetti!: ParticleSystem

    @ComponentUtils.required()
    @property({ type: UITransform, group: 'Container' })
    private readonly container!: UITransform

    @ComponentUtils.required()
    @property({ type: UITransform, group: 'Container' })
    private readonly tempContainer!: UITransform

    @ComponentUtils.required()
    @property({ type: Node, group: 'Container' })
    private readonly topPlayerOverlayRef!: Node

    private signal$: AnimAbortSignal = new AnimAbortSignal()
    private isOverlayMode$: boolean = false

    private payload$!: SwitchScreenPayload

    private tournamentPayload$!: TournamentData
    private leadersPayload$: ITournamentLeadersPayload | null = null

    private tournamentDetailOverlay$: TournamentDetailOverlay | null = null

    public __preload() {
        this.enabled = false

        this.screen = this.getComponent(BaseScreen) as BaseScreen

        ComponentUtils.validateRequiredProperties(this, TournamentDetailScreen)
        this.listenScreenEvents$()
        this.createTournamentOverlay$()
    }

    public onDestroy(): void {
        this.signal$.abort()
        this.unListenScreenEvents$()
        this.cleanupOverlay$()
    }

    private listenScreenEvents$ = (): void => {
        // ? Here's a demo of all the events a screen has
        event.on(Events.OPENING_SCREEN, this.handleOpeningScreen$)
        event.on(Events.OPENED_SCREEN, this.handleOpenedScreen$)
    }

    private unListenScreenEvents$ = (): void => {
        event.off(Events.OPENING_SCREEN, this.handleOpeningScreen$)
        event.off(Events.OPENED_SCREEN, this.handleOpenedScreen$)
    }

    private readonly handleOpeningScreen$ = (payload: SwitchScreenPayload) => {
        if (!this.screen) {
            throw new Error('TournamentDetailScreen: screen not found')
        }

        if (!this.validateEventPayload$(payload)) return
        this.enabled = true
    }

    private readonly handleOpenedScreen$ = (payload: SwitchScreenPayload) => {
        if (!this.validateEventPayload$(payload)) return
        this.open()
    }

    private validateEventPayload$ = (payload: SwitchScreenPayload): boolean => {
        const { screenName } = payload ?? {}
        if (screenName !== this.node.name) return false

        this.payload$ = payload

        return true
    }

    public open() {
        this.updateContainer$()
        this.updateInfo()
        this.updateOverlayScreenStatus$(true)

        this.setInteractableButtons$(true)
        this.tournamentLeaders.hideLeaders()
        this.topLeaders.hideLeaders()
        this.updateButtonVisibility$()
        this.setPlayerCountWaiting$()

        this.node.scene.emit(GameplayEvents.HIDE_UI)
    }

    public close() {
        ScreenUtils.closeTournamentDetailScreen()
        this.setInteractableButtons$(false)
        this.tournamentLeaders.scrollView.unlistenEvents()
        this.updateOverlayScreenStatus$(false)

        this.node.scene.emit(GameplayEvents.SHOW_UI)

        //? Handle cancelling gracefully
        this.cleanup$()
    }

    private cleanup$ = (): void => {
        this.signal$?.abort()
        this.signal$ = new AnimAbortSignal()

        this.tournamentLeaders.hideLeaders()
        this.topLeaders.hideLeaders()
        this.cleanupOverlay$()
    }

    private cleanupOverlay$ = (): void => {
        this.tournamentDetailOverlay$?.hideOverlay()
    }

    private handleOverlayMode$ = async (signal: AnimAbortSignal): Promise<boolean> => {
        if (!this.leadersPayload$) return false

        const promise = new Promise<boolean>((resolve) => {
            signal.call(() => {
                this.updateOverlay$().then((result) => {
                    this.isOverlayMode$ = result
                    resolve(result)
                })
            })
        })

        return promise
    }

    private updateOverlay$ = async (): Promise<boolean> => {
        if (!this.leadersPayload$ || !this.tournamentDetailOverlay$) return false

        const sortedLeaders = this.leadersPayload$.leaders ?? []
        sortedLeaders.sort((a, b) => a.rank - b.rank)

        const tournamentPlayers = sortedLeaders.map((leader) => ({
            playerId: leader.playerId,
            rank: leader.rank,
            score: leader.score,
        }))

        const result = await this.tournamentDetailOverlay$.updateOverlay({
            tournamentPlayers,
            leaderboardPosition: this.getLeaderboardPosition$(),
            topPlayerPosition: this.getTopPlayerPosition$(),
            currentPlayerId: player.getPlayerId(),
        })

        return result ?? false
    }

    @ComponentUtils.onlyRunInLifeCycleAsync
    private async updateInfo(): Promise<void> {
        const signal = this.signal$

        const tournamentStr = this.payload$?.screenData?.tournament
        if (!tournamentStr) return

        const playAnimation = this.payload$.screenData?.playAnimation ?? true

        const tournament = JSON.parse(tournamentStr) as TournamentData

        // ? Fix tournament id type from FB
        tournament.id = tournament.id.toString()

        this.tournamentPayload$ = tournament
        const { endTime, payload, title, tournamentType } = this.tournamentPayload$
        const { leaderboardId = '', themeId = ThemeKeys.AUTUMN, playerName } = payload

        if (tournamentType === 'PAGE_HOSTED') {
            this.updateTitleTournamentTitle$(title)
        } else {
            this.updateTitleWithPlayerName$(playerName ?? title)
        }

        this.updateEndTime$(endTime)
        this.updateTournamentBanner$(themeId, signal)

        const { previousScore, animateTournamentLeader } = storage.getStorage('tournament')
        storage.setStorageData('tournament', 'animateTournamentLeader', false)

        await this.showLoading(signal)
        await this.loadLeaders(leaderboardId)

        if (!this.leadersPayload$ || this.leadersPayload$.leaders.length === 0 || signal.aborted) {
            this.updatePlayerCount(0)
            await this.hideLoading(signal)
            return
        }

        const { player: playerLeaderData, amountPlayer } = this.leadersPayload$
        const leaders = this.leadersPayload$.leaders ?? []
        leaders.sort((a, b) => a.rank - b.rank)

        await this.hideLoading(signal)

        if (this.isOverlayMode$) {
            const result = await this.handleOverlayMode$(signal)
            if (result) return
        }

        if (!this.topLeaders || !this.tournamentLeaders || signal.aborted) return

        this.updatePlayerCount(amountPlayer)
        this.topLeaders.updateData(leaders)
        this.topLeaders.playShowLeadersAnimation(signal)

        const currentScore = playerLeaderData?.score ?? 0
        const currentRank = playerLeaderData?.rank ?? 0

        const scoreChanging = animateTournamentLeader && previousScore !== -1 && previousScore < currentScore

        this.tournamentLeaders.updateData(leaders)

        // ? If player is in the leaderboard, we need to check if the score and rank has changed
        let rankChanging = false
        if (playerLeaderData && scoreChanging) {
            rankChanging = this.tournamentLeaders.updateDisplayPlayerLeaderForRankChanging(
                previousScore,
                currentScore,
                currentRank,
            )
        }

        this.tournamentLeaders.initScrollViews()
        this.tournamentLeaders.scrollView.scrollView.enabled = true

        await signal.callAsync((s) => this.runShowUpAnimation(s, playAnimation))

        if (!animateTournamentLeader || !rankChanging || previousScore === -1 || !this.tournamentLeaders) return

        await signal.callAsync((s) =>
            this.tournamentLeaders.playRankChangeAnimation(s, previousScore, currentScore, currentRank),
        )

        this.tryPlayConfettiEffect(currentRank)
    }

    private updateContainer$ = async (): Promise<void> => {
        const { height } = this.tempContainer
        this.container.node.position = this.tempContainer.node.position.clone()

        if (height < CONTAINER_HEIGHT && this.container.height !== height) {
            this.container.height = height
            View.instance.emit('canvas-resize')
        } else if (height > CONTAINER_HEIGHT && this.container.height < CONTAINER_HEIGHT) {
            this.container.height = CONTAINER_HEIGHT
            View.instance.emit('canvas-resize')
        }
    }

    @ComponentUtils.onlyRunInLifeCycleAsync
    private async loadLeaders(leaderboardId: string) {
        this.leadersPayload$ = await this.getLeadersPayload$(leaderboardId)
    }

    @ComponentUtils.onlyRunInLifeCycleAsync
    private async showLoading(signal: AnimAbortSignal, skeletonDelay = 0): Promise<void> {
        if (this.isOverlayMode$) {
            this.cleanupOverlay$()
            return
        }

        await Promise.all([
            this.tournamentLeaders.scrollView.unlistenEvents(),
            this.tournamentLeaders.scrollView.scrollToTop(signal, 0.1).then(() => {
                this.tournamentLeaders.scrollView.scrollView.enabled = false
                this.tournamentLeaders.scrollView.listenEvents()
            }),
            this.leaderSkeletons.map((skeleton, index) => skeleton.show(index * skeletonDelay, signal)),
        ])
    }

    @ComponentUtils.onlyRunInLifeCycleAsync
    private async hideLoading(signal: AnimAbortSignal): Promise<void> {
        if (this.isOverlayMode$) {
            return
        }

        await Promise.all(this.leaderSkeletons.map((skeleton) => skeleton.hide(signal)))
    }

    @ComponentUtils.onlyRunInLifeCycleAsync
    private async runShowUpAnimation(signal: AnimAbortSignal, playAppearAnim = true): Promise<void> {
        const stagger = playAppearAnim ? 0 : 0.1

        await this.tournamentLeaders?.playShowLeadersAnimation(signal, stagger)
        await this.tournamentLeaders?.scrollToPlayerLeader(1.5, signal)
    }

    @ComponentUtils.onlyRunInLifeCycle
    private tryPlayConfettiEffect(playerRank: number): void {
        if (playerRank > 10) return

        const isLandscape = LayoutUtils.isLandscape()

        const burstCount = isLandscape ? 50 : 30
        const startSpeedMin = isLandscape ? 5000 : 2000
        const startSpeedMax = isLandscape ? 15000 : 10000
        const leftAngle = isLandscape ? 40 : 60
        const rightAngle = isLandscape ? 110 : 100
        const arc = isLandscape ? 30 : 20

        this.leftConfetti.bursts[0]!.count = this.rightConfetti.bursts[0]!.count = burstCount
        this.leftConfetti.startSpeed.constantMin = this.rightConfetti.startSpeed.constantMin = startSpeedMin
        this.leftConfetti.startSpeed.constantMax = this.rightConfetti.startSpeed.constantMax = startSpeedMax
        this.leftConfetti.shapeModule.angle = leftAngle
        this.rightConfetti.shapeModule.angle = rightAngle
        this.leftConfetti.shapeModule.arc = this.rightConfetti.shapeModule.arc = arc

        this.leftConfetti.play()
        this.rightConfetti.play()

        AudioUtils.playSound(CodeSoundKeys.LEADERBOARD_TRIUMPH)
    }

    private updateTitleWithPlayerName$ = (name?: string): void => {
        if (name === undefined) return
        this.titleLocalizer.enabled = true
        this.titleLocalizer.setParams([name])
    }

    private updateTitleTournamentTitle$ = (title: string): void => {
        this.titleLocalizer.enabled = false
        this.title.string = title
    }

    private updateEndTime$ = (endTime: number): void => {
        try {
            const timeString = T.getRelativeTime(endTime, language.getCurrentLocale(), { style: 'short' })
            this.timeLocalizer.key = 'game.endsIn'
            this.timeLocalizer.setParams([timeString])
        } catch {
            this.timeLocalizer.key = 'game.endsSoon'
        }
    }

    private updateTournamentBanner$ = async (themeId: ThemeKeys, signal: AnimAbortSignal): Promise<void> => {
        this.banner.spriteFrame = null

        let sprite = ResourceUtils.getAsset(`${ThemeTournamentResourcesMapping[themeId]}/spriteFrame`, SpriteFrame)

        if (!sprite) {
            this.bannerMask.active = true
            this.bannerLoadingRing.active = true
            sprite = await ResourceUtils.loadThemeTournamentAsync(themeId)
        }

        if (!sprite) {
            //? Fallback to summer theme
            sprite = await ResourceUtils.loadThemeTournamentAsync(ThemeKeys.SUMMER)
        }

        if (!this.banner || !sprite || signal.aborted) return

        this.banner.spriteFrame = sprite
        this.bannerMask.active = false
        this.bannerLoadingRing.active = false
    }

    @ComponentUtils.onlyRunInLifeCycle
    private updatePlayerCount(count: number): void {
        const shortPLayerNumber = N.getShortNumber(count, 3)

        if (count === 1) {
            this.playerCountLocalizer.key = 'game.player'
        } else {
            this.playerCountLocalizer.key = 'game.nPlayers'
            this.playerCountLocalizer.setParams([shortPLayerNumber])
        }
    }

    private setPlayerCountWaiting$ = (): void => {
        this.playerCountLocalizer.key = 'status.loading'
    }

    private updateButtonVisibility$ = (): void => {
        this.playButton.active = false
        this.continueButton.active = false
        this.replayButton.active = false
        this.logInButton.active = false

        if (GameSDK.extra.isGuest) {
            this.logInButton.active = true
            return
        }

        const { tournamentContextType = 'tournament_start' } = this.payload$?.screenData ?? {}

        switch (tournamentContextType) {
            case 'tournament_end': {
                const { replayCount = 0 } = player.getGameData()?.tournamentProgress[this.tournamentPayload$.id] ?? {}

                this.continueButton.active = replayCount > 1
                this.replayButton.active = true
                this.buttonsLayout.updateLayout(true)
                break
            }

            case 'tournament_start':
                this.playButton.active = true
                break
        }
    }

    private getLeadersPayload$ = async (leaderboardId: string): Promise<ITournamentLeadersPayload | null> => {
        const leaderboardName = leaderboard.getLeaderboardName(leaderboardId)
        if (!leaderboardName) return null

        const leaderboardData = await LeaderboardUtils.getAllLeaderboardAsync(leaderboardId)
        if (!leaderboardData) return null

        const leaders = O.vals(leaderboardData.leaders) as Ludex.Plugins.Leaderboards.Leader[]

        const currentPlayerId = player.getPlayerId()

        const leadersPayload: ITournamentLeadersPayload = {
            leaders: [],
            amountPlayer: leaderboardData.amountPlayer,
        }

        for (const leader of leaders) {
            const { playerId } = leader

            if (playerId === currentPlayerId) {
                leadersPayload.player = leader
                leadersPayload.leaders.push(leader)
            } else {
                leadersPayload.leaders.push(leader)
            }
        }

        if (!leadersPayload.player) {
            const playerData = await leaderboard.getLeadersByPlayerIdsAsync(leaderboardName, [currentPlayerId])
            const player = playerData[0]
            if (player) {
                leadersPayload.player = player
                leadersPayload.leaders.push(player)
            }
        }

        return leadersPayload
    }

    public async handleOnLogIn(): Promise<void> {
        try {
            if (!('initPlayerAsync' in GameSDK.player)) return

            ScreenUtils.showLoadingScreen()
            await GameSDK.player.initPlayerAsync()
            ScreenUtils.closeLoadingScreen()

            if (GameSDK.extra.isGuest) {
                ScreenUtils.showNotificationScreenWithLocalizedKey('error.login', 1)
                return
            }
        } catch {
            ScreenUtils.closeLoadingScreen()
            ScreenUtils.showNotificationScreenWithLocalizedKey('error.login', 1)
        }
    }

    public async handleOnPlay(): Promise<void> {
        if (GameSDK.isGameSDK('Yandex')) {
            // ? Show interstitial ad before starting the tournament in Yandex SDK
            await AdsUtils.showInterstitialAdAsync()
        }

        await this.processPlayTournament$({ level: 1, score: 0, replay: false })
    }

    public async handleOnReplay(): Promise<void> {
        if (GameSDK.isGameSDK('Yandex')) {
            // ? Show interstitial ad before starting the tournament in Yandex SDK
            await AdsUtils.showInterstitialAdAsync()
        }

        await this.processPlayTournament$({ level: 1, score: 0, replay: true })
    }

    public async handleOnContinue(): Promise<void> {
        const success = await AdsUtils.showRewardVideoAdAsync()
        if (!success) return

        //? Resume the tournament at half the last level
        const { level = 1, score = 0 } = player.getGameData()?.tournamentProgress[this.tournamentPayload$.id] ?? {}
        const halfLevel = level > 1 ? Math.floor(level / 2) : 1
        const halfScore = Math.floor(score / 2)

        await this.processPlayTournament$({ level: halfLevel, score: halfScore, replay: false })
    }

    private processPlayTournament$ = async (payload: PlayTournamentPayload): Promise<void> => {
        const tournamentId = this.tournamentPayload$.id || ''
        const isTournamentExpired = TournamentUtils.validateTournamentExpired(tournamentId)
        TournamentUtils.syncTournamentProgress()

        if (isTournamentExpired) {
            this.close()
            this.node.scene.emit(TournamentEvents.RELOAD_TOURNAMENT)
            ScreenUtils.showNotificationScreenWithLocalizedKey('error.tourEnded', 2)
            return
        }

        const { level, score, replay } = payload

        try {
            ScreenUtils.showLoadingScreen()
            await MatchUtils.setMatchCustomDataAsync({ level, score })
            await this.processJoinTournament$(level)
            if (replay) this.increaseTournamentReplayCount$()
        } catch (joinError) {
            if (O.hasOwn(joinError, 'code') && joinError.code === 'USER_INPUT') {
                // ? User cancelled. Safely ignore
                return
            }

            try {
                await this.processContinueTournament$(level)
                if (replay) this.increaseTournamentReplayCount$()
            } catch (continueError) {
                if (O.hasOwn(continueError, 'code') && continueError.code === 'USER_INPUT') {
                    // ? User cancelled. Safely ignore
                    return
                }

                // ? If error is unexpected, show error message
                console.error('processPlayButtonPressed', continueError)
                ScreenUtils.showNotificationScreenWithLocalizedKey('error.tourJoinFail', 2)
            }
        } finally {
            ScreenUtils.closeLoadingScreen()
        }
    }

    private processJoinTournament$ = async (level: number = 1): Promise<void> => {
        try {
            const tournamentId = this.tournamentPayload$.id || ''
            await MatchUtils.joinTournamentAsync(tournamentId)
            SceneUtils.switchToGameScene({ level })
        } catch (error) {
            console.warn('processJoinTournament', error)
            throw error
        }
    }

    private processContinueTournament$ = async (level: number = 1): Promise<void> => {
        try {
            const contextId = GameSDK.context.getID() ?? ''
            await MatchUtils.continueTournamentAsync(contextId)
            SceneUtils.switchToGameScene({ level })
        } catch (error) {
            console.warn('processContinueTournament', error)
            throw error
        }
    }

    private increaseTournamentReplayCount$ = (): void => {
        const { tournamentId } = MatchUtils.getMatchCustomData() ?? {}
        if (!tournamentId) return

        const gameData = player.getGameData()
        if (!gameData) return

        const { tournamentProgress } = gameData

        const { replayCount = 0 } = tournamentProgress[tournamentId] ?? {}

        player.setGameData({
            tournamentProgress: {
                [tournamentId]: {
                    level: 1,
                    score: 0,
                    ...tournamentProgress[tournamentId],
                    replayCount: replayCount + 1,
                },
            },
        })
    }

    private setInteractableButtons$ = (interactable: boolean): void => {
        this.screen.backgroundMaskButton.interactable = interactable
    }

    private createTournamentOverlay$ = (): void => {
        try {
            OverlayUtils.isOverlayViewAPIAvailable(GameSDK)

            this.isOverlayMode$ = true
            this.tournamentDetailOverlay$ = new TournamentDetailOverlay(this.node.scene)
        } catch {
            return
        }
    }

    private getLeaderboardPosition$ = (): { worldX: number; worldY: number; width: number; height: number } => {
        const leaderboardUITransform = this.tournamentLeaders.getComponent(UITransform)
        if (!leaderboardUITransform)
            return {
                worldX: 0,
                worldY: 0,
                width: 0,
                height: 0,
            }

        const { width, height } = leaderboardUITransform
        const { worldPositionX, worldPositionY } = this.tournamentLeaders.node

        return {
            worldX: worldPositionX,
            worldY: worldPositionY,
            width,
            height,
        }
    }

    private getTopPlayerPosition$ = (): { worldX: number; worldY: number; width: number; height: number } => {
        const topPlayerUITransform = this.topPlayerOverlayRef.getComponent(UITransform)
        if (!topPlayerUITransform)
            return {
                worldX: 0,
                worldY: 0,
                width: 0,
                height: 0,
            }

        const { width, height } = topPlayerUITransform
        const { worldPositionX, worldPositionY } = this.topPlayerOverlayRef

        return {
            worldX: worldPositionX,
            worldY: worldPositionY,
            width,
            height,
        }
    }

    private updateOverlayScreenStatus$ = (isScreenActive: boolean): void => {
        this.tournamentDetailOverlay$?.setIsScreenActive(isScreenActive)
    }
}
