import { _decorator, Button, Component, Node } from 'cc'
import { Localize } from '../../../../../../components/text/Localize'
import DashboardEvents from '../../../../../../constants/DashboardEvents'
import ComponentUtils from '../../../../../../utils/ComponentUtils'

const { ccclass, property } = _decorator

@ccclass('PlayButton')
export class PlayButton extends Component {
    @ComponentUtils.required()
    @property(Localize)
    private localizer!: Localize

    @ComponentUtils.required()
    @property(Node)
    private defaultBackground!: Node

    @ComponentUtils.required()
    @property(Node)
    private hardLevelBackground!: Node

    @ComponentUtils.required()
    @property(Button)
    private button!: Button

    public set interactable(value: boolean) {
        this.button.interactable = value
    }

    public updateStatus(level: number): void {
        const isHardLevel = level % 5 === 0

        this.localizer.setParams([level.toString()])
        this.defaultBackground.active = !isHardLevel
        this.hardLevelBackground.active = isHardLevel
    }

    public handleOnClicked(): void {
        this.node.scene.emit(DashboardEvents.PLAY_BUTTON_PRESS)
    }
}
